import child_process from "child_process"
import process from "process"
import { lazy } from "../../core/Lazy"
import { later } from "../../fe/Delay"
import { Maybe } from "../../fe/MaybeTypes"
import {
  compactValues,
  entries,
  filter,
  fromEntries,
  omit,
  StringValued,
  values
} from "../../fe/Object"
import { partition } from "../Array"
import { env } from "../env/Env"
import {
  ELECTRON_RUN_AS_NODE,
  PS_IS_CHILD_PROCESS,
  PS_IS_DOCKER
} from "../env/EnvKeys"
import { caseInsensitiveEnv } from "../env/GetEnv"
import { ee } from "../event/EventEmitter"
import { childProcLocale } from "../Locale"
import { isLogged } from "../log/LogFilter"
import { mkLogger } from "../Logger"
import { nodeEnv } from "../NodeEnv"
import { mapEntries, sortedKeys } from "../Object"
import { isDocker } from "../platform/isDocker"
import { isElectron, isPosix } from "../platform/Platform"
import {
  allSettings,
  getSettingByNameOrKey,
  pathWithDefaults,
  persistedSettings,
  Settings,
  transientSettings
} from "../settings/Settings"

const SettingsKeys = lazy(() => new Set(values(Settings).map(ea => ea.key)))

export function psenv(): StringValued {
  const set = SettingsKeys()
  return sortedKeys(filter(env(), k => k === "NODE_ENV" || set.has(k)))
}

const sensitiveEnvRE = lazy(() => {
  try {
    return new RegExp(Settings.sensitiveEnvRegExp.valueOrDefault, "i")
  } catch (err) {
    console.error(
      `Invalid setting for "sensitiveEnvRegExp": ${err}. Using default value.`
    )
    return new RegExp(Settings.sensitiveEnvRegExp.defaultValue, "i")
  }
})

later(() => {
  function unset() {
    sensitiveEnvRE.unset()
    sanitizedEnv.unset()
  }
  ee().on("clearCache", unset)
  ee().on("settingsChanged", unset)
  for (const ea of allSettings()) {
    // allSettings because anything might need to be included in the child env!
    ea.watchLater(unset)
  }
})

const RequiredPosixEnv = ["HOME", "LANG", "USER"]
const RequiredWindowsEnv = [
  "APPDATA",
  "HOME",
  "HOMEDRIVE",
  "LOCALAPPDATA",
  "SYSTEMDRIVE",
  "SYSTEMROOT",
  "TEMP"
]

const requiredEnv = lazy(() => {
  const result = caseInsensitiveEnv().pick(
    ...(isPosix ? RequiredPosixEnv : RequiredWindowsEnv)
  )
  // set PATH, and respect prior case:
  const p = caseInsensitiveEnv().lookup("PATH")
  result[p?.key ?? "PATH"] = pathWithDefaults()
  return result
})

export const sanitizedEnv = lazy(() => {
  const re = sensitiveEnvRE()
  return mapEntries(process.env, (k, v) => (re.exec(k) == null ? v : undefined))
})

export function childProcEnvSettings(forWorker: boolean) {
  const result: StringValued = {
    NODE_ENV: nodeEnv,
    NO_COLOR: "1" // children shouldn't be rendering anything to the terminal
  }
  result[PS_IS_CHILD_PROCESS] = "1"

  if (isDocker()) {
    result[PS_IS_DOCKER] = "1"
  }
  if (isElectron) {
    result[ELECTRON_RUN_AS_NODE] = "1"
  }

  for (const s of persistedSettings().filter(ea => ea.hasValue())) {
    forWorker ? s.addToEnv(result) : s.addToEnvMaybe(result)
  }
  return result
}

export type SpawnOptionsWithLocale = child_process.SpawnOptions & {
  forceCLocale?: boolean
  forThread?: boolean
}

export function spawnOptions(
  maybeOpts?: SpawnOptionsWithLocale
): child_process.SpawnOptions {
  const opts: SpawnOptionsWithLocale = maybeOpts ?? {}
  return {
    ...omit(opts, "forceCLocale"),
    env: childEnv({
      overrides: opts.env,
      forceCLocale: opts.forceCLocale
    }),
    detached: false,
    shell: false
    // NOTE: don't use `windowsHide: true`, it doesn't work!
  }
}

let childEnvLogged = false

export function childEnv({
  overrides,
  forceCLocale = true,
  forWorker = false
}: {
  overrides?: Maybe<StringValued>
  forceCLocale?: boolean
  forWorker?: boolean
} = {}) {
  const e: StringValued = compactValues({
    // Include all (non-sensitive) env values, because things like `gio` need
    // GNOME* and DBUS* variables set:
    ...sanitizedEnv(),
    ...requiredEnv(),
    ...(forceCLocale ? childProcLocale() : {}),
    ...childProcEnvSettings(forWorker),
    ...(overrides ?? {}) // < env param wins
  })!

  // Don't let parent ENV set transients:
  for (const s of transientSettings()) {
    s.deleteFromEnv(e)
  }

  if (isLogged("debug") && !childEnvLogged) {
    childEnvLogged = true
    const logger = mkLogger("child.ChildEnv")
    const arr = entries(e)
    const [ps, nonPs] = partition(
      arr,
      ([k]) =>
        k === "TZ" ||
        k === "NODE_ENV" ||
        k.startsWith("LC_") ||
        // we have some env that aren't settings (like PS_KEEP_LIBRARY):
        k.startsWith("PS_") ||
        getSettingByNameOrKey(k) != null
    )
    logger.debug("PS env:", fromEntries(ps))
    logger.debug("non PS env:", fromEntries(nonPs))
  }

  return sortedKeys(e)
}
