import { PromiseState } from "./PromiseState"

// This is used by logging, so it can't have any dependencies.

/**
 * Simple one-count concurrent barrier
 */
export class Latch implements Promise<void> {
  // required by lib.es2015:
  readonly [Symbol.toStringTag] = "Latch"
  // Expose `promise` so we can have a honest-to-goodness Promise
  readonly promise: Promise<void>
  private _state: PromiseState = "pending"
  error?: Error
  private _resolve!: () => void
  private _reject!: (err?: Error) => void

  constructor(readonly id?: any) {
    this.promise = new Promise<void>((resolve, reject) => {
      this._resolve = resolve
      this._reject = reject
    })
  }

  // This is used by LogMeta for logging:
  toLogJSON() {
    return {
      _ctor: "Latch",
      id: this.id,
      state: this.state()
    }
  }

  resolve(): Latch {
    if (this.isPending()) {
      this._resolve()
      this._state = "resolved"
    }
    return this
  }

  reject(error?: Error): Latch {
    if (this.isPending()) {
      this._reject(error)
      this._state = "rejected"
      this.error = error
    }
    return this
  }

  observe(p: Promise<any>): this {
    p.then(
      () => this.resolve(),
      err => this.reject(err)
    )
    return this
  }

  observeQuietly(p: Promise<any>): this {
    p.then(
      () => this.resolve(),
      () => this.resolve()
    )
    return this
  }

  isPending(): boolean {
    return this._state === "pending"
  }

  isSettled() {
    return !this.isPending()
  }

  isResolved() {
    return this._state === "resolved"
  }

  isRejected() {
    return this._state === "rejected"
  }

  state(): PromiseState {
    return this._state
  }

  then<T1, T2>(
    onfulfilled?: ((v: void) => T1 | PromiseLike<T1>) | undefined | null,
    onrejected?: ((reason: any) => T2 | PromiseLike<T2>) | undefined | null
  ): Promise<T1 | T2> {
    return this.promise.then(onfulfilled, onrejected)
  }

  catch<TResult = never>(
    onrejected?:
      | ((reason: any) => TResult | PromiseLike<TResult>)
      | undefined
      | null
  ): Promise<void | TResult> {
    return this.promise.catch(onrejected)
  }

  finally(onfinally?: (() => void) | undefined | null): Promise<void> & this {
    void this.promise.finally(onfinally)
    return this
  }
}
