import { basename } from "path"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { isFalse, isTrue } from "../../fe/Boolean"
import { later } from "../../fe/Delay"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { thenOrOnTimeout, thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { FifoCacheAsync } from "../FifoCacheAsync"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { isWorkerService } from "../ServiceNames"
import { stripComments } from "../String"
import { withBoundedConcurrency } from "../async/Promises"
import { ee } from "../event/EventEmitter"
import { splitLines } from "../fs/CRLF"
import { PosixFile } from "../fs/PosixFile"
import { isDocker } from "../platform/isDocker"
import { Settings } from "../settings/Settings"
import { mountpoints } from "./Mountpoints"
import { Volume } from "./Volume"
import { commandTimeoutMs } from "./VolumeTtls"
import { writeVolumeUuid } from "./WriteVolumeUuid"

const logger = lazy(() => mkLogger("volumes.VolumeUUID"))

// This isn't a TTLMap because we don't expect to have bajillions of
// mountpoints, they don't expire, and we force-clear it whenever mountpoints
// change.
const cache = lazy(
  () =>
    new FifoCacheAsync<Maybe<string>>({
      name: "volumes.VolumeUUID",
      maxSize: 500,
      timeoutMs: commandTimeoutMs(),
      writeFsCache: !isWorkerService(),
      readFsCache: true
    })
)

later(() => {
  ee().on("clearCache", () => cache.clear())
  mountpoints.watchLater(() => cache.clear())
})

export async function addVolumeUUIDs(vols: Volume[]): Promise<void> {
  await withBoundedConcurrency({
    name: "addVolumeUUIDs",
    laters: vols.map(
      ea => () =>
        thenOrOnTimeout(addVolumeUUID(ea), commandTimeoutMs(), () =>
          logger().warn("addVolumeUUIDs(" + ea.mountpoint + ") timed out", {
            timeoutMs: commandTimeoutMs()
          })
        )
    )
  })
}

export function volumeUuidNotExpected(v: Volume) {
  return (
    isTrue(v.ignorable) ||
    (isDocker() && v.mountpoint === "/") ||
    (isTest && basename(v.mountpoint) === "node_modules")
  )
}

/**
 * Prefer the UUID stored on the mountpoint/.uuid.
 *
 * If that cannot be read, use the UUID already on the volume.
 *
 * If that is missing/timesout, use the last-set UUID for that volume, stored in
 * APP_DATA/mountpoint-uuids.json
 */
async function addVolumeUUID(v: Volume): Promise<void> {
  if (volumeUuidNotExpected(v)) {
    // Don't bother reading the .uuid.
    return
  }

  if (isFalse(v.ok)) {
    // We don't want to touch this filesystem, it may hang!
    return
  }

  const uuid = await cache().getOrSetAsync(v.mountpoint, async () =>
    readVolumeUUID(v)
  )
  if (uuid != null) v.uuid = uuid
}

export function toVolumeUUID(s: Maybe<string>): Maybe<string> {
  if (blank(s)) return
  const clean = stripComments(s)
    .replace(/[^0-9a-z-]+/gi, "")
    .trim()
    .replace(/^-|-$/g, "")
  // reject 000-00000-00000
  return clean.length <= 8 || /^[0-]*$/i.test(clean) || /^[F-]*$/i.test(clean)
    ? undefined
    : clean
}

export async function readUuidFile_(file: PosixFile) {
  const buf = await thenOrTimeoutError(file.readTextFile_(), commandTimeoutMs())
  // return the first line, stripped of comments, that contains something uuid-esque:
  for (const line of splitLines(buf)) {
    const result = toVolumeUUID(line)
    if (result != null) return result
  }
  throw new Error("No UUID found in " + file)
}

/**
 * If the vol UUID file exists, use that instead of the device UUID.
 */
export async function readVolumeUUID(v: Volume): PromiseMaybe<string> {
  // TODO: if we can't find a .uuid in the mountpoint, should we look at a, then
  // b, then c for a .uuid for `mountpoint/a/b/c/file.jpg`?

  if (Settings.readVolumeUuidFiles.valueOrDefault) {
    for (const subpath of Settings.volumeUuidFilePaths.values) {
      const volUuidFile = PosixFile.for(v.mountpoint).join(subpath)

      const savedUUID = await readUuidFile_(volUuidFile).catch(error =>
        logger().info("Failed to read " + volUuidFile, { error })
      )
      if (savedUUID != null) {
        return logger().tap({
          msg: "Serving UUID from " + subpath,
          result: savedUUID,
          meta: {
            mountpoint: v.mountpoint
          }
        })
      }
    }
  }

  // Don't try to write to the root directory:
  if (v.mountpoint === "/") return v.uuid

  if (Settings.writeVolumeUuidFiles.valueOrDefault) {
    const uuid = await writeVolumeUuid(v)
    if (uuid != null) return uuid
  }

  // Only return the hardware uuid if we couldn't persist the safeUUID for later
  return v.uuid
}
