import { join } from "path"
import { lazy } from "../../core/Lazy"
import { toS } from "../../fe/toS"

// Expect __filename to be something like
// '/home/mrm/src/photostructure/src/core/dist/core/platform/IsPacked.js' if
// we're not web-packed.

export const isPacked = lazy(
  () => !toS(__filename).includes(join("core", "platform", "IsPacked"))
)
