import { compact, uniq } from "../../fe/Array"
import { stringify } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { StrEnum } from "../../fe/StrEnum"
import { BaseSettingOpts } from "./Setting"
import { StringArraySetting, splitStringArray } from "./StringArraySetting"

function parseStringEnum<T extends string>(
  str: Maybe<string>,
  strEnum: StrEnum<T>
): Maybe<T[]> {
  return compact(splitStringArray(str)?.map(ea => strEnum.getCI(ea)))
}

export class StringEnumsSetting<T extends string> extends StringArraySetting {
  readonly strEnum: StrEnum<T>
  constructor(
    opts: BaseSettingOpts<string[]> & {
      defaultValue?: T[]
      strEnum: StrEnum<T>
    }
  ) {
    super({
      toEnv: arr => map(arr, ea => stringify(uniq(ea))),
      fromEnv: s => parseStringEnum(s, opts.strEnum),
      defaultValue: opts.defaultValue ?? opts.strEnum.values,
      ...opts
    })
    this.strEnum = opts.strEnum
  }

  protected override toValidValue(s: string): Maybe<T> {
    return this.strEnum.getCI(s)
  }

  override addToJSON() {
    return { "valid values": this.strEnum.values }
  }
}
