import { Maybe } from "../fe/MaybeTypes"
import { time } from "./async/PromiseTimer"

// TODO: add a cache to Shim0

export interface Shim0<R> {
  (): Promise<R>
  setShim(f: Maybe<() => Promise<R>>): void
  hasShim(): boolean
}

export function shim0<R>({
  impl,
  name
}: {
  impl: () => Promise<R>
  name: string
}): Shim0<R> {
  let shim: undefined | (() => Promise<R>)

  const f: Shim0<R> = () =>
    time(
      name + (shim == null ? "(local)" : "(remote)"),
      shim != null ? shim() : impl()
    )

  f.setShim = (i: Maybe<() => Promise<R>>) => {
    shim = i
  }

  f.hasShim = () => shim != null

  return f
}
