import { toS } from "../fe/toS"

export function camel2snake(s: string): string {
  return toS(s)
    .replace(/([A-Z])([a-z])/g, (_, a, b) => "_" + a.toLowerCase() + b)
    .replace(/[A-Z]+|[0-9]+/g, ea => "_" + ea)
    .replace(/^_/, "")
    .replace(/_+/g, "_")
}

export function camel2words(s: string): string {
  return toS(s)
    .replace(/([A-Z])([a-z])/g, (_, a, b) => " " + a.toLowerCase() + b)
    .replace(/[A-Z]+|[0-9]+/g, ea => " " + ea)
    .trim()
}

export function snake2camel(s: string): string {
  return toS(s).replace(/_([A-Z])/gi, (_, a) => a.toUpperCase())
}

export function asObjectKey(s: string) {
  return snake2camel(
    s.replace(/^[A-Z]+/, ea => ea.toLowerCase()).replace(/[^a-z]+/gi, "_")
  )
}
