import _os from "os"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { Maybe } from "../../fe/MaybeTypes"
import { stripDiacritics } from "../String"
import { Settings } from "../settings/Settings"

export const hostname = lazy(() => Settings.hostname.value ?? _os.hostname())

later(() => Settings.hostname.watchLater(() => hostname.unset()))

export function cleanHostname(_hostname?: Maybe<string>) {
  if (blank(_hostname)) {
    if (!blank(Settings.hostname.value)) return Settings.hostname.value
  }
  return (_hostname ?? _os.hostname())
    .replace(/\.local$/, "")
    .replace(/-?\d+$/, "")
}

export function fsSafeHostname(_hostname = hostname()) {
  return stripDiacritics(_hostname)
    .replace(/[^a-z0-9-\s]/gi, "")
    .trim()
    .replace(/\s+/g, "-")
    .toLowerCase()
}
