import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { getEnv } from "../env/GetEnv"
import { mkdirpSync_ } from "../fs/Path"

export function envConfigDir(): Maybe<string> {
  // not "configDir", because that could be something else:
  const fromEnv = getEnv("PS_CONFIG_DIR")
  if (!blank(fromEnv)) {
    try {
      mkdirpSync_(fromEnv)
      return fromEnv
    } catch (cause) {
      console.error(
        "The environment variable PS_CONFIG_DIR is set to " +
          fromEnv +
          ", but mkdir failed.",
        cause
      )
    }
  }
  return
}
