const he = require("he")

export function rightPad(s: any, minLength: number, padChar: string): string {
  if (padChar.length === 0) throw new Error("rightPad() given empty pad")
  const str = String(s)
  return str + padding(padChar, minLength - str.length)
}

export function padReplace(
  s: string,
  fromIdx: number,
  length: number,
  padChar: string
) {
  return (
    strslice(s, 0, fromIdx) +
    padding(padChar, length) +
    strslice(s, fromIdx + length)
  )
}

export function contains(
  haystack: string,
  needle: string,
  fromPosition?: number
): boolean {
  return toS(haystack).indexOf(toS(needle), fromPosition) > -1
}

export function countChars(
  haystack: string,
  needle: string,
  fromPosition = 0
): number {
  if (needle == null || needle.length === 0) return 0
  // NOTE: this assumes .indexOf is going to be quick native code, and
  // recursion will be faster than a simple for loop over ths string
  // characters.
  const idx = haystack.indexOf(needle, fromPosition)
  return idx === -1 ? 0 : 1 + countChars(haystack, needle, idx + needle.length)
}

export function maybeToS(a?: any): Maybe<string> {
  return a == null ? undefined : String(a)
}

export function trim(arr: any[]): string[] {
  return arr.map(toS).filter(ea => notBlank(ea))
}

export function splitEvery(s: string, n: number, maxSplits?: number): string[] {
  const sliceTimes =
    Math.min(Math.ceil(s.length / n), maxSplits ?? s.length) - 1
  if (sliceTimes <= 0) return [s]
  return [
    ...times(sliceTimes, i => s.slice(i * n, (i + 1) * n)),
    s.slice(sliceTimes * n)
  ]
}

/**
 * Removes the first capture group defined by `re` found in `s`.
 */
export function spliceCapture(
  s: string,
  re: RegExp
): Maybe<{
  captured: string
  uncaptured: string
  unmatched: string
  matchedIndex: number
}> {
  const m = re.exec(s)
  if (m == null || m[1] == null) return
  const matchedIndex = m[0].indexOf(m[1]) + m.index
  return {
    captured: m[1],
    uncaptured:
      s.substring(0, matchedIndex) + s.substring(matchedIndex + m[1].length),
    unmatched: s.substring(0, m.index) + s.substring(m.index + m[0].length),
    matchedIndex
  }
}

export function stripPrefixIgnoreCase(s: string, prefix: string): string {
  s = toS(s)
  prefix = toS(prefix)
  return startsWithIgnoreCase(s, prefix) ? s.slice(prefix.length) : s
}

export function stripSuffixIgnoreCase(
  s: string,
  suffix: string,
  opts: { normalize?: boolean } = {}
): string {
  if (suffix == null) return s
  const str = toS(s)
  const sfx = toS(suffix)

  return sfx.length > 0 && equalsIgnoreCase(str.slice(-sfx.length), sfx, opts)
    ? str.slice(0, -sfx.length)
    : str
}

export function gist(a: string, maxPre = 80, maxPost = 80): string {
  const s = toS(a)
  const charsOmitted = s.length - (maxPre + maxPost)
  return charsOmitted <= 0
    ? s
    : s.slice(0, maxPre).trim() +
        " (+" +
        charsOmitted +
        " chars)" +
        s.slice(-maxPost).trim()
}

export function isLowerCase(s: Maybe<string>): boolean {
  return toS(s).toLocaleLowerCase() === s
}

export function isUpperCase(s: Maybe<string>): boolean {
  return toS(s).toLocaleUpperCase() === s
}
const CapRE = /^[A-Z]/

/**
 * Is the first character in `s` `[A-Z]`?
 */
export function isFirstCharAZ(s: Maybe<string>): boolean {
  return CapRE.test(toS(s))
}

export function capitalize(s: Maybe<string>): string {
  s = toS(s)
  return blank(s) ? s : strslice(s, 0, 1).toLocaleUpperCase() + strslice(s, 1)
}

export function compareIgnoreCase(a: string, b: string) {
  const result = a.localeCompare(b, undefined, { sensitivity: "base" })
  return result === 0
    ? // for determinism:
      a.localeCompare(b)
    : result
}

export function equalsIgnoreCase(
  a: MaybeNull<string | Buffer>,
  b: MaybeNull<string | Buffer>,
  { normalize = true }: { normalize?: boolean } = {}
): boolean {
  if (a == null || b == null) return false
  const as = toS(a)
  const bs = toS(b)
  if (as.length !== bs.length) return false
  if (as === bs) return true
  // cheap, but false is sometimes wrong (like in Turkish):
  if (as.toLowerCase() === bs.toLowerCase()) return true
  return (
    (normalize ? as.normalize() : as).localeCompare(
      normalize ? bs.normalize() : bs,
      undefined,
      { sensitivity: "base" }
    ) === 0
  )
}

export function uniqIgnoreCase(arr: string[]): string[] {
  return uniqBy2(arr, equalsIgnoreCase)
}

export function sortIgnoreCase(arr: Iterable<string>): string[] {
  return toA(arr).sort(compareIgnoreCase)
}

export function sortByCaseInsensitive<T>(
  arr: Iterable<T> | T[],
  f: (t: T, index: number) => Maybe<string>
): T[] {
  return toA(arr)
    .filter(ea => ea != null)
    .map((item, idx) => ({
      item,
      cmp: map(f(item, idx), ea => [ea, idx]) as [string, number]
    }))
    .filter(ea => ea.cmp != null)
    .sort((a, b) => {
      const ea = compareIgnoreCase(a.cmp[0], b.cmp[0])
      return ea !== 0 ? ea : cmp(a.cmp[1], b.cmp[1])
    })
    .map(ea => ea.item)
}

/** TODO DELETE IF UNUSED
 * @return true iff `haystack` starts with `needle`
 */
export function startsWithIgnoreCase(
  haystack: string,
  needle: string
): boolean {
  return haystack == null ||
    needle == null ||
    needle.length === 0 ||
    haystack.length === 0
    ? false
    : equalsIgnoreCase(haystack.substring(0, needle.length), needle)
}

/**
 * @return the first item in `haystack` that is a case-insensitive substring of
 * `needle`.
 */
export function firstSubstringIgnoreCase(
  haystack: string[],
  needle: string
): Maybe<{ index: number; match: string }> {
  if (isEmpty(haystack) || blank(needle)) return
  for (const ea of haystack) {
    if (equalsIgnoreCase(ea, needle)) return { index: 0, match: ea }
  }
  for (const ea of haystack) {
    const index = needle.indexOf(ea)
    if (index >= 0) return { index, match: ea }
  }
  const nn = needle.normalize()
  for (const ea of haystack) {
    {
      const index = nn.indexOf(ea)
      if (index >= 0) return { index, match: ea }
    }
    const hn = ea.normalize()
    {
      const index = nn.indexOf(hn)
      if (index >= 0) return { index, match: hn }
    }
    {
      const nl = nn.toLowerCase()
      const hl = hn.toLowerCase()
      const index = nl.indexOf(hl)
      if (index >= 0) return { index, match: hl }
    }
  }
  return
}

/**
 * Is any item in `haystack` included in `needle`?
 */
export function includesIgnoreCase(
  haystack: string[],
  needle: string
): boolean {
  return isEmpty(haystack) || blank(needle)
    ? false
    : haystack.some(ea => equalsIgnoreCase(needle, ea))
}

export function reverse(s: string): string {
  return s == null ? s : [...s].reverse().join("")
}

export type Stringable = { valueOf(): string }

/**
 * Return the longest-matching prefix pattern from `patterns` that needle
 * startsWith.
 */
export function longestPrefix(
  needle: string,
  patterns: string[]
): Maybe<string> {
  return greatestBy(
    patterns.filter(ea => needle.startsWith(ea)),
    ea => ea.length
  )
}

// \u001b[90m
export function stripAnsiEsc(s: string): string {
  // DON'T REPLACE WITH https://github.com/chalk/ansi-regex/blob/main/index.js :: that shite doesn't work.

  return toS(s).replace(/\u001b\[[0-9;]+[a-z]/gi, "")
}

const Smart2Dumb: [RegExp, string][] = [
  [/[]/g, `'`],
  [/[]/g, `"`]
]

export function dumbquote(s: string): string {
  return Smart2Dumb.reduce(
    (acc, [smart, dumb]) => acc.replace(smart, dumb),
    s
  ).normalize()
}

const quoted = /^(['"]).+\1$/

export function stripQuotes(s: string): string {
  if (blank(s)) return s
  // Some Vivitar cameras have numeric models (!!) so we need to toS().
  s = toS(s).trim()
  if (quoted.exec(dumbquote(s)) != null) {
    s = s.slice(1, -1).trim()
  }
  return s
}

export function wbrPath(s: string): string {
  return s
    .split(/(?<=[\\/_,:=-]+)/)
    .map(ea => he.escape(ea.normalize()) as string)
    .join("<wbr>")
}

export function zipStrings(...arr: string[]): string {
  let s = ""
  const cb = compactBlanks(arr)
  const maxLen = Math.max(...cb.map(ea => ea.length))
  for (let j = 0; j < maxLen; j++) {
    for (let i = 0; i < cb.length; i++) {
      map(cb[i], str => map(str[j], ea => (s += ea)))
    }
  }
  return s
}

export function splitKeep(s: string, re: RegExp): string[] {
  const result = splitUp(s, re)
  return flatZip(result.nonSeparators, result.separators).filter(
    ea => ea != null && ea.length > 0
  ) as string[]
}

/**
 * @return (nonSeparator + separator)*
 */
export function splitUp(
  s: string,
  re: RegExp
): { nonSeparators: string[]; separators: string[] } {
  if (!re.global)
    throw new Error("bad regex (missing global flag)" + NonRetriableErrorFlag)
  const nonSeparators: string[] = []
  const separators: string[] = []
  let pos = 0
  let m: RegExpExecArray | null
  while ((m = re.exec(s)) != null) {
    // This is necessary to avoid infinite loops with zero-width matches
    if (m.index === re.lastIndex) {
      re.lastIndex++
    } else {
      re.lastIndex = m[0].length + m.index
      nonSeparators.push(s.substring(pos, m.index))
      separators.push(s.substring(m.index, re.lastIndex))
      pos = re.lastIndex
    }
  }
  if (pos < s.length) {
    nonSeparators.push(s.substring(pos))
  }
  return { nonSeparators, separators }
}

export function sortNaturalBy(line: string) {
  return splitKeep(toS(line), /\d+(?:\.\d+)?/g).map(
    ea => toFloat(ea.trim()) ?? ea
  )
}

export function sortNatural(lines: string[]): string[] {
  return sortBy(lines, line => sortNaturalBy(line))
}

export function stripDiacritics(s: string): string {
  // see https://stackoverflow.com/a/37511463/1268016
  return toS(s)
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
}

export function stripEmoji(s: string): string {
  // see https://stackoverflow.com/a/38987183/1268016
  return toS(s).replace(
    /\ud83c[\udf00-\udfff]|\ud83d[\udc00-\ude4f]|\ud83d[\ude80-\udeff]/g,
    ""
  )
}

/**
 * Remove strings that are prefixes to longer strings in `arr`, ignoring case
 * differences
 * @example (["abc", "bcd", "a", "ab", "b"]) -> ["abc", "bcd"]
 */
export function uniqSubstr(arr: string[]): string[] {
  const rev = sortIgnoreCase(compactBlanks(arr))
  const result = rev.filter(
    (ea, idx) => !startsWithIgnoreCase(rev[idx + 1], ea)
  )
  return sortBy(result, ea => arr.indexOf(ea))
}

export function commonPrefix(a: Maybe<string>, b: Maybe<string>) {
  if (blank(a) || blank(b)) return ""
  const max = Math.min(a.length, b.length)
  for (let i = 0; i < max; i++) {
    if (a[i] !== b[i]) {
      return a.slice(0, i)
    }
  }
  return a.slice(0, max)
}

export function commonPrefixIgnoreCase(
  a: Maybe<string>,
  b: Maybe<string>,
  opts: { normalize?: boolean } = {}
) {
  if (blank(a) || blank(b)) return ""
  const max = Math.min(a.length, b.length)
  for (let i = 0; i < max; i++) {
    if (!equalsIgnoreCase(a[i], b[i], opts)) {
      return a.slice(0, i)
    }
  }
  return a.slice(0, max)
}

export function commonPrefixLengthIgnoreCase(
  a: Maybe<string>,
  b: Maybe<string>,
  opts: { normalize?: boolean } = {}
): number {
  if (blank(a) || blank(b)) return 0
  const max = Math.min(a.length, b.length)
  for (let i = 0; i < max; i++) {
    if (!equalsIgnoreCase(a[i], b[i], opts)) {
      return i
    }
  }
  return max
}

export function indexOfNonSpace(s: string): Maybe<number> {
  return s.match(/\S/)?.index
}

export function lastIndexOfNonSpace(s: string): Maybe<number> {
  return s.match(/\S\s*$/)?.index
}

export function trimLeftPadding(s: string): string {
  return s.replace(/^[-0_\s]+/, "")
}

/**
 * Convert "hello\040world" to "hello world"
 */
export function unoctal(s: string): string {
  const o = splitUp(s, /\\\d\d\d/g)

  return flatZip(
    o.nonSeparators,
    o.separators.map(ea =>
      ea?.length === 4
        ? mapNumeric(parseInt(ea.slice(1), 8), String.fromCharCode)
        : undefined
    )
  )
    .map(toS)
    .join("")
}

const CommentRE = /#.*$/gm

export function stripComments(s: string): string {
  return s.replace(CommentRE, "")
}

export function uniqPath(s: MaybeNull<string>[], sep = ":"): string {
  // This impl may be easier to follow than 2 compactBlanks() and a uniq()
  const set = new Set<string>()
  for (const ea of toA(s)) {
    if (ea != null && !blank(ea)) {
      for (const ea2 of ea.split(sep)) {
        if (!blank(ea2)) set.add(ea2)
      }
    }
  }
  return [...set].join(sep)
}

/**
 * First needle wins, all other needles are replaced with `replacement`. Used
 * to make error messages a bit shorter.
 */
export function dedupeNeedle(
  arr: string[],
  needle: string,
  replacement: string
) {
  if (blank(needle)) return arr
  const re = new RegExp(
    // `(?<quot>['"]?)` + escapeRegExp(needle) + `$<quot>`,
    `(['"]?)` + escapeRegExp(needle) + `\\1`,
    "mg"
  )
  const idx = arr.findIndex(ea => re.test(ea))
  const dedupeRe = new RegExp(
    "(" + escapeRegExp(replacement) + "[,:\\s]*){2,}",
    "gmi"
  )
  const trimRe = /(^\s+)|(\s+$)/gm
  if (idx < 0) return arr
  for (let i = idx + 1; i < arr.length; i++) {
    arr[i] = arr[i]
      .replace(re, replacement)
      .replace(/(\s)\s+/gm, " ")
      // de-stutter:
      .replace(dedupeRe, replacement)
      .replace(trimRe, "")
  }
  return arr
}

export function splitUniq(arr: Maybe<string>[], delim: string): string[] {
  return uniq(
    compactBlanks(
      arr
        .join(delim)
        .split(delim)
        .map(ea => ea.trim())
    )
  )
}
