import { fmtHMS } from "./Date"
import { Maybe } from "./MaybeTypes"
import { maybeCall } from "./Object"
import { SyncOrAsync } from "./SyncOrAsync"
import { ThunkOrT, tot } from "./Thunk"
import { TimeoutError } from "./TimeoutError"

export const Timeout = Symbol("timeout")

export function toNotTimeout<T>(p: T | typeof Timeout): Maybe<T> {
  return p === Timeout ? undefined : p
}

export async function thenOrTimeout<T>(
  p: SyncOrAsync<T>,
  timeoutMs: number,
  unref = true
): Promise<T | typeof Timeout> {
  timeoutMs = Math.round(timeoutMs ?? 0)
  return timeoutMs <= 0
    ? p
    : new Promise<T | typeof Timeout>(async (resolve, reject) => {
        let pending = true
        const t = setTimeout(() => {
          if (pending) {
            pending = false
            resolve(Timeout)
          }
        }, timeoutMs)
        try {
          if (unref) maybeCall(t, "unref") // timeouts don't have an unref method on fe.
          const result = await p
          if (pending) {
            pending = false
            resolve(result)
          }
        } catch (err) {
          if (pending) {
            pending = false
            reject(err)
          }
        } finally {
          clearTimeout(t)
        }
      })
}

export async function thenOrTimeoutAt<T>(
  p: SyncOrAsync<T>,
  timeoutAtTs: number,
  unref = true
): Promise<T | typeof Timeout> {
  const timeoutMs = timeoutAtTs - Date.now()
  return timeoutMs <= 0 ? Timeout : thenOrTimeout(p, timeoutMs, unref)
}

export async function thenOrOnTimeout<T, U>(
  p: SyncOrAsync<T>,
  timeoutMs: number,
  onTimeout: () => SyncOrAsync<U>
): Promise<T | U> {
  return new Promise<T | U>(async (resolve, reject) => {
    let pending = true
    try {
      const t = setTimeout(async () => {
        if (pending) {
          pending = false
          try {
            resolve(await onTimeout())
          } catch (err) {
            reject(err)
          }
        }
      }, timeoutMs)
      maybeCall(t, "unref") // timeouts don't have an unref method on fe.
      const result = await p
      if (pending) {
        pending = false
        clearTimeout(t)
        resolve(result)
      }
    } catch (err) {
      if (pending) {
        pending = false
        reject(err)
      }
    }
  })
}

/**
 * @param timeoutMs disable timeouts if <= 0
 */
export async function thenOrTimeoutError<T>(
  p: SyncOrAsync<T>,
  timeoutMs: number,
  unref = true
): Promise<T> {
  const result = await thenOrTimeout(p, timeoutMs, unref)
  if (result === Timeout) {
    throw new TimeoutError("timeout (" + fmtHMS(timeoutMs) + ")")
  } else {
    return result as T
  }
}

export async function thenOrTimeoutAtError<T>(
  p: SyncOrAsync<T>,
  timeoutAtTs: number,
  unref = true
): Promise<T> {
  const timeoutMs = timeoutAtTs - Date.now()
  if (timeoutMs <= 0) {
    throw new TimeoutError("timeout (" + fmtHMS(timeoutMs) + ")")
  }
  return thenOrTimeoutError(p, timeoutMs, unref)
}

export async function thenOrTimeoutAs<T>({
  f,
  timeoutMs,
  as,
  unref = true
}: {
  f: SyncOrAsync<T>
  timeoutMs: number
  as: ThunkOrT<T>
  unref?: boolean
}): Promise<T> {
  const result = await thenOrTimeout(f, timeoutMs, unref)
  return result === Timeout ? tot(as) : result
}
