import { lazy } from "../core/Lazy"
import { compact } from "../fe/Array"
import { blank } from "../fe/Blank"
import { secondMs } from "../fe/Date"
import { Maybe, PromiseMaybe } from "../fe/MaybeTypes"
import { Obj } from "../fe/Object"
import { parseEnvTokens } from "./EnvTokens"
import { mkLogger } from "./Logger"
import { equalsIgnoreCase } from "./String"
import { lazyAsync } from "./async/LazyAsync"
import { thenMap } from "./async/Promise"
import { StdoutOpts, stdout_ } from "./child/ChildProcess"
import { env } from "./env/Env"
import { isMac, isWin } from "./platform/Platform"
import { PowerShell } from "./pwsh/PowerShell"
import { commandTimeoutMs } from "./volumes/VolumeTtls"

export const DefaultLocale = "en"

const logger = lazy(() => mkLogger("Locale"))

export function localeSync() {
  return locale.lastValue() ?? envLocale() ?? DefaultLocale
}

/**
 * @see https://en.wikipedia.org/wiki/ISO_639
 */
export const locale = lazyAsync<string>({
  desc: "locale",
  later: async () =>
    lc2locale(
      extractLocale() ??
        (await (isWin
          ? localeWin()
          : isMac
          ? localeMac()
          : localePosix()
        ).catch(error => {
          logger().warn("Failed to get locale", { error })
          return DefaultLocale
        }))
    ),
  timeoutMs: commandTimeoutMs()
})

export const envLocale = lazy(extractLocale)

// See https://wiki.archlinux.org/index.php/Locale
function extractLocale(e: Obj = env()) {
  for (const ea of [e.LC_ALL, e.LC_MESSAGES, e.LANG, e.LANGUAGE]) {
    const lc = toLocale(ea)
    if (lc != null) return lc
  }
  return
}

// Like "en_US.UTF-8"
const regex = /^(?<lang>[a-z]{2,3})(?:[_-](?<country>[a-z]{2,3}))?\b/i

function toLocale(str: Maybe<string>) {
  if (blank(str)) return
  // Some people set their locale to "C":
  if (
    str === DefaultLocale ||
    equalsIgnoreCase("c", str) ||
    equalsIgnoreCase("posix", str)
  ) {
    return DefaultLocale
  }
  const g = regex.exec(str.trim())?.groups
  return g == null ? undefined : compact([g.lang, g.country]).join("-")
}

export function lc2locale(lc: Maybe<string>): string {
  return toLocale(lc) ?? DefaultLocale
}

export function localeWin(): PromiseMaybe<string> {
  return thenMap(
    PowerShell.instance().executeJson(
      "Get-WinSystemLocale | Select-Object -Property Name"
    ),
    ea => ea.Name as string
  )
}

const opts: StdoutOpts = {
  timeoutMs: 10 * secondMs
}

export async function localeMac(): PromiseMaybe<string> {
  return toLocale(
    await stdout_("defaults", ["read", "-globalDomain", "AppleLocale"], opts)
  )
}

export async function localePosix() {
  return lc2locale(
    extractLocale(
      parseEnvTokens({
        lowerCaseKeys: false,
        input: await stdout_("locale", [], opts)
      })
    )
  )
}

export function childProcLocale() {
  return {
    LANG: "C",
    LC_ALL: "C"
  }
}
