import {
  BatchCluster,
  BatchClusterEmitter,
  BatchClusterOptions
} from "batch-cluster"
import { ExifTool } from "exiftool-vendored"
import process from "process"
import { lazy } from "../core/Lazy"
import { minuteMs, secondMs } from "../fe/Date"
import { map } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { gt0 } from "../fe/Number"
import { TimeoutError } from "../fe/TimeoutError"
import { toS } from "../fe/toS"
import { mkLogger } from "./Logger"
import { Pids, addPid } from "./Pids"
import { renice } from "./Renice"
import { ShortCommandTimeoutMs } from "./ShortCommandTimeoutMs"
import { EndableRank, EndableRanks, ending } from "./async/Endable"
import { EndableWrapper } from "./async/EndableWrapper"
import { onTimeout } from "./async/TimeoutHandler"
import { errorToS } from "./error/Error"
import { FatalErrorFlag } from "./error/ErrorFlags"
import { isIgnorableError } from "./error/ErrorTypes"
import { onError } from "./error/OnError"
import { isWin } from "./platform/Platform"
import { Settings } from "./settings/Settings"
import { commandTimeoutMs } from "./volumes/VolumeTtls"

// TODO: move to ./proc/? ./bc/?
/**
 * Rather than extend ExifTool or BatchCluster, just facade it to be an
 * Endable
 */
export class BatchClusterObserver<
  T extends ExifTool | BatchCluster
> extends EndableWrapper {
  lastStartError: Maybe<Error>
  lastInternalError: Maybe<Error>
  lastTaskError: Maybe<Error>

  constructor(
    name: string,
    readonly t: T,
    rank: EndableRank = EndableRanks.service
  ) {
    super(
      `proc.BatchClusterObserver(${name})`,
      () => this.t.end(),
      rank,
      name === "worker" ? commandTimeoutMs() : ShortCommandTimeoutMs
    )
    // TODO: TYPESCRIPT SITS: this cast shouldn't be necessary
    const bce: Pick<BatchClusterEmitter, "on"> = t
    bce.on("childStart", async bp => {
      if (bp.pid == null) {
        this.onError("No PID returned for " + name, new TimeoutError())
        return
      }
      this.logger.info("Started child process", { pid: bp.pid })
      renice(bp.pid).catch(err => this.onError("renice failed", err))
      addPid(
        {
          pid: bp.pid,
          ppid: process.pid,
          cmd: name,
          maxAgeMs: t.options.maxProcAgeMillis + minuteMs
        },
        new Date()
      )?.catch(err => this.onError("addPid failed for " + name, err))
    })
    bce.on("startError", error => {
      this.lastStartError = error
      this.onError("failed to start", error)
    })
    bce.on("taskError", (error, task) => {
      if (errorToS(error).match(/timeout/) != null) onTimeout()
      this.lastTaskError = error
      this.onError("failed to run " + map(task, ea => ea.command), error)
    })
    bce.on("fatalError", error => {
      this.onError("on(fatalError)" + FatalErrorFlag, error)
    })
    bce.on("internalError", error => {
      this.lastInternalError = error
      this.onError("internal error", error)
    })
    bce.on("noTaskData", (stdout, stderr, proc) => {
      // This is probably due to streamFlushMillis being too aggressive.
      const prior = t.options.streamFlushMillis
      // Bump up the streamFlushMillis if it's not "too big" already, to try to
      // avoid this in the future:
      if (prior < secondMs) {
        t.options.streamFlushMillis += 100
        this.logger.warn("on(noTaskData): bumping up streamFlushMillis.", {
          prior,
          new: t.options.streamFlushMillis,
          stout: toS(stdout),
          stderr: toS(stderr),
          child_pid: proc?.pid
        })
      }
    })
    bce.on("endError", err => {
      this.logger.error("observeBatchCluster.endError()", err)
    })
    bce.on("childEnd", cp => {
      if (gt0(cp?.pid)) {
        this.logger.info("on(childExit)", cp.pid)
        void Pids.instance()?.onKill(cp.pid)
      }
    })
  }

  onError(reason: string, error: Error): void {
    // Avoid stack overflow on end (where end error asks services to shut down,
    // which causes an end error, which ...)
    if (!this.t.ended && !ending() && isIgnorableError(error) === false) {
      onError(this.name + ": " + reason, error)
    } else {
      this.logger.warn("onError() (ending or ignorable): " + reason, error)
    }
  }
}

export function batchClusterOptions(
  maxProcs: number,
  loggerName: string
): Partial<BatchClusterOptions> {
  return {
    maxProcs,
    // Shut down idle procs after a minute or two (longer on Windows because
    // forking is so expensive)
    maxIdleMsPerProcess: (isWin ? 3 : 1) * minuteMs,
    maxTasksPerProcess: Settings.maxTasksPerProcess.valueOrDefault,
    // it shouldn't take longer than a second for node to spin up, but
    // antivirus and slow external disks can cause huge latency:
    spawnTimeoutMillis: commandTimeoutMs(),
    // no fork-bombs
    minDelayBetweenSpawnMillis: Settings.minDelayBetweenSpawnMs.valueOrDefault,
    streamFlushMillis: Settings.streamFlushMs.valueOrDefault,
    // We'll take care of it with the pid reaper:
    cleanupChildProcs: false,
    logger: lazy(() => mkLogger(loggerName))
  }
}
