import { rm } from "fs/promises"
import { lazy } from "../../core/Lazy"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { isProd, isTest } from "../NodeEnv"
import { ensurePrefix, splitEvery } from "../String"
import { shortFsStringSha } from "../StringHash"
import { ciSafePath } from "../ciSafePath"
import { cacheDir_ } from "../dir/CacheDir"
import { getDevEnvFlag } from "../env/DevEnvFlags"
import { ee } from "../event/EventEmitter"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFile, basename } from "../fs/SimpleFile"
import { TokenRadix } from "../math/Radix"
import { commandTimeoutMs } from "../volumes/VolumeTtls"

const logger = lazy(() => mkLogger("img.ImgCache"))

export const ImageCacheName = "image-cache"
// NOTE: sync service is in charge of cleaning cacheDir. See `setup()`.

/**
 * @throws if imgCacheDir cannot be created.
 */
export const imageCacheDir_ = lazy(() => {
  watchCacheDir()
  return PosixFile.for(cacheDir_()).join(ImageCacheName).mkdirp_()
})

const watchCacheDir = lazy(() => {
  cacheDir_.watchLater(() => imageCacheDir_.unset())
})

export async function rmImageCacheDir() {
  if (isProd) throw new Error("rmImgCacheDir is for tests")
  for (const ea of toA(await (await imageCacheDir_()).clear().children())) {
    await rm(ea.nativePath, {
      recursive: ea.isDirectorySync(),
      maxRetries: 32,
      retryDelay: 250,
      force: true
    })
  }
  ee().emit("fileChanged")
  ee().emit("clearCache")
}

function imgCacheSubdir(src: PosixFile): string[] {
  // Ideally, we'd use the file SHA, but that's expensive, so use a less stable but cheaper hash that includes filesize and mtime:
  const s = src.statSync({ refresh: true })

  // We're SHA'ing the JSON to avoid name collisions with files that end with
  // a file size (! highly unlikely)
  const sha = shortFsStringSha(
    JSON.stringify({
      // we have to use baseWithGrandparent for tests (as SharpReadable.spec
      // uses these paths!)
      np: isTest ? ciSafePath(src) : src.nativePath,
      size: s?.size,
      mtime: isTest ? 0 : s?.mtime // < make tests deterministic
    }),
    24
  )

  // Truncated to 24 to hopefully avoid max path issues on windows: (linux
  // kernel has to truncate to 15 chars (64 bits) to avoid collisions, so 24
  // at base32 (120 bits) should be fine). we could probably get away with
  // dropping all the files directly into the imgtmp dir because they have to
  // be cleaned up, but having 2-deep directories (1024 * 1024) should cover
  // at least an hour of processing:

  return splitEvery(sha.slice(0, 24), 2, 3)
}

/**
 * Random tempfile for easy-to-produce image caching.
 *
 * DON'T USE FOR RAW! use cachedImgFile_() instead!
 */
export async function tmpImageFile_(args: {
  src: SimpleFile
  tag: string
  ext: string
}): Promise<PosixFile> {
  const dir = await imageCacheDir_()
  const result = getDevEnvFlag("PS_NO_RANDOM_IMAGE_CACHE")
    ? // path needs to be predictable for tests
      dir.join(
        "tmp",
        basename(args.src.dir).normalize(),
        args.src.name.normalize(),
        args.tag + args.ext
      )
    : dir.join(
        "tmp-" + TokenRadix.randomChars(2),
        TokenRadix.randomChars(16) + toS(args.ext)
      )
  await result.parent().mkdirp_()
  return result
}

/**
 * @throws on error
 */
export async function cachedImageFile_(
  src: PosixFile,
  desc?: string,
  ext?: string
): Promise<PosixFile> {
  const dir = await imageCacheDir_()

  desc ??= src.name
  ext = ensurePrefix(ext ?? src.ext, ".")

  const parent = dir.join(...imgCacheSubdir(src))
  await parent.mkdirp_()
  // make sure we don't prematurely delete the parent cache dir.

  // note: we don't need to touch the whole directory ancestry: we just need to
  // touch the parent (grandparents will then not be empty)

  // we're using utimes() here instead of touch() to avoid the extra call to
  // ensureExists():
  await parent.utimes()

  const result = parent.join(desc + ext)

  logger().debug("cachedImgFile(" + src.baseWithGrandparent + ")", {
    desc,
    ext,
    result
  })
  return result
}

/**
 * Only applies the readable thunk if the tmpfile doesn't already exist.
 *
 * @throws if f() has issues generating a readable, or the readable has errors
 * during stream.
 */
export async function readableToFile_({
  src,
  desc,
  suffix,
  f
}: {
  src: PosixFile
  desc: string
  suffix: string
  f: (destination: PosixFile) => Promise<void>
}): PromiseMaybe<PosixFile> {
  const dest = await cachedImageFile_(src, desc, suffix)
  return dest.applyIfEmpty_({ fn_: f })
}

/** Only applies the thunk if the tmpfile doesn't already exist */
export async function withImageCache_(
  src: PosixFile,
  desc: string,
  ext: string,
  applyIfEmpty_: (dest: PosixFile) => Promise<any>
): Promise<PosixFile> {
  try {
    const dest = await cachedImageFile_(src, desc, ext)
    await dest.applyIfEmpty_({
      fn_: applyIfEmpty_,
      skipFsLock: false,
      skipWip: false,
      timeoutMs: commandTimeoutMs(),
      minSizeBytes: 256,
      dirty: true // It's ok to leave the fslock directories. They'll get cleaned up later. Speedy finishes SharpReadable.spec in 40s with dirty, 55s with !dirty.
    })
    return dest
  } catch (error) {
    return logger().throw("withImgCache_() failed", {
      error,
      src: src.nativePath,
      desc,
      ext
    })
  }
}
