import { isBoolean } from "./Boolean"

const type_detect = require("type-detect")

export interface MemoizeMap extends WeakMap<object, WeakMap<object, boolean>> {}

export interface DeepEqlOptions<T1 = unknown, T2 = unknown> {
  /**
   * Override default algorithm, determining custom equality.
   */
  comparator?: (
    leftHandOperand: T1,
    rightHandOperand: T2,
    keyname?: string | symbol
  ) => boolean | null

  /**
   * Provide a custom memoization object which will cache the results of
   * complex objects for a speed boost.
   *
   * By passing `false` you can disable memoization, but this will cause circular
   * references to blow the stack.
   */
  memoize?: MemoizeMap | false
}

/*!
 * Check to see if the MemoizeMap has recorded a result of the two operands
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {MemoizeMap} memoizeMap
 * @returns {Boolean|null} result
 */
function memoizeCompare(
  leftHandOperand: any,
  rightHandOperand: any,
  memoizeMap?: MemoizeMap
): boolean | null {
  // Technically, WeakMap keys can *only* be objects, not primitives.
  if (
    memoizeMap == null ||
    isPrimitive(leftHandOperand) ||
    isPrimitive(rightHandOperand)
  ) {
    return null
  }
  const result = memoizeMap.get(leftHandOperand)?.get(rightHandOperand)
  if (typeof result === "boolean") {
    return result
  }
  return null
}

/*!
 * Set the result of the equality into the MemoizeMap
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {MemoizeMap} memoizeMap
 * @param {Boolean} result
 */
function memoizeSet(
  leftHandOperand: any,
  rightHandOperand: any,
  memoizeMap: MemoizeMap | undefined,
  result: boolean
) {
  // Technically, WeakMap keys can *only* be objects, not primitives.
  if (
    memoizeMap == null ||
    isPrimitive(leftHandOperand) ||
    isPrimitive(rightHandOperand)
  ) {
    return
  }
  let leftHandMap = memoizeMap.get(leftHandOperand)
  if (leftHandMap != null) {
    leftHandMap.set(rightHandOperand, result)
  } else {
    leftHandMap = new WeakMap()
    leftHandMap.set(rightHandOperand, result)
    memoizeMap.set(leftHandOperand, leftHandMap)
  }
}

/**
 * Assert deeply nested sameValue equality between two objects of any type.
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {Object} [options] (optional) Additional options
 * @param {Array} [options.comparator] (optional) Override default algorithm,
 * determining custom equality.
 * @param {Array} [options.memoize] (optional) Provide a custom memoization
    object which will cache the results of complex objects for a speed boost.
    By passing `false` you can disable memoization, but this will cause
    circular references to blow the stack.
 * @return {Boolean} equal match
 */
export function deepEql<T1 = unknown, T2 = unknown>(
  leftHandOperand: T1,
  rightHandOperand: T2,
  options?: DeepEqlOptions<T1, T2> | undefined
): boolean {
  options ??= {}
  if (false !== options.memoize) {
    options.memoize ??= new WeakMap()
  }
  return _deepEqual(leftHandOperand, rightHandOperand, options)
}

function _deepEqual<T1 = unknown, T2 = unknown>(
  leftHandOperand: T1,
  rightHandOperand: T2,
  options?: DeepEqlOptions<T1, T2> | undefined,
  key?: string | symbol
): boolean {
  // If we have a comparator, we can't assume anything; so bail to its check first.
  if (options?.comparator != null) {
    return extensiveDeepEqual(leftHandOperand, rightHandOperand, options, key)
  }

  const simpleResult = simpleEqual(leftHandOperand, rightHandOperand)
  if (simpleResult !== null) {
    return simpleResult
  }

  // Deeper comparisons are pushed through to a larger function
  return extensiveDeepEqual(leftHandOperand, rightHandOperand, options)
}

/**
 * Many comparisons can be canceled out early via simple equality or primitive checks.
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @return {Boolean|null} equal match
 */
function simpleEqual(leftHandOperand: any, rightHandOperand: any) {
  // Equal references (except for Numbers) can be returned early
  if (leftHandOperand === rightHandOperand) {
    // Handle +-0 cases
    return leftHandOperand !== 0 || 1 / leftHandOperand === 1 / rightHandOperand
  }

  // handle NaN cases
  if (
    leftHandOperand !== leftHandOperand && // eslint-disable-line no-self-compare
    rightHandOperand !== rightHandOperand // eslint-disable-line no-self-compare
  ) {
    return true
  }

  // Anything that is not an 'object', i.e. symbols, functions, booleans, numbers,
  // strings, and undefined, can be compared by reference.
  if (isPrimitive(leftHandOperand) || isPrimitive(rightHandOperand)) {
    // Easy out b/c it would have passed the first equality check
    return false
  }
  return null
}

/*!
 * The main logic of the `deepEqual` function.
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {Object} [options] (optional) Additional options
 * @param {Array} [options.comparator] (optional) Override default algorithm, determining custom equality.
 * @param {Array} [options.memoize] (optional) Provide a custom memoization object which will cache the results of
    complex objects for a speed boost. By passing `false` you can disable memoization, but this will cause circular
    references to blow the stack.
 * @return {Boolean} equal match
*/
function extensiveDeepEqual<T1 = unknown, T2 = unknown>(
  leftHandOperand: T1,
  rightHandOperand: T2,
  options: DeepEqlOptions<T1, T2> | undefined,
  key?: string | symbol
): boolean {
  options ??= {}
  const memoize: MemoizeMap | undefined =
    options.memoize === false
      ? undefined
      : (options.memoize ??= new WeakMap() as any)
  const comparator = options?.comparator

  // Check if a memoized result exists.
  const memoizeResultLeft = memoizeCompare(
    leftHandOperand,
    rightHandOperand,
    memoize
  )
  if (memoizeResultLeft !== null) {
    return memoizeResultLeft
  }
  const memoizeResultRight = memoizeCompare(
    rightHandOperand,
    leftHandOperand,
    memoize
  )
  if (memoizeResultRight !== null) {
    return memoizeResultRight
  }

  // If a comparator is present, use it.
  if (comparator) {
    const comparatorResult = comparator(leftHandOperand, rightHandOperand, key)
    // Comparators may return null, in which case we want to go back to default behavior.
    if (comparatorResult === false || comparatorResult === true) {
      memoizeSet(leftHandOperand, rightHandOperand, memoize, comparatorResult)
      return comparatorResult
    }
    // To allow comparators to override *any* behavior, we ran them first. Since it didn't decide
    // what to do, we need to make sure to return the basic tests first before we move on.
    const simpleResult = simpleEqual(leftHandOperand, rightHandOperand)
    if (simpleResult !== null) {
      // Don't memoize this, it takes longer to set/retrieve than to just compare.
      return simpleResult
    }
  }

  const leftHandType = type_detect(leftHandOperand)
  if (leftHandType !== type_detect(rightHandOperand)) {
    memoizeSet(leftHandOperand, rightHandOperand, memoize, false)
    return false
  }

  // Temporarily set the operands in the memoize object to prevent blowing the stack
  memoizeSet(leftHandOperand, rightHandOperand, memoize, true)

  const result = extensiveDeepEqualByType(
    leftHandOperand,
    rightHandOperand,
    leftHandType,
    options as any
  )
  memoizeSet(leftHandOperand, rightHandOperand, memoize, result)
  return result
}

function extensiveDeepEqualByType(
  leftHandOperand: any,
  rightHandOperand: any,
  leftHandType: string,
  options?: DeepEqlOptions | undefined
) {
  switch (leftHandType) {
    case "String":
    case "Number":
    case "Boolean":
    case "Date":
      // If these types are their instance types (e.g. `new Number`) then re-deepEqual against their values
      return _deepEqual(leftHandOperand.valueOf(), rightHandOperand.valueOf())
    case "Promise":
    case "Symbol":
    case "function":
    case "WeakMap":
    case "WeakSet":
      return leftHandOperand === rightHandOperand
    case "Error":
      return keysEqual(
        leftHandOperand,
        rightHandOperand,
        ["name", "message", "code"],
        options
      )
    case "Arguments":
    case "Int8Array":
    case "Uint8Array":
    case "Uint8ClampedArray":
    case "Int16Array":
    case "Uint16Array":
    case "Int32Array":
    case "Uint32Array":
    case "Float32Array":
    case "Float64Array":
    case "Array":
      return iterableEqual(leftHandOperand, rightHandOperand, options)
    case "RegExp":
      return regexpEqual(leftHandOperand, rightHandOperand)
    case "Generator":
      return generatorEqual(leftHandOperand, rightHandOperand, options)
    case "DataView":
      return iterableEqual(
        new Uint8Array(leftHandOperand.buffer),
        new Uint8Array(rightHandOperand.buffer),
        options
      )
    case "ArrayBuffer":
      return iterableEqual(
        new Uint8Array(leftHandOperand),
        new Uint8Array(rightHandOperand),
        options
      )
    case "Set":
      return entriesEqual(leftHandOperand, rightHandOperand, options)
    case "Map":
      return entriesEqual(leftHandOperand, rightHandOperand, options)
    case "Temporal.PlainDate":
    case "Temporal.PlainTime":
    case "Temporal.PlainDateTime":
    case "Temporal.Instant":
    case "Temporal.ZonedDateTime":
    case "Temporal.PlainYearMonth":
    case "Temporal.PlainMonthDay":
      return leftHandOperand.equals(rightHandOperand)
    case "Temporal.Duration":
      return (
        leftHandOperand.total("nanoseconds") ===
        rightHandOperand.total("nanoseconds")
      )
    case "Temporal.TimeZone":
    case "Temporal.Calendar":
      return leftHandOperand.toString() === rightHandOperand.toString()
    default:
      return objectEqual(leftHandOperand, rightHandOperand, options)
  }
}

/*!
 * Compare two Regular Expressions for equality.
 *
 * @param {RegExp} leftHandOperand
 * @param {RegExp} rightHandOperand
 * @return {Boolean} result
 */

function regexpEqual(leftHandOperand: RegExp, rightHandOperand: RegExp) {
  return leftHandOperand.toString() === rightHandOperand.toString()
}

/*!
 * Compare two Sets/Maps for equality. Faster than other equality functions.
 *
 * @param {Set} leftHandOperand
 * @param {Set} rightHandOperand
 * @param {Object} [options] (Optional)
 * @return {Boolean} result
 */

function entriesEqual(
  leftHandOperand: Set<any> | Map<any, any>,
  rightHandOperand: Set<any> | Map<any, any>,
  options?: DeepEqlOptions | undefined
): boolean {
  // IE11 doesn't support Set#entries or Set#@@iterator, so we need manually populate using Set#forEach
  if (leftHandOperand.size !== rightHandOperand.size) {
    return false
  }
  if (leftHandOperand.size === 0) {
    return true
  }
  const leftHandItems: any[] = []
  const rightHandItems: any[] = []
  leftHandOperand.forEach(function gatherEntries(key, value) {
    leftHandItems.push([key, value])
  })
  rightHandOperand.forEach(function gatherEntries(key, value) {
    rightHandItems.push([key, value])
  })
  return iterableEqual(leftHandItems.sort(), rightHandItems.sort(), options)
}

/*!
 * Simple equality for flat iterable objects such as Arrays, TypedArrays or Node.js buffers.
 *
 * @param {Iterable} leftHandOperand
 * @param {Iterable} rightHandOperand
 * @param {Object} [options] (Optional)
 * @return {Boolean} result
 */

function iterableEqual(
  leftHandOperand: ArrayLike<any>,
  rightHandOperand: ArrayLike<any>,
  options?: DeepEqlOptions | undefined
): boolean {
  const length = leftHandOperand.length
  if (length !== rightHandOperand.length) {
    return false
  }
  if (length === 0) {
    return true
  }
  let index = -1
  while (++index < length) {
    if (
      _deepEqual(leftHandOperand[index], rightHandOperand[index], options) ===
      false
    ) {
      return false
    }
  }
  return true
}

/*!
 * Simple equality for generator objects such as those returned by generator functions.
 *
 * @param {Iterable} leftHandOperand
 * @param {Iterable} rightHandOperand
 * @param {Object} [options] (Optional)
 * @return {Boolean} result
 */

function generatorEqual(
  leftHandOperand: Iterator<any>,
  rightHandOperand: Iterator<any>,
  options?: DeepEqlOptions | undefined
): boolean {
  return iterableEqual(
    getGeneratorEntries(leftHandOperand),
    getGeneratorEntries(rightHandOperand),
    options
  )
}

/*!
 * Determine if the given object has an @@iterator function.
 *
 * @param {Object} target
 * @return {Boolean} `true` if the object has an @@iterator function.
 */
function hasIteratorFunction(target: any): boolean {
  return (
    typeof Symbol !== "undefined" &&
    typeof target === "object" &&
    typeof Symbol.iterator !== "undefined" &&
    typeof target[Symbol.iterator] === "function"
  )
}

/*!
 * Gets all iterator entries from the given Object. If the Object has no @@iterator function, returns an empty array.
 * This will consume the iterator - which could have side effects depending on the @@iterator implementation.
 *
 * @param {Object} target
 * @returns {Array} an array of entries from the @@iterator function
 */
function getIteratorEntries(target: any): any[] {
  if (hasIteratorFunction(target)) {
    try {
      return getGeneratorEntries(target[Symbol.iterator]())
    } catch (iteratorError) {
      return []
    }
  }
  return []
}

/*!
 * Gets all entries from a Generator. This will consume the generator - which could have side effects.
 *
 * @param {Generator} target
 * @returns {Array} an array of entries from the Generator.
 */
function getGeneratorEntries(generator: Iterator<any>): any[] {
  let generatorResult = generator.next()
  const accumulator = [generatorResult.value]
  while (generatorResult.done === false) {
    generatorResult = generator.next()
    accumulator.push(generatorResult.value)
  }
  return accumulator
}

/*!
 * Gets all own and inherited enumerable keys from a target.
 *
 * @param {Object} target
 * @returns {Array} an array of own and inherited enumerable keys from the target.
 */
function getEnumerableKeys(target: any): string[] {
  const keys = []
  for (const key in target) {
    if (target[key] !== undefined) keys.push(key)
  }
  return keys
}

function getEnumerableSymbols(target: any): symbol[] {
  const keys = []
  const allKeys = Object.getOwnPropertySymbols(target)
  for (let i = 0; i < allKeys.length; i += 1) {
    const key = allKeys[i]
    if (Object.getOwnPropertyDescriptor(target, key)?.enumerable === true) {
      keys.push(key)
    }
  }
  return keys
}

/*!
 * Determines if two objects have matching values, given a set of keys. Defers to deepEqual for the equality check of
 * each key. If any value of the given key is not equal, the function will return false (early).
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {Array} keys An array of keys to compare the values of leftHandOperand and rightHandOperand against
 * @param {Object} [options] (Optional)
 * @return {Boolean} result
 */
function keysEqual(
  leftHandOperand: any,
  rightHandOperand: any,
  keys: (string | symbol)[],
  options?: DeepEqlOptions | undefined
): boolean {
  const length = keys.length
  if (length === 0) {
    return true
  }
  for (let i = 0; i < length; i += 1) {
    const key = keys[i]
    const lhs = leftHandOperand[key]
    const rhs = rightHandOperand[key]
    if (typeof lhs === "function" && typeof rhs === "function") {
      if (lhs.toString() !== rhs.toString()) {
        return false
      }
    } else {
      const eql = _deepEqual(lhs, rhs, options, key)
      if (eql === false) {
        return false
      }
    }
  }
  return true
}

/*!
 * Recursively check the equality of two Objects. Once basic sameness has been
 * established it will defer to `deepEqual` for each enumerable key in the
 * object.
 *
 * @param {Mixed} leftHandOperand
 * @param {Mixed} rightHandOperand
 * @param {Object} [options] (Optional)
 * @return {Boolean} result
 */
function objectEqual(
  leftHandOperand: Record<string | symbol, any>,
  rightHandOperand: Record<string | symbol, any>,
  options?: DeepEqlOptions | undefined
): boolean {
  if (typeof leftHandOperand.eql === "function") {
    const result = leftHandOperand.eql(rightHandOperand)
    if (isBoolean(result)) return result
  }

  if (typeof rightHandOperand.eql === "function") {
    const result = rightHandOperand.eql(leftHandOperand)
    if (isBoolean(result)) return result
  }

  if (typeof leftHandOperand.toJSON === "function") {
    return objectEqual(leftHandOperand.toJSON(), rightHandOperand, options)
  }

  if (typeof rightHandOperand.toJSON === "function") {
    return objectEqual(leftHandOperand, rightHandOperand.toJSON(), options)
  }

  if (
    typeof leftHandOperand === "object" &&
    typeof rightHandOperand === "object" &&
    typeof leftHandOperand.constructor === "function" &&
    typeof rightHandOperand.constructor === "function"
  ) {
    if (
      false ===
      objectEqual(
        leftHandOperand.constructor,
        rightHandOperand.constructor,
        options
      )
    ) {
      return false
    }
  }

  const leftHandKeys = [
    ...getEnumerableKeys(leftHandOperand),
    ...getEnumerableSymbols(leftHandOperand)
  ]
  const rightHandKeys = [
    ...getEnumerableKeys(rightHandOperand),
    ...getEnumerableSymbols(rightHandOperand)
  ]

  if (leftHandKeys.length && leftHandKeys.length === rightHandKeys.length) {
    if (
      iterableEqual(
        mapSymbols(leftHandKeys).sort(),
        mapSymbols(rightHandKeys).sort()
      ) === false
    ) {
      return false
    }
    return keysEqual(leftHandOperand, rightHandOperand, leftHandKeys, options)
  }

  const leftHandEntries = getIteratorEntries(leftHandOperand)
  const rightHandEntries = getIteratorEntries(rightHandOperand)
  if (
    leftHandEntries.length &&
    leftHandEntries.length === rightHandEntries.length
  ) {
    leftHandEntries.sort()
    rightHandEntries.sort()
    return iterableEqual(leftHandEntries, rightHandEntries, options)
  }

  if (
    leftHandKeys.length === 0 &&
    leftHandEntries.length === 0 &&
    rightHandKeys.length === 0 &&
    rightHandEntries.length === 0
  ) {
    return true
  }

  return false
}

/*!
 * Returns true if the argument is a primitive.
 *
 * This intentionally returns true for all objects that can be compared by reference,
 * including functions and symbols.
 *
 * @param {Mixed} value
 * @return {Boolean} result
 */
function isPrimitive(value: any) {
  return value === null || typeof value !== "object"
}

function mapSymbols(arr: any) {
  return arr.map(function mapSymbol(entry: any) {
    if (typeof entry === "symbol") {
      return entry.toString()
    }

    return entry
  })
}
