import _fse from "fs-extra"
import _path from "path"
import { ensureSuffix, wrap } from "../../fe/String"
import { toS } from "../../fe/toS"
import { ee } from "../event/EventEmitter"
import { crlf } from "./CRLF"
import { mkdirpSync_, mkdirp_ } from "./Path"

export async function writeFile_(nativePath: string, data: string | Buffer) {
  const dir = _path.dirname(nativePath)
  await mkdirp_(dir)
  await _fse.writeFile(nativePath, data)
  ee().emit("fileChanged", dir) // because the parent directory has a new child!
}

export async function writeText_(nativePath: string, ...txt: string[]) {
  return writeFile_(
    nativePath,
    crlf(ensureSuffix(txt.map(toS).join("\n"), "\n"))
  )
}

export async function writeTextfile_(nativePath: string, ...txt: string[]) {
  const data = crlf(...wrap(txt))
  await writeFile_(nativePath, data)
}

export function writeFileSync_(nativePath: string, data: string | Buffer) {
  const dir = _path.dirname(nativePath)
  mkdirpSync_(dir)
  _fse.writeFileSync(nativePath, data)
  ee().emit("fileChanged", dir) // because the parent directory has a new child!
}

export function writeTextSync_(nativePath: string, ...txt: string[]) {
  return writeFileSync_(
    nativePath,
    crlf(ensureSuffix(txt.map(toS).join("\n"), "\n"))
  )
}
