import { arch, platform } from "os"
import { argv, versions } from "process"
import { lazy } from "../core/Lazy"
import { pick } from "../fe/Object"
import { mkLogger } from "./Logger"
import { osFullName } from "./OperatingSystem"
import { serviceName } from "./ServiceNames"
import { StartTs } from "./StartTs"
import { version } from "./Version"
import { psenv } from "./child/ChildEnv"
import { isPacked } from "./platform/IsPacked"
import { isElectron } from "./platform/Platform"
import { Settings } from "./settings/Settings"

export const logStartup = lazy(() => {
  mkLogger("LogStartup").info("setup(): starting " + serviceName(), {
    version,
    StartTs,
    argv,
    arch: arch(),
    platform: platform(),
    os: osFullName(),
    isPacked: isPacked(),
    isElectron,
    versions: pick(versions, "electron", "node"),
    settings: {
      logLevel: Settings.logLevel.valueOrDefault,
      httpPort: Settings.httpPort.valueOrDefault,
      libraryDir: Settings.libraryDir.valueOrDefault
    },
    ...psenv()
  })
})
