import { asPromise } from "../../fe/AsPromise"
import { delay } from "../../fe/Delay"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"

export const timeoutStacks: string[][] = []

// TODO: convert consumers to fe/thenOrTimeout?
export async function thenOrTimeout<T>(
  p: T | (() => T | Promise<T>),
  timeoutMs: number,
  onTimeout?: () => any,
  onSuccess?: (t: T) => any | Promise<any>
): PromiseMaybe<T> {
  if (!gt0(timeoutMs)) {
    const result = await asPromise(p)
    await onSuccess?.(result)
    return result
  }
  let _resolved = false
  let timedOut = false
  let result: Maybe<T>
  await Promise.race([
    asPromise(p).then(ea => {
      if (!timedOut) {
        result = ea
        _resolved = true
        return ea
      } else {
        return
      }
    }),
    // This delay must be unref'ed so as to not prevent node from exitting:
    delay(timeoutMs).then(() => {
      if (!_resolved) {
        timedOut = true
      }
    })
  ])
  if (_resolved) {
    await onSuccess?.(result!)
  } else {
    await onTimeout?.()
  }
  return result
}
