import { compactBlanks } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import { fmtPct } from "../../fe/Number"
import { ellipsizePath } from "../fs/Path"
import { within } from "../Number"

// "ProgressEvent" is part of lib.dom. Let's not collide.

export interface ProgressEvt {
  path?: string
  op: string
  pct: number
  elapsedMs?: number
  done?: boolean
}

export function progressEvtToS(pe: ProgressEvt): string {
  const path = blank(pe.path)
    ? null
    : ellipsizePath({ p: pe.path, maxLength: 80 })
  return compactBlanks([
    path,
    pe.op,
    pe.done === true ? "done" : fmtPct(pe.pct)
  ]).join(": ")
}

export type ProgressEvtWithPath = ProgressEvt &
  Required<Pick<ProgressEvt, "path">>

export type ProgressEvtWithoutPct = Pick<ProgressEvt, "path" | "op">

export function isProgressEvt(o: any): o is ProgressEvt {
  return o != null && notBlank(o.op) && within(0, 100, o.pct)
}
