import {
  moveSync,
  outputFile,
  readFileSync,
  unlink,
  unlinkSync
} from "fs-extra"
import { rm } from "fs/promises"
import { join } from "path"
import { lazy } from "../core/Lazy"
import { secondMs } from "../fe/Date"
import { stringify } from "../fe/JSON"
import { map } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { gt0, gte } from "../fe/Number"
import { MaybeSyncOrAsync, SyncOrAsync } from "../fe/SyncOrAsync"
import { TimeoutError } from "../fe/TimeoutError"
import { toS } from "../fe/toS"
import { bufferToString } from "./Buffer"
import { FifoCache } from "./FifoCache"
import { parseJSON } from "./JSON"
import { mkLogger } from "./Logger"
import { shortFsStringSha } from "./StringHash"
import { Deferred } from "./async/Deferred"
import { filestamp } from "./date/Filestamp"
import { isMtimeRecentSync } from "./fs/StatSync"
import { Settings } from "./settings/Settings"

// NOTE: this is used statically by Shim, so BE CAREFUL WITH NEW CORE IMPORTS:
// they will probably cause a dependency loop.

export type FifoCacheAsyncOpts = ConstructorParameters<typeof FifoCacheAsync>[0]

export class FifoCacheAsync<V = any> {
  readonly #logger = lazy(() => mkLogger(this.opts.name))
  static cacheDir: Maybe<string> // < set by configDir()
  private cacheHitsSync = 0
  private cacheHitsAsync = 0
  private cacheMisses = 0
  private rejections = 0
  private timeouts = 0
  readFsCache = false
  writeFsCache = false
  readonly cache: FifoCache<Deferred<V> | V>
  constructor(
    readonly opts: {
      name: string
      maxSize: number
      timeoutMs: number
      clearEveryMs?: number
      /** does fs cache make sense for this instance? Used by shim to set the read and write flags properly */
      useFsCache?: boolean
      fsCacheTtlMs?: number
      /** explicit setting only for tests: */
      readFsCache?: boolean
      /** explicit setting only for tests: */
      writeFsCache?: boolean
    }
  ) {
    this.cache = new FifoCache(opts.maxSize, opts.clearEveryMs)
    this.readFsCache = opts.readFsCache ?? this.readFsCache
    this.writeFsCache = opts.writeFsCache ?? this.writeFsCache
  }

  get name() {
    return this.opts.name
  }

  get size() {
    return this.cache.size
  }

  get fsCacheTtlMs() {
    return (
      this.opts.fsCacheTtlMs ??
      this.opts.clearEveryMs ??
      this.opts.timeoutMs * 5
    )
  }

  mergeStats(stats: ReturnType<FifoCacheAsync["stats"]>) {
    this.cacheHitsSync += stats.cacheHitsSync
    this.cacheHitsAsync += stats.cacheHitsAsync
    this.cacheMisses += stats.cacheMisses
    this.rejections += stats.rejections
    this.timeouts += stats.timeouts
  }

  stats() {
    return {
      size: this.size,
      cacheHitsSync: this.cacheHitsSync,
      cacheHitsAsync: this.cacheHitsAsync,
      cacheMisses: this.cacheMisses,
      rejections: this.rejections,
      timeouts: this.timeouts
    }
  }

  has(key: string) {
    return this.cache.has(key)
  }

  #fsCacheForKey(key: string) {
    if (FifoCacheAsync.cacheDir == null) return
    const sha = shortFsStringSha(key)
    return join(
      FifoCacheAsync.cacheDir,
      this.name,
      sha.slice(0, 2),
      sha.slice(2) + ".json"
    )
  }

  hasFsCacheSync(key: string, fsCachePath?: string) {
    return isMtimeRecentSync(
      fsCachePath ?? this.#fsCacheForKey(key),
      this.fsCacheTtlMs
    )
  }

  #readFsCacheSync(key: string): Maybe<V> {
    if (!this.readFsCache) return
    const fsCachePath = this.#fsCacheForKey(key)
    if (fsCachePath != null && this.hasFsCacheSync(key, fsCachePath)) {
      try {
        const result = parseJSON<V>(bufferToString(readFileSync(fsCachePath)))
        this.cache.set(key, result!)
        return this.#logger().tap({
          msg: "#readFsCacheSync(): hit",
          result,
          meta: { key }
        })
      } catch {
        //
      }
    }
    if (fsCachePath != null) {
      void unlink(fsCachePath).catch(() => null)
    }
    this.#logger().debug("#readFsCacheSync(): miss", { key })
    return
  }

  #writeFsCache(key: string, v: V) {
    if (this.writeFsCache) {
      const fsCachePath = this.#fsCacheForKey(key)
      if (fsCachePath != null) {
        this.#logger().debug("#writeFsCache()", { key })
        return outputFile(fsCachePath, stringify(v)).catch(async error => {
          this.#logger().error("#writeFsCache() failed", { error })
          await unlink(fsCachePath)
        })
      }
    }
    return
  }

  /**
   * If the prior getOrSetAsync() threw an error, this method will also.
   */
  get_(key: string): MaybeSyncOrAsync<V> {
    if (!this.cache.has(key)) {
      this.cacheMisses++
      return
    }
    const v = this.cache.get(key) ?? this.#readFsCacheSync(key)
    if (v instanceof Deferred) {
      if (v.isRejected) {
        throw v.error
      } else if (v.isPending) {
        this.cacheHitsAsync++
        return v.promise
      } else {
        this.cacheHitsSync++
        return v.value
      }
    } else if (v instanceof Error) {
      throw v
    } else {
      this.cacheHitsSync++
      return v
    }
  }

  /**
   * @return a value if the key was in cache and the promise was resolved.
   */
  get(key: string): Maybe<V> {
    const v = this.cache.get(key) ?? this.#readFsCacheSync(key)
    if (v == null || (v instanceof Deferred && v.isPending)) {
      this.cacheMisses++
    } else {
      this.cacheHitsSync++
    }
    return v instanceof Deferred ? v.value : v
  }

  clear() {
    this.cache.clear()
    this.cacheHitsSync = 0
    this.cacheHitsAsync = 0
    this.cacheMisses = 0
    this.rejections = 0
    this.timeouts = 0
    if (FifoCacheAsync.cacheDir != null) {
      try {
        const d = join(FifoCacheAsync.cacheDir, this.name)
        const victim = d + ".trash-" + filestamp()
        moveSync(d, victim)
        return rm(victim, {
          recursive: true,
          maxRetries: 3,
          retryDelay: secondMs,
          force: true
        }).catch()
      } catch {
        // ignore
      }
    }
    return
  }

  delete(key: string, deleteFsCache = true) {
    if (deleteFsCache) {
      map(this.#fsCacheForKey(key), ea => {
        try {
          unlinkSync(ea)
        } catch {
          //
        }
      })
    }
    return this.cache.delete(key)
  }

  deleteIf(predicate: (key: string) => boolean) {
    for (const k of this.cache.keys()) {
      if (predicate(k)) {
        this.delete(k)
      }
    }
  }

  set(key: string, value: V): V {
    this.cache.set(key, value)
    return value
  }

  getOrSet(key: string, valueThunk: () => V): V {
    const prior = this.get(key)
    if (prior != null) {
      if (prior instanceof Error) {
        throw prior
      } else {
        return prior
      }
    }
    return this.set(key, valueThunk())
  }

  // THIS METHOD IS NOT ASYNC: it relies on stable internal state!
  getOrSetAsync(key: number | string, later: () => Promise<V>): SyncOrAsync<V> {
    const start = Date.now()
    const k = toS(key)
    {
      const prior = this.get_(k)
      if (prior != null) {
        return prior instanceof Deferred ? prior.promise : prior
      }
    }

    const d = new Deferred<V>(this.opts.name).observe(later())
    // Add the deferred to the cache ASAP:
    this.cache.set(k, d)

    if (gt0(this.opts.timeoutMs)) {
      void d.setTimeout(this.opts.timeoutMs)
    }

    void d.promise.then(
      result => {
        // remove the Deferred and release the scoped memory:
        this.set(k, result)
        if (
          gt0(Settings.fsCacheSlowMs.valueOrDefault) &&
          gte(Date.now() - start, Settings.fsCacheSlowMs.valueOrDefault - 1)
        ) {
          return this.#writeFsCache(k, result)
        }
        return
      },
      error => {
        this.cache.set(k, error)
        this.rejections++
        if (error instanceof TimeoutError) {
          this.timeouts++
        }
      }
    )
    return d.promise
  }
}
