import { commonPrefixLength, compactBlanks, count } from "../fe/Array"
import { sortBy } from "../fe/ArraySort"
import { blank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { clamp, mapNumericOr, times, toInt } from "../fe/Number"
import { leftPad } from "../fe/Pad"
import { Primitive } from "../fe/Primitive"
import { strslice, substr } from "../fe/String"
import { toS } from "../fe/toS"
import { RadixAlphaNum } from "./math/Radix"
import { Array2D } from "./Number"
import { firstThunk } from "./Object"
import { intersection } from "./Set"
import { stripDiacritics } from "./String"

/**
 * @return [0,1], where 0 means no common string, and 1 is equality
 */
export function commonSubstringRatio(a: string, b: string): number {
  return [a, b].some(blank)
    ? 0
    : lcs(a, b).length / Math.max(a.length, b.length)
}

/**
 * @see https://en.wikipedia.org/wiki/Longest_common_substring_problem
 */
export function lcs(a: string, b: string): string {
  if (a == null) return b
  if (b == null) return a
  a = a.normalize()
  b = b.normalize()
  if (a === b || b.includes(a)) return a
  if (a.includes(b)) return b
  const m = new Array2D(a.length)
  let z = 0
  let ret = ""
  for (let i = 0; i < a.length; i++) {
    for (let j = 0; j < b.length; j++) {
      if (a[i] === b[j]) {
        if (i === 0 || j === 0) {
          m.set(i, j, 1)
        } else {
          m.set(i, j, m.get(i - 1, j - 1) + 1)
        }
        if (m.get(i, j) >= z) {
          z = m.get(i, j)
          ret = substr(a, i - z + 1, z)
        }
      }
    }
  }
  return ret
}

/**
 * Hamming distance. Note that `a` and `b` (before or after normalization) must
 * be the same length, or `undefined` will be returned.
 *
 * Note that this is case-sensitive!
 *
 * @see https://en.wikipedia.org/wiki/Hamming_distance
 */
export function hamming(a: string, b: string): Maybe<number> {
  if (a == null || b == null) return undefined
  if (a === b) return 0
  const r = _hamming(a, b)
  if (r != null) return r
  const an = a.normalize()
  const bn = b.normalize()
  return a !== an || b !== bn ? _hamming(an, bn) : undefined
}

function _hamming(a: string, b: string): Maybe<number> {
  return a.length !== b.length
    ? undefined
    : [...a].reduce((acc, ea, idx) => (ea === b.charAt(idx) ? acc : acc + 1), 0)
}

/**
 * Return the SrensenDice similarity index between two strings.
 * @see https://en.wikipedia.org/wiki/S%C3%B8rensen%E2%80%93Dice_coefficient
 * @return [0,1]. O for no match, 1 for full, case-insensitive match.
 */
export function diceCoeff(a: string, b: string): number {
  const A = a.toUpperCase().normalize()
  const B = b.toUpperCase().normalize()
  return firstThunk<number>(
    () => (A === B ? 1 : undefined),
    () => (blank(a) !== blank(b) ? 0 : undefined),
    () => (a.length === 1 && b.length === 1 ? 0 : undefined),
    () => {
      const aGrams = bigrams(A)
      const bGrams = bigrams(B)
      const intersections = nonUniqIntersection(aGrams, bGrams).length
      return (2 * intersections) / (aGrams.length + bGrams.length)
    }
  )!
}

/**
 * Convert "abcd" to ["ab", "bc", "cd"]
 */
export function bigrams(s: string): string[] {
  if (s == null || s.length === 0) return []
  const arr = [...s]
  return arr.slice(0, -1).map((ea, i) => ea + arr[i + 1])
}

export function nonUniqIntersection<T extends Primitive>(a: T[], b: T[]): T[] {
  const uniqIntersections = intersection(a, b)
  const i: T[] = []
  uniqIntersections.forEach(ea => {
    const n = Math.min(
      count(a, s => s === ea),
      count(b, s => s === ea)
    )
    times(n, () => i.push(ea))
  })
  return i
}

/**
 * Tries to prevent numeric overflow:
 */
function diffWithoutCommonPrefix(
  a: string,
  b: string,
  f: (s: string) => number
): number {
  const cpc = commonPrefixLength(a, b)
  return f(strslice(a, cpc)) - f(strslice(b, cpc))
}

/**
 * Return the value of the longest string of digits in `s`
 */
function longestNumericString(s: string): Maybe<string> {
  const digits = compactBlanks(toS(s).split(/[^\d]+/))
  return sortBy(digits, ea => -ea.length)[0]
}

export function lnsDiff(a: string, b: string): number {
  const [a1, b1] = [a, b]
    .map(longestNumericString)
    .map(ea => (blank(ea) ? "" : ea))
  return diffWithoutCommonPrefix(a1, b1, s => toInt(s, { defaultValue: 0 })!)
}

const notAlphaNumRe = /[^0-9a-z]+/gi
export function radixDiff(a: string, b: string): number {
  const [a1, b1] = [a, b].map(s =>
    stripDiacritics(s).replace(notAlphaNumRe, "").toLowerCase()
  )
  return diffWithoutCommonPrefix(a1, b1, s => RadixAlphaNum.decode(s)!)
}

export function str(a: string, b: string) {
  return {
    pref: commonPrefixLength(a, b),
    ham: hamming(a, b),
    dice: diceCoeff(a, b),
    lns: lnsDiff(a, b),
    radixDiff: radixDiff(a, b)
  }
}

/**
 * @return the number of non-lower-case characters in `s`
 */
export function lcdiff(s: string): number {
  return count(
    [...s.normalize()],
    ea => ea.toLowerCase().localeCompare(ea) !== 0
  )
}

/**
 * @return the weighted average of the character differences per character index
 */
export function positionalDiff(a: string, b: string) {
  let result: Maybe<number>
  for (let i = Math.max(a.length, b.length); i >= 0; i--) {
    const aChar = mapNumericOr(a.charCodeAt(i), ea => ea, 256)
    const bChar = mapNumericOr(b.charCodeAt(i), ea => ea, 256)
    const diff = clamp(-256, 256, aChar - bChar)
    if (result == null) result = diff
    else result = (result + diff) / 2
  }
  return result
}

export function paddedPositionalDiff(a: string, b: string, minLen = 8) {
  return positionalDiff(leftPad(a, minLen, " "), leftPad(b, minLen, " "))
}
