const logger = lazy(() => mkLogger("volumes.LocalVolumesLinux"))

export async function addLocalVolumeInfoLinux(vols: DfVolume[]) {
  // this doesn't work on docker (UUID is always blank!)
  if (isDocker()) return
  const lsblk = await localVolumes_lsblk_().catch(error =>
    logger().warn("lsblk failed", { error })
  )
  const blkid = await localVolumes_blkid_().catch(error =>
    logger().warn("blkid failed", { error })
  )
  if (lsblk == null && blkid == null) return
  const mountpoints = sortIgnoreCase(
    uniq([
      ...(lsblk ?? []).filter(ea => !ea.ignorable).map(ea => ea.mountpoint),
      ...vols.map(ea => ea.mountpoint)
    ])
  )
  const result: Volume[] = []
  for (const mountpoint of mountpoints) {
    const vol = vols.find(ea => ea.mountpoint === mountpoint)
    const info = lsblk?.find(ea => ea.mountpoint === mountpoint)
    const merged = { ...vol, ...info } as Volume
    const uuid = blkid?.find(ea => ea.filesystem === merged.filesystem)
    if (uuid != null) assignMissingPrimitives(merged, uuid)
    if (merged.size == null || merged.available == null) {
      // this may only be needed within Docker...
      const df = (
        await dfPosixRaw({ localsOnly: false, paths: [mountpoint] })
      )?.[0]
      logger().info("backfilling mountpoint", { info, vol, df })
      if (df != null) assignMissingPrimitives(merged, df)
    }
    result.push(merged)
  }
  return result
}

const VersionRe = /lsblk from util-linux ([\d\.]+)$/i

const lsblkVersion = lazy(async () => {
  const s = await stdout_("lsblk", ["--version"], {
    timeoutMs: ShortCommandTimeoutMs
  })
  return VersionRe.exec(s)?.[1]
    ?.split(".")
    .map(ea => toInt(ea))
})

// lsblk from ubuntu 18 (bionic), 2.31, doesn't know about fsused and fsavail.
const lsblkOutput = lazy(async () => {
  const cols = ["mountpoint", "label", "uuid"]
  const v = await lsblkVersion()
  if (v != null && gte(v[0], 2) && gte(v[1], 34)) {
    cols.push("fsused", "fsavail")
  }
  return cols.join(",")
})

export const localVolumes_lsblk_ = lazyFsAsync(
  "localVolumes_lsblk",
  async () => {
    const sout = await stdout_(
      "lsblk",
      ["-P", "-b", "--output", await lsblkOutput()],
      {
        timeoutMs: commandTimeoutMs() // we may need to wait for spinning rust to spin up.
      }
    )
    const result = splitLines(sout)
      .map(input => parseEnvTokens({ input, lowerCaseKeys: true }))
      .filter(ea => ea != null)
      .map(ea => {
        const ignorable =
          blank(ea.mountpoint) ||
          ea.mountpoint.startsWith("/snap/") ||
          ea.mountpoint === "/boot" ||
          ea.mountpoint.startsWith("/boot/") ||
          !isDirectorySync(ea.mountpoint) // < docker can bind-mount files. Ignore those.

        const used = toInt(ea.fsused)
        const available = toInt(ea.fsavail)
        return {
          mountpoint: ea.mountpoint,
          label: ea.label as Maybe<string>,
          uuid: ea.uuid,
          ignorable,
          ...(used != null && available != null
            ? { used, available, size: used + available }
            : undefined),
          // if we're docker, we don't know:
          ...(!isDocker() ? { remote: false } : undefined)
        }
      })

    return logger().tap({
      msg: "localVolumes_lsblk",
      level: LogLevels.trace,
      result
    })
  },
  0
)

