import { map } from "../fe/Maybe"
import { SyncOrAsync } from "../fe/SyncOrAsync"
import { Thunk } from "../fe/Thunk"
import { Logger } from "./Logger"

export class Elapsed {
  private ts = Date.now()
  constructor(
    readonly l: Logger,
    readonly listener?: (name: string, elapsedMs: number) => any
  ) {}

  elapsed(msg: string) {
    const now = Date.now()
    const diff = now - this.ts
    this.ts = now
    map(this.listener, ea => ea(msg, diff))
    if (diff > 2) {
      this.l.log(diff > 500 ? "warn" : diff > 100 ? "info" : "debug", msg, {
        elapsedMs: diff
      })
    }
  }
}

export function elapsed<T>(t: Thunk<T>): { elapsedMs: number; result: T } {
  const start = Date.now()
  const result = t()
  return { elapsedMs: Date.now() - start, result }
}

export async function thenElapsed<T>(
  t: SyncOrAsync<T>
): Promise<{ elapsedMs: number; result: T }> {
  const start = Date.now()
  const result = await t
  return { elapsedMs: Date.now() - start, result }
}
