import os from "os"
import path from "path"
import { lazy } from "../core/Lazy"
import { compactBlanks } from "../fe/Array"
import { minuteMs, secondMs } from "../fe/Date"
import { later } from "../fe/Delay"
import { flatten } from "../fe/Flatten"
import { gt0 } from "../fe/Number"
import { ensureSuffix } from "../fe/String"
import { toS } from "../fe/toS"
import { mkLogger } from "./Logger"
import {
  PriorityClass,
  PriorityClassToNode,
  PriorityClassToPosix,
  PriorityClasses,
  priorityPosixToClass
} from "./PriorityClass"
import { TTLSet } from "./TTLSet"
import { stdoutResult_ } from "./child/ChildProcess"
import { ee } from "./event/EventEmitter"
import { isWin } from "./platform/Platform"
import { PowerShell } from "./pwsh/PowerShell"
import { Settings } from "./settings/Settings"

const logger = lazy(() => mkLogger("Renice"))

// HEY FUTURE ME: reniced.prior()?.clear() is NOT THE SAME as reniced.unset()
// -- we want to keep the prior TTLSet to avoid creating more memory garbage.
later(() => ee().on("clearCache", () => reniced.prior()?.clear()))

// 20220215: changed to a include-list, because we really only care to nice
// sync, worker, ffmpeg, and vlc.

// Don't renice web, renice, ps, df, ...

const niceableCmds = lazy(() => [
  "sync.js",
  "worker.js",
  ...flatten(
    [
      "dcraw_emu",
      path.basename(Settings.ffmpegPath.valueOrDefault),
      path.basename(Settings.vlcPath.valueOrDefault)
    ].map(ea => (isWin ? [ea, ensureSuffix(ea, ".exe")] : ea))
  )
])

export function niceable(
  cmd: string,
  args: string[] | readonly string[]
): boolean {
  return compactBlanks([
    path.basename(cmd),
    ...args.map(ea => path.basename(ea))
  ]).some(ea => niceableCmds().includes(ea.toLowerCase()))
}

let nodeReniceWorks = true

const reniced = lazy(() => new TTLSet<number>(minuteMs))

// both the batch cluster observer and child services renice, which makes sync-file get
// double-renice-d.
export async function renice(pid: number | undefined, priority?: number) {
  if (!gt0(pid) || reniced().has(pid)) return
  reniced().add(pid)

  const priorityClass: PriorityClass =
    priorityPosixToClass(priority) ??
    (Settings.processPriority.valueOrDefault as PriorityClass)

  priority ??= PriorityClassToNode[priorityClass]

  if (nodeReniceWorks) {
    try {
      os.setPriority(pid, priority)
      return priority
    } catch (error: any) {
      // if it's a permission issue, don't try other methods.
      const permissionDenied =
        error?.errno === -13 || error?.info?.code === "EACCES"

      logger().warn("Failed to renice using node:os", {
        pid,
        priority,
        permissionDenied,
        error
      })

      if (permissionDenied) {
        // it's not node's fault!
        return
      }
      nodeReniceWorks = false
      // try os-specific methods below:
    }
  }

  try {
    await (isWin
      ? reniceWin(pid, priorityClass)
      : renicePosix(
          pid,
          PriorityClassToPosix[priorityClass] ??
            PriorityClassToPosix.BelowNormal
        ))
    logger().info("Renice pid " + pid + " to " + priorityClass)
    return priority
  } catch (err) {
    // Probably because the process already ended:
    logger().info("Failed to renice pid " + pid, err)
    return
  }
}

async function reniceWin(pid: number, pc: PriorityClass) {
  if (gt0(pid) && PriorityClasses.includes(pc)) {
    await PowerShell.instance()
      .execute(`(Get-Process -Id ${pid}).PriorityClass = "${pc}"`, ea => ea)
      .catch(error => logger().info("reniceWin() failed", error))
  }
}

async function renicePosix(pid: number, nice = 19) {
  await stdoutResult_("renice", [nice, "-p", pid].map(toS), {
    timeoutMs: 10 * secondMs,
    isIgnorableError: () => true,
    ignoreExitCode: true
  }).catch(error => logger().info("renicePosix() failed", error))
}
