import { createConnection, Socket } from "net"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { stringify } from "../../fe/JSON"
import { Latch } from "../../fe/Latch"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, toInt } from "../../fe/Number"
import { EndableRanks } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { mkConsoleLogger } from "../Logger"
import { rateLimited } from "../RateLimited"
import { Settings } from "../settings/Settings"
import { GelfLogFormatter } from "./GelfLogFormatter"
import { LogFilterImpl } from "./LogFilterImpl"
import { SimpleLogger } from "./Logger"
import { LogLevel } from "./LogLevel"

const consoleLogger = lazy(() => mkConsoleLogger("log.LogBroadcaster"))

const nul = String.fromCharCode(0)

function parseLogServerSetting() {
  const [host, portStr] = (Settings.logServer.valueOrDefault ?? "").split(":")
  const port = toInt(portStr)
  return blank(host) || !gt0(port) ? undefined : { host, port }
}

export const logBroadcaster = lazy(() => {
  const server = parseLogServerSetting()
  if (server == null) {
    if (Settings.logServer.hasValue()) {
      consoleLogger().info(
        `setup(): no-op: invalid PS_LOG_SERVER setting (expecting "host:port", got ${stringify(
          Settings.logServer.valueOrDefault
        )})`
      )
    }
    return
  } else {
    return new LogBroadcaster(server.host, server.port)
  }
})

class LogBroadcaster extends EndableWrapper implements SimpleLogger {
  readonly logFormatter = new GelfLogFormatter()
  readonly logFilter = new LogFilterImpl(Settings.logServerLevel)
  socket: Maybe<Socket>

  constructor(
    readonly host: string,
    readonly port: number
  ) {
    super(
      "log.LogBroadcaster(" + host + ":" + port + ")",
      () => this.socket?.end(),
      EndableRanks.logtail
    )
    void this.#mkSocket()
  }

  enabled(level: LogLevel, context?: string | undefined): boolean {
    return this.logFilter.enabled(level, context)
  }

  log(level: LogLevel, context: string, msg: string, meta?: any) {
    if (this.enabled(level, context)) {
      try {
        const s = this.socket
        if (s == null) {
          void this.#mkSocket()
        } else {
          s.write(this.logFormatter.format(level, context, msg, meta) + nul)
        }
      } catch (error) {
        consoleLogger().warn("Failed to write log entry to socket", {
          level,
          context,
          msg,
          meta,
          error
        })
      }
    }
  }

  flush() {
    const l = new Latch()
    this.socket?.write("", () => l.resolve())
    return l.promise
  }

  readonly #mkSocket = rateLimited({
    f: async () => {
      try {
        const s = (this.socket = createConnection({
          host: this.host,
          port: this.port
        }))
        s.on("close", () => {
          this.socket = undefined
        })
        s.on("error", (error: any) => {
          consoleLogger().warn("Error from logging server", error)
          try {
            s.end()
          } catch {
            //
          }
        })
      } catch (error) {
        consoleLogger().warn("Failed to connect to the log server", error)
      }
    },
    minCallDelayMs: minuteMs,
    name: "#mkSocket"
  })
}
