import { Command, program } from "commander"
import { argv, exit } from "process"
import { version } from "../../core/Version"
import { mapNotBlankOr } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Obj } from "../../fe/Object"
import { ServiceName } from "../../fe/ServiceNames"
import { EditionName } from "../EditionName"
import { isTest } from "../NodeEnv"
import { setServiceName } from "../SetServiceName"
import { joinLines } from "../fs/CRLF"
import { isMainElectron } from "../platform/Platform"
import { CliDesc, addFooter } from "./CliConstants"
import { cliWrap } from "./CliWrap"
import { DescriptionFooter } from "./DescriptionFooter"
import { verifyUidGid } from "./VerifyUidGid"

export interface CommandPlugin {
  beforeParse(cmd: Command): Command
  afterParse?(opts: Obj): void
}

export class CLI {
  private readonly plugins: CommandPlugin[] = []

  /**
   * @param args if non-empty, passed to `Command.arguments()`
   */
  constructor(
    readonly serviceName: keyof typeof CliDesc & ServiceName,
    readonly args?: string,
    readonly additionalDescription?: string
  ) {
    setServiceName(serviceName)
    verifyUidGid()
  }

  add(...plugins: CommandPlugin[]) {
    this.plugins.push(...plugins)
    return this
  }

  parse() {
    let cmd = addFooter(
      program.description(
        cliWrap(
          CliDesc[this.serviceName] +
            mapNotBlankOr(
              this.additionalDescription,
              ea => "\n\n" + ea,
              () => ""
            )
        ).join("\n")
      ) as Command
    )

    map(this.args, ea => {
      cmd = cmd.arguments(ea)
    })

    for (const ea of this.plugins) {
      cmd = ea.beforeParse(cmd)
    }

    cmd.option(
      "-V, --version",
      "Output version information (spoiler: it's " +
        (isTest ? "1.2.3-test" : version) +
        ")"
    )

    cmd.on("option:version", () => {
      console.log(
        joinLines(
          ...cliWrap(EditionName() + " v" + version + "\n" + DescriptionFooter)
        )
      )
      exit(0)
    })

    cmd.parse(argv, { from: isMainElectron ? "electron" : "node" })

    const opts = cmd.opts()

    for (const ea of this.plugins) {
      ea.afterParse?.(opts)
    }

    return cmd
  }
}
