import { stringify } from "../../fe/JSON"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { FifoCacheAsync, FifoCacheAsyncOpts } from "../FifoCacheAsync"

export interface MemoizedAsyncFunc<A, R> {
  (a: A): Promise<R>
  clear(a?: A): void | Promise<void>
  size(): number
  callCount(): number
}

export function memoizeAsync<A, R>(
  f: (a: A) => SyncOrAsync<R>,
  opts: FifoCacheAsyncOpts
): MemoizedAsyncFunc<A, R> {
  let callCount = 0
  const store = new FifoCacheAsync<R>(opts)
  const r: any = (a: A) => {
    callCount++
    return store.getOrSetAsync(stringify(a), async () => f(a))
  }
  r.clear = (a?: A) => {
    if (a == null) {
      return store.clear()
    } else {
      const aKey = stringify(a)
      return store.deleteIf(ea => aKey === ea)
    }
  }
  r.size = () => store.size
  r.callCount = () => callCount
  return r
}
