import { orList } from "../../fe/FmtList"
import { stringify } from "../../fe/JSON"

export function valueToS(v: any): string {
  return 1 < 2
    ? stringify(v, undefined, 2) //  just use stringify, it's valid TOML.
    : Array.isArray(v)
    ? orList(v.map(ea => stringify(ea)))
    : stringify(v)
}
