import { isFunction } from "./isFunction"

export interface Thunk<T> {
  (): T
}

export interface ThunkOrLater<T> {
  (): T | Promise<T>
}

export type ThunkOrT<T> = T | Thunk<T>

/**
 * Convert a ThunkOrT into a T.
 *
 * Caution: Don't have T be a function type!
 */
export function tot<T>(t: ThunkOrT<T>): T {
  return isFunction(t) ? t() : t
}

/**
 * Thunk Or Later
 */
export async function tol<T>(t: T | ThunkOrLater<T>): Promise<T> {
  return isFunction(t) ? t() : t
}

export interface ThunkMaybe<T> {
  (): T | undefined
}

export function firstDefinedThunk<T>(
  iter: Iterable<ThunkMaybe<T>>
): T | undefined {
  for (const f of iter) {
    const result = f()
    if (result != null) return result
  }
  return
}

export function firstDefinedSuccess<T>(
  iter: Iterable<ThunkMaybe<T>>
): T | undefined {
  for (const f of iter) {
    try {
      const result = f()
      if (result != null) return result
    } catch {
      //
    }
  }
  return
}

export const NoOp = () => undefined
