import { uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { ensurePrefix, stripPrefix } from "../../fe/String"
import { PS_LIBRARY_SCHEME } from "../../fe/URI"
import { libraryOriginalsDirPosixFile } from "../dir/LibraryDirs"
import { containedByNativePath, posixPathFrom } from "../fs/Path"
import { PosixFile } from "../fs/PosixFile"
import { Settings } from "../settings/Settings"
import { URI } from "./URI"

export const PSLIB_ROOT_URI = URI.from({
  scheme: PS_LIBRARY_SCHEME,
  path: ""
})

export function nativePath2pslib(nativePath: string) {
  if (blank(nativePath)) return

  const libDir = Settings.libraryDir.valueOrDefault // < valueOrDefault for Docker
  if (blank(libDir)) return

  const parent = uniq([libDir, libraryOriginalsDirPosixFile()]).find(dir =>
    containedByNativePath({
      ancestor: dir,
      descendant: nativePath,
      acceptSelf: true
    })
  )

  if (parent == null) return

  // We can't just `"/" + ` because that breaks for the root library dir :(

  const path = ensurePrefix(posixPathFrom(parent, nativePath), "/")

  return URI.from({
    scheme: PS_LIBRARY_SCHEME,
    path
  })
}

export function pslib2nativePath(uri: URI): Maybe<string> {
  if (uri.scheme !== PS_LIBRARY_SCHEME) {
    throw new Error("invalid URI: " + uri + " (bad scheme)")
  }
  const origDir = libraryOriginalsDirPosixFile()
  if (origDir == null) {
    throw new Error("invalid URI: " + uri + " (no library set)")
  }
  // strip initial "/" because we want a relative path
  const relativePath = stripPrefix(uri.path, "/")
  const orig = origDir.join(relativePath)

  // there may be two directories to look in: $PS_LIBRARY and $PS_ORIGINALS.
  // Return the originals dir if it exists, else the library dir if it exists,
  // else the originals dir path.

  if (orig.isFileSync()) return orig.nativePath

  if (!origDir.eql(Settings.libraryDir.valueOrDefault)) {
    const lib = PosixFile.for(Settings.libraryDir.valueOrDefault!).join(
      relativePath
    )
    if (lib.isFileSync()) return lib.nativePath
  }

  // give up
  return orig.nativePath
}
