import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { Settings } from "../settings/Settings"
import { StringSetting } from "../settings/StringSetting"
import { LogFilter, LogLevelContext, LogLevelRe } from "./LogFilter"
import { LogLevel, LogLevels, levelIndex } from "./LogLevel"

export class LogFilterImpl implements LogFilter {
  silent = false
  defaultLevelIndex!: number
  private readonly contexts: LogLevelContext[] = []

  /**
   * Parse out directives like LOG=ExifTag:debug,FileIterator:info,warn
   */
  constructor(readonly setting: StringSetting = Settings.logLevel) {
    setting.watch(() => this.#setup.refresh())
  }

  readonly #setup = lazy(() => {
    this.contexts.length = 0
    const logLevel = this.setting.valueOrDefault
    this.defaultLevelIndex =
      LogLevels.indexOf(logLevel) ??
      LogLevels.indexOf(this.setting.defaultValue) ??
      LogLevels.indexOf(LogLevels.warn)!
    const arr = compactBlanks(logLevel.split(/[,;]/))
    for (const ea of arr) {
      const match = LogLevelRe.exec(ea.trim())
      if (match == null) {
        console.error("LogFilterImpl: Ignoring '" + ea + "' from " + logLevel)
      } else {
        const prefix = toS(match[1]).toLowerCase()
        const idx = levelIndex(match[2])
        if (blank(prefix)) {
          this.defaultLevelIndex = idx
        } else {
          this.contexts.push({ prefix, levelIndex: idx })
        }
      }
    }
  })

  private contextOverride(context?: string): Maybe<LogLevelContext> {
    if (context == null || this.contexts.length === 0 || blank(context)) return
    const s = toS(context).toLowerCase()
    return this.contexts.find(ea => s.startsWith(ea.prefix))
  }

  enabled(level: LogLevel, context?: string): boolean {
    if (this.silent) return false
    const li = levelIndex(level)

    // PERF: unrolled from Opt
    if (context != null) {
      const co = this.contextOverride(context)
      if (co != null) {
        return li <= co.levelIndex
      }
    }
    return li <= this.defaultLevelIndex
  }

  highlight(context: string): boolean {
    // PERF: unrolled from Opt
    const co = this.contextOverride(context)
    return co != null && co.levelIndex >= this.defaultLevelIndex
  }
}
