import path from "path"
import { compactBlanks } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { AppName } from "../AppName"
import { env } from "../env/Env"
import { getEnv } from "../env/GetEnv"
import { isMac, isWin } from "../platform/Platform"
import { FirstDirArg, firstDir } from "./FirstDir"
import { homeDir } from "./HomeDir"

export function desktopConfigDir() {
  return firstDir({
    dirs: desktopConfigDirs(),
    desc: "config"
  })!
}

export function desktopConfigDirs(): FirstDirArg[] {
  const dirs: Maybe<string>[] = []

  // Note that process.env.PS_CONFIG_DIR is examined before this method is invoked.

  if (isWin) {
    dirs.push(getEnv("APPDATA"), path.resolve(homeDir(), "AppData", "Roaming"))
  } else if (isMac) {
    dirs.push(path.resolve(homeDir(), "Library", "Application Support"))
  } else {
    dirs.push(
      env().XDG_DATA_HOME,
      env().XDG_CONFIG_HOME,
      path.resolve(homeDir(), ".config")
    )
  }

  return compactBlanks(dirs).map(ea => ({
    dir: path.join(ea, AppName()),
    preexistingDir: ea
  }))
}
