import process from "process"
import { isTrue } from "../fe/Boolean"
import { toS } from "../fe/toS"
import { isPacked } from "./platform/IsPacked"

// new mocha 10 uses "mocha.js"
const MaybeTestArgRe = /mocha(?:\.js)$|\.spec\.js$/

export function _nodeEnv() {
  switch (toS(process.env.NODE_ENV).toLowerCase()) {
    case "test":
    case "testing":
      return "test"
    case "dev":
    case "development":
      return "development"
    case "prod":
    case "production":
      return "production"
    default:
      if (
        !isPacked() &&
        process.argv.some(ea => MaybeTestArgRe.exec(ea) != null)
      ) {
        return "test"
      } else {
        return "production"
      }
  }
}

// Make sure NODE_ENV gets the final value. If not, stuff like Pug stays in
// dev mode: https://pugjs.org/api/express.html
export const nodeEnv = (process.env.NODE_ENV = _nodeEnv())

export const isDev = nodeEnv === "development"
export const isTest = nodeEnv === "test"
export const isProd = nodeEnv === "production"

export function isSingleSpecTests() {
  return isTest && isTrue(process.env.SINGLE_SPEC_TESTS)
}

export function setSingleSpecTests(b: boolean) {
  process.env.SINGLE_SPEC_TESTS = b ? "true" : "false"
}
