import { ChildProcess } from "child_process"
import _n from "net"
import _s from "stream"
import { delay } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { Maybe, MaybeNull } from "../../fe/MaybeTypes"
import { maybeCall } from "../../fe/Object"
import { NoOp } from "../../fe/Thunk"
import { Try } from "../Object"
import { ending } from "../async/Endable"
import { isIgnorableError } from "../error/ErrorTypes"

export class PassthroughStream extends _s.Duplex {
  _write(chunk: any, encoding: BufferEncoding) {
    this.push(chunk, encoding)
  }
}

/**
 * Handles backpressure by optionally waiting for the drain event
 */
export async function writeAsync(
  stream: _s.Writable,
  data: string | Buffer
): Promise<void> {
  return new Promise<void>((resolve, reject) => {
    if (
      false ===
      stream.write(data, err => {
        if (err) reject(err)
      })
    ) {
      stream.once("drain", resolve)
    } else {
      resolve()
    }
  })
}

// I don't want to just Pick<Writable, "end"> because I don't want to require
// all the overloaded signatures as well.
export interface EndableStream {
  end(chunk: any, cb?: () => void): void
}

export async function endStream(
  endable: MaybeNull<EndableStream>
): Promise<void> {
  if (endable == null) return
  // Make sure we don't hang waiting for this stream:
  Try(() => maybeCall(endable, "unref"))
  if (ending()) {
    // no need to wait for the end callback:
    endable.end(null)
  } else {
    // Half-closes the socket (!!)
    await new Promise<void>(resolve => endable.end(null, resolve))
  }
  await delay(50)

  // Ensures that no more I/O activity happens on this socket
  Try(() => maybeCall(endable, "destroy"))
  return
}

export interface ClosableStream {
  close(callback: () => void): void
}

export async function closeStream(
  closable: Maybe<ClosableStream>
): Promise<void> {
  if (closable == null) return
  Try(() => maybeCall(closable, "unref"))
  if (ending()) {
    closable.close(NoOp)
  } else {
    await new Promise<void>(resolve => closable.close(resolve))
  }
  // I don't think this is necessary (and caused errors with electron last year):
  // await delay(50)
  // Try(() => maybeCall(closable, "destroy"))
  return
}

export function onChildError(
  cp: ChildProcess,
  f: (src: "cp" | "stdin" | "stdout" | "stderr", err: Error) => void
): void {
  ;[
    { name: "cp", ea: cp },
    { name: "stdin", ea: cp.stdin },
    { name: "stdout", ea: cp.stdout },
    { name: "stderr", ea: cp.stderr }
  ].forEach(({ name, ea }) =>
    map(ea, ea2 =>
      ea2.on("error", err => {
        if (isIgnorableError(err) === false) f(name as any, err)
      })
    )
  )
}

export function closeStreams(cp: ChildProcess) {
  for (const ea of [cp?.stdin, cp?.stdout, cp?.stderr]) {
    try {
      ea?.destroy()
    } catch {
      //
    }
  }
}

export type Pipeline = [
  NodeJS.ReadableStream,
  ...(NodeJS.ReadWriteStream | NodeJS.WritableStream)[]
]

export function remoteDesc(s: _n.Socket): string {
  return s.destroyed
    ? "destroyed"
    : `${s.remoteFamily}:${s.remoteAddress}:${s.remotePort}`
}

export class ByteCounter extends _s.Transform {
  private bytes = 0
  constructor(readonly onProgress: (bytes: number) => any) {
    super({
      transform: (
        chunk: any,
        _encoding: BufferEncoding,
        callback: _s.TransformCallback
      ) => {
        this.onProgress((this.bytes += chunk.length))
        callback(chunk)
      }
    })
  }
}
