import { inspect } from "util"
import { mapNotEmpty } from "../../fe/Array"
import { flatten } from "../../fe/Flatten"
import { map, orElse } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { isNumber, sigFigs } from "../../fe/Number"
import { tap } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { zip } from "../Array"
import { BoundedList } from "../BoundedList"
import { CountingSet } from "../CountingSet"
import { mapGte0f } from "../Number"
import {
  MaybeNumericList,
  avg,
  max,
  min,
  slope,
  stdDev,
  variance,
  weightedAvg
} from "./Vector"

export interface AverageStats {
  k: number
  mean?: number
  mode?: number
  sd?: number
  max?: number
  min?: number
  sum?: number
}

export function averageStats(vec: number[]) {
  return new Average().pushAll(vec).stats()
}

export class Average {
  static merge(a: Average, b: Average): Average {
    if (a.n === 0 && b.n === 0) {
      return new Average(Math.max(a.maxSamples, b.maxSamples))
    } else if (a.n === 0) {
      return b.clone()
    } else if (b.n === 0) {
      return a.clone()
    } else if (a.n <= a.maxSamples) {
      const r = b.clone()
      r.pushAll(a.samples)
      return r
    } else if (b.n <= b.maxSamples) {
      const r = a.clone()
      r.pushAll(b.samples)
      return r
    } else {
      const r = new Average(Math.max(a.maxSamples, b.maxSamples))
      r._n = a.n + b.n
      r._min = Math.min(a._min!, b._min!)
      r._max = Math.max(a._max!, b._max!)
      r._m = (a._m! * a.n) / r.n + (b._m! * b.n) / r.n
      r._s = (a._s! * a.n) / r.n + (b._s! * b.n) / r.n
      const samples = flatten(zip(a.samples, b.samples))
      r._samples.push(...samples)
      r._weightedTotalAvg = weightedAvg([r._m, ...samples])
      return r
    }
  }

  protected _n: number
  protected _min?: number
  protected _max?: number
  protected _m?: number
  protected _s?: number
  protected _weightedTotalAvg?: number
  protected readonly _samples: BoundedList<number>

  constructor(readonly maxSamples: number = 20) {
    this._n = 0
    this._samples = new BoundedList(maxSamples)
  }

  [inspect.custom]() {
    return this.stats()
  }

  toJSON() {
    return {
      _ctor: "Average",
      n: this._n,
      _m: this._m,
      _s: this._s,
      min: this._min,
      max: this._max,
      samples: this._samples.toA()
    }
  }

  static fromJSON(obj: ReturnType<Average["toJSON"]>) {
    return new Average().with(obj)
  }

  with(obj: ReturnType<Average["toJSON"]>) {
    this._n = obj.n
    this._min = obj.min
    this._max = obj.max
    this._m = obj._m
    this._s = obj._s
    this._samples.push(...toA(obj.samples))
  }

  merge(b: Maybe<Average>) {
    if (b == null || b.n === 0) return this
    if (this.n === 0) return this.with(b.toJSON())
    this._min = min([this._min, b.min])
    this._max = max([this._max, b.max])
    const n = this.n + b.n
    this._m = (this._m! * this.n) / n + (b._m! * b.n) / n
    this._s = (this._s! * this.n) / n + (b._s! * b.n) / n
    this._n += b._n
    this._samples.push(...toA(b.samples))
    return this
  }

  push(x: number): number {
    if (!isFinite(x)) throw new Error("Average.push(" + x + "): not a number")
    this._n++
    this._samples.push(x)
    this._min = this._min == null ? x : Math.min(x, this._min)
    this._max = this._max == null ? x : Math.max(x, this._max)

    // https://math.stackexchange.com/a/116344
    // https://www.johndcook.com/blog/standard_deviation/
    if (
      this._n === 1 ||
      this._m == null ||
      this._s == null ||
      this._weightedTotalAvg == null
    ) {
      this._m = x
      this._s = 0
      this._weightedTotalAvg = x
    } else {
      const priorM = this._m
      this._m += (x - priorM) / this._n
      // CAUTION: this isn't correct variation: it's a streaming estimation.
      this._s += ((x - this._m) * (x - this._m) - this._s!) / this._n
      this._weightedTotalAvg = (this._weightedTotalAvg + x) / 2
    }
    return x
  }

  clone(): Average {
    return tap(new Average(this.maxSamples), ea => {
      ea._n = this._n
      ea._m = this._m
      ea._s = this._s
      ea._min = this._min
      ea._max = this._max
      ea._weightedTotalAvg = this._weightedTotalAvg
      ea._samples.push(...this._samples)
    })
  }

  pushAll(arr: MaybeNumericList): this {
    if (arr != null) {
      for (const ea of arr) {
        if (isNumber(ea)) this.push(ea)
      }
    }
    return this
  }

  stats(sigfigs = 2): AverageStats {
    const sf = (i?: number) =>
      map(i, (ea: number) => (ea > 100 ? Math.round(ea) : sigFigs(ea, sigfigs)))
    const o: AverageStats = {} as any
    o.k = sf(this.n)!
    if (!this.empty) {
      o.mean = sf(this.avg)
      o.sum = sf(this.sum)
      // o.mode = sf(this.sampleMode)
      o.sd = sf(this.stdDev)
      o.max = sf(this.max)
      o.min = sf(this.min)
    }
    return o
  }

  get empty(): boolean {
    return this._n === 0
  }

  /**
   * @return the total number of samples provided to `push()`
   */
  get n(): number {
    return this._n
  }

  get avg(): Maybe<number> {
    return this.empty ? undefined : sigFigs(this._m!, 4)
  }

  get sum(): number {
    return this._n > 0 ? this._m! * this._n : 0
  }

  get max(): Maybe<number> {
    return this._max
  }

  get min(): Maybe<number> {
    return this._min
  }

  get last(): Maybe<number> {
    return this._samples.last
  }

  #p(sigmasFromMean: number) {
    return mapGte0f(this.avg, mu =>
      mapGte0f(this.stdDev, sigma => sigFigs(mu + sigma * sigmasFromMean, 4))
    )
  }

  // https://www.desmos.com/calculator/s2ybihaqjup

  /**
   * @return mean - 1 SD
   */
  get p16(): Maybe<number> {
    return this.#p(-1)
  }

  get p29(): Maybe<number> {
    return this.#p(-0.5)
  }

  get p38(): Maybe<number> {
    return this.#p(-0.25)
  }

  /**
   * @return mean + 1 SD
   * @see https://en.wikipedia.org/wiki/68%E2%80%9395%E2%80%9399.7_rule
   */
  get p84(): Maybe<number> {
    return this.#p(1)
  }

  /**
   * 97.5% = 50% + (95% / 2)
   * @return mean + 1 SD
   * @see https://en.wikipedia.org/wiki/68%E2%80%9395%E2%80%9399.7_rule
   */
  get p98(): Maybe<number> {
    return this.#p(2)
  }

  /**
   * 99.86% = 50% + (99.73% / 2)
   * @return mean + 1 SD
   * @see https://en.wikipedia.org/wiki/68%E2%80%9395%E2%80%9399.7_rule
   */
  get p99(): Maybe<number> {
    return this.#p(3)
  }

  /**
   * @return streaming estimation of variance
   */
  get variance(): Maybe<number> {
    return this._s
  }

  /**
   * @return streaming estimation of stdev
   */
  get stdDev(): Maybe<number> {
    const v = this.variance
    return v == null ? undefined : Math.sqrt(v)
  }

  get sampleMode(): Maybe<number> {
    return this.sampleModes(1)?.[0]
  }

  sampleModes(n: number): Maybe<number[]> {
    if (this.empty) return
    const c = new CountingSet<number>()
    this._samples.forEach(ea => c.incr(ea))
    return c.topKeys(n)
  }

  get sampleVariance(): Maybe<number> {
    return mapNotEmpty(this._samples, variance)
  }

  get sampleStdDev(): Maybe<number> {
    return mapNotEmpty(this._samples, stdDev)
  }

  get sampleAvg(): Maybe<number> {
    return mapNotEmpty(this._samples, avg)
  }

  get sampleSlope(): number {
    return orElse(mapNotEmpty(this._samples, slope), 0)
  }

  get samples(): number[] {
    return [...this._samples]
  }

  /**
   * weighted average of the last `sampleCount` values
   */
  get weightedSampleAvg(): Maybe<number> {
    return map(weightedAvg(this._samples), ea => sigFigs(ea, 4))
  }

  /**
   * weighted average of all values
   */
  get weightedTotalAvg(): Maybe<number> {
    return this._weightedTotalAvg
  }

  clear(): void {
    this._n = 0
    this._m = undefined
    this._s = undefined
    this._weightedTotalAvg = 0
    this._samples.length = 0
  }
}
