import _path from "path"
import { SimpleFile, toNativePath_ } from "./SimpleFile"
import { writeTextfile_ } from "./WriteFile"

export function mkNoMedia_(dir: SimpleFile | string) {
  return writeTextfile_(
    _path.join(toNativePath_(dir), ".NoMedia"),
    `This directory's contents are excluded from PhotoStructure libraries.`,
    ``,
    `See https://photostructure.com/nomedia/ for details.`
  )
}
