import { compact } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { map, orElse } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { opt } from "../../fe/Opt"
import { toS } from "../../fe/toS"
import { thenMap } from "../async/Promise"
import { stdout_ } from "../child/ChildProcess"
import { parseFixed } from "../Fixed"
import { wmic } from "../fs/PathTo"
import { toMap } from "../Map"
import { Try } from "../Object"
import { isWin } from "../platform/Platform"
import { PowerShell } from "../pwsh/PowerShell"
import { ensureSuffix } from "../String"
import { lazyFsAsync } from "./LazyFsAsync"
import { Volume } from "./Volume"

/*
$ wmic netuse get LocalName, RemoteName, COnnectionState, Status
ConnectionState  LocalName  RemoteName        Status
Connected        H:         \\10.1.1.7\homes  OK
Disconnected     I:         \\10.1.1.3\mrm    Unavailable
*/

export async function addRemoteVolumeInfoWin(
  volumes: Volume[],
  netInfos?: NetInfo[]
): Promise<Volume[]> {
  if (!isWin) throw new Error("wtf")
  await thenMap(
    orElse<NetInfo[] | PromiseMaybe<NetInfo[]>>(netInfos, () => netInfoWin()),
    arr => {
      const m = toMap(arr, ea => [ea.mountpoint, ea])
      volumes.forEach(vol => {
        map(m.get(vol.mountpoint), netInfo => {
          vol.remote = true
          vol.remoteHost = netInfo.host
          vol.remoteShare = netInfo.share
          vol.ok = netInfo.ok
        })
      })
    }
  )
  return volumes
}

const columns = ["LocalName", "RemoteName", "Status"]
const netuseCmd = ["NETUSE", "get", columns.join(",")]

export interface NetInfo {
  /** "H:\" */
  mountpoint: string
  /** probably the IP address of the remote share */
  host: string
  /** the name of the share */
  share: string
  /** true if the Status of the share is "OK" */
  ok: boolean
}

const UNC_RE = /^\\\\(.+?)\\(.+)$/
const driveLetterRE = /^[a-z]:\\?$/i

export const NetInfoCmd = `Get-WmiObject Win32_NetworkConnection | Select-Object -Property LocalName,RemoteName,ConnectionState,Status`

async function _netInfoWin(): Promise<NetInfo[]> {
  const result = await PowerShell.instance().executeJsonToA(NetInfoCmd)
  return result == null
    ? _netInfoWinWmic()
    : compact(
        result
          .filter((ea: any) => notBlank(ea.LocalName)) // can't do anything if we don't have a localname.
          .map((ea: any) =>
            map(parseRemoteName(ea.RemoteName), ({ host, share }) =>
              map(driveLetterRE.exec(toS(ea.LocalName)), driveLetter => ({
                mountpoint: ensureSuffix(driveLetter[0], "\\"),
                host,
                share,
                ok: ea.Status === "OK" && ea.ConnectionState === "Connected"
              }))
            )
          )
      )
}

export function parseRemoteName(
  remoteName: string
): Maybe<{ host: string; share: string }> {
  if (blank(remoteName)) return

  return opt(remoteName)
    .flatMap(ea => UNC_RE.exec(ea))
    .map(ea => ({
      host: ea[1],
      share: ea[2]
    }))
    .orElse(() =>
      opt(remoteName)
        .flatMap(url => Try(() => new URL(url)))
        .filter(url => notBlank(url.hostname))
        .map(url => ({
          host: url.hostname,
          share: opt(url.pathname)
            .filter(notBlank)
            .getOrElse(() => "/")
        }))
    )
    .get()
}

export async function _netInfoWinWmic(): Promise<NetInfo[]> {
  const cmd = wmic()
  const sout = await stdout_(cmd, netuseCmd, { timeoutMs: 15 * secondMs })
  const parsed = parseFixed(columns, sout)
  return compact(
    parsed.map((ea: any) =>
      map(UNC_RE.exec(toS(ea.RemoteName)), remoteName =>
        map(driveLetterRE.exec(toS(ea.LocalName)), driveLetter => ({
          mountpoint: ensureSuffix(driveLetter[0], "\\"),
          host: remoteName[1],
          share: remoteName[2],
          ok: ea.Status === "OK"
        }))
      )
    )
  )
}

const cacheForever = 0
const netInfoWin = lazyFsAsync("netInfoWin", _netInfoWin, cacheForever)
