import {
  filterInPlace,
  isEmpty,
  isNotEmpty,
  pushUniq,
  sum,
  uniq
} from "./Array"
import { sortBy } from "./ArraySort"
import { eql } from "./Eql"
import { stringify } from "./JSON"
import { getOrSet } from "./Map"
import { map } from "./Maybe"
import { Maybe } from "./MaybeTypes"
import { Primitive } from "./Primitive"
import { isString } from "./String"
import { toA } from "./toA"

/**
 * Multi-valued Map.
 */
export class MultiMap<K, V> {
  readonly store: Map<K, V[]>

  constructor(store = new Map<K, V[]>()) {
    this.store = store
  }

  get(key: K): V[] | undefined {
    return this.store.get(key)
  }

  has(key: K): boolean {
    return this.store.has(key)
  }

  includes(key: K, value: V): boolean {
    return this.store.get(key)?.includes(value) === true
  }

  /**
   * @return the number of unique keys in this store
   */
  keyCount(): number {
    return this.store.size
  }

  /**
   * @return the number of values in this store
   */
  valueCount(): number {
    return sum(
      [...this.store.values()].map(ea => ea.length),
      ea => ea
    )
  }

  add(key: K, ...values: V[]): V[] {
    const store = getOrSet(this.store, key, () => [])
    store.push(...values)
    return store
  }

  addUniq(key: K, ...values: V[]): V[] {
    return pushUniq(
      getOrSet(this.store, key, () => []),
      ...values
    )
  }

  set(key: K, values: V[]) {
    this.store.set(key, values)
  }

  delete(key: K, value?: V): boolean {
    if (value == null) {
      return this.store.delete(key)
    } else {
      const arr = this.store.get(key)
      if (arr == null) {
        return false
      } else {
        const result = arr.filter(ea => !eql(ea, value))
        if (result.length === 0) {
          this.store.delete(key)
        } else {
          this.store.set(key, result)
        }
        return arr.length !== result.length
      }
    }
  }

  clear(): this {
    this.store.clear()
    return this
  }

  keys(): IterableIterator<K> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<K> {
      for (const [k, v] of self.store.entries()) {
        if (v.length > 0) {
          yield k
        }
      }
    }
    return iter()
  }

  values(): IterableIterator<V[]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<V[]> {
      for (const [, v] of self.store.entries()) {
        if (v.length > 0) {
          yield v
        }
      }
    }
    return iter()
  }

  vacuum(f: (key: K, values: V[]) => Maybe<V[]> = (_k, v) => uniq(v)) {
    for (const k of this.keys()) {
      const prior = this.store.get(k)
      const arr = prior == null ? [] : f(k, prior)
      if (isEmpty(arr)) {
        this.store.delete(k)
      } else {
        this.store.set(k, arr)
      }
    }
  }

  flatValues(): IterableIterator<V> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<V> {
      for (const [, arr] of self.store.entries()) {
        if (arr.length > 0) {
          for (const ea of arr) {
            yield ea
          }
        }
      }
    }
    return iter()
  }

  entriesArray(): [K, V[]][] {
    return [...this.store.entries()].filter(([, v]) => isNotEmpty(v))
  }

  entries(): IterableIterator<[K, V[]]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<[K, V[]]> {
      for (const [k, v] of self.store.entries()) {
        if (v.length > 0) {
          yield [k, v]
        }
      }
    }
    return iter()
  }

  tuples(): IterableIterator<[K, V]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<[K, V]> {
      for (const [k, v] of self.store.entries()) {
        for (const ea of toA(v)) {
          if (ea != null) yield [k, ea]
        }
      }
    }
    return iter()
  }

  filterInPlace(predicate: (key: K, value: V) => boolean): boolean {
    let changed = false
    for (const [k, arr] of this.store.entries()) {
      const len = arr.length
      filterInPlace(arr, v => predicate(k, v))
      changed = changed || len !== arr.length
      if (arr.length === 0) {
        this.store.delete(k)
      }
    }
    return changed
  }

  inverse(): MultiMap<V, K> {
    const m = new MultiMap<V, K>()
    for (const [k, v] of this.store.entries()) {
      for (const ea of v) {
        m.add(ea, k)
      }
    }
    return m
  }

  findKeyByTuple(predicate: (key: K, value: V) => boolean): Maybe<K> {
    for (const [k, v] of this.tuples()) {
      if (predicate(k, v)) return k
    }
    return
  }

  filterKeysByTuple(predicate: (key: K, value: V) => boolean): K[] {
    const result: K[] = []
    for (const [k, arr] of this.entries()) {
      if (arr.some(v => predicate(k, v))) result.push(k)
    }
    return result
  }

  toJSON(): Record<string, V[]> {
    const result: Record<string, V[]> = {}
    for (const [k, v] of this.entries()) {
      result[isString(k) ? k : stringify(k)] = v
    }
    return result
  }
}

/**
 * Groups the given enumeration
 * @return a copy of arr, sorted by the given constraint
 */
export function groupBy<K extends Primitive, V>(
  arr: V[],
  f: (t: V) => Maybe<K>
): MultiMap<K, V> {
  const m = new MultiMap<K, V>()
  arr.forEach(ea => map(f(ea), k => m.add(k, ea)))
  return m
}

export function groupByValues<K extends Primitive, V>(
  arr: V[],
  f: (t: V) => Maybe<K>
): V[][] {
  const g = groupBy(arr, f)
  return sortBy(toA(g.values()), ea => f(ea[0]))
}
