import { lazy } from "../../core/Lazy"
import { uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import {
  PS_LIBRARY_SCHEME,
  PS_LOCAL_FILE_SCHEME,
  PS_NETWORK_FILESYSTEM_SCHEME
} from "../../fe/URI"
import { toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { thenCompact, thenMap } from "../async/Promise"
import { exists } from "../fs/Stat"
import { Volume } from "../volumes/Volume"
import { bestVolumeForPath } from "../volumes/Volumes"
import { URI } from "./URI"
import { uriEncodingVariants } from "./UriNormalization"
import { nativePath2psfile, psfile2nativePath } from "./psfile"
import { nativePath2pslib, pslib2nativePath } from "./pslib"
import { nativePath2psnet, psnet2nativePath } from "./psnet"

const logger = lazy(() => mkLogger("uri.FileURI"))

export async function nativePath2uri(
  nativePath: string,
  volume?: Volume
): Promise<URI> {
  if (nativePath == null || blank(nativePath)) {
    return logger().throw("empty nativePath passed to nativePath2uri()", {
      retriable: false
    })
  }

  return (
    nativePath2pslib(nativePath) ??
    (await nativePath2psfile(nativePath, volume)) ??
    (await nativePath2psnet(nativePath, volume)) ??
    URI.file(nativePath)
  )
}

export async function nativePath2uris(nativePath: string): Promise<URI[]> {
  const vol = await bestVolumeForPath(nativePath)
  return thenCompact([
    nativePath2pslib(nativePath),
    nativePath2psfile(nativePath, vol),
    nativePath2psnet(nativePath, vol),
    URI.file(nativePath)
  ])
}

export async function nativePath2uriVariants(
  nativePath: string
): Promise<string[]> {
  const arr = await nativePath2uris(nativePath)
  return uniq(flatten(arr.map(uriEncodingVariants)))
}

export async function uriVariants(
  uri: string,
  mountpoint?: string
): Promise<string[]> {
  const result = uriEncodingVariants(uri)
  result.push(
    ...toA(
      await thenMap(uri2nativePath(uri, mountpoint), nativePath2uriVariants)
    )
  )
  return uniq(result)
}

function toURI(uri: string | URI): Maybe<URI> {
  try {
    if (URI.isUri(uri)) return uri
    return URI.parse(uri, true)
  } catch (err) {
    logger().warn("bad URI", { uri, err })
    return
  }
}

export async function uri2nativePath(
  uri: string | URI,
  mountpoint?: string
): PromiseMaybe<string> {
  if (blank(uri)) return
  const u = toURI(uri)
  if (u == null) return

  switch (u.scheme) {
    case "file":
      return u.fsPath
    case PS_LOCAL_FILE_SCHEME:
      return psfile2nativePath(u, mountpoint)
    case PS_NETWORK_FILESYSTEM_SCHEME:
      return psnet2nativePath(u, mountpoint)
    case PS_LIBRARY_SCHEME:
      return pslib2nativePath(u)
    default:
      throw new Error("unsupported URI: " + uri)
  }
}

export async function uriExists(uri: string | URI, mountpoint?: string) {
  const np = await uri2nativePath(uri, mountpoint)
  return np != null && (await exists(np))
}
