import { Maybe, MaybeNull } from "./MaybeTypes"
import { fromEntries } from "./Object"
import { ThunkOrT, tot } from "./Thunk"

// See https://basarat.gitbooks.io/typescript/content/docs/types/literal-types.html

export type StrEnumType<T extends string> = {
  [K in T]: K
}

export type StrEnumHelpers<T extends string> = {
  values: T[]
  length: number
  /** synonym for includes */
  has(s: MaybeNull<string>): s is T
  includes(s: MaybeNull<string>): s is T
  getCI(s: MaybeNull<string>): Maybe<T>
  omit<O extends T>(...t: O[]): Exclude<T, O>[]
  indexOf(s: MaybeNull<string>): Maybe<number>
  ordinal(s: MaybeNull<string>): number
  validOrElse<R>(s: MaybeNull<string>, defaultValue: ThunkOrT<R>): T | R
  mapValid<R>(s: MaybeNull<string>, f: (t: T) => R): Maybe<R>
  cmp(a: MaybeNull<string>, b: MaybeNull<string>): Maybe<number>
}

export type StrEnum<T extends string> = StrEnumType<T> & StrEnumHelpers<T>

export type StrEnumKeys<Type> = Type extends StrEnum<infer X> ? X : never

export function strEnum<T extends string>(...o: T[]): StrEnum<T> {
  const values = Object.freeze(o) as T[]
  const valueToIndex = fromEntries(values.map((ea, idx) => [ea, idx]))

  const dict: StrEnumType<T> = {} as any
  for (const ea of values) {
    dict[ea] = ea
  }

  // toLowerCase() is safe because we know all strEnum values are latin ASCII:
  const getCI = (s: MaybeNull<string>) => {
    if (s == null) return // < remember that some strEnum values are ""
    s = s.toLowerCase()
    return values.find(ea => ea.toLowerCase() === s)
  }

  const indexOf = (s: MaybeNull<string>) =>
    s != null ? valueToIndex[s] : undefined

  const ordinal = (s: MaybeNull<string>) => indexOf(s) ?? values.length

  const includes = (s: MaybeNull<string>): s is T => indexOf(s) != null

  const omit = (...t: T[]) => values.filter(ea => !t.includes(ea)) as any

  const validOrElse = <R>(s: MaybeNull<string>, defaultValue: ThunkOrT<R>) =>
    includes(s) ? s : tot(defaultValue)

  const mapValid = <R>(s: MaybeNull<string>, f: (t: T) => R) =>
    includes(s) ? f(s as T) : undefined

  const cmp = (a: MaybeNull<string>, b: MaybeNull<string>) => {
    const a_ = indexOf(a)
    const b_ = indexOf(b)
    return a_ == null || b_ == null ? undefined : a_ > b_ ? 1 : a_ < b_ ? -1 : 0
  }

  return {
    ...dict,
    values,
    length: values.length,
    has: includes, // alias for includes
    includes,
    getCI,
    omit,
    indexOf,
    ordinal,
    validOrElse,
    mapValid,
    cmp
  }
}

/* 

Example usage:

export const Directions = strEnum("North", "South", "East", "West")
export type Direction = StrEnumKeys<typeof Directions>

*/
