import { notBlank } from "../../fe/Blank"
import { minuteMs, secondMs } from "../../fe/Date"
import { times } from "../../fe/Number"
import { opt } from "../../fe/Opt"
import { toS } from "../../fe/toS"
import { memoizeAsync } from "../async/MemoizedAsyncFunc"
import { stdout_ } from "../child/ChildProcess"
import { pingWin } from "../fs/PathTo"
import { isWin } from "../platform/Platform"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"

export const ping = memoizeAsync(
  async (target: string) => {
    const cmd = isWin ? pingWin() : "ping"
    return stdout_(cmd, [isWin ? "-n" : "-c", "1", target], {
      timeoutMs: 5 * secondMs
    })
  },
  {
    name: "net.ping",
    maxSize: 255,
    timeoutMs: ShortCommandTimeoutMs,
    clearEveryMs: 10 * minuteMs
  }
)

export const ipv4Re = new RegExp(
  "\\b" + times(4, () => "[0-9]{1,3}").join("\\.") + "\\b"
)

export const ipAddrFromPing = memoizeAsync(
  async (target: string) => {
    return opt(
      await ping(target).catch(err => {
        console.warn("failed to ping: " + err)
        return undefined
      })
    )
      .filter(notBlank)
      .flatMap(result => ipv4Re.exec(toS(result)))
      .map(match => match[0])
      .get()
  },
  {
    name: "net.ipAddrFromPing",
    maxSize: 255,
    timeoutMs: ShortCommandTimeoutMs,
    clearEveryMs: 10 * minuteMs
  }
)
