import _path, { join } from "path"
import { lazy } from "../../core/Lazy"
import { compact, compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { SimpleAppName, SimpleAppNameLC } from "../AppName"
import { FifoCacheAsync } from "../FifoCacheAsync"
import { userid } from "../UserInfo"
import { env } from "../env/Env"
import { getEnv } from "../env/GetEnv"
import { onError } from "../error/OnError"
import { WrappedError } from "../error/WrappedError"
import { mkNoMedia_ } from "../fs/MkNoMedia"
import { mkdirpSync_ } from "../fs/Path"
import { isLinux, isMac, isWin } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"
import { Settings } from "../settings/Settings"
import { setSettingsDefaults } from "../settings/SettingsDefaults"
import { DefaultDockerLibraryDir } from "./DefaultDockerLibraryDir"
import { FirstDirArg, filterDirs, firstDir } from "./FirstDir"
import { homeDir } from "./HomeDir"

const uidSuffix = lazy(() => map(userid(), ea => "-" + ea) ?? "")

export function tmpCacheDirs(): FirstDirArg[] {
  if (isDocker()) {
    return [
      {
        dir: "/tmp/.photostructure-cache" + uidSuffix(),
        preexistingDir: "/tmp"
      }
    ]
  } else {
    // Fallback to /tmp and /var/tmp on Linux and macOS, and %TEMP% on Windows:
    const result: FirstDirArg[] = []
    const dirs = isWin ? [getEnv("TEMP"), getEnv("TMP")] : ["/tmp", "/var/tmp"]
    for (const tmpDir of dirs) {
      if (!blank(tmpDir)) {
        result.push({
          dir: join(tmpDir, ".photostructure-cache" + uidSuffix()),
          preexistingDir: tmpDir
        })
      }
    }
    return result
  }
}

export function tmpDirs(): string[] {
  return filterDirs({ dirs: tmpCacheDirs(), desc: "tmp" })
}

export function cacheDirs(): FirstDirArg[] {
  if (isDocker()) {
    return compact([
      map(env().XDG_CACHE_HOME, ea => ({
        dir: join(ea, SimpleAppNameLC),
        preexistingDir: ea
      })),
      // these should be mountpoints. We shouldn't make them.
      { dir: "/ps/tmp", preexistingDir: "/ps/tmp" },
      { dir: "/ps/cache", preexistingDir: "/ps/cache" },
      {
        dir: DefaultDockerLibraryDir + "/.photostructure/cache" + uidSuffix(),
        preexistingDir: DefaultDockerLibraryDir
      },
      ...tmpCacheDirs()
    ])
  }

  const dirs = compactBlanks(
    isWin
      ? [getEnv("LOCALAPPDATA"), _path.resolve(homeDir(), "AppData", "Local")]
      : isMac
      ? [_path.resolve(homeDir(), "Library", "Caches")]
      : // see https://forum.photostructure.com/t/1471/6
        [env().XDG_CACHE_HOME, join(homeDir(), ".cache")]
  )

  // .cache directories on linux seem to be uniformly lowercase, and uniformly
  // UpperCased on macOS and Windows.
  const cacheSubDir = isLinux ? SimpleAppNameLC : SimpleAppName

  // These directories should exist already:
  const result: FirstDirArg[] = dirs.map(ea => ({
    dir: join(ea, cacheSubDir),
    preexistingDir: ea
  }))
  result.push(...tmpCacheDirs())
  return result
}

/**
 * Should only be used by Settings.cacheDir.defaultValue
 */
// lazy for tests
export const defaultCacheDir = lazy<string>(() => {
  return firstDir({ dirs: cacheDirs(), desc: "cache" })!
})

const addSettingsListener = lazy(() => {
  // make sure the config dir setting has the default wired up:
  setSettingsDefaults()
  Settings.cacheDir.watchLater(resetCacheDir)
})

export const cacheDir = lazy<Maybe<string>>(() => {
  try {
    const result = cacheDir_()
    // this is a hack to get around a deploop:
    FifoCacheAsync.cacheDir = result
    return result
  } catch (error) {
    onError("cacheDir_() failed", { error })
    return undefined
  }
})

export const cacheDir_ = lazy<string>(() => {
  addSettingsListener()
  const nativePath = Settings.cacheDir.valueOrDefault
  try {
    mkdirpSync_(nativePath) // < throw if we can't mkdirp
    void mkNoMedia_(nativePath)
    return nativePath
  } catch (cause) {
    throw new WrappedError("Failed to set up cacheDir, " + nativePath, {
      path: nativePath,
      fatal: true,
      cause
    })
  }
})

export function resetCacheDir() {
  Settings.cacheDir.refreshEnvValue({ broadcastChange: false })
  defaultCacheDir.unset()
  cacheDir.unset()
  cacheDir_.unset()
}
