import { delimiter } from "path"
import {
  compact,
  compactBlankish,
  isEmpty,
  isNotEmpty,
  uniq
} from "../../fe/Array"
import { notBlankToS } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import {
  BaseSettingOpts,
  DefaultValued,
  Setting,
  SettingConverters
} from "./Setting"

export function _join(arr: Maybe<string[]>): Maybe<string> {
  return arr == null ? undefined : stringify(arr)
}

function _split(s: Maybe<string>): Maybe<string[]> {
  const str = notBlankToS(s)
  if (str == null) return
  if (str.startsWith("[") && str.endsWith("]")) {
    try {
      return compact(toA(JSON.parse(str)).map(notBlankToS))
    } catch {
      //
    }
  }
  // broken pipe kept for backward compatibility:
  for (const ea of ["", delimiter]) {
    if (str.includes(ea)) {
      return compactBlankish(str.split(ea))
    }
  }
  return [str]
}

export function splitStringArray(s: Maybe<string>): Maybe<string[]> {
  return map(_split(s), compactBlankish)
}

export class StringArraySetting<T extends string = string> extends Setting<
  T[]
> {
  constructor(
    opts: BaseSettingOpts<T[]> &
      Partial<DefaultValued<T[]>> &
      Partial<SettingConverters<T[]>>
  ) {
    super({
      defaultValue: [],
      fromEnv: splitStringArray as any,
      toEnv: _join,
      ...opts
    })
  }

  protected toValidValue(s: Maybe<string>): Maybe<T> {
    return notBlankToS(s) as T
  }

  protected toValidValues(arr: Maybe<string[]>): Maybe<T[]> {
    return arr == null ? undefined : uniq(arr.map(ea => this.toValidValue(ea)))
  }

  push(...values: string[]) {
    if (isEmpty(values)) return
    this.value = this.toValidValues([...toA(this.value), ...toA(values)])
  }

  has(value: string) {
    return toA(this.values).includes(value as any)
  }

  /**
   * valueOrDefault for arrays
   *
   * @todo replace callers with valueOrDefault? This method as it stands isn't
   * safe, as it could be modified by a caller.
   */
  get values(): T[] {
    return this.value ?? this.defaultValue
  }

  set values(arr: Maybe<string[]>) {
    this.userValue = this.toValidValues(arr)
  }

  /**
   * @return if `this.values` is an empty array. Note that this.values delegates to this.defaultValue if this.value is nullish.
   */
  isEmpty() {
    return isEmpty(this.values)
  }

  isNotEmpty() {
    return isNotEmpty(this.values)
  }
}
