import { lazy, MemoizedThunk } from "../../core/Lazy"
import { retryOnReject } from "../../fe/AsyncRetry"
import { later } from "../../fe/Delay"
import { Later } from "../../fe/Later"
import { time } from "../async/PromiseTimer"
import { isIgnorableError, isRetriableError } from "../error/ErrorTypes"
import { ee } from "../event/EventEmitter"
import { Settings } from "../settings/Settings"
import { mountpoints } from "./Mountpoints"
import { commandTimeoutMs } from "./VolumeTtls"

/**
 * lazy-ify `l` with retries
 */
export function lazyFsAsync<T>(
  name: string,
  l: Later<T>,
  ttlMs?: number
): MemoizedThunk<Promise<T>> {
  const result = lazy(
    () =>
      time("volumes." + name, () =>
        retryOnReject(l, {
          maxRetries: 2,
          timeoutMs: commandTimeoutMs(),
          errorIsRetriable: err =>
            isRetriableError(err) !== false && isIgnorableError(err) !== false
        })
      ),
    ttlMs
  )
  later(() => {
    mountpoints.watchLater(() => result.unset())
    ee().on("clearCache", () => result.unset())
    if (ttlMs == null) setupVolumeTTL(result)
  })
  return result
}

export function setupVolumeTTL<T>(l: MemoizedThunk<T>) {
  l.setTTL(Settings.volumeMetadataTtlMs.valueOrDefault)
  Settings.volumeMetadataTtlMs.watchLater(ea => l.setTTL(ea))
}
