import { toS } from "../fe/toS"

const ByteOrderMarks: { bom: number[]; encoding: BufferEncoding }[] = [
  { bom: [0xef, 0xbb, 0xbf], encoding: "utf8" },
  { bom: [0xff, 0xfe], encoding: "utf16le" }
]

// tested by SettingsIO.spec.ts:
export function debom(b: Buffer): string {
  for (const { bom, encoding } of ByteOrderMarks) {
    if (bufferStartsWith(b, bom)) {
      return b.subarray(bom.length).toString(encoding)
    }
  }
  return b.toString()
}

export function bufferToString(b: Buffer | string): string {
  return Buffer.isBuffer(b) ? debom(b) : toS(b)
}

export function bufferStartsWith(b: Buffer, prefix: number[]) {
  return prefix.every((ea, idx) => b[idx] === ea)
}
