import {
  AsyncSubscription,
  Event,
  subscribe,
  SubscribeCallback
} from "@parcel/watcher"
import { Stats } from "fs"
import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { EndableRank, EndableRanks } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { filterAsync } from "../async/Promise"
import { onError } from "../error/OnError"
import { FifoCache } from "../FifoCache"
import { mkLogger } from "../Logger"
import { mkdirp_ } from "./Path"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"
import { statEql, statMaybe } from "./Stat"
import { WipPrefix } from "./WIP"

export type FsEventListener = (events: Event[]) => any

const logger = lazy(() => mkLogger("fs.DirWatcher"))

// TODO: ADD TESTS!

export class DirWatcher extends EndableWrapper {
  static async for(
    dir: SimpleFileOrString,
    cb: SubscribeCallback,
    rank = EndableRanks.first
  ) {
    const nativePath = toNativePath_(dir)
    await mkdirp_(nativePath)

    const path2stat = new FifoCache<Maybe<Stats>>(1024)
    const sub = await subscribe(nativePath, async (err, arr) => {
      logger().trace("callback", { nativePath, err, arr })
      if (err != null) {
        onError("FsWatcher error", { cause: err, path: nativePath })
      }
      if (arr != null) {
        // don't ignore delete events: FsLock wants to know!

        // ignore WIPs and unchanged files:
        arr = await filterAsync({
          name: this.name,
          arr,
          f: async ea => {
            if (ea.path.includes(WipPrefix)) return false
            const prior = path2stat.get(ea.path)
            const current = await statMaybe(ea.path)
            if (true === current?.isFile() && statEql(prior, current)) {
              // only skip _files_ that didn't change
              return false
            } else {
              path2stat.set(ea.path, current)
              return true
            }
          }
        })
      }
      if (err != null || isNotEmpty(arr)) cb(err, arr)
    })

    return new DirWatcher(nativePath, sub, path2stat, rank)
  }

  private constructor(
    readonly nativePath: string,
    readonly subscription: AsyncSubscription,
    readonly path2stat: FifoCache<Maybe<Stats>>,
    rank?: EndableRank
  ) {
    super("fs.DirWatcher(" + nativePath + ")", () => this.close(), rank)
  }

  clear() {
    this.path2stat.clear()
  }

  readonly close = lazy(() => this.subscription.unsubscribe())
}
