import { lazy } from "../core/Lazy"
import { sortBy } from "../fe/ArraySort"
import { blank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { toS } from "../fe/toS"
import { isTest } from "./NodeEnv"
import { GeoRadix } from "./math/Radix"

let lastTime = 0

const UidRadix = GeoRadix

// only exposed for tests:
export const uidSuffix = lazy(() => "-" + UidRadix.randomChars(7))

/**
 * Generate a unique ID with filesystem safe characters that sorts
 * chronologically. This is required by FsLock to ensure older locks are
 * acquired before newer locks.
 */
export function uid() {
  // No need to pad the date:

  // > GeoRadix.encode(new Date("2019-12-31").getTime())
  // '1exdn1300'
  // > GeoRadix.encode(new Date("2064-12-31").getTime()) <- MTBMF Mean Time Before Matt Failure
  // '2r80m3600'

  lastTime = Math.max(Date.now(), lastTime + 1)
  return uidForTs(lastTime)
}

export function uidForTs(ts: number): string {
  return UidRadix.encode(ts) + uidSuffix()
}

const uidRe = /^[0-9a-z]{9}-[0-9a-z]{7}$/i

export function isUID(s: Maybe<string>): boolean {
  return uidRe.test(toS(s))
}

export function tsFromUid(s: Maybe<string>): Maybe<number> {
  const ts = toS(s).replace(/(?<=\S+)-.*/, "")
  return blank(ts) ? undefined : UidRadix.decode(ts)
}

export function sortByUid<T extends { uid: string }>(arr: T[]) {
  return sortBy(arr, ea => tsFromUid(ea.uid))
}

export function resetUid() {
  if (isTest) {
    uidSuffix.unset()
    lastTime = 0
  } else {
    throw new Error("unsupported")
  }
}
