import { SortedSet } from "../SortedSet"
import { stdoutEnded } from "../StdoutEnded"
import { DefaultLogFormatter } from "./DefaultLogFormatter"
import { DefaultLogFlushMs } from "./LogCommon"
import { LogEntry, logEntrySorter } from "./LogEntry"

// Separated from LogTail to break dependency loops

let _logTailEnabled = false

export function logTailEnabled() {
  return _logTailEnabled
}

export function setLogTailEnabled(b: boolean) {
  _logTailEnabled = b
}

export const logEntries = new SortedSet<LogEntry>(logEntrySorter)

// WTH, AMIRITE? ConsoleLogger calls this method. If we're not tailing logs,
// we don't need to order our log entries correctly, and can just console.log
// immediately.

export function pushLogEntries(...arr: LogEntry[]) {
  // PERF: for loop instead of .forEach
  for (const ea of arr) {
    // don't use Settings.logTail:
    if (_logTailEnabled) {
      logEntries.add(ea)
    } else {
      // if stdout is closed, don't try to write to it!
      if (!stdoutEnded()) {
        console.log(DefaultLogFormatter().formatLogEntry(ea))
      }
    }
  }
}

/**
 * @param ttl if zero, pop everything (we're shutting down)
 */
export function popExpiredLogEntries(ttl = DefaultLogFlushMs * 2) {
  return logEntries.shiftLte(Date.now() - ttl)
}
