import { Logger } from "batch-cluster"
import { toNotBlank } from "../../fe/Blank"
import { shortStack } from "../../fe/Error"
import { Later } from "../../fe/Later"
import { map } from "../../fe/Maybe"
import { Obj } from "../../fe/Object"
import { Thunk } from "../../fe/Thunk"
import { isFunction } from "../../fe/isFunction"
import { rootLoggers } from "../Logger"
import { mapGt0 } from "../Number"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { AbortError } from "../async/AbortError"
import { NewWrappedError, toWrappedError } from "../error/WrappedError"
import { LogLevel, LogLevels, msTolevel } from "./LogLevel"
import { SimpleLogger } from "./Logger"

/**
 * Delegates to a SimpleLogger, applying filters, and exposing level-specific
 * log methods.
 */
export class ContextualLogger implements SimpleLogger, Logger {
  readonly context: string
  constructor(
    context: string,
    readonly loggers = rootLoggers
  ) {
    // shite goes real south if context is blank, so let's make sure it's not:
    this.context =
      toNotBlank(context) ??
      // We have to skip over lazy(), mkLogger(), and this constructor:
      shortStack().find(ea => !ea.includes("Logger")) ??
      ""
  }

  toLogJSON() {
    return "CtxLogger(" + this.context + ")"
  }

  /**
   * @return `this.context + s`
   */
  addContext(s: string) {
    return new ContextualLogger(this.context + s, this.loggers)
  }

  addParameterContext(param: string) {
    return new ContextualLogger(this.context + "(" + param + ")", this.loggers)
  }

  throwIfAborted(signal?: AbortSignal) {
    if (signal?.aborted === true) {
      const reason = signal.reason ?? "(no reason given)"
      this.warn("process aborted: " + (signal.reason ?? "(no reason given)"))
      throw new AbortError(reason)
    }
  }

  throw(
    e: string | Error,
    meta?: (NewWrappedError & { error: Error }) | Obj
  ): never {
    const result = toWrappedError(e, { ...meta, message: this.context })
    this.log(
      result.ignorable === true ? "warn" : "error",
      ".throw() " + result.message,
      { stack: result.stack, ...meta }
    )
    throw result
  }

  tap<T>(o: { level?: LogLevel; msg: string; result: T; meta?: any }): T {
    const level =
      o.level ?? mapGt0(o.meta?.elapsedMs, msTolevel) ?? LogLevels.debug
    this.log(level, o.msg, { result: o.result, ...o.meta })
    return o.result
  }

  tapThunk<T>(o: {
    level?: LogLevel
    msg: string
    result: Thunk<T>
    meta?: any
  }): T {
    const start = Date.now()
    let slowTimer
    try {
      slowTimer = setTimeout(
        () => this.log("warn", "slow: " + o.msg),
        ShortCommandTimeoutMs
      )
      const result = o.result()
      const elapsedMs = Date.now() - start
      return this.tap({
        level: o.level,
        msg: o.msg,
        result,
        meta: { ...o.meta, elapsedMs }
      })
    } catch (error) {
      this.log("error", "error: " + o.msg, { error, ...o.meta })
      throw error
    } finally {
      map(slowTimer, clearTimeout)
    }
  }

  async tapAsync<T>(o: {
    level?: LogLevel
    errorLevel?: LogLevel
    msg: string
    errorMsg?: string
    result: Later<T> | Promise<T>
    meta?: any
  }): Promise<T> {
    const level =
      o.level ?? mapGt0(o.meta?.elapsedMs, msTolevel) ?? LogLevels.debug
    try {
      const result = await (isFunction(o.result) ? o.result() : o.result)
      this.log(level, o.msg, { result, ...o.meta })
      return result
    } catch (error) {
      this.log(o.errorLevel ?? "warn", o.errorMsg ?? o.msg + " failed", {
        error,
        ...o.meta
      })
      throw error
    }
  }

  /**
   * Is _any_ logger enabled?
   */
  enabled(level: LogLevel, context?: string): boolean {
    // PERF: avoid closure allocation:
    for (const ea of this.loggers()) {
      if (ea.enabled(level, context ?? this.context)) return true
    }
    return false
  }

  log(level: LogLevel, message: string, meta?: any): void {
    for (const ea of this.loggers()) {
      ea.log(level, this.context, message, meta)
    }
  }

  elapsed(message: string, elapsedMs: number, meta?: any) {
    this.log(msTolevel(elapsedMs), message, { elapsedMs, ...meta })
  }

  async flush() {
    for (const ea of this.loggers()) {
      await ea.flush()
    }
  }

  async end() {
    for (const ea of this.loggers()) {
      await ea.end()
    }
  }

  // Using fat arrows force the methods to stay bound to this instance:
  readonly error = (msg: string, args?: any) => {
    this.log("error", msg, args)
  }

  readonly warn = (msg: string, args?: any) => {
    this.log("warn", msg, args)
  }

  readonly info = (msg: string, args?: any) => {
    this.log("info", msg, args)
  }

  readonly debug = (msg: string, args?: any) => {
    this.log("debug", msg, args)
  }

  readonly trace = (msg: string, args?: any) => {
    this.log("trace", msg, args)
  }
}
