export function firstGt0(...objects: any[]): Maybe<number> {
  return objects.find(gt0)
}

/**
 * @return the first element in `objects` that can be parsed into a number and is
 * greater than zero.
 */
export function firstNonZero(...objects: any[]): Maybe<number> {
  for (const ea of flatten(objects)) {
    const f = toFloat(ea)
    if (f != null && f !== 0) return f
  }
  return undefined
}

export function mapGte0<T>(n: any, f: (i: number) => T): Maybe<T> {
  return mapInt(n, i => (i >= 0 ? f(i) : undefined))
}

export function mapGte0Or<T>(
  n: any,
  f: (i: number) => T,
  defaultValue: T | (() => T)
): T {
  return orElse(mapGte0(n, f), defaultValue)
}

export function mapGte0f<T>(n: any, f: (i: number) => T): Maybe<T> {
  return mapNumeric(n, i => (i >= 0 ? f(i) : undefined))
}

export function mapGt0<T>(n: any, f: (i: number) => T): Maybe<T> {
  const ea = toInt(n)
  return ea != null && ea > 0 ? f(ea) : undefined
}

export function map2Gt0<T>(
  a: any,
  b: any,
  f: (i: number, j: number) => T
): Maybe<T> {
  const i = toInt(a)
  const j = toInt(b)
  return i != null && i > 0 && j != null && j > 0 ? f(i, j) : undefined
}

export function mapGt0f<T>(n: any, f: (i: number) => T): Maybe<T> {
  const ea = toFloat(n)
  return ea != null && ea > 0 ? f(ea) : undefined
}

export function mapGt0Or<T>(
  n: any,
  f: (i: number) => T,
  defaultValue: T | (() => T)
): T {
  return orElse(mapGt0(n, f), defaultValue)
}

// WAT WAT WAAAAAT? (this is the simplest regex I could come up with that
// supported both "-123", "+123.", "-123.456" and "-.789") \

// I could do this:
// const validFloatRe = /((?:[+-]?[0-9]+(?:\.[0-9]*)?)|(?:[+-]?[0-9]*(?:\.[0-9]+)))/

// but that sort of rigor isn't necessary. parseFloat is fine with parsing
// "-123.456GUACAMOLE".
const numstartRe = /[+-]?[0-9\,\.]+/

/**
 * Pull out the first float from `value`.
 *
 * This method ignores any non-numeric characters that prefix or suffix the
 * first number (as opposed to `toFloat`, which ignores only non-numeric
 * suffixes)
 */
export function extractFloat(value: any): Maybe<number> {
  if (isNumber(value)) return value
  if (blank(value)) return undefined
  const s = String(value)
  return map(numstartRe.exec(s), m => toFloat(s.substr(m.index)))
}

/**
 * Pull out the first integer from `value`.
 *
 * This method ignores any non-numeric characters that prefix or suffix the
 * first number (as opposed to `toInt`, which ignores only non-numeric suffixes)
 */
export function extractInt(value: any): Maybe<number> {
  return toInt(extractFloat(value))
}

export function extractFraction(value: any): Maybe<number> {
  if (isNumber(value)) return value
  const s = toS(value)
  if (s.includes("/")) {
    const arr = s.split("/", 2)
    return map2Numeric(extractInt(arr[0]), extractInt(arr[1]), (i, j) => i / j)
  } else {
    return extractFloat(s)
  }
}

export function assertPositive(name: string, value?: number) {
  if (value == null || value <= 0) {
    throw new Error(name + " must be positive")
  }
}

export class Array2D {
  private readonly store: number[] = []
  constructor(readonly columns: number) {}
  get(row: number, col: number): number {
    return row < 0 || col < 0
      ? 0
      : orElse(this.store[row * this.columns + col], () => 0)
  }
  set(row: number, col: number, value: number) {
    this.store[row * this.columns + col] = value
  }
}

function prepHammBigInts(
  a: number | bigint,
  b: number | bigint
): Maybe<[string, string]> {
  if (a == null || b == null) return
  const arr = [a, b].map(ea => ea.toString(2))
  const maxLen = Math.max(...arr.map(ea => ea.length))
  return arr.map(ea => leftPad(ea, maxLen, "0")) as [string, string]
}

/**
 * @return the number of bits not matching between a and b
 */
export function hammingDistanceBigInt(
  a: number | bigint,
  b: number | bigint
): Maybe<number> {
  return map(prepHammBigInts(a, b), ([s1, s2]) =>
    count([...s1], (ea, idx) => ea !== s2.charAt(idx))
  )
}

/**
 * @return `matching bits / total bits`. 1 == complete match.
 */
export function hammRatioBigInt(
  a: Maybe<number | bigint>,
  b: Maybe<number | bigint>
): Maybe<number> {
  if (a == null || b == null) return 0
  return map(prepHammBigInts(a, b), ([i, j]) => hammRatioBinaryString(i, j))
}

export function hammRatioBinaryString(a: string, b: string) {
  if (a === b) return 1
  if (a.length !== b.length)
    throw new Error(`hammRatioBinaryString(${a}, ${b}): invalid lengths`)
  let matching = 0
  // console.log("hammRatioBinaryString a")
  // console.log(splitEvery(a, 24).join("\n"))
  // console.log("hammRatioBinaryString b")
  // console.log(splitEvery(b, 24).join("\n"))
  for (let i = 0; i < a.length; i++) {
    if (a[i] === b[i]) matching++
  }
  // console.log("matching: " + matching)
  // 50% match is random. Scale that to 0.
  return clamp(0, 1, (2 * matching) / a.length - 1)
}

export function valuesToBigInt(arr: number[], bitsPerValue: number): bigint {
  if (isEmpty(arr)) return BigInt(0)
  return BigInt(
    "0b0" +
      arr.map(ea => leftPad(ea.toString(2), bitsPerValue - 1, "0")).join("")
  )
}

export function hammRatioIntArrays(
  a: number[],
  b: number[],
  bitsPerCell: number
) {
  if (a.length !== b.length)
    throw new Error("hammRatioIntArrays(): inequal arrays")
  let diff = 0
  const total = bitsPerCell * a.length
  for (let i = 0; i < a.length; i++) {
    diff += pop(a[i] ^ b[i])
  }

  // 50% match is random. Scale that to 0:
  return clamp(0, 1, (2 * (total - diff)) / total - 1)
}

/**
 * "safe" square-root
 *
 * @return 0 if i is not a number or is negative
 */
export function sqrt(i: number): number {
  return gt0(i) ? Math.sqrt(i) : 0
}
