import * as _path from "path"
import { blank, notBlank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { PS_NETWORK_FILESYSTEM_SCHEME } from "../../fe/URI"
import { toA } from "../../fe/toA"
import { equalsIgnoreCase, stripPrefix } from "../String"
import { native2posix } from "../fs/PosixNative"
import { isReadableDirectory } from "../fs/Stat"
import { isEquivalentHost } from "../net/nslookup"
import { isWin } from "../platform/Platform"
import { Volume } from "../volumes/Volume"
import { bestVolumeForPath, volumes } from "../volumes/Volumes"
import { URI } from "./URI"

export async function nativePath2psnet(
  nativePath: string,
  vol?: Maybe<Volume>
): PromiseMaybe<URI> {
  if (blank(nativePath)) return

  vol ??= await bestVolumeForPath(nativePath)

  // Prefer the volume, if available:
  if (
    vol != null &&
    vol.remote === true &&
    notBlank(vol.remoteHost) &&
    notBlank(vol.remoteShare)
  ) {
    return URI.from({
      scheme: PS_NETWORK_FILESYSTEM_SCHEME,
      authority: vol.remoteHost,
      path: _path.posix.join(
        "/" + vol.remoteShare,
        stripPrefix(native2posix(nativePath), native2posix(vol.mountpoint))
      )
    })
  }

  // If it's a UNC path, URI knows how to handle it:
  if (nativePath.startsWith("\\\\")) {
    return URI.file(nativePath).with({ scheme: PS_NETWORK_FILESYSTEM_SCHEME })
  }

  // Give up:
  return
}

export async function psnet2nativePath(
  uri: URI,
  mountpoint?: string
): PromiseMaybe<string> {
  if (uri.scheme !== PS_NETWORK_FILESYSTEM_SCHEME) {
    throw new Error("invalid URI: " + uri + " (bad scheme)")
  }

  if (blank(uri.authority)) {
    throw new Error("invalid URI: " + uri + " (missing authority)")
  }

  const arr = uri.path.split("/").slice(1) // < slice(1) to skip over the '' due to the absolute path
  const share = arr[0]

  if (blank(share)) {
    throw new Error("invalid URI: " + uri + " (missing share)")
  }

  if (isWin) {
    return `\\\\${uri.authority}\\${arr.join(_path.sep)}`
  }

  const path = arr.slice(1)

  // If there's a mounted drive with this share, use that:
  const vols = await volumes()

  for (const vol of toA(vols)) {
    if (vol.remote !== true) continue
    if (
      equalsIgnoreCase(vol.remoteShare, share) &&
      (await isEquivalentHost(uri.authority, vol.remoteHost))
    ) {
      return _path.join(vol.mountpoint, ...path)
    }
  }

  if (await isReadableDirectory(mountpoint)) {
    return _path.join(mountpoint!, ...path)
  }

  return
}
