export function isExcludedFilesystem(filesystem: string) {
  if (blank(filesystem)) return false
  return isPosix && Settings.excludedFilesystemTypes.values.includes(filesystem)
}

const LinuxDevMapperRE =
  isLinux || isTest ? /^\/dev(?!\/mapper)(?:\/|\/?$)/i : undefined

const LinuxVarSnapRE = isLinux || isTest ? /^\/var\/snap(?:$|\/)/ : undefined

const LinuxRunUserGVFSRE =
  isLinux || isTest ? /^\/run\/user\/\d+\/gvfs\/.+?$/i : undefined

const LinuxDockerRE = isLinux || isTest ? /^\/var\/lib\// : undefined

// Avoid stuff like

// mrm@m1 % mount | grep /System
// /dev/disk3s6 on /System/Volumes/VM (apfs, local, noexec, journaled, noatime, nobrowse)
// /dev/disk3s2 on /System/Volumes/Preboot (apfs, local, journaled, nobrowse)
// /dev/disk3s4 on /System/Volumes/Update (apfs, local, journaled, nobrowse)
// /dev/disk1s2 on /System/Volumes/xarts (apfs, local, noexec, journaled, noatime, nobrowse)
// /dev/disk1s1 on /System/Volumes/iSCPreboot (apfs, local, journaled, nobrowse)
// /dev/disk1s3 on /System/Volumes/Hardware (apfs, local, journaled, nobrowse)
// /dev/disk3s1 on /System/Volumes/Data (apfs, local, journaled, nobrowse, protect)
// map auto_home on /System/Volumes/Data/home (autofs, automounted, nobrowse)

// mrm@mini ~ % mount | grep System
// /dev/disk6s4 on /System/Volumes/VM (apfs, local, noexec, journaled, noatime, nobrowse)
// /dev/disk6s2 on /System/Volumes/Preboot (apfs, local, journaled, nobrowse)
// /dev/disk6s6 on /System/Volumes/Update (apfs, local, journaled, nobrowse)
// /dev/disk6s1 on /System/Volumes/Data (apfs, local, journaled, nobrowse)
// map auto_home on /System/Volumes/Data/home (autofs, automounted, nobrowse)

// DO NOT AVOID "/System/Volumes/Data": that's where people's home dir is!

// (memoized mostly to avoid all the duplicate log messages)
export const isExcludedMountpoint = memoize(
  (mountpoint: Maybe<string>) => {
    if (isWin) return false // no excluded drive letters
    const why = whyExcludedMountpoint(mountpoint)
    return logger().tap({
      msg: "isExcludedMountpoint",
      result: why != null,
      meta: { mountpoint, why }
    })
  },
  { maxSize: 512 }
)

export function whyExcludedMountpoint(
  mountpoint: Maybe<string>
): Maybe<string> {
  if (blank(mountpoint)) {
    return "blank"
  }
  const arr = toPathnames(mountpoint)
  const root = arr[0]
  const basename = arr[arr.length - 1]
  if (Settings.excludedRootDirectories.has(root)) {
    return "ExcludedRootDirectory"
  }
  if (Settings.excludedMountpointPaths.has(mountpoint)) {
    return "excludedMountpointPaths"
  }
  if (Settings.excludedMountpointBasenames.has(basename)) {
    return "excludedMountpointBasenames"
  }
  if (LinuxDevMapperRE?.test(mountpoint) === true) {
    return "LinuxDevMapperRE"
  }
  if (LinuxVarSnapRE?.test(mountpoint) === true) {
    return "LinuxVarSnapRE"
  }
  if (root === "run" && LinuxRunUserGVFSRE?.test(mountpoint) === false) {
    return "LinuxRunRE"
  }
  if (LinuxDockerRE?.test(mountpoint) === true) {
    return "LinuxDockerRE"
  }
  return
}
