import { join } from "path"
import { lazy } from "../../core/Lazy"
import { AppNameVersion } from "../AppName"
import { mkLogger } from "../Logger"
import { joinNativePath, mkdirp_ } from "./Path"
import { readFilePart_ } from "./ReadFilePart"
import { SimpleFileOrString, basename, toNativePath_ } from "./SimpleFile"
import { isNonEmptyFile } from "./Stat"
import { writeTextfile_ } from "./WriteFile"

// https://bford.info/cachedir/
export const CACHEDIR_TAG = "CACHEDIR.TAG"

export const CACHEDIR_TAG_CONTENT =
  "Signature: 8a477f597d28d172789f06886806bc55"

const logger = lazy(() => mkLogger("fs.CachedirTag"))

export async function isCachedirTagDirectory(dir: SimpleFileOrString) {
  return isCachedirTagFile(join(toNativePath_(dir), CACHEDIR_TAG))
}

export async function isCachedirTagFile(
  f: SimpleFileOrString
): Promise<boolean> {
  if (basename(f) !== CACHEDIR_TAG) {
    return false
  }
  if (!(await isNonEmptyFile(toNativePath_(f), CACHEDIR_TAG_CONTENT.length))) {
    return false
  }
  try {
    const header = (
      await readFilePart_({
        nativePath: toNativePath_(f),
        position: 0,
        length: CACHEDIR_TAG_CONTENT.length
      })
    )?.buffer?.toString()
    return header === CACHEDIR_TAG_CONTENT
  } catch (error) {
    logger().debug("isCachedirTagFile(): failed to read " + f, error)
    return false
  }
}

export async function writeCachedirTag_(dir: SimpleFileOrString) {
  const dirPath = toNativePath_(dir)
  await mkdirp_(dirPath)
  await writeTextfile_(
    joinNativePath([dirPath, CACHEDIR_TAG]),
    CACHEDIR_TAG_CONTENT,
    "# This file is a cache directory tag",
    `# created by ${AppNameVersion()}`
  )
}
