import { List } from "../../fe/List"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, finiteOrElse, isNumber } from "../../fe/Number"
import { toA } from "../../fe/toA"
import { CountingSet } from "../CountingSet"
import { intersection, union } from "../Set"
import { Average } from "./Average"

export type NumericList =
  | List<number>
  | Iterable<number>
  | number[]
  | Float32Array
  | Uint32Array
  | Int32Array

export type MaybeNumericList =
  | List<Maybe<number>>
  | Iterable<Maybe<number>>
  | Maybe<number>[]
  | Float32Array
  | Uint32Array
  | Int32Array

export function min<T>(vec: List<Maybe<T>>): Maybe<T> {
  let result
  for (const ea of vec) {
    if (ea != null) {
      if (result == null || ea < result) {
        result = ea
      }
    }
  }
  return result
}

export function max<T>(vec: List<Maybe<T>>) {
  let result
  for (const ea of vec) {
    if (ea != null) {
      if (result == null || ea > result) {
        result = ea
      }
    }
  }
  return result
}

/**
 * @return `[arr[1] - arr[0], arr[2] - arr[1], ...]`
 */
export function deltas(vec: NumericList): number[] {
  const l = toA(vec)
  if (vec == null || l.length <= 1) return []
  return l.slice(1).map((ea, idx) => ea - l[idx])
}

export function modes(vec: MaybeNumericList, topN: number): number[] {
  const cs = new CountingSet<number>()
  for (const ea of vec) {
    if (isNumber(ea)) {
      cs.incr(ea)
    }
  }
  return cs.topKeys(topN)
}

export function mode(vec: MaybeNumericList): Maybe<number> {
  return modes(vec, 1)[0]
}

export function sum(vec: MaybeNumericList | Iterable<Maybe<number>>): number {
  let acc = 0
  for (const ea of vec) {
    if (isNumber(ea)) {
      acc += ea
    }
  }
  return acc
}

export function sumf<T>(
  iter: Iterable<Maybe<T>>,
  f: (t: T, index: number) => Maybe<number>
): number {
  let index = 0
  let acc = 0
  for (const ea of iter) {
    if (ea != null) {
      const i = f(ea, index)
      if (isNumber(i)) {
        acc += i
      }
    }
    index++
  }
  return acc
}

export class Mean {
  #idx = 0
  #mean: Maybe<number>

  push(i: Maybe<number>) {
    if (isNumber(i)) {
      // prevent overflow:
      // https://math.stackexchange.com/a/116344
      // https://www.johndcook.com/blog/standard_deviation/
      this.#mean =
        this.#mean == null
          ? i
          : (this.#mean * this.#idx) / (this.#idx + 1) + i / (this.#idx + 1)
      this.#idx++
    }
  }

  get mean() {
    return this.#mean
  }
}

export function avg(vec: MaybeNumericList): Maybe<number> {
  const m = new Mean()
  for (const ea of vec) {
    m.push(ea)
  }
  return m.mean
}

