/**
 * @param strength how strong should the normalization be applied. [0,1]
 */
export function normalize({
  x,
  strength = 1,
  normMin,
  normMax
}: {
  x: number[]
  strength?: number
  normMin: number
  normMax: number
}): number[] {
  const xMin = min(x)!
  const xRange = max(x)! - xMin
  const normRange = normMax - normMin
  return x.map(
    ea =>
      (1 - strength) * ea +
      strength * (normMin + (normRange * (ea - xMin)) / xRange)
  )
}

export function slope(vec: MaybeNumericList): Maybe<number> {
  const l = toA(vec).filter(isNumber)
  const x_mean = avg(l)
  if (x_mean != null) {
    const y_mean = (l.length - 1) / 2
    const num = sumf(l, (x, y) => (x - x_mean) * (y - y_mean))
    const denom = sumf(l, x => (x - x_mean) ** 2)
    return denom === 0 ? 0 : num / denom
  } else {
    return
  }
}

export function variance(arr: MaybeNumericList): Maybe<number> {
  const mean = avg(arr)
  if (mean == null) return undefined
  let acc = 0
  let len = 0
  for (const i of arr) {
    if (isNumber(i)) {
      len++
      acc += (i - mean) * (i - mean)
    }
  }
  return acc / len
}

export function stdDev(arr: MaybeNumericList): Maybe<number> {
  return map(variance(arr), Math.sqrt)
}

export function p84(arr: MaybeNumericList): Maybe<number> {
  return new Average().pushAll(arr).p84
}

/**
 * @param carryForward ratio of prior means to average with subsequent values
 */
export function weightedAvg(
  arr: List<number> | Iterable<number>,
  carryForward = 0.5
): Maybe<number> {
  let acc
  carryForward = clamp(0, 1, carryForward)
  for (const ea of arr) {
    acc = acc == null ? ea : acc * carryForward + ea * (1 - carryForward)
  }
  return acc
}

