export function centroid(vectors: number[][]): Maybe<number>[] {
  const len = vectors[0].length
  const arr: Maybe<number>[] = []
  for (let i = 0; i < len; i++) {
    arr.push(avg(vectors.map(ea => ea[i])))
  }
  return arr
}

/**
 * Euclidean norm, or L2 norm, of a given vector
 * @see https://en.wikipedia.org/wiki/Norm_(mathematics)
 */
export function l2norm(vec: List<number>): number {
  // PERF: no closures
  let acc = 0
  for (const ea of vec) {
    acc += ea * ea
  }
  return Math.sqrt(acc)
}

/**
 * @see https://en.wikipedia.org/wiki/Euclidean_vector#Dot_product
 */
export function dot(x: number[], y: number[]): number {
  // PERF: no closures
  let acc = 0
  for (let i = 0; i < x.length; i++) {
    acc += x[i] * y[i]
  }
  return acc
}

/**
 * Ranges between -1 (anticorrelation) to 1 (correlation)
 * @see https://en.wikipedia.org/wiki/Cosine_similarity
 */
export function cosineSimilarity(x: number[], y: number[]): Maybe<number> {
  return finiteOrElse(dot(x, y) / (l2norm(x) * l2norm(y)), undefined)
}

/**
 * Ranges between 0 (no overlap) to 1 (complete overlap)
 */
export function jaccard(x: number[], y: number[]): Maybe<number> {
  return isEmpty(x) && isEmpty(y)
    ? 0
    : finiteOrElse(intersection(x, y).size / union(x, y).size, undefined)
}
