import { isTrue } from "../../fe/Boolean"
import { minuteMs } from "../../fe/Date"
import { delay } from "../../fe/Delay"
import { isFunction } from "../../fe/isFunction"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { onTimeout } from "./TimeoutHandler"

export type UntilOpts<T> = {
  timeoutMs?: number
  intervalMs?: number
  acceptable?: (t: Maybe<T>) => boolean
  timeoutResult?: T
  unref?: boolean
}

/**
 * Run `f()` until the result either passes `acceptable`, or is not `null`,
 * `undefined` or `false`.
 */
export async function until<T>(
  f: () => MaybeSyncOrAsync<T>,
  { timeoutMs, intervalMs, acceptable, timeoutResult, unref }: UntilOpts<T> = {}
): PromiseMaybe<T> {
  let sentSoftTimeout = false
  const timeoutAt = timeoutMs == null ? undefined : timeoutMs + Date.now()
  let delayCount = 1
  while (timeoutAt == null || Date.now() < timeoutAt) {
    const start = Date.now()
    const result = await f()
    const accept =
      result != null &&
      (isFunction(acceptable) ? acceptable(result) : (result as any) !== false)

    if (accept) {
      return result
    } else {
      const elapsedMs = Date.now() - start
      if (timeoutMs != null && elapsedMs > timeoutMs / 2 && !sentSoftTimeout) {
        sentSoftTimeout = true
        onTimeout({ soft: true })
      }
      const delayMs =
        intervalMs ??
        clamp(
          10 * delayCount, // < wait longer for subsequent delays.
          ShortCommandTimeoutMs,
          (timeoutMs ?? minuteMs) / 5
        )
      await delay(delayMs, unref)
      delayCount++
    }
  }
  if (!sentSoftTimeout) onTimeout({ soft: false })
  return timeoutResult
}

/**
 * Run the given thunk until the promise is resolved to true, or the timeout
 * passes.
 * @return false on timeout, or true when the the given Later returned true.
 */
export async function untilTrue(
  f: () => MaybeSyncOrAsync<boolean>,
  opts: Omit<UntilOpts<boolean>, "acceptable" | "timeoutResult"> = {}
): Promise<boolean> {
  return until(f, {
    ...opts,
    acceptable: isTrue,
    timeoutResult: false
  }) as Promise<boolean>
}
