import { randomBytes } from "crypto"
import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { isFunction } from "../../fe/isFunction"
import { identity } from "../Object"
import { splitEvery } from "../String"

const zero: any = BigInt(0)

export function encodeDigits(base: number, i: number, minLength = 0): number[] {
  if (!isFinite(i) || base <= 1) return []

  const digits: number[] = []
  if (i === 0) {
    digits.unshift(0)
  } else {
    while (i > 0) {
      digits.unshift(i % base)
      i = Math.floor(i / base)
    }
  }
  while (digits.length < minLength) digits.unshift(0)
  return digits
}

export class Radix {
  readonly base: number
  constructor(
    readonly name: string,
    readonly numerals: string,
    readonly decodePreparser: (s: string) => string = identity
  ) {
    this.base = numerals.length
  }

  private digitsToNumerals(digits: number[]): string {
    return digits.map(d => this.numerals[d]).join("")
  }

  encode(num: number, minLength: number = 0): string {
    if (!isFinite(num)) return ""
    // r is a reversed array of chars:
    const negate = num < 0
    if (negate) {
      num = Math.abs(num)
      minLength-- // accommodate the sign
    }
    return (
      (negate ? "-" : "") +
      this.digitsToNumerals(encodeDigits(this.base, num, minLength))
    )
  }

  encodeBigInt(bi: bigint): string {
    if (typeof bi !== "bigint") throw new Error("bad input")
    if (bi === zero) return this.numerals[0]

    const digits: number[] = []
    const b = BigInt(this.base)
    let i: any = bi

    while (i > zero) {
      digits.push(Number(i % b))
      i = i / b
    }
    return this.digitsToNumerals(digits.reverse())
  }

  encodeBuffer(buf: Buffer): string {
    if (buf == null || buf.length === 0) return ""
    const digits = [0]
    for (let b of buf) {
      digits.forEach((d, i) => {
        // const b0 = b
        b += d << 8
        digits[i] = b % this.base
        b = Math.floor(b / this.base)
        // console.dir({ i, b0, b, d0: d, d: digits[i], digits })
      })

      while (b > 0) {
        digits.push(b % this.base)
        b = Math.floor(b / this.base)
        // console.dir({ b, digits })
      }
    }
    return this.digitsToNumerals(digits.reverse())
  }

  decode(s: Maybe<string>): Maybe<number> {
    return map(this.decodeBigInt(s), ea => {
      if (ea > BigInt(Number.MAX_SAFE_INTEGER)) {
        throw new Error("decode(" + s + ") is > 2^53")
      } else {
        return Number(ea)
      }
    })
  }

  normalize(s: string): string {
    return this.decodePreparser(s)
  }

  decodeBigInt(s: Maybe<string>): Maybe<bigint> {
    if (s == null || blank(s)) return
    s = isFunction(this.decodePreparser) ? this.decodePreparser(s) : s
    const negate = s[0] === "-"
    if (negate) {
      s = s.slice(1)
    }
    const b = BigInt(this.base)
    let acc = BigInt(0)
    for (const ch of s) {
      const idx = this.numerals.indexOf(ch)
      if (idx < 0) {
        return undefined
      }
      acc = acc * b + BigInt(idx)
    }
    return negate ? BigInt(-1) * acc : acc
  }

  randomChars(length: number): string {
    // bits per char is Math.log2(this.base).
    // we need bpc * length / 8 = random bytes.
    const s = this.encodeBuffer(
      randomBytes(Math.ceil((Math.log2(this.base) * (length + 4)) / 8))
    )
    return s.slice(2, 2 + length)
  }

  /**
   * UIDs are easier to parse or read if dashes break up the string.
   *
   * Reading 4-char groups is comfortable, 5 chars "won-aye-six-gee-why" is
   * doable, I think, so split every 5?
   *
   * For GeoRadix, each char encodes 5 bits, so 20 chars is only 100 bits of
   * entropy. 24 chars is 120. We need 32 chars for 160 bits, that's a lot.
   */
  randomUid(chars = 20, splitEveryN = 5, sepChar = "-"): string {
    return splitEvery(this.randomChars(chars), splitEveryN).join(sepChar)
  }

  tokenEql(a: string, b: string, minLen: number): boolean {
    const an = this.normalizeToken(a)
    const bn = this.normalizeToken(b)
    return an.length >= minLen && an === bn
  }

  normalizeToken(a: string) {
    return [...this.decodePreparser(a.trim())]
      .filter(ea => this.numerals.includes(ea))
      .join("")
  }
}

export const Hex = new Radix("hex", "0123456789abcdef", s => s.toLowerCase())

/**
 * URL-safe charset used for bitcoin and IPFS hashes
 * @see https://en.wikipedia.org/wiki/Base58
 */
export const Radix58 = new Radix(
  "Radix58",
  "123456789ABCDEFGHJKLMNPQRSTUVWXYZabcdefghijkmnopqrstuvwxyz"
)

/**
 * CAUTION! This doesn't do standard base64 padding!
 * @see b64.ts
 */
export const Base64 = new Radix(
  "Radix64",
  "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/"
)

/**
 * Used by StringSimilarity.radixDiff
 */
export const RadixAlphaNum = new Radix(
  "RadixAlphaNum",
  "0123456789abcdefghijklmnopqrstuvwxyz",
  s => s.toLowerCase()
)

/**
 * Base 32 charset for case-insensitive filesystems and human consumption
 * @see https://en.wikipedia.org/wiki/Geohash
 */
export const GeoRadix = new Radix(
  "GeoRadix",
  "0123456789bcdefghjkmnpqrstuvwxyz", // y no a?
  s => s.toLowerCase()
)

/**
 * Base 32 charset for case-insensitive filesystems and human consumption.
 *
 * Similar glyphs include 0O, 1lI, 2z, 5S, 9g
 */
export const TokenRadix = new Radix(
  "TokenRadix",
  "0123456789abcdefhjkmnpqrtuvwxy",
  s =>
    s
      .toLowerCase()
      .replace(/o/g, "0")
      .replace(/[il]/g, "1")
      .replace(/z/g, "2")
      .replace(/s/g, "5")
      .replace(/g/g, "9")
)

export const AlphaRadix = new Radix(
  "AlphaRadix",
  "abcdefghjkmnopqrtuvwxyz",
  s =>
    s
      .toLowerCase()
      .replace(/0/g, "o")
      .replace(/2/g, "z")
      .replace(/5/g, "s")
      .replace(/9/g, "g")
)

export const NumericRadix = new Radix("NumericRadix", "0123456789", s =>
  s
    .toLowerCase()
    .replace(/o/g, "0")
    .replace(/[il]/g, "1")
    .replace(/z/g, "2")
    .replace(/s/g, "5")
    .replace(/g/g, "9")
)
