import { sort, sortBy } from "../fe/ArraySort"
import { isNumber, mapNumericOr } from "../fe/Number"
import { tap } from "../fe/Object"
import { toS } from "../fe/toS"
import { Average } from "./math/Average"
import { sum } from "./math/Vector"

export class CountingSet<K extends number | string> {
  private readonly m = new Map<K, number>()

  incr(key: K, count: number = 1): number {
    const v = this.get(key) + count
    if (v === 0) this.m.delete(key)
    else this.m.set(key, v)
    return v
  }

  get(key: K): number {
    return this.m.get(key) ?? 0
  }

  /**
   * If no keys are provided, return the max value
   */
  max(...arr: K[]): number {
    return Math.max(
      ...(arr.length === 0 ? this.m.values() : arr.map(ea => this.get(ea)))
    )
  }

  has(key: K): boolean {
    return this.m.has(key)
  }

  delete(key: K): boolean {
    return this.m.delete(key)
  }

  /**
   * Return the number of unique non-zero `incr`emented keys
   */
  get size(): number {
    return this.m.size
  }

  /**
   * Return the sum of all counts
   */
  get countSum(): number {
    return sum(this.m.values())
  }

  keys(): IterableIterator<K> {
    return this.m.keys()
  }

  /**
   * @return if all keys are numeric, the average value of keys, otherwise, `undefined`.
   */
  keyAvg() {
    const avg = new Average(0)
    for (const k of this.keys()) {
      if (isNumber(k)) {
        avg.push(k)
      } else {
        return
      }
    }
    return avg.avg
  }

  entries(): IterableIterator<[K, number]> {
    return this.m.entries()
  }

  toJSON() {
    return this.toRecord()
  }

  toRecord(): Record<string, number> {
    const obj: any = {}
    for (const [k, v] of this.m.entries()) {
      obj[toS(k)] = v
    }
    return obj
  }

  /** Multimodal ties are solved by proximity to mean */
  entriesByCountDesc(): [K, number][] {
    const keyAvg = this.keyAvg()
    return sortBy([...this.entries()], ([k, v]) => [
      -v,
      mapNumericOr(keyAvg, ea => Math.abs((k as number) - ea), 0)
    ])
  }

  top(n = 1): [K, number][] {
    return this.entriesByCountDesc().slice(0, n)
  }

  topKeys(n = 1): K[] {
    return this.top(n).map(ea => ea[0])
  }

  get averageCounts(): Average {
    return tap(new Average(this.size), a =>
      [...this.m.values()].forEach(ea => a.push(ea))
    )
  }

  forEach(callbackfn: (count: number, key: K) => void): void {
    this.m.forEach(callbackfn)
  }

  clear(): void {
    this.m.clear()
  }

  addAll(s: this) {
    for (const [k, i] of s.entries()) {
      this.incr(k, i)
    }
    return this
  }

  get toS() {
    return sort([...this.keys()])
      .map(key => key + " " + this.get(key))
      .join("\n")
  }

  valuesToA<T>(f: (k: K) => T): T[] {
    const arr: T[] = []
    // pre-allocate:
    arr.length = sum(this.m.values())
    let idx = 0
    for (const [k, count] of this.m.entries()) {
      const value = f(k)
      arr.fill(value, idx, idx + count)
      idx += count
    }
    return arr
  }
}
