import { uniq } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { version } from "../Version"
import { Schemas, pathToDbDir } from "../db/Schemas"
import { NoLibraryErrorFlag } from "../error/ErrorFlags"
import { mkNoMedia_ } from "../fs/MkNoMedia"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { writeTextfile_ } from "../fs/WriteFile"
import { imageCacheDir_ } from "../img/ImageCache"
import { Settings } from "../settings/Settings"
import {
  libraryDataDir,
  libraryDir,
  libraryPreviewsDir,
  originalsDir
} from "./LibraryDir"

const README = `
Hello, and thank you for using PhotoStructure!

The files in this folder support your PhotoStructure Library, including

  * a database with the filepaths to your photos and movies
  * previews and thumbnails of your photos and movies
  * content metadata about these assets, like ratings and sharing information
  * albums that both you and PhotoStructure Curators have assembled
  * synchronization reports describing what directories and files were synced (and why)

Moving or deleting any files here will cause problems using your library.

If you have any questions, please visit https://forum.photostructure.com .

Sincerely,

Your Friendly Neighborhood PhotoStructure, v${version}
`

export function libraryDirPosixFile(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): Maybe<PosixFile> {
  return PosixFile.forMaybe(libraryDir(libraryDirOverride))
}

/**
 * libraryDataDir was pulled out of Library so settings could be written into
 * .photostructure, rather than directly into the library root.
 */
export function libraryDataDirPosixFile(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): Maybe<PosixFile> {
  return PosixFile.forMaybe(libraryDataDir(libraryDirOverride))
}

export async function setupLibraryDirs_(
  libraryDirOverride?: SimpleFileOrString
): Promise<PosixFile[]> {
  const l = libraryDirPosixFile(libraryDirOverride)
  await mkdirRW_(l, false)
  const dataDir = await setupLibraryDataDir_(l)
  return uniq([
    l,
    dataDir,
    // this _is not_ parallel to avoid DDOS on smaller boxen
    await setupLibraryOriginalsDir_(l),
    await setupLibraryPreviewsDir_(l),
    await setupLibrarySyncReportsDir_(l),
    await setupLibraryDbDir_(l),
    await imageCacheDir_()
  ])
}

async function mkdirRW_(dir: Maybe<PosixFile>, neverIndex = true) {
  if (dir == null) return
  await dir?.mkdirp_() // if we can't make this directory, fail.
  await dir?.assertReadWriteExecutable()
  // Tell macOS Spotlight to not index this directory:
  if (neverIndex) await dir?.join(".metadata_never_index").touch()
  return dir
}

/**
 * @throws if there are issues
 */
export async function setupLibraryDataDir_(
  libraryDirOverride?: SimpleFileOrString
): Promise<PosixFile> {
  const dataDir = libraryDataDirPosixFile(libraryDirOverride)
  if (dataDir == null) {
    throw new Error("empty libraryDataDir" + NoLibraryErrorFlag)
  }
  await mkdirRW_(dataDir)
  await mkNoMedia_(dataDir) // < optional, don't fail here
  const readmeFile = dataDir.join("README.txt")
  await writeTextfile_(readmeFile.nativePath, README)
  return dataDir
}

export function libraryOriginalsDirPosixFile(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): Maybe<PosixFile> {
  return PosixFile.forMaybe(originalsDir(libraryDirOverride))
}

export async function setupLibraryOriginalsDir_(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): PromiseMaybe<PosixFile> {
  return mkdirRW_(libraryOriginalsDirPosixFile(libraryDirOverride))
}

export function libraryPreviewsDirPosixFile(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): Maybe<PosixFile> {
  return PosixFile.forMaybe(libraryPreviewsDir(libraryDirOverride))
}

export async function setupLibraryPreviewsDir_(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): PromiseMaybe<PosixFile> {
  return mkdirRW_(libraryPreviewsDirPosixFile(libraryDirOverride))
}

export function librarySyncReportsDir(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): Maybe<PosixFile> {
  return libraryDirPosixFile(libraryDirOverride)?.join(
    // NOTE: the default is .photostructure/sync-reports, so don't add the data dir here!
    Settings.syncReportsDir.valueOrDefault
  )
}

export async function setupLibrarySyncReportsDir_(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): PromiseMaybe<PosixFile> {
  return mkdirRW_(librarySyncReportsDir(libraryDirOverride))
}

export async function setupLibraryDbDir_(
  libraryDirOverride?: Maybe<SimpleFileOrString>
): PromiseMaybe<PosixFile> {
  return mkdirRW_(
    map(libraryDataDirPosixFile(libraryDirOverride), ea =>
      pathToDbDir(ea, Schemas.models)
    )
  )
}
