import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { resolve } from "../fs/Path"
import { OptionalStringSetting } from "./OptionalStringSetting"
import { BaseSettingOpts, SettingOpts } from "./Setting"

export class OptionalFileSetting extends OptionalStringSetting {
  constructor(
    opts: BaseSettingOpts<Maybe<string>> & Partial<SettingOpts<Maybe<string>>>
  ) {
    super({
      toEnv: ea => ea?.trim(),
      fromEnv: ea => (blank(ea) ? undefined : resolve(ea)),
      defaultValue: undefined,
      ...opts
    })
  }
}
