import { sort } from "./ArraySort"
import { blank } from "./Blank"
import { orElse } from "./Maybe"
import { Maybe, MaybeNull } from "./MaybeTypes"
import { ThunkOrT, tot } from "./Thunk"
import { toS } from "./toS"

export function isNumber(o: any): o is number {
  return typeof o === "number" && !isNaN(o) && isFinite(o)
}

export function isInvalidNumber(o: any): o is number {
  return typeof o === "number" && (isNaN(o) || !isFinite(o))
}

// thinking about adding toNumber? Please use toInt or toFloat instead.

const numeralRE = /^\d+$/
export function isDigits(o: any): boolean {
  return toS(o).match(numeralRE) != null
}

const nonNumericRE = /[^-\.0-9\s]/

export function isNumeric(o: any): boolean {
  return toS(o).match(nonNumericRE) == null && toFloat(o) != null
}

export function isBigInt(o: any): o is bigint {
  return typeof o === "bigint"
}

export function mapFinite<T>(i: Maybe<number>, f: (ea: number) => T): Maybe<T> {
  return isNumber(i) ? f(i) : undefined
}

const mapPredicate =
  (f: (lhs: number, rhs: number) => boolean) =>
  (lhs: MaybeNull<number>, rhs: MaybeNull<number>) =>
    isNumber(lhs) && isNumber(rhs) && f(lhs, rhs)

export const lt = mapPredicate((i, j) => i < j)
export const lte = mapPredicate((i, j) => i <= j)
export const gt = mapPredicate((i, j) => i > j)
export const gte = mapPredicate((i, j) => i >= j)

export function finiteOrElse<T>(i: Maybe<number>, defaultValue: T): number | T {
  return isNumber(i) ? i : defaultValue
}

export function diff(i: Maybe<number>, j: Maybe<number>): Maybe<number> {
  return isNumber(i) && isNumber(j) ? i - j : undefined
}

export function absdiff(i: Maybe<number>, j: Maybe<number>): Maybe<number> {
  return isNumber(i) && isNumber(j) ? Math.abs(i - j) : undefined
}

export function safeDivide(numerator: number, denominator: number): number {
  return numerator / (denominator === 0 ? 1e-8 : denominator)
}

/**
 * @return true iff a is within ((1-minCoeff)*b)b
 */
export function approximates(
  a: MaybeNull<number>,
  b: MaybeNull<number>,
  minCoeff: number = 0.95
): boolean {
  if (!isNumber(a) || !isNumber(b)) return false
  if (a === b) return true
  if (minCoeff >= 1) return false // it doesn't match!
  const delta = (b === 0 ? 1e-5 : b) * (1 - minCoeff)
  return within(b - delta, b + delta, a)
}

/**
 * @param min inclusive
 * @param max inclusive
 */
export function within(min: number, max: number, n: any): n is number {
  if (n == null || !isNumber(n)) return false
  ;[min, max] = sort([min, max])
  return lte(min, n) && gte(max, n)
}

export function max_(...arr: Maybe<number>[]): number {
  let max: Maybe<number>
  for (const ea of arr) {
    if (isNumber(ea) && (max == null || max < ea)) max = ea
  }
  if (max == null) throw new Error("no numbers given to max()")
  else return max
}

/**
 * @return true iff expected is within delta of actual
 */
export function closeTo(
  expected: MaybeNull<number>,
  actual: MaybeNull<number>,
  delta: number
): boolean {
  return isNumber(expected) && isNumber(actual)
    ? // if the delta is _less than or equal to_, pass.
      Math.abs(expected - actual) <= delta
    : false
}

export function trunc(n: Maybe<number>): Maybe<number> {
  if (!isNumber(n)) return undefined
  const i = Math.trunc(n)
  return i === 0 ? Math.abs(i) : i
}

export interface ToNumber {
  toNumber(): number
}

export function isToNumber(v: any): v is ToNumber {
  return typeof v?.toNumber === "function"
}

function toNumber(
  value: MaybeNull<number | bigint | string | ToNumber>,
  opts: {
    defaultValue: Maybe<number>
    nton: (n: number) => number
    ston: (s: string) => Maybe<number>
  }
): Maybe<number> {
  if (blank(value)) return opts.defaultValue
  if (isNumber(value)) return opts.nton(value)
  if (isBigInt(value)) {
    // CAREFUL! Old Safari will keel over due to lack of BigInt support: it's
    // OK here, because the value typeof is "bigint", which would only be true
    // where BigInt *is* supported.
    if (
      value > BigInt(Number.MAX_SAFE_INTEGER) ||
      value < BigInt(Number.MIN_SAFE_INTEGER)
    ) {
      throw new Error("precision loss for " + value)
    }
    return opts.nton(Number(value))
  }
  if (isToNumber(value)) return opts.nton(value.toNumber())
  try {
    const i = opts.ston(toS(value).trim())
    return isNumber(i) ? opts.nton(i) : opts.defaultValue
  } catch {
    return opts.defaultValue
  }
}

export function toInt(
  value: MaybeNull<number | bigint | string | ToNumber>,
  opts?: { defaultValue?: number }
): Maybe<number> {
  return toNumber(value, {
    defaultValue: undefined,
    nton: i => trunc(i)!,
    ston: parseInt,
    ...opts
  })
}

export function toFloat(
  value: MaybeNull<number | string>,
  opts?: { defaultValue?: number }
): Maybe<number> {
  return toNumber(value, {
    defaultValue: undefined,
    nton: i => i,
    ston: parseFloat,
    ...opts
  })
}

/**
 * @return `Math.trunc(n)` if n is a positive integer, else undefined
 */
export function toGt0(n: any): Maybe<number> {
  const i = toInt(n)
  return i != null && i > 0 ? i : undefined
}

export function toGt0f(n: any): Maybe<number> {
  const i = toFloat(n)
  return i != null && i > 0 ? i : undefined
}

export function lt0(n: any): n is number {
  return isNumber(n) && n < 0
}

export function gt0(n: any): n is number {
  return isNumber(n) && n > 0
}

export function firstGt0(...arr: any[]): Maybe<number> {
  for (const ea of arr) {
    if (gt0(ea)) return ea
    const i = toInt(ea)
    if (gt0(i)) return i
  }
  return
}

export function gtOrElse(
  n: Maybe<number>,
  mustBeGreaterThan: number
): Maybe<number> {
  return isNumber(n) && isNumber(mustBeGreaterThan) && n > mustBeGreaterThan
    ? n
    : undefined
}

export function lte0(n: any): n is number {
  return isNumber(n) && n <= 0
}

export function gte0(n: any): n is number {
  return isNumber(n) && n >= 0
}

export function toGte0(n: any): Maybe<number> {
  return gte0(n) ? n : undefined
}

export function mapInt<T>(o: any, f: (i: number) => T): Maybe<T> {
  // PERF: UNROLLED FROM opt()
  const i = toInt(o)
  return i == null ? undefined : f(i)
}

export function mapFloat<T>(o: any, f: (i: number) => T): Maybe<T> {
  // PERF: UNROLLED FROM opt()
  const i = toFloat(o)
  return i == null ? undefined : f(i)
}

/**
 * @return a stringified `value` iff `value` is a positive integer
 */
export function id(value: Maybe<number | string>): Maybe<string> {
  const i = toInt(value)
  return gt0(i) ? String(i) : undefined
}

export function mapIntOr<T>(o: any, f: (i: number) => T, orElseF: () => T): T {
  return orElse(mapInt(o, f), orElseF)
}

export function mapNumeric<T>(o: any, f: (i: number) => T): Maybe<T> {
  return isNumber(o) ? f(o) : undefined
}

export function map2Numeric<T>(
  i: any,
  j: any,
  f: (ea1: number, ea2: number) => T
): Maybe<T> {
  return mapNumeric(i, ea1 => mapNumeric(j, ea2 => f(ea1, ea2)))
}

export function mapNumericOr<T>(
  o: any,
  f: (i: number) => T,
  defaultValue: T
): T {
  return isNumber(o) ? f(o) : defaultValue
}

export function numericOr(o: any, defaultValue: ThunkOrT<number>): number {
  return isNumber(o) ? o : tot(defaultValue)
}

export function round(i: number): number {
  // Workaround for bug in javascript (Math.round(-1.5) === -1 (!!)):
  return !isNumber(i) ? 0 : i < 0 ? -Math.round(-i) : Math.round(i)
}

/**
 * @see Number#toFixed
 */
export function toFixed(i: Maybe<number>, decimals: number): Maybe<number> {
  try {
    return mapNumeric(i, ea => round(ea * 10 ** decimals) / 10 ** decimals)
  } catch (err) {
    return
  }
}

/**
 * Just like Number.toFixed, without extraneous padded zeroes
 *
 * @see Number#toFixed
 */
export function toFixedStr(i: number, fractionDigits: number) {
  // this is NOT a general-purpose zero-stripper-RegExp!
  return i.toFixed(fractionDigits).replace(/(\.0*$)|(?<=\d+\.\d+)0+$/, "")
}

/**
 * @see Number#toPrecision
 */
export function toPrecision(i: number, decimalPlaces: number): number {
  if (i == null) return 0
  // pow should be 1 for decimalPlaces = 0
  const pow = Math.pow(10, decimalPlaces)
  return round(i * pow) / pow
}

export function toPrecisionMaybe(
  i: Maybe<number>,
  decimalPlaces: number
): Maybe<number> {
  return mapFinite(i, ea => toPrecision(ea, decimalPlaces))
}

export function sigFigs(i: number, digits: number): number {
  if (i === 0 || digits === 0) {
    return 0
  }
  const exp = digits - round(Math.ceil(Math.log10(Math.abs(i))))
  const pow = Math.pow(10, Math.abs(exp))
  // Prevent 120.000000001:
  return exp < 0 ? round(i / pow) * pow : round(i * pow) / pow
}

export function base2Ceil(i: number): number {
  return Math.pow(2, Math.ceil(Math.log2(i)))
}

export function base10Ceil(i: number): number {
  return Math.pow(10, Math.ceil(Math.log10(i)))
}

export function clamp(min: number, max: number, value: number): number {
  if (min > max || !isNumber(min) || !isNumber(max))
    throw new Error(`invalid clamp(${min}, ${max}, ${value})`)
  if (!isNumber(value)) return (min + max) / 2
  return value < min ? min : value > max ? max : value
}

/**
 * Accumulate the result of `f`, called `count` times. `f` receives a
 * zero-indexed argument.
 */
export function times<T>(count: number, f: (i: number) => T): T[] {
  if (!gt0(count)) return []
  const c = Math.round(count)
  if (c <= 0) return []
  return [...Array(c)].map((_, i) => f(i))
}

export function pct(numerator: Maybe<number>, denominator: number): number {
  return clamp(
    0,
    100,
    gt0(denominator)
      ? round((100 * (gt0(numerator) ? numerator : 0)) / denominator)
      : 0
  )
}

export function fmtPct(i: Maybe<number>): Maybe<string> {
  const rnd = Math.round(i ?? -1)
  return within(0, 100, rnd) ? rnd + "%" : undefined
}

const _factorials: number[] = [1, 1]

/**
 * @return the factorial of `n`
 */
export function factorial(n: number): number {
  if (!gte0(n) || Math.round(n) !== n)
    throw new Error(`factorial(${n}): must be non-negative integer`)
  // Number.MAX_VALUE < 171!
  if (n > 170) return Infinity

  // TODO: replace with ??= when fe is upgraded to TS 3.7+
  return _factorials[n] != null
    ? _factorials[n]
    : (_factorials[n] = factorial(n - 1) * n)
}

/**
 * @return the number of ways to choose `r` items from `n` items
 */
export function choose(n: number, r: number) {
  return factorial(n) / (factorial(r) * factorial(n - r))
}
