import { gt0, gte0, lt0 } from "./Number"

/**
 * @return true if obj is both Iterable *and not a string* (because srsly wth)
 */
export function isIterable(obj: any): obj is Iterable<any> {
  return (
    obj != null &&
    typeof obj !== "string" &&
    typeof obj[Symbol.iterator] === "function"
  )
}

