export function sliceIterable<T>(
  iterable: Iterable<T>,
  start: number,
  end?: number
): T[] {
  // If start is negative, we have to iterate the whole thing anyway, so
  // delegate to Array.slice():
  if (lt0(start)) {
    return [...iterable].slice(start, end)
  }

  const iter = iterable[Symbol.iterator]()
  if (gt0(start)) {
    for (let i = 0; i < start; i++) {
      iter.next()
    }
  }
  const result: T[] = []

  // if end is null, run until .done:
  for (let i = start; gte0(end) ? i < end : true; i++) {
    const next = iter.next()
    // This is pretty lame of the iterator interface, IMHO. Don't return an
    // object with a value if it doesn't have a value!
    if (next.value != null || next.done !== true) {
      result.push(next.value)
    }
    if (next.done === true) {
      break
    }
  }
  // don't splice! This may change the underlying iterable!
  if (lt0(end)) {
    return result.slice(0, end)
  }
  return result
}

export function countIterable(i: Iterable<any>): number {
  // Avoids GC from creating an array
  let c = 0
  const iter = i[Symbol.iterator]()
  while (true !== iter.next().done) c++
  return c
}
