import fsp from "fs/promises"
import { join } from "path"
import { lazy } from "../../core/Lazy"
import { sortBy } from "../../fe/ArraySort"
import { blank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, toGt0 } from "../../fe/Number"
import {
  Timeout,
  thenOrTimeout,
  thenOrTimeoutAt,
  toNotTimeout
} from "../../fe/ThenOrTimeout"
import { FifoSet } from "../FifoSet"
import { mkLogger } from "../Logger"
import { onTimeout } from "../async/TimeoutHandler"
import { ee } from "../event/EventEmitter"
import { Settings } from "../settings/Settings"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { FileCache } from "./FileCache"
import { SimpleDirent } from "./SimpleDirent"
import { statTimeoutMs } from "./StatTimeout"

export interface CachedReaddir {
  ts: number
  path: string
  result: SimpleDirent[]
}

export const ReadDirCacheName = "readdircache"

export const readdirCacheDir = lazy(() =>
  join(Settings.cacheDir.valueOrDefault, ReadDirCacheName)
)

const logger = lazy(() => mkLogger("fs.Readdir"))

export async function clearReaddirCaches() {
  try {
    await fsp.rm(readdirCacheDir(), { force: true, recursive: true })
  } catch (error) {
    logger().warn("clearReaddirCaches(): failed", { error })
  }
}

const path2cache = lazy(
  () =>
    new FileCache<SimpleDirent[]>({
      name: "fs.readdir",
      maxSize: 256,
      timeoutMs: commandTimeoutMs(),
      clearEveryMs: minuteMs,
      useFsCache: true,
      fsCacheTtlMs: Settings.readdirCacheMs.valueOrDefault
    })
)

export function clearCachedReaddir(nativePath: string) {
  if (!blank(nativePath)) {
    path2cache.prior()?.delete(nativePath)
  }
}

later(() => {
  Settings.cacheDir.watchLater(() => {
    readdirCacheDir.clear()
    void path2cache.prior()?.clear()
    slowDirs.prior()?.clear()
  })
  ee().on("clearCache", () => {
    slowDirs.prior()?.clear()
    // path2cache is a FileCache--they're already watching for "clearCache" and fileChanged.
    return clearReaddirCaches()
  })
  ee().on("fileChanged", nativePath =>
    blank(nativePath) ? clearReaddirCaches() : clearCachedReaddir(nativePath)
  )
})

// Only exposed for tests
export const slowDirs = lazy(() => new FifoSet<string>(256))

export async function isSlowDir(nativePath: string) {
  if (blank(nativePath)) return false
  if (slowDirs().has(nativePath)) return true
  if (path2cache().has(nativePath)) {
    slowDirs().add(nativePath)
    return true
  }
  return false
}

export function setSlowDir(nativePath: string) {
  clearCachedReaddir(nativePath)
  slowDirs().add(nativePath)
}

export async function isCachedDir(nativePath: string) {
  return path2cache().get(nativePath) != null
}

export async function childDirectories_(nativePath: string): Promise<string[]> {
  return (await readdir_(nativePath))
    .filter(ea => ea.isDirectory)
    .map(ea => join(nativePath, ea.basename))
}

export async function readdir(
  nativePath: string
): PromiseMaybe<SimpleDirent[]> {
  try {
    return await (gt0(Settings.readdirCacheMs.valueOrDefault)
      ? readdir_(nativePath)
      : readdirUncached_(nativePath))
  } catch (err) {
    logger().warn("readdir() failed for " + nativePath, err)
    return
  }
}

/**
 * @throws if readdir() fails.
 */
export async function readdir_(nativePath: string): Promise<SimpleDirent[]> {
  return logger().tap({
    level: "trace",
    msg: "readdir_(" + nativePath + ")",
    result: await path2cache().getOrSetAsync(nativePath, () =>
      _readdir_(nativePath)
    )
  })
}

export function readdirCached(nativePath: string): Maybe<SimpleDirent[]> {
  return path2cache().get(nativePath)
}

async function _readdir_(nativePath: string): Promise<SimpleDirent[]> {
  const ts = Date.now()
  const timeoutAt = ts + statTimeoutMs() * 2

  const p = readdirUncached_(nativePath)

  // If readdir() completes quickly, don't bother with .json caching (which is
  // expensive and generates a bunch of GC)

  const quickResult = await thenOrTimeout(
    p,
    toGt0(Settings.fsCacheSlowMs.valueOrDefault) ?? statTimeoutMs()
  )

  if (quickResult === Timeout) {
    onTimeout({ soft: false })
  }

  if (
    quickResult !== Timeout &&
    quickResult.length < Settings.readdirSizeCacheThresh.valueOrDefault
  ) {
    return quickResult
  }

  // Dang, we're dealing with a slow or very large directory.
  logger().info("readdir(): slow or large dir", { nativePath })
  slowDirs().add(nativePath)

  const result =
    toNotTimeout(quickResult) ?? (await thenOrTimeoutAt(p, timeoutAt))

  if (result === Timeout) {
    return logger().throw("readdir() timeout", { nativePath, doNotSend: true })
  }

  return result
}

/**
 * NOTE: CALLER SHOULD WRAP WITH A TIMEOUT!
 */
export async function readdirUncached_(
  nativePath: string
): Promise<SimpleDirent[]> {
  const r = await fsp.readdir(nativePath, { withFileTypes: true })
  return sortBy(
    // this strips off the node 20 added "path" field:
    r.map(ea => ({
      basename: ea.name,
      isFile: ea.isFile(),
      isDirectory: ea.isDirectory()
    })),
    ea => [
      ea.isFile, // < order files last
      ea.basename.toLowerCase(), // < case insensitive sort (ish, but good enough here)
      ea.basename // < for determinism with colliding FILE.txt and file.txt
    ]
  )
}
