import { compact } from "../fe/Array"
import { map } from "../fe/Maybe"
import { Maybe, MaybePromiseMaybe } from "../fe/MaybeTypes"
import { toA } from "../fe/toA"
import { concat } from "./Array"

export function hasAll<K>(m: Map<K, any> | Set<K>, arr: K[]): boolean {
  return arr.every(ea => m.has(ea))
}

export function flatMap<T, K, V>(iter: T[], f: (t: T) => [K, V][]): Map<K, V> {
  return new Map<K, V>(concat(...iter.map(ea => f(ea))))
}

export function compactMap<K, V>(
  m: Iterable<Maybe<[Maybe<K>, Maybe<V>]>>
): Map<K, V> {
  const arr = compact(m).filter(([k, v]) => k != null && v != null) as [K, V][]
  return new Map<K, V>(arr)
}

export function toMap<T, K, V>(
  i: Maybe<Iterable<Maybe<T>>>,
  f: (entry: T) => Maybe<[Maybe<K>, Maybe<V>]>
): Map<K, V> {
  return compactMap(compact(i).map(f))
}

export async function toMapAsync<T, K, V>(
  iterable: Maybe<Iterable<Maybe<T>>>,
  f: (entry: T) => MaybePromiseMaybe<[Maybe<K>, Maybe<V>]>
): Promise<Map<K, V>> {
  if (iterable == null) return new Map()
  const entries = await Promise.all(compact(toA(iterable)).map(ea => f(ea)))
  return compactMap(entries)
}

export function toObj<T>(m: Map<string, T>): { [key: string]: T } {
  const obj: any = {}
  for (const [k, v] of m) {
    obj[k] = v
  }
  return obj
}

export function filter<K, V>(
  m: Map<K, V>,
  f: (key: K, value: V) => boolean
): Map<K, V> {
  return new Map<K, V>([...m.entries()].filter(([k, v]) => f(k, v)))
}

/**
 * Only retain the entries in m where f() returns true.
 */
export function filterInPlace<K, V>(
  m: Map<K, V>,
  retainIfTrue: (key: K, value: V) => boolean
): void {
  ;[...m.entries()].forEach(([k, v]) => retainIfTrue(k, v) || m.delete(k))
}

export function pickKeys<K, V>(m: Map<K, V>, keys: K[]): Map<K, V> {
  return filter(m, k => keys.indexOf(k) >= 0)
}

/**
 * Return the first value associated to the key that matches the given predicate
 */
export function getLike<K, V>(
  m: Map<K, V>,
  predicate: (k: K) => boolean
): Maybe<V> {
  return map(
    [...m.entries()].find(([k]) => predicate(k)),
    ([, v]) => v
  )
}

export function inverse<K, V>(m: Map<K, V>): Map<V, K> {
  return new Map([...m.entries()].map(([k, v]) => [v, k] as [V, K]))
}
