export async function someOrTimeout<T>(
  arr: ThunkOrLater<T>[],
  opts: { timeoutMs: number }
): Promise<(T | TimeoutError | Error)[]> {
  return Promise.all(
    arr.map(ea => thenOrTimeoutError(tol(ea), opts.timeoutMs).catch(err => err))
  )
}

export async function thenCompact<T>(
  arr: MaybeSyncOrAsync<MaybeSyncOrAsync<T>[]>
): Promise<T[]> {
  if (arr == null) return []
  const c = compact(await arr)
  return isEmpty(c) ? [] : compact(await Promise.all(c))
}

export async function thenCollectBatched<T1, T2>(
  arr: MaybeSyncOrAsync<MaybeSyncOrAsync<T1>[]>,
  batchSize: number,
  f: (batch: T1[]) => MaybeSyncOrAsync<T2[]>
) {
  const result: T2[] = []
  for (const batchArr of batches(toA(await arr), batchSize)) {
    const batchInput: T1[] = await thenCompact(batchArr)
    result.push(...(await thenCompact(f(batchInput))))
  }
  return result
}

export async function thenMapResolved<T1, T2>(
  objP: MaybeSyncOrAsync<T1>,
  f: (t: T1) => MaybeSyncOrAsync<T2>
): PromiseMaybe<T2> {
  if (objP == null) return Promise.resolve(undefined)
  try {
    return await thenMap(objP, f)
  } catch (e) {
    return undefined
  }
}

export function resolvedWithin<T>(
  p: Promise<T>,
  withinMs: number
): Promise<boolean> {
  return Promise.race([
    p.then(() => true),
    delay(withinMs).then(() => false)
  ]).catch(() => false)
}

export async function resolved(p: Promise<any>): Promise<boolean> {
  try {
    await p
    return true
  } catch (e) {
    return false
  }
}

export async function rejected(p: Promise<any>): Promise<boolean> {
  return !(await resolved(p))
}

export async function thenDefined(p: Promise<any>): Promise<boolean> {
  return (await p) != null
}

export async function allSerial<T>(
  arr: Maybe<() => MaybeSyncOrAsync<T>>[]
): Promise<T[]> {
  const result: T[] = []
  for (const ea of compact(arr)) {
    const v = await ea()
    if (v != null) result.push(v)
  }
  return result
}

export async function awaitAll(
  arr: Maybe<(PromiseMaybe<any> | any)[]>
): Promise<void> {
  for (const ea of toA(arr)) {
    if (ea != null) await ea
  }
}

export async function awaitSettled(
  arr: Maybe<(PromiseMaybe<any> | any)[]>
): Promise<void> {
  for (const ea of toA(arr)) {
    try {
      if (ea != null) await ea
    } catch {
      //
    }
  }
}

// maybeist champion:
export async function thenFlatten<T>(
  arr: MaybeSyncOrAsync<MaybeSyncOrAsync<T | MaybeSyncOrAsync<T>[]>[]>
): Promise<T[]> {
  const result: T[] = []
  for (const ea of toA(await arr)) {
    const v = await ea
    if (v != null) {
      if (Array.isArray(v)) {
        for (const ea2 of v) {
          const v2 = await ea2
          if (v2 != null) result.push(v2)
        }
      } else {
        result.push(v)
      }
    }
  }
  return result
}

export async function thenUniq<T>(
  arr: MaybeSyncOrAsync<MaybeSyncOrAsync<T>[]>
): Promise<T[]> {
  const result: T[] = []
  for (const ea of toA(await arr)) {
    const v = await ea
    if (v != null) {
      result.push(v)
    }
  }
  return uniq(result)
}

export async function asyncFind<T>(
  arr: T[],
  f: (t: T) => Promise<boolean>
): PromiseMaybe<T> {
  for (const ea of arr) {
    if (await f(ea)) return ea
  }
  return
}

/**
 * @see thenCollect in fe -- which doesn't have logging or timeout support
 */
export async function thenCollectSerial<T, V>({
  name,
  arr,
  f,
  timeoutMs
}: {
  name: string
  arr: Maybe<MaybeSyncOrAsync<Maybe<T>[]>>
  f: (t: T) => SyncOrAsync<Maybe<V>>
  timeoutMs?: number
}): Promise<[V, T][]> {
  const result: [V, T][] = []
  for (const ea of compact(await arr)) {
    try {
      const v = await (gt0(timeoutMs)
        ? thenOrTimeoutError(f(ea), timeoutMs)
        : f(ea))
      if (v != null) result.push([v, ea])
    } catch (error) {
      mkLogger(name).warn("failed to apply", {
        ea,
        error
      })
    }
  }
  return result
}

export async function mapAsyncSerial<T, V>(
  args: Arg0<typeof thenCollectSerial<T, V>>
): Promise<V[]> {
  return (await thenCollectSerial(args)).map(ea => ea[0])
}

/**
 * For each in `V[]`, apply `f(v)` concurrently, returning `[v, await f(v)]`
 * @see thenCollect
 */
export async function thenCollectParallel<T, V>({
  arr,
  f,
  name,
  maxConcurrent,
  timeoutMs,
  promises
}: {
  arr: Maybe<MaybeSyncOrAsync<Maybe<T>[]>>
  f: (t: T) => SyncOrAsync<Maybe<V>>
  name: string
  maxConcurrent?: number
  timeoutMs?: number
  promises?: Promises
}): Promise<[V, T][]> {
  if (arr == null) return []
  const array = compact(await arr)
  if (isEmpty(array)) return []
  maxConcurrent = Math.round(maxConcurrent ?? maxCpus())
  if (maxConcurrent <= 1) return thenCollectSerial({ arr, f, name, timeoutMs })
  const p =
    promises ??
    (array.length <= maxConcurrent
      ? PermissivePromises
      : new Promises(name, () => maxConcurrent))
  // ^ creating new Promises() is expensive
  const func = gt0(timeoutMs)
    ? (t: T) => thenOrTimeoutError(f(t), timeoutMs)
    : f
  const t = await p.enqueueAll(
    name,
    array.map(ea => async () => {
      try {
        return [await func(ea), ea] as [V, T]
      } catch (error) {
        mkLogger(name).warn("failed to apply", {
          ea,
          error
        })
        return
      }
    })
  )
  // compact prevents TypeError: undefined is not iterable:
  return compact(t).filter(([k, v]) => k != null && v != null)
}

/**
 * Apply `f` to each element of `arr` concurrently, returning the results
 */
export async function mapAsync<T, V>({
  arr,
  f,
  name,
  maxConcurrent,
  timeoutMs,
  promises
}: {
  arr: Maybe<MaybeSyncOrAsync<Maybe<T>[]>>
  f: (t: T) => SyncOrAsync<Maybe<V>>
  name: string
  maxConcurrent?: number
  timeoutMs?: number
  promises?: Promises
}): Promise<V[]> {
  return (
    await thenCollectParallel({
      arr,
      f,
      name,
      maxConcurrent,
      timeoutMs,
      promises
    })
  ).map(ea => ea[0])
}

/**
 * @return all items in `arr` that `f` doesn't reject and returns true or
 * Promise<true>
 */
export async function filterAsync<T>({
  name,
  arr,
  f,
  maxConcurrent,
  timeoutMs
}: {
  name: string
  arr: Maybe<T>[]
  f: Predicate<T>
  maxConcurrent?: number
  timeoutMs?: number
}): Promise<T[]> {
  const t = await thenCollectParallel({
    name,
    arr: compact(arr),
    f,
    maxConcurrent,
    timeoutMs
  })
  return t.filter(([b]) => b).map(([, v]) => v)
}

export async function partitionAsync<T>(
  arr: T[],
  filter: (t: T) => MaybeSyncOrAsync<boolean>
): Promise<[T[], T[]]> {
  const t = await thenCollectParallel({
    name: "partitionAsync",
    arr,
    f: filter
  })
  return [
    t.filter(([ea]) => isTrue(ea)).map(([, ea]) => ea),
    t.filter(([ea]) => isFalse(ea)).map(([, ea]) => ea)
  ]
}

export async function tryAsync<T>(f: () => T | Promise<T>) {
  try {
    return await f()
  } catch {
    return
  }
}

export const DefaultTryAllTimeoutMs = 30 * secondMs

export async function tryAll(
  arr: (any | (() => any))[],
  onError = (err: Error) => console.error(err),
  timeoutMs = DefaultTryAllTimeoutMs
): Promise<void> {
  for (const ea of arr) {
    try {
      await thenOrTimeout(ea, timeoutMs)
    } catch (err) {
      onError(toErr(err)!)
    }
  }
  return
}

export async function thenFinally<T>(
  p: SyncOrAsync<T> | (() => SyncOrAsync<T>),
  err_: (err: Error | any) => any = () => {
    // no-op
  },
  finally_: (r: T | Error) => any = () => {
    // no-op
  }
): Promise<T> {
  let result: T
  let caught = null
  try {
    result = await (isFunction(p) ? p() : p)
  } catch (err) {
    caught = toErr(err)
    try {
      await err_(err)
    } catch {
      // no-op
    }
  }
  try {
    await finally_(caught ?? result!)
  } catch {
    // no-op
  }
  if (caught != null) {
    throw caught
  } else {
    return result!
  }
}

export async function thenNot(
  p: Maybe<boolean> | MaybePromiseMaybe<boolean>,
  defaultIfNull = true
): Promise<boolean> {
  if (p == null) return defaultIfNull
  const r = await p
  return r == null ? defaultIfNull : !isTrue(r)
}

export async function thenMap2<T1, T2, T3>(
  objP1: MaybeSyncOrAsync<T1>,
  objP2: MaybeSyncOrAsync<T2>,
  f: (t1: T1, t2: T2) => MaybeSyncOrAsync<T3>
): PromiseMaybe<T3> {
  const o1 = await objP1
  if (o1 == null) return
  const o2 = await objP2
  if (o2 == null) return
  return f(o1, o2)
}

/**
 * Safe calls for Optional promises, with default
 */
export async function thenMapOr<T1, T2, T3>(
  objP: MaybeSyncOrAsync<T1>,
  f: (t: T1) => MaybeSyncOrAsync<T2>,
  ifUndefined: () => SyncOrAsync<T3>
): Promise<T2 | T3> {
  const obj = await objP
  if (obj == null) return ifUndefined()
  const result = await f(obj)
  return result == null ? ifUndefined() : result
}
/**
 * Safe calls for Optional promises, with default
 */
export async function thenMap2Or<A1, A2, R1, R2>(
  objP1: MaybeSyncOrAsync<A1>,
  objP2: MaybeSyncOrAsync<A2>,
  f: (t1: A1, t2: A2) => MaybeSyncOrAsync<R1>,
  ifUndefined: () => SyncOrAsync<R2>
): Promise<R1 | R2> {
  const o1 = await objP1
  if (o1 == null) return ifUndefined()
  const o2 = await objP2
  if (o2 == null) return ifUndefined()
  const result = await f(o1, o2)
  return result == null ? ifUndefined() : result
}

/**
 * Only call f if the promise resolves to truthy
 */
export async function thenAnd<T>(
  predicatePromise: MaybeNull<Promise<true | any>>,
  f: () => SyncOrAsync<T>
): PromiseMaybe<T> {
  return predicatePromise != null && isTrue(await predicatePromise)
    ? f()
    : undefined
}

export async function thenOrElse<T>(
  objP: Promise<MaybeNull<T>> | MaybeNull<T>,
  f: () => SyncOrAsync<T>
): Promise<T> {
  return orElse(await objP, f)
}

/**
 * Just like `firstDefinedPromise` but applies a function to all defined
 * elements in `arr`.
 */
export async function first<T, R>(
  arr: Maybe<Maybe<T>[]>,
  f: (t: T, index: number) => SyncOrAsync<MaybeNull<R>>
): PromiseMaybe<R> {
  if (arr != null) {
    let index = -1
    for (const t of arr) {
      index++
      try {
        if (t == null) continue
        const r = await f(t, index)
        if (r != null) {
          return r
        }
      } catch {
        // no-op
      }
    }
  }
  return undefined
}

/**
 * @see #firstDefinedLater
 */
export async function firstDefinedPromise<T, U = T>(
  promiseThunks: Thunk<MaybeSyncOrAsync<T>>[],
  filter: (t: T) => SyncOrAsync<Maybe<U>> = identity as any
): PromiseMaybe<U> {
  for (const ea of promiseThunks) {
    const o = await ea()
    if (o != null) {
      const result = await filter(o)
      if (result != null) {
        return result
      }
    }
  }
  return
}

export async function firstResolvedDefinedPromise<T>(
  promiseThunks: Thunk<MaybeSyncOrAsync<T>>[],
  onError: (err: Error) => void
): PromiseMaybe<T> {
  for (const t of promiseThunks) {
    try {
      const result = await t()
      if (result != null) {
        return result
      }
    } catch (err) {
      onError(toErr(err)!)
    }
  }
  return
}

export async function firstTruePromise<T>(
  predicate: (t: T) => Maybe<boolean> | PromiseMaybe<boolean>,
  ...promiseThunks: (() => MaybeSyncOrAsync<T>)[]
): PromiseMaybe<T> {
  for (const thunk of promiseThunks) {
    try {
      const ea = await thunk()
      if (ea != null && (await predicate(ea)) === true) {
        return ea
      }
    } catch (_) {
      //
    }
  }
  return
}

/**
 * @return `arr` sorted by the tuples in f. elements that `f` returns undefined
 * will be removed from the returned array.
 * @see https://en.wikipedia.org/wiki/Schwartzian_transform
 */
export async function sortByAsync<T, V extends Primitive | Primitive[]>({
  name,
  arr,
  f
}: {
  name: string
  arr: Maybe<MaybeSyncOrAsync<Maybe<T>[]>>
  f: (t: T) => PromiseMaybe<V>
}): Promise<T[]> {
  const unsorted: [V, T][] = await thenCollectParallel({ name, arr, f })
  return sortBy(unsorted, ea => ea[0]).map(ea => ea[1])
}
