const logger = lazy(() => mkLogger("event.SharedState"))

// See worker/WorkRequest

export interface SharedStateJson {
  pid: number
  syncing?: boolean // < only set by SyncService processes
  paused: boolean
  // task results are stored as events. Events are only retained for a couple minutes:
  events: StoredEvent<any>[]
  // tasks are retained until there is a taskResult StoredEvent for it's uid, or it expires:
  tasks: StoredTask<any>[]
  // only set if there wasn't any shared state.
  default?: true
}

export function isSharedStateJson(o: any): o is SharedStateJson {
  return (
    o != null &&
    typeof o === "object" &&
    isNumber(o.pid) &&
    isBoolean(o.paused) &&
    Array.isArray(o.events) &&
    Array.isArray(o.tasks)
  )
}

export const EventTimeoutMs = minuteMs

function isStateFile(ea: SimpleFileOrString) {
  return isJsonExt(ea) && !isWip(ea)
}

export function isSharedStateFileOrDir(f: SimpleFileOrString | Dirent) {
  return isPsLockFileOrDir(f) || isJsonExt(f) || isWip(f)
}

export function isFreshEvent(evt: StoredEvent<any>): boolean {
  const ts = tsFromUid(evt?.uid)
  const fresh = isStoredEvent(evt) && isRecentMs(ts, EventTimeoutMs)
  if (fresh && evt.name === "exit") {
    // exit events should be disregarded if they happened before we started:
    return gt(ts, StartTs)
  } else {
    return fresh
  }
}

export function dedupeStoredEvents(
  arr: Maybe<StoredEvent<any>>[]
): StoredEvent<any>[] {
  const sorted = sortBy(compact(arr).filter(isFreshEvent), ea => ea.uid)

  return uniqByLast(sorted, ea => stringify({ name: ea.name, args: ea.args }))
}

/**
 * Handles watching and broadcasting changes in shared state.
 *
 * This class replaces v1.x's RPC stuff.
 *
 * 1. Each process writes to it's own shared state file, living in $configDir/shared-state/$pid.json (no locking!)
 * 2. Each process is in charge of removing its own broadcast events after a minute.
 * 3. If a process handles a task, it should broadcast a taskResult.
 * 4. The process that submitted the task will remove the task when it sees the taskResult event.
 */
export class SharedState extends EndableWrapper {
  readonly jsonFile: PosixFile
  #pollInterval: Maybe<NodeJS.Timeout>
  readonly #jsoncontent2event = new TTLMap<string, StoredEvent<any>>(
    EventTimeoutMs
  )
  // readonly #myTasks = new TTLMap<string, StoredTask<any>>(
  //   Settings.sharedStateTaskTimeoutMs.valueOrDefault
  // )
  readonly handledEventUids = new TTLSet<string>(
    Settings.sharedStateTaskTimeoutMs.valueOrDefault
  )
  readonly handledTaskUids = new TTLSet<string>(
    Settings.sharedStateTaskTimeoutMs.valueOrDefault
  )
  readonly #uid2pendingTasks = new TTLMap<
    string,
    Deferred<TaskResult, StoredTask<any>>
  >(Settings.sharedStateTaskTimeoutMs.valueOrDefault)

  static readonly instance = lazy(() => {
    if (isWorkerService()) {
      logger().warn(
        "SharedState.instance() is not supported within worker services.",
        shortStack()
      )
      return
    }
    const result = map(
      Settings.sharedStateDir.valueOrDefault,
      ea => new SharedState(PosixFile.for(ea))
    )
    if (result != null) {
      Settings.sharedStateTaskTimeoutMs.watchLater(ea => {
        // result.#myTasks.ttlMs = ea
        result.handledEventUids.ttlMs = ea
        result.handledTaskUids.ttlMs = ea
        result.#uid2pendingTasks.ttlMs = ea
      })
    }
    return result
  })

  constructor(
    readonly dir: PosixFile,
    readonly id: string = String(pid),
    readonly eventEmitter = ee(),
    readonly processors: TaskProcessor[] = taskProcessors
  ) {
    super(
      "event.SharedState(" + dir + ")#" + id,
      () => this.#onEnd(),
      EndableRanks.first
    )
    this.jsonFile = dir.join(id + ".json")

    mapGt0(
      Settings.sharedStatePollMs.valueOrDefault,
      ms => (this.#pollInterval = setInterval(() => this.read(), ms))
    )
    void this.setup()
  }

  #onDirChange(_err: Error | null, events: Event[]) {
    const files = toA(events)
      .filter(
        ea =>
          ea.type !== "delete" && !ea.path.endsWith(sep + this.jsonFile.base)
      )
      .map(ea => ea.path)
    this.logger.debug("#onDirChange()", { files, _err, events })
    void this.read({ files })
  }

  async #onEnd() {
    // WE DON'T UNLINK HERE--we want to leave the file around so our last
    // events get picked up by our siblings.
    map(this.#pollInterval, clearInterval)
    this.#pollInterval = undefined
  }

  readonly setup = lazy(async () => {
    this.#uid2pendingTasks.on("expire", (k, v) => {
      this.logger.warn(
        "failed to resolve task before timeout (see sharedStateTaskTimeoutMs)",
        {
          task: v.payload,
          sharedStateTaskTimeoutMs:
            Settings.sharedStateTaskTimeoutMs.valueOrDefault
        }
      )
      const err: TaskFailure = {
        uid: k,
        ts: Date.now(),
        pid: processName(),
        error: "timeout"
      }
      void v.resolve(err)
    })
    await this.read({ setup: true })
    await DirWatcher.for(
      this.dir,
      (err, events) => this.#onDirChange(err, events),
      EndableRanks.first
    )
  })

  async read({
    files,
    setup
  }: { files?: SimpleFileOrString[]; setup?: boolean } = {}) {
    if (isEmpty(files)) {
      files = toA(
        await this.jsonFile.clearThisAndParent().siblings(ea => isStateFile(ea))
      )
    }
    const arr = []

    for (const file of files) {
      if (
        isStateFile(file) &&
        basename(file) !== this.jsonFile.base &&
        (await isMtimeRecent(toNativePath_(file), hourMs))
      ) {
        const bytes = await readFileMaybe(toNativePath_(file))
        const json = parseJSON<SharedStateJson>(bytes)
        if (json != null) arr.push(json)
      }
    }

    if (isEmpty(arr)) {
      this.logger.debug("read(): given empty array")
      return
    }

    if (setup === true) {
      // bootstrap paused state:
      pause(arr.some(ea => ea?.paused === true))
    }

    this.logger.debug("read()", { arr })

    let updated = false

    // even if a shared state JSON only has 1 field, still parse it:
    if (isEmpty(arr)) {
      return
    }

    const events = dedupeStoredEvents(flatten(arr.map(ea => ea.events)))

    this.logger.debug("read() events", { arr, events })

    for (const event of events) {
      // don't re-do tasks that have been started or finished:
      if (event.name === "taskResult" || event.name === "taskStarted")
        this.handledTaskUids.add(event.uid)

      // process the remote event:
      updated ||= this.#handleRemoteEvent(event)
    }

    if (updated) await this.save()

    const unfilteredTasks = flatten(arr.map(ea => ea.tasks))

    const taskExpireTs = Date.now() - Settings.taskTimeoutMs.valueOrDefault
    const tasks = sortUniqBy(
      unfilteredTasks.filter(
        ea =>
          isStoredTask(ea) &&
          !this.handledTaskUids.has(ea.uid) &&
          gt(tsFromUid(ea?.uid), taskExpireTs)
      ),
      ea => ea.uid
    )

    if (isNotEmpty(tasks)) {
      this.logger.info("read()", { tasks })
    }

    // don't run these tasks again:
    this.handledTaskUids.addAll(tasks.map(ea => ea.uid))

    for (const task of tasks) {
      // don't wait for each task: get each running ASAP. This may edit
      // the JSON if we handle a task.

      // these are possibly contested tasks, so we need an fs lock.
      void this.#handleTask({
        task,
        sendEvents: true,
        withFsLock: true
      })
    }

    this.#updateSyncState(arr)

    return
  }

  vacuum() {
    deleteIf(this.#uid2pendingTasks, (_, v) => v.isSettled)
  }

  currentState(): SharedStateJson {
    this.vacuum()
    return {
      pid: process.pid,
      paused: isPaused(),
      syncing: isSyncService() ? isSyncing() : undefined,
      events: [...this.#jsoncontent2event.values()],
      tasks: [...this.#uid2pendingTasks.values()].map(ea => ea.payload!)
    }
  }

  async maybeSave() {
    const state = this.currentState()
    const prior = await this.jsonFile.readJson()
    if (!eql(state, prior)) {
      await this.save()
    }
  }

  readonly save = oneAtATime({
    fn: () => {
      const state = this.currentState()
      this.logger.info("save()", { state })
      return this.jsonFile.writeJson_(state)
    }
  })

  async broadcast<E extends EventName>(
    name: E,
    ...args: Args<PhotoStructureEvents[E]>
  ) {
    return this.broadcastEvent_({ name, args })
  }

  async broadcastEvent_<E extends EventName>(evt: {
    uid?: string
    name: E
    args: Args<PhotoStructureEvents[E]>
  }): PromiseMaybe<StoredEvent<E>> {
    const event = { ...evt, uid: evt.uid ?? uid() }
    this.logger.info("addEvent_()", { event })
    const key = stringify(pick(event, "name", "args"))
    // newer events should override prior:
    this.#jsoncontent2event.set(key, event)
    this.handledEventUids.add(event.uid)
    this.eventEmitter.emit(event.name, ...event.args)
    await this.save()
    return event
  }

  #updateSyncState(arr: SharedStateJson[]) {
    // only the webservice cares about sync state:
    if (!isWebService()) return

    const syncingPids = arr.filter(ea => ea.syncing).map(ea => ea.pid)

    // we're only syncing if the process is still alive:
    for (const syncingPid of syncingPids) {
      if (pidExists(syncingPid)) {
        this.logger.debug("#updateSyncState(): syncing", { syncingPid })
        setIsSyncing(true)
        return
      }
    }

    this.logger.debug("#updateSyncState(): no syncing states", { syncingPids })

    setIsSyncing(false)
  }

  /**
   * @return true iff this should re-save
   */
  #handleRemoteEvent(event: StoredEvent<any>): boolean {
    if (!isFreshEvent(event)) {
      this.logger.warn("#handleRemoteEvent(): ignoring stale event", { event })
      return false
    }

    // don't double-process any events:
    if (this.handledEventUids.has(event.uid)) return false
    this.handledEventUids.add(event.uid)

    this.logger.info("#handleRemoteEvent()", event)
    if (event.name === "resume") {
      resume()
    } else if (event.name === "pause") {
      pause()
    } else if (event.name === "taskResult") {
      const taskResult: TaskResult = event.args[0] as any
      if (!isTaskResult(taskResult)) {
        this.logger.warn("Invalid task result", { event })
        return true // "handled"?
      }
      const d = this.#uid2pendingTasks.get(taskResult.uid)
      if (d != null) {
        this.logger.info("Received a task result for one of my tasks!", {
          event
        })
        void d.resolve(taskResult)
      }
      return true
    } else {
      this.eventEmitter.emit(event.name, ...event.args)
    }
    return false
  }

  canHandleTaskLocally(task: StoredTask<any>): boolean {
    return this.processors.some(ea => ea.accept(task))
  }

  async #handleTask({
    task,
    sendEvents,
    withFsLock
  }: {
    task: StoredTask<any>
    sendEvents: boolean
    withFsLock: boolean
  }): PromiseMaybe<TaskResult> {
    const processors = this.processors.filter(ea => ea.accept(task))
    if (processors.length === 0) {
      this.logger.info("#handleTask(): no local task processor", { task })
      return
    }

    if (withFsLock) {
      return withLock_(
        {
          name: "task-" + task.name + "-" + task.uid,
          file: this.dir.join(task.uid),
          timeoutMs: Settings.sharedStateTaskTimeoutMs.valueOrDefault,
          noopIfContested: true // < don't run a task multiple times
        },
        () => this.#handleTask({ task, sendEvents, withFsLock: false })
      )
    }

    let result: TaskResult

    if (processors.length > 1) {
      this.logger.warn(
        "#handleTask(): multiple local task processors for " + task.name,
        { processors: processors.map(ea => ea.name) }
      )
      // first one will get a crack at it...
    }

    for (const ea of processors) {
      try {
        result = {
          uid: task.uid,
          ts: Date.now(),
          pid: processName() + "-" + ea.name,
          result: await thenOrTimeoutError(
            ea.apply(task),
            Settings.sharedStateTaskTimeoutMs.valueOrDefault
          )
        }
        this.logger.info("#handleTask(): processor success", {
          processor: ea.name,
          result,
          task
        })
        break
      } catch (error) {
        this.logger.warn("#handleTask(): processor failed", {
          processor: ea.name,
          error
        })
        result = {
          uid: task.uid,
          ts: Date.now(),
          pid: processName() + "-" + ea.name,
          error: errorToS(error)
        }
      }
    }
    if (result! == null) {
      this.logger.warn("#handleTask(): null result" + InternalErrorFlag, {
        task
      })
      result = {
        uid: task.uid,
        ts: Date.now(),
        pid: processName() + "-error",
        error: "null result" + InternalErrorFlag
      }
    }

    // resolve the task before broadcasting event to only save once:

    void this.#uid2pendingTasks.get(task.uid)?.resolve(result)

    if (sendEvents) {
      this.logger.warn("#handleTask(): broadcasting result", { task, result })
      await this.broadcastEvent_({ name: "taskResult", args: [result] })
    } else {
      this.logger.warn("#handleTask(): NOT broadcasting result", {
        task,
        result
      })
    }

    return result
  }

  /**
   * Process the given task, either locally, or request another process to
   * handle it.
   */
  submitTask_<T extends TaskName>(
    name: T,
    ...args: Args<PhotoStructureTasks[T]>
  ): Deferred<TaskResult, StoredTask<any>> {
    this.logger.info("submitTask_()", { name, args })

    const task: StoredTask<any> = { uid: uid(), name, args }

    // We always return a Deferred just to make the API consistent.
    const d = new Deferred<TaskResult, StoredTask<any>>(name, {
      payload: task
    }).setTimeout(Settings.sharedStateTaskTimeoutMs.valueOrDefault)

    // if we can handle the task locally, we don't need to do any bookkeeping--just run it and return.
    if (this.canHandleTaskLocally(task)) {
      return d.observe(
        this.#handleTask({
          task,
          withFsLock: false, // we're the only one that's seen this task so far
          sendEvents: false
        }) as any
      ) // we know it will be defined
    }

    // Dang. Hope there's someone out there that can do this!

    // broadcast failure:
    void d.catch(err => {
      void this.broadcastEvent_({
        name: "taskResult",
        args: [
          {
            uid: task.uid,
            ts: Date.now(),
            pid: processName() + "-error",
            error: "uncaught rejection: " + errorToS(err) + InternalErrorFlag
          }
        ]
      })
    })

    this.#uid2pendingTasks.set(task.uid, d)

    // this will add the task to our shared-state.json, which the other
    // processes will see:
    void this.save()
    return d
  }
}

export function submitTask_<T extends TaskName>(
  name: T,
  ...args: Args<PhotoStructureTasks[T]>
): Maybe<Deferred<TaskResult, StoredTask<any>>> {
  return SharedState.instance()?.submitTask_(name, ...args)
}

export async function clearSharedStates() {
  const prior = SharedState.instance.clear()
  await prior?.end()
  const root =
    prior?.dir ??
    map(Settings.sharedStateDir.valueOrDefault, ea => PosixFile.for(ea))
  await root?.visitDescendants(ea => {
    return isSharedStateFileOrDir(ea)
      ? ea.isDirectorySync()
        ? ea.rmdir()
        : ea.unlink()
      : undefined
  })
}
