import { pidExists } from "batch-cluster"
import { toA } from "../fe/toA"
import { untilTrue } from "./async/until"

export function existingPids(pids: number[]): number[] {
  return toA(pids).filter(pidExists)
}

export function waitForPidExit(
  pid: number,
  timeoutMs: number
): Promise<boolean> {
  return untilTrue(() => !pidExists(pid), { timeoutMs, intervalMs: 250 })
}
