import { Rate } from "batch-cluster"
import { lazy } from "../../core/Lazy"
import { compact, compactBlanks, isEmpty, uniqSubstrings } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import {
  ErrorDelimiter,
  IgnoredErrorNames,
  errorCode,
  errorName,
  shortStack
} from "../../fe/Error"
import { flatten } from "../../fe/Flatten"
import { Maybe } from "../../fe/MaybeTypes"
import { isBuffer } from "../../fe/String"
import { isError } from "../../fe/isError"
import { toS } from "../../fe/toS"
import { BoundedList } from "../BoundedList"
import { mkLogger } from "../Logger"
import { ellipsize, isString, stripAnsiEsc } from "../String"
import { splitCompactLines } from "../fs/CRLF"
import { SimpleFile } from "../fs/SimpleFile"
import { getErrorDescriptions } from "./ErrorCodes"
import { ErrorFlag, sortErrorFlags } from "./ErrorFlags"
import { extractErrorFlags, stripErrorFlags } from "./ErrorTypes"

export const StartTs = Date.now()

export const logger = lazy(() => mkLogger("Error"))

export const errorRate = new Rate()
export const fatalErrorRate = new Rate()
export const internalErrorRate = new Rate()
export const lastInternalErrors = new BoundedList(10)

export function mapError<T>(obj: any, f: (error: Error) => T): Maybe<T> {
  return obj instanceof Error ? f(obj) : undefined
}

export function errorContains(err: any, re: RegExp): boolean {
  return re.test(errorToS(err))
}

export function throws(f: () => any): boolean {
  try {
    f()
    return false
  } catch {
    return true
  }
}

export function addMessage(error: Maybe<Error>, message: Maybe<string>) {
  if (error == null) return new Error(message)

  if (notBlank(message)) {
    if (!error.message.toLowerCase().includes(message.toLowerCase())) {
      error.message += ": " + message
    }
  }
  return error
}

export function errorsToPath(
  ...errs: Maybe<Error | SimpleFile | { path: string }>[]
): Maybe<string> {
  for (const ea of errs as any[]) {
    for (const k of ["nativePath", "path"]) {
      const s = toS(ea?.[k])
      if (notBlank(s)) return s
    }
  }
  return
}

/**
 * Errors can contain a "name", a "code", and a "message". Wrapped Errors may
 * contain a concatenated set of errors ("Error: failed to import foo: missing
 * database entry")
 */
export function splitErrorMessage(...errs: Maybe<Error | string>[]): string[] {
  if (isEmpty(errs)) return []

  const messages: string[] = errs.map((ea: any) =>
    isString(ea) ? ea : isBuffer(ea) ? ea.toString() : toS(ea?.message ?? ea)
  )

  messages.push(...getErrorDescriptions(errs))

  const arr = [
    ...errs.filter(isError).map(errorName),
    ...errs.map(errorCode),
    // only split(": ") on message and strings to avoid URL splitting
    ...compactBlanks(messages)
      .map(stripAnsiEsc)
      .flatMap(ea => splitCompactLines(ea))
      .flatMap(ea => ea.split(ErrorDelimiter))
  ]
  return uniqSubstrings(normalizeErrorMessages(flatten(arr)))
}

export function normalizeErrorMessages(arr: Maybe<string>[]): string[] {
  return compact(arr)
    .filter(ea => !IgnoredErrorNames.includes(ea))
    .map(ea =>
      ea
        .replace(/\s+/gm, " ")
        .trim()
        .replace(/^code ([A-Z]+)$/i, "$1")
        .trim()
    )
    .filter(notBlank)
}

export function joinErrorMessages(
  arr: Maybe<string>[],
  opts?: { maxLen?: number; trailingChars?: number; flags?: ErrorFlag[] }
): string {
  const trimmed = uniqSubstrings(normalizeErrorMessages(arr))
  const maxLen = opts?.maxLen ?? 400
  const trailingChars = opts?.trailingChars ?? 64
  const flags = sortErrorFlags([
    ...(opts?.flags ?? []),
    ...extractErrorFlags(arr.join(""))
  ])
  return (
    ellipsize(
      stripErrorFlags(trimmed.join(ErrorDelimiter)),
      maxLen - flags.length,
      trailingChars
    ) + flags.sort().join("")
  )
}

export function errorToS(
  err: any,
  opts?: { maxLen?: number; trailingChars?: number }
): string {
  if (blank(err)) {
    return ""
  }
  const arr = splitErrorMessage(err)
  return joinErrorMessages(arr, opts)
}

export const MissingError = "(missing error)"

export function errorToVerbose(
  err: any,
  maxMsgLen = 256,
  maxStackLines = 5
): string {
  if (blank(err)) err = MissingError // < if blank, at least fill in a stacktrace...
  return (
    ellipsize(errorToS(err), maxMsgLen) +
    " at " +
    shortStack(err?.stack, maxStackLines).join("; ")
  )
}
