import { compact, compactBlankish, isEmpty, uniq } from "./Array"
import { blank, notBlank, toNotBlank } from "./Blank"
import { flatten } from "./Flatten"
import { Maybe } from "./MaybeTypes"
import { toInt } from "./Number"
import { assignFields, compactValues, omit } from "./Object"
import { toS } from "./toS"

// the space after the colon prevents URLs from being split:
export const ErrorDelimiter = ": "

export const IgnoredErrorNames = ["Error", "String", "Object", "WrappedError"]

/**
 * @return the `.name` or class name of the Error, or undefined if not
 * informative (like "Error" or "WrappedError")
 */
export function errorName(
  err: Maybe<Error | { name?: string }>
): Maybe<string> {
  return err == null
    ? undefined
    : [err?.name, err?.constructor?.name]
        .find(ea => ea != null && !IgnoredErrorNames.includes(ea))
        ?.toString()
}

export function errorCode(err: any): Maybe<string> {
  return err == null ? undefined : toNotBlank(err?.code) // like "EBUSY"
}

export function errorErrno(err: any): Maybe<number> {
  return err == null ? undefined : toInt(err?.errno)
}

// We should skip over the stack frames that are created from creating frames:
const FramesToIgnoreRE = /shortStack|errorToVerbose/

export function shortStack(stack?: string, maxLines = 9): string[] {
  try {
    if (blank(stack)) {
      const err = new Error()
      // captureStackTrace may not be a function on some browsers
      Error.captureStackTrace?.(err)
      stack = err.stack
    }

    const result = toS(stack)
      .split("\n")
      .filter(
        ea =>
          !blank(ea) &&
          ea.trim().startsWith("at ") &&
          FramesToIgnoreRE.exec(ea) == null
      )
      .slice(0, maxLines)
      .map(ea => ea.replace(/^\s*at /i, ""))

    return isEmpty(result) ? ["(missing stack)"] : result
  } catch (err) {
    return ["Failed to captured stacktrace: " + err]
  }
}

export function asError(reason?: any): Error {
  if (blank(reason)) {
    throw new Error("undefined error")
  } else if (reason instanceof Error) {
    return reason
  } else if (Array.isArray(reason)) {
    const first = reason[0]
    if (first instanceof Error) {
      if (reason.length > 1) {
        ;(first as any).errors = reason.slice(1)
      }
      return first
    } else {
      return new Error(
        reason
          .map(ea => toS(ea))
          .filter(notBlank)
          .join(", ")
      )
    }
  } else {
    // Errors render as "Error: something bad happened". If you create a new
    // Error from that string, you'll get an Error that toString's to "Error:
    // Error: something bad happened", so strip the name of the error and give
    // it to the return value:
    const s = errorToS(reason).replace(/^Error: /i, "")
    return new Error(s)
  }
}

export function errorToJson(error: Error) {
  return compactValues({
    _ctor: "Error",
    message: error.message ?? String(error),
    stack: error.stack,
    name: errorName(error),
    code: errorCode(error),
    errno: errorErrno(error),
    ...omit(error as any, "name", "message", "code", "errno")
  })!
}

export function errorFromJson(obj: any) {
  return assignFields(new Error(), omit(obj, "_ctor"))
}

// core has a fancier version of this:
export function errorToArr(...errors: any[]): string[] {
  const arr = []
  for (const ea of errors) {
    if (ea == null) continue
    if (ea instanceof Error) {
      arr.push(errorName(ea), errorCode(ea), ea?.message)
    } else {
      arr.push(toS(ea))
    }
  }
  return uniq(
    compactBlankish(flatten(compact(arr).map(ea => ea.split(ErrorDelimiter))))
  )
}

// core has a fancier version of this:
export function errorToS(...errors: any[]): string {
  return errorToArr(...errors).join(ErrorDelimiter)
}
