import { lazy } from "../../core/Lazy"
import { isProd, isTest } from "../NodeEnv"
import { defaultCacheDir } from "../dir/CacheDir"
import { configDir } from "../dir/ConfigDir"
import { DefaultDockerLibraryDir } from "../dir/DefaultDockerLibraryDir"
import {
  defaultLibraryDir,
  defaultOriginalsDir
} from "../dir/DefaultLibraryDir"
import { defaultLogDir_ } from "../dir/LogDir"
import { defaultPicturesDir } from "../dir/PicturesDir"
import { isEnvTrue } from "../env/GetEnv"
import { DefaultSharedStateDir } from "../event/DefaultSharedStateDir"
import { useFsWatch } from "../fs/BaseFile"
import { DefaultPaths, DefaultPosixPaths } from "../fs/DefaultPaths"
import { isDocker } from "../platform/isDocker"
import { maxCpus } from "../work/MaxCpus"
import { Settings } from "./Settings"

// This file was extracted to prevent Settings-related dependency loops.

// Note that we're actually changing .opts.defaultValue here--it's the "real"
// default.

export const setSettingsDefaults = lazy(() => {
  Settings.configDir.opts.defaultValue = configDir

  Settings.logDir.opts.defaultValue = defaultLogDir_

  Settings.cacheDir.opts.defaultValue = defaultCacheDir

  Settings.sharedStateDir.opts.defaultValue = () =>
    DefaultSharedStateDir()?.nativePath

  Settings.toolPaths.opts.defaultValue = () =>
    (isEnvTrue("SETTINGS_IO_TEST" as any)
      ? DefaultPosixPaths
      : DefaultPaths) as string[]

  Settings.libraryDir.opts.exampleValue = () =>
    isProd && isDocker()
      ? DefaultDockerLibraryDir
      : isTest
      ? "/home/test/Pictures"
      : defaultPicturesDir()

  Settings.libraryDir.opts.defaultValue = defaultLibraryDir

  Settings.originalsDir.opts.defaultValue = defaultOriginalsDir

  Settings.scanPaths.opts.exampleValue = () => [defaultPicturesDir()]

  Settings.powerShellProcs.opts.defaultValue = () =>
    Math.max(2, Math.round(maxCpus() / 4))

  // This breaks a deploop between Settings and BaseFile:
  Settings.useFsWatch.watch(useFsWatch)
})
