import { compactBlanks } from "../../fe/Array"
import { libraryOriginalsDirPosixFile } from "../dir/LibraryDirs"
import { containedByNativePath, pathIsRoot } from "../fs/Path"
import { isPosix } from "../platform/Platform"
import { Settings } from "../settings/Settings"

export function mightBeAssetMountpoint(nativePath: string) {
  for (const dir of compactBlanks([
    Settings.libraryDir.valueOrDefault,
    libraryOriginalsDirPosixFile(),
    ...Settings.scanPaths.values
  ])) {
    // Don't let a "/" in scanPaths make everything not ignorable
    if (isPosix && pathIsRoot(dir)) continue
    if (
      containedByNativePath({
        ancestor: dir,
        descendant: nativePath,
        acceptSelf: true
      })
    )
      return true
  }
  return false
}
