import { at } from "./At"
import { flatten } from "./Flatten"
import { countIterable, sliceIterable } from "./Iterable"
import { Maybe } from "./MaybeTypes"
import { clamp, round } from "./Number"
import { Segmenter } from "./Segmenter"
import { toA } from "./toA"
import { toS } from "./toS"

export function isString(obj: any): obj is string {
  return typeof obj === "string"
}

// safe for frontend (which doesn't have a Buffer)
export function isBuffer(obj: any): obj is Buffer {
  return obj?.constructor?.name === "Buffer"
}

/**
 * @return true iff `s` only uses characters in the basic multilingual plane
 * @see https://en.wikipedia.org/wiki/Plane_(Unicode)#Basic_Multilingual_Plane
 */
export function isBMP(s: string): boolean {
  for (let i = s.length - 1; i >= 0; i--) {
    if (s.charCodeAt(i) !== s.codePointAt(i)) {
      return false
    }
  }
  return true
}

export function charAt(s: string, i: number): Maybe<string> {
  if (isBMP(s)) {
    i = Math.trunc(i) || 0
    if (i < 0) i += s.length
    if (i < 0 || i >= s.length) return undefined
    return s[i]
  } else {
    return at([...toS(s)], i)
  }
}

export function strlen(s: string) {
  const g = graphemeSegmenter()
  if (g != null) return countIterable(g.segment(s))

  // Crap, we have to do it the hard (buggy) way
  if (isBMP(s)) return s.length
  return [...s].length
}

// can't use lazy here--circular dependency.
let _graphemeSegmenter: Segmenter | null | undefined

function graphemeSegmenter(): Segmenter | null {
  // only try once:
  if (_graphemeSegmenter === undefined) {
    try {
      _graphemeSegmenter = new (Intl as any).Segmenter(undefined, {
        granularity: "grapheme"
      })
    } catch {
      // dang, no grapheme support in this environment
      _graphemeSegmenter = null
    }
  }
  return _graphemeSegmenter ?? null // < SITS, TS should have figured it out
}

/**
 * Returns a substring of the given string. Emoji and astral symbol friendly.
 *
 * @param from The starting position of the desired substring. The index of the
 * first character in the string is zero.
 * @param end The end index of the specified portion of the string. This is
 * exclusive of the element at the index 'end'. Negative values count from the
 * end index (like Array#slice). If end is undefined, then the returned value
 * extends to the end of the string.
 * @see https://mathiasbynens.be/notes/javascript-unicode
 * @see https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/length
 * @see https://blog.jonnew.com/posts/poo-dot-length-equals-two
 */
export function strslice(s: string, from: number, end?: number) {
  if (isBMP(s)) {
    // allow negative indexing from the end
    if (from < 0) from += s.length
    return s.slice(from, end)
  } else {
    const g = graphemeSegmenter()
    if (g != null) {
      const arr = sliceIterable(g.segment(s), from, end)
      return arr.map((ea: any) => ea.segment).join("")
    }
    // Sadness, no grapheme support. This doesn't work for compound emoji!
    return [...toS(s)].slice(from, end).join("")
  }
}

/**
 * Gets a substring beginning at the specified location and having the specified length.
 * @param from The starting position of the desired substring. The index of the first character in the string is zero.
 * @param length The number of characters to include in the returned substring.
 */
export function substr(str: string, from: number, length?: number): string {
  return isBMP(str)
    ? str.slice(from, length == null ? undefined : from + length)
    : strslice(str, from, length == null ? undefined : from + length)
}

export function charArr(s: string): string[] {
  const g = graphemeSegmenter()
  return g == null
    ? // unicode-friendly way of splitting a string into chars (ish):
      [...toS(s)]
    : [...g.segment(s)].map((ea: any) => ea.segment)
}

export function trimRight(s: string): string {
  return toS(s).replace(/\s+$/, "")
}

export function stripPrefix(s: string, prefix: string): string {
  const str = toS(s)
  const pfx = toS(prefix)
  return pfx.length > 0 && str.startsWith(pfx) ? str.slice(pfx.length) : str
}

export function stripPrefixes(s: string, prefixes: string[]): string {
  if (s == null) return ""
  const str = toS(s)
  for (const pfx of prefixes) {
    if (pfx != null && pfx.length > 0 && str.startsWith(pfx))
      return stripPrefixes(str.slice(pfx.length), prefixes)
  }
  return str
}

export function stripSuffix(s: string, suffix: string): string {
  if (suffix == null) return s
  const str = toS(s)
  const sfx = toS(suffix)
  return sfx.length > 0 && str.endsWith(sfx) ? str.slice(0, -sfx.length) : str
}

export function stripPrefixSuffix(
  s: string,
  { prefix, suffix }: { prefix: string; suffix: string }
): string {
  return stripSuffix(stripPrefix(s, prefix), suffix)
}

export function ensurePrefix(s: string, prefix: string): string {
  if (prefix == null || prefix === "") return s
  s = toS(s)
  prefix = toS(prefix)
  return s.startsWith(prefix) ? s : prefix + s
}

export function ensureSuffix(s: string, suffix: string): string {
  if (suffix == null || suffix === "") return s
  s = toS(s)
  suffix = toS(suffix)
  return s.endsWith(suffix) ? s : s + suffix
}

export function ensurePrefixSuffix(
  s: string,
  { prefix, suffix }: { prefix: string; suffix: string }
): string {
  return ensureSuffix(ensurePrefix(s, prefix), suffix)
}

export function ellipsize(a: any, maxLen = 80, trailingChars = 0): string {
  if (a == null) {
    return ""
  }
  maxLen = Math.max(1, round(maxLen))
  trailingChars = clamp(0, maxLen - 1, round(trailingChars))
  const arr = charArr(a)
  // ensure valid trailingChars
  return arr.length <= maxLen
    ? arr.join("")
    : arr.slice(0, maxLen - 1 - trailingChars).join("") +
        "" +
        (trailingChars > 0 ? arr.slice(-trailingChars).join("") : "")
}

export function newlineRe() {
  return /\r?\n/gm // this can't be a const as it has state
}

export function trimLastNewline(s: string): string {
  return toS(s).replace(/\r?\n$/, "")
}

/**
 * Returns the nearest occurrence of a substring to the left of a given position in the string.
 * @param haystack The string to search against.
 * @param needle The substring to search for.
 * @param position The index at which to begin searching. If omitted, the search begins at the end of the string.
 */
export function leftIndexOf(
  haystack: string,
  needle: string,
  position?: number
) {
  if (position == null) position = haystack.length
  for (let i = position; i >= 0; i--) {
    if (strslice(haystack, i).startsWith(needle)) return i
  }
  return -1
}

export function wrap(
  s: string | string[],
  opts?: { maxLineLen?: number; prefix?: string }
): string[] {
  const maxLineLen = opts?.maxLineLen ?? 80
  const prefix = opts?.prefix ?? ""

  if (Array.isArray(s) || s.includes("\n")) {
    const lines = flatten(toA(s).map(ea => toS(ea).split(newlineRe())))
    return flatten(lines.map(ea => wrap(ea, opts)))
  }
  s = ensurePrefix(toS(s), prefix).trim()
  if (s.length <= maxLineLen) {
    return [s]
  }
  const leftSliceAt = leftIndexOf(s, " ", maxLineLen)

  if (leftSliceAt > prefix.length) {
    return [
      strslice(s, 0, leftSliceAt),
      ...wrap(strslice(s, leftSliceAt + 1), opts)
    ]
  } else {
    const rightSliceAt = s.indexOf(" ", prefix.length + 1)
    if (rightSliceAt > 0 && rightSliceAt < s.length - 1) {
      return [
        strslice(s, 0, rightSliceAt),
        ...wrap(strslice(s, rightSliceAt + 1), opts)
      ]
    } else {
      return [s]
    }
  }
}

export function eqlStrings(a: string, b: string): boolean {
  return a == null || b == null
    ? false
    : a === b || a.normalize() === b.normalize()
}

export function replaceAll(
  s: string,
  searchValue: string | RegExp,
  replaceValue: string
): string {
  if (searchValue === "") return s
  return s.split(searchValue).join(replaceValue)
}

export function compressWhitespace(...s: string[]): string {
  return s.join(" ").replace(/\s+/g, " ").trim()
}

/**
 * @return s.split(sep).at(-1)
 */
export function splitLast(s: string, sep: string): string {
  s = toS(s)
  const idx = s.lastIndexOf(sep)
  return idx === -1 ? s : s.slice(idx + sep.length)
}
