import { stringify } from "../fe/JSON"
import { FifoCache } from "./FifoCache"

export interface MemoizedFunc<A, R> {
  (a: A): R
  clear(a?: A): void
  size(): number
  callCount(): number
}

export function memoize<A, R>(
  f: (a: A) => R,
  opts: { maxSize: number; ttlMs?: number }
): MemoizedFunc<A, R> {
  let callCount = 0
  const store = new FifoCache<R>(opts.maxSize, opts.ttlMs)
  const r: any = (a: A) => {
    if (a == null) return
    callCount++
    return store.getOrSet(stringify(a), () => f(a))
  }
  r.clear = (a?: A) => (a == null ? store.clear() : store.delete(stringify(a)))
  r.size = () => store.size
  r.callCount = () => callCount
  return r
}
