import { env, stdout } from "process"
import { toInt } from "../../fe/Number"
import { wrap } from "../../fe/String"

export function cliWrap(
  s: string,
  opts?: { maxLineLen?: number; prefix?: string }
) {
  const maxLineLen =
    opts?.maxLineLen ?? toInt(env.maxLineLen) ?? stdout.columns ?? 75
  return wrap(s, { maxLineLen, prefix: opts?.prefix ?? "" })
}
