import { stripCopySuffixFromName, stripDSC } from "../fs/FuzzyNames"
import { SimpleFile } from "../fs/SimpleFile"
import { isString, trimLeftPadding } from "../String"

/**
 * Pull out the basename, remove prefixes like "IMG_", "VID_", and "MOV_",
 * strip any count suffixes, then downcase.
 */
export function bname(f: SimpleFile | string, stripCounts = true): string {
  let name = isString(f) ? f : f.name
  if (stripCounts) {
    name = stripCopySuffixFromName(name)
  }
  name = trimLeftPadding(stripDSC(name))
  return name.toLowerCase().normalize()
}
