import { stderr } from "process"
import { SyncOrAsync } from "../fe/SyncOrAsync"
import { stdoutEnded } from "./StdoutEnded"
import { crlf } from "./fs/CRLF"
import { streamEnded } from "./fs/StreamEnded"
import { Settings } from "./settings/Settings"

/**
 * Emit `msg` to stdout unless `quiet` is true
 */
export function consoleLog(msg: string, ...optionalParams: any[]) {
  if (!Settings.quiet.valueOrDefault && !stdoutEnded()) {
    console.log(crlf(msg), ...optionalParams)
  }
}

export function consoleError(msg: string, ...optionalParams: any[]) {
  if (!Settings.quiet.valueOrDefault && !streamEnded(stderr)) {
    console.error(msg, ...optionalParams)
  }
}

export async function quietly<T>(f: () => SyncOrAsync<T>): Promise<T> {
  const prior = Settings.quiet.envValue
  try {
    Settings.quiet.envValue = true
    return await f()
  } finally {
    Settings.quiet.envValue = prior
  }
}
