import { lazy } from "../../core/Lazy"
import { compact, compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { KiB } from "../../fe/Units"
import { parseFixed } from "../Fixed"
import { mkLogger } from "../Logger"
import { stdout_ } from "../child/ChildProcess"
import { isMac } from "../platform/Platform"
import { DfVolume } from "./Volume"
import { commandTimeoutMs } from "./VolumeTtls"

const logger = lazy(() => mkLogger("volumes.DfPosixRaw"))

// KiB: https://en.wikipedia.org/wiki/Byte#Units_based_on_powers_of_2
function kibToBytes(s: string): number {
  return toInt(s, { defaultValue: 0 })! * KiB
}

export async function dfPosixRawLocal_() {
  return stdout_("df", ["-l", "-k", "-P"], {
    timeoutMs: commandTimeoutMs(),
    ignoreStderr: false,
    ignoreExitCode: false
  })
}

export async function dfPosixRaw({
  localsOnly,
  paths,
  output
}: {
  localsOnly?: boolean
  paths?: string[]
  output?: string
} = {}): Promise<DfVolume[]> {
  // HEY FUTURE ME: Make sure you run `coreutils` version of `df` on Alpine!
  const args = ["-k", "-P"]
  if (localsOnly === true) args.push("-l")
  args.push(...compactBlanks(paths))
  output ??= await stdout_("df", args, {
    timeoutMs: commandTimeoutMs(),
    // https://askubuntu.com/questions/1227667/df-command-throws-error-on-run-user-1000-doc-folder
    ignoreStderr: true,
    ignoreExitCode: true
  })
  if (blank(output)) {
    logger().warn("empty output", { localsOnly, paths })
    return []
  }

  logger().debug("output", { localsOnly, paths, output })

  const arr = parseDfVolumes(output)
  if (localsOnly === true) {
    arr.forEach(ea => (ea.remote = false))
  }
  return arr
}

interface ParsedVolume {
  Filesystem: string
  "1024-blocks": string
  Used: string
  Available: string
  "Mounted on": string
}

export function parseDfVolumes(output: string) {
  const parsed = parseFixed(
    [
      "Filesystem",
      "1024-blocks",
      "Used",
      "Available",
      "Capacity",
      "Mounted on"
    ],
    output
  )
  return compact(parsed.map(ea => parseDfVolume(ea as any)))
}

export function parseDfVolume(vol: ParsedVolume): Maybe<DfVolume> {
  // HEY FUTURE ME: Don't filter "ignorable" volumes here. `mountpoints()` and
  // `volumes()` does that for us.

  const filesystem = vol["Filesystem"]
  const mountpoint = vol["Mounted on"]
  const size = kibToBytes(vol["1024-blocks"]) ?? 0
  if (size === 0) {
    logger().info("parseDfVolume(): skipping (size is 0)", { vol })
    return
  }

  const used = kibToBytes(vol["Used"]) ?? 0
  const available = kibToBytes(vol["Available"]) ?? 0

  if (used === 0 && available === 0) {
    logger().info("parseDfVolume(): skipping (used and available is 0)", {
      vol,
      size,
      used,
      available
    })
    return
  }

  return {
    filesystem,
    mountpoint,
    // 20220530: macOS MIS-REPORTS VOLUME SIZES, YAY
    size: isMac ? used + available : size,
    used,
    available
  }
}
