import { Command } from "commander"
import process from "process"
import { promises, Transform } from "stream"
import { filterInPlace, isEmpty, isNotEmpty, uniq } from "../fe/Array"
import { sortBy } from "../fe/ArraySort"
import { dayMs, minuteMs } from "../fe/Date"
import { delay } from "../fe/Delay"
import { flatten } from "../fe/Flatten"
import { map } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { gt0 } from "../fe/Number"
import { Obj } from "../fe/Object"
import { gt } from "../fe/Primitive"
import { ServiceNames } from "../fe/ServiceNames"
import { exit } from "./async/Exit"
import { mapAsync, thenCompact } from "./async/Promise"
import { CLI, CommandPlugin } from "./cli/CLI"
import { ColorArgs } from "./cli/ColorArgs"
import { decodeDuration } from "./date/DurationCodec"
import { BaseFile } from "./fs/BaseFile"
import { Newline } from "./fs/CRLF"
import { DirectoryEntry } from "./fs/DirectoryEntry"
import { matcherForExtOrCompressedExt } from "./fs/Gzip"
import { LineReader } from "./fs/LineReader"
import { DefaultLogFormatter } from "./log/DefaultLogFormatter"
import { LogEntry, logEntrySorter } from "./log/LogEntry"
import { LogReader } from "./log/LogReader"
import { chunkToLogEntry } from "./log/ReadLogEntries"
import { mapGt0 } from "./Number"
import { Settings } from "./settings/Settings"
import { readSystemSettings } from "./settings/SettingsIO"

let recentMs: number

export const Recent: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      `--recent [duration]`,
      `Automatically cat recent logs. The optional "duration" argument can either be a number, which will be interpreted in minutes, or an ISO 8601 duration string, like "P1D" for 1 day, or "PT20M" for 20 minutes. Defaults to 1 day.`
    ),
  afterParse: (opts: Obj) => {
    if (opts.recent != null) {
      if (opts.recent === true) recentMs = dayMs
      else
        mapGt0(opts.recent, ea => (recentMs = ea * minuteMs)) ??
          decodeDuration(opts.recent)
    }
  }
}

function consolelog(s: string) {
  process.stdout.write(s + Newline)
}

async function fileOrDir(path: string) {
  const f = BaseFile.for(path)
  if (await f.isFile()) {
    return [f.nativePath]
  } else if (await f.isDirectory()) {
    const arr = await f.descendants(ea => ea.ext.startsWith(".log"))
    return arr?.map(ea => ea.nativePath)
  }
  return
}

async function run() {
  const c = new CLI(
    ServiceNames.logcat,
    "[FILE_OR_DIR...]",
    "FILE_OR_DIR may be either a logfile or a directory that will be scanned for .log, .log.gz, and .log.bz files."
  )
    .add(Recent, ColorArgs)
    .parse()
  process.on("SIGINT", () => exit({ reason: "SIGINT", status: 0 }))
  process.on("SIGTERM", () => exit({ reason: "SIGTERM", status: 0 }))

  const files: string[] = uniq(
    flatten(await mapAsync({ name: "parseArgs", arr: c.args, f: fileOrDir }))
  )

  if (gt0(recentMs)) {
    const matcher = matcherForExtOrCompressedExt(".log")
    await readSystemSettings()
    const minStatMs = Date.now() - recentMs
    await BaseFile.for(Settings.logDir.valueOrDefault).visitDescendants(
      async ea => {
        const maxStat = await ea.maxStatMs()
        if (matcher(ea.ext) && (await ea.isFile()) && gt(maxStat, minStatMs))
          files.push(ea.nativePath)
      }
    )
  }

  const lf = DefaultLogFormatter()

  if (isEmpty(files)) {
    if (isNotEmpty(c.args)) {
      console.error(
        lf.format("error", "logcat", "No valid logfiles were provided.")
      )
      c.help()
    }
    consolelog(
      lf.format("info", "logcat", "No log files provided: reading from stdin.")
    )
    consolelog(
      lf.format(
        "warn",
        "logcat",
        "The output will not be sorted chronologically."
      )
    )

    await promises.pipeline(
      process.stdin,
      new LineReader(),
      new Transform({
        transform(chunk, _encoding, done) {
          map(chunkToLogEntry(chunk), le =>
            consolelog(lf.formatLogEntry({ from: "", ...le }))
          )
          done()
        }
      }) as any
    )

    return
  }

  const readers = await thenCompact(
    files.map(async ea => {
      const de = await DirectoryEntry.for(ea)
      return de != null && gt0(await de.size())
        ? new LogReader(de, console.error)
        : undefined
    })
  )

  consolelog(
    lf.format(
      "debug",
      "logcat",
      "Reading from ",
      readers.map(ea => ea.f.nativePath)
    )
  )

  function vacuum() {
    filterInPlace(readers, ea => !ea.complete())
  }

  function oldestLogEntries() {
    let ts: Maybe<number>
    vacuum()
    for (const reader of readers) {
      if (!reader.ready()) {
        return // wait for I/O
      }
      const le = reader.peek()
      if (le?.ts != null && (ts == null || le.ts < ts)) {
        ts = le.ts
      }
    }

    if (ts == null) return

    const arr: LogEntry[] = []
    for (const reader of readers) {
      arr.push(...reader.shiftLte(ts))
    }
    return sortBy(arr, logEntrySorter)
  }

  while (readers.length > 0) {
    try {
      const arr = oldestLogEntries()
      if (arr != null) {
        for (const ea of arr) {
          consolelog(lf.formatLogEntry(ea))
        }
      } else {
        await delay(10, false)
      }
    } catch (err) {
      console.error("caught error", err)
    }
  }

  // Don't exit until we've flushed the last line!

  process.stdout.write(Newline, () => exit({ reason: "", status: 0 }))
}

void run()
