import { compact, isEmpty, uniq } from "./Array"
import { sort } from "./ArraySort"
import { blank, notBlank } from "./Blank"
import { isFunction } from "./isFunction"
import { Defined, defined, map, Unpick } from "./Maybe"
import {
  DefinedValued,
  Maybe,
  MaybeNull,
  MaybeValued,
  ReqValued
} from "./MaybeTypes"
import { MultiMap } from "./MultiMap"
import { isPrimitive } from "./Primitive"
import { toS } from "./toS"

export type Valued<T> = Record<string, T>

export type KeyOf<T> = string & keyof T
// export type KeyOf<T> = Extract<keyof T, string>

export type StringValued = Valued<Maybe<string>>
export type StrBoolValued = Valued<Maybe<string | boolean>>
export type NativeValued = Valued<Maybe<string | boolean | number>>

export type Obj = Record<string, any>

export function isObject(o: any): o is object {
  return o != null && typeof o === "object"
}

export function tap<T>(o: T, block?: (t: T) => void): T {
  if (block != null) {
    block(o)
  } else {
    if (typeof o === "string") {
      console.log(o)
    } else {
      console.dir(o, { depth: null })
    }
  }
  return o
}

export function keys<T extends Obj>(obj: Maybe<T>): KeyOf<T>[] {
  // `typeof null === "object"` because js wants to F*CK YOU UP
  if (obj == null || typeof obj !== "object") return []
  return Object.keys(obj).filter(
    k =>
      typeof k === "string" &&
      (obj["propertyIsEnumerable"] == null ||
        obj["propertyIsEnumerable"](k) === true)
  )
}

export function keysWithDefinedValues<T extends Obj>(
  obj: Maybe<T>
): KeyOf<T>[] {
  return obj == null ? [] : keys(obj).filter(k => obj[k] !== undefined)
}

/**
 * @return true iff o is null, undefined, or only has keys associated to an
 * `undefined` value
 */
export function isEmptyObj(o: any): o is undefined {
  if (typeof o !== "object") return true
  for (const k in o) {
    if (o[k] !== undefined) return false
  }
  return true
}

export function notEmptyObj(o: any): o is Obj {
  return !isEmptyObj(o)
}

export function mapCompactObj<T>(o: Maybe<Obj>, f: (_o: Obj) => T): Maybe<T> {
  const c = compactValues(o)
  return notEmptyObj(c) ? f(c) : undefined
}

export function values<T extends Obj>(o: T): T[KeyOf<T>][] {
  return keys(o).map(k => o[k]) as any
}

// polyfill for Object.entries. Does no nullish-value compaction!
export function entries<T extends Obj>(obj: T): [KeyOf<T>, T[KeyOf<T>]][] {
  return obj == null
    ? []
    : Object.entries?.(obj) ?? keys(obj ?? {}).map(ea => [ea, obj[ea]])
}

export function fromEntries(
  arr: MaybeNull<Iterable<Maybe<[Maybe<string>, any]>>>,
  obj?: any
): any {
  if (arr == null) return obj
  // don't use Object.create(null), json stringify will break!
  for (const ea of arr) {
    if (ea != null && Array.isArray(ea)) {
      const [k, v] = ea
      // allow NULL fields:
      if (k != null && v !== undefined) {
        if (typeof obj !== "object") obj = {}
        obj[k] = v
      }
    }
  }
  return obj
}

export function assignFields<T>(
  dest: T,
  src: Maybe<Partial<T>>,
  opts?: { omitKeys?: string[]; assignNullish?: boolean }
): T {
  if (src == null) {
    return dest
  }
  for (const [k, v] of entries(src)) {
    if (opts?.omitKeys?.includes(k) === true) continue
    if (v != null || true === opts?.assignNullish) (dest as any)[k] = v
  }
  return dest
}

/**
 * Only overwrite fields from src that are undefined in dest.
 */
export function assignUndefinedFields<T>(
  dest: T,
  src: Maybe<Pick<T, any>>,
  ...srcKeys: KeyOf<T>[]
): T {
  if (src == null) {
    return dest
  }
  for (const ea of srcKeys) {
    const v = src[ea]
    if (v !== undefined && dest[ea] === undefined) dest[ea] = v
  }
  return dest
}

/**
 * Assigns all keys from src to dest, even if they are null or undefined.
 */
export function assignAllFields<T>(dest: T, src: Maybe<Pick<T, any>>): T {
  for (const [k, v] of entries(src ?? {})) {
    ;(dest as any)[k] = v
  }
  return dest
}

export function definedValues<T extends Obj>(
  t: Maybe<T>
): Maybe<DefinedValued<Partial<T>>> {
  if (t == null) return undefined
  const arr = entries(t)
  if (arr.every(([, v]) => v !== undefined)) return t
  const pairs = arr.filter(([, v]) => v !== undefined)
  return isEmpty(pairs) ? undefined : fromEntries(pairs)
}

export function compactValues<T extends Obj>(
  t: Maybe<T>
): Maybe<ReqValued<Partial<T>>> {
  if (t == null) return undefined
  const arr = entries(t)
  if (arr.every(([, v]) => v != null)) return t
  const pairs = arr.filter(([, v]) => v != null)
  return isEmpty(pairs) ? undefined : fromEntries(pairs)
}

export function compactValuesDeep(o: any): any {
  if (o == null) return
  if (isPrimitive(o)) return o
  if (Array.isArray(o)) {
    return compact(o.map(compactValuesDeep))
  } else {
    const arr = compact(
      entries(o).map(([k, v]) => map(compactValuesDeep(v), ea => [k, ea]))
    )
    return arr.length === 0 ? undefined : fromEntries(arr as any)
  }
}

export function compactBlankValues<T extends Obj>(
  t: Maybe<T>
): Maybe<ReqValued<Partial<T>>> {
  if (t == null) return undefined
  const pairs = entries(t).filter(([k, v]) => k != null && notBlank(v))
  return isEmpty(pairs) ? undefined : fromEntries(pairs)
}

export function mapFields(
  o: Obj,
  f: (key: string, value: any) => Maybe<[string, any]>,
  obj = {}
): Obj {
  // DON'T DELETE THIS SORT! This makes model upserts have the same prepared
  // statements:

  const arr = compact(sort(keys(o)).map(k => f(k, o[k])))
  return fromEntries(
    arr.filter(([k, v]) => k != null && v !== undefined), // < allow NULL values!
    obj
  )
}

export function pick<T, K extends keyof T>(
  obj: T,
  ...keyNames: K[]
): Pick<T, K> {
  if (obj == null) return obj
  const result = {} as any
  for (const key of keyNames) {
    const v = obj[key]
    if (v !== undefined) result[key] = obj[key]
  }
  return result
}

export function pickCompact<T, K extends keyof T>(
  obj: T,
  ...keyNames: K[]
): Maybe<Pick<T, K>> {
  if (obj == null) return
  const result = {} as any
  for (const key of keyNames) {
    const v = obj[key]
    if (v !== undefined) result[key] = obj[key]
  }
  return result
}

/**
 * @param keynames can be "key.child.grandchild" format to pick deep objects.
 */
export function pickDeep<T, K extends keyof T>(
  obj: T,
  ...keyNames: (K | string)[]
): Pick<T, K> {
  if (obj == null) return obj
  if (Array.isArray(obj)) return obj.map(ea => pickDeep(ea, ...keyNames)) as any
  const flat: string[] = []
  const mm = new MultiMap<string, string>()
  for (const key of keyNames.map(toS)) {
    const indexOf = key.indexOf(".")
    if (indexOf === -1) {
      flat.push(key)
    } else {
      mm.add(key.slice(0, indexOf) as any, key.slice(indexOf + 1))
    }
  }
  const result = {} as any
  for (const key of flat) {
    const v = obj[key as K]
    // allow NULL values:
    if (v !== undefined) {
      result[key as K] = v
    }
  }
  const deepKeysByLocalField = [...mm.entries()]
  for (const [key, arr] of deepKeysByLocalField) {
    const v = obj[key as K]
    if (v != null) {
      const ea = pickDeep(v as any, ...(arr as any))
      // allow NULL values:
      if (ea !== undefined) {
        result[key] = ea
      }
    }
  }
  return result
}

export function pickNonBlank<T, K extends keyof T>(
  obj: T,
  ...keyNames: K[]
): Pick<T, K> {
  if (obj == null) return obj
  const result = {} as any
  for (const key of keyNames) {
    const v = obj[key as K]
    if (notBlank(v)) {
      result[key as K] = v
    }
  }
  return result
}

/**
 * @return the first value in `obj` associated to `keyNames` that `predicate` returns true.
 */
export function pickFirst<T, K extends keyof T>(
  obj: T,
  keyNames: K[],
  predicate: (v: T[K]) => boolean = defined
): Maybe<T[K]> {
  if (obj == null) return
  for (const key of keyNames) {
    if (predicate(obj[key])) return obj[key]
  }
  return
}

export function omit<T extends Record<string, any>, S extends keyof T>(
  t: Maybe<T>,
  ...keysToOmit: S[]
): Unpick<T, S> {
  if (t == null || keysToOmit.every(ea => blank(t[ea]))) {
    return t as any
  }
  const pairs = entries(t).filter(([k]) => !keysToOmit.includes(k as any))
  return isEmpty(pairs) ? undefined : fromEntries(pairs)
}

export function isReqValued<T>(t: MaybeValued<T>): t is ReqValued<T> {
  return values(t as any).every(ea => ea != null)
}

export function allKeysDefined<T, K extends keyof T>(
  obj: Maybe<T>,
  ...keyNames: K[]
): obj is Defined<T> {
  return obj != null && keyNames.every(k => obj[k] != null)
}

/**
 * @return t if it `isReqValued()`, otherwise undefined
 */
export function toReqValued<T>(t: MaybeValued<T>): Maybe<ReqValued<T>> {
  return isReqValued(t) ? t : undefined
}

export function reqValuedOrElse<T>(t: MaybeValued<T>): Maybe<ReqValued<T>> {
  return isReqValued(t) ? t : undefined
}

export function mapReqValued<T, U>(
  t: MaybeValued<T>,
  f: (ea: ReqValued<T>) => U
): Maybe<U> {
  return isReqValued(t) ? f(t) : undefined
}

export function onlyReqValued<T>(arr: MaybeValued<T>[]): ReqValued<T>[] {
  return arr.filter(isReqValued)
}

export function filter<T extends Obj, K extends string & keyof T>(
  obj: T,
  predicate: (k: K, value: T[K]) => boolean
): Partial<T> {
  if (obj == null) return obj
  return fromEntries(
    entries(obj).filter(([k, v]) => predicate(k as K, v as T[K]))
  )
}

/**
 * As opposed to .keys() which only returns fields directly associated to the
 * given Obj, `allKeys` returns all direct *and inherited* properties (if
 * `obj` is a class instance, for example)
 */
export function allKeys(obj: any): string[] {
  const methods: string[] = keys(obj)
  while (null != (obj = Reflect.getPrototypeOf(obj))) {
    methods.push(
      ...(Reflect.ownKeys(obj).filter(
        key => typeof key === "string"
      ) as string[])
    )
  }
  return uniq(methods)
}

export function maybeCall(o: any, method: string, ...args: any[]) {
  const f = o?.[method]
  return isFunction(f) ? f.bind(o)(...args) : undefined
}

export function firstValueCaseInsensitive(o: any, key: string): any {
  if (blank(key)) return
  if (o[key] != null) return o[key]
  const lckey = key.toLowerCase().normalize()
  for (const k of keys(o)) {
    if (lckey === k.toLowerCase().normalize() && o[k] != null) return o[k]
  }
  return
}

/**
 * Recursively reduce single-value objects to only their value, and retain
 * multi-value objects.
 */
export function flattenObject(o: any): any {
  if (Array.isArray(o)) return o.map(flattenObject)
  if (typeof o !== "object") return o
  const f = mapFields(o, (key, value) => [key, flattenObject(value)])
  const v = values(f)
  return v.length === 1 ? v[0] : f
}
