import { inspect } from "util"
import { isNotEmpty } from "../../fe/Array"
import { keys } from "../../fe/Object"
import { isPromiseLike } from "../../fe/Promise"
import { ellipsize, isString } from "../../fe/String"
import { isError } from "../../fe/isError"
import { diff } from "../Array"
import { mapNullEntries } from "../Object"
import { errorToVerbose } from "../error/Error"
import { Settings } from "../settings/Settings"
export const PromisePlaceholder = "(promise)"

/**
 * Prepare a log entry's `meta` for serialization
 */
export function prepMeta(meta: any, levels = 4): any {
  if (meta == null) {
    return null // <- util.inspect removes `undefined` object key/value pairs
  }
  if (levels < 0) return ""
  // If it's a primitive, we're done:
  if (isString(meta) || Buffer.isBuffer(meta)) {
    return ellipsize(meta.toString(), 256, 32)
  }
  if (isError(meta)) {
    return errorToVerbose(meta)
  }
  if (Array.isArray(meta)) {
    const limit = Settings.logContextLimit.valueOrDefault
    const len = Math.floor(limit / 2)
    return meta.length <= limit
      ? meta
      : [
          ...meta.slice(0, len).map(ea => prepMeta(ea, levels - 1)),
          ` (${meta.length} total entries)`,
          ...meta.slice(-len).map(ea => prepMeta(ea, levels - 1))
        ]
  }
  // lazy() returns a function!
  if (typeof meta === "object" || typeof meta === "function") {
    for (const meth of ["toLogJSON", "toJSON", inspect.custom]) {
      if (typeof meta[meth] === "function") {
        return prepMeta(meta[meth](), levels - 1)
      }
    }
    if (isPromiseLike(meta)) {
      // Deferred and Latch already have a toLogJSON, so meta must not be
      // something from us.
      return PromisePlaceholder
    }

    // Have we already truncated this object?
    if ("" in meta) return meta

    const result = mapNullEntries(
      meta,
      (_, v) => prepMeta(v, levels - 1),
      Settings.logContextLimit.valueOrDefault
    )

    const omittedKeys = diff(keys(meta), keys(result))
    if (isNotEmpty(omittedKeys)) {
      result[""] = "omitted: " + ellipsize(omittedKeys.join(", "), 128, 32)
    }
    return result
  }

  // Otherwise give up:
  return meta
}
