import { Dirent } from "fs"
import { blank } from "../../fe/Blank"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { basename, SimpleFile } from "./SimpleFile"

export const WipTimeoutMs = ShortCommandTimeoutMs
export const WipPrefix = ".WIP-"

export function isWip(p: SimpleFile | string | Dirent): boolean {
  return !blank(p) && basename(p).startsWith(WipPrefix)
}
