import { arch, platform, release } from "os"
import { lazy } from "../core/Lazy"
import { mapNotBlankOr, notBlank } from "../fe/Blank"
import { minuteMs } from "../fe/Date"
import { map2Or } from "../fe/Maybe"
import { isString } from "../fe/String"
import { uniqCount } from "./Array"
import { parseEnvTokens } from "./EnvTokens"
import { mkLogger } from "./Logger"
import { isLinux } from "./platform/Platform"
import { isDocker } from "./platform/isDocker"
import { cpuInfo } from "./work/CpuInfo"

const logger = lazy(() => mkLogger("os"))

export const osArch = lazy(() => `${osName()} on ${arch()}`)
export const osFullName = lazy(() => osArch() + (isDocker() ? " (Docker)" : ""))

function osName() {
  switch (platform()) {
    case "linux":
      return osNameLinux()
    case "darwin":
      return osNameMac()
    case "win32": // doesn't mean 32 bit (BECAUSE WINDOWS)
      return osNameWin()
    default:
      return osNameDefault()
  }
}

function osNameDefault() {
  return platform() + " " + release()
}

