// `man os-release`:
const OsReleaseFiles = ["/etc/os-release", "/usr/lib/os-release"]

export const osReleaseTokens = lazy(() => {
  if (!isLinux) return

  for (const filename of OsReleaseFiles) {
    try {
      const input = readFileSync(filename).toString()
      if (notBlank(input)) {
        return parseEnvTokens({ input, lowerCaseKeys: true })
      }
    } catch (error) {
      logger().warn("failed to read os-release file", { filename, error })
    }
  }

  return
})

export const osNameLinux = lazy(() => {
  const t = osReleaseTokens()
  if (notBlank(t?.pretty_name)) return t?.pretty_name
  return map2Or(
    t?.name,
    t?.version ?? t?.version_id,
    (n, v) => n + " " + v,
    osNameDefault
  )
})

const MacCodenames = {
  "10": {
    "6": "Snow Leopard",
    "7": "Lion",
    "8": "Mountain Lion",
    "9": "Mavericks",
    "10": "Yosemite",
    "11": "El Capitan",
    "12": "Sierra",
    "13": "High Sierra",
    "14": "Mojave",
    "15": "Catalina"
  },
  "11": "Big Sur", // 11.1, 11.2, ...
  "12": "Monterey" // 12.1, 12.2, ...
}

// Windows 11:
// require("os").release() === '10.0.22000'

// Windows 10:
// require("os").release() === '10.0.19044'

// YAY

const WinReleaseToVersions = {
  "10": "10",
  "6": { "3": "8.1", "2": "8", "1": "7", "0": "Vista" },
  "5": { "2": "Server 2003", "1": "XP", "0": "2000" },
  "4": { "9": "ME", "1": "98", "0": "95" }
}

function majorMinor(ver: string) {
  return ver.split(".").slice(0, 2)
}

function versionToCodename(
  ver: string,
  codenames: typeof MacCodenames | typeof WinReleaseToVersions
) {
  const [major, minor] = majorMinor(ver)
  const m = (codenames as any)[major]
  return isString(m) ? m : m?.[minor]
}

const macProductVersion = lazy(() =>
  execSync("sw_vers -productVersion").toString().trim()
)

function macCodename(productVersion = macProductVersion()) {
  return versionToCodename(productVersion, MacCodenames)
}

export function osNameMac(productVersion = macProductVersion()) {
  try {
    return mapNotBlankOr(
      macCodename(productVersion),
      ea => `macOS ${ea} (${productVersion})`,
      osNameDefault
    )
  } catch (err) {
    logger().warn("osNameMac(): unknown release", err)
    return osNameDefault()
  }
}

export function osNameWin(r = release()) {
  const codename = r.startsWith("10.0.22")
    ? "11"
    : versionToCodename(r, WinReleaseToVersions)
  if (codename != null) {
    return `Windows ${codename} (${r})`
  } else {
    logger().warn("osNameWin(): unknown release: " + r)
    return `Windows (${r})`
  }
}

export const CPUs = lazy(
  () =>
    uniqCount(cpuInfo().map(c => c.model))
      .map(ea => `${ea.count}  ${ea.t}`)
      .join(", "),
  minuteMs
)
