import fs from "fs"
import { delimiter } from "path"
import process from "process"
import { lazy } from "../../core/Lazy"
import { uniq } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { entries, StringValued } from "../../fe/Object"
import { debom } from "../Buffer"
import { parseEnvTokens } from "../EnvTokens"
import { ee } from "../event/EventEmitter"
import { PS_ENV_KEYS } from "./EnvKeys"
import { SensitiveEnvRE } from "./GetEnv"

export const env = lazy<StringValued>(() => {
  const paths = uniq(
    PS_ENV_KEYS.map(k => process.env[k])
      // handle FILE and PATH values:
      .join(delimiter)
      .split(delimiter)
      .filter(notBlank)
  )

  const re = SensitiveEnvRE()
  const result = { ...process.env }
  for (const file of paths) {
    try {
      const input = debom(fs.readFileSync(file))
      const tokens = parseEnvTokens({ input, lowerCaseKeys: false })
      for (const [k, v] of entries(tokens)) {
        if (!re.test(k)) {
          result[k] = v
        }
      }
    } catch (error) {
      // not logger, as we may not have the logger up and running yet.
      console.warn("env(): failed to read .env file, " + file, error)
    }
  }
  return result
})

later(() => {
  ee().on("clearCache", () => env.unset())
})
