import { lazy } from "../../core/Lazy"
import { isDocker } from "../platform/isDocker"
import { setSettingsDefaults } from "../settings/SettingsDefaults"
import { DefaultDockerLibraryDir } from "./DefaultDockerLibraryDir"
import { desktopConfigDir, desktopConfigDirs } from "./DesktopConfigDir"
import { envConfigDir } from "./EnvConfigDir"
import { firstDir } from "./FirstDir"

// PhotoStructure's "system" config dir is not backed by a Setting, but will
// respect customizations via env.PS_CONFIG_DIR (but not configDir, because that
// may not be photostructure-specific)

export const configDir = lazy(() => {
  setSettingsDefaults()
  return envConfigDir() ?? (isDocker() ? dockerConfigDir() : desktopConfigDir())
})

const dockerConfigDirs = [
  {
    dir: "/ps/config",
    preexistingDir: "/ps/config"
  },
  {
    dir: DefaultDockerLibraryDir + "/.photostructure/docker-config",
    preexistingDir: DefaultDockerLibraryDir
  }
]

function dockerConfigDir() {
  return firstDir({
    dirs: dockerConfigDirs,
    desc: "config"
  })
}

export function resetConfigDir() {
  configDir.unset()
  configDir.unset()
}

export function osConfigDirs() {
  return (isDocker() ? dockerConfigDirs : desktopConfigDirs()).map(ea => ea.dir)
}
