import { ServiceName, ServiceNames } from "../fe/ServiceNames"
import { setupLogger } from "./log/SetupLogger"
import { logStartup } from "./LogStartup"
import { isTest } from "./NodeEnv"
import { isMainService, serviceName } from "./ServiceNames"

export function setServiceName(s: ServiceName) {
  // Normalize "desktop" to "main":
  if (isMainService(s as any)) s = ServiceNames.main
  const prior = serviceName.prior()
  if (prior === s) return // already done

  if (!isTest && prior != null && prior !== s) {
    console.error("Don't set service name twice", {
      prior,
      new: s
    })
  }
  serviceName.set(s)
  setupLogger()
  logStartup()
}
