import fs from "fs"
import { lazy } from "../../core/Lazy"
import { isLinux, isLinux_arm, isMac, isWin } from "../platform/Platform"

// this works in and out of docker containers:
export function containsRpiModel(cpuinfoPath = "/proc/cpuinfo") {
  if (isWin || isMac) return false
  try {
    const contents = fs.readFileSync(cpuinfoPath).toString()
    return null != contents.match(/^\s*model\s*:\s*Raspberry Pi/im)
  } catch {
    return false
  }
}

export const isRaspberryPi = lazy(() => isLinux_arm && containsRpiModel())

/**
 * This seems to only be set on Raspberry PIs:
 */
export const procDeviceModel = lazy(() => {
  try {
    return isLinux
      ? // "Raspberry Pi 4 Model B Rev 1.1" on pi4
        fs.readFileSync("/proc/device-tree/model").toString()
      : undefined
  } catch {
    return
  }
})
