import _fs from "fs"
import _path from "path"
import { blank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe, MaybePromiseMaybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { equalsIgnoreCase, isString, stripPrefix } from "../String"
import { isWin } from "../platform/Platform"
import { NativePathed, resolve } from "./Path"
import { isSimpleDirent } from "./SimpleDirent"

// BEST TYPE NAME EVAR
export type SimpleFileOrString = SimpleFile | string

export interface SimpleFile {
  /**
   * Absolute path
   */
  readonly nativePath: string
  /**
   * Absolute path elements
   */
  readonly pathnames: string[]
  /**
   * The file name including extension (if any) such as 'index.html'
   */
  readonly base: string
  /**
   * The file extension (if any) such as '.html'
   */
  readonly ext: string
  /**
   * The file name without extension (if any) such as 'index'
   */
  readonly name: string
  /**
   * The nativePath of the parent directory
   */
  readonly dir: string

  /**
   * True iff this directory has no parent
   */
  readonly isRoot: boolean

  isFile(): SyncOrAsync<boolean>
  isDirectory(): SyncOrAsync<boolean>
  isFileSync(): boolean
  isDirectorySync(): boolean

  // No isSymbolicLink, as this method is only valid when using fs.lstat():
  // https://nodejs.org/dist/latest-v14.x/docs/api/fs.html#fs_stats_issymboliclink

  children(): MaybePromiseMaybe<this[]>
  childNames(): MaybePromiseMaybe<string[]>
  parent(): SyncOrAsync<Maybe<this>>

  size(): SyncOrAsync<Maybe<number>>
  mtimeMs(): SyncOrAsync<Maybe<number>>
}

export function isSimpleFile(object: any): object is SimpleFile {
  return (
    object != null &&
    // HEY FUTURE ME! Don't `instanceof DirectoryEntry` or BaseFile here: that
    // causes a deploop
    typeof object === "object" &&
    !Array.isArray(object) &&
    typeof object.nativePath === "string" &&
    typeof object.base === "string" &&
    typeof object.ext === "string" &&
    typeof object.base === "string" &&
    typeof object.dir === "string" &&
    typeof object.isFile === "function"
  ) // meh good enough
}

export function toNativePath_(s: string | NativePathed) {
  if (s == null) {
    throw new Error("toNativePath(null)")
  }
  if (blank(s)) {
    throw new Error("toNativePath(blank)")
  }
  if (isString(s)) {
    // only resolve strings--if it has a nativePath, it's already resolved:
    return resolve(s)
  }
  if (blank(s.nativePath)) {
    throw new Error(`toNativePath(${stringify(s)})`)
  }
  return s.nativePath
}

export function basename(s: SimpleFileOrString | _fs.Dirent) {
  return s instanceof _fs.Dirent
    ? s.name
    : isSimpleFile(s)
    ? s.base
    : _path.basename(toNativePath_(s))
}

export function dirname(s: SimpleFileOrString) {
  return isSimpleFile(s) ? s.dir : _path.dirname(toNativePath_(s))
}

const winDriveRE = /^[a-z]:$/i

export function rootDir(s: SimpleFileOrString): Maybe<string> {
  // TODO: handle UNC paths
  if (isSimpleFile(s)) {
    const arr = s.pathnames
    return isWin && arr[0].match(winDriveRE) != null ? arr[1] : arr[0]
  } else {
    const str = stripPrefix(s, "/") // ignore the initial "/"
    const i = str.indexOf(_path.sep)
    return i > 0 ? str.slice(1, i) : undefined
  }
}

export function findFileIndex(
  needle: SimpleFile,
  haystack: SimpleFile[]
): number {
  for (let i = 0; i < haystack.length; i++) {
    if (haystack[i].nativePath === needle.nativePath) return i
  }
  for (let i = 0; i < haystack.length; i++) {
    if (equalsIgnoreCase(haystack[i].nativePath, needle.nativePath)) return i
  }
  return -1
}

export function isFileSync(s: SimpleFileOrString) {
  if (blank(s)) return false

  if (s instanceof _fs.Dirent) return s.isFile()

  for (const ea of [s, (s as any)["dirent"]]) {
    if (isSimpleDirent(ea)) return ea.isFile // < not a method.
  }

  try {
    const stat = _fs.statSync(toNativePath_(s))
    return stat.isFile()
  } catch {
    return false
  }
}

export async function someSelfOrAncestor<T extends SimpleFile>(
  sf: Maybe<T>,
  predicate: (t: T) => SyncOrAsync<boolean>
): Promise<boolean> {
  if (sf == null) return false
  const result = await predicate(sf)
  return result === true || sf.isRoot
    ? result
    : someSelfOrAncestor(await sf.parent(), predicate)
}

export async function firstSelfOrAncestor<T extends SimpleFile, R>(
  sf: Maybe<T>,
  predicate: (t: T) => SyncOrAsync<R>
): PromiseMaybe<R> {
  if (sf == null) return
  const result = await predicate(sf)
  return result != null || sf.isRoot
    ? result
    : firstSelfOrAncestor(await sf.parent(), predicate)
}
