import { notBlankToS } from "../../fe/Blank"
import { toFloat } from "../../fe/Number"
import { Setting, SettingOpts } from "./Setting"

export class FloatSetting extends Setting<number> {
  constructor(opts: SettingOpts<number>) {
    super({
      ...opts,
      toEnv: notBlankToS,
      fromEnv: toFloat
    })
  }
}
