import { Maybe } from "../fe/MaybeTypes"
import { setUnrefTimeout } from "./async/Timers"

export class TTLSet<T> implements Set<T> {
  readonly [Symbol.toStringTag] = "TTLSet"
  private readonly expireListeners: ((k: T) => void)[] = []

  // Maps entries to insertion time values. Entries should expire after value + this.ttlMs.
  private readonly delegate = new Map<T, number>()

  /**
   * @param ttlMs if external consumers change this public value, we won't
   * auto-re-vacuum existing values, but the new ttl will be correct for new
   * values.
   */
  constructor(
    public ttlMs: number,
    readonly scheduleExpirationTimers = false
  ) {}

  get size(): number {
    this.#vacuum()
    return this.delegate.size
  }

  add(value: T): this {
    this.delegate.set(value, Date.now())
    if (this.scheduleExpirationTimers)
      setUnrefTimeout(() => this.#vacuum(), this.ttlMs + 1)
    return this
  }

  addAll(values: T[]) {
    for (const ea of values) {
      this.add(ea)
    }
    return
  }

  addIfMissing<R>(value: T, onMissing: () => R): Maybe<R> {
    const prior = this.delegate.get(value)
    if (prior == null || this.#isEntryExpired(value, prior)) {
      this.add(value)
      return onMissing()
    } else {
      return
    }
  }

  clear(): this {
    this.delegate.clear()
    return this
  }

  delete(value: T): boolean {
    return this.delegate.delete(value)
  }

  forEach(callbackfn: (value: T, index: T, set: Set<T>) => void): void {
    for (const [value, ctime] of this.delegate) {
      if (!this.#isEntryExpired(value, ctime)) {
        callbackfn(value, value, this)
      }
    }
  }

  has(value: Maybe<T>): boolean {
    return value != null && !this.#isEntryExpired(value)
  }

  values(): IterableIterator<T> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<T> {
      for (const [k, v] of self.delegate) {
        if (!self.#isEntryExpired(k, v)) {
          yield k
        }
      }
    }
    return iter()
  }

  readonly keys = this.values.bind(this)

  entries(): IterableIterator<[T, T]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<[T, T]> {
      for (const [k, v] of self.delegate) {
        if (!self.#isEntryExpired(k, v)) {
          yield [k, k]
        }
      }
    }
    return iter()
  }

  toA(): T[] {
    this.#vacuum()
    return [...this.delegate.keys()]
  }

  [Symbol.iterator](): IterableIterator<T> {
    return this.values()
  }

  on(_event: "expire", listener: (k: T) => void) {
    this.expireListeners.push(listener)
  }

  /**
   * @param ctime is the last-set time associated to the key.
   */
  #isEntryExpired(key: T, ctime?: number): boolean {
    ctime ??= this.delegate.get(key)
    if (ctime == null || !this.delegate.has(key)) return true

    const expired = ctime <= Date.now() - this.ttlMs
    if (expired) {
      // delete the key before we tell the listerners
      this.delegate.delete(key)
      for (const ea of this.expireListeners) {
        ea(key)
      }
    }
    return expired
  }

  /**
   * remove all expired entries
   */
  #vacuum() {
    for (const [k, v] of this.delegate) {
      this.#isEntryExpired(k, v)
    }
  }
}
