import { lazy } from "../../core/Lazy"
import { uniq } from "../../fe/Array"
import { eql } from "../../fe/Eql"
import { Maybe } from "../../fe/MaybeTypes"
import {
  PS_LOCAL_FILE_SCHEME,
  PS_NETWORK_FILESYSTEM_SCHEME
} from "../../fe/URI"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { uri2nativePath } from "./FileURI"
import { URI, toURI } from "./URI"

const uriPrefixes = [
  "http:",
  "https:",
  "file:",
  PS_LOCAL_FILE_SCHEME,
  PS_NETWORK_FILESYSTEM_SCHEME
].map(ea => ea + "//")

export function isUri(s: string): boolean {
  const l = toS(s).toLowerCase()
  return uriPrefixes.some(ea => l.startsWith(ea))
}

const logger = lazy(() => mkLogger("uri.UriNormalization"))

export function normalizeURI(uri: string): string {
  try {
    return URI.parse(uri).toString()
  } catch (err) {
    logger().warn("Failed to normalize invalid URI", { uri, err })
    return uri
  }
}

/**
 * NOT FOR GENERAL USE (I don't think?)
 * @return true iff a and b are defined and are either equal or have the same
 * normalized content
 */
function strEql(a: Maybe<string>, b: Maybe<string>) {
  return a == null || b == null
    ? false
    : // normalize() is expensive, so we only do it if the paths are different
      a === b || a.normalize() === b.normalize()
}

//
// URI UNICODE NORMALIZATION
//

// We don't store normalized unicode URIs, as this allows us to store the actual
// filename characters.

// We *do* compare URIs by normalizing them, so macOS (using NFD) and other
// filesystems (using NFC) can be considered equivalent.

export function uriEqlSync(a: Maybe<string | URI>, b: Maybe<string | URI>) {
  try {
    if (a == null || b == null) return false
    const au = toURI(a)
    const bu = toURI(b)
    return (
      au.scheme === bu.scheme &&
      au.authority === bu.authority &&
      strEql(au.path, bu.path)
    )
  } catch {
    return false
  }
}

export async function uriIsEquivalent(
  a: Maybe<string | URI>,
  b: Maybe<string | URI>
): Promise<boolean> {
  try {
    if (a == null || b == null) return false
    return (
      eql(a, b) ||
      uriEqlSync(a, b) ||
      strEql(await uri2nativePath(a), await uri2nativePath(b))
    )
  } catch {
    return false
  }
}

export function uriEncodingVariants(uri: string | URI): string[] {
  const u = toURI(uri)
  return uniq([
    u.toString(),
    u.with({ path: u.path.normalize("NFC") }).toString(),
    u.with({ path: u.path.normalize("NFD") }).toString()
  ])
}
