import events from "events"
import { compact, count, filterInPlace, isEmpty, remove } from "../../fe/Array"
import { Later } from "../../fe/Later"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { Comparable } from "../../fe/Primitive"
import { Thunk, tot } from "../../fe/Thunk"
import { toS } from "../../fe/toS"
import { BoundedList } from "../BoundedList"
import { TypedEventEmitter } from "../event/TypedEventEmitter"
import { Average } from "../math/Average"
import { maxCpus } from "../work/MaxCpus"
import { Deferred } from "./Deferred"

export type NamedObject = Record<string, any> & { name: string }

export function toName(o: string | NamedObject) {
  return (o as any)?.name ?? toS(o)
}

export interface PromisesEvents {
  vacancy: () => void
  drain: () => void
}

/**
 * Aggregate promises efficiently
 *
 * Note that `N` is either a descriptive name for the promise, or can be a data
 * payload associated to the promise.
 */
export class Promises {
  readonly ee = new events.EventEmitter() as TypedEventEmitter<PromisesEvents>

  #maxConcurrency: Maybe<Thunk<Maybe<number>>>
  lastWorkTs = 0
  settleMsAvg = new Average()

  #settledCount = 0
  readonly #running: Deferred<any>[] = []
  readonly #pending: {
    d: Deferred<any>
    l: Later<any>
  }[] = []

  readonly lastPushedNames = new BoundedList<string>(16)

  constructor(
    readonly name: string,
    maxConcurrency?: Thunk<Maybe<number>>
  ) {
    this.#maxConcurrency = maxConcurrency
  }

  get maxConcurrency(): number {
    return Math.floor(
      clamp(1, maxCpus(), this.#maxConcurrency?.() ?? maxCpus())
    )
  }

  end() {
    this.#pending.length = 0
    return this.awaitAll()
  }

  vacuum() {
    filterInPlace(this.#running, ea => ea.isPending)
  }

  private get running() {
    this.vacuum()
    return this.#running
  }

  /**
   * @return the total number of promises that have settled
   */
  get settledCount() {
    return this.#settledCount
  }

  stats() {
    return {
      maxConcurrency: this.maxConcurrency,
      lastPushMsAgo: Date.now() - this.lastWorkTs,
      freeSlots: this.freeSlots(),
      unsettledCount: this.unsettledCount(),
      runningNames: this.#running.map(ea => ea.name),
      pendingNames: this.#pending.map(ea => ea.d.name),
      totalWorkCount: this.#settledCount
    }
  }

  #enqueue<T>({
    name,
    l,
    payload,
    serialId
  }: {
    name: string
    l: Later<T>
    payload?: any
    serialId?: Comparable
  }) {
    const d = new Deferred<T>(name, { payload, serialId })
    this.#pending.push({ d, l })
    return d
  }

  /**
   * Start the given promise once concurrency is below
   */
  enqueue<T>({
    name,
    l,
    payload,
    serialId
  }: {
    name: string
    l: Later<T>
    payload?: any
    serialId?: Comparable
  }): Deferred<T> {
    const d = this.#enqueue({ name, l, payload, serialId })
    this.laterPop()
    return d
  }

  enqueueAll<T>(name: string, laters: Later<T>[]): Promise<T[]> {
    const p = laters.map(l => this.#enqueue({ name, l }).promise)
    this.laterPop()
    return Promise.all(p)
  }

  /**
   * Run f() after all prior-enqueued promises with the same name have resolved.
   */
  serial<T>(name: string, f: () => Promise<T>): Promise<T> {
    return this.enqueue({ name, l: f, serialId: name })
  }

  /**
   * Immediately run the given promise
   */
  push<T>(
    name: string,
    promiseOrLater: Promise<T> | Later<T>,
    payload?: any
  ): Promise<T> {
    return this.#pushDeferred(
      new Deferred<T>(name, { payload }),
      promiseOrLater
    ).promise
  }

  #pushDeferred<T>(d: Deferred<T>, promiseOrLater: Promise<T> | Later<T>) {
    const start = Date.now()
    this.lastWorkTs = start

    void d.observe(tot(promiseOrLater)).finally(() => {
      this.#settledCount++
      remove(this.#running, d)
      this.settleMsAvg.push(Date.now() - start)
      this.laterPop()
    })
    this.lastPushedNames.push(d.name)
    this.#running.push(d)
    return d
  }

  /**
   * If there are any free slots and pending work, this may enqueue pending
   * work.
   */
  laterPop() {
    setImmediate(() => this.maybePopPendingWork())
  }

  // this doesn't need a mutex (like oneAtATime) because it's not async:
  maybePopPendingWork() {
    this.vacuum()
    const maxJobsToStart = this.maxConcurrency - this.#running.length
    if (maxJobsToStart === 0) {
      return
    }
    // vacancy AND drain TESTS MUST BE DONE IN THIS ORDER:
    if (isEmpty(this.#pending) && maxJobsToStart > 0) {
      // listeners may add pending work, so this should be first:
      this.ee.emit("vacancy")
    }
    // If we're still empty after emitting vacancy, we're drained:
    if (this.isIdle()) {
      this.ee.emit("drain")
    }
    const jobsToStart = Math.min(this.#pending.length, maxJobsToStart)
    const runningSerialIds = compact(this.#running.map(ea => ea.serialId))
    const blockedBySerialId = []
    for (let i = 0; i < jobsToStart; i++) {
      const pending = this.#pending.shift()!
      if (pending == null) break // < shouldn't happen, but it's a backstop for Later Me to hit.
      if (
        pending.d.serialId != null &&
        runningSerialIds.includes(pending.d.serialId)
      ) {
        // Nope, shove it to the back of the queue.
        blockedBySerialId.push(pending)
        // But keep going: other pending tasks may be eligible to run now!
      } else {
        if (pending.d.serialId != null) {
          // Don't push another of these!
          runningSerialIds.push(pending.d.serialId)
        }
        void this.#pushDeferred(pending.d, pending.l)
      }
    }
    this.#pending.push(...blockedBySerialId)
  }

  isTaskRunning(taskName: string) {
    return this.#running.some(ea => ea.name === taskName)
  }

  /**
   * If you don't care about the returned promise: only run f() if all prior
   * with the same name have finished, and return no-op otherwise
   */
  maybeRun<T>(name: string, f: () => Promise<T>): Maybe<Promise<T>> {
    return this.freeSlots() > 0 && !this.isTaskRunning(name)
      ? this.push(name, f)
      : undefined
  }

  runningCount() {
    return count(this.#running, ea => ea.isPending)
  }

  /**
   * @return the count of both currently-running *and* enqueued jobs.
   */
  unsettledCount() {
    // We don't want to muck with _arr state, so use this._arr directly:
    return this.runningCount() + this.#pending.length
  }

  /**
   * @return true if there are jobs that are running or enqueued.
   */
  hasPending() {
    return this.#pending.length > 0 || this.running.length > 0
  }

  isIdle() {
    return !this.hasPending()
  }

  freeSlots() {
    return clamp(
      0,
      this.maxConcurrency,
      this.maxConcurrency - this.unsettledCount()
    )
  }

  isFull() {
    return this.freeSlots() === 0
  }

  pendingNames(): string[] {
    return [
      ...this.#pending.map(ea => ea.d.name),
      ...this.running.map(ea => ea.name)
    ]
  }

  get deferreds() {
    return [...this.running, ...this.#pending.map(ea => ea.d)]
  }

  pendingWithName(name: string) {
    return this.deferreds.filter(ea => ea.name === name)
  }

  payloadsWithName(name: string) {
    return this.pendingWithName(name)
      .filter(ea => ea.payload != null)
      .map(ea => ea.payload!)
  }

  get pendingPromises() {
    return this.deferreds.map(ea => ea.promise)
  }

  /**
   * @return a promise that will be resolved when all previously pushed and
   * enqueued Promises are resolved. Rejections are swallowed.
   * @see Promise.allSettled
   */
  async awaitSettled() {
    await Promise.allSettled(this.pendingPromises)
  }

  /**
   * @return a promise that will be resolved when all previously pushed and
   * enqueued Promises are resolved, or any promises are rejected.
   * @see Promise.all
   */
  async awaitAll() {
    await Promise.all(this.pendingPromises)
  }

  async awaitAllByName(name: string) {
    await Promise.all(
      this.deferreds.filter(ea => ea.name === name).map(ea => ea.promise)
    )
  }
}

/**
 * Run the given thunk never more than once concurrently. Invocations while
 * prior runs were invoked will return after prior runs have completed but will
 * be no-ops.
 */
export function maybeRun<T>(
  name: string,
  l: Later<T>
): () => Maybe<Promise<T>> {
  const p = new Promises(name)
  return () => p.maybeRun(name, l)
}

export async function withBoundedConcurrency<T>({
  name,
  laters,
  maxConcurrent
}: {
  name: string
  laters: Later<T>[]
  maxConcurrent?: number
}): Promise<T[]> {
  // SITS: NOTE: if we share the same Promises instance, and a code block within
  // withBoundedConcurrency calls withBoundedConcurrency, we'll starve.
  return new Promises(
    name,
    map(maxConcurrent, ea => () => ea)
  ).enqueueAll(name, laters)
}

export const PermissivePromises: Pick<Promises, "enqueueAll"> = {
  enqueueAll<T>(_name: string, laters: Later<T>[]): Promise<T[]> {
    return Promise.all(laters.map(f => f()))
  }
}
