import timers from "timers"
import util from "util"
import { isEmpty } from "../../fe/Array"
import { asError } from "../../fe/Error"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { omit } from "../../fe/Object"
import { Comparable } from "../../fe/Primitive"
import { isPromise } from "../../fe/Promise"
import { PromiseState, PromiseStates } from "../../fe/PromiseState"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { TimeoutError } from "../../fe/TimeoutError"
import { Logger, mkLogger } from "../Logger"
import { isIgnorableError } from "../error/ErrorTypes"
import { onTimeout } from "./TimeoutHandler"

/**
 * Deferred resolution of a promise with synchronous state
 */
export class Deferred<T, P = any> implements Promise<T> {
  static async *toAsyncIterable<I, O>(arr: Deferred<I, O>[]) {
    while (!isEmpty(arr)) {
      // Yield the first completed value:
      const idx = arr.findIndex(ea => !ea.isPending)
      if (idx >= 0) {
        const result = arr[idx]
        arr.splice(idx, 1)
        yield result
      } else {
        try {
          // Wait for the next one to complete, and run the loop again:
          await Promise.race(arr)
        } catch {
          // if the next promise raises, we still want to return that Deferred
          // (the caller can check on the rejected status)
        }
      }
    }
  }

  static #id = 0

  static resolve<I, O = any>(
    t: I,
    opts?: { payload?: O; serialId?: Comparable }
  ): Deferred<I, O> {
    const d = new Deferred<I>(".resolved()", opts)
    return d.resolve(t)
  }

  readonly id: number
  readonly payload?: P
  /**
   * If this is defined, Promises should prevent concurrent execution of other
   * Deferred instances with the same serialId.
   */
  readonly serialId?: Comparable
  readonly startedAt = Date.now()
  #settledAt?: number
  #state: PromiseState = PromiseStates.pending
  #value?: T
  readonly promise: Promise<T>
  #resolve!: (value: T) => void
  #reject!: (reason?: any) => void
  #error: Maybe<Error>
  #priorTimeout: Maybe<NodeJS.Timeout>
  #logger: Maybe<Logger>

  /**
   * @param name can be either a string or a data payload to associate with the
   * promise.
   */
  constructor(
    readonly name: string,
    opts?: { payload?: P; serialId?: Comparable }
  ) {
    this.id = Deferred.#id++
    this.promise = new Promise<T>((resolve, reject) => {
      this.#resolve = resolve
      this.#reject = reject
    })
    this.payload = opts?.payload
    this.serialId = opts?.serialId
  }

  private get logger() {
    return this.#logger ?? mkLogger("async.Deferred(" + this.name + ")")
  }

  // Only true for the same instance
  eql(d: Deferred<T>): boolean {
    return d instanceof Deferred && d.id === this.id
  }

  // This is used by LogMeta for logging:
  toLogJSON() {
    return {
      _ctor: "Deferred",
      name: this.name,
      state: this.#state,
      value: this.value,
      serialId: this.serialId,
      payload: this.payload,
      elapsedMs: this.elapsedMs
    }
  }

  toJSON() {
    return omit(this.toLogJSON(), "value", "elapsedMs")
  }

  get settledAt() {
    return this.#settledAt
  }

  get elapsedMs() {
    return (this.#settledAt ?? Date.now()) - this.startedAt
  }

  get [Symbol.toStringTag]() {
    return "async.Deferred(" + this.name + ")"
  }

  [util.inspect.custom]() {
    return this.toJSON()
  }

  observeQuietly(p: Promise<T>): Deferred<Maybe<T>> {
    if (isPromise(p)) {
      p.then(resolution => {
        void this.maybeResolve(resolution)
      }).catch(err => {
        this.logger.warn("observeQuietly.reject()", err)
        void this.maybeResolve(undefined as any)
      })
    } else {
      void this.maybeResolve(p)
    }
    return this as any
  }

  observe(p: SyncOrAsync<T>): this {
    if (isPromise(p)) {
      p.then(resolution => {
        void this.maybeResolve(resolution)
      }).catch(err => {
        void this.maybeReject(err)
      })
    } else {
      void this.maybeResolve(p)
    }
    return this
  }

  /**
   * Reject the promise if it hasn't been resolved or rejected within
   * `timeoutMs`
   */
  setTimeout(timeoutMs: number, unref: boolean = true): this {
    if (this.#priorTimeout != null) {
      timers.clearTimeout(this.#priorTimeout)
      this.#priorTimeout = undefined
    }

    if (gt0(timeoutMs)) {
      // unref so it doesn't prevent node from exiting:
      this.#priorTimeout = timers.setTimeout(() => {
        if (this.isPending) {
          void this.reject(
            new TimeoutError(
              "Timeout " +
                this.name +
                " after " +
                (Date.now() - this.startedAt) +
                "ms"
            )
          )
          onTimeout()
        }
      }, timeoutMs)
      if (unref) {
        this.#priorTimeout.unref()
      }
    }
    return this
  }

  get stateStr(): PromiseState {
    return this.isPending
      ? "pending"
      : this.isResolved
      ? "resolved"
      : "rejected"
  }

  get isPending() {
    return this.#state === PromiseStates.pending
  }

  /**
   * @return the resolved value for this Deferred, or `undefined` if this
   * is either still pending or rejected.
   */
  get value(): Maybe<T> {
    return this.isResolved ? this.#value : undefined
  }

  get error(): Maybe<Error> {
    return this.#error
  }

  /**
   * true iff either resolved or rejected.
   */
  get isSettled() {
    return this.#state !== PromiseStates.pending
  }

  get isResolved() {
    return this.#state === PromiseStates.resolved
  }

  get isRejected() {
    return this.#state === PromiseStates.rejected
  }

  get settledMs(): number | undefined {
    return this.#settledAt == null
      ? undefined
      : this.#settledAt - this.startedAt
  }

  /**
   * Resolves the internal promise. Cannot be invoked more than once.
   */
  resolve(value: T): this {
    return this.#settle(() => {
      this.#state = PromiseStates.resolved
      this.#value = value
      this.#resolve(value)
    })
  }

  maybeResolve(value: T): this {
    return this.isPending ? this.resolve(value) : this
  }

  reject(reason?: any): this {
    this.logger.log(
      isIgnorableError(reason) === true ? "info" : "warn",
      ".reject()",
      reason
    )
    const err = asError(reason)
    return this.#settle(() => {
      this.#error = err
      this.#state = PromiseStates.rejected
      this.#reject(err)
    })
  }

  maybeReject(reason?: any): this {
    return this.isPending ? this.reject(reason) : this
  }

  finally(f: () => any): this {
    // don't create a new promise chain that may have an unhandled error!
    void this.promise.finally(f).catch(() => undefined)
    return this
  }

  then<TResult1 = T, TResult2 = never>(
    onfulfilled?:
      | ((value: T) => TResult1 | PromiseLike<TResult1>)
      | undefined
      | null,
    onrejected?:
      | ((reason: any) => TResult2 | PromiseLike<TResult2>)
      | undefined
      | null
  ): Promise<TResult1 | TResult2> {
    return this.promise.then(onfulfilled, onrejected)
  }

  catch<U>(onrejected: (reason: any) => U | PromiseLike<U>): Promise<T | U> {
    return this.promise.catch(err => onrejected(err))
  }

  #settle(firstback: () => void): this {
    if (this.#state === PromiseStates.pending) {
      map(this.#priorTimeout, timers.clearTimeout)
      firstback()
      this.#settledAt = Date.now()
      const ms = this.settledMs!
      if (this.isResolved && ms > 5000) {
        this.logger.info("Completed in " + ms + "ms")
      }
    } else {
      this.logger.warn(
        "settled multiple times (already " + this.stateStr + ")",
        { value: this.#value }
      )
    }
    return this
  }
}
