import { blank, toNotBlank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { isString, stripPrefix } from "../../fe/String"
import { toS } from "../../fe/toS"
import { extractInt } from "../Number"
import { ParsedFile, parsePosixPath } from "./Path"
import { SimpleFile } from "./SimpleFile"

const CopiesRe = [
  // macOS, when you copy-paste-paste, you get
  // img.jpg
  // img copy.jpg
  // img copy 2.jpg
  // img copy 3.jpg

  // Windows, when you copy-paste-paste, you get
  // img.jpg
  // img - Copy.jpg
  // img - Copy 2.jpg
  // img - Copy 3.jpg
  /\s*-?\s*copy(?:\s*\d{1,3}\s*)?$/i,

  // img - Copy (2).jpg
  // img - Copy (3).jpg
  // img - Copy (101).jpg
  /\s*-?\s*copy(?:\s*\(\s*\d{1,3}\s*\))$/i,

  // Ubuntu when you copy-paste-paste-paste, you get
  // img.jpg
  // img (copy).jpg
  // img (another copy).jpg // <-- srsly wth
  // img (3rd copy).jpg
  // img (4th copy).jpg
  /\s*\((?:another|\d+[a-z]{2})?\s*copy\)$/i,

  // foobar(1).jpg
  // foobar (1).jpg
  // foobar ( 32 ).jpg
  // foobar-(1).jpg
  // foobar-( 32 ).jpg
  /\s*-?\(\s*\d{1,4}\s*\)$/,

  // frakkin google sidecars chop the right bit off the UUID.
  // https://forum.photostructure.com/t/574/7

  // The "38" means we leave a - on the end of UUIDs. That dash should make all
  // other patterns not match, so this regex will be the only one applied.
  /(?<=[0-9a-f-]{37}-)[0-9a-f-]+$/i,

  // Variations added by EnsureNew.
  // P432781234-1.jpg
  // P432781234-01.jpg
  // P432781234-001.jpg

  /\s*-\s*\d{1,3}$/
]

const Copies2Re = [
  ...CopiesRe,
  // // img edit.jpg
  // // img-edit.jpg
  // // img edited.jpg
  // // img-edited.jpg
  /\s*-?edit(?:ed)?$/i
]

export const ymdIsoRE = /^(?:\d\d){1,2}-\d\d-\d\d$/

export function ciStablePosixPath(posixPath: string): string {
  const path = posixPath.split("/")
  const basename = path.pop()
  if (blank(basename)) return posixPath
  const parsed = parsePosixPath(basename)
  const strippedName = stripCopySuffixFromName(parsed, { aggressive: false })
  return [...path, strippedName + parsed.ext].join("/").normalize()
}

/**
 * @return the file's name, stripped of any suffixes due to copies. DOES NOT LOWER CASE OR NORMALIZE.
 */
export function stripCopySuffixFromName(
  f: SimpleFile | ParsedFile | string,
  opts: { aggressive: boolean } = { aggressive: true }
): string {
  return _stripCopySuffixFromName(toNotBlank((f as any).name) ?? toS(f), opts)
}

function _stripCopySuffixFromName(
  initialName: string,
  opts: { aggressive: boolean }
): string {
  // Don't touch yyyy-mm-dd:
  if (ymdIsoRE.test(initialName)) return initialName
  let name = initialName
  for (const re of opts.aggressive ? Copies2Re : CopiesRe) {
    const m = name.match(re)
    if (gt0(m?.index)) {
      const s = name.slice(0, m!.index).trim()
      if (s.length > 0) name = s
    }
  }
  // If you make a copy of a copy, you'll get "image - Copy 2 - copy". We
  // don't want to hit the exported function, because then the file ext may be
  // stripped multiple times.
  return initialName === name
    ? initialName
    : _stripCopySuffixFromName(name, opts)
}

export function copySuffixCountFromName(
  f: string | SimpleFile | ParsedFile
): Maybe<number> {
  const name = (isString(f) ? f : f.name).toLowerCase().normalize()
  const strip = stripCopySuffixFromName(name)
  const stripped = stripPrefix(name.toLowerCase().normalize(), strip)
  return map(extractInt(stripped), Math.abs)
}

const burstRe = /(?:burst)\s*([\w-]{8,})\s*$/i

const coverRe = /(?<=.{6})_cover$/i

// https://en.wikipedia.org/wiki/Design_rule_for_Camera_File_system
// IMG_E0001.JPG is an edited version of IMG_0001.JPG
const dscPrefixRe =
  /^(?:(?:dsc|dscf|img|gpfr|gopro?|gf|gh|gp|mov|mvi|mvimg|p|vid)(?:[-_ ]{0,2}e?))(\d.+)$/i
export function stripDSC(f: SimpleFile | string): string {
  let name = (isString(f) ? f : f.name).trim().replace(coverRe, "").trim()
  map(burstRe.exec(name), m => (name = m[1].trim()))
  map(dscPrefixRe.exec(name), m => (name = m[1].trim()))
  return name
}
