import fs from "fs"
import * as fse from "fs-extra"
import { join, parse, sep } from "path"
import { inspect } from "util"
import { lazy } from "../../core/Lazy"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { MaybeSyncOrAsync, SyncOrAsync } from "../../fe/SyncOrAsync"
import { toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { stripSuffix } from "../String"
import { thenMap } from "../async/Promise"
import { ee } from "../event/EventEmitter"
import { ParsedFile, parentBasename, parseNativePath } from "./Path"
import { readdir } from "./Readdir"
import { SimpleDirent, isSimpleDirent } from "./SimpleDirent"
import { SimpleFile, isSimpleFile } from "./SimpleFile"
import { stat_ } from "./Stat"

// DON'T REFERENCE PosixFile HERE, you'll have circular deps.

export class StatDirent implements Partial<Pick<fs.Stats, "size" | "mtimeMs">> {
  readonly isFile: boolean
  readonly isDirectory: boolean
  size: Maybe<number>
  mtimeMs: Maybe<number>
  // Allow the Stats instance to be GC'ed by copying what I need out of it:
  constructor(
    readonly base: string,
    s: fs.Stats | fs.Dirent | SimpleDirent
  ) {
    if (isSimpleDirent(s)) {
      // TODO: assert that base === s.basename
      this.isFile = s.isFile
      this.isDirectory = s.isDirectory
    } else {
      this.isFile = s.isFile()
      this.isDirectory = s.isDirectory()
      // isSymbolicLink is always false unless you use lstat, which we never use.
    }
    if (s instanceof fs.Stats) {
      this.size = s.size
      this.mtimeMs = s.mtimeMs
    }
  }
}

const logger = lazy(() => mkLogger("fs.DirectoryEntry"))

export class DirectoryEntry implements ParsedFile, SimpleFile {
  /**
   * Full path
   */
  readonly nativePath: string

  /**
   * @return extension (`.jpg` for `image.jpg`)
   */
  readonly ext: string

  /**
   * Should only be constructed by BaseFile
   *
   * @param dir The full parent directory path such as `/home/user/dir` or
   * `c:\\path\\dir` for `/home/user/dir/file.txt` or `c:\\path\\dir\\file.txt`
   */
  constructor(
    readonly dir: string,
    public dirent: StatDirent
  ) {
    this.nativePath = join(this.dir, dirent.base)
    this.ext = parseNativePath(dirent.base).ext
  }

  static fromSimpleDirent(dir: string, dirent: SimpleDirent) {
    return new DirectoryEntry(dir, new StatDirent(dirent.basename, dirent))
  }

  static async for(
    nativePathOrFile: string | SimpleFile
  ): PromiseMaybe<DirectoryEntry> {
    try {
      return await this.for_(nativePathOrFile)
    } catch {
      return
    }
  }

  static async for_(
    nativePathOrFile: string | SimpleFile
  ): Promise<DirectoryEntry> {
    const { nativePath, dir, base } = isSimpleFile(nativePathOrFile)
      ? nativePathOrFile
      : { nativePath: nativePathOrFile, ...parseNativePath(nativePathOrFile) }

    const s = await fse.stat(nativePath)
    return new DirectoryEntry(dir, new StatDirent(base, s))
  }

  clear() {
    ee().emit("fileChanged", this.nativePath)
    this.dirent.size = undefined
    this.dirent.mtimeMs = undefined
    return this
  }

  async join(...path: string[]) {
    return DirectoryEntry.for(join(this.nativePath, ...path))
  }

  /**
   * @return the full basename `image.jpg`
   */
  get base() {
    return this.dirent.base
  }

  /**
   * @return basename without the ext (`image` for `image.jpg`)
   */
  get name() {
    return stripSuffix(this.base, this.ext)
  }

  get pathnames(): string[] {
    return this.nativePath.split(sep)
  }

  get parentAndBase(): string {
    return this.pathnames.slice(-2).join("/")
  }

  get parentBasename(): string {
    return parentBasename(this.nativePath)
  }

  toJSON() {
    return {
      _ctor: this.constructor.name,
      nativePath: this.nativePath
    }
  }

  toLogJSON() {
    return this.nativePath
  }

  [inspect.custom]() {
    return this.toJSON()
  }

  toString() {
    return this.nativePath
  }

  isFile() {
    return this.dirent.isFile
  }

  isFileSync() {
    return this.dirent.isFile
  }

  isDirectory() {
    return this.dirent.isDirectory
  }

  isDirectorySync() {
    return this.dirent.isDirectory
  }

  get isRoot() {
    return this.dir === parse(this.dir).dir
  }

  isNameHidden() {
    return this.name.startsWith(".")
  }

  parent(): SyncOrAsync<Maybe<this>> {
    const p = parseNativePath(this.dir)
    return p.dir === this.dir
      ? this // root
      : (new DirectoryEntry(p.dir, {
          base: p.base,
          isFile: false,
          isDirectory: true,
          mtimeMs: undefined as any,
          size: undefined as any
        }) as this)
  }

  async childNames() {
    try {
      return !this.isDirectory()
        ? undefined
        : (await readdir(this.nativePath))?.map(ea => ea.basename)
    } catch (err) {
      logger().warn(
        "childNames() failed to readdir(" + this.nativePath + ")",
        err
      )
      return
    }
  }

  async children() {
    try {
      if (!this.isDirectory()) return undefined
      const arr = await readdir(this.nativePath) // readdir is already sorted
      return arr?.map(
        ea => DirectoryEntry.fromSimpleDirent(this.nativePath, ea) as this
      )
    } catch (err) {
      logger().warn(
        "children() failed to readdir(" + this.nativePath + ")",
        err
      )
      return
    }
  }

  childrenSync() {
    try {
      if (!this.isDirectory()) return undefined
      const arr = fs.readdirSync(this.nativePath, { withFileTypes: true }) // readdir is already sorted
      return arr?.map(
        ea => new DirectoryEntry(this.nativePath, new StatDirent(ea.name, ea))
      )
    } catch (err) {
      logger().warn(
        "children() failed to readdir(" + this.nativePath + ")",
        err
      )
      return
    }
  }

  async childDirectories() {
    return toA(await this.children()).filter(ea => ea.isDirectory())
  }

  async childFiles() {
    return toA(await this.children()).filter(ea => ea.isFile())
  }

  /**
   * Depth-first
   */
  async visitDescendants(f: (child: this) => any): Promise<void> {
    const children = await this.children()
    if (children == null) return

    for (const dir of children.filter(ea => ea.isDirectory())) {
      await dir.visitDescendants(f)
    }

    for (const ea of children) {
      if (ea.isFile()) {
        await f(ea)
      }
    }

    for (const ea of children) {
      if (ea.isDirectory()) {
        await f(ea)
      }
    }

    return
  }

  visitDescendantsSync(f: (child: DirectoryEntry) => any): void {
    const children = this.childrenSync()
    if (children == null) return
    for (const ea of children) {
      if (ea.isFile()) f(ea)
    }
    for (const ea of children) {
      if (ea.isDirectory()) {
        ea.visitDescendantsSync(f)
        f(ea)
      }
    }
  }

  async visitDescendantFiles(f: (child: this) => any): Promise<void> {
    return this.visitDescendants(ea => (ea.isFile() ? f(ea) : undefined))
  }

  async filterDescendantFiles(
    predicate: (child: this) => SyncOrAsync<boolean>
  ) {
    const arr: this[] = []
    await this.visitDescendantFiles(async ea => {
      if (true === (await predicate(ea))) arr.push(ea)
    })
    return arr
  }

  async stat() {
    try {
      const s = await stat_(this.nativePath)
      this.dirent = new StatDirent(this.base, s)
      return s
    } catch {
      return
    }
  }

  size(): MaybeSyncOrAsync<number> {
    // Try to reduce the number of promises we're making:
    return this.dirent.size ?? thenMap(this.stat(), ea => ea.size)
  }

  mtimeMs(): MaybeSyncOrAsync<number> {
    // Try to reduce the number of promises we're making:
    return this.dirent.mtimeMs ?? thenMap(this.stat(), ea => ea.mtimeMs)
  }

  unlink_() {
    ee().emit("fileChanged", this.nativePath)
    return fse.unlink(this.nativePath)
  }

  rmdir_() {
    ee().emit("fileChanged", this.nativePath)
    return fse.rmdir(this.nativePath)
  }
}
