import { lazy } from "../../core/Lazy"
import { notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { assignMissingPrimitives } from "../Object"
import { isReadableDirectorySync } from "../fs/StatSync"
import { isDocker } from "../platform/isDocker"
import { Settings } from "../settings/Settings"
import { mightBeAssetMountpoint } from "./AssetMountpoint"
import { dfPosixRaw } from "./DfPosixRaw"
import { gioVolumes, isGioSupported } from "./Gio"
import { lazyFsAsync } from "./LazyFsAsync"
import { mountpoints } from "./Mountpoints"
import { isExcludedFilesystem, isExcludedMountpoint } from "./PosixVolumes"
import { DfVolume } from "./Volume"

const logger = lazy(() => mkLogger("volumes.DfPosix"))

const localMountpoints = lazyFsAsync(
  "localMountpoints",
  // We'll assume gio mountpoints are always remote, so we can ignore gio stuff
  // here:
  async () =>
    (await dfPosixRaw({ localsOnly: true, paths: await mountpoints() }))?.map(
      vol => vol.mountpoint
    )
)

function isRemoteFilesystem(fs: Maybe<string>) {
  return notBlank(fs) && Settings.remoteFilesystemTypes.values.includes(fs)
}

export function acceptPosixVolume(vol: DfVolume): boolean {
  const notAssetMountpoint = !mightBeAssetMountpoint(vol.mountpoint)
  const excludedFS =
    vol.filesystem == null ? false : isExcludedFilesystem(vol.filesystem)
  const excludedMP = isExcludedMountpoint(vol.mountpoint)
  const excludedDir = Settings.validateMountpoints.valueOrDefault
    ? !isReadableDirectorySync(vol.mountpoint)
    : false

  const excluded =
    notAssetMountpoint && (excludedFS || excludedMP || excludedDir)

  return logger().tap({
    msg: "acceptPosixVolume()",
    result: !excluded,
    meta: {
      volume: vol,
      notAssetMountpoint,
      excludedFS,
      excludedMP,
      excludedDir
    }
  })
}

// This doesn't need to be cached: it's cached already by _volumesImpl
export async function dfPosix() {
  // df on docker doesn't return all mountpoints (!!) unless we tell `df` which paths we want:
  const paths = isDocker() ? await mountpoints() : undefined
  const vols = await dfPosixRaw({ paths })

  if (vols == null) return
  const locals = toA(await localMountpoints())
  vols.forEach(vol => {
    vol.remote =
      !locals.includes(vol.mountpoint) || isRemoteFilesystem(vol.filesystem)
  })
  if (await isGioSupported()) {
    for (const vol of toA(await gioVolumes())) {
      // only push unique mountpoints
      const prior = vols.find(ea => ea.mountpoint === vol.mountpoint)
      if (prior == null) {
        vols.push(vol)
      } else {
        logger().info("Merging GIO and df volume", {
          dfVol: prior,
          gioVol: vol
        })
        // We don't trust gio as much as we trust df, so only assign missing fields (like remote host):
        assignMissingPrimitives(prior, vol)
      }
    }
  }
  return vols.filter(acceptPosixVolume)
}
