import process from "process"
import { lazy } from "../../core/Lazy"
import { firstNotBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { later } from "../../fe/Delay"
import { Maybe } from "../../fe/MaybeTypes"
import { CaseInsensitiveValued } from "../CaseInsensitiveValued"
import { DefaultSensitiveEnvRegexPattern } from "../DefaultSensitiveEnvRegexPattern"
import { camel2snake } from "../StringCase"
import { env } from "./Env"
import { EnvKeys, EnvName } from "./EnvName"

// !!!!!!!!!!!!!!!!!!!
// IMPORTANT: To avoid deploops: `Settings` can be referenced ONLY by TypeScript
// types, BUT NOT ACTUAL FUNCTIONALITY.
// !!!!!!!!!!!!!!!!!!!

export const SensitiveEnvRegexPattern = lazy(
  () =>
    firstNotBlank(
      process.env.PS_SENSITIVE_ENV_REGEX_PATTERN,
      process.env.sensitiveEnvRegexPattern,
      DefaultSensitiveEnvRegexPattern
    )!
)

export const SensitiveEnvRE = lazy(() => {
  try {
    return new RegExp(SensitiveEnvRegexPattern(), "i")
  } catch (err) {
    console.error(
      `Invalid setting for "sensitiveEnvRegExp": ${err}. Using default value.`
    )
    return new RegExp(DefaultSensitiveEnvRegexPattern, "i")
  }
})

export const caseInsensitiveEnv = lazy(() => {
  return new CaseInsensitiveValued(env())
})

export function onEnvChange() {
  caseInsensitiveEnv.unset()
  SensitiveEnvRegexPattern.unset()
  SensitiveEnvRE.unset()
}

later(() => {
  env.watchLater(onEnvChange)
})

/**
 * Case-insensitive search for a given ENV key
 *
 * @param key either XDG_CACHE_HOME for non-settings, or camelCaseSettingsName
 */
export function getEnv(key: EnvName): Maybe<string> {
  return (
    caseInsensitiveEnv().get(key) ??
    (EnvKeys.includes(key)
      ? undefined // < no need to search for PS_windir
      : caseInsensitiveEnv().get("PS_" + camel2snake(key)))
  )
}

export function isEnvTrue(key: EnvName): boolean {
  return isTrue(getEnv(key))
}
