import path from "path"
import process from "process"
import { lazy } from "../../core/Lazy"
import { mapNotBlankOr } from "../../fe/Blank"
import { isMac, isWin } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"

const DefaultMacOSPaths = Object.freeze([
  "/opt/homebrew/bin",
  "/opt/homebrew/sbin",
  "/usr/local/bin",
  "/usr/local/sbin",
  "/usr/bin",
  "/bin",
  "/usr/sbin",
  "/sbin"
])

// exported for tests:
export const DefaultPosixPaths = Object.freeze([
  // From Ubuntu's /etc/environment:
  "/usr/local/bin",
  "/usr/local/sbin",
  "/usr/lib/libraw", // where dcraw_emu lives
  "/opt/local/bin", // macports
  "/opt/local/sbin", // macports
  "/usr/sbin",
  "/usr/bin",
  "/sbin",
  "/bin"
])

export const DefaultDockerPaths = Object.freeze([
  "/opt/photostructure/tools/bin",
  ...DefaultPosixPaths
])

export const DefaultWinPaths = lazy(() => [
  ...mapNotBlankOr(
    process.env.SYSTEMROOT,
    ea => [ea, path.join(ea, "System32"), path.join(ea, "System32", "webm")],
    () => []
  ),
  "C:\\cygwin64\\bin"
])

// exported for tests:
export const DefaultPaths = Object.freeze(
  isDocker()
    ? DefaultDockerPaths
    : isWin
    ? DefaultWinPaths()
    : isMac
    ? DefaultMacOSPaths
    : DefaultPosixPaths
)
