import { secondMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { gte } from "../../fe/Number"
import { strEnum, StrEnumKeys } from "../../fe/StrEnum"

/**
 * These are driven by the `console` API
 */
export const LogLevels = strEnum(
  "fatal",
  "error",
  "warn",
  "info",
  "debug",
  "trace"
)
export type LogLevel = StrEnumKeys<typeof LogLevels>

const TraceLevelIndex = LogLevels.indexOf("trace")!

export function levelIndex(logLevel: string): number {
  return (
    LogLevels.indexOf(logLevel) ??
    LogLevels.indexOf(logLevel.trim().toLowerCase()) ??
    TraceLevelIndex
  )
}

export function levelGte(lhs: LogLevel | string, rhs: LogLevel | string) {
  return gte(LogLevels.indexOf(lhs), LogLevels.indexOf(rhs))
}

// https://en.wikipedia.org/wiki/Syslog#Severity_level
const LogLevel2SyslogLevel = new Map<LogLevel, number>([
  [LogLevels.fatal, 1],
  [LogLevels.error, 3],
  [LogLevels.warn, 4],
  [LogLevels.info, 6],
  [LogLevels.debug, 7]
])

export function level2syslog(logLevel: string): Maybe<number> {
  return LogLevel2SyslogLevel.get(logLevel as any)
}

export function msTolevel(
  elapsedMs: number,
  errorMs: number = 7 * secondMs
): LogLevel {
  return elapsedMs >= errorMs
    ? "error"
    : elapsedMs >= errorMs / 2
    ? "warn"
    : elapsedMs >= errorMs / 4
    ? "info"
    : "debug"
}
