// LIMIT DEPS TO FE AND BASE CLASSES! #NODEPLOOPS NO DE PLOOPS!

export const SettingCategories = strEnum(
  // System settings:
  "Paths",
  "Desktops",
  "Filesystem",
  "Cache",
  "Logging",
  "Networking",
  "Processes",
  "Security",
  "Tools",
  "Updates",
  // Library settings:
  "Auth",
  "Color",
  "Db",
  "Deduping",
  "Events",
  "Filters",
  "HealthChecks",
  "Parsing",
  "Previews",
  "Reporting",
  "Sidecars",
  "Subscriptions",
  "Sync",
  "Tagging",
  "Video",
  "Volumes",
  "Web"
)
export type SettingCategory = StrEnumKeys<typeof SettingCategories>

export const LibraryCategories: ReadonlyArray<SettingCategory> = Object.freeze([
  // we include reporting in the library because their email won't change
  SettingCategories.Auth,
  SettingCategories.Color,
  SettingCategories.Db,
  SettingCategories.Deduping,
  SettingCategories.Events,
  SettingCategories.Filters,
  SettingCategories.HealthChecks,
  SettingCategories.Parsing,
  SettingCategories.Previews,
  SettingCategories.Reporting,
  SettingCategories.Sidecars,
  SettingCategories.Subscriptions,
  SettingCategories.Sync,
  SettingCategories.Tagging,
  SettingCategories.Video,
  SettingCategories.Volumes,
  SettingCategories.Web
])

// System is everything not library:
export const SystemCategories: ReadonlyArray<SettingCategory> = Object.freeze(
  SettingCategories.values.filter(ea => !LibraryCategories.includes(ea))
)

export interface BaseSettingOpts<T> {
  aliases?: string[]
  envAliases?: string[]
  category: SettingCategory
  description: string
  exampleValue?: Thunk<Maybe<T>>
  transient?: boolean // defaults to false
  advanced?: Thunk<boolean>
  deprecated?: boolean // be sure to add a migrator to MetaSettings!
  sensitive?: boolean
}

export interface SettingConverters<T> {
  readonly toEnv: (value: Maybe<T>) => Maybe<string>
  readonly fromEnv: (value: Maybe<string>) => Maybe<T>
}

export interface DefaultValued<T> {
  defaultValue: T | Thunk<T>
}

export type SettingOpts<T> = BaseSettingOpts<T> & DefaultValued<T>

export interface SavedSetting {
  key: string
  value: any
}

export function settingsToObj(arr: Setting[]): Obj {
  const obj: Obj = {}
  for (const ea of arr) {
    if (ea.hasValue()) obj[ea.name] = ea.value
  }
  return obj
}

export interface SettingState<T> {
  user: Maybe<T>
  env: Maybe<T>
  file: Maybe<T>
  tmp: Maybe<T>
}

/**
 * Supports env-overridden preferences that can be persisted to a file.
 *
 * 1. Setting an environment variable or command-line argument must not be
 *    persisted to the settings file, as that would become the default value.
 * 2. Settings file values should not override the command-line or environment's
 *    values, as that would cause the env or CLI value to be ignored.
 *
 * SO: we have to be able to distinguish between env or cli values and persisted
 * values.
 */
export class Setting<T = any> {
  protected _name!: string
  protected _names!: string[]
  protected _key!: string
  protected _keys!: string[]
  protected _userValue: Maybe<T>
  // This lets us only parse values out of the env when we actually need the
  // value. We get circular dependency errors if we do this on construction
  // time:
  _envValue = lazy(() => this.readFromEnv())
  protected _fileValue: Maybe<T>
  protected _tmpValue: Maybe<T>
  protected readonly watchers: ChangeListener<T>[] = []
  protected optsOverrides: Partial<SettingOpts<T> & SettingConverters<T>> = {}

  constructor(readonly opts: SettingOpts<T> & SettingConverters<T>) {}

  getState(): SettingState<T> {
    return {
      user: this._userValue,
      env: this.envValue,
      file: this._fileValue,
      tmp: this._tmpValue
    }
  }

  dump() {
    return {
      name: this.name,
      key: this.key,
      ...this.getState(),
      hasValue: this.hasValue(),
      isUnset: this.isUnset(),
      defaultValue: this.defaultValue
    }
  }

  /**
   * Used for testing: supports changing and restoring state without firing
   * change observers.
   */
  setState(s: Partial<SettingState<T>>) {
    if (s != null) {
      this._userValue = s.user
      this.envValue = s.env
      this._fileValue = s.file
      this._tmpValue = s.tmp
    }
  }

  // TODO: Doesn't work. I don't know why it doesn't.
  // runAndRestore<L>(newValue: T, l: () => L): L {
  //   const priorState = this.getState()
  //   try {
  //     this.value = newValue
  //     return l()
  //   } finally {
  //     this.setState(priorState)
  //   }
  // }

  normalizeValue(v: Maybe<T>): Maybe<T> {
    return v == null ? undefined : this.opts.fromEnv(this.opts.toEnv(v))
  }

  /**
   * @return the value that should be persisted in a settings.toml. Ignores env
   * and tmp values.
   */
  get fileValue(): Maybe<T> {
    return this._userValue ?? this._fileValue
  }

  set fileValue(value: Maybe<T>) {
    this.onChange(value, ea => (this._fileValue = ea))
  }

  get envValue(): Maybe<T> {
    return this._userValue ?? this._envValue()
  }

  /**
   * - Set the value for the current process and child processes.
   * - Don't persist this value in the settings.toml.
   */
  set envValue(value: Maybe<T>) {
    this.onChange(value, ea => {
      this._envValue.set(ea)
      this.addToEnv()
    })
  }

  refreshEnvValue(args: { broadcastChange?: boolean } = {}) {
    // if the env value doesn't change valueOrDefault, don't bother the watchers:
    const prior = shallowClone(this.valueOrDefault)
    this._envValue.unset()
    if (true === args?.broadcastChange) this.maybeBroadcastChange(prior)
  }

  get tmpValue(): Maybe<T> {
    return this._userValue ?? this._tmpValue
  }

  /**
   * - Set the value for the current process (not child processes).
   * - Don't persist this value in the settings.toml.
   */
  set tmpValue(value: Maybe<T>) {
    this.onChange(value, ea => (this._tmpValue = ea))
  }

  /**
   * @return the first set value from user, env, file, or tmp.
   */
  get value(): Maybe<T> {
    return (
      this._userValue ?? this._envValue() ?? this._fileValue ?? this._tmpValue
    )
  }

  set value(value: MaybeNull<T>) {
    this.userValue = value == null ? undefined : value
  }

  /**
   * If there is a "human-friendly" version of the value, return that.
   */
  get humanValue(): Maybe<T | string> {
    return this.value
  }

  set userValue(value: Maybe<T>) {
    this.onChange(value, ea => (this._userValue = ea))
  }

  get valueOrThrow(): Defined<T> {
    const v = this.value
    if (v == null) {
      throw new Error("Missing value for setting " + this.name)
    } else {
      return v as any
    }
  }

  hasValue() {
    return this.value != null
  }

  isUnset() {
    return this.value == null
  }

  /**
   * Read the value from the given `Obj` or the ENV.
   *
   * THIS DOESN'T CHANGE ANY STATE. If you want to import from the env, use
   * `importFromEnv`.
   */
  readFromEnv(obj?: StringValued): Maybe<T> {
    const civ =
      obj == null ? caseInsensitiveEnv() : new CaseInsensitiveValued(obj)
    // Support PS_LOG_LEVEL and logLevel:
    for (const k of [...this.keys, ...this.names]) {
      const v = map(civ.get(k), ea => this.opts.fromEnv(ea))
      if (v != null) return v
    }
    return
  }

  /**
   * Should only be used directly by tests, as env shouldn't be wiggling around
   * after a process starts.
   */
  setFromEnv(environment: StringValued = env()): Maybe<T> {
    return (this.envValue = this.readFromEnv(environment))
  }

  setValueIfDefined(value: Maybe<T>) {
    if (value != null) this.value = value
  }

  watchLater(l: ChangeListener<T>) {
    this.watchers.push(l)
  }

  watch(l: ChangeListener<T>) {
    this.watchLater(l)
    // setImmediate allows the call to addListener() reference classes or
    // instances that haven't been parsed yet:
    setImmediate(() => this.broadcastChange())
  }

  unWatch(l: ChangeListener<T>) {
    filterInPlace(this.watchers, ea => ea === l)
  }

  broadcastChange() {
    const curr = this.valueOrDefault
    for (const ea of this.watchers) {
      ea(curr)
    }
    ee().emitDebounced("settingsChanged")
  }

  /**
   * @param mutateFunc may change this.value via one of the delegates
   */
  protected onChange(v: Maybe<T>, mutateFunc: (ea: Maybe<T>) => any) {
    // We have to shallow clone prior if it's an array or object, because
    // mutateFunc may modify prior directly, and then eql() will false-positive.
    const prior = shallowClone(this.valueOrDefault)
    mutateFunc(this.normalizeValue(v))
    this.maybeBroadcastChange(prior)
  }

  protected maybeBroadcastChange(prior: T) {
    if (!eql(this.valueOrDefault, prior)) this.broadcastChange()
  }

  /**
   * This is the camelCasedVersionOfTheSettingName
   */
  get name(): string {
    return this._name
  }

  get names(): string[] {
    return this._names
  }

  // this is public so Settings can push field names down to instances
  _setName(name: string) {
    if (this._name != null) throw new Error("cannot set name twice")
    this._name = name
    this._names = uniq([name, ...toA(this.opts.aliases)])
    this._key = envFromName(name)
    this._keys = uniq([
      ...this._names.map(envFromName),
      this._key,
      ...toA(this.opts.envAliases)
    ])
  }

  /**
   * This is the environment variable name for this setting:
   */
  get key(): string {
    return this._key
  }

  /**
   * This is a list of all environment keys for this setting. It will always
   * include `this.key`.
   */
  get keys(): string[] {
    return this._keys
  }

  get altKeys(): string[] {
    return this._keys.filter(ea => ea !== this._key)
  }

  get category(): SettingCategory {
    return this.opts.category
  }

  get categoryType(): "system" | "library" {
    return LibraryCategories.includes(this.category) ? "library" : "system"
  }

  get transient(): boolean {
    return this.opts.transient === true
  }

  /**
   * Is this setting an "advanced" option (and can be initially hidden)?
   */
  get advanced(): boolean {
    return mapOr(
      this.opts.advanced,
      ea => ea(),
      () => true
    )
  }

  /**
   * @return the env value encoding `this.valueOrDefault`
   */
  get envValueOrDefault(): Maybe<string> {
    return this.opts.toEnv(this.valueOrDefault)
  }

  set tmpValueIfUnset(t: Maybe<T>) {
    if (this.isUnset()) this.tmpValue = t
  }

  get defaultValue(): T {
    return tot(this.optsOverrides.defaultValue ?? this.opts.defaultValue)
  }

  set defaultValue(t: T | Thunk<T>) {
    const prior = shallowClone(this.valueOrDefault)
    this.optsOverrides.defaultValue = t
    this.maybeBroadcastChange(prior)
  }

  resetDefaultValue() {
    this.optsOverrides.defaultValue = undefined
  }

  get exampleValue(): Maybe<T> {
    return tot(this.optsOverrides.exampleValue ?? this.opts.exampleValue)
  }

  set exampleValue(t: Maybe<T>) {
    this.optsOverrides.exampleValue = t as any
  }

  get valueOrDefault(): T {
    return this.value ?? this.defaultValue
  }

  /**
   * no-op if unset
   */
  addToEnvMaybe<SV extends StringValued>(obj?: SV, overrideValue?: T): SV {
    const e: any = obj ?? env()
    // If we don't have a value, let the child take the default, or the
    // persisted value. This fixes
    // https://gitlab.com/photostructure/photostructure/issues/202
    const v = this.envValue ?? overrideValue
    if (v != null) {
      e[this.key] = this.opts.toEnv(v)
    }
    if (obj == null) {
      caseInsensitiveEnv.unset()
    }
    return e as SV
  }

  toEnvValue(overrideValue?: T): Maybe<string> {
    return this.opts.toEnv(overrideValue ?? this.valueOrDefault)
  }

  addToEnv<SV extends StringValued>(obj?: SV, overrideValue?: T): SV {
    const e: any = obj ?? env()
    const value = this.toEnvValue(overrideValue)
    if (value != null) {
      e[this.key] = value
      if (obj == null) {
        caseInsensitiveEnv.unset()
      }
    }
    return e as SV
  }

  deleteFromEnv<SV extends StringValued>(obj?: SV): SV {
    const cie =
      map(obj, ea => new CaseInsensitiveValued(ea)) ?? caseInsensitiveEnv()
    for (const ea of [...this.names, ...this.keys]) {
      cie.delete(ea)
    }
    return cie.obj as SV
  }

  /**
   * Removes tmp, env, and user-set values
   */
  unset() {
    this.onChange(undefined, () => {
      this._userValue = undefined
      this._envValue.unset()
      this._fileValue = undefined
      this._tmpValue = undefined
      this.optsOverrides = {}
      this.deleteFromEnv()
    })
    return this
  }

  addToJSON(): Obj {
    return {}
  }

  toJSON() {
    return {
      key: this.key,
      value: this.value,
      defaultValue: this.opts.defaultValue
    }
  }

  toCommentedLines() {
    const title = this.name + " or " + this.key
    const lines = wrap(
      [
        padding("-", title.length),
        title,
        padding("-", title.length),
        "",
        ...(this.opts.deprecated === true
          ? [
              "NOTE: this setting has been deprecated and will be removed in a future version of PhotoStructure.",
              ""
            ]
          : []),
        // make all newlines double-spaced:
        `${this.opts.description.replace(/\n+/g, "\n\n")}`
      ],
      WrapComments
    )

    function maybeAddList(key: string, arr: Maybe<string[]>) {
      if (!isEmpty(arr)) {
        lines.push(
          ...wrap(
            [
              "",
              (arr.length === 1 ? key : pluralize(key)) +
                ": " +
                orList(arr.map(ea => stringify(ea)))
            ],
            WrapComments
          )
        )
      }
    }
    maybeAddList("alias", this.opts.aliases)
    maybeAddList("environment alias", this.altKeys)

    for (const [key, value] of entries(this.addToJSON())) {
      const v = Array.isArray(value)
        ? orList(value.map(ea => stringify(ea)))
        : stringify(value)
      lines.push(...wrap(["", key + ": " + v], WrapComments))
    }

    return lines
  }

  toEnvLine(value?: T): string {
    return (
      this.key +
      "=" +
      (map(value, ea => this.opts.toEnv(ea)) ?? this.envValueOrDefault)
    )
  }

  toTomlLines(): string[] {
    const lines: string[] = this.toCommentedLines()

    wrapTomlToLines({
      lines,
      wrap: WrapComments,
      key: "example value",
      value: tot(this.exampleValue)
    })

    wrapTomlToLines({
      lines,
      wrap: WrapComments,
      prepend: ["Default value:"],
      key: this.name,
      value: tot(this.defaultValue)
    })

    wrapTomlToLines({
      lines,
      key: this.name,
      value: tot(this.fileValue)
    })

    return lines
  }
}
