import { compact, uniq } from "../fe/Array"
import { blank } from "../fe/Blank"
import { isDate } from "../fe/Date"
import { eql } from "../fe/Eql"
import { flatten } from "../fe/Flatten"
import { defined, map } from "../fe/Maybe"
import { Maybe, MaybeNull } from "../fe/MaybeTypes"
import { gt } from "../fe/Number"
import { Obj, entries, keys } from "../fe/Object"
import { Primitive, isPrimitive } from "../fe/Primitive"
import { Thunk } from "../fe/Thunk"
import { toErr } from "../fe/toErr"
import { first } from "./Array"
import { equalsIgnoreCase, sortIgnoreCase } from "./String"

// "{ new(): T }"
// is from https://www.typescriptlang.org/docs/handbook/generics.html#using-class-types-in-generics
export interface Constructor<M> {
  new (...args: any[]): M
}

export function definedThunks(...thunks: Thunk<any>[]): boolean {
  return thunks.every(ea => defined(ea()))
}

export function firstThunk<T>(...thunks: Thunk<Maybe<T>>[]): Maybe<T> {
  for (const t of thunks) {
    const r = t()
    if (r != null) {
      return r
    }
  }
  return
}

export function firstTrueThunk<T>(
  thunks: Thunk<Maybe<T>>[],
  predicate?: (t: T) => boolean
): Maybe<T> {
  for (const t of thunks) {
    const r = t()
    if (r != null && (predicate == null || predicate(r))) {
      return r
    }
  }
  return
}

export function firstDefined<T>(...objects: MaybeNull<T>[]): Maybe<T> {
  return objects.find(defined)
}

export function firstDefinedField<T, K extends keyof T>(
  obj: T,
  ...fieldNames: K[]
): Maybe<T[K]> {
  return map(
    fieldNames.find(field => null != obj[field]),
    fieldName => obj[fieldName]
  )
}

export function firstFieldLike<T extends Obj, K extends keyof T>(
  obj: T,
  predicate: (key: K, value: T[K]) => boolean
): Maybe<T[K]> {
  return first(
    keys(obj) as K[], // SITS: why is this cast needed?
    key => (predicate(key, obj[key]) ? obj[key] : undefined)
  )
}

// knex interprets undefined parameters as runtime mistakes, so use null:
export function ornull<T>(a?: T): T | null {
  return a === undefined ? null : a
}

export function mapAnd<T>(obj: MaybeNull<T>, f: (t: T) => boolean): boolean {
  return obj != null ? f(obj) : false
}

export function mapOrThrow<T, R>(
  obj: MaybeNull<T>,
  f: (t: T) => R,
  errIfMissing: string
): R {
  if (obj != null) {
    return f(obj)
  } else {
    throw new Error(errIfMissing)
  }
}

export function Try<T>(
  f: () => T,
  onError?: (error: Error) => Maybe<T>
): Maybe<T> {
  try {
    return f()
  } catch (err) {
    return onError?.(toErr(err)!)
  }
}

export function tryEach<T>(iter: Iterable<T>, f: (t: T) => any): void {
  ;[...iter].forEach(ea => Try(() => f(ea)))
}

export function identity<T>(t: T) {
  return t
}

export function ctor(obj: any): Maybe<string> {
  return map(obj.constructor, ea => ea.name)
}

export function hasKeys(obj: any): boolean {
  return Object.keys(obj).some(
    k => typeof k === "string" && obj.propertyIsEnumerable(k)
  )
}

/**
 * primitive and Dates
 */
export function primitiveEntries<T extends Obj>(o: T): [keyof T, Primitive][] {
  return keys(o)
    .filter(k => isPrimitive(o[k]) || isDate(o[k]))
    .map(k => [k, o[k]] as [string, Primitive])
}

export function spread<T extends Obj>(
  defaults: T,
  ...sources: MaybeNull<Partial<T>>[]
): T {
  return Object.assign({} as T, defaults, ...compact(sources))
}

/**
 * Assign fields from src that have Primitive value types
 */
export function assignMissingPrimitives<T>(dest: T, src: Maybe<Partial<T>>): T {
  if (src == null) {
    return dest
  }
  for (const [k, v] of primitiveEntries(src)) {
    if (dest[k] == null) {
      dest[k] = v as any
    }
  }
  return dest
}

/**
 * Only assign fields that are missing or nullish
 */
export function assignNullishFields<T>(dest: T, src: Maybe<Partial<T>>): T {
  if (src == null) {
    return dest
  }
  for (const [k, v] of entries(src)) {
    if (dest[k] == null) {
      ;(dest[k] as any) = v
    }
  }
  return dest
}

export function pickMap<K extends string, V, U>(
  obj: Record<K, V>,
  keysToRetain: K[],
  f: (key: K, value: V) => U
): Record<K, U> {
  const r = {} as Record<K, U>
  for (const k of keysToRetain) {
    r[k] = f(k, obj[k])
  }
  return r
}

export function mapEntries<T extends Obj, U>(
  obj: T,
  f: (key: keyof T & string, value: T[keyof T]) => Maybe<U>
): Record<keyof T, U> {
  const r = {} as Record<keyof T, U>
  for (const [k, v] of entries(obj)) {
    // PERF: UNROLL
    const val = f(k, v)
    if (val != null) r[k] = val
  }
  return r
}

export function mapNullEntries<T extends Obj, U>(
  obj: T,
  f: (key: keyof T & string, value: T[keyof T]) => MaybeNull<U>,
  maxKeys?: number
): Record<keyof T, U> {
  const r = {} as Record<keyof T, U>
  let keyCount = 0
  for (const k of keys(obj)) {
    r[k] = f(k, obj[k])!
    keyCount++
    if (gt(keyCount, maxKeys)) break
  }
  return r
}

/**
 * Do all fields in `a` match `b`?
 */
export function eqlSubset<T>(a: Maybe<Pick<T, any>>, b: T): boolean {
  return a == null ? false : keys(a).every((ea: keyof T) => eql(a[ea], b[ea]))
}

/**
 * For a given field name, what's o[fieldpath]?
 *
 * If fieldpath includes ".", those will be used to find sub-objects.
 */
export function valpath(o: any, ciFieldPath: string): any {
  if (ciFieldPath == null || o == null || blank(ciFieldPath)) return o
  if (Array.isArray(o)) return compact(o.map(ea => valpath(ea, ciFieldPath)))
  const nextDot = ciFieldPath.indexOf(".")
  const field = nextDot < 0 ? ciFieldPath : ciFieldPath.slice(0, nextDot)
  const fieldRemains = nextDot < 0 ? undefined : ciFieldPath.slice(nextDot + 1)
  const k = keys(o)
  if (k.includes(field)) return valpath(o[field], fieldRemains!)
  const ciField = k.find(ea => equalsIgnoreCase(ea, field))
  if (ciField != null) {
    return valpath(o[ciField], fieldRemains!)
  }
  return
}

export function sortedKeys(o: any): any {
  if (o == null || isPrimitive(o)) return o
  if (Array.isArray(o)) {
    return o.map(ea => sortedKeys(ea))
  }
  if (typeof o === "object") {
    const a: any = {}
    for (const k of sortIgnoreCase(keys(o))) {
      a[k] = sortedKeys(o[k])
    }
    return a
  }
  return o
}

export function deepDelete(o: any, ...keysToDelete: string[]): any {
  if (o == null || typeof o !== "object") return o
  if (Array.isArray(o)) return o.map(ea => deepDelete(ea, ...keysToDelete))
  return mapEntries(o, (k, v) =>
    keysToDelete.includes(k) ? undefined : deepDelete(v, ...keysToDelete)
  )
}

/**
 * @return the value associated to `caseInsensitiveKeyName`
 */
export function pluckCaseInsensitive(
  o: any,
  caseInsensitiveKeyName: string
): any {
  if (typeof o !== "object") return
  if (o[caseInsensitiveKeyName] !== undefined) return o[caseInsensitiveKeyName]
  for (const ea of keys(o).filter(s =>
    equalsIgnoreCase(s, caseInsensitiveKeyName)
  )) {
    if (o[ea] !== undefined) return o[ea]
  }
  return
}

export function pairToObject(key: string, value: any) {
  const o: any = {}
  o[key] = value
  return o
}

export function mergeObjects(...arr: Maybe<object>[]) {
  const o: any = {}
  for (const obj of compact(arr)) {
    for (const k of keys(obj)) {
      o[k] = obj[k]
    }
  }
  return o
}

export function zipPojos(...arr: Maybe<Obj>[]): Obj {
  const result: Obj = {}
  for (const key of uniq(flatten(arr.map(keys)))) {
    result[key] = arr.map(obj => obj?.[key])
  }
  return result
}
