// Added isNotEmpty so I can make the arr is not null assertion:
export function isNotEmpty<L extends List<any>>(arr: MaybeNull<L>): arr is L {
  return arr != null && toA(arr).length > 0
}

export function toNotEmpty<L extends List<any>>(arr: MaybeNull<L>): Maybe<L> {
  return isNotEmpty(arr) ? arr : undefined
}

export function notEmptyOr<L extends List<any>>(
  arr: Maybe<L>,
  defaultValue: ThunkOrT<L>
): L {
  return isNotEmpty(arr) ? arr : tot(defaultValue)
}

export function isEmpty(arr: MaybeNull<List<any>>): arr is undefined {
  return arr == null || toA(arr).length === 0
}

export function mapArray<T, U>(
  arr: MaybeNull<T[]>,
  f: (t: T[]) => U
): Maybe<U> {
  return Array.isArray(arr) ? f(arr) : undefined
}

export function mapNotEmpty<L extends List<any>, R>(
  arr: Maybe<L>,
  f: (ea: L) => R
): Maybe<R> {
  return isNotEmpty(arr) ? f(arr) : undefined
}

export function mapNotEmptyOr<L extends List<any>, R>(
  arr: Maybe<L>,
  f: (ea: L) => R,
  defaultValue: ThunkOrT<R>
): R {
  return isNotEmpty(arr) ? f(arr) : tot(defaultValue)
}

export function toPrimitive(
  a: Primitive | Primitive[] | Primitivable | Primitivables
): Primitive | Primitive[] {
  return isPrimitive(a) || isPrimitiveArray(a) ? a : a.valueOf()
}

// only deals with primitive arrays. use isEqlArray() instead.
function eqlArray<T extends Primitive>(a: T[], b: T[]): boolean {
  return (
    a != null &&
    b != null &&
    a.length === b.length &&
    a.every((ea, idx) => ea === b[idx])
  )
}

/**
 * @return true if `prefix` == `haystack.slice(0, prefix.length)`.
 */
export function startsWith<T extends Primitive>(
  haystack: T[],
  prefix: T[]
): boolean {
  return eqlArray(haystack.slice(0, prefix.length), prefix)
}

/**
 * Retain all items from `arr` where the `keepIfTrue` returns `true`.
 * @return true if `arr` was changed.
 */
export function filterInPlace<T>(
  arr: T[],
  keepIfTrue: (item: T, index: number, ea: T[]) => boolean
): T[] {
  for (let i = 0; i < arr.length; ) {
    if (keepIfTrue(arr[i], i, arr)) {
      i++
    } else {
      arr.splice(i, 1)
    }
  }
  return arr
}

/**
 * Retain all items from `arr` where the `keepIfTrue` returns `true`.
 * @return true if `arr` was changed.
 */
export async function filterInPlaceAsync<T>(
  arr: T[],
  keepIfTrue: (item: T, index: number, ea: T[]) => MaybeNullSyncOrAsync<boolean>
): Promise<T[]> {
  for (let i = 0; i < arr.length; ) {
    if (true === (await keepIfTrue(arr[i], i, arr))) {
      i++
    } else {
      arr.splice(i, 1)
    }
  }
  return arr
}

export function move<T>(arr: T[], fromIndex: number, toIndex: number): T[] {
  if (
    fromIndex === toIndex ||
    fromIndex < 0 ||
    toIndex < 0 ||
    fromIndex >= arr.length ||
    toIndex >= arr.length
  ) {
    return arr
  }
  const ea = arr[fromIndex]
  arr.splice(fromIndex, 1)
  arr.splice(toIndex, 0, ea)
  return arr
}

// ES2016 polyfill that also supports Iterable
export function includes(
  haystack: MaybeNull<any[] | Iterable<any>>,
  needle: any
): boolean {
  if (haystack == null) return false
  for (const ea of haystack) {
    if (needle.valueOf() === ea.valueOf()) return true
  }
  return false
}

export function indexOf<T>(
  iter: Maybe<Iterable<T>>,
  f: (t: T, index: number) => boolean
): Maybe<number> {
  if (iter == null) return
  let index = 0
  for (const ea of iter) {
    if (f(ea, index)) return index
    index++
  }
  return
}

export function indexOfPrimitive<T>(
  iter: Maybe<Iterable<T>>,
  t: T
): Maybe<number> {
  if (iter == null) return
  let index = 0
  for (const ea of iter) {
    if (ea === t) return index
    index++
  }
  return
}

export function includesAny(
  haystack?: Primitive[],
  needles?: Primitive[]
): boolean {
  return (
    isNotEmpty(haystack) &&
    isNotEmpty(needles) &&
    haystack.some(ea => needles.includes(ea))
  )
}

/**
 * @returns true iff all `needles` are found in `haystack`
 */
export function includesAll(
  haystack?: Primitive[],
  needles?: Primitive[]
): boolean {
  if (isEmpty(haystack) || isEmpty(needles)) return false
  for (const needle of needles) {
    if (!haystack.includes(needle)) return false
  }
  return true
}

export function eqlPrimitiveUnordered(a: Primitive[], b: Primitive[]): boolean {
  if (a == null || b == null || a.length !== b.length) return false
  const a1 = sortBy(a, toPrimitive)
  const b1 = sortBy(b, toPrimitive)
  return a1.every((ea, idx) => ea === b1[idx])
}

export function pushUniq<T>(arr: T[], ...items: T[]): T[] {
  for (const item of items) {
    if (item != null && !arr.some(ea => eql(ea, item))) {
      arr.push(item)
    }
  }
  return arr
}

export function pushUniqBy<T>(
  arr: T[],
  items: T[],
  valueOf: (t: T) => Comparable
): T[] {
  const vArr = arr.map(valueOf)
  for (const item of items) {
    const v = valueOf(item)
    if (!vArr.includes(v)) {
      arr.push(item)
      vArr.push(v)
    }
  }
  return arr
}

export function insertAt<T>(arr: T[], index: number, ...items: T[]): T[] {
  arr.splice(index, 0, ...items)
  return arr
}

/**
 * Insert `item` into `arr` only if `item` is not in `arr` (according to `cmp`).
 *
 * Assumes `arr` is ascending-sorted.
 */
export function insertUniq<T>(
  arr: T[],
  item: T,
  cmp_: (a: T, b: T) => number
): T[] {
  // verify the array is already in proper sort-order
  for (let i = 0; i < arr.length - 1; i++) {
    if (cmp_(arr[i], arr[i + 1]) > 0) {
      throw new Error("badly sorted array: " + arr)
    }
  }
  for (let i = 0; i < arr.length; i++) {
    const ea = arr[i]
    const c = cmp_(ea, item)
    if (c === 0) return arr
    // if item is greater than ea, insert right before ea
    if (c > 0) {
      arr.splice(i, 0, item)
      return arr
    }
  }
  arr.push(item)
  return arr
}

// Can't call this delete, as that's a javascript keyword
export function remove<T>(arr: T[], ...element: T[]): boolean {
  const before = arr.length
  filterInPlace(arr, ea => element.every(ea2 => !eql(ea, ea2)))
  return before !== arr.length
}

export function removeSame<T>(arr: T[], ...element: T[]): boolean {
  const before = arr.length
  filterInPlace(arr, ea => element.every(ea2 => ea !== ea2))
  return before !== arr.length
}

export function compact<T>(iter: MaybeNull<Iterable<T>>): Defined<T>[] {
  // SITS: don't remove the toA: some callers may not give a proper iterable!
  return iter == null ? [] : (filterInPlace(toA(iter), ea => ea != null) as any)
}

export function compactBlanks<T>(iter: MaybeNull<Iterable<T>>): string[] {
  // trim() to fix https://gitlab.com/mceachen/photostructure/issues/58
  return iter == null
    ? []
    : toA(iter)
        .map(ea => toS(ea).trim())
        .filter(ea => ea.length > 0)
}

export function compactBlankish<T>(iter: MaybeNull<Iterable<T>>): Defined<T>[] {
  return iter == null ? [] : [...iter].filter(notBlankish)
}

export function uniq<T>(arr: Maybe<Iterable<Maybe<T>>>): Defined<T>[] {
  // PERF: if `arr` is empty or only 1 or 2 elements, maybe we don't have to
  // call uniqBy:
  if (arr == null) return []
  const c = compact(arr)
  if (c.length <= 1) return c
  if (c.length < 10) {
    const f = c[0]
    if (c.every(ea => ea === f)) {
      return [f]
    }
  }
  return uniqBy(c, ea => (isPrimitive(ea) ? ea : stringify(ea)))
}

export function uniqFirst<T>(arr: Maybe<Iterable<Maybe<T>>>): NonNullable<T>[] {
  if (arr == null) return []
  const result: NonNullable<T>[] = []
  for (const ea of arr) {
    if (ea != null && !eql(result[result.length - 1], ea)) {
      result.push(ea as any)
    }
  }
  return result
}

export function uniqSubstrings(arr: Maybe<string>[]): string[] {
  if (arr == null) return []
  const strings = compact(arr)
  if (arr.length === 0) return []
  const result: string[] = []
  for (const str of sortBy(strings, ea => [-ea.length, ea.toLowerCase()])) {
    if (!result.some(ea => ea.includes(str))) {
      result.push(str)
    }
  }
  return sortBy(result, ea => strings.indexOf(ea))
}

/**
 * First-one-in-wins
 */
export function uniqBy<T, V extends Primitive>(
  arr: Iterable<Maybe<T>>,
  f: (t: T) => Maybe<V> = ea => stringify(ea) as V
): T[] {
  const m = new Map<V, T>()
  for (const ea of arr) {
    if (ea != null) {
      const v = f(ea)
      if (v != null) {
        getOrSet(m, v, () => ea)
      }
    }
  }
  return [...m.values()]
}

/**
 * Last-one-in-wins
 */
export function uniqByLast<T, V extends Primitive>(
  arr: Iterable<Maybe<T>>,
  f: (t: T) => Maybe<V> = ea => stringify(ea) as V
): T[] {
  const m = new Map<V, T>()
  for (const ea of arr) {
    if (ea != null) {
      const key = f(ea)
      if (key != null) {
        m.set(key, ea)
      }
    }
  }
  return [...m.values()]
}

/**
 * First-one-in-wins
 */
export function uniqBy2<T>(
  arr: Maybe<T>[],
  equals: (a: T, b: T) => boolean
): T[] {
  const result: T[] = []
  for (const a of arr) {
    if (a != null && result.every(b => !equals(a, b))) {
      result.push(a)
    }
  }
  return result
}

export function clear<T>(arr: T[]): T[] {
  arr.length = 0
  return arr
}

/**
 * Equivalent to filtering all items in an array to a new array and calling
 * length. This method avoids creating the intermediate array.
 *
 * @return the number of elements in `arr` that `predicate` returns `true`.
 */
export function count<T>(
  arr: T[],
  predicate: (t: T, idx: number) => boolean
): number {
  return arr.reduce((acc, ea, idx) => acc + (predicate(ea, idx) ? 1 : 0), 0)
}

/**
 * Equivalent to mapping all items in an array to a new number array and calling
 * Vector.sum(). This method avoids creating the intermediate array.
 *
 * @return the sum of elements of `f`-transmuted numbers from `arr`.
 */
export function sum<T>(
  arr: T[],
  f: (t: T, currentIndex: number) => number
): number {
  return arr.reduce((acc, ea, idx) => acc + f(ea, idx), 0)
}

export function firstMatch(
  re: RegExp,
  arr: Maybe<string>[]
): Maybe<RegExpExecArray> {
  for (const s of compact(arr)) {
    const m = re.exec(s)
    if (m != null) return m
  }
  return
}

export function commonPrefixLength<T extends Primitive>(
  a: Maybe<T[] | string>,
  b: Maybe<T[] | string>
): number {
  if (a == null || b == null) return 0
  if (a === b) return a.length
  if (typeof a === "string") a = [...a] as T[]
  if (typeof b === "string") b = [...b] as T[]
  if (eqlArray(a, b)) return a.length
  let result = 0
  while (a[result] === b[result]) result++
  return result
}

/**
 * Make random perturbations to `array` to minimize the given expense function
 */
export function anneal<T>({
  array,
  expense,
  allowedDelta
}: {
  array: T[]
  expense: (arr: T[], fromIndex: number, toIndex: number) => Comparable
  allowedDelta: number
}): T[] {
  const delta = Math.round(allowedDelta)
  if (delta < 2) return array
  for (let mid = 0; mid < array.length - 1; mid++) {
    const newPos = randomInt(
      Math.max(0, mid - delta),
      Math.min(array.length, mid + delta),
      [mid]
    )
    if (newPos == null) continue
    // the range is either [mid - 1, newPos + 1] or [newPos - 1, mid + 1]
    const i = Math.max(0, Math.min(newPos, mid) - 1)
    const j = Math.min(array.length, Math.max(newPos, mid) + 1)
    const currentExpense = expense(array, i, j)
    move(array, mid, newPos)
    const newExpense = expense(array, i, j)
    // undo the move if newExpense isn't better:
    if (lt(currentExpense, newExpense)) {
      move(array, newPos, mid)
    }
  }
  return array
}

/**
 * @param from inclusive
 * @param to exclusive
 * @param f
 */
export function range<T = number>(
  from: number,
  to: number,
  f: (i: number) => T = ea => ea as any
): T[] {
  return stepRange(from, to, 1, f)
}

/**
 * @param to return an array up to but not including `to`
 * @see https://docs.python.org/2/library/functions.html#range
 */
export function stepRange<T = number>(
  from: number,
  to: number,
  step: number = 1,
  f: (i: number) => T = ea => ea as any
): T[] {
  const r: T[] = []
  if (from < to) {
    for (let i = from; i < to; i += step) {
      r.push(f(i))
    }
  } else {
    for (let i = from; i > to; i -= step) {
      r.push(f(i))
    }
  }
  return r
}

export const primitiveValueOfOrElse = (a: any) => {
  if (isPrimitive(a)) {
    return a
  }
  // [].valueOf() is worthless, so use json
  if (Array.isArray(a)) {
    return stringify(a)
  }
  if (isFunction(a.valueOf)) {
    return a.valueOf()
  } else {
    throw new Error("Cannot get primitive value for " + JSON.stringify(a))
  }
}

/**
 * `minuend - subtrahend`.
 *
 * @template T must have a .valueOf() implementation
 * @param {T[]} minuend (haystack)
 * @param {T[]} subtrahend (needles)
 * @returns {T[]} values in `minuend` that, according to `.valueOf()`, are
 * not in `subtrahend`. Neither arg is mutated.
 */
export function diff<T>(
  minuend: MaybeReadonlyArray<T>,
  subtrahend: T[],
  valueOf: (t: T) => Primitive = primitiveValueOfOrElse
): T[] {
  const s = new Set(subtrahend.map(valueOf))
  return minuend.filter(ea => !s.has(valueOf(ea)))
}

export function last<T>(arr: Maybe<T[]>): Maybe<T> {
  return arr != null ? arr[arr.length - 1] : undefined
}

export function commaList(arr: string[], finalJoin = "or"): string {
  if (arr.length <= 1) return arr.join("")
  if (arr.length === 2) return arr.join(" " + finalJoin + " ")
  return (
    arr.slice(0, -1).join(", ") + ", " + finalJoin + " " + arr[arr.length - 1]
  )
}

export function arrToIterator<T>(arr: T[]): IterableIterator<T> {
  return arr[Symbol.iterator]()
}

export function arrayEndsWith<T extends Comparable>(
  arr: T[],
  suffix: T[]
): boolean {
  if (arr.length < suffix.length) return false
  for (let i = 0; i < suffix.length; i++) {
    // PERF: avoids the slice (and subsequent GC)
    if (arr[arr.length - suffix.length + i] !== suffix[i]) return false
  }
  return true
}
