import { Transform, TransformCallback } from "stream"
import { newlineRe } from "../../fe/String"
import { toS } from "../../fe/toS"

export class LineReader extends Transform {
  private _prior: string = ""

  constructor() {
    super({ objectMode: false, autoDestroy: true })
  }

  _transform(chunk: any, _encoding: BufferEncoding, done: TransformCallback) {
    const lines = (this._prior + toS(chunk)).split(newlineRe())
    const last = lines.pop()
    // if the last line is "", we ended with a newline, and we don't have a prior.
    this._prior = last ?? ""
    let delay = false
    for (const ea of lines) {
      // .push returns true if additional chunks of data may continue to be pushed; false otherwise
      delay ||= !this.push(ea)
    }
    if (delay) setTimeout(done, 1)
    else done()
  }

  _flush(done: TransformCallback): void {
    if (this._prior !== "") this.push(this._prior)
    this._prior = ""
    done()
  }
}
