import _path from "path"
import process from "process"
import { lazy, MemoizedThunk } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { uniqInPlace } from "../Array"
import { mkLogger } from "../Logger"
import { isDocker } from "../platform/isDocker"
import { isElectron, isMac } from "../platform/Platform"
import { ancestors, hasChildren } from "./Ancestors"
import { isPhotoStructureDmg } from "./IgnorableMountpoint"

export const execDir = lazy(() => _path.dirname(process.execPath))

function pathTo(...child: string[]): MemoizedThunk<string> {
  return lazy<string>(() => _path.join(ProjectPath.Root() as string, ...child))
}

export const ProjectPath = {
  Root: lazy(() => {
    const projectDirs = ["data", "public", "views"]
    const dirs: string[] = []
    if (isDocker()) {
      dirs.push("/opt/photostructure")
    }
    if (isElectron) {
      dirs.push(
        _path.join(execDir(), "resources"), // win electron
        _path.join(execDir(), "..", "Resources") // mac electron
      )
    }
    dirs.push(...compactBlanks([execDir(), process.cwd(), __dirname]))
    uniqInPlace(dirs)
    for (const dir of dirs) {
      if (hasChildren(dir, projectDirs)) return dir
      // don't go too far up:
      for (const parent of ancestors(dir).slice(0, 4)) {
        if (hasChildren(parent, projectDirs)) return parent
        const npx = _path.join(dir, "node_modules", "photostructure")
        if (hasChildren(npx, projectDirs)) return npx
      }
    }
    return mkLogger("fs.ProjectPath").throw(
      "Failed to find project root. Looked in " + dirs
    )
  }),
  Bin: pathTo("bin"), // only present in docker and node
  Data: pathTo("data"),
  ICC: pathTo("data", "icc"),
  Migrations: pathTo("data", "migrations"),
  Public: pathTo("public"),
  Tools: pathTo("tools"),
  Views: pathTo("views"),
  isInDMG: function (projectPathRoot?: string) {
    if (!isMac) return false

    // If PhotoStructure.app has a sibling called .background with a
    // nobg-background.tiff, we're still in the DMG, but that will break if we
    // change the DMG background. We could also check to see that we live in an
    // Applications directory, but that's also not guaranteed.
    return isPhotoStructureDmg(projectPathRoot ?? ProjectPath.Root())
  }
}
