import { isTrue } from "../../fe/Boolean"
import { StrEnumKeys } from "../../fe/StrEnum"
import { isProd } from "../NodeEnv"
import { DevEnvFlags } from "../env/EnvKeys"
import { isPacked } from "../platform/IsPacked"
import { env } from "./Env"

export type DevFlag = StrEnumKeys<typeof DevEnvFlags>

export function getDevEnvFlag(key: DevFlag): boolean {
  return !isProd && !isPacked() && isTrue(env()[key])
}

export function setDevEnvFlag(key: DevFlag, b: boolean) {
  if (b) {
    env()[key] = "true"
  } else {
    delete env()[key]
  }
}
