import { lazy } from "../../core/Lazy"
import { secondMs } from "../../fe/Date"
import { mapOr } from "../../fe/Maybe"
import { toInt } from "../../fe/Number"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { stdout_ } from "../child/ChildProcess"
import { ee } from "../event/EventEmitter"
import { isLinux, isMac, isWin } from "../platform/Platform"
import { PowerShell, pwshQuote } from "../pwsh/PowerShell"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { BaseFile } from "./BaseFile"
import { FileCache } from "./FileCache"
import {
  SimpleFile,
  SimpleFileOrString,
  isSimpleFile,
  toNativePath_
} from "./SimpleFile"
import { exists } from "./Stat"

export async function hide_(file: SimpleFile | string) {
  if (!isWin && !isMac) return
  const nativePath = toNativePath_(file)
  if (!(await exists(nativePath))) return
  if (isWin) {
    await PowerShell.instance().execute(
      `(Get-Item ${pwshQuote(nativePath)}).Attributes += 'Hidden'`,
      () => undefined
    )
  } else if (isMac) {
    await stdout_("chflags", ["hidden", nativePath], {
      timeoutMs: 10 * secondMs
    })
  }
  ee().emit("fileChanged", nativePath)
  return
}

async function isHiddenWin(file: SimpleFile) {
  const json = await PowerShell.instance().executeJsonToA(
    [
      "Get-Item -Force -LiteralPath",
      pwshQuote(file.nativePath),
      "-ErrorAction SilentlyContinue",
      "| Select-Object -Property Name, Mode"
    ].join(" ")
  )
  // See https://docs.microsoft.com/en-us/powershell/module/microsoft.powershell.management/get-childitem?view=powershell-5.1#examples
  return mapOr(
    json?.[0]?.Mode,
    mode => ["s", "h"].some(ea => mode.includes(ea)),
    () => false
  )
}

async function isHiddenMac(file: SimpleFile) {
  try {
    const out = await stdout_("stat", ["-f", "%f", file.nativePath], {
      timeoutMs: 10 * secondMs
    })
    const flags = toInt(out)
    if (flags != null) {
      return (flags & 0x8000) > 0 // from /usr/include/sys/stat.h
    } else {
      return false
    }
  } catch (err) {
    return false
  }
}

const cache = lazy(
  () =>
    new FileCache<boolean>({
      name: "fs.hidden",
      maxSize: 512,
      timeoutMs: commandTimeoutMs()
    })
)

/**
 * Doesn't match posix-hidden ("."-prefixed) files--only looks for windows or
 * mac hidden metadata flags.
 */
export function isHidden(f: SimpleFileOrString): SyncOrAsync<boolean> {
  // Don't check for posixHidden here-that's handled by a glob (that the user
  // can omit!)
  if (isLinux) return false // no way to hide on linux except with "."
  const sf = isSimpleFile(f) ? f : BaseFile.for(f)
  return isWin
    ? cache().getOrSetAsync(sf.nativePath, () => isHiddenWin(sf))
    : isMac
    ? cache().getOrSetAsync(sf.nativePath, () => isHiddenMac(sf))
    : false
}
