import path from "path"
import { leftPad } from "../../fe/Pad"
import { mkdirpSync_, mkdirp_, parseNativePath } from "./Path"
import { isEmptyFile } from "./Stat"
import { isEmptyFileSync } from "./StatSync"

export interface EnsureNewOptions {
  nativePath: string
  emptyIsNew?: boolean
  maxVersions?: number
  requireNumber?: boolean
  leftPad?: number
  startIndex?: number
}

export const DefaultEnsureNewOptions: Omit<
  Required<EnsureNewOptions>,
  "nativePath"
> = Object.freeze({
  emptyIsNew: true,
  maxVersions: 512,
  requireNumber: false,
  leftPad: 1,
  startIndex: 1
})

export async function ensureNewNativePath_(
  arg: EnsureNewOptions
): Promise<string> {
  const opts: Required<EnsureNewOptions> = {
    ...DefaultEnsureNewOptions,
    ...arg
  }
  const p = parseNativePath(opts.nativePath)
  await mkdirp_(p.dir)
  {
    if (!opts.requireNumber && (await isEmptyFile(opts.nativePath, opts))) {
      return opts.nativePath
    }
  }
  for (let i = opts.startIndex; i <= opts.maxVersions; i++) {
    const f = path.join(
      p.dir,
      `${p.name}-${leftPad(i, opts.leftPad, "0")}${p.ext}`
    )
    if (await isEmptyFile(f, opts)) return f
  }
  throw new Error(
    "There are already more than " + opts.maxVersions + " of " + opts.nativePath
  )
}

export function ensureNewNativePathSync_(arg: EnsureNewOptions): string {
  const opts: Required<EnsureNewOptions> = {
    ...DefaultEnsureNewOptions,
    ...arg
  }
  const p = parseNativePath(opts.nativePath)
  mkdirpSync_(p.dir)
  if (!opts.requireNumber && isEmptyFileSync(opts.nativePath, opts)) {
    return opts.nativePath
  }
  for (let i = opts.startIndex; i <= opts.maxVersions; i++) {
    const f = path.join(
      p.dir,
      `${p.name}-${leftPad(i, opts.leftPad, "0")}${p.ext}`
    )
    if (isEmptyFileSync(f, opts)) return f
  }
  throw new Error(
    "There are already more than " + opts.maxVersions + " of " + opts.nativePath
  )
}
