import { Defined, orElse } from "./Maybe"
import { Maybe, MaybeNull } from "./MaybeTypes"
import { ThunkOrT, tot } from "./Thunk"
import { toS } from "./toS"

/**
 * @return true if o is nullish, an empty string, or only whitespace.
 */
export function blank(o: any): o is undefined {
  // PERF: unrolled from Opt
  return o == null || o === ""
    ? true
    : typeof o === "string"
    ? o.trim() === ""
    : false
}

export function notBlank<T>(s: T): s is Defined<T> {
  return !blank(s)
}

export function notBlankToS(s: Maybe<any>) {
  return notBlank(s) && s !== "undefined" ? toS(s).trim() : undefined
}

/** aka "trimToUndefined" */
export function toNotBlank(o: any): Maybe<string> {
  if (o == null) return
  const s = toS(o)
  return s.length === 0 || s.trim().length === 0 ? undefined : s
}

/**
 * Equivalent to `Opt(s).flatMap(toS).filter(notBlank).getOrElse(() => orElse)`
 */
export function notBlankOr(s: any, ifBlank: ThunkOrT<string>): string {
  if (s == null) return tot(ifBlank)
  const str = toS(s).trim()
  return str.length > 0 ? str : tot(ifBlank)
}

export function notBlankAnd(s: any, f: (ea: string) => boolean): boolean {
  return !blank(s) ? f(s) : false
}

export function mapNotBlank<T>(arg: any, f: (s: string) => T): Maybe<T> {
  if (arg === false || arg == null || arg === "") {
    return undefined
  }
  const s = toS(arg)
  return notBlank(s) ? f(s!) : undefined
}

export function mapNotBlankOr<T>(
  arg: any,
  f: (s: string) => T,
  defaultValue: T | (() => T)
): T {
  return orElse(mapNotBlank(arg, f), defaultValue)
}

export function firstNotBlank(...arr: MaybeNull<string>[]): Maybe<string> {
  // PERF: unrolled
  for (const ea of arr) {
    if (typeof ea === "string" && ea.trim().length > 0) return ea
  }
  return
}

const BlankishRE = /^\s*(?:null|undefined)?\s*$/i

export function blankish(s: any): s is undefined {
  return s == null || BlankishRE.exec(toS(s)) != null
}

export function notBlankish<T>(s: T): s is Defined<T> {
  return !blankish(s)
}
