import { mapNotBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { memoize } from "../MemoizedFunc"
import { shortStringSha } from "../StringHash"

export const volsha = memoize(
  (uuid: Maybe<string>) => mapNotBlank(uuid, shortStringSha),
  { maxSize: 128, ttlMs: minuteMs }
)
