export function getOrSet<K, V>(
  m: Map<K, V> | WeakMap<any, V>,
  k: K,
  valueThunk: () => V
): V {
  if (k == null) throw new Error("null key")
  if (m.has(k)) {
    return m.get(k)!
  } else {
    const v = valueThunk()
    if (v != null) m.set(k, v)
    return v
  }
}

export function deleteIf<K, V>(
  m: Map<K, V>,
  predicate: (key: K, value: V) => boolean
) {
  for (const [k, v] of m.entries()) {
    if (predicate(k, v)) {
      m.delete(k)
    }
  }
}
