import { Maybe } from "../fe/MaybeTypes"
import { pick } from "../fe/Object"
import { toS } from "../fe/toS"

export type IndexedString = [string, number]

/**
 * Given a RegExp and string, return all captured groups and their starting
 * index.
 */
export function captures(
  regex: RegExp,
  str: string
): Pick<RegExpExecArray, "index" | "groups">[] {
  const result = []
  if (!regex.global) throw new Error("must provide global regex")
  let m: RegExpExecArray | null
  while ((m = regex.exec(str)) != null) {
    // This is necessary to avoid infinite loops with zero-width matches
    if (m.index === regex.lastIndex) {
      regex.lastIndex++
    } else {
      result.push(pick(m, "index", "groups"))
    }
  }
  return result
}

export function escapeRegExp(s: string | RegExpEscaped): string {
  return s instanceof RegExpEscaped
    ? s.source
    : toS(s).replace(/[-.,\\^$*+?()|[\]{}]/g, "\\$&")
}

export function matchQuotes(s: string) {
  return s.replace(/[']/g, "[']").replace(/["]/g, `["]`)
}

export function joinPatterns(arr: string[], flags?: string): RegExp {
  const result = []
  for (const ea of arr) {
    try {
      new RegExp(ea)
      result.push(ea)
    } catch {
      result.push(escapeRegExp(ea))
    }
  }
  return new RegExp(result.join("|"), flags)
}

/**
 * When provided to `concatRegexp()`, this `source` won't be further
 * escaped.
 *
 * This is only useful if a snippet of a regular expression is not valid by
 * itself (as it doesn't include balanced parenthesis or other grouping
 * constructs).
 */
export class RegExpEscaped {
  constructor(
    readonly source: string,
    readonly flags?: string
  ) {}

  get global() {
    return this.flags?.includes("g") ?? false
  }
  get ignoreCase() {
    return this.flags?.includes("m") ?? false
  }
  get multiline() {
    return this.flags?.includes("i") ?? false
  }
}

export class RegExpOptional extends RegExpEscaped {
  static from(...arr: Maybe<RegExp | string | RegExpEscaped>[]) {
    const re = concatRegexp(arr)
    return new RegExpOptional("(?:" + re.source + ")?", re.flags)
  }
}

export function concatRegexp(
  arr: Maybe<RegExp | string | RegExpEscaped>[],
  flags?: string
): RegExp {
  let re = ""
  let global = flags?.includes("g") ?? false
  let ignoreCase = flags?.includes("i") ?? false
  let multiline = flags?.includes("m") ?? false
  for (const ea of arr) {
    if (ea instanceof RegExpEscaped || ea instanceof RegExp) {
      re += ea.source
      global ||= ea.global
      ignoreCase ||= ea.ignoreCase
      multiline ||= ea.multiline
    } else {
      re += escapeRegExp(toS(ea))
    }
  }
  return new RegExp(
    re,
    flags ??
      (global ? "g" : "") + (ignoreCase ? "i" : "") + (multiline ? "m" : "")
  )
}
