import { blank, mapNotBlank } from "./Blank"
import { lazy } from "./Lazy"
import { Maybe } from "./MaybeTypes"
import { isNumber, round, sigFigs, toInt } from "./Number"
import { pluralize } from "./Pluralize"
import { StrEnumKeys, strEnum } from "./StrEnum"
import { replaceAll } from "./String"

// these instances are expensive to create, and can be reused for a given
// locale.
const numberFormat = lazy(() => new Intl.NumberFormat())

export const thousandsSep = lazy(() =>
  replaceAll(numberFormat().format(1111), "1", "").charAt(0)
)

export const decimalSep = lazy(() =>
  replaceAll(numberFormat().format(1.1), "1", "").charAt(0)
)

export function bytesToMB(...bytes: Maybe<number>[]) {
  let sum = 0
  for (const ea of bytes) {
    if (isNumber(ea)) sum += ea / MB
  }
  return round(sum)
}

export function fmt(i: number): string {
  // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/NumberFormat
  return numberFormat().format(i)
}

/**
 * Like `Number.toInt`, but accepts `fmt`ed strings by stripping thousands
 * separators before parsing.
 */
export function fmtToInt(s: Maybe<string>): Maybe<number> {
  return mapNotBlank(s, ea => toInt(replaceAll(ea, thousandsSep(), "")))
}

// See https://en.wikipedia.org/wiki/Mebibyte
export const KB = 1000
export const MB = KB * 1000
export const GB = MB * 1000
export const TB = GB * 1000

export const KiB = 1024
export const MiB = KiB * 1024
export const GiB = MiB * 1024
export const TiB = GiB * 1024

const byteUnits = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"]
const mebiUnits = ["B", "KiB", "MiB", "GiB", "TiB", "PiB", "EiB", "ZiB", "YiB"]

export function fmtMB(megabytes: number, sigfigs = 3): string {
  return fmtBytes(megabytes * MB, sigfigs)
}

export function fmtBytes(bytes: number, sigfigs = 3): string {
  if (bytes === 0) return "0"
  if (!isNumber(bytes)) return "-"
  const l = Math.floor(Math.log10(bytes))
  const mag = Math.floor(l / 3)
  const val = Math.pow(10, mag * 3)
  const name = byteUnits[mag]
  return sigFigs(bytes / val, sigfigs) + " " + name
}

export function fmtMebi(bytes: number, sigfigs = 3): string {
  if (bytes === 0) return "0"
  const l = Math.floor(Math.log2(bytes))
  const mag = Math.floor(l / 10)
  const val = Math.pow(2, mag * 10)
  const name = mebiUnits[mag]
  return sigFigs(bytes / val, sigfigs) + " " + name
}

export const MP = 1e6

export function megapixels(pixels: number): number {
  return sigFigs(pixels / MP, 2)
}

export const SizeDescriptions = strEnum(
  "tiny",
  "small",
  "medium",
  "large",
  "original"
)
export type SizeDescription = StrEnumKeys<typeof SizeDescriptions>

export function pixels2size(pixels: number): SizeDescription {
  return pixels < 320 * 240 // qvga
    ? "tiny"
    : pixels < 720 * 480 // wvga
    ? "small"
    : pixels < 1920 * 1080 // fhd
    ? "medium"
    : "large"
}

export function plur(
  i: Maybe<number>,
  singular: Maybe<string>,
  plural?: string
) {
  if (i == null) return ""
  if (blank(singular)) return fmt(i)
  return (
    fmt(i) + " " + (i === 1 ? singular : (plural ?? pluralize(singular)).trim())
  )
}

export interface Metric {
  count: string
  desc: string
}

export function plurMetric(i: number, singular: string, plural?: string) {
  plural ??= pluralize(singular)
  return { count: fmt(i), desc: i === 1 ? singular : plural }
}
