import { parseJSON } from "../fe/JSON"
import { Maybe } from "../fe/MaybeTypes"
import { Obj } from "../fe/Object"

// TODO: inline
export { parseJSON } from "../fe/JSON"

export function mapParsed<T>(s: string, f: (o: Obj) => T): Maybe<T> {
  const j = parseJSON(s)
  return j == null ? undefined : f(j)
}
