export class FifoSet<T> implements Set<T> {
  readonly [Symbol.toStringTag] = "FifoSet"
  readonly values = (): IterableIterator<T> => this.delegate.values()
  readonly keys = this.values

  private readonly delegate = new Set<T>()

  constructor(readonly maxSize: number) {}

  get size(): number {
    return this.delegate.size
  }

  add(value: T): this {
    this.delegate.add(value)
    this.vacuum()
    return this
  }

  clear(): this {
    this.delegate.clear()
    return this
  }

  delete(value: T): boolean {
    return this.delegate.delete(value)
  }

  forEach(callbackfn: (value: T, index: T, set: Set<T>) => void): void {
    this.delegate.forEach(callbackfn)
  }

  has(value: T): boolean {
    return this.delegate.has(value)
  }

  entries(): IterableIterator<[T, T]> {
    return this.delegate.entries()
  }

  [Symbol.iterator](): IterableIterator<T> {
    return this.values()
  }

  /**
   * remove all expired entries
   */
  private vacuum() {
    if (this.size > this.maxSize) {
      const iter = this.delegate.entries()
      while (this.size > this.maxSize) {
        this.delete(iter.next().value[0])
      }
    }
  }
}
