import { blank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { splitLines } from "../fs/CRLF"
import { stripCopySuffixFromName } from "../fs/FuzzyNames"
import { SimpleFile } from "../fs/SimpleFile"
import { zcat } from "../fs/zcat"
import { LogEntry, isLogEntry } from "./LogEntry"

export async function readLogEntries(
  f: SimpleFile,
  options?: { start?: number; end?: number }
): PromiseMaybe<LogEntry[]> {
  const from = stripCopySuffixFromName(f.name)
  const result: LogEntry[] = []
  const buf = await zcat(f.nativePath, options)
  if (buf != null) {
    for (const line of splitLines(buf)) {
      const le = chunkToLogEntry(line)
      if (le != null) result.push({ ...le, from })
    }
  }
  return result
}

export function chunkToLogEntry(chunk: any): Maybe<LogEntry> {
  if (blank(chunk)) return
  try {
    const le = JSON.parse(chunk)
    return isLogEntry(le) ? le : undefined
  } catch (error) {
    return
  }
}
