import { Duration } from "luxon"
import { compact } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import {
  dayMs,
  hourMs,
  minuteMs,
  secondMs,
  weekMs,
  yearMs
} from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { gte0, isDigits, isNumber, lt0, toFloat, toInt } from "../../fe/Number"
import { isString } from "../../fe/String"
import { captures } from "../RegExp"
import { sum } from "../math/Vector"

export function parseDuration(str: number | string): Maybe<number> {
  try {
    if (str == null || (!isString(str) && !isNumber(str))) return

    // Assume milliseconds if the string is just a number.
    if (isNumber(str)) return str
    if (isDigits(str)) return toInt(str)

    str = str.trim().toUpperCase()
    if (str.length === 0) return

    // Is it ISO formatted?
    const d = Duration.fromISO(str)
    if (d.isValid) return d.toMillis()

    // Nope, not ISO. Maybe something like "1d 2h 3m 4s 5ms"?

    const re =
      /[\s,]*?(?<value>-?(?:\d+)?\.?\d+)\s*(?<type>milliseconds?|msecs?|millis|ms|seconds?|secs?|s|minutes?|mins?|m|hours?|hrs?|h|days?|d|weeks?|w|years?|yrs?|y)?[\s,]*?/gim

    const groups = captures(re, str)
    if (groups.length === 0) return

    const units = compact(
      groups.map(ea => parseUnit(ea.groups?.value, ea.groups?.type))
    )

    // if the first value is negative, flip the sign of the remainder of the values.
    const values = lt0(units[0])
      ? [units[0], ...units.slice(1).map(ea => -ea)]
      : units

    return sum(values)
  } catch {
    return
  }
}

function parseUnit(value?: string, type?: string): Maybe<number> {
  const n = toFloat(value)
  if (n == null) return
  if (blank(type)) {
    // assume milliseconds
    return n
  }
  switch (type.toLowerCase()) {
    case "years":
    case "year":
    case "yrs":
    case "yr":
    case "y":
      return n * yearMs
    case "weeks":
    case "week":
    case "w":
      return n * weekMs
    case "days":
    case "day":
    case "d":
      return n * dayMs
    case "hours":
    case "hour":
    case "hrs":
    case "hr":
    case "h":
      return n * hourMs
    case "minutes":
    case "minute":
    case "mins":
    case "min":
    case "m":
      return n * minuteMs
    case "seconds":
    case "second":
    case "secs":
    case "sec":
    case "s":
      return n * secondMs
    case "milliseconds":
    case "millisecond":
    case "millis":
    case "msecs":
    case "msec":
    case "ms":
      return n
    default:
      // This should never occur.
      throw new Error(
        `parseDuration(): unit ${type} was matched, but no matching case exists.`
      )
  }
}

const Units = [
  { ms: yearMs, s: "y" },
  { ms: weekMs, s: "w" },
  { ms: dayMs, s: "d" },
  { ms: hourMs, s: "h" },
  { ms: minuteMs, s: "m" },
  { ms: secondMs, s: "s" },
  { ms: 1, s: "ms" }
]

export function fmtFullDuration(
  ms: Maybe<number>,
  sep: "" | " " | "," = ""
): Maybe<string> {
  if (!isNumber(ms)) return
  if (ms === 0) return "0"
  if (!gte0(ms)) {
    return !isNumber(ms) ? undefined : "-" + fmtFullDuration(Math.abs(ms))
  }
  const result = []
  for (const unit of Units.slice(Units.findIndex(ea => ea.ms <= ms!))) {
    const i = Math.floor(ms / unit.ms)
    ms -= i * unit.ms
    if (i > 0) result.push(i + unit.s)
  }
  return result.join(sep)
}
