import _fs from "fs"
import { parse } from "path"
import _s from "stream"
import { pipeline } from "stream/promises"
import { createBrotliDecompress, createGunzip, createGzip } from "zlib"
import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { stringify } from "../../fe/JSON"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { parseJSON } from "../JSON"
import { mkLogger } from "../Logger"
import { WrappedError } from "../error/WrappedError"
import { mkdirp_ } from "../fs/Path"
import { LogLevel, LogLevels } from "../log/LogLevel"
import { ReadableBuffer } from "./ReadableBuffer"
import { Pipeline } from "./Streams"
import { WritableToBuffer } from "./WritableToBuffer"

const logger = lazy(() => mkLogger("fs.zcat"))

/**
 * @throw if the file is missing or unreadable
 */
export async function zcat_(
  nativePath: string,
  options?: { start?: number; end?: number }
): Promise<string> {
  return (await zCopyToBuffer_(nativePath, options)).toString()
}

export async function zcat(
  nativePath: string,
  options?: { start?: number; end?: number }
): PromiseMaybe<string> {
  try {
    return zcat_(nativePath, options)
  } catch (err) {
    logger().warn("zcat failed to read " + nativePath, err)
    return undefined
  }
}

export async function zpipe_(
  nativePath: string,
  outputStream: _s.Writable,
  options?: { start?: number; end?: number }
) {
  const causes: Error[] = []
  const p: Pipeline = [
    _fs
      .createReadStream(nativePath, { autoClose: true, ...options })
      .on("error", err => causes.push(err))
  ]
  if (nativePath.toLowerCase().endsWith(".gz")) {
    p.push(createGunzip().on("error", err => causes.push(err)))
  } else if (nativePath.toLowerCase().endsWith(".br")) {
    p.push(createBrotliDecompress().on("error", err => causes.push(err)))
  }
  p.push(outputStream)
  await pipeline(p)
  if (isNotEmpty(causes)) {
    throw new WrappedError("zPipe(" + nativePath + ") failed", {
      causes,
      path: nativePath
    })
  }
}

/**
 * `readFile`, but on-the-fly decompression for .gz and .br
 * @throws on error
 */
export async function zCopyToBuffer_(
  nativePath: string,
  options?: { start?: number; end?: number }
): Promise<Buffer> {
  const w = new WritableToBuffer()
  await zpipe_(nativePath, w, options)
  return await w.buffer
}

export async function readJsonGz_<T = any>(nativePath: string) {
  return parseJSON(await zCopyToBuffer_(nativePath)) as Maybe<T>
}

export async function readJsonGz<T = any>(
  nativePath: string,
  level: LogLevel = LogLevels.warn
) {
  try {
    return await readJsonGz_<T>(nativePath)
  } catch (err) {
    logger().log(level, "readJsonGz(" + nativePath + "): failed", err)
    return
  }
}

export async function outputJsonGz_<T = any>(nativePath: string, obj: T) {
  const json = stringify(obj)
  await mkdirp_(parse(nativePath).dir)
  await pipeline(
    new ReadableBuffer(json),
    createGzip(),
    _fs.createWriteStream(nativePath, { autoClose: true })
  )
}

export async function outputJsonGz<T = any>(
  nativePath: string,
  obj: T,
  level: LogLevel = LogLevels.warn
) {
  try {
    await outputJsonGz_(nativePath, obj)
  } catch (err) {
    logger().log(level, "outputJsonGz(" + nativePath + "): failed", err)
  }
}
