import { lazy } from "../../core/Lazy"
import { blank, notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { hourMs, minuteMs } from "../../fe/Date"
import { gt0 } from "../../fe/Number"
import { entries } from "../../fe/Object"
import { ensureSuffix } from "../../fe/String"
import { mkLogger } from "../Logger"
import { mapGt0 } from "../Number"
import { picturesDir } from "../dir/PicturesDir"
import { ColorDistanceFunctions } from "./ColorDistanceFunctions"
import { DominantColorKmeansRunsDefault } from "./DominantColorKmeansRunsDefault"
import { DominantColorPixelsDefault } from "./DominantColorPixelsDefault"
import { Setting } from "./Setting"
import { Settings } from "./Settings"
import { TagInferenceSettingValues } from "./TagInferenceSettingValues"

const logger = lazy(() => mkLogger("settings.MetaSettings"))

function getSettingValue<T extends keyof typeof Settings>(
  name: T,
  arr: Setting<any>[]
): (typeof Settings)[T]["valueOrDefault"] {
  return (Settings[name].value ??
    arr.find(ea => ea.name === name)?.value ??
    Settings[name].defaultValue) as T
}

export function handleMetaSettings(imported: Setting<any>[]) {
  // Yes, we actually want valueOrDefault (so if we're rpi, we're true if
  // unset), and we don't need getSettingValue here.
  setQuickSyncMode(Settings.quickSyncMode.valueOrDefault)

  if (getSettingValue("disableAllFilters", imported)) {
    disableAllFilters()
  }

  if (getSettingValue("strictDeduping", imported)) {
    setStrictDeduping()
  }

  if (getSettingValue("allowFilesystemSleep", imported)) {
    allowFilesystemSleep()
  }
}

export async function handleDeprecatedSettings() {
  {
    //
    // scanMyPictures
    //
    if (isTrue(Settings.scanMyPictures.value)) {
      Settings.scanMyPictures.unset()
      const newPath = await picturesDir()
      logger().info("Upgrading setting scanMyPictures to scanPaths", {
        priorValues: Settings.scanPaths.values,
        newPath
      })
      Settings.scanPaths.push(newPath)
    }
  }

  {
    //
    // assetSubdirectoryDatestampFormat
    //
    const prior = Settings.assetSubdirectoryDatestampFormat.value
    Settings.assetSubdirectoryDatestampFormat.unset()
    if (blank(Settings.assetPathnameFormat.value) && notBlank(prior)) {
      const newValue = ensureSuffix(prior, "/") + "BASE"
      logger().info(
        "Upgrading setting assetSubdirectoryDatestampFormat to assetPathnameFormat",
        { prior, newValue }
      )
      Settings.assetPathnameFormat.value = newValue
    }
  }

  {
    //
    // syncIntervalHours
    //
    const prior = Settings.syncIntervalHours.value
    Settings.syncIntervalHours.unset()
    const newValue = mapGt0(prior, ea => ea * hourMs)

    if (!Settings.syncNewIntervalMs.hasValue() && gt0(newValue)) {
      logger().info(
        "Upgrading setting syncIntervalHours to syncNewIntervalMs",
        { prior, newValue }
      )
      Settings.syncNewIntervalMs.value = newValue
    }

    if (!Settings.syncChangedIntervalMs.hasValue() && gt0(newValue)) {
      logger().info(
        "Upgrading setting syncIntervalHours to syncChangedIntervalMs",
        { prior, newValue }
      )
      Settings.syncChangedIntervalMs.value = newValue
    }
  }

  {
    //
    // dbBackupIntervalMinutes
    //
    if (
      Settings.dbBackupIntervalMinutes.hasValue() &&
      !Settings.dbBackupIntervalMs.hasValue()
    ) {
      const newValue =
        Settings.dbBackupIntervalMinutes.valueOrDefault * minuteMs
      logger().info(
        "Upgrading setting dbBackupIntervalMinutes to dbBackupIntervalMs",
        { newValue }
      )

      Settings.dbBackupIntervalMs.value = newValue
    }
  }

  {
    //
    // enableSiblingInference
    //
    if (
      Settings.enableSiblingInference.hasValue() &&
      !Settings.siblingInference.hasValue()
    ) {
      const newValue = Settings.enableSiblingInference.valueOrDefault
        ? Settings.siblingInference.valueOrDefault
        : TagInferenceSettingValues.never
      logger().info(
        "Upgrading setting enableSiblingInference to siblingInference",
        { newValue }
      )

      Settings.siblingInference.value = newValue
    }
  }
}

// We use envValue here (rather than tmpValue) to forcefully ignore any env or
// file overrides.

export function disableAllFilters() {
  Settings.disableAllFilters.envValue = true

  Settings.disableIgnorableFilters.envValue = true

  Settings.respectFileExtensions.envValue = false

  Settings.requireMakeModel.envValue = false
  Settings.rejectRatingsLessThan.envValue = -100

  Settings.minImageDimension.envValue = 0
  Settings.minVideoDimension.envValue = 0

  Settings.minVideoDurationSec.envValue = 0
  Settings.maxVideoDurationSec.envValue = 0

  Settings.minAssetFileSizeBytes.envValue = 0
  Settings.maxAssetFileSizeBytes.envValue = 0

  Settings.validateJpegImages.envValue = false
  Settings.validateRawImages.envValue = false
  Settings.validateVideos.envValue = false
}

export function setStrictDeduping() {
  Settings.strictDeduping.envValue = true
  Settings.useImageHashes.envValue = true
  Settings.minExposureSettingsCoeffPct.envValue = 98
  Settings.minImageCoeffPct.envValue = 95
  Settings.imageHashFuzzyDateDelta.envValue = 1
  Settings.imageHashDifferentMimetypesDelta.envValue = -10
  Settings.imageHashGreyscaleDelta.envValue = 1
}

export function allowFilesystemSleep() {
  Settings.sharedStatePollMs.envValue = 0
}

const quickSyncSettings = [
  Settings.validateJpegImages,
  Settings.validateRawImages,
  Settings.validateVideos,
  Settings.transcodeVideos,
  Settings.previewMinimized,
  Settings.previewProgressive,
  Settings.previewSharpen,
  Settings.useImageHashes,
  Settings.enableSiblingInference
]

const argonDefaults = {
  argon2TimeCost: 8,
  argon2MemoryCostMB: 64,
  argon2Parallelism: 1
} as const
const argonQuick = {
  argon2TimeCost: 4,
  argon2MemoryCostMB: 32,
  argon2Parallelism: 1
} as const

export function setQuickSyncMode(quick: boolean) {
  for (const s of quickSyncSettings) {
    if (quick) {
      s.tmpValueIfUnset = false
    } else {
      s.tmpValue = undefined
    }
  }
  const argon = quick ? argonQuick : argonDefaults
  for (const [k, v] of entries(argon)) {
    Settings[k].defaultValue = v
  }
  Settings.dominantColorPixels.defaultValue = quick
    ? 256
    : DominantColorPixelsDefault
  Settings.dominantColorKmeansRuns.defaultValue = quick
    ? 0
    : DominantColorKmeansRunsDefault

  Settings.dominantColorDeltaE.defaultValue = quick
    ? ColorDistanceFunctions.cie76
    : ColorDistanceFunctions.ciede2000
}
