const logger = lazy(() => mkLogger("volumes.Volumes"))

// volumes() is the public method, which does caching and timeouts via lazyFsAsync

// volumesImpl is the BiImpl which may delegate to _volumesImpl or an RPC call.

const os: string = _os.platform()

export interface CachedVolume extends Volume {
  os: string
  /**
   * If cached, what hostname extracted this volume?
   */
  fromHost?: string
}

const mount2vol = new Map<string, Volume>()
const mount2volsha = new Map<string, string>()
const volsha2vol = new Map<string, Volume>()

export const mountpointToVolsha = lazyFsAsync(
  "mountpointToVolsha",
  async () => {
    await loadCachedVolumes()
    return mount2volsha
  }
)

function replaceIfNewer(
  key: Maybe<string>,
  vol: Volume,
  cache: Map<string, Volume>
) {
  if (blank(key)) return
  const prior = cache.get(key)
  if (prior == null || !gt(prior.updatedAt, vol.updatedAt)) {
    cache.set(key, vol)
    map(volsha(vol.uuid), ea => mount2volsha.set(vol.mountpoint, ea))
  }
}

function cacheVolume<T extends Volume>(vol: Maybe<T>) {
  if (vol == null) return
  replaceIfNewer(vol.mountpoint, vol, mount2vol)
  replaceIfNewer(volsha(vol.uuid), vol, volsha2vol)
  return vol
}

function systemVolumeCacheDir() {
  return PosixFile.forMaybe(cacheDir())?.join("volumes")
}

function libraryVolumeCacheDir() {
  return PosixFile.forMaybe(libraryDataDir())?.join("volumes")
}

function volumeCacheDirs() {
  return compact([systemVolumeCacheDir(), libraryVolumeCacheDir()])
}

async function readCachedVolumes() {
  const arr = await mapAsync({
    name: "readCachedVolumes",
    arr: thenFlatten(
      volumeCacheDirs().map(dir =>
        dir.childFiles(ea => ea.ext === ".json" && !ea.base.startsWith("."))
      )
    ),
    f: file => file.readJson<CachedVolume>()
  })
  const sorted = sortBy(arr, ea => [ea.mountpoint, -ea.updatedAt!])
  ;[...sorted.reverse()].forEach(cacheVolume)

  const uniq = uniqBy(sorted, ea => ea.mountpoint).map(ea => ({
    ...ea,
    fromCache: true
  }))

  return sortByCaseInsensitive(uniq, ea => ea.mountpoint)
}

export async function clearVolumesCache({
  deleteFileCache = false
}: { deleteFileCache?: boolean } = {}) {
  logger().warn("clearVolumesCache()", { deleteFileCache })
  volumes.unset()
  mount2vol.clear()
  volsha2vol.clear()
  loadCachedVolumes.unset()
  cachedVolumes.unset()
  if (deleteFileCache) {
    await Promise.all(volumeCacheDirs().map(ea => ea.rmrf()))
  }
}

const loadCachedVolumes = lazyFsAsync("loadCachedVolumes", async () => {
  const vols = await readCachedVolumes()
  vols?.forEach(cacheVolume)
  return vols
})

export const canWriteVolumesToCache = lazy(() => isSyncService() || isTest)

// only exposed for tests:
export const priorWriteVolumesToCacheLatch = lazy<Maybe<Latch>>(() => undefined)

export async function writeVolumesToCache(arr: Maybe<Volume[]>) {
  if (arr == null) return
  const l = new Latch()
  void priorWriteVolumesToCacheLatch.set(l)
  const updatedAt = Date.now()
  const vols: CachedVolume[] = arr
    .filter(ea => !isTrue(ea.fromCache))
    .map(ea => ({
      ...ea,
      ...(isDocker() ? {} : { fromHost: cleanHostname() }),
      updatedAt,
      os
    }))

  // Figure out what to write once, then write the volumes into both dirs:

  const name2vol = new Map<string, CachedVolume>()
  for (const vol of vols) {
    // we need to include both the mountpoint and the volsha: some volumes
    // won't have UUIDs.
    for (const key of compactBlanks([
      volsha(vol.uuid),
      volsha(vol.mountpoint)
    ])) {
      name2vol.set(key, vol)
    }
  }
  const dirs = volumeCacheDirs() // may be empty!
  for (const dir of dirs) {
    await withLock_(
      {
        file: dir,
        timeoutMs: ShortCommandTimeoutMs
      },
      async () => {
        for (const [name, vol] of name2vol.entries()) {
          const f = dir.join(name + ".json")
          try {
            await f.writeJson_(vol)
          } catch (error) {
            logger().warn(
              "writeVolumesToCache(): failed to write volumes cache",
              {
                error,
                output: f.nativePath,
                vol
              }
            )
            await f.unlink()
          }
        }
      }
    )
  }
  logger().debug("writeVolumesToCache(): complete.", {
    dirs,
    vol_mountpoints: vols.map(ea => ea.mountpoint)
  })

  void l.resolve()
}

export const cachedVolumes = lazy<Promise<CachedVolume[]>>(async () => {
  const arr = compact(await loadCachedVolumes())
  const m = toA(await mountpoints())
  const withCurrentMountpoints = arr.filter(ea => m.includes(ea.mountpoint))
  const result = isNotEmpty(withCurrentMountpoints)
    ? withCurrentMountpoints
    : arr.filter(ea => os === ea?.os) // Ideally we're only include from current host
  return logger().tap({
    msg: "cachedVolumes()",
    level: "trace",
    result: result.filter(ea => !isTrue(ea.ignorable)),
    meta: {
      currentMountpoints: m,
      withCurrentMountpoints: withCurrentMountpoints.map(ea => ea.mountpoint)
    }
  })
}, minuteMs)

// NOT CACHED
export async function priorVolumesIfMountpointsMatch() {
  const prior = await cachedVolumes()
  // workers should just use priorVolumes():
  if (isWorkerService() && isNotEmpty(prior)) return prior
  const m = toA(await mountpoints())
  const staleAt =
    Date.now() -
    clamp(minuteMs, dayMs, Settings.volumeMetadataTtlMs.valueOrDefault)
  const priorIsStale =
    Settings.volumeMetadataTtlMs.valueOrDefault === 0
      ? false
      : prior.some(ea => !gt(ea.updatedAt, staleAt))
  const priorIsMissingMountpoints = !eqlPrimitiveUnordered(
    m,
    prior.map(ea => ea.mountpoint)
  )
  logger().debug("priorVolumesIfMountpointsMatch()", {
    priorIsStale,
    priorIsMissingMountpoints
  })
  return !priorIsStale && !priorIsMissingMountpoints ? prior : undefined
}

export const volumes = lazyFsAsync("volumes", _volumes)

async function _volumes() {
  try {
    const prior = await thenOrTimeout(
      priorVolumesIfMountpointsMatch(),
      commandTimeoutMs()
    )
    if (prior != null) {
      logger().debug("volumes(): returning prior-cached volumes")
      return prior
    }

    const result = await thenOrTimeoutError(volumesImpl(), commandTimeoutMs())
    if (isNotEmpty(result)) {
      return result
    }
  } catch (cause) {
    onError(toWrappedError("volumes() failed", { cause }))
  }
  logger().debug("fallback: returning priorVolumes()")
  return cachedVolumes()
}

// only exposed so the shim can be set:
export const volumesImpl = shim0({ name: "fs.volumes", impl: _volumesImpl })

// for emitVolumesChanged():
let priorMountpoints: string[]

async function _volumesImpl(): PromiseMaybe<Volume[]> {
  // FOR TESTING:
  if (isTest) {
    const hangMs = toInt(env().PS_VOLUMES_HANG_MS)
    if (gt0(hangMs)) {
      logger().warn("PS_VOLUMES_HANG_MS: delay for " + hangMs)
      await delay(hangMs, false)
    }

    const fail = getDevEnvFlag("PS_FAIL_VOLUMES")
    if (fail) {
      logger().throw("PS_FAIL_VOLUMES")
      return
    }
  }

  const rawDfVols = await thenOrTimeout(
    isWin ? dfWin() : dfPosix(),
    mountpointsTtlMs(),
    () => onError("Timed out getting local volume metadata")
  )
  if (rawDfVols == null) {
    logger().warn("df failed")
    return
  }

  const dfVols = Settings.validateMountpoints.valueOrDefault
    ? compact(
        await mapAsync({
          name: "nonRpcVolumes: filter unhealthy volumes",
          arr: rawDfVols,
          timeoutMs: commandTimeoutMs(),
          f: async vol => {
            try {
              // Exclude volumes whose mountpoint doesn't exist (like unhealthy gio
              // mountpoints):
              const isDir = await isReadableDirectory(vol.mountpoint)
              if (isDir) {
                return vol
              } else {
                logger().info(
                  "validateMountpoints(): " +
                    vol.mountpoint +
                    " is not a directory"
                )
              }
            } catch (err) {
              logger().info(
                "validateMountpoints(): failed to stat " + vol.mountpoint,
                err
              )
            }
            return
          }
        })
      )
    : rawDfVols

  // We only need to fetch remote volume information if there are remote volumes
  // that are missing remoteHost:
  if (dfVols.some(ea => ea.remote === true && blank((ea as any)?.remoteHost))) {
    await thenOrTimeout(
      isWin ? addRemoteVolumeInfoWin(dfVols) : addRemoteVolumeInfoPosix(dfVols),
      10 * secondMs // < windows NET USE is flaky
    ).catch(error => {
      onError("Failed to get remote volume info", { error })
    })
  }
  const vols: Volume[] =
    (isWin
      ? dfVols
      : isMac
      ? await addLocalVolumeInfoMac(dfVols)
      : await addLocalVolumeInfoLinux(dfVols)) ?? dfVols

  for (const vol of vols) {
    vol.updatedAt ??= Date.now()
    // force boolean:
    vol.remote = isTrue(vol.remote)
    // undefine any blank labels:
    if (blank(vol.label)) delete vol.label
    // Fix hostnames:
    mapNotBlank(
      vol.remoteHost,
      // remote DNS hosts cannot be case sensitive
      ea => (vol.remoteHost = ea.toLowerCase().normalize().trim())
    )
  }

  await addVolumeUUIDs(vols)

  const sorted = sortBy(vols, ea => ea.mountpoint)

  logger().debug("_volumes(): final result", { sorted })

  if (priorMountpoints != null && !eql(priorMountpoints, sorted)) {
    later(() => ee().emit("volumesChanged"))
  }

  sorted.forEach(cacheVolume)

  await writeVolumesToCache(sorted)

  return Object.freeze(sorted) as Volume[]
}

export const rootPath = lazy(() =>
  isWin
    ? opt(getEnv("SystemDrive")) // < looks like "C:", not "C:\"
        .filter(notBlank)
        .orElse(() => "C:")
        .map(ea => ensureSuffix(ea, "\\"))
        .get()!
    : "/"
)

export function rootVolume(): PromiseMaybe<Volume> {
  return bestVolumeForPath(rootPath())
}

export function bestMountpoint(
  nativePath: Maybe<string>,
  mounts: Maybe<string[]>
): Maybe<string> {
  return blank(nativePath) || isEmpty(mounts)
    ? undefined
    : greatestBy(
        mounts.filter(mount =>
          containedByNativePath({
            ancestor: mount,
            descendant: nativePath,
            acceptSelf: true
          })
        ),
        mount =>
          commonPrefixLength(
            nativePath.split(_path.sep),
            mount.split(_path.sep)
          )
      )
}

export async function bestVolumeForPath(
  file: Maybe<string | SimpleFile>,
  vols?: Maybe<Volume[]>
): PromiseMaybe<Volume> {
  const np = map(file, toNativePath_)
  if (blank(np)) return
  const nativePath = await actualPath(np)

  for (const vp of [
    { name: "args", vols },
    { name: "cachedVolumes()", vols: cachedVolumes },
    { name: "volumes()", vols: volumes },
    { name: "allPriorVolumes()", vols: loadCachedVolumes }
  ]) {
    const arr = await tol(vp.vols)
    if (isEmpty(arr)) continue
    const mp = bestMountpointForDir(
      nativePath,
      arr.map(ea => ea.mountpoint)
    )
    if (mp == null) continue
    const result = arr.find(ea => ea.mountpoint === mp)
    if (result != null) {
      logger().debug("bestVolumeForPath()", {
        nativePath,
        result: result.mountpoint,
        src: vp.name
      })
      return result
    }
  }
  return logger().tap({
    msg: "bestVolume(): no volumes were applicable(!!)",
    result: undefined,
    meta: {
      nativePath
    }
  })
}

export async function bestVolumeForVolsha(volumeSha: string) {
  for (const vp of [
    { name: "allPriorVolumes()", vols: loadCachedVolumes },
    { name: "volumes()", vols: volumes }
  ]) {
    const match = toA(await vp.vols()).find(ea => volsha(ea.uuid) === volumeSha)
    logger().debug("bestVolumeForVolsha(): " + vp.name, { match })
    if (match != null) return match
  }
  return
}

export async function remoteVolumeFor(
  remoteHost: string,
  remoteShare: string
): PromiseMaybe<Volume> {
  return thenMap(volumes(), vols =>
    bestRemoteVolume(remoteHost, remoteShare, vols)
  )
}

/**
 * Find the first volume in `vols` that matches (case-insensitively) the given
 * `remoteHost` and `remoteShare`.
 */
export async function bestRemoteVolume(
  remoteHost: string,
  remoteShare: string,
  vols: Volume[]
): PromiseMaybe<Volume> {
  // Share names may be mostly unique, and they aren't messed with, so filter by that first:
  const withCorrectShare = vols.filter(vol =>
    equalsIgnoreCase(remoteShare, vol.remoteShare)
  )
  if (isEmpty(withCorrectShare)) return

  const withExactHostname = withCorrectShare.find(
    vol =>
      isTrue(vol.remote) &&
      notBlank(vol.remoteHost) &&
      equalsIgnoreCase(remoteHost, vol.remoteHost)
  )
  if (withExactHostname != null) return withExactHostname

  const friendlyRemoteHost = await friendlyname(remoteHost)
  return asyncFind(withCorrectShare, async vol =>
    equalsIgnoreCase(friendlyRemoteHost, await friendlyname(vol.remoteHost!))
  )
}

export async function findVolumeForVolsha(
  volumeSha: string
): PromiseMaybe<Volume> {
  if (blank(volumeSha)) {
    return undefined
  }
  return (
    volsha2vol.get(volumeSha) ??
    (await loadCachedVolumes())?.find(ea => volsha(ea.uuid) === volumeSha) ??
    (await volumes())?.find(ea => volsha(ea.uuid) === volumeSha)
  )
}
