import os from "node:os"
import process from "node:process"
import { lazy } from "../core/Lazy"
import { uniq } from "../fe/Array"
import { blank, toNotBlank } from "../fe/Blank"
import { toBoolean } from "../fe/Boolean"
import { PromiseMaybe } from "../fe/MaybeTypes"
import { gte0 } from "../fe/Number"
import { mkLogger } from "./Logger"
import { ShortCommandTimeoutMs } from "./ShortCommandTimeoutMs"
import { stdout_ } from "./child/ChildProcess"
import { getEnv } from "./env/GetEnv"
import { isWin } from "./platform/Platform"
import { PowerShell } from "./pwsh/PowerShell"

const logger = lazy(() => mkLogger("UserInfo"))

export async function userDesc() {
  let result = "user " + (await username())
  if (gte0(userid())) {
    result += " (userid " + userid() + ")"
  }
  return result
}

export const userids = lazy<number[]>(() =>
  // we prefer the effective id, but use getgid and userInfo to backfill.
  // uniq also compacts
  uniq([process.geteuid?.(), process.getuid?.(), os.userInfo().uid])
)

export const userid = lazy<number>(() => userids()[0])

export const groupids = lazy<number[]>(() =>
  // we prefer the effective id, but use getgid and userInfo to backfill:
  uniq([process.getegid?.(), process.getgid?.(), os.userInfo().gid])
)

export const groupid = lazy<number>(() => groupids()[0])

export const usernameFromEnv = lazy(() => {
  for (const ea of [
    // https://github.com/sindresorhus/username/blob/f4a47485fcdf3bafee221cf5e6d6180344020d08/index.js#L9
    "SUDO_USER",
    "C9_USER", // Cloud9
    "LOGNAME",
    "USER",
    "LNAME",
    "USERNAME" // windows calls this UserName, so we're using getEnv() (which is case-insensitive)
  ]) {
    const v = getEnv(ea as any)
    if (!blank(v)) return v
  }
  return
})

export const usernameFromUserInfo = lazy(() =>
  toNotBlank(os.userInfo()?.username)
)

export const userNameFromWhoami = lazy(async () =>
  stdout_("whoami", [], {
    ignoreExitCode: true,
    timeoutMs: ShortCommandTimeoutMs,
    isIgnorableError: () => true
  })
    .then(toNotBlank)
    .catch(error => logger().warn("$(whoami) failed", { error }))
)

export const userNameFromWindowsAccessToken = lazy(async () =>
  !isWin
    ? undefined
    : PowerShell.instance().execute(
        // https://stackoverflow.com/a/29955210
        "[System.Security.Principal.WindowsIdentity]::GetCurrent().Name",
        toNotBlank
      )
)

export const username = lazy<PromiseMaybe<string>>(
  async () =>
    usernameFromEnv() ??
    usernameFromUserInfo() ??
    (await userNameFromWhoami()) ??
    (await userNameFromWindowsAccessToken())
)

function pwshIsAdmin() {
  return isWin
    ? PowerShell.instance().execute(
        // https://serverfault.com/a/95464
        "(New-Object Security.Principal.WindowsPrincipal([Security.Principal.WindowsIdentity]::GetCurrent())).IsInRole([Security.Principal.WindowsBuiltInRole]::Administrator)",
        toBoolean
      )
    : undefined
}

export const isRootUser = lazy<PromiseMaybe<boolean>>(async () =>
  isWin ? pwshIsAdmin() : userids().includes(0)
)
