// Note that "pslib" is not a schema that is registered by IANA, but it doesn't
// collide with anything there, so we should be fine. See
// https://www.iana.org/assignments/uri-schemes/uri-schemes.xhtml

// Node URL parsing uses "protocol" instead of "scheme", and protocol is always
// suffixed with a colon.

/**
 * URI scheme used for asset files found in the PhotoStructure Library directory
 * hierarchy
 */
export const PS_LIBRARY_SCHEME = "pslib"
export const PS_LIBRARY_PROTOCOL = PS_LIBRARY_SCHEME + ":"

/**
 * URI scheme used for asset files found on a local disk volume
 */
export const PS_LOCAL_FILE_SCHEME = "psfile"
export const PS_LOCAL_FILE_PROTOCOL = PS_LOCAL_FILE_SCHEME + ":"

/**
 * URI scheme used for asset files found on a network filesystem
 */
export const PS_NETWORK_FILESYSTEM_SCHEME = "psnet"
export const PS_NETWORK_FILESYSTEM_PROTOCOL = PS_NETWORK_FILESYSTEM_SCHEME + ":"

/**
 * URI scheme only used for tests to reference exemplar photos and videos
 */

export const PS_EXAMPLES_SCHEME = "examples"
