import { readdirSync } from "fs"
import { dirname } from "path"
import { Maybe } from "../../fe/MaybeTypes"

export function ancestors(path: string): string[] {
  const arr = []
  while (path !== dirname(path)) {
    path = dirname(path)
    arr.push(path)
  }
  return arr
}

export function childrenSync(path: string): string[] {
  try {
    return readdirSync(path)
  } catch (err) {
    return []
  }
}
export function hasChildren(path: string, childNames: string[]): boolean {
  const actual = childrenSync(path)
  return childNames.every(ea => actual.includes(ea))
}

export function ancestorWithChildren(
  path: string,
  childNames: string[]
): Maybe<string> {
  return ancestors(path).find(ea => hasChildren(ea, childNames))
}
