import { blank } from "../../fe/Blank"
import { lt, sigFigs } from "../../fe/Number"
import { isEmptyObj } from "../../fe/Object"
import { isTest } from "../NodeEnv"
import { isServiceThatAcceptsFatalErrors, isWebService } from "../ServiceNames"
import { exit } from "../async/Exit"
import { ee } from "../event/EventEmitter"
import { Settings } from "../settings/Settings"
import {
  StartTs,
  errorRate,
  fatalErrorRate,
  internalErrorRate,
  lastInternalErrors,
  logger
} from "./Error"
import { InternalErrorFlag } from "./ErrorFlags"
import { isInternalError } from "./ErrorTypes"
import { NewWrappedError, toWrappedError } from "./WrappedError"
import { stack } from "./stack"

/**
 * Got an error? Give it here, please!
 *
 * @return true iff fatal
 */

export function onError(
  err: string | Error,
  meta?: unknown | Error | (NewWrappedError & { error?: Error | unknown })
): boolean {
  try {
    if (blank(err) && isEmptyObj(meta)) {
      logger().warn("onError() with no args", stack())
      return false
    }
    const error = toWrappedError(err, meta)

    const internalError = isInternalError(err)
    if (internalError) {
      internalErrorRate.onEvent()
      lastInternalErrors.push(err)
    }
    const fatal = error.fatal === true
    const ignorable = error.ignorable === true

    if (!fatal && ignorable) {
      logger().info("onError(): (ignorable)", { error: err })
      return false
    }

    errorRate.onEvent()
    if (fatal) {
      fatalErrorRate.onEvent()
      ee().emit("fatal", error)
    }
    const event = !fatal || isFatalErrorAllowed() ? "nonFatal" : "fatal"
    logger().log(event === "fatal" ? "error" : "warn", "onError()", {
      event,
      error
    })
    if (fatal && !isTest && !isWebService()) {
      void exit({ reason: error.toString(), status: error.errno ?? 13 })
    }
    return fatal
  } catch {
    // woof, something's really wrong
    console.error("onError() threw an error!", { err, meta })
    return false
  }
}
/**
 * Should this error be considered fatal to this process?
 */

export function isFatalErrorAllowed(): boolean {
  const postProbation =
    Date.now() > StartTs + Settings.probationMs.valueOrDefault

  const lowErrorRate = lt(
    fatalErrorRate.eventsPerMinute,
    Settings.fatalErrorRatePerMinute.valueOrDefault
  )

  const acceptsFatalErrors = isServiceThatAcceptsFatalErrors()

  // The only time a fatal error should not be considered fatal is by the main
  // service, after passing probation, and the error rate is not too high:
  return logger().tap({
    level: "info",
    msg: "isFatalErrorAllowed()",
    result: acceptsFatalErrors && postProbation && lowErrorRate,
    meta: {
      acceptsFatalErrors,
      postProbation,
      lowErrorRate,
      fatalErrorRatePerMin: sigFigs(fatalErrorRate.eventsPerMinute, 2),
      errorRatePerMin: sigFigs(errorRate.eventsPerMinute, 2),
      fatalErrorRatePerMinuteSetting:
        Settings.fatalErrorRatePerMinute.valueOrDefault
    }
  })
}

/**
 * These should never happen!
 */
export function onInternalError(
  message: string,
  cause?: Error | unknown,
  context?: any
): boolean {
  return onError(message + InternalErrorFlag, { cause, ...context })
}
