import { Dirent } from "fs"
import { lazy } from "../../core/Lazy"
import { flatten } from "../../fe/Flatten"
import { getOrSet } from "../../fe/Map"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { values } from "../../fe/Object"
import { toS } from "../../fe/toS"
import { MaybeFileish } from "../fs/MaybeFileish"
import { JpegExts } from "./JpegExts"
import { isSimpleFile } from "./SimpleFile"

const ShortExtRe = /(?:^\.?|.\.)([a-z0-9]{2,4})$/i

/**
 * @return the file extension, in lower case, without a "." prefix. File
 * extensions longer than 4 characters are not supported.
 */
export function normalizeExt(ext: MaybeFileish): Maybe<string> {
  ext = isSimpleFile(ext) ? ext.ext : ext instanceof Dirent ? ext.name : ext
  const result = map(ShortExtRe.exec(toS(ext)), m => m[1])?.toLowerCase()
  // hack to normalize all JPEG to "jpg"
  return JpegExts.includes(result as any) ? "jpg" : result
}

export function isExt(ext: MaybeFileish, ...exts: string[]): boolean {
  ext = normalizeExt(ext)
  return exts.some(ea => normalizeExt(ea) === ext)
}

export function isJpegExt(ext: MaybeFileish): boolean {
  return isExt(ext, ...JpegExts)
}
export function isJsonExt(ext: MaybeFileish): boolean {
  return isExt(ext, "json")
}
export function isCsvExt(ext: MaybeFileish): boolean {
  return isExt(ext, "csv")
}

export const SidecarFiletypes = {
  "application/rdf+xml": ["xmp"],
  "application/json": ["json"],
  "application/x-mie": ["mie"],
  "application/x-exif": ["exif"],
  "application/vnd.apple.photos": ["aae"],
  "image/x-exv": ["exv"] // BOO it's not an image!
} as const

export const SidecarExts = flatten(
  values(SidecarFiletypes)
) as unknown as string[]

/**
 * Note that JSON is considered a sidecar type
 */
export function isSidecarExt(ext: MaybeFileish): boolean {
  return isExt(ext, ...SidecarExts)
}

export const extAliases = lazy(() => {
  const result = new Map<string, string[]>()
  for (const arr of [
    JpegExts,
    ["tif", "tiff"],
    ["heif", "heic"],
    ["html", "htm"],
    ["jp2", "jp2k"]
  ] as string[][]) {
    for (const ea of arr) {
      result.set(ea, arr)
    }
  }
  return result
})

const regexCache = new Map<string, RegExp>()

/**
 * NOTE: Assumes exts doesn't need normalization, and comes directly from getExtInfo!
 */
export function mkExtsRegExp(exts: string[]): RegExp {
  return getOrSet(
    regexCache,
    exts.join(","),
    () => new RegExp("\\.(?:" + exts.join("|") + ")$", "im")
  )
}
