import _path from "path"
import { lazy } from "../../core/Lazy"
import { equalsIgnoreCase } from "../String"
import { WrappedError } from "../error/WrappedError"
import { isLinux } from "../platform/Platform"
import { FileCache } from "./FileCache"
import { logger, parseNativePath, pathIsRoot, resolve } from "./Path"
import { readdir_ } from "./Readdir"

export async function actualPath(...nativePath: string[]): Promise<string> {
  try {
    return await actualPath_(...nativePath)
  } catch (error) {
    logger().warn("actualPath() failed, reverting to resolve()", {
      nativePath,
      error
    })
    return resolve(...nativePath)
  }
}
const actualPathCache = lazy(
  () => new FileCache<string>({ name: "fs.Path.actualPathCache" })
)
/**
 * `node:fs.resolve()`, `node:fs.normalize()`, and `node:fs.realpath()` don't
 * actually return the _actual_ correctly-cased filename for case-insensitive
 * filesystems on Windows and macOS.
 *
 * This method does that.
 */

export async function actualPath_(...nativePath: string[]): Promise<string> {
  // our resolve() upcases drive letters:
  const resolved = resolve(...nativePath)
  // No need to check on Linux (filesystems are case-sensitive), or for root
  // paths (which would cause an infinite loop)
  if (isLinux || pathIsRoot(resolved)) {
    return resolved
  }

  return actualPathCache().getOrSetAsync(resolved, async () => {
    const p = parseNativePath(resolve(...nativePath))
    const actualDir = await actualPath_(p.dir)
    for (const ea of await readdir_(actualDir)) {
      // equalsIgnoreCase does unicode normalization!
      if (equalsIgnoreCase(ea.basename, p.base)) {
        return _path.join(actualDir, ea.basename)
      }
    }
    throw new WrappedError(resolved + " not found", {
      code: "ENOENT",
      path: resolved
    })
  })
}
