import { notBlank } from "../../fe/Blank"
import { Latch } from "../../fe/Latch"

export function onDataChunked(
  r: NodeJS.ReadableStream,
  sep: string | RegExp,
  onData: (data: string) => any
): Latch {
  const c = new Chunker(sep, onData, true)
  c.read(r)
  return c.done
}

/**
 * Handles data streams that may come in with disjoint chunks (like `["hel",
 * "lo\n"]`)
 */
export class Chunker {
  private incompleteChunk = ""
  readonly done = new Latch()

  constructor(
    readonly sep: string | RegExp,
    readonly onData: (data: string) => any,
    readonly filterBlanks = true
  ) {}

  onChunk(chunk: string | Buffer) {
    if (chunk == null) return
    const s = this.incompleteChunk + chunk.toString()
    const split = s.split(this.sep)
    // If s is not terminated by a separator, the last element in the array will
    // be non-blank. If the string ends in a separator, an empty string will be
    // the last element of the array, and this "clears out" incompleteChunk (as
    // we'd want), so both cases are handled appropriately here:
    this.incompleteChunk = split.pop()!
    split.forEach(ea => {
      if (!this.filterBlanks || notBlank(ea)) {
        this.onData(ea)
      }
    })
  }

  clear() {
    this.onChunk("")
    if (notBlank(this.incompleteChunk)) this.onData(this.incompleteChunk)
    this.incompleteChunk = ""
  }

  read(r: NodeJS.ReadableStream) {
    r.on("data", ea => this.onChunk(ea))
    r.on("end", () => {
      this.clear()
      void this.done.resolve()
    })
    return this
  }
}
