import { lazy } from "../../core/Lazy"
import { mapNotEmpty } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { minuteMs, secondMs } from "../../fe/Date"
import { getOrSet } from "../../fe/Map"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, mapFinite, round, sigFigs } from "../../fe/Number"
import {
  Obj,
  compactValues,
  fromEntries,
  mapCompactObj,
  omit
} from "../../fe/Object"
import { opt } from "../../fe/Opt"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { Thunk, tot } from "../../fe/Thunk"
import { toErr } from "../../fe/toErr"
import { CountingSet } from "../CountingSet"
import { Elapsed } from "../Elapsed"
import { Logger, mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { ee } from "../event/EventEmitter"
import { Average, AverageStats } from "../math/Average"
import { EndableRanks } from "./Endable"
import { EndableWrapper } from "./EndableWrapper"
import { setUnrefTimeout } from "./Timers"

const MinMs = 15

// by undocumented convention, names are
// ${section}.${filename}.${function?}?:${context?}

function normalizeName(name: string) {
  return name.split(":")[0].trim()
}

export class PromiseTimer {
  static readonly instance = lazy(() => {
    const pt = new PromiseTimer()
    new EndableWrapper(
      "PromiseTimer",
      () => {
        const logger = mkLogger("async.PromiseTimer")
        mapCompactObj(pt.report(), ea => logger.info("timings:", ea))
        mapNotEmpty(pt.errorCounts(), err =>
          logger.warn("error counts:", fromEntries(err))
        )
      },
      EndableRanks.stats
    )
    ee().on("timing", (name: string, elapsedMs: number) =>
      pt.push(name, elapsedMs)
    )
    // no need to clear stats except in tests:
    if (isTest) ee().on("clearCache", () => pt.clear())
    return pt
  })
  private readonly errors = new CountingSet<string>()
  private readonly times = new Map<string, Average>()

  clear() {
    this.errors.clear()
    this.times.clear()
  }

  async time<T>(
    desc: string,
    p: Promise<T> | (() => SyncOrAsync<T>),
    post?: (result: T | Error, elapsed: number) => void
  ): Promise<T> {
    const start = Date.now()
    const expectedTime = clamp(
      10 * secondMs,
      minuteMs,
      Math.round(this.times.get(desc)?.p98 ?? 0)
    )
    const slowTimer = setUnrefTimeout(
      () =>
        mkLogger("time(" + desc + ")").warn(
          "unsettled for " + (Date.now() - start) + "ms"
        ),
      expectedTime
    )

    try {
      const result = await tot(p)
      const elapsed = Date.now() - start
      post?.(result, elapsed)
      this.push(desc, elapsed)
      return result
    } catch (err) {
      this.errors.incr(desc)
      post?.(toErr(err)!, Date.now() - start)
      throw err
    } finally {
      clearTimeout(slowTimer)
    }
  }

  get entriesBySumDesc() {
    return sortBy([...this.times.entries()], ([, v]) => -v.sum)
  }

  stats(namePrefix: string) {
    const arr = this.entriesBySumDesc.filter(([k]) => k.startsWith(namePrefix))
    const merged = arr.reduce(
      (agg, ea) => Average.merge(ea[1], agg),
      new Average()
    )
    const stats = arr.map(
      ([name, avg]) => [name, avg.stats()] as [string, AverageStats]
    )
    return fromEntries([["merged", merged.stats()], ...stats])
  }

  mkElapsed(l: Logger) {
    return new Elapsed(l, (s, ts) => this.push(s, ts))
  }

  push(name: string, elapsedMs: number) {
    if (elapsedMs > MinMs) {
      getOrSet(this.times, normalizeName(name), () => new Average()).push(
        elapsedMs
      )
    }
  }

  weightedAvg(name: string): Maybe<number> {
    return opt(this.times.get(normalizeName(name)))
      .map(avg => avg.weightedSampleAvg)
      .get()
  }

  errorCounts() {
    return this.errors.top(10)
  }

  callCounts(): { [name: string]: number } {
    return [...this.times.entries()].reduce(
      (o, [k, v]) => ({ ...o, [k]: v.n }),
      {}
    )
  }

  weightedAvgs(): { [name: string]: number } {
    return compactValues(
      [...this.times.entries()].reduce(
        (o, [k, v]) => ({ ...o, [k]: mapFinite(v.weightedSampleAvg, round) }),
        {}
      )
    ) as any
  }

  toJSON() {
    return this.entriesBySumDesc.reduce(
      (o, [k, v]) => ({
        ...o,
        [k]: v.toJSON()
      }),
      {}
    )
  }

  report(): { [name: string]: Obj } {
    // Sort by most time first:
    return this.entriesBySumDesc.reduce(
      (o, [k, v]) => ({
        ...o,
        [k]: {
          sumSec: sigFigs(v.sum / secondMs, 3),
          ...omit(v.stats(), "sum")
        }
      }),
      {}
    )
  }
}

export function mkElapsed(name: string) {
  return PromiseTimer.instance().mkElapsed(mkLogger(name))
}

export function time<T>(
  name: string,
  p: Promise<T> | (() => SyncOrAsync<T>),
  post?: (result: T | Error, elapsed: number) => void
): Promise<T> {
  return PromiseTimer.instance().time(name, p, post)
}

export function timeSync<T>(name: string, t: Thunk<T>): T {
  const start = Date.now()
  const result = t()
  pushTime(name, Date.now() - start)
  return result
}

export function pushTime(name: string, elapsedMs: number) {
  PromiseTimer.instance().push(name, elapsedMs)
}

export function timeStats(namePrefix: string) {
  return PromiseTimer.instance().stats(namePrefix)
}

export function timedLazy<T>(
  name: string,
  thunk: () => SyncOrAsync<T>,
  ttlMs?: number
) {
  return lazy(async () => time(name, thunk), ttlMs)
}
