import { strEnum, StrEnumKeys } from "../../fe/StrEnum"

// order here is the default sort order!
export const AssetFileSortCriteria = strEnum(
  // more pixels are always better (this is quantized using variantSortCriteriaPower):
  "resolution",
  // prefer files in the library:
  "schemeIdx",

  // prefer files with a more accurate date:
  "capturedAtPrecision",
  // prefer files with more fields with metadata we care about:
  "metadataCoverage",
  // prefer files we can directly throw at the browser:
  "isBrowserSupported",

  // if they edit a file, we should prefer that newly edited version:
  "mtime",

  // If we have a burst files, prefer the "burst cover"
  "isCover",

  // If there are many copies of a file (image.jpg, image (1).jpg, image
  // (2).jpg), prefer the one with the highest number (assuming that's the
  // latest copy):
  "count",

  // help make things deterministic:
  "fileSize",
  // help make things deterministic:
  "basename",
  // help make things deterministic:
  "parentBasename",
  // just to make things deterministic:
  "uri"
)

export type AssetFileSortCriterion = StrEnumKeys<typeof AssetFileSortCriteria>
