import { Dirent } from "fs"
import { blank, notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { ensurePrefixSuffix } from "../../fe/String"
import { toS } from "../../fe/toS"
import { BaseFile } from "./BaseFile"
import { SimpleFileOrString, basename } from "./SimpleFile"

export const PsLockExt = ".pslock"

export function fsLockDir(f: BaseFile): BaseFile {
  return f.sibling(
    ensurePrefixSuffix(f.base, { prefix: ".", suffix: PsLockExt })
  )
}

export function fsLockFileBasename(id: string): string {
  // The lockfiles don't need to be hidden, but it makes isPsLockFileOrDir()
  // much simpler.
  return ensurePrefixSuffix(id, { prefix: ".", suffix: PsLockExt })
}

export function uidFromFsLockfile(
  f: Maybe<SimpleFileOrString | Dirent>
): Maybe<string> {
  if (f == null || blank(toS(f))) return
  const name = basename(f)
  return basenameIsPsLockFileOrDir(name)
    ? name.substring(1, name.length - PsLockExt.length)
    : undefined
}

export function isPsLockFileOrDir(f: SimpleFileOrString | Dirent): boolean {
  return blank(f) ? false : basenameIsPsLockFileOrDir(basename(f))
}

function basenameIsPsLockFileOrDir(name: string): boolean {
  return notBlank(name) && name.startsWith(".") && name.endsWith(PsLockExt)
}
