import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { isTest } from "../NodeEnv"
import { isMac } from "../platform/Platform"
import { isExcludedMountpoint } from "../volumes/PosixVolumes"

const DevMounts = /^\/dev(\/|$)/i

// Mojave mounts to /private/var/vm.
// BigSur mounts to /System/Volumes/VM, /System/Volumes/Preboot, /System/Volumes/xarts, ...

// mrm@mini ~ % mount | sed -e "s/map auto/map_auto/" | cut -d\  -f3
// /
// /dev
// /System/Volumes/Data
// /private/var/vm
// /Volumes/Mojave
// /System/Volumes/Data/home
// /System/Volumes/Hardware
// /Volumes/Update
// /Volumes/Install
// /Volumes/SSD
// /Volumes/Big
// /Volumes/Update
// /Volumes/Mojave
// /Volumes/Recovery
// mrm@mini ~ % uname -a
// Darwin mini 19.6.0 Darwin Kernel Version 19.6.0: Sun Nov 14 19:58:51 PST 2021; root:xnu-6153.141.50~1/RELEASE_X86_64 x86_64

const MacSystemVolumes =
  isMac || isTest
    ? [
        "/dev",
        "/System/Volumes/Hardware",
        "/System/Volumes/iSCPreboot",
        "/System/Volumes/Preboot",
        "/System/Volumes/Update",
        "/System/Volumes/VM",
        "/System/Volumes/xarts",
        "/Volumes/Install",
        "/Volumes/Recovery"
      ].map(ea => ea.toLowerCase())
    : undefined

const MacPrivate = isMac || isTest ? /^\/private\//i : undefined

// macOS installation media
const MacOsInstall =
  isMac || isTest ? /^\/Volumes\/install macos\b/i : undefined

const MacNotDmg =
  isMac || isTest // support "v1.2", "v1.2.3", "v1.2.3-beta.4", "v1.2.3-beta.4-pre", ...
    ? /^\/Volumes\/PhotoStructure v?[\d\.]{3,}/i
    : undefined

export function isPhotoStructureDmg(mountpoint: string): boolean {
  return MacNotDmg?.exec(toS(mountpoint)) != null
}

export function isIgnorableMountpoint(mountpoint: string): boolean {
  return whyIgnorableMountpoint(mountpoint) != null
}

export function whyIgnorableMountpoint(mountpoint: string): Maybe<string> {
  return blank(mountpoint)
    ? "blank"
    : isExcludedMountpoint(mountpoint)
    ? "excluded"
    : DevMounts.test(mountpoint)
    ? "/dev"
    : MacSystemVolumes?.includes(mountpoint.toLowerCase()) === true
    ? "macOS system volume"
    : MacOsInstall?.test(mountpoint) === true
    ? "macOS installer volume"
    : MacPrivate?.test(mountpoint) === true
    ? "macOS private volume"
    : isPhotoStructureDmg(mountpoint)
    ? "PhotoStructure dmg"
    : undefined
}
