import { EventEmitter } from "events"
import { lazy } from "../../core/Lazy"
import { CapturingEventEmitter } from "./CapturingEventEmitter"
import {
  PhotoStructureEmitter,
  PhotoStructureEvents
} from "./PhotoStructureEvents"

export function mkEE(
  eventsToRetain?: number
): CapturingEventEmitter<PhotoStructureEvents> {
  const e = new EventEmitter()
  e.setMaxListeners(70) // because of ee().on("clearCache")
  return new CapturingEventEmitter(e as PhotoStructureEmitter, eventsToRetain)
}

export const ee = lazy(mkEE)
