import os from "os"
import { Maybe } from "../fe/MaybeTypes"
import { strEnum, StrEnumKeys } from "../fe/StrEnum"

// From https://docs.microsoft.com/en-us/dotnet/api/system.diagnostics.processpriorityclass?view=netframework-4.8

// AboveNormal Specifies that the process has priority higher than Normal but
// lower than High.

// BelowNormal Specifies that the process has priority above Idle but below
// Normal.

// High Specifies that the process performs time-critical tasks that must be
// executed immediately, such as the Task List dialog, which must respond
// quickly when called by the user, regardless of the load on the operating
// system. The threads of the process preempt the threads of normal or idle
// priority class processes. Use extreme care when specifying High for the
// process's priority class, because a high priority class application can use
// nearly all available processor time.

// Idle Specifies that the threads of this process run only when the system is
// idle, such as a screen saver. The threads of the process are preempted by the
// threads of any process running in a higher priority class. This priority
// class is inherited by child processes.

// Normal Specifies that the process has no special scheduling needs.

export const PriorityClasses = strEnum("Normal", "BelowNormal", "Idle", "Low")

export type PriorityClass = StrEnumKeys<typeof PriorityClasses>

// PRIORITY_LOW: 19,
// PRIORITY_BELOW_NORMAL: 10,
// PRIORITY_NORMAL: 0,
// PRIORITY_ABOVE_NORMAL: -7,
// PRIORITY_HIGH: -14,
// PRIORITY_HIGHEST: -20

export const PriorityClassToPosix = Object.freeze({
  Normal: 0,
  BelowNormal: 10,
  Low: 19,
  Idle: 19
}) satisfies Record<PriorityClass, number>

export const PriorityClassToNode = Object.freeze({
  AboveNormal: os.constants.priority.PRIORITY_ABOVE_NORMAL,
  Normal: os.constants.priority.PRIORITY_NORMAL,
  BelowNormal: os.constants.priority.PRIORITY_BELOW_NORMAL,
  Idle: os.constants.priority.PRIORITY_LOW,
  Low: os.constants.priority.PRIORITY_LOW
}) satisfies Record<PriorityClass, number>

export function priorityPosixToClass(
  priority: Maybe<number>
): Maybe<PriorityClass> {
  if (priority == null || !isFinite(priority)) {
    return
  } else if (priority < PriorityClassToPosix.BelowNormal) {
    return PriorityClasses.Normal
  } else if (priority < PriorityClassToPosix.Idle) {
    return PriorityClasses.BelowNormal
  } else {
    return PriorityClasses.Idle
  }
}
