import { Command } from "commander"
import { Settings } from "../../core/settings/Settings"
import { map } from "../../fe/Maybe"
import { Obj } from "../../fe/Object"
import { isEnvTrue } from "../env/GetEnv"
import { CommandPlugin } from "./CLI"

export const ColorArgs: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd
      .option(
        "--color",
        "Enable ASCII terminal colors (this overrides NO_COLOR, if set)"
      )
      .option(
        "--no-color",
        "Disable ASCII terminal colors (see https://no-color.org/)"
      ),

  afterParse: (opts: Obj) => {
    // these don't need to be assigned to .envValue, but it means info
    // --child-env will emit these changes (and avoid confusion)
    if (isEnvTrue("NO_COLOR")) Settings.logColor.envValue = false
    // but let the env to be overridden by an arg:
    map(opts.color, ea => (Settings.logColor.envValue = ea))
  }
}
