const cache = lazy(() => new FileCache<PosixFile>({ name: "fs.PosixFile" }))

function sortFiles(paths: PosixFile[]) {
  return sortBy(paths, ea => ea.nativePath)
}

export function uniquePrefixes(paths: PosixFile[]) {
  return contextFilter(
    sortFiles(paths),
    // This also does a uniq():
    (ea, _idx, last) => last == null || !ea.isDescendantOf(last)
  )
}

export const NoOp = Symbol("no-op")

/**
 * Ensures a given path is only "posix-ized" once, and holds promises for more
 * expensive information about the file, like inode stats, SHA1, and EXIF tags.
 */
export class PosixFile extends BaseFile implements SimpleFile {
  protected readonly pflog = lazy(() =>
    mkLogger("fs.PosixFile(" + this.nativePath + ")")
  )

  protected constructor(
    readonly nativePath: string,
    dirent?: DirectoryEntry
  ) {
    super(nativePath, dirent)
  }

  [util.inspect.custom]() {
    return this.toJSON()
  }

  static forDirectoryEntry(de: DirectoryEntry) {
    return PosixFile.for(de.nativePath, de)
  }

  static forMaybe(nativePathOrFile: Maybe<SimpleFileOrString>) {
    return notBlank(nativePathOrFile)
      ? PosixFile.for(nativePathOrFile)
      : undefined
  }

  static for(
    nativePathOrFile: SimpleFileOrString,
    dirent?: DirectoryEntry
  ): PosixFile {
    if (nativePathOrFile instanceof PosixFile) {
      return nativePathOrFile
    }

    if (blank(nativePathOrFile)) {
      throw new Error("PosixFile.for(): empty nativePathOrFile")
    }

    if (isString(nativePathOrFile)) {
      // avoid expensive resolve() call:
      const prior = cache().get(nativePathOrFile)
      if (prior != null) return prior
    }
    const resolved = toNativePath_(nativePathOrFile)
    const result = new PosixFile(resolved, dirent)
    if (isString(nativePathOrFile)) {
      cache().set(nativePathOrFile, result)
    }
    cache().set(resolved, result)
    return result
  }

  static forPosix(posixPath: string): PosixFile {
    return PosixFile.for(posixPath.replace(/\//g, _path.sep))
  }

  static forUri(uri: string, mountpoint?: string): PromiseMaybe<PosixFile> {
    return thenMap(uri2nativePath(uri, mountpoint), nativePath =>
      PosixFile.for(nativePath)
    )
  }

  for(nativePath: string, dirent?: DirectoryEntry): this {
    return PosixFile.for(nativePath, dirent) as this
  }

  forDirectoryEntry(de: DirectoryEntry): this {
    return PosixFile.for(de.nativePath, de) as this
  }

  clear({ emit } = { emit: true }): this {
    super.clear({ emit })
    this.uriObject_.unset()
    this.uri_.unset()
    this.normalizedPathUri_.unset()
    this.fileuri.unset()
    this.etag.unset()
    this.existingSidecars.unset()
    this.unlock.unset()
    return this
  }

  readonly uriObject_ = lazy(() => nativePath2uri(this.nativePath))
  readonly uri_ = lazy(async () => (await this.uriObject_()).toString())
  uri(): PromiseMaybe<string> {
    return this.trap("uri", () => this.uri_())
  }
  readonly fileuri = lazy(() => URI.file(this.nativePath).toString())

  readonly normalizedPathUri_: MemoizedThunk<PromiseMaybe<string>> = lazy(
    async () => (await this.normalize()).uri_()
  )

  async mountpoint(): PromiseMaybe<PosixFile> {
    if (
      containedByNativePath({
        // HEY FUTURE ME: referencing libraryDirPosixFile creates a deploop _don't do that_
        ancestor: libraryDir(),
        descendant: this,
        acceptSelf: true
      }) ||
      containedByNativePath({
        ancestor: originalsDir(),
        descendant: this,
        acceptSelf: true
      })
    ) {
      return
    }
    if (isWin && this.nativePath.startsWith("\\\\")) {
      return PosixFile.for(this.nativePath.split("\\").slice(0, 4).join("\\"))
    }
    return map(bestMountpoint(this.nativePath, await mountpoints()), ea =>
      PosixFile.for(ea)
    )
  }

  /**
   * @return true iff this file or directory doesn't exist, and the mountpoint
   * is currently mounted, or some parent directory exists.
   */
  async isDeletedUri(_uri?: URI | string): PromiseMaybe<boolean> {
    if (await this.exists({ refresh: true })) return false // NOT deleted

    if (this.isUNC) {
      return this.pflog().tap({
        result: await this.isDeleted(),
        msg: "isDeletedUri(): is UNC, delegating to isDeleted()"
      })
    }

    const uri = map(
      notBlankOr(_uri, () => this.uri_() as any),
      toURI
    )

    if (uri == null) {
      return this.pflog().tap({
        result: await this.isDeleted(),
        level: "warn",
        msg: "isDeletedUri(): missing URI, delegating to isDeleted()"
      })
    }

    if (uri.isRootPath()) {
      // don't descend any farther, we're at (least a prior) mountpoint
      return this.pflog().tap({
        result: await this.notExists(),
        msg: "isDeletedUri(): uri isRootPath",
        meta: { uri }
      })
    }

    // Is the uri relevant to this path?

    if (toS(uri.pathBase).normalize() !== this.base.normalize()) {
      return this.pflog().tap({
        level: "warn",
        result: await this.notExists(),
        msg: "isDeleted(): uri isn't correct, returning notExists()",
        meta: { uri, expectedBase: this.base, uriBase: uri.pathBase }
      })
    }

    // C:\goo\bar\file.jpg
    // /opt/library/file.jpg

    // NOTE: we're counting slashes in the _URI_, not the native path, because
    // that goes to the mountpoint.

    // pslib:/dir/file.jpg
    // psfile:/UUID/dir/file.jpg
    // psnet:/server/share/dir/file.jpg

    const schemeDiscount =
      uri.scheme === PS_LIBRARY_SCHEME
        ? 1
        : uri.scheme === PS_LOCAL_FILE_SCHEME
        ? 2
        : uri.scheme === PS_NETWORK_FILESYSTEM_SCHEME
        ? 3
        : 1

    const maxDepth = countChars(uri.path, "/") - schemeDiscount

    // we don't need to log.tap this result: BaseFile will already log it.
    return this.isDeleted(maxDepth)
  }

  readonly etag = lazy(() =>
    thenMap(this.stat(), s =>
      [s.size, s.mtime.getTime()].map(n => Radix58.encode(n)).join("-")
    )
  )

  async httpHeaders(): Promise<StringValued> {
    return {
      ETag: await this.etag(),
      "Last-Modified": await this.lastModifiedUtc()
    }
  }

  hide(): MaybePromiseMaybe<this> {
    if (!isWin && !isMac) return
    return this.trap("hide", async () => {
      await hide_(this)
      return this
    })
  }

  /**
   * @throws on error
   */
  async mkNoMedia_() {
    return mkNoMedia_(this)
  }

  /**
   * @return this, not the .NoMedia file, so the method can be chained.
   */

  async mkNoMedia(): PromiseMaybe<this> {
    try {
      await this.mkNoMedia_()
      return this
    } catch (err) {
      this.pflog().warn("Could not add .NoMedia file to " + this, err)
      return undefined
    }
  }

  hasNoMedia(): PromiseMaybe<boolean> {
    return hasNoMedia(this)
  }

  hasNoMediaRecursive(): PromiseMaybe<boolean> {
    return hasNoMediaRecursive(this)
  }

  async hidden() {
    return isHidden(this)
  }

  readonly isMountpoint = lazy(
    async () =>
      (await this.isDirectory()) &&
      (await thenMapOr(
        mountpoints(),
        arr => arr.includes(this.nativePath),
        () => false
      ))
  )

  isSidecar() {
    return isSidecarExt(this.ext)
  }

  /**
   * @return all JSON and non-JSON sidecars
   */
  readonly existingSidecars = lazy(() => existingSidecars(this))

  /**
   * Returns non-JSON sidecars that ExifTool can read (.xmp, .mie, ...)
   *
   * @return oldest sidecars first (so newest sidecar metadata wins, as they are
   * layered on top of eachother)
   */
  async existingExifSidecars() {
    return (await this.existingSidecars()).filter(ea => !isJsonExt(ea))
  }

  /**
   * Should only be used for writes
   */
  async sidecar() {
    const existing = await this.existingExifSidecars()
    return (
      existing.find(ea => equalsIgnoreCase(ea.ext, defaultSidecarExt())) ??
      this.defaultSidecar()
    )
  }

  defaultSidecar() {
    return this.sibling(this.base + defaultSidecarExt())
  }

  async jsonSidecars() {
    return (await this.existingSidecars()).filter(ea => isJsonExt(ea))
  }

  async sidecareStats(opts?: RefreshArgs): Promise<fs.Stats[]> {
    const refresh = opts?.refresh ?? false
    if (refresh) this.existingSidecars.unset()
    return compact(
      await Promise.all(
        [this, ...(await this.existingSidecars())].map(ea => ea.stat(opts))
      )
    )
  }

  async thisOrSidecareMaxMtimeMs(opts?: RefreshArgs) {
    const ss = await this.sidecareStats(opts)
    return max(ss.map(ea => ea.mtimeMs))
  }

  /**
   * @throws so when readables encounter errors in the midst of reading, they
   * can propagate the error to the caller
   */
  async writeStream_(
    readable: stream.Readable,
    options?: WriteOptions & { onProgress?: (bytes: number) => any }
  ): PromiseMaybe<this> {
    await pipeline(
      compact([
        readable,
        map(options?.onProgress, f => new ByteCounter(f)),
        _fse.createWriteStream(this.nativePath, {
          autoClose: true,
          ...omit(options, "onProgress")
        })
      ])
    )
    readable.destroy()
    return this.clearThisAndParent()
  }

  /**
   * @param f_ must throw an error on failure, or we'll wait for a minute for
   * the command to finish (!!)
   */
  async applyIfEmpty_({
    fn_,
    minSizeBytes = 0,
    returnUndefinedIfNotEmpty = false,
    timeoutMs = 2 * minuteMs,
    skipWip = false,
    skipFsLock = false,
    retries = 2,
    dirty = false
  }: {
    fn_: (destination: PosixFile) => Promise<any>
    minSizeBytes?: number
    returnUndefinedIfNotEmpty?: boolean
    timeoutMs?: number
    skipWip?: boolean
    skipFsLock?: boolean
    retries?: number
    dirty?: boolean
  }): PromiseMaybe<this> {
    return retryOnReject(
      async () => {
        const initiallyNotEmpty = await this.clear().isNonEmptyFile(
          minSizeBytes
        )

        if (!initiallyNotEmpty) {
          if (skipWip && skipFsLock) {
            return fn_(this)
          } else {
            await this.applyWip_({
              fn_: async dest => {
                if (await this.clear().isNonEmptyFile(minSizeBytes)) {
                  // someone got a file lock before us and did the work!
                  return NoOp
                } else {
                  return fn_(dest)
                }
              },
              skipFsLock,
              minSizeBytes,
              timeoutMs,
              dirty
            })
          }
        }

        return initiallyNotEmpty && returnUndefinedIfNotEmpty
          ? undefined
          : this.utimes()
      },
      {
        maxRetries: retries,
        timeoutMs: timeoutMs * 2,
        retryDelay: 3 * secondMs,
        errorIsRetriable: e => errorCode(e) === "EBUSY"
      }
    )
  }

  /**
   * @returns a hidden file .wip file
   */
  wip(): this {
    return this.sibling(ensurePrefix(this.base, WipPrefix))
  }

  isWip(): boolean {
    return this.name.startsWith(WipPrefix)
  }

  async wipIsRecent(staleMs = WipTimeoutMs): Promise<boolean> {
    return (
      (await this.wip().isFile()) &&
      gte(await this.wip().clear().maxStatMs(), Date.now() - staleMs)
    )
  }

  unwipBase() {
    return stripPrefix(this.base, WipPrefix)
  }

  /**
   * If this is a WIP, rename to the "source" file (by removing the WipToken
   * (".WIP-") prefix
   */
  unwip_(): Promise<this> {
    return this.mv_(this.sibling(this.unwipBase()))
  }

  async applyWip_<T>({
    fn_,
    minSizeBytes = 0,
    skipFsLock = false,
    timeoutMs,
    dirty = false
  }: {
    fn_: (destination: PosixFile) => Promise<T>
    timeoutMs: number
    skipFsLock: boolean
    minSizeBytes?: number
    dirty?: boolean
  }): PromiseMaybe<T> {
    await this.parent().mkdirp_()
    return withLock_(
      {
        file: this,
        skipFsLock,
        timeoutMs,
        dirty
      },
      async () => {
        const wip = this.wip()
        try {
          // NOTE: don't touch() WIP file, ExifTool's no-clobber policy will be
          // grumpy!
          await wip.unlink("trace") // < should be a no-op

          const result = await thenOrTimeoutError(fn_(wip), timeoutMs)

          if ((result as any) === NoOp) return result

          const isNonEmpty = await untilTrue(
            () => wip.clear().isNonEmptyFile(minSizeBytes),
            {
              timeoutMs: ShortCommandTimeoutMs,
              intervalMs: 250 // short interval because it's just a stat()
            }
          )

          if (isNonEmpty) {
            await wip.mv_(this) // throws errors if issues
            return result
          } else {
            throw new Error(
              this +
                "applyWip(): still empty after waiting " +
                fmtDuration(ShortCommandTimeoutMs)
            )
          }
        } catch (err) {
          this.pflog().warn("applyWip() threw error", err)
          await wip.unlink("trace")
          await this.unlink("trace")
          throw err
        }
      }
    )
  }

  /**
   * Atomically overwrite `destOrDestDir.join(paths)` with the contents of
   * `this`. Use `.ensureNew` on destOrDestDir if you want to prevent
   * overwriting.
   *
   * If the result of joining `destOrDestDir` and `paths` results in an
   * extensionless name, the path will be assumed to be a containing directory,
   * and this name will be appended to the result.
   *
   * @throws if there are any errors
   */
  copyFile_(destFileOrDir: this): Promise<this> {
    return time("fs.copyFile", async () => {
      const dest = this.dest(destFileOrDir)
      if (this.nativePath === dest.nativePath) {
        return this
      }

      if (Settings.onlyNativeFileCopy.valueOrDefault !== true)
        try {
          return await this.#copyFile_(dest)
        } catch (error) {
          if (isNonRetriableError(error) === true) {
            return this.pflog().throw(
              "_copyFile failed, but error wasn't retriable",
              {
                error,
                dest: dest.nativePath,
                src: this.nativePath
              }
            )
          }

          this.pflog().warn("_copyFile failed, trying _nativeCopyFile", {
            dest: dest.nativePath,
            src: this.nativePath,
            error
          })
        }

      return await this.#nativeCopyFile_(dest)
    })
  }

  async maybeVerifySameOrThrow_(dest: this) {
    const start = Date.now()
    if (Settings.verifyFileCopies.valueOrDefault) {
      // we don't need to refresh this.sha().
      if (
        await untilTrue(() => this.matchesContent(dest), {
          timeoutMs: commandTimeoutMs(),
          intervalMs: secondMs
        })
      ) {
        // YAY COPY WORKED
      } else {
        this.pflog().throw(
          "copyFile_() failed (contents did not match after copy)",
          { dest, elapsedMs: Date.now() - start }
        )
      }
    }
  }

  async #copyFile_(dest: this): Promise<this> {
    let obs: Maybe<PullProgressObserver>
    let result = dest
    const copyDest = dest.wip()
    try {
      const s = await this.stat_()
      if (s == null) {
        return this.pflog().throw(
          "Can't copy missing files" + NonRetriableErrorFlag
        )
      }
      if (s.size > 0) {
        // skip copy for empty files
        if (Settings.verifyFileCopies.valueOrDefault) {
          if ((await this.sha()) == null) {
            return this.pflog().throw(
              "Can't copy file without SHA" + NonRetriableErrorFlag
            )
          }
        }
        const mkdir = await copyDest.parent().mkdirp()
        if (mkdir == null)
          return this.pflog().throw("Cannot mkdirp " + dest.dir)

        await fsp.copyFile(
          this.nativePath,
          copyDest.nativePath,
          // COPYFILE_FICLONE: copy operation will attempt to create a
          // copy-on-write reflink. If the underlying platform does not
          // support copy-on-write, then a fallback copy mechanism is used.
          fs.constants.COPYFILE_FICLONE
        )
        if (s.size > 5 * MiB) {
          obs = new PullProgressObserver(
            {
              op: "Copying",
              path: this.nativePath,
              dest: dest.nativePath
            } as any,
            s.size,
            () => copyDest.size({ refresh: true })
          )
        }
        const sizeMatches = await untilTrue(
          async () =>
            approximates(s.size, await copyDest.size({ refresh: true })),
          // it was already supposed to be done, so waiting a minute is really
          // just to wait for sync (which might take ages!):
          { intervalMs: secondMs, timeoutMs: minuteMs }
        )
        if (!sizeMatches) {
          return this.pflog().throw("copyFile_() failed", {
            expectedSize: s.size,
            actualSize: await copyDest.size({ refresh: true })
          })
        }

        await this.maybeVerifySameOrThrow_(copyDest)

        result = dest.eql(copyDest)
          ? dest
          : await copyDest.mv_(dest, LogLevels.trace)
      }
      try {
        // this doesn't need to fail the whole operation:
        await touch_({
          file: result,
          btimeMs: s.birthtimeMs,
          mtimeMs: s.mtimeMs,
          ensureFile: false
        })
      } catch (err) {
        this.pflog().warn(
          `copyFile_(${result.nativePath}): couldn't set utimes to match source: ${err}`
        )
      }
      try {
        // this doesn't need to fail the whole operation:
        await _fse.chmod(result.nativePath, s.mode)
      } catch (err) {
        this.pflog().warn(
          `copyFile_(${result.nativePath}): couldn't chmod to ${s.mode}: ${err}`
        )
      }
      this.pflog().debug(`copyFile_(${result.nativePath}): success`)
      return result
    } catch (err) {
      this.pflog().warn(`copyFile_(${copyDest?.nativePath}) failed: ${err}`)
      await copyDest.unlink()
      if (!copyDest.eql(dest)) {
        await dest.unlink()
      }
      throw err
    } finally {
      result?.clearThisAndParent()
      void map(obs, ea => ea.end())
    }
  }

  async zCopyFile_(dest: this, options?: { start?: number; end?: number }) {
    await dest.parent().mkdirp_()
    await zpipe_(
      this.nativePath,
      fs.createWriteStream(dest.nativePath, { autoClose: true }),
      options
    )
    return dest.clear()
  }

  async copyTimeoutMs(): Promise<number> {
    return clamp(
      ShortCommandTimeoutMs, // don't go shorter than this!
      10 * minuteMs,
      ((await this.size()) ?? 0) * MinIoRate
    )
  }

  async #nativeCopyFile_(dest: this): Promise<this> {
    let obs: Maybe<PullProgressObserver>
    const wipDest = dest.wip()

    try {
      if (null == (await dest.parent().mkdirp())) {
        return this.pflog().throw("Can't mkdir destination directory", {
          src: this.nativePath,
          dest: dest.nativePath
        })
      }
      const s = await this.stat_()
      const size = s?.size
      if (s == null || size == null) {
        return this.pflog().throw("Can't copy missing files")
      }
      if (size > 5 * MiB) {
        obs = new PullProgressObserver(
          {
            op: "Copying",
            path: this.nativePath,
            dest: wipDest.nativePath
          } as any,
          size,
          () => dest.clear().size()
        )
      }

      if (isWin) {
        await PowerShell.instance().execute(
          `Copy-Item -LiteralPath ${pwshQuote(
            this.nativePath
          )} -Destination ${pwshQuote(wipDest.nativePath)}`,
          ea => ea
        )
      } else if (isMac) {
        await stdout_("ditto", [this.nativePath, wipDest.nativePath], {
          timeoutMs: await this.copyTimeoutMs()
        })
      } else {
        // -a means archive, or -pPR on macOS or -dR --preserve=all on debian
        // -f means force. If existing dest cannot be opened, remove it and try again.
        await stdout_("cp", ["-a", "-f", this.nativePath, wipDest.nativePath], {
          timeoutMs: await this.copyTimeoutMs()
        })
      }
      await this.maybeVerifySameOrThrow_(wipDest)
      await wipDest.mv_(dest, LogLevels.trace)
      await touch_({ file: dest, reference: this, ensureFile: false })
      return dest.clearThisAndParent()
    } catch (error) {
      await wipDest.unlink()
      return this.pflog().throw(
        "_nativeCopyFile(" + dest + ") failed" + DoNotSendErrorFlag,
        { error }
      )
    } finally {
      void map(obs, ea => ea.end())
    }
  }

  dest(destOrDestDir: this): this {
    const destOrDir = destOrDestDir.clear() // < bust cache
    return (notBlank(this.ext) && blank(destOrDir.ext)) ||
      destOrDir.isDirectorySync()
      ? destOrDir.join(this.base)
      : destOrDir
  }

  /**
   * @throws if errors
   */
  async renameYMDHMS_(
    args: { subdir?: string; ts?: number } = {}
  ): Promise<this> {
    if (await this.clear().notExists()) {
      throw new Error("Cannot rename: " + this + " doesn't exist.")
    }
    const ts = fmtYMDHMS(args.ts ?? (await this.mtime()) ?? Date.now())
    const destDir = mapOr(
      args.subdir,
      ea => this.parent().join(ea),
      () => this.parent()
    )
    return this.mv_(destDir.join(this.name + "-" + ts + this.ext))
  }

  /**
   * @throws on error
   */
  async renameWithNameSuffix_(nameSuffix: string): PromiseMaybe<this> {
    return thenMap(
      this.withNameSuffix(nameSuffix).ensureNew_({ emptyIsNew: true }),
      dest => dest.unlink("debug").then(() => this.mv_(dest))
    )
  }

  /**
   * If `this` exists and has new contents for this current directory, rename it
   * to a new file with the given base (and possibly a count, to ensure new
   * overwriting).
   *
   * If `this` is empty, or already has the same contents as another file in
   * this directory, return that file and unlink this file.
   */
  async saveIfNewOrDelete(base: string): PromiseMaybe<this> {
    if (await this.clear().isEmpty()) {
      // don't complain about removing a missing file:
      await this.unlink("trace")
      return
    }
    const prior = await this.siblingWithSameContents()
    if (prior != null) {
      await this.unlink()
      return prior
    }
    const dest = await this.sibling(base).ensureNew_()
    return this.mv_(dest)
  }

  /**
   * SITS: Mac sometimes locks files when they've been copied. No, I couldn't
   * find out why, but if I own the file, I can chflags the file back to being
   * unlocked.
   *
   * This should only be applied to newly-copied files, and not for files that
   * PhotoStructure didn't create.
   */
  readonly unlock = lazy(() =>
    !isMac
      ? undefined
      : stdout_("chflags", ["nouchg", this.nativePath], {
          quiet: true,
          timeoutMs: ShortCommandTimeoutMs
        })
  )

  /**
   * Move this to `destFileOrDir`. If dest exists, it will be overwritten.
   *
   * @return the file that was copied into
   * @throws error if there are issues
   */
  async mv_(
    destFileOrDir: this | string,
    level: LogLevel = LogLevels.debug
  ): Promise<this> {
    const dest = isString(destFileOrDir)
      ? this.parent().join(destFileOrDir)
      : this.dest(destFileOrDir)
    if (this.nativePath === dest.nativePath) {
      this.pflog().warn("mv(): no-op", new Error("internal error"))
      return this
    }
    if (this.dir !== dest.dir) {
      await dest.parent().mkdirp_()
    }
    this.pflog().log(level, "mv_()", { dest })
    try {
      // windows can throw EBUSY:
      await move_(this.nativePath, dest.nativePath)
    } catch (err) {
      this.pflog().warn("mv() failed. Calling unlock() and retrying...", err)
      await Promise.all([this.unlock(), dest.unlock()])
      await move_(this.nativePath, dest.nativePath)
    }
    if (this.dir !== dest.dir) this.clearThisAndParent()
    return dest.clearThisAndParent()
  }

  /**
   * @return true if the provided URI is equivalent to this
   */
  async equalsUri_(uri: string | URI): Promise<boolean> {
    return uriIsEquivalent(await this.uriObject_(), uri)
  }
}
