import fs from "fs"
import { resolve } from "path"
import { blank, notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { isRecentMs } from "../date/Date"
import { access } from "./Access"
import { posix2native } from "./PosixNative"

export function statSync(nativePath: Maybe<string>) {
  if (blank(nativePath)) return
  try {
    return fs.statSync(nativePath, { throwIfNoEntry: false })
  } catch {
    // only will throw on older node.js versions:
    return
  }
}

export function nativePathExistsSync(nativePath: Maybe<string>) {
  if (blank(nativePath)) return false
  try {
    return fs.existsSync(nativePath)
  } catch {
    return false
  }
}

export function nativePathSizeSync(nativePath: Maybe<string>): Maybe<number> {
  return statSync(nativePath)?.size
}

export function isFileSync(nativePath: string, minSize = 0): boolean {
  const s = statSync(nativePath)
  return s != null && s.isFile() && s.size >= minSize
}

export function isEmptyFileSync(
  nativePath: string,
  opts: { emptyIsNew: boolean } = { emptyIsNew: true }
) {
  const s = statSync(nativePath)
  return opts.emptyIsNew ? s == null : isEmptyStats(s)
}

export function isMtimeRecentSync(
  nativePath: Maybe<string>,
  staleMs: number
): boolean {
  return isRecentMs(statSync(nativePath)?.mtimeMs, staleMs)
}

export function isEmptyStats(s: Maybe<fs.Stats>) {
  return s == null || (s.isFile() && s.size === 0)
}

export function isDirectorySync(nativePath: Maybe<string>): boolean {
  return statSync(nativePath)?.isDirectory() === true
}

export function isReadableDirectorySync(nativePath: Maybe<string>): boolean {
  const stat = statSync(nativePath)
  return (
    stat != null && stat.isDirectory() && access({ stat, r: true, x: true })
  )
}

export function isReadWriteableDirectorySync(
  nativePath: Maybe<string>
): boolean {
  if (blank(nativePath)) return false
  const stat = statSync(nativePath)
  return (
    stat != null &&
    stat.isDirectory() &&
    access({ stat, r: true, w: true, x: true })
  )
}

export function firstExistingDirectory(paths: Maybe<string>[]): Maybe<string> {
  for (const ea of paths) {
    if (notBlank(ea)) {
      const d = resolve(ea)
      if (isDirectorySync(d)) return d
    }
  }
  return undefined
}

export function posixPathExistsSync(posixPath: Maybe<string>) {
  return blank(posixPath)
    ? false
    : nativePathExistsSync(posix2native(posixPath))
}
