import { filterInPlace } from "../../fe/Array"
import { getOrSet } from "../../fe/Map"
import { map } from "../../fe/Maybe"
import { toS } from "../../fe/toS"
import { Args } from "../Args"
import { Deferred } from "../async/Deferred"
import { BoundedList } from "../BoundedList"
import { isTest } from "../NodeEnv"
import { OmniEventListener } from "./OmniEventListener"
import { TypedEventEmitter } from "./TypedEventEmitter"

export interface CapturedEvent<T, E extends keyof T = keyof T> {
  name: E
  args: Args<T[E]>
}

export class CapturingEventEmitter<T> implements TypedEventEmitter<T> {
  readonly priorEvents: BoundedList<CapturedEvent<T>>
  readonly omniListeners: OmniEventListener<T>[] = []
  readonly #eventDeferreds = new Map<keyof T, Deferred<any>>()
  readonly #eventDebounceTimers = new Map<keyof T, NodeJS.Timeout>()

  constructor(
    readonly target: TypedEventEmitter<T>,
    readonly eventsToRetain = isTest ? 10 : 0
  ) {
    this.priorEvents = new BoundedList(eventsToRetain)
  }

  once<E extends keyof T>(
    eventName: E,
    listener: (...args: Args<T[E]>) => void
  ) {
    this.target.once(eventName, listener)
    return this
  }

  // CAUTION: an omniEventListener that logs may cause a stack overflow on errors!

  addOmniListener(l: OmniEventListener<T>) {
    this.omniListeners.push(l)
    return this
  }

  watchEvent<E extends keyof T>(eventName: E): Deferred<Args<T[E]>> {
    return getOrSet(
      this.#eventDeferreds,
      eventName,
      () => new Deferred<any>("watchEvent(" + toS(eventName) + ")")
    )
  }

  removeOmniListener(l: OmniEventListener<T>) {
    return filterInPlace(this.omniListeners, ea => ea !== l)
  }

  on<E extends keyof T>(eventName: E, listener: (...args: Args<T[E]>) => void) {
    this.target.on(eventName, listener)
    return this
  }

  off<E extends keyof T>(
    eventName: E,
    listener: (...args: Args<T[E]>) => void
  ) {
    this.target.off(eventName, listener)
    return this
  }

  /**
   * @return `true` if the event had listeners, `false` otherwise.
   */
  emit<E extends keyof T>(eventName: E, ...args: Args<T[E]>) {
    map(this.#eventDebounceTimers.get(eventName), clearTimeout)
    this.#eventDebounceTimers.delete(eventName)
    for (const ea of this.omniListeners) {
      ea(eventName, ...args)
    }
    if (this.eventsToRetain > 0) {
      this.priorEvents.push({
        name: eventName,
        args
      })
    }
    const result = this.target.emit(eventName, ...args)

    const d = this.#eventDeferreds.get(eventName)
    if (d != null) {
      void d.resolve(args)
      this.#eventDeferreds.delete(eventName)
    }

    return result
  }

  emitDebounced<E extends keyof T>(eventName: E, ...args: Args<T[E]>) {
    map(this.#eventDebounceTimers.get(eventName), clearTimeout)
    this.#eventDebounceTimers.set(
      eventName,
      setTimeout(() => this.emit(eventName, ...args), 50)
    )
  }

  // eslint-disable-next-line @typescript-eslint/ban-types
  listeners<E extends keyof T>(event: E): Function[] {
    return this.target.listeners(event)
  }

  removeAllListeners(eventName?: keyof T): this {
    this.target.removeAllListeners(eventName)
    return this
  }
}
