import timers from "timers"
import { gt0 } from "../../fe/Number"

export interface Debounced {
  (...args: any[]): any
  reset(): void
  force(): void
  setTimeoutMs(timeoutMs: number): void
  timeoutMs(): number
}

export function debounce(
  f: (...args: any[]) => any,
  timeoutMs: number
): Debounced {
  let lastTimeout: any
  let args: any[] = []

  const r: any = gt0(timeoutMs)
    ? (...a: any[]) => {
        args = a
        if (lastTimeout != null) timers.clearTimeout(lastTimeout)
        lastTimeout = timers.setTimeout(() => {
          f(...args)
        }, timeoutMs)
      }
    : f
  r.reset = () => {
    if (lastTimeout != null) timers.clearTimeout(lastTimeout)
    lastTimeout = undefined
  }
  r.force = () => {
    r.reset()
    f()
  }
  r.setTimeoutMs = (ea: number) => {
    r.reset()
    timeoutMs = ea
  }
  r.timeoutMs = () => timeoutMs
  return r
}
