import { lazy } from "../../core/Lazy"
import { isEmpty, isNotEmpty } from "../../fe/Array"
import { sort } from "../../fe/ArraySort"
import { minuteMs, secondMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { Maybe } from "../../fe/MaybeTypes"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { rateLimited } from "../RateLimited"
import { isSyncService } from "../ServiceNames"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { includesIgnoreCase } from "../String"
import { end } from "../async/Endable"
import { awaitAll, filterAsync } from "../async/Promise"
import { time } from "../async/PromiseTimer"
import { stdoutResult_ } from "../child/ChildProcess"
import { mkBasicWatchedChild } from "../child/WatchedChild"
import { onError } from "../error/OnError"
import { broadcastEvent_ } from "../event/BroadcastEvent"
import { ee } from "../event/EventEmitter"
import { SimpleFileOrString, toNativePath_ } from "../fs/SimpleFile"
import { isReadableDirectory } from "../fs/Stat"
import { isLinux, isMac, isWin } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { mightBeAssetMountpoint } from "./AssetMountpoint"
import {
  GioCommand,
  GioMountMonitorArgs,
  gioVolumes,
  isGioSupported
} from "./Gio"
import { maybeWatchProcMounts, mountpointsPosix } from "./MountpointsPosix"
import { mountpointsWin } from "./MountpointsWin"
import { isExcludedMountpoint } from "./PosixVolumes"
import {
  commandTimeoutMs,
  mountpointsTtlMs,
  volumeMetadataTtlMs
} from "./VolumeTtls"

const logger = lazy(() => mkLogger("volumes.Mountpoints"))

function unsetMountpoints() {
  mountpoints.unset()
  mountpoints_.unset()
}

function setMountpointsTTL(ttl: number = mountpointsTtlMs()) {
  mountpoints.setTTL(ttl)
  mountpoints_.setTTL(ttl)
}

export const localMountpointSetup = lazy(async () => {
  // These may be excluded paths:
  Settings.libraryDir.watchLater(unsetMountpoints)
  Settings.scanPaths.watchLater(unsetMountpoints)

  // Only do mountpoint monitoring by sync (which cares)
  if (isSyncService()) {
    later(async () => {
      const log = mkLogger("Mountpoints.localMountpointSetup()")
      if (isMac) {
        log.info("Setting up Mac diskutil activity watcher")
        setMountpointsTTL(volumeMetadataTtlMs())
        diskUtilActivity()
      }
      if (isLinux) {
        await maybeWatchProcMounts()
        if (await isGioSupported()) {
          log.info("Setting up Linux gio mount monitor")
          setMountpointsTTL(volumeMetadataTtlMs())
          gioMountMonitor()
        }
        if (await isFindmntSupported()) {
          log.info("Setting up Linux findmnt mount monitor")
          setMountpointsTTL(volumeMetadataTtlMs())
          findmntPoll()
        }
      }
    }, 30 * secondMs) // let startup go faster. Mountpoint monitoring can wait.
  } else {
    await awaitAll([
      end(diskUtilActivity.clear()),
      end(gioMountMonitor.clear()),
      end(findmntPoll.clear())
    ])
  }
})

async function onMountpointsChanged() {
  unsetMountpoints()
  if (isSyncService()) {
    await broadcastEvent_("mountpointsChanged")
  }
}

// Stores the last successful mountpoints() call. No TTL needed.
const priorMountpoints = lazy<Maybe<string[]>>(() => undefined)

function mountpointsImpl() {
  return time("volumes.Mountpoints", isWin ? mountpointsWin : mountpointsPosix)
}

export const mountpoints_ = lazy(async () => {
  {
    const arr = Settings.mountpoints.values
    if (isNotEmpty(arr)) {
      return arr
    }
  }
  const arr = await thenOrTimeoutError(mountpointsImpl(), ShortCommandTimeoutMs)
  if (isEmpty(arr)) {
    return logger().throw("mountpointsImpl() returned empty.", { fatal: false })
  } else {
    const result = sort(
      await filterAsync({
        arr,
        name: "mountpointFilter",
        timeoutMs: commandTimeoutMs(),
        f: async ea => {
          if (!mightBeAssetMountpoint(ea) && isExcludedMountpoint(ea))
            return false
          if (Settings.validateMountpoints.valueOrDefault) {
            if (!(await isReadableDirectory(ea))) {
              logger().warn(`mountpoint ${ea} is not readable`)
              return false
            }
          }
          return true
        }
      })
    )
    priorMountpoints.set(result)
    return result
  }
})

export const mountpoints = lazy(async () => {
  try {
    return await mountpoints_()
  } catch (cause) {
    onError("mountpoints() failed", { cause })
    return priorMountpoints()
  }
})

export async function isPosixMountpoint(
  d: SimpleFileOrString
): Promise<boolean> {
  if (isWin) return false
  const arr = (await mountpoints()) ?? []
  const np = toNativePath_(d)
  return isMac // case insensitive, YAY
    ? includesIgnoreCase(arr, np)
    : arr.includes(np)
}

// we don't delete the priorMountpoints because that's a safety backstop
later(
  () => {
    ee().on("clearCache", () => {
      gioVolumes.unset()
      unsetMountpoints()
    })
    Settings.mountpointsTtlMs.watch(setMountpointsTTL)
  },
  isTest ? 1 : minuteMs // < wait until settings have been read
)

/**
 * Provides near-real-time updates when volumes change on macs:
 */
export const diskUtilActivity = lazy(() =>
  mkBasicWatchedChild({
    cmd: "diskutil",
    args: ["activity"],
    onStdout: rateLimited({
      name: "diskUtilActivity",
      f: () => onMountpointsChanged(),
      minCallDelayMs: 1.5 * secondMs
    })
  })
)

export const gioMountMonitor = lazy(() =>
  mkBasicWatchedChild({
    cmd: GioCommand,
    args: GioMountMonitorArgs,
    onStdout: rateLimited({
      name: "gioMountMonitor",
      f: () => {
        gioVolumes.unset()
        return onMountpointsChanged()
      },
      minCallDelayMs: ShortCommandTimeoutMs
    })
  })
)

const isFindmntSupported = lazy(async () => {
  if (!isLinux) return false
  try {
    const result = await stdoutResult_("findmnt", ["--version"], {
      timeoutMs: commandTimeoutMs(),
      ignoreStderr: true
    })
    return result.code === 0
  } catch (err) {
    return false
  }
})

export const findmntPoll = lazy(() =>
  mkBasicWatchedChild({
    cmd: "findmnt",
    args: ["--poll"],
    onStdout: rateLimited({
      name: "findmntPoll",
      f: () => onMountpointsChanged(),
      minCallDelayMs: ShortCommandTimeoutMs
    })
  })
)
