import { Maybe } from "../../fe/MaybeTypes"
import {
  BaseSettingOpts,
  Setting,
  SettingConverters,
  SettingOpts
} from "./Setting"

export class OptionalStringSetting extends Setting<Maybe<string>> {
  constructor(
    opts: BaseSettingOpts<Maybe<string>> &
      Partial<SettingOpts<Maybe<string>>> &
      Partial<SettingConverters<string>>
  ) {
    super({
      toEnv: ea => ea?.trim(),
      fromEnv: ea => ea,
      defaultValue: undefined,
      ...opts
    })
  }

  hasValue() {
    return this.value != null
  }
}
