import { compact, toPrimitive } from "./Array"
import { copyArrayTo } from "./ArrayCopy"
import { isIterable } from "./Iterable"
import { stringify } from "./JSON"
import { getOrSet } from "./Map"
import { Defined, map } from "./Maybe"
import { Maybe } from "./MaybeTypes"
import {
  Comparable,
  Primitivable,
  Primitivables,
  Primitive,
  cmp
} from "./Primitive"
import { toA } from "./toA"

export function sort<
  T extends Primitive | Primitive[] | Primitivable | Primitivables
>(arr: Maybe<T>[] | ReadonlyArray<Maybe<T>>): Defined<T>[] {
  return sortByInPlace(compact(arr), toPrimitive)
}

export function sortByInPlace<T>(arr: T[], f: (t: T) => Comparable): T[] {
  return copyArrayTo(sortBy(arr, f), arr)
}

export function sortUniqBy<T>(arr: T[], f: (t: T) => Comparable): T[] {
  const m = new Map<string, T>()
  for (const ea of arr) {
    getOrSet(m, stringify(f(ea)), () => ea)
  }
  return sortBy(m.values(), f)
}

export function sortUniq<T extends Comparable>(arr: T[]): T[] {
  return sortUniqBy(arr, ea => ea)
}

export function sortUniqByInPlace<T>(arr: T[], f: (t: T) => Comparable): T[] {
  return copyArrayTo(sortUniqBy(arr, f), arr)
}

export function isSorted(arr: Primitivable[]): boolean {
  return arr.every((ea, idx) => idx === 0 || ea > arr[idx - 1])
}

export function sortedBy<T>(arr: T[], f: (t: T) => Primitive): boolean {
  return arr.every((ea, idx) => idx === 0 || f(ea) > f(arr[idx - 1]))
}
/**
 * Returns a copy of arr, stable sorted by the given constraint. Note that false
 * < true, and that `f` may return an array for sort priorities, or undefined if
 * the item should be skipped from the returned result.
 *
 * Note: localeSort() thinks lower case should come before upper case (!!)
 */

export function sortBy<T>(
  arr: Iterable<Maybe<T>> | Maybe<T>[],
  f: (t: T, index: number) => Maybe<Comparable | Comparable[]>
): T[] {
  return (toA(arr).filter(ea => ea != null) as T[])
    .map((item, idx) => ({
      item,
      cmp: map(f(item, idx), ea => [ea, idx])
    }))
    .filter(ea => ea.cmp != null)
    .sort((a, b) => cmp(a.cmp!, b.cmp!))
    .map(ea => ea.item)
}

export function deepSortBy<T, V extends Primitive | Primitive[]>(
  arr: Iterable<T> | T[],
  f: (t: T) => Maybe<V>
): T[] {
  return sortBy(arr, f).map(ea =>
    isIterable(ea) ? deepSortBy(ea, f) : ea
  ) as any // SITS typing
}

export function pushSorted<T>(
  arr: T[],
  item: T,
  f: (t: T) => Comparable | Comparable[]
): T[] {
  const idx = arr.findIndex(ea => f(item) < f(ea))
  if (idx === -1) {
    arr.push(item)
  } else {
    arr.splice(idx, 0, item)
  }
  return arr
}
