import { UnwrapLazy, lazy } from "../../core/Lazy"
import { compactBlanks, uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { stringify } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { entries, fromEntries } from "../../fe/Object"
import { SimpleAppName } from "../AppName"
import { serviceName } from "../ServiceNames"
import { stripAnsiEsc } from "../String"
import { errorToVerbose } from "../error/Error"
import { LogEntry } from "./LogEntry"
import { LogFormatter } from "./LogFormatter"
import { level2syslog } from "./LogLevel"
import { prepMeta } from "./LogMeta"

// https://docs.greylog.org/v1/docs/gelf#gelf-payload-specification
interface Payload {
  version: "1.1"
  host: string
  short_message: string
  full_message?: string
  timestamp: number
  // https://en.wikipedia.org/wiki/Syslog#Severity_level
  level: number
}

const defaultFrom = lazy<{ _service: string; _pid?: number }>(() => ({
  _service: serviceName(),
  _pid: process.pid
}))

later(() => serviceName.watchLater(() => defaultFrom.unset()))

function extractFrom(from?: Maybe<string>): UnwrapLazy<typeof defaultFrom> {
  if (from == null) {
    return defaultFrom()
  } else {
    const [_service, pidStr] = from.split("-", 2)

    return blank(_service) ? defaultFrom() : { _service, _pid: toInt(pidStr) }
  }
}

/**
 * @see https://docs.greylog.org/v1/docs/gelf
 */
export class GelfLogFormatter implements LogFormatter {
  formatLogEntry(logEntry: LogEntry): string {
    const payload: Payload = {
      version: "1.1",
      level: level2syslog(logEntry.l) ?? 7,
      host: SimpleAppName,
      timestamp: logEntry.ts / 1000,
      short_message: stripAnsiEsc(
        uniq(compactBlanks([logEntry.ctx, logEntry.msg])).join(" ")
      ),
      full_message: map(logEntry.meta?.error, errorToVerbose)
    }

    return stringify({
      ...payload,
      ...extractFrom(logEntry.from),
      ...fromEntries(
        entries(prepMeta(logEntry.meta, 3) ?? {}).map(([k, v]) =>
          k === "id" ? undefined : ["_" + k, stringify(v)]
        )
      )
    })
  }

  format(
    level: "fatal" | "error" | "warn" | "info" | "debug" | "trace",
    ctx: string,
    msg: string,
    meta?: any
  ): string {
    return this.formatLogEntry({
      ts: Date.now(),
      l: level,
      ctx,
      msg,
      meta
    })
  }
}
