import { notBlankToS } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toFloat } from "../../fe/Number"
import { BaseSettingOpts, DefaultValued, Setting } from "./Setting"

export class OptionalFloatSetting extends Setting<Maybe<number>> {
  constructor(
    opts: BaseSettingOpts<Maybe<number>> & Partial<DefaultValued<Maybe<number>>>
  ) {
    super({
      defaultValue: undefined,
      ...opts,
      toEnv: notBlankToS,
      fromEnv: toFloat
    })
  }
}
