import { lazy } from "../core/Lazy"
import { last, range } from "../fe/Array"
import { blank, notBlank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { max_ } from "../fe/Number"
import { fromEntries, values } from "../fe/Object"
import { mkLogger } from "./Logger"
import { escapeRegExp } from "./RegExp"
import { indexOfNonSpace, isString, lastIndexOfNonSpace } from "./String"
import { splitLines } from "./fs/CRLF"

export interface Header<T> {
  text: string & keyof T
  greedyLeft?: boolean
  greedyRight?: boolean
}

class IndexedHeader<T> {
  readonly text: string
  readonly greedyLeft: boolean
  indexOf?: number
  leftIdx?: number
  rightIdx?: number
  constructor(h: Header<T> | string) {
    this.text = isString(h) ? h : h.text
    this.greedyLeft = isString(h) ? false : h["greedyLeft"] ?? false
  }

  toEntry(row: string): [string, string] {
    return [this.text, row.substring(this.leftIdx!, this.rightIdx).trim()]
  }
}

const logger = lazy(() => mkLogger("Fixed"))

export type Headers<T> = (Header<T> | string)[]

/**
 * Parse fixed-width input into an array of objects
 */
export function parseFixed<T>(
  headers: Headers<T>,
  table: string,
  warnIfMissingHeaders = true
): T[] {
  return new Fixed(headers, table, warnIfMissingHeaders).entries
}

/**
 * Support for parsing fixed-width tabular data
 */
export class Fixed<T> {
  readonly headers: IndexedHeader<T>[]
  readonly headerRow: string
  readonly skippedHeaders: string[] = []
  readonly rows: string[] // doesn't include the header row!
  readonly entries: T[]
  readonly blankColumns: Set<number>

  constructor(
    headers: Headers<T>,
    table: string,
    readonly warnIfMissingHeaders = true
  ) {
    this.rows = splitLines(table)
    this.headerRow = this.rows.shift()!
    const maxLen = max_(...this.rows.map(ea => ea.length))
    this.blankColumns = new Set(
      range(0, maxLen).filter(idx => this.rows.every(ea => blank(ea[idx])))
    )

    this.headers = this.extractHeaders(headers.map(ea => new IndexedHeader(ea)))
    this.entries = this.rows
      .map(row => this.headers.map(h => h.toEntry(row)))
      .map(arr => fromEntries(arr))
      // Remove rows that have all blank values:
      .filter(row => values(row).some(notBlank))
  }

  /**
   * @param fromIdx inclusive
   * @param toIdx inclusive
   */
  private firstBlankColumn(fromIdx: number, toIdx: number): Maybe<number> {
    for (let idx = fromIdx; idx !== toIdx; toIdx > fromIdx ? idx++ : idx--) {
      if (this.blankColumns.has(idx)) return idx
    }
    if (this.blankColumns.has(toIdx)) return toIdx
    if (fromIdx === 0 || toIdx === 0) {
      return 0
    }
    return undefined
  }

  private extractHeaders(headers: IndexedHeader<T>[]): IndexedHeader<T>[] {
    // For every column, determine which header it belongs to.

    // First parse headers columns
    const re = new RegExp(
      headers
        .map(
          ea => (ea.greedyLeft ? "\\s+" : "\\b") + escapeRegExp(ea.text) + "\\b"
        )
        .join("|"),
      "ig"
    )
    const result: IndexedHeader<T>[] = []

    // basically String.splitKeep
    // we're looking for tokens at or right of pos
    let m: RegExpExecArray | null

    // this is the "re.lastIndex" from the prior match
    let priorLastIndex = 0

    while ((m = re.exec(this.headerRow)) != null) {
      const skippedOver = this.headerRow.substring(priorLastIndex, m.index)
      if (!blank(skippedOver)) {
        logger().debug("extractHeaders: skipping over unknown header", {
          skippedOver,
          m_index: m.index
        })
        this.skippedHeaders.push(skippedOver.trim())
      }

      const matched = this.headerRow.substring(m.index, re.lastIndex)
      // this is the leftmost non-whitespace index of the current header
      const left = m.index + (indexOfNonSpace(matched) ?? 0)
      // this is the rightmost non-whitespace index of the current header
      const right = m.index + (lastIndexOfNonSpace(matched) ?? matched.length)
      const text = matched.trim()
      const h = headers.find(ea => ea.text === text)
      if (h == null || left == null || right == null) {
        this.skippedHeaders.push(text)
        logger().debug("extractHeaders: internal error", {
          matched,
          match: m,
          left,
          right
        })
      } else {
        const prior = last(result)
        result.push(h)

        if (prior != null) {
          const priorRightBoundary =
            prior.rightIdx! +
            (indexOfNonSpace(skippedOver) ?? skippedOver.length) +
            1
          prior.rightIdx = h.greedyLeft
            ? this.firstBlankColumn(prior.rightIdx!, priorRightBoundary)
            : this.firstBlankColumn(priorRightBoundary, prior.rightIdx!)
        }
        const leftmost = max_(
          prior?.rightIdx,
          m.index - (lastIndexOfNonSpace(skippedOver) ?? skippedOver.length)
        )
        h.leftIdx = h.greedyLeft
          ? this.firstBlankColumn(leftmost, left)
          : this.firstBlankColumn(left, leftmost)
        h.rightIdx = right // < temporary!
      }
      priorLastIndex = re.lastIndex
    }
    // expand the right header... but how much?
    const lastHeader = last(result)
    if (lastHeader != null) {
      if (!blank(this.headerRow.slice(priorLastIndex))) {
        lastHeader.rightIdx = priorLastIndex
      } else {
        // If the remainder of the header is whitespace, just expand to the end:
        lastHeader.rightIdx = max_(...this.rows.map(ea => ea.length))
      }
    }
    return result
  }
}
