import { BatchCluster, Parser, Task } from "batch-cluster"
import { lazy } from "../../core/Lazy"
import { blank, mapNotBlankOr, notBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { map, orElse } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { Obj } from "../../fe/Object"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { BatchClusterObserver } from "../BatchClusterObserver"
import { thenElapsed } from "../Elapsed"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { ellipsize, ensureSuffix, stripPrefix } from "../String"
import { EndableRanks, ending } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { thenMap } from "../async/Promise"
import { until } from "../async/until"
import { execFile } from "../child/ChildProcess"
import { ee } from "../event/EventEmitter"
import { isWin } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { commandTimeoutMs } from "../volumes/VolumeTtls"

const Done = "{ready}"

const ConvertToCompressedJson = " | ConvertTo-Json -Compress"

export type Result = { stdout: string; stderr?: string; passed: boolean }

export interface PowerShellVersion {
  Major: number
  Minor: number
  Build: number
  Revision: number
  MajorRevision: number
  MinorRevision: number
}

export function pwshQuote(s: string): string {
  const s2 = s
    .replace(/['`"#]/g, ea => "`" + ea)
    .replace(/\0/g, "`0")
    .replace(/\n/g, "`n")
    .replace(/\r/g, "`r")
    .replace(/\t/g, "`t")
    .replace(/\v/g, "`v")
  return '"' + s2 + '"'
}

function versionCommand() {
  return [
    `function prompt {"${Done}"}`,
    ...mapNotBlankOr(
      Settings.powerShellCulture.valueOrDefault,
      ea => [
        `[System.Threading.Thread]::CurrentThread.CurrentCulture = '${ea}'`,
        `[System.Threading.Thread]::CurrentThread.CurrentUICulture = '${ea}'`
      ],
      []
    )
  ].join(";")
}

later(() =>
  ee().on("clearCache", () => PowerShell.instance.prior()?.clearMockResults())
)

export class PowerShell extends EndableWrapper {
  static ensureInstance() {
    const prior = this.instance()
    return prior.ended ? this.instance.refresh() : prior
  }
  static readonly instance = lazy(() => {
    if (!isWin) throw new Error("PowerShell isn't available on this platform")
    return new PowerShell()
  })
  private readonly bco: BatchClusterObserver<BatchCluster>
  private readonly pwsh: BatchCluster
  private readonly mockResults = new Map<string, Result>()

  // always access through PowerShell.instance():
  private constructor() {
    super("PowerShell", () => this.bco.end(), EndableRanks.postdb)
    this.bco = new BatchClusterObserver(
      "PowerShell",
      new BatchCluster({
        processFactory: () =>
          execFile("powershell", Settings.powerShellArgs.values),
        logger: () => mkLogger("PowerShell"),
        versionCommand: versionCommand(),
        pass: Done,
        fail: "Error",
        exitCommand: "exit",
        maxProcs: Settings.powerShellProcs.valueOrDefault,
        taskTimeoutMillis: commandTimeoutMs(),

        maxIdleMsPerProcess: minuteMs,
        cleanupChildProcs: false // we do this with the Pids class.
      }),
      EndableRanks.postdb // last to shut down.
    )
    Settings.powerShellProcs.watch(() =>
      this.bco.t.setMaxProcs(Settings.powerShellProcs.valueOrDefault)
    )
    this.pwsh = this.bco.t
  }

  get lastStartError() {
    return this.bco.lastStartError
  }

  get lastTaskError() {
    return this.bco.lastTaskError
  }

  get ended() {
    return this.pwsh.ended
  }

  versionPojo(): PromiseMaybe<PowerShellVersion> {
    return this.executeJson("$PSVersionTable.PSVersion")
  }

  version(): PromiseMaybe<string> {
    return thenMap(
      this.executeJson("$PSVersionTable.PSVersion"),
      ea => `${ea.Major}.${ea.Minor}.${ea.Build}`
    )
  }

  get spawnedProcCount() {
    return this.pwsh.spawnedProcCount
  }

  pushMockJsonResult(cmd: string, result: Result) {
    this.pushMockResult(ensureSuffix(cmd, ConvertToCompressedJson), result)
  }

  pushMockResult(cmd: string, result: Result) {
    this.mockResults.set(cmd, result)
  }

  clearMockResults() {
    this.mockResults.clear()
  }

  async execute<T>(cmd: string, parser: Parser<T>): PromiseMaybe<T> {
    if (this.pwsh.ended || ending()) {
      this.logger.warn("execute() failed (ended)", { cmd })
      return
    }

    if (isTest && this.mockResults.has(cmd)) {
      const f = this.mockResults.get(cmd)!
      return parser(f.stdout, f.stderr, f.passed)
    }

    try {
      const r = await thenElapsed(
        this.pwsh.enqueueTask(
          new Task(
            cmd,
            (stdout: string, stderr: string | undefined, passed: boolean) =>
              parser(map(stdout, ea => stripPrefix(ea, cmd))!, stderr, passed)
          )
        )
      )
      return this.logger.tap({
        msg: "execute()",
        result: r.result,
        meta: {
          elapsedMs: r.elapsedMs,
          cmd
        }
      })
    } catch (err) {
      this.logger.warn("execute() failed: " + err, { cmd })
      return
    }
  }

  async executeJson(cmd: string): PromiseMaybe<any> {
    const r = await this.execute(
      ensureSuffix(cmd, ConvertToCompressedJson),
      (stdout, stderr, passed) => ({ stdout, stderr, passed })
    )
    if (r == null) {
      this.logger.warn("executeJson(): null result", { cmd })
      return
    }
    if (blank(r.stdout) || notBlank(r.stderr) || !r.passed) {
      this.logger.warn("executeJson(): failed result", { cmd, ...r })
      return
    }
    try {
      return JSON.parse(r.stdout)
    } catch (err) {
      const fixed = r.stdout.replace(/\\/g, "\\\\")
      this.logger.info(
        "executeJson(): parsing failed, trying dub-whack fix...",
        { before: ellipsize(r.stdout), after: ellipsize(fixed) }
      )
      return JSON.parse(fixed)
    }
  }

  async executeJsonToA(cmd: string): PromiseMaybe<Obj[]> {
    return thenMap(this.executeJson(cmd), json =>
      Array.isArray(json) ? json : [json]
    )
  }

  /**
   * @return the path to the command, if it's available on the PATH
   */
  async which(cmd: string): PromiseMaybe<string> {
    // Note to Future Me: `where` (Windows' version of `which`) doesn't seem to
    // find binaries reliably, and this prevents spawning a binary (double win!)
    const json = await this.executeJson(
      "Get-Command -ErrorAction SilentlyContinue " +
        pwshQuote(cmd) +
        " | Select-Object -Property Source"
    )
    return Array.isArray(json) ? json[0]?.Source : json?.Source
  }
}

/**
 * @return the PowerShell version, or undefined on unsupported platforms.
 * @throws if PowerShell times out or has issues
 */
export async function checkPowerShell_() {
  if (!isWin) return
  const ps = PowerShell.instance()
  if (ps.ended || ending()) return
  const version = await thenOrTimeoutError(
    () => ps.version(),
    commandTimeoutMs()
  )
  if (blank(version)) {
    const err = await until(() => orElse(ps.lastStartError, ps.lastTaskError), {
      timeoutMs: ShortCommandTimeoutMs,
      intervalMs: 250
    })
    if (err != null) throw err
    else throw new Error("(unknown error)")
  }
  return version
}
