import _fs from "fs"
import _fse from "fs-extra"
import { pipeline } from "stream/promises"
import _zlib from "zlib"
import { getOrSet } from "../../fe/Map"
import { escapeRegExp } from "../RegExp"

export async function gzip_(nativePath: string): Promise<string> {
  if (nativePath.endsWith(".gz")) return nativePath
  const out = nativePath + ".gz"
  await pipeline(
    _fs.createReadStream(nativePath, { autoClose: true }),
    _zlib.createGzip(),
    _fs.createWriteStream(out, { autoClose: true })
  )
  await _fse.unlink(nativePath)
  return out
}

export function isExtOrCompressedExt(
  actualExt: string,
  expectedExt: string
): boolean {
  return matcherForExtOrCompressedExt(expectedExt)(actualExt)
}

const extMatchers = new Map<string, (actualExt: string) => boolean>()

export function matcherForExtOrCompressedExt(
  ext: string
): (actualExt: string) => boolean {
  return getOrSet(extMatchers, ext, () => {
    const re = new RegExp("^" + escapeRegExp(ext) + "(?:\\.(gz|br))?$", "i")
    return (actualExt: string) => re.exec(actualExt) != null
  })
}
