import _path from "path"
import { blank, notBlank } from "../../fe/Blank"
import { equalsIgnoreCase } from "../String"

export function posix2native(posixPath: string, hostname?: string): string {
  if (blank(posixPath)) return posixPath
  if (_path.sep === _path.posix.sep) return posixPath
  const prefix = notBlank(hostname)
    ? _path.sep + _path.sep + hostname + _path.sep
    : ""
  const split = posixPath.split(_path.posix.sep)
  if (equalsIgnoreCase(split[0], hostname)) split.unshift()
  return prefix + split.join(_path.sep)
}

export function native2posix(nativePath: string): string {
  if (blank(nativePath)) return nativePath
  if (_path.sep === _path.posix.sep) return nativePath
  return _path.posix.sep === _path.sep
    ? nativePath
    : nativePath.split(_path.sep).join(_path.posix.sep)
}
