import { Maybe } from "../fe/MaybeTypes"

export function isBoolean(object: any): object is boolean {
  return typeof object === "boolean"
}

/**
 * (Fairly) strict coercion to true.
 *
 * @return false unless `o` is `true` or `1` (or stringifies to "true" or "1")
 */
export function isTrue(o: any): boolean {
  if (typeof o === "boolean") {
    return o
  } else if (o == null) {
    return false
  } else if (o === 1) {
    return true
  } else {
    const s = String(o).toLowerCase().trim()
    return ["true", "1"].includes(s)
  }
}

/**
 * (Fairly) strict coercion to boolean. If parsing fails, returns undefined.
 */
export function toBoolean(o: any): Maybe<boolean> {
  return isTrue(o) ? true : isFalse(o) ? false : undefined
}

export function boolToInt(o: any) {
  return isTrue(o) ? 1 : 0
}

/**
 * (Fairly) strict coercion to false.
 *
 * @return false unless `o` is `false` or `0` (or stringifies to "false" or
 * "0")
 */
export function isFalse(o: any, ...synonyms: string[]): boolean {
  if (typeof o === "boolean") return !o
  if (o == null) return false
  if (o === 0) return true
  const s = String(o).toLowerCase()
  return ["false", "0", ...synonyms].map(ea => ea.toLowerCase()).includes(s)
}

export function isDisabled(o: any): boolean {
  return isFalse(o, "no", "disable", "disabled")
}

export function or(arr: any[]): boolean {
  return arr.some(ea => isTrue(ea))
}

export function and(arr: any[]): boolean {
  return arr.every(ea => isTrue(ea))
}

/**
 * @return true if `arr` has at least one `true` and no `false` entries
 */
export function maybeAnd(arr: Maybe<boolean>[]): Maybe<boolean> {
  let result: Maybe<boolean>
  for (const ea of arr) {
    if (ea === false) return false
    if (ea === true) result = true
  }
  return result
}

export function mapBoolean<T>(obj: any, f: (b: boolean) => T): Maybe<T> {
  return isTrue(obj) ? f(true) : isFalse(obj) ? f(false) : undefined
}

export function mapTrue<T>(b: any, f: () => T): Maybe<T> {
  return isTrue(b) ? f() : undefined
}
