import * as path_posix from "node:path/posix"
import { SimpleFileOrString } from "../core/fs/SimpleFile"
import { examples } from "./ExamplesDir"
import { TestLibraryDirPrefix } from "./TestLibraryDirPrefix"
import { originalsDir } from "./dir/LibraryDir"
import { BaseFile } from "./fs/BaseFile"
import { stripCopySuffixFromName } from "./fs/FuzzyNames"
import { PosixFile } from "./fs/PosixFile"

/**
 * Directories end with a "/"
 */

export function ciSafePath(nativePath: SimpleFileOrString): string {
  const pf = PosixFile.for(nativePath)
  const f = pf.sibling(stableBasename(pf))

  if (f.isSelfOrDescendantOf(examples())) {
    return path_posix.join("$examples", f.posixPathFrom(examples()))
  }

  const origDir = originalsDir()

  if (f.isSelfOrDescendantOf(origDir)) {
    return path_posix.join("$library", f.posixPathFrom(origDir!))
  }

  const anc = pf.findAncestor(ea => ea.base.startsWith(TestLibraryDirPrefix))
  if (anc != null) {
    return path_posix.join("$library", f.posixPathFrom(anc))
  }
  // well crap, we don't know what to do with this path:
  return pf.baseWithGrandparent
}

export function stableBasename(f: BaseFile): string {
  return stripCopySuffixFromName(f, { aggressive: false }).normalize() + f.ext
}
