import { secondMs } from "../../fe/Date"
import { MiB } from "../../fe/Units"
import { Settings } from "../settings/Settings"

/**
 * This needs to be short enough to pick up when drives are inserted or
 * ejected in reasonable-ish time, but not so often it breaks the OS
 */
export function mountpointsTtlMs() {
  return Settings.mountpointsTtlMs.valueOrDefault
}

/**
 * Update frequently enough to detect substantive changes in available bytes
 */
export function volumeMetadataTtlMs() {
  return Settings.volumeMetadataTtlMs.valueOrDefault
}

// TODO: MOVE to .. (this isn't a volume-specific thing!)
/**
 * How long the `df` or `wmic` should be allowed to run before timing out.
 * Note that external hard drives can take longer than 10 seconds to spin
 * up...
 */
export function commandTimeoutMs() {
  return Settings.commandTimeoutMs.valueOrDefault
}

/**
 * The worst-expected transfer rate for file I/O:
 */
export const MinIoRate = MiB / secondMs
