import { sortBy } from "../fe/ArraySort"
import { Maybe } from "../fe/MaybeTypes"
import { Primitive } from "../fe/Primitive"
import { setUnrefInterval } from "./async/Timers"

export class TTLMap<K extends Primitive, V> implements Map<K, V> {
  readonly [Symbol.toStringTag] = "TTLMap"

  readonly #expireListeners: ((k: K, v: V) => void)[] = []

  // Maps entries to insertion time values. Entries should expire after ttlMs.
  readonly #delegate = new Map<K, { ts: number; v: V }>()

  #ttlMs!: number
  #interval!: NodeJS.Timeout

  constructor(ttlMs: number) {
    this.ttlMs = ttlMs
  }

  get ttlMs() {
    return this.#ttlMs
  }

  set ttlMs(ms: number) {
    this.#ttlMs = ms
    if (this.#interval != null) {
      clearInterval(this.#interval)
    }
    this.#interval = setUnrefInterval(() => this.#vacuum(), ms)
    this.#vacuum()
  }

  clear(): this {
    this.#delegate.clear()
    return this
  }

  delete(key: K): boolean {
    return this.#delegate.delete(key)
  }

  deleteAll(keys: K[]) {
    for (const ea of keys) {
      this.delete(ea)
    }
  }

  forEach(callbackfn: (value: V, key: K, map: Map<K, V>) => void): void {
    for (const [k, v] of this.#delegate.entries()) {
      if (this.#chkEntry(k, v)) {
        callbackfn(v.v, k, this)
      }
    }
  }

  get(key: K): V | undefined {
    const v = this.#delegate.get(key)
    if (this.#chkEntry(key, v)) {
      v.ts = Date.now()
      return v.v
    } else {
      return
    }
  }

  getOrMaybeSet(key: K, valueThunk: () => Maybe<V>): Maybe<V> {
    const v = this.#delegate.get(key)
    if (this.#chkEntry(key, v)) return v.v
    const value = valueThunk()
    if (value != null) this.set(key, value)
    return value
  }

  /**
   * Get the timestamp of the last set() or get() for the given key.
   */
  lastSetOrGetTs(key: K) {
    // NOTE: don't use this.get() here: that'd update the timestamp to now!
    return this.#delegate.get(key)?.ts
  }

  set(key: K, value: V): this {
    this.#delegate.set(key, { ts: Date.now(), v: value })
    return this
  }

  has(key: K): boolean {
    return this.#chkEntry(key, this.#delegate.get(key))
  }

  get size() {
    this.#vacuum()
    return this.#delegate.size
  }

  keys(): IterableIterator<K> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<K> {
      for (const [k, v] of self.#delegate.entries()) {
        if (self.#chkEntry(k, v)) {
          yield k
        }
      }
    }
    return iter()
  }

  values(): IterableIterator<V> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<V> {
      for (const [k, v] of self.#delegate.entries()) {
        if (self.#chkEntry(k, v)) {
          yield v.v
        }
      }
    }
    return iter()
  }

  valuesByRecency() {
    this.#vacuum()
    return sortBy(this.#delegate.values(), ea => -ea.ts)
  }

  entries(): IterableIterator<[K, V]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<[K, V]> {
      for (const [k, v] of self.#delegate.entries()) {
        if (self.#chkEntry(k, v)) {
          yield [k, v.v]
        }
      }
    }
    return iter()
  }

  [Symbol.iterator](): IterableIterator<[K, V]> {
    return this.entries()
  }

  on(_event: "expire", listener: (k: K, v: V) => void) {
    this.#expireListeners.push(listener)
  }

  #chkEntry(
    key: K,
    value: Maybe<{ v: V; ts: number }>
  ): value is { v: V; ts: number } {
    if (value == null) {
      return false
    } else if (value.ts + this.ttlMs > Date.now()) {
      return true
    } else {
      this.#delegate.delete(key)
      for (const ea of this.#expireListeners) {
        ea(key, value.v)
      }
      return false
    }
  }

  /**
   * remove all expired entries
   */
  #vacuum() {
    for (const [k, v] of this.#delegate.entries()) {
      this.#chkEntry(k, v)
    }
  }
}
