import { Maybe } from "../fe/MaybeTypes"
import { Primitive } from "../fe/Primitive"
import { toA } from "../fe/toA"

export function asSet<T>(s: Maybe<Iterable<T>>): Set<T> {
  return s instanceof Set ? s : new Set(toA(s))
}

export function setEql<T>(a: Set<T>, b: Set<T>): boolean {
  return (
    toA(a.keys()).every(ea => b.has(ea)) && toA(b.keys()).every(ea => a.has(ea))
  )
}

export function getOrAdd<K, V>(s: Set<K>, k: K, onAdd: () => V): Maybe<V> {
  if (k == null) throw new Error("null key")
  if (s.has(k)) {
    return undefined
  } else {
    s.add(k)
    return onAdd()
  }
}

//
// These only support primitives, as comparison is done with ==
//

/**
 * @return unique elements in a or b
 */
export function union<T extends Primitive>(
  a: Iterable<T>,
  b: Iterable<T>
): Set<T> {
  return new Set([...a, ...b])
}

/**
 * @return elements in a that are also in b
 */
export function intersection<T extends Primitive>(
  a: Iterable<T>,
  b: Iterable<T>
): Set<T> {
  const s = asSet(b)
  return new Set([...a].filter(ea => s.has(ea)))
}

/**
 * @return elements in `a` that are not in `b`
 */
export function diff<T extends Primitive>(
  a: Iterable<T>,
  b: Iterable<T>
): Set<T> {
  const s = asSet(b)
  return new Set([...a].filter(ea => !s.has(ea)))
}
