import { Thunk } from "../fe/Thunk"
import { UpgradeChannel } from "./settings/UpgradeChannels"
import { version, versionMajor, versionMinor, versionPatch } from "./Version"

export const isPreAlphaVersion = () => version.includes("-prealpha")
export const isAlphaVersion = () => version.includes("-alpha")
export const isBetaVersion = () => version.includes("-beta")
export const isStableVersion = () => !isAlphaVersion() && !isBetaVersion()

export const channel: Thunk<UpgradeChannel> = () =>
  isPreAlphaVersion()
    ? "prealpha"
    : isAlphaVersion()
    ? "alpha"
    : isBetaVersion()
    ? "beta"
    : "stable"

/**
 * @return "1.2.3", even if `version` is "1.2.3-alpha.0" or "1.2.3-beta.32"
 */
export const baseVersion = () =>
  [versionMajor, versionMinor, versionPatch].join(".")

export const versionMajorMinor = versionMajor + "." + versionMinor
