import { isTest } from "../NodeEnv"
import { StoredTask } from "./PhotoStructureEvents"

export interface TaskProcessor {
  name: string

  /**
   * Can this processor handle this task?
   */
  accept(task: StoredTask<any>): boolean

  /**
   * @return null if the task was not handled, and a non-blank string iff the
   * listener "handled" the task
   * @throw if the error task was attempted but failed
   */
  apply(task: StoredTask<any>): Promise<string>
}

export const taskProcessors: TaskProcessor[] = []
// outside of the instance, so it can be set up before we read shared state:

export function addTaskProcessor(processor: TaskProcessor) {
  taskProcessors.push(processor)
}

export function clearTaskProcessors() {
  if (isTest) taskProcessors.length = 0
}
