import { inspect } from "util"
import { isNotEmpty, uniq } from "../fe/Array"
import { GetOrSet } from "../fe/GetOrSet"
import { orElse } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { gt0, isNumber, round } from "../fe/Number"
import { toS } from "../fe/toS"
import { union } from "./Set"
import { setUnrefInterval } from "./async/Timers"

// simple null-safe keys()
function keys(o: any): string[] {
  return o == null ? [] : Object.keys(o)
}

/**
 * First-in-first-out cache. Implementation inspired by
 * https://github.com/dominictarr/hashlru.
 *
 * https://en.wikipedia.org/wiki/Cache_replacement_policies
 */
export class FifoCache<V> implements GetOrSet<string, V> {
  private currentCache!: Record<string, V>
  private setsSinceLastSpill: number = 0
  private priorCache!: Record<string, V>
  private clearInterval?: NodeJS.Timeout

  private readonly expireListeners: ((k: string, v: V) => void)[] = []

  constructor(
    readonly maxSize: number,
    readonly clearEveryMs?: number
  ) {
    if (maxSize < 1) {
      throw new Error("maxSize must be positive")
    }
    if (maxSize > 30_000) {
      // 30 seconds
      throw new Error("maxSize is too big")
    }
    // Set up _size and caches:
    this.clear()
    if (gt0(clearEveryMs)) {
      this.clearInterval = setUnrefInterval(
        () => {
          this.spill()
        },
        round(clearEveryMs / 2)
      )
    }
  }

  private spill() {
    if (
      this.priorCache != null &&
      this.currentCache != null &&
      isNotEmpty(this.expireListeners)
    ) {
      for (const k in this.priorCache) {
        if (this.currentCache[k] == null) {
          const v = this.priorCache[k]
          if (v != null) {
            for (const el of this.expireListeners) {
              el(k, v)
            }
          }
        }
      }
    }
    this.priorCache = this.currentCache ?? Object.create(null)
    this.currentCache = Object.create(null)
    this.setsSinceLastSpill = 0
  }

  [inspect.custom]() {
    return {
      ...this.priorCache,
      ...this.currentCache
    }
  }

  end() {
    if (this.clearInterval != null) clearInterval(this.clearInterval)
  }

  clear(): this {
    this.visit((k, v) => {
      for (const el of this.expireListeners) {
        el(k, v)
      }
    })
    this.currentCache = Object.create(null)
    this.priorCache = Object.create(null)
    this.setsSinceLastSpill = 0
    return this
  }

  get size(): number {
    if (this.currentCache == null || this.priorCache == null) return 0
    let sum = 0
    for (const k of union(keys(this.priorCache), keys(this.currentCache))) {
      if (this.has(k)) sum++
    }
    return sum
  }

  has(key: string): boolean {
    return this.currentCache[key] != null || this.priorCache[key] != null
  }

  keys(): string[] {
    return uniq([...keys(this.priorCache), ...keys(this.currentCache)]).filter(
      k => null != this.currentCache[k] ?? this.priorCache[k]
    )
  }

  delete(key: string) {
    // We don't decrement size here, because we don't use `delete
    // this.currentCache[key]` (because delete is slow (!!))
    const v = this.currentCache[key]
    if (v != null) {
      this.currentCache[key] = undefined as any
      for (const el of this.expireListeners) {
        el(key, v)
      }
    }
    const v2 = this.priorCache[key]
    if (v2 != null) {
      this.priorCache[key] = undefined as any
      if (v == null) {
        for (const el of this.expireListeners) {
          el(key, v2)
        }
      }
    }
  }

  visit(visitor: (key: string, value: V) => any) {
    for (const k of union(keys(this.priorCache), keys(this.currentCache))) {
      const v = this.currentCache[k] ?? this.priorCache[k]
      if (v != null) visitor(k, v)
    }
  }

  deleteIf(predicate: (key: string, value: V) => boolean) {
    for (const k of this.keys()) {
      const v = orElse(this.currentCache[k], this.priorCache[k])
      if (v != null) {
        if (predicate(k, v)) {
          this.delete(k)
        }
      }
    }
  }

  get(key: string | number): Maybe<V> {
    key = toS(key)
    return this.currentCache[key] ?? this.priorCache[key]
  }

  set(key: string | number, value: V) {
    key = toS(key)
    if (this.currentCache[key] == null) {
      if (this.setsSinceLastSpill >= this.maxSize) this.spill()
      this.setsSinceLastSpill++
    }
    this.currentCache[key] = value
  }

  getOrSet(key: string | number, valueThunk: () => V): V {
    key = toS(key)
    const prior = this.get(key)
    if (prior != null) return prior

    const v = valueThunk()
    this.set(key, v)
    return v
  }

  on(_event: "expire", listener: (k: string, v: V) => void) {
    this.expireListeners.push(listener)
  }
}

export interface Placeholder {
  __uid: number
  __start: number
}

export function isPlaceholder(s: any): s is Placeholder {
  return s != null && isNumber(s.__uid) && isNumber(s.__start)
}
