import { sortByInPlace } from "../../fe/ArraySort"
import { isNumber } from "../../fe/Number"
import { isString } from "../../fe/String"
import { LogLevel, LogLevels } from "./LogLevel"

/**
 * field names are abbreviated as they are encoded in JSON on disk.
 */
export interface LogEntry {
  /**
   * timestamp
   */
  ts: number
  /**
   * log level
   */
  l: LogLevel
  /**
   * context
   */
  ctx: string
  /** message */
  msg: string
  meta?: any
  from?: string
}

export function isLogEntry(ea: any): ea is LogEntry {
  return (
    ea != null &&
    isNumber(ea?.ts) &&
    isString(ea?.msg) &&
    LogLevels.includes(ea.l)
  )
}

export function logEntrySorter(ea: LogEntry) {
  return ea?.ts
}

export function sortLogEntriesInPlace(arr: LogEntry[]) {
  sortByInPlace(arr, ea => ea.ts)
}
