import { join } from "path"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { configDir } from "../dir/ConfigDir"
import { SettingsToml } from "./SettingsToml"

export function systemSettingsFile(): Maybe<string> {
  return map(configDir(), ea => join(ea, SettingsToml))
}
