import { notBlankToS } from "../../fe/Blank"
import { toInt } from "../../fe/Number"
import { Setting, SettingOpts } from "./Setting"

export class IntegerSetting extends Setting<number> {
  constructor(opts: SettingOpts<number>) {
    super({
      ...opts,
      toEnv: notBlankToS,
      fromEnv: toInt
    })
  }
}
