import { close, open, read, stat } from "fs-extra"
import { mapGte0 } from "../Number"

/**
 * @param position where to begin reading from in the file
 * @param length the number of bytes to read
 * @throws if fs.open or fs.read throws
 */
export async function readFilePart_({
  nativePath,
  position = 0,
  length
}: {
  nativePath: string
  position?: number
  length?: number
}) {
  let fd = -1
  try {
    // only stat() if the length is not provided:
    const bufLen = length ?? (await stat(nativePath)).size - position
    const b = Buffer.alloc(bufLen)
    fd = await open(nativePath, "r")
    return await read(fd, b, 0, bufLen, position)
  } finally {
    mapGte0(fd, close)
  }
}
