import { lazy } from "../core/Lazy"
import { SimpleAppName } from "./AppName"
import { isElectron } from "./platform/Platform"
import { Settings } from "./settings/Settings"

export const EditionName = lazy(
  () =>
    SimpleAppName +
    (isElectron
      ? ` for Desktops (${Settings.updateChannel.valueOrDefault} channel)`
      : " for Servers")
)
