import { compact } from "./Array"
import { blank } from "./Blank"
import { Maybe } from "./MaybeTypes"
import { Valued } from "./Object"
import { toS } from "./toS"

export function isURLSearchParams(o: any): o is URLSearchParams {
  return o?.constructor?.name === "URLSearchParams"
}

export type URLSearchParamable =
  | string[][]
  | Valued<string | (string | null)[]>
  | string
  | URLSearchParams

export function toURLSearchParams(
  o: Maybe<URLSearchParamable>
): Maybe<URLSearchParams> {
  return o == null
    ? undefined
    : isURLSearchParams(o)
    ? o
    : new URLSearchParams(o as any)
}

export function mergeURLSearchParams(
  ...params: Maybe<URLSearchParamable>[]
): Maybe<URLSearchParams> {
  const arr = compact(params.map(toURLSearchParams))
  const result = arr.shift()
  if (result == null) return
  for (const ea of arr) {
    for (const [k, v] of ea.entries()) {
      result.set(k, v)
    }
  }
  return result
}

/**
 * fullPath is /path?query#anchor of https://host.com/path/?query#anchor
 */
export function assembleFullPath(
  path: string,
  ...queries: Maybe<URLSearchParamable>[]
): string {
  const q = toS(mergeURLSearchParams(...queries))
  return path + (blank(q) ? "" : "?" + q)
}

export function addQueryToFullpath(
  fullPath: string,
  ...queries: Maybe<URLSearchParamable>[]
) {
  const q = toS(mergeURLSearchParams(...queries))
  return blank(q)
    ? fullPath
    : fullPath + (fullPath.includes("?") ? "&" : "?") + q
}
