import { join, posix, win32 } from "path"
import { blank, notBlank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { ensurePrefix } from "../../fe/String"
import { PS_LOCAL_FILE_SCHEME } from "../../fe/URI"
import { containedByNativePath } from "../fs/Path"
import { native2posix } from "../fs/PosixNative"
import { isPosix } from "../platform/Platform"
import { Volume } from "../volumes/Volume"
import { bestVolumeForPath, bestVolumeForVolsha } from "../volumes/Volumes"
import { URI } from "./URI"
import { volsha } from "./volsha"

export async function nativePath2psfile(
  nativePath: string,
  vol?: Maybe<Volume>
) {
  if (blank(nativePath)) return
  vol =
    vol != null &&
    containedByNativePath({
      ancestor: vol.mountpoint,
      descendant: nativePath,
      acceptSelf: true // we shouldn't normally need to accept self--the volume mountpoint shouldn't be an asset native path!
    })
      ? vol
      : await bestVolumeForPath(nativePath)
  if (vol == null || blank(vol.uuid)) return

  const fullPath = native2posix(nativePath)
  const volPath = native2posix(vol.mountpoint)
  if (!fullPath.normalize().startsWith(volPath.normalize())) return
  const path = ensurePrefix(fullPath.slice(volPath.length), "/")

  return URI.from({
    scheme: PS_LOCAL_FILE_SCHEME,
    authority: volsha(vol.uuid),
    path
  })
}

export function joinMountpoint(mountpoint: string, posixPath: string) {
  return join(mountpoint, ...posixPath.split("/").slice(1))
}

export async function psfile2nativePath(
  uri: URI,
  mountpoint?: string
): PromiseMaybe<string> {
  if (uri.scheme !== PS_LOCAL_FILE_SCHEME) {
    throw new Error("invalid URI: " + uri + " (bad scheme)")
  }
  if (blank(uri.authority)) {
    throw new Error("invalid URI: " + uri + " (missing authority)")
  }

  // Best-case, the volsha matches the mountpoint:

  // Don't look at the mountpoint if it includes a separator character for the
  // "other" platform (windows vs posix):

  const notMySep = isPosix ? win32.sep : posix.sep

  const mountpointMatchesPlatform =
    notBlank(mountpoint) && !mountpoint.includes(notMySep)

  if (mountpointMatchesPlatform && !blank(mountpoint)) {
    const vol = await bestVolumeForPath(mountpoint)
    if (vol?.uuid != null) {
      const authority = volsha(vol.uuid)
      if (authority === uri.authority) {
        // Best case: everything still matches!
        // We trust the volume mountpoint more that the given one, though:
        return joinMountpoint(vol.mountpoint!, uri.path)
      }
    }
  }

  // Dang, the mountpoint's volsha doesn't match.

  // See if we can resolve the volsha from current volumes:

  const volshaVolume = await bestVolumeForVolsha(uri.authority)

  if (volshaVolume != null) {
    return joinMountpoint(volshaVolume.mountpoint, uri.path)
  }

  // last-ditch hope that the mountpoint actually works:

  if (mountpointMatchesPlatform && notBlank(mountpoint)) {
    return joinMountpoint(mountpoint, uri.path)
  }

  return undefined
}
