import { notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { keys } from "../../fe/Object"
import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { Args } from "../Args"
import { LogLevel, LogLevels } from "../log/LogLevel"
import { ProgressEvt } from "../progress/ProgressEvt"
import { ForceContext } from "../settings/ForceContext"
import { isUID } from "../UID"
import { TypedEventEmitter } from "./TypedEventEmitter"

// THIS FILE CANNOT IMPORT ANY CORE CODE (other than SimpleEventEmitter): it's
// used everywhere!

export interface UpdateAssetArgs extends ForceContext {
  assetId: number
}
export interface UpdateAssetFileArgs extends ForceContext {
  assetFileId: number
}
export interface UpdateAssetTagsArgs extends ForceContext {
  assetId: number
}

/*
 * Some events (like "progress" or "timing") are transient and if there isn't
 * anything to handle it immediately (like in seconds), they should be
 * discarded.
 *
 * Non-transient events need to be handled and then explicitly removed by the
 * process that can service the request.
 */

const Events = {
  exit: () => null,
  updateReadyToInstall: () => null,
  clearCache: () => null,
  clearDbCache: () => null,
  clearToolCache: () => null,
  error: (_msg: string, _err?: Error | unknown, _ctx?: any) => null,
  fatal: (_error: Error) => null,
  nonFatal: (_error: Error) => null,
  fileChanged: (_nativePath?: Maybe<string>) => null,
  invalidFile: (_nativePath: string) => null,
  mountpointsChanged: () => null,
  reloadLicenses: () => null,
  pause: () => null,
  resume: () => null,
  syncing: (_isSyncing: boolean) => null,
  progress: (_p: ProgressEvt) => null,
  updateProgress: () => null,
  settingsChanged: () => null,
  showAbout: () => null /* task for Desktop */,
  taskResult: (_result: TaskResult) => null,
  timing: (_name: string, _elapsedMs: number) => null,
  vacuuming: (_isVacuuming: boolean) => null,
  volumesChanged: () => null
}

export type PhotoStructureEvents = typeof Events
export const EventNames = strEnum(...keys(Events))
export type EventName = StrEnumKeys<typeof EventNames>

const Tasks = {
  checkOperations: () => null, // web tells sync to check for new Operations to perform
  repairAsset: (_arg: UpdateAssetArgs) => null,
  repairAssetFile: (_arg: UpdateAssetFileArgs) => null,
  runTagMaintenance: () => null,
  syncDir: (_arg: { nativePath: string }) => null
}

export type PhotoStructureTasks = typeof Tasks
export const TaskNames = strEnum(...keys(Tasks))
export type TaskName = StrEnumKeys<typeof TaskNames>

export interface PhotoStructureEvent<E extends EventName> {
  name: E
  args: Args<PhotoStructureEvents[E]>
}

export interface StoredEvent<E extends EventName>
  extends PhotoStructureEvent<E> {
  uid: string
}

export function isStoredEvent(o: any): o is StoredEvent<any> {
  return (
    typeof o === "object" &&
    isUID(o.uid) &&
    EventNames.includes(o.name) &&
    Array.isArray(o.args)
  )
}

export interface StoredTask<T extends TaskName> {
  uid: string
  name: T
  args: Args<PhotoStructureTasks[T]>
}

export function isStoredTask(o: any): o is StoredTask<any> {
  return (
    typeof o === "object" &&
    isUID(o.uid) &&
    TaskNames.includes(o.name) &&
    Array.isArray(o.args)
  )
}

export interface TaskSuccess {
  uid: string
  ts: number
  pid: string
  result: any
}

export interface TaskFailure {
  uid: string
  ts: number
  pid: string
  error: string
}

export type TaskResult = TaskSuccess | TaskFailure

export function isTaskResult(o: any): o is TaskResult {
  return (
    (typeof o === "object" && isUID(o.uid) && notBlank(o.result)) ||
    notBlank(o.error)
  )
}

export type PhotoStructureEmitter = TypedEventEmitter<PhotoStructureEvents>

export const EventNameToLevel: Record<EventName, LogLevel> = Object.freeze({
  exit: LogLevels.warn,
  updateReadyToInstall: LogLevels.warn,
  clearCache: LogLevels.trace,
  clearDbCache: LogLevels.trace,
  clearToolCache: LogLevels.trace,
  error: LogLevels.error,
  fatal: LogLevels.fatal,
  fileChanged: LogLevels.trace,
  invalidFile: LogLevels.warn,
  mountpointsChanged: LogLevels.info,
  nonFatal: LogLevels.warn,
  pause: LogLevels.info,
  progress: LogLevels.info,
  updateProgress: LogLevels.info,
  reloadLicenses: LogLevels.info,
  resume: LogLevels.info,
  settingsChanged: LogLevels.info,
  showAbout: LogLevels.info,
  syncing: LogLevels.info,
  taskResult: LogLevels.info,
  timing: LogLevels.trace,
  vacuuming: LogLevels.info,
  volumesChanged: LogLevels.info
})

export const TaskNameToLevel: Record<TaskName, LogLevel> = {
  checkOperations: LogLevels.info,
  repairAsset: LogLevels.info,
  repairAssetFile: LogLevels.info,
  runTagMaintenance: LogLevels.info,
  syncDir: LogLevels.info
}
