export function isBaseFile(object: any): object is BaseFile {
  return isSimpleFile(object) && object instanceof BaseFile
}

export interface FileProgress {
  path: string
  /**
   * operation (like "sha" or "cp")
   */
  op: string
  /**
   * percent complete
   */
  pct: number
}
const cache = lazy(() => new FileCache<BaseFile>({ name: "fs.BaseFile" }))

export type UtimesArgs = Omit<
  Parameters<typeof touch_>[0],
  "file" | "ensureFile"
>

export type RefreshArgs = {
  refresh?: boolean
  logLevel?: LogLevel
}

/**
 * Extracted from PosixFile, this superclass is used by the FileLogger.
 *
 * It must not depend on core code that also requires a Logger.
 */
export class BaseFile implements SimpleFile, CacheableFile {
  protected readonly bflog = lazy(() =>
    mkLogger("fs.BaseFile(" + this.nativePath + ")")
  )

  protected static attrTTL = 3 * minuteMs
  readonly posixPath: string
  readonly nativePath: string

  /** "/home/user/dir" of "/home/user/dir/file.txt" */
  readonly dir: string
  /** "file.txt" of "/home/user/dir/file.txt" */
  readonly base: string
  /** "file" of "/home/user/dir/file.txt" */
  readonly name: string
  /** ".txt" of "/home/user/dir/file.txt" */
  readonly ext: string

  protected constructor(
    nativePath: string,
    private dirent?: DirectoryEntry
  ) {
    if (dirent != null) {
      this.nativePath = dirent.nativePath
      this.dir = dirent.dir
      this.base = dirent.base
      this.name = dirent.name
      this.ext = dirent.ext
    } else {
      this.nativePath = nativePath
      const parsed = parseNativePath(this.nativePath)
      this.dir = parsed.dir
      this.base = parsed.base
      this.name = parsed.name
      this.ext = parsed.ext
    }
    this.posixPath = native2posix(this.nativePath)
  }

  toJSON() {
    return {
      _ctor: this.constructor.name,
      nativePath: this.nativePath
    }
  }

  toLogJSON() {
    return this.nativePath
  }

  [util.inspect.custom]() {
    return this.toJSON()
  }

  static async withFastestAccess<F extends BaseFile>(
    files: Maybe<F>[]
  ): PromiseMaybe<F> {
    const arr = compact(files)
    const msArr = await Promise.all(arr.map(f => f.shaMs()))
    return arr[leastIndex(msArr)]
  }

  static projectRoot = lazy(() => {
    const root = ProjectPath.Root()
    if (root == null) {
      throw new Error("Cannot find project path")
    } else {
      return BaseFile.for(root)
    }
  })

  static forPosix(posixPathOrFile: string | BaseFile) {
    return posixPathOrFile instanceof BaseFile
      ? posixPathOrFile
      : this.for(posixPathOrFile.split("/").join(_path.sep))
  }

  static forDirectoryEntry(de: DirectoryEntry) {
    return this.for(de.nativePath, de)
  }

  static for(nativePathOrFile: SimpleFileOrString, dirent?: DirectoryEntry) {
    if (nativePathOrFile instanceof BaseFile) {
      return nativePathOrFile
    }

    // resolve() is expensive! Try to avoid it...

    const nativePath = isSimpleFile(nativePathOrFile)
      ? nativePathOrFile.nativePath
      : toS(nativePathOrFile)

    if (blank(nativePath)) {
      throw new Error("BaseFile.for(): empty nativePathOrFile")
    }
    const prior = cache().get(nativePath)
    if (prior != null) return prior

    // no need for a mutex: we're sync!
    const resolved = resolve(nativePath)
    const result = new BaseFile(resolved, dirent)
    cache().set(nativePath, result)
    cache().set(resolved, result)
    return result
  }

  /**
   * Wipe the instance cache and caches of all instances.
   *
   * This should only be used by tests.
   *
   * @param fromPath if undefined, all instances will be cleared.
   */
  static clear(fromPath?: string) {
    ee().emit("fileChanged", fromPath)
  }

  for(path: string, dirent?: DirectoryEntry): this {
    return BaseFile.for(path, dirent) as this
  }

  forDirectoryEntry(de: DirectoryEntry): this {
    return BaseFile.for(de.nativePath, de) as this
  }

  forChildDirent(dirent: SimpleDirent): this {
    return this.forDirectoryEntry(
      DirectoryEntry.fromSimpleDirent(this.nativePath, dirent)
    )
  }

  forSiblingDirent(dirent: SimpleDirent): this {
    return this.forDirectoryEntry(
      DirectoryEntry.fromSimpleDirent(this.dir, dirent)
    )
  }

  clear({ emit }: { emit?: boolean } = {}): this {
    if (emit === true) ee().emit("fileChanged", this.nativePath)
    this.dirent = undefined
    this.#childDirectoryEntries.unset()
    this.#stat = undefined
    return this
  }

  clearThisAndParent(): this {
    ee().emit("fileChanged", this.dir)
    // this .clear() shouldn't be necessary, but it prevents race conditions
    // from async onFileChanged listeners.
    return this.clear({ emit: false })
  }

  toString() {
    return this.nativePath
  }

  valueOf() {
    return this.pathnames
  }

  eql(that: Maybe<SimpleFileOrString>): boolean {
    if (that == null) return false
    const thatNativePath = toNativePath_(that)
    // These paths have already been resolve()d. We don't need to do it again
    // here.
    return isLinux
      ? this.nativePath === thatNativePath
      : // darn Windows and macOS have case-insensitive filesystems!
        equalsIgnoreCase(this.nativePath, thatNativePath)
  }

  get isUNC() {
    return isUNC(this.nativePath)
  }

  /**
   * Posix path from parent
   */
  get baseWithParent(): string {
    return (
      this.isRoot
        ? "/"
        : this.parent().isRoot
        ? "/" + this.base
        : (this.parent().parent().isRoot ? "/" : "") + // <  prefixes the path with a / if grandparent is root.
          this.parent().base +
          "/" +
          this.base
    ).normalize() // < normalize for macOS
  }

  /**
   * @return nativePath "/path/to/file.ext" with ancestors such that the
   * result doesn't exceed maxLength
   */
  ellipsize({
    maxLength = 80
  }: { maxLength?: number; maxDepth?: number } = {}) {
    return ellipsizePath({ p: this, maxLength })
  }

  get baseWithParentNoExt(): string {
    return stripSuffix(this.baseWithParent, this.ext)
  }

  /**
   * posix path from grandparent
   */
  get baseWithGrandparent(): string {
    return (
      this.isRoot
        ? "/"
        : this.parent().isRoot
        ? this.baseWithParent
        : this.parent().baseWithParent + "/" + this.base
    ).normalize()
  }

  posixPathFrom(from: SimpleFileOrString): string {
    return posixPathFrom(from, this) + (this.isDirectorySync() ? "/" : "")
  }

  async directoryEntry(): PromiseMaybe<DirectoryEntry> {
    return (this.dirent ??= await thenMap(
      this.stat(),
      ea => new DirectoryEntry(this.dir, new StatDirent(this.base, ea))
    ))
  }

  directoryEntrySync(): Maybe<DirectoryEntry> {
    return (this.dirent ??= map(
      this.statSync(),
      ea => new DirectoryEntry(this.dir, new StatDirent(this.base, ea))
    ))
  }

  readonly #childDirectoryEntries = lazy(() =>
    thenMap(this.directoryEntry(), de => de.children())
  )

  async childDirectoryEntries(f?: Predicate<DirectoryEntry>) {
    const dirents = await this.#childDirectoryEntries()
    if (dirents == null || f == null || isEmpty(dirents)) return dirents

    // PERF: don't filterAsync here to avoid lots of Promises GC
    const result: DirectoryEntry[] = []
    for (const dirent of dirents) {
      if (true === (await f(dirent))) result.push(dirent)
    }
    return result
  }

  _directoryEntryChild(de: DirectoryEntry) {
    return this.for(_path.join(this.nativePath, de.base), de)
  }

  /**
   * @return the basename of the children of `this`, locale sorted.
   */
  childNames(): PromiseMaybe<string[]> {
    return thenMap(this.childDirectoryEntries(), arr => arr.map(ea => ea.base))
  }

  /**
   * For case-insensitive filesystems, return the closest-matching existing file
   * **with case matching whats on the filesystem**
   */
  async resolve_(): PromiseMaybe<this> {
    return (
      (await this.#resolve_()) ?? (await this.clearThisAndParent().#resolve_())
    )
  }
  async #resolve_(): PromiseMaybe<this> {
    // DON'T USE this.siblingDirectoryEntries(): that filters `this`!

    // Use readdir_() directly to avoid needless DirectoryEntry creation:
    const sibs = (await readdir_(this.dir)).filter(ea =>
      equalsIgnoreCase(ea.basename, this.base)
    )
    const closest = leastBy(sibs, ea => hamming(ea.basename, this.base))
    return this.bflog().tap({
      msg: "resolve()",
      result:
        closest?.basename === this.base
          ? this
          : map(closest, ea => this.forSiblingDirent(ea)),
      meta: { sibs }
    })
  }

  /**
   * @throws if `readdir` fails
   */
  async children_(predicate?: Predicate<SimpleDirent>): Promise<this[]> {
    const dirents = await readdir_(this.nativePath)
    if (predicate != null) {
      await filterInPlaceAsync(dirents, predicate)
    }
    // readdir_ gives us sorted files already!
    return dirents.map(ea => this.forChildDirent(ea))
  }

  async children(predicate?: Predicate<DirectoryEntry>): PromiseMaybe<this[]> {
    return (await this.childDirectoryEntries(predicate))?.map(ea =>
      this._directoryEntryChild(ea)
    )
  }

  async childFiles(
    predicate?: Predicate<DirectoryEntry>
  ): PromiseMaybe<this[]> {
    const arr = await this.childDirectoryEntries(async ea => {
      return (
        ea.isFile() && (predicate == null || true === (await predicate(ea)))
      )
    })
    return arr == null
      ? undefined
      : arr.map(ea => this._directoryEntryChild(ea))
  }

  async childDirectories(
    predicate?: Predicate<DirectoryEntry>
  ): PromiseMaybe<this[]> {
    const arr = await this.childDirectoryEntries(async ea => {
      return (
        ea.isDirectory() &&
        (predicate == null || true === (await predicate(ea)))
      )
    })
    return arr == null
      ? undefined
      : arr.map(ea => this._directoryEntryChild(ea))
  }

  /**
   * ONLY FOR TESTS
   */
  childrenSync(): this[] {
    return orElse(
      this.trapSync("childrenSync", () =>
        fs.readdirSync(this.nativePath).map(ea => this.join(ea))
      ),
      []
    )
  }
  childFilesSync(): this[] {
    return orElse(
      this.trapSync("childrenSync", () =>
        fs
          .readdirSync(this.nativePath, { withFileTypes: true })
          .filter(ea => ea.isFile())
          .map(ea => this.join(ea.name))
      ),
      []
    )
  }

  async hasChildren(childNames?: string[]) {
    const actualChildNames = await this.childNames()
    return isNotEmpty(childNames)
      ? includesAll(actualChildNames, childNames)
      : isNotEmpty(actualChildNames)
  }

  async hasNoChildren() {
    return (await this.isFile()) || isEmpty(await this.childNames())
  }

  /**
   * Depth-first recursion, includes child directories.
   */
  async visitDescendants(
    f: (descendant: this) => any | Promise<any>
  ): Promise<void> {
    return thenMap(this.children(), async children => {
      for (const child of children) {
        await child.visitDescendants(f)
        await f(child)
      }
    })
  }

  /**
   * Breadth-first recursion
   *
   * @return all files (no directories) that pass the given predicate.
   */
  async descendants(
    predicate: Predicate<DirectoryEntry>
  ): PromiseMaybe<this[]> {
    const result: this[] = []
    result.push(...toA(await this.childFiles(predicate)))
    const dirs = await this.childDirectories()
    if (dirs == null) return result
    for (const dir of dirs) {
      result.push(...toA(await dir.descendants(predicate)))
    }
    return result
  }

  async someDescendant(
    predicate: Predicate<DirectoryEntry>,
    depth: number = 0
  ): PromiseMaybe<this> {
    if (!gt0(depth)) return
    const arr = (await this.#childDirectoryEntries()) ?? []
    for (const ea of arr) {
      if (true === (await predicate(ea))) {
        return this._directoryEntryChild(ea)
      }
    }
    if (depth > 0) {
      for (const ea of arr) {
        if (ea.isDirectory()) {
          const result = await this._directoryEntryChild(ea).someDescendant(
            predicate,
            depth - 1
          )
          if (result != null) return result
        }
      }
    }
    return
  }

  /**
   * Lightweight breadth-first directory entry visiting
   *
   * @return all files (no directories) that pass the given predicate.
   */
  descendantsSync(predicate: SyncPredicate<DirectoryEntry>): this[] {
    const thisDe = this.directoryEntrySync()
    const result: this[] = []
    thisDe?.visitDescendantsSync(ea => {
      if (true === predicate(ea)) {
        result.push(this.forDirectoryEntry(ea))
      }
    })
    return result
  }

  async ancestorWithChildren(childNames: string[]): PromiseMaybe<this> {
    if (await this.hasChildren(childNames)) {
      return this
    } else if (this.isRoot) {
      return undefined
    } else {
      return this.parent().ancestorWithChildren(childNames)
    }
  }

  async siblings(f?: Predicate<DirectoryEntry>): PromiseMaybe<this[]> {
    const p = this.parent()
    return (await this.siblingDirectoryEntries(f))?.map(ea =>
      p._directoryEntryChild(ea)
    )
  }

  async siblingDirectoryEntries(
    f?: Predicate<DirectoryEntry>
  ): PromiseMaybe<DirectoryEntry[]> {
    return this.parent().childDirectoryEntries(
      async ea => ea.base !== this.base && (f == null || true === (await f(ea)))
    )
  }

  async selfAndSiblings() {
    return this.parent().children()
  }

  async firstExistingSelfOrAncestor(): Promise<this> {
    return this.isRoot || (await this.exists())
      ? this
      : this.parent().firstExistingSelfOrAncestor()
  }

  /**
   * @return ["C:", "Users", "Bob", "image.jpg"] on win, ["home", "bob",
   * "image.jpg"] on !win
   */
  get pathnames(): string[] {
    return splitNativePath(this.nativePath)

    // return this.bflog().tap({
    //   msg: "pathnames()",
    //   result: splitNativePath(this.nativePath),
    //   meta: { nativePath: this.nativePath }
    // })
  }

  get pathsForDateParsing(): string[] {
    return [
      ...(this.isRoot ? [] : this.parent().pathnamesWithoutDrive),
      bname(this.name)
    ]
  }

  get pathnamesWithoutDrive(): string[] {
    return isWin ? this.pathnames.slice(1) : this.pathnames
  }

  /**
   * @return 0 for "/" or "C:\", 1 for "C:\Users" or "/etc", ...
   */
  get depth(): number {
    return pathDepth(this)!
  }

  get isRoot(): boolean {
    return pathIsRoot(this)
  }

  /**
   * @param depth The number of directories from root to include. For example,
   * "C:\" or "/" for 0, "C:\Users" or "/home" for depth 1
   */
  root(depth: number = 0): this {
    // "/".root(1) should return "/"
    return this.depth <= depth ? this : this.parent().root(depth)
  }

  parent(): this {
    // MEMLEAK: don't cache this: this.for will do that for us.
    return this.isRoot ? this : (this.for(this.dir) as this)
  }

  isAncestorOf(
    maybeDescendant: Maybe<string | SimpleFile>,
    args?: { acceptSelf: boolean }
  ): boolean {
    return containedByNativePath({
      ancestor: this,
      descendant: maybeDescendant,
      ...args
    })
  }

  isDescendantOf(maybeAncestor: Maybe<string | SimpleFile>): boolean {
    return containedByNativePath({ descendant: this, ancestor: maybeAncestor })
  }

  isSelfOrDescendantOf(maybeAncestor: Maybe<string | SimpleFile>): boolean {
    return (
      maybeAncestor != null &&
      (this.nativePath === toNativePath_(maybeAncestor) ||
        this.isDescendantOf(maybeAncestor))
    )
  }

  parentsAndSelf(): this[] {
    return [...this.parents(), this]
  }

  selfAndParents(depth: number): this[] {
    return [
      this,
      ...(this.isRoot || depth <= 0
        ? []
        : this.parent().selfAndParents(depth - 1))
    ]
  }

  ancestor(depth = 1): Maybe<this> {
    // TODO: remove recursion
    return this.isRoot && depth > 0
      ? undefined
      : depth === 0
      ? this
      : this.parent().ancestor(depth - 1)
  }

  findAncestor(predicate: (ea: this) => boolean): Maybe<this> {
    return this.isRoot
      ? undefined
      : predicate(this)
      ? this
      : this.parent().findAncestor(predicate)
  }

  /**
   * Root-first order of paths (so [/, /var, /var/tmp, ...]
   */
  parents(): this[] {
    const p = this.parent()
    // NOTE: ALWAYS RETURN A NEW ARRAY, because consumers may .reverse or do
    // other nasty things to it.
    return this.isRoot ? [] : [...p.parents(), p]
  }

  /**
   * Paths from URIs can have differently-encoded unicode paths (which switching
   * from macOS to linux or Windows, for example.)
   *
   * If `this` exists, return it.
   *
   * If it doesn't, walk from the root of the path and try to find elements that,
   * when unicode-normalized, match the current filesystem.
   *
   * As a last-ditch effort, try a case-insensitive match.
   */
  async normalize(): Promise<this> {
    // Don't need to handle UNC paths (they are always windows-only):
    return this.isUNC ? this : this.for(await actualPath(this.nativePath))
  }

  sibling(base: string): this {
    return this.parent().join(base)
  }

  withPrefix(prefix: string): this {
    return this.sibling(prefix + this.base)
  }

  /**
   * @return this.sibling(this.name + suffix + this.ext)
   */
  withNameSuffix(suffix: string): this {
    return this.sibling(this.name + suffix + this.ext)
  }

  /**
   * @return this.sibling(this.base + suffix)
   */
  withSuffix(suffix: string): this {
    return this.sibling(this.base + suffix)
  }

  siblingOf(possibleSibling: BaseFile): boolean {
    return (
      this.nativePath !== possibleSibling.nativePath &&
      this.dir === possibleSibling.dir
    )
  }

  /**
   * Allows ../... directory traversals.
   */
  join(...paths: string[]): this {
    if (isEmpty(paths) || eql(["."], paths) || paths.every(blank)) return this
    return isAbsolute(paths[0])
      ? this.for(_path.join(...paths))
      : this.for(_path.join(this.nativePath, ...paths))
  }

  joinYMD(d = new Date()): Maybe<this> {
    return map3(
      d?.getFullYear(),
      d?.getMonth(),
      d?.getDate(),
      (year, month, day) => this.join(toS(year), pad2(month + 1), pad2(day))
    )
  }

  /**
   * Prevents `..` directory traversals
   * @param paths
   */
  child(...paths: string[]): this {
    if (isEmpty(paths)) return this
    const pathElements = flatten(paths.map(ea => ea.split(_path.sep))).filter(
      ea => ea !== ".."
    )
    // Convert to posix to prevent "\\" and "/" from confusing the split:
    return this.join(...pathElements)
  }

  //  _  _    _
  // (_)| |_ ( )___    __ _
  // | || __||// __|  / _` |
  // | || |_   \__ \ | (_| | _  _  _
  // |_| \__|  |___/  \__,_|(_)(_)(_)
  protected async trap<T>(
    methodName: string,
    p: () => SyncOrAsync<T>,
    errLogLevel: LogLevel = "warn"
  ): PromiseMaybe<T> {
    try {
      return await time("fs." + methodName, p)
    } catch (err) {
      this.bflog().log(errLogLevel, `trap: ${methodName}() failed: ${err}`)
      return undefined
    }
  }

  // Return true if p() doesn't raise an error, else return false.
  protected async trapOr(
    methodName: string,
    p: () => Promise<any>,
    errLogLevel: LogLevel = "warn"
  ): Promise<boolean> {
    try {
      await time("fs." + methodName, p)
      return true
    } catch (err) {
      this.bflog().log(errLogLevel, `trapOr: ${methodName}() failed: ${err}`)
      return false
    }
  }

  protected trapSync<T>(
    methodName: string,
    p: () => T,
    errLogLevel: LogLevel = "warn"
  ): Maybe<T> {
    try {
      // this.bflog().trace(`trapSync ${methodName}()`)
      return timeSync("fs." + methodName, p)
    } catch (err) {
      this.bflog().log(errLogLevel, `${methodName}() failed: ${err}`)
      return undefined
    }
  }

  /**
   * Never cached
   */
  stat_() {
    return stat_(this.nativePath)
  }

  #stat: Maybe<fs.Stats> = undefined

  async stat(opts?: RefreshArgs) {
    if (opts?.refresh ?? false) {
      this.#stat = undefined
    }
    return (this.#stat ??= await statMaybe(this.nativePath))
  }

  statSync(opts?: RefreshArgs) {
    if (opts?.refresh ?? false) {
      this.#stat = undefined
    }
    return (this.#stat ??= statSync(this.nativePath))
  }

  async exists(opts?: RefreshArgs): Promise<boolean> {
    if (opts?.refresh ?? false) {
      this.dirent = undefined
    }
    return this.dirent != null || (await thenDefined(this.stat(opts)))
  }

  existsSync(opts?: RefreshArgs): boolean {
    if (opts?.refresh ?? true) {
      this.dirent = undefined
    }
    return this.dirent != null || this.statSync(opts) != null
  }

  async notExists(opts?: RefreshArgs): Promise<boolean> {
    return thenNot(this.exists(opts))
  }

  async isDeleted(maxDepth = 3): PromiseMaybe<boolean> {
    if (await this.exists({ refresh: true })) {
      return this.bflog().tap({
        result: false,
        msg: "isDeleted(): file exists"
      })
    }

    if (this.isRoot || maxDepth <= 0) {
      return this.bflog().tap({
        result: undefined,
        msg: "isDeleted(): isRoot() and doesn't exist (volume is just unmounted)"
      })
    }

    const parentIsDeleted = await this.parent().isDeleted(maxDepth - 1)

    if (parentIsDeleted == null) {
      return this.bflog().tap({
        result: undefined,
        msg: "isDeleted(): parent().isDeleted was undefined",
        meta: { parentIsDeleted }
      })
    } else {
      return this.bflog().tap({
        result: true,
        msg: "isDeleted(): parent was either deleted or not deleted: either way, that means I am deleted.",
        meta: { parentIsDeleted }
      })
    }
  }

  mtime(): PromiseMaybe<Date> {
    return thenMap(this.stat(), s => s.mtime)
  }

  mtimeMs(opts?: RefreshArgs): PromiseMaybe<number> {
    return thenMap(this.stat(opts), s => Math.floor(s.mtimeMs))
  }

  async mtimeSec(): PromiseMaybe<number> {
    const s = await this.stat()
    return s == null ? undefined : unixtime(s.mtimeMs)
  }

  async lastModifiedUtc(): PromiseMaybe<string> {
    return (await this.stat())?.mtime.toUTCString()
  }

  // The mtime, rather than the birthtime, will actually be the capture
  // time if the file is "copied and pasted" via the Windows explorer
  // (rather than using `rsync --times` or `cp --preserve=all`).

  // Birthtime isn't supported by default in linux, and seems to just return
  // the ctime.

  /**
   * @return the uniq stat times, including birthtime, mtime, and ctime.
   */
  async statTimes(): PromiseMaybe<number[]> {
    const s = await this.stat()
    return s == null ? undefined : statTimes(s)
  }

  maxStatMs(): PromiseMaybe<number> {
    return thenMap(this.statTimes(), max)
  }

  maxStatDate(): PromiseMaybe<Date> {
    return thenMap(this.maxStatMs(), ea => new Date(ea))
  }

  minStatMs(): PromiseMaybe<number> {
    return thenMap(this.statTimes(), min)
  }

  minStatDate(): PromiseMaybe<Date> {
    return thenMap(this.minStatMs(), ea => new Date(ea))
  }

  async size(opts?: RefreshArgs): PromiseMaybe<number> {
    return thenMap(this.stat(opts), s => s.size)
  }

  /**
   * Always uncached
   */
  async size_(): Promise<number> {
    return (await this.stat_()).size
  }

  /**
   * @return Promise<true> if the file is executable (not relevant on windows)
   */
  async isExecutable(): Promise<boolean> {
    return access({ stat: await this.stat(), r: true, x: true })
  }

  async isReadable(): Promise<boolean> {
    return access({ stat: await this.stat(), r: true })
  }

  isNotReadable(): Promise<boolean> {
    return thenNot(this.isReadable())
  }

  async isReadWritable(): Promise<boolean> {
    return access({ stat: await this.stat(), r: true, w: true })
  }

  isNotReadWritable(): Promise<boolean> {
    return thenNot(this.isReadWritable())
  }

  async isReadWriteExecutable(): Promise<boolean> {
    return isStatRWX(await this.stat())
  }

  isReadWriteExecutableSync(): boolean {
    return isStatRWX(this.statSync())
  }

  async isReadWriteExecutable_(): Promise<boolean> {
    return isStatRWX(await this.stat_())
  }

  async isReadWriteDirectory(): Promise<boolean> {
    const stat = await this.stat()
    return stat?.isDirectory() === true && isStatRWX(stat)
  }

  async assertReadWriteExecutable(): Promise<void> {
    if (!(await this.isReadWriteExecutable())) {
      throw new Error(
        `Please check directory permissions for ${
          this.nativePath
        }: it must be read/write${
          isWin ? "" : "/execute"
        } by ${await userDesc()}`
      )
    }
  }

  /**
   * Is this a rw dir, or **is it possible** for this directory to be made rw?
   */
  async maybeReadWriteDirectory(): Promise<boolean> {
    if ((await this.isFile()) === true) {
      return false
    } else if (await this.isDirectory()) {
      return this.isReadWriteExecutable()
    } else {
      // I don't exist! ask my parent:
      return this.parent().maybeReadWriteDirectory()
    }
  }

  isHiddenBasename(): boolean {
    return isHiddenBasename(this.base)
  }

  async isEmpty(bytesConsideredEmpty = 0): Promise<boolean> {
    if (await this.isDirectory()) {
      return isNotEmpty(await this.childNames())
    } else {
      const s = await this.size()
      return s == null || s <= bytesConsideredEmpty
    }
  }

  isNonEmpty(minSizeBytes = 1): Promise<boolean> {
    return thenNot(this.isEmpty(minSizeBytes))
  }

  async isEmptyFile(minSizeBytes = 1): Promise<boolean> {
    const s = await this.stat()
    return (
      s == null ||
      ((await this.isFile()) && !gte(await this.size(), minSizeBytes))
    )
  }

  async isNonEmptyFile(minSizeBytes = 1): Promise<boolean> {
    return (await this.isFile()) && (await this.isNonEmpty(minSizeBytes))
  }

  async modifiedGTE(mtime: Date): PromiseMaybe<boolean> {
    return thenMap(
      this.mtime(),
      thisMtime =>
        // Filesystems have only second-level resolution
        unixtime(thisMtime) >= unixtime(mtime)
    )
  }

  async modifiedCloseTo(
    mtimeMs: number,
    deltaMs: number
  ): PromiseMaybe<boolean> {
    return thenMap(
      this.mtimeMs(),
      thisMtimeMs => Math.abs(thisMtimeMs - mtimeMs) <= deltaMs
    )
  }

  async isRecent(agoMs: number): Promise<boolean> {
    const mtime = await this.maxStatMs()
    return mtime != null && mtime > Date.now() - agoMs
  }

  async modifiedGT(mtime: Maybe<Date>): PromiseMaybe<boolean> {
    if (mtime == null) return
    return thenMap(
      this.mtime(),
      thisMtime =>
        // Filesystems have only second-level resolution
        unixtime(thisMtime) > unixtime(mtime)
    )
  }

  isDirectory(opts?: RefreshArgs): SyncOrAsync<boolean> {
    if (this.dirent != null) return this.dirent.isDirectory()
    return thenMapOr(
      this.stat(opts),
      ea => ea.isDirectory(),
      () => false
    )
  }

  async isNotDirectory(): Promise<boolean> {
    return thenNot(this.isDirectory())
  }

  isDirectorySync(opts?: RefreshArgs): boolean {
    if (this.dirent != null) return this.dirent.isDirectory()
    return mapOr(
      this.statSync(opts),
      ea => ea.isDirectory(),
      () => false
    )
  }

  async nearestDir(): Promise<this> {
    return (await this.isDirectory()) ? this : this.parent()
  }

  async isFile(): Promise<boolean> {
    if (this.dirent != null) return this.dirent.isFile()
    const s = await this.stat()
    return s != null ? s.isFile() : false
  }

  isFileSync(): boolean {
    if (this.dirent != null) return this.dirent.isFile()
    return opt(this.statSync()).filter(ea => ea.isFile()).isDefined
  }

  async rmdir(errLogLevel: LogLevel = "warn"): Promise<boolean> {
    this.clear()
    return (
      (await this.trap(
        "rmdir",
        async () => {
          await _fse.rmdir(this.nativePath, {
            recursive: false,
            maxRetries: 8,
            retryDelay: 500
          })
          return true
        },
        errLogLevel
      )) ?? false
    )
  }

  rmdirSync(errLogLevel: LogLevel = "warn"): boolean {
    this.clear()
    return (
      this.trapSync(
        "rmdirSync",
        () => {
          _fse.rmdirSync(this.nativePath, {
            recursive: false,
            maxRetries: 8,
            retryDelay: 500
          })
          return true
        },
        errLogLevel
      ) ?? false
    )
  }

  /**
   * @throws on error
   */
  async mkdirp_(): Promise<this> {
    try {
      await mkdirp_(this.nativePath)
    } catch (err: any) {
      // EEXIST is from race conditions, and can be ignored.
      if (err?.code !== "EEXIST") throw err
    }
    if (
      // kernel FS lag can't be longer than a second or two
      false ===
      (await untilTrue(() => this.clear().isDirectory(), {
        timeoutMs: 2 * secondMs,
        intervalMs: 200
      }))
    ) {
      throw new Error("Failed to mkdirp " + this)
    } else {
      // this and parent because parent caches children.
      return this.clearThisAndParent()
    }
  }

  async mkdirp(): PromiseMaybe<this> {
    if ((await this.clear().isDirectory()) || this.isRoot) return this
    return this.trap("mkdirp", async () => this.mkdirp_())
  }

  /**
   * @throws on error
   */
  mkdirpSync_(): this {
    mkdirpSync_(this.nativePath)
    return this.clearThisAndParent()
  }

  mkdirpSync(): Maybe<this> {
    if (this.isRoot) return this
    return this.trapSync("mkdirpSync", () => this.mkdirpSync_())
  }

  // no need lazy() or cache: fileSha_ already has smart caching.
  sha_(): Promise<string> {
    return fileSha_(this.nativePath)
  }

  /**
   * base64 encoded SHA-512/192 (ish)
   */
  async sha(): PromiseMaybe<string> {
    return this.trap("sha", () => this.sha_())
  }

  /**
   * @return ms to gather the sha for this file
   */
  async shaMs(): PromiseMaybe<number> {
    // we don't care if the metadata is stale!
    return this.trap(
      "shaMs",
      async () =>
        (await fileShaMeta_(this, { autoInvalidate: false })).elapsedMs
    )
  }

  async readJson_<T>(): Promise<T> {
    return parseJSON((await _fse.readFile(this.nativePath)).toString()) as any
  }

  readJson<T>(errLogLevel: LogLevel = "warn"): PromiseMaybe<T> {
    return this.trap("readJson", () => this.readJson_<T>(), errLogLevel)
  }

  readJsonSync<T>(): Maybe<T> {
    return this.trapSync("readJsonSync", () =>
      parseJSON<T>(fs.readFileSync(this.nativePath).toString())
    )
  }

  readFileSync_() {
    return readFileSync(this.nativePath)
  }

  readFile_(): Promise<Buffer> {
    return readFile(this.nativePath)
  }

  readFile(errLogLevel: LogLevel = "warn"): PromiseMaybe<Buffer> {
    return this.trap("readFile", () => this.readFile_(), errLogLevel)
  }

  async readTextFile_() {
    return bufferToString(await this.readFile_())
  }

  readTextFile(errLogLevel: LogLevel = "warn") {
    return this.trap("readTextFile", () => this.readTextFile_(), errLogLevel)
  }

  /**
   * `readFile`, but on-the-fly decompression for .gz and .br
   * @throws on error
   */
  async zReadFile_(options?: {
    start?: number
    end?: number
  }): PromiseMaybe<Buffer> {
    return zCopyToBuffer_(this.nativePath, options)
  }

  async zcat(options?: { start?: number; end?: number }): PromiseMaybe<string> {
    return this.trap("zcat", () => thenMap(this.zReadFile_(options), toS))
  }

  readLines(errLogLevel: LogLevel = "warn"): PromiseMaybe<string[]> {
    return thenMap(this.readTextFile(errLogLevel), splitLines)
  }

  readFileSync(): Maybe<string> {
    try {
      return fs.readFileSync(this.nativePath).toString()
    } catch (error) {
      this.bflog().warn("readFileSync() failed", { error })
      return
    }
  }

  /**
   * @throws on error
   */
  async writeText_(...txt: string[]) {
    await writeText_(this.nativePath, ...txt)
    return this
  }

  writeTextSync_(...txt: string[]) {
    writeTextSync_(this.nativePath, ...txt)
    return this
  }

  /**
   * @throws on error
   */
  async writeFile_(data: string | Buffer) {
    await writeFile_(this.nativePath, data)
    return this
  }

  async writeJson(object: any, options?: JsonWriteOptions): PromiseMaybe<this> {
    // outputJSON is almost the same as writeJson, except that if the directory
    // does not exist, it's created
    return this.trap("writeJsonMaybe", () => this.writeJson_(object, options))
  }

  /**
   * @throws on error
   */
  async writeJson_(object: any, options?: JsonWriteOptions): Promise<this> {
    await mkdirp_(this.dir)
    await _fse.writeFile(
      this.nativePath,
      stringify(object, options?.replacer, options?.spaces),
      omit(options, "replacer", "spaces")
    )
    this.clearThisAndParent()
    return this
  }

  async matchesContent(that: this): Promise<boolean> {
    const stats = await Promise.all([
      this.stat({ refresh: true }),
      that.stat({ refresh: true })
    ])
    if (stats[0] == null || stats[1] == null || stats[0].size !== stats[1].size)
      return false
    return (await this.sha()) === (await that.sha())
  }

  async touch(args: UtimesArgs = {}): PromiseMaybe<this> {
    return this.trap("touch", () => this.touch_(args))
  }

  async touch_(args: UtimesArgs = {}): Promise<this> {
    await touch_({
      ...args,
      file: this,
      ensureFile: true
    })
    return this.clearThisAndParent()
  }

  async utimes(args: UtimesArgs = {}): PromiseMaybe<this> {
    return this.trap("utimes", async () => {
      await touch_({
        ...args,
        file: this,
        ensureFile: false
      })
      this.clearThisAndParent()
      return this
    })
  }

  async rm(errLogLevel: LogLevel = "warn"): PromiseMaybe<this> {
    return this.unlink(errLogLevel)
  }

  /**
   * Deletes the current file.
   * @return Promise<this> if unlink was successful
   */
  async unlink(errLogLevel: LogLevel = "warn"): PromiseMaybe<this> {
    return this.trap("unlink", () => this.unlink_(), errLogLevel)
  }

  /**
   * Deletes the current file.
   */
  unlinkSync(logLevel: LogLevel = "info") {
    return this.trapSync(
      "unlinkSync",
      () => {
        _fse.unlinkSync(this.nativePath)
        this.clearThisAndParent()
      },
      logLevel
    )
  }

  async unlink_(): Promise<this> {
    await _fse.unlink(this.nativePath)
    return this.clearThisAndParent()
  }

  /**
   * Delete files or directories. Should only be needed by tests or applied to
   * cache dirs.
   *
   * Note that the promise may be returned before the file op is actually
   * complete due to a (bug?) in fs-extra.
   */
  async rmrf(logLevel: LogLevel = "info"): PromiseMaybe<this> {
    return this.trap("rmrf", () => this.rmrf_(), logLevel)
  }

  async rmrf_(): Promise<this> {
    // retries may be required for windows
    return retryOnReject(
      async () => {
        const s = await this.stat({ refresh: true })
        if (s != null) {
          await _fse.rm(this.nativePath, {
            recursive: s.isDirectory(),
            force: true
          })
        }
        return this.clearThisAndParent()
      },
      {
        maxRetries: 3,
        timeoutMs: 0, // recursive deletions may take a while
        retryDelay: secondMs,
        errorIsRetriable: isRetriableError
      }
    )
  }

  /**
   * Supports gzip/gunzip/brotli/whatever transform you've got:
   *
   * THIS FILE IS DELETED AFTER THE PIPELINE COMPLETES
   */
  async #pipeTo(base: string, transform: stream.Transform) {
    return this.trap("pipeTo(" + base + ")", async () => {
      const dest = await this.sibling(base).ensureNew_()
      await pipeline([
        fs.createReadStream(this.nativePath, { autoClose: true }),
        transform,
        fs.createWriteStream(dest.nativePath, { autoClose: true })
      ])
      await this.unlink()
      return dest
    })
  }

  async gunzip(): PromiseMaybe<this> {
    return this.#pipeTo(stripSuffix(this.base, ".gz"), zlib.createGunzip())
  }

  async gzip(): PromiseMaybe<this> {
    return this.#pipeTo(this.base + ".gz", zlib.createGzip())
  }

  async compressBrotli(): PromiseMaybe<this> {
    return this.#pipeTo(this.base + ".br", zlib.createBrotliCompress())
  }

  ensureFile_(): Promise<this> {
    return _fse
      .ensureFile(this.nativePath)
      .then(() => this.clearThisAndParent())
  }

  ensureFile(): PromiseMaybe<this> {
    return this.trap("ensureFile", () => this.ensureFile_())
  }

  /**
   * @throws on error
   */
  ensureFileSync_() {
    _fse.ensureFileSync(this.nativePath)
    return this.clearThisAndParent()
  }

  /**
   * Returns a basename in this directory with this same .ext and prefixed with
   * this.name which either doesn't exist or is zero length.
   *
   * If `options.emptyIsNew` is `true`, the file may exist, but must be
   * zero-length. If `options.emptyIsNew` is `false`, the returned path will be
   * nonexistent.
   */
  async ensureNewNativePath(
    options: Omit<EnsureNewOptions, "nativePath">
  ): Promise<string> {
    return ensureNewNativePath_({ nativePath: this.nativePath, ...options })
  }

  /**
   * @throws if errors
   */
  ensureNew_(opts: Partial<EnsureNewOptions> = {}): Promise<this> {
    return this.ensureNewNativePath(opts).then(p => this.for(p))
  }

  /**
   * @throws if errors
   */
  ensureNewSync_(opts: Partial<EnsureNewOptions> = {}): this {
    return this.for(
      ensureNewNativePathSync_({ nativePath: this.nativePath, ...opts })
    )
  }

  /**
   * Make sure you encode `mode` in octal! 0o644
   */
  async chmod_(mode: string | number): Promise<this> {
    await _fse.chmod(this.nativePath, mode)
    return this.clear()
  }

  async chmod(mode: string | number): PromiseMaybe<this> {
    try {
      await this.chmod_(mode)
      return this
    } catch (err) {
      this.bflog().warn("failed to chmod to " + mode.toString(8), err)
      return
    }
  }

  chmodSync(mode: string | number): Maybe<this> {
    try {
      fs.chmodSync(this.nativePath, mode)
      return this
    } catch (err) {
      this.bflog().warn("failed to chmod to " + mode.toString(8), err)
      return
    }
  }

  zreadline(): LineReader {
    return fs
      .createReadStream(this.nativePath)
      .on("error", (err: any) => {
        throw new Error("Failed to read from " + this + ": " + err)
      })
      .pipe(zlib.createGunzip())
      .on("error", (err: any) => {
        throw new Error("Failed to gunzip " + this + ": " + err)
      })
      .pipe(new LineReader())
  }

  async siblingWithSameContents(): PromiseMaybe<this> {
    return this.parent().childWithSameContents(this)
  }

  async childWithSameContents(target: BaseFile): PromiseMaybe<this> {
    return time("fs.childWithSameContents", async () => {
      if (!(await this.isDirectory())) return

      const targetSize = await target.size()
      if (targetSize == null) return

      // Only look for a same-named file if the parent directories are different:
      if (!this.eql(target.parent())) {
        const withSameName = this.join(target.base)
        // SWEET we don't have to this.readdir()!
        if (await target.matchesContent(withSameName)) return withSameName
      }

      const sameSize = await this.children(
        async ea =>
          !ea.isNameHidden() &&
          targetSize === (await ea.size()) &&
          // Don't include target!
          ea.nativePath !== target.nativePath
        // (we're not normalizing or case-insensitive matching here: there shouldn't be a need to, and that will be wrong on non-ci filesystems.)
      )

      // Don't need to sha target if none are the same size:
      if (isEmpty(sameSize)) return

      const targetSha = await target.sha()
      if (targetSha == null) return

      // Assume similar names are more likely to be the same, so try those first.
      // if we have to check _64_ files with the same exact size, something screwy is going on: give up.
      for (const child of sameSize
        .sort((a, b) => -diceCoeff(a.base, b.base))
        .slice(0, 64)) {
        if ((await child.sha()) === targetSha) {
          return child
        }
      }
      return
    })
  }

  firstMatchingLine(re: RegExp): PromiseMaybe<RegExpMatchArray> {
    const d = new Deferred<Maybe<RegExpMatchArray>>(
      "firstMatchingLine(" + this + ")"
    )
    const r = fs.createReadStream(this.nativePath, { flags: "r" })
    r.on("error", (err: any) => {
      if (err.errno === -2 || err.code === "ENOENT") {
        void d.maybeResolve(undefined)
        r.close()
      } else {
        void d.maybeReject(err)
      }
    })
    r.on("close", () => d.maybeResolve(undefined))
    void onDataChunked(r, newlineRe(), ea => {
      const m = re.exec(ea)
      if (m != null) {
        void d.maybeResolve(m)
        r.close()
      }
    })
    return d.promise
  }

  contemporary(b: BaseFile, maxDiffMs: number): Promise<boolean> {
    return thenMap2Or(
      this.statTimes(),
      b.statTimes(),
      (arr1, arr2) => {
        for (const i of arr1) {
          for (const j of arr2) {
            if (closeTo(i, j, maxDiffMs)) {
              return true
            }
          }
        }
        return false
      },
      () => false
    )
  }

  /**
   * NOTE: recursive is only supported if Settings.useFsWatch is true.
   */
  watchUntil<T>(opts: {
    f: (event?: WatchEventType, filename?: string) => PromiseMaybe<T>
    timeoutMs: number
    persistent: boolean
    recursive: boolean
    intervalMs?: number
    accept?: (t: Maybe<T>) => boolean
  }): Deferred<T> {
    const accept = (opts.accept ??= (t: Maybe<T>) => t != null && t !== false)
    const d = new Deferred<T>("watchUntil(" + this.nativePath + ")")
    if (opts.timeoutMs > 0) {
      void d.setTimeout(opts.timeoutMs, !opts.persistent)
    }
    if (_useFsWatch) {
      try {
        const watcher = watch(
          this.nativePath,
          {
            persistent: opts.persistent,
            recursive: opts.recursive,
            encoding: "utf8"
          },
          async (event, filename) => {
            try {
              const result = await opts.f(event, denull(filename))
              if (accept(result)) {
                void d.resolve(result!)
              }
            } catch (error) {
              void d.reject(error)
            }
          }
        )
        void d.finally(() => {
          watcher.close()
        })
      } catch (error) {
        opts.intervalMs ??= 500
        this.bflog().warn("fs.watch failed, using polling", { error, opts })
      }
    } else {
      // Force polling:
      opts.intervalMs ??= 500
      this.bflog().warn("watchUntil(): fsWatch disabled, using polling", {
        opts
      })
    }

    if (gt0(opts.intervalMs)) {
      const interval = setInterval(async () => {
        try {
          const result = await opts.f()
          if (accept(result)) {
            void d.resolve(result!)
          }
        } catch (error) {
          void d.reject(error)
        }
      }, opts.intervalMs)
      if (!opts.persistent) {
        interval.unref()
      }
      void d.finally(() => {
        clearInterval(interval)
      })
    }

    return d
  }
}

export function execDir(): BaseFile {
  return BaseFile.for(process.execPath).parent()
}

// This is used to break a circular dependency between fs and Settings:
let _useFsWatch: boolean = true

// This is set in SettingsDefaults to watch Settings.useFsWatch:
export function useFsWatch(b: boolean) {
  _useFsWatch = b
}
