import { Rate } from "batch-cluster"
import { setInterval } from "timers"
import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { ee } from "../event/EventEmitter"
import { rateStats } from "../math/Rate"
import { maxConcurrentImports, maxCpus } from "./MaxCpus"

const logger = lazy(() => mkLogger("work.WorkTimeout"))

export const timeoutRate = lazy(
  () => new Rate(5 * minuteMs, isTest ? 100 : ShortCommandTimeoutMs)
)

const refreshEveryMinute = lazy(() =>
  // Make sure if we recover, we throttle back up:
  // (this was an EndableInterval, but that caused a deploop (!?))
  setInterval(refresh, minuteMs).unref()
)

function refresh() {
  // we need to update these every minute if we've had any (soft or hard)
  // timeouts.

  // We want .refresh(), not just .unset(), so .watchLater() listeners gets
  // invoked:
  maxCpus.refresh()
  maxConcurrentImports.refresh()
  refreshEveryMinute()
}

export function onTimeout() {
  const r = timeoutRate()
  r.onEvent()
  logger().warn("onTimeout()", rateStats(r))
  refresh()
}

later(() => {
  ee().on("clearCache", () => {
    maxCpus.unset()
    maxConcurrentImports.unset()
    timeoutRate.unset()
  })
})
