import _path, { join } from "path"
import { lazy } from "../../core/Lazy"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { Halt } from "../Halt"
import { mkLogger } from "../Logger"
import { LogLevels } from "../log/LogLevel"
import { BaseFile } from "./BaseFile"
import {
  CACHEDIR_TAG,
  isCachedirTagDirectory,
  isCachedirTagFile
} from "./CachedirTag"
import { FileCache } from "./FileCache"
import { readdir, readdirCached } from "./Readdir"
import { SimpleDirent } from "./SimpleDirent"
import {
  SimpleFileOrString,
  isSimpleFile,
  someSelfOrAncestor
} from "./SimpleFile"
import { exists } from "./Stat"

const logger = lazy(() => mkLogger("fs.hasNoMedia()"))

// We're making the dot optional (although that isn't part of the spec)
const nomediaRe = /^\.?NoMedia$/i

const NoMedia = "NoMedia"

function variants(s: string) {
  return [s, s.toLowerCase(), s.toUpperCase()]
}

const NoMediaNames = Object.freeze([
  ...variants("." + NoMedia),
  ...variants(NoMedia)
])

export function isNoMediaName(basename: string): boolean {
  return nomediaRe.exec(basename) != null
}

const noMediaDirsCache = lazy(
  () => new FileCache<boolean>({ name: "fs.noMediaDirsCache" })
)

export async function hasNoMediaRecursive(
  f: Maybe<SimpleFileOrString>
): PromiseMaybe<boolean> {
  if (f == null) return false
  const sf = isSimpleFile(f) ? f : BaseFile.for(f)
  return someSelfOrAncestor(sf, hasNoMedia)
}

export async function isNoMedia(f: SimpleFileOrString): Promise<boolean> {
  if (f == null) return false
  const sf = isSimpleFile(f) ? f : BaseFile.for(f)
  return isNoMediaName(sf.base) || (await isCachedirTagFile(sf))
}

export async function hasNoMedia(f: SimpleFileOrString): Promise<boolean> {
  if (f == null) return false
  const sf = isSimpleFile(f) ? f : BaseFile.for(f)

  if (sf.base === CACHEDIR_TAG) {
    return logger().tap({
      msg: sf.nativePath + " is a CACHEDIR.TAG file",
      // return FALSE if the header doesn't exist?
      result: await isCachedirTagFile(sf)
    })
  }

  // Are we an actual NoMedia file?

  if (isNoMediaName(sf.base)) {
    // this doesn't need to be cached, it's almost a no-op:
    return logger().tap({ msg: sf + " basename is NoMedia", result: true })
  }

  if (await sf.isDirectory()) {
    return noMediaDirsCache().getOrSetAsync(sf.nativePath, () =>
      dirHasNoMediaChild(sf.nativePath)
    )
  } else {
    return false
  }
}

async function dirHasNoMediaChild(nativePath: string): Promise<boolean> {
  const halt = new Halt()

  // If we can only hit the readdir cache, that'd be great:
  const cached: Maybe<SimpleDirent[]> = readdirCached(nativePath)
  if (cached != null) {
    return someIsNoMedia(nativePath, cached, halt)
  }

  // Otherwise, race:
  const result: boolean = await Promise.race([
    _dirHasNoMediaChild(nativePath, halt),
    _readdirHasNoMediaChild(nativePath, halt)
  ])
  halt.halt() // < stop the loser
  return result
}

async function _dirHasNoMediaChild(
  nativePath: string,
  halt?: Halt
): Promise<boolean> {
  for (const ea of NoMediaNames) {
    if (halt?.halted === true) {
      return null as any
    }
    if (await exists(_path.join(nativePath, ea), LogLevels.trace)) {
      return logger().tap({
        msg: nativePath + " is a directory and has a noMedia child, " + ea,
        result: true
      })
    }
  }
  if (halt?.halted === true) return null as any
  return isCachedirTagDirectory(nativePath)
}

async function someIsNoMedia(
  dir: string,
  arr: SimpleDirent[],
  halt?: Halt
): Promise<boolean> {
  for (const ea of arr) {
    if (halt?.halted === true) return null as any
    if (isNoMediaName(ea.basename)) {
      return logger().tap({
        msg: dir + " has a noMedia child, " + ea.basename,
        result: true
      })
    }
    if (ea.basename === CACHEDIR_TAG) {
      if (await isCachedirTagFile(join(dir, ea.basename))) {
        return true
      }
    }
  }
  return false
}

async function _readdirHasNoMediaChild(
  nativePath: string,
  halt?: Halt
): Promise<boolean> {
  const arr = await readdir(nativePath)
  return arr == null ? false : someIsNoMedia(nativePath, arr, halt)
}
