import { Tags } from "exiftool-vendored"

type TagKeys = keyof Tags | "creationTime" | "photoTakenTime" | "HistoryWhen"

export const IgnoredCapturedAtTags = [
  "creationTime", // Google Photos upload time
  "HistoryWhen", // is when history records happen, NOT when the file was captured!
  "MediaModifyDate", // is the last media edit time
  "MetadataDate", // is the last time the _metadata was edited_. See https://forum.photostructure.com/t/combining-images/524/8 for details
  "SubSecTime", // is fractional seconds (not the full date and time) for ModifyDate
  "TrackModifyDate"
] as const satisfies readonly TagKeys[]

/**
 * These tags should only be used if "standard" captured-at tags have no valid
 * values. The first valid ExifDateTime value will be used.
 */
export const ProblematicCapturedAtTags = [
  // (called DateTimeDigitized by the EXIF spec)
  // https://forum.photostructure.com/t/incorrect-date-assigned-to-photo/419
  // and
  // https://forum.photostructure.com/t/photostructure-ignored-the-date-metadata-of-some-photos-marked-them-as-taken-in-2002/340
  // Note that this tag may have an incorrect zone offset (it may incorrectly
  // be UTC or local system time), so it's "problematic".
  "SubSecCreateDate",
  "SubSecMediaCreateDate",
  "SubSecModifyDate",

  "CreateDate",
  "MediaCreateDate", // < may have incorrect timezone like CreateDate
  "ModifyDate", // is called DateTime by the EXIF spec, and may be the last edit time, or the actual captured-at (see the QuickTake DC50 example)
  "DateTime",
  "DateTimeCreated", // composite tag, sometimes has wrong timezone.

  "photoTakenTime", // < Google Takeout (not really reliable, though)

  "TrackCreateDate", // < may not have correct timezone

  "CreationTime" // < set by some screenshot software, and _MAY INCLUDE DATETIME!_ . May be rendered as "Wed 16 Jun 2021 03:34:46 PM PDT"
] as const satisfies readonly Exclude<
  TagKeys,
  (typeof IgnoredCapturedAtTags)[number] // < prevents duplicate keys
>[]

export const DefaultCapturedAtTagsConst = [
  // By the specification, DateTimeOriginal should be the time of the
  // shutter actuation, and CreateDate should be the time that the file
  // was written to the memory card (but not all mfrs follow the spec)
  // http://u88.n24.queensu.ca/exiftool/forum/index.php?topic=2568.0

  // https://exiftool.org/TagNames/EXIF.html

  "CreationDate", // only found on iPhone movies, but they include timezone offsets, which we really want. See https://exiftool.org/forum/index.php?topic=12837.msg69410#msg69410

  "DateTimeOriginal", // date/time when original image was taken. Quicktime:DateTimeOriginal will also include the timezone offset.
  "SubSecDateTimeOriginal", // (fractional seconds for DateTimeOriginal)

  "DateTimeDigitized",

  "OriginalCreateDateTime",

  "DigitalCreationDateTime" // composite tag
] as const satisfies readonly Exclude<
  TagKeys,
  | (typeof IgnoredCapturedAtTags)[number] // < prevents duplicate keys
  | (typeof ProblematicCapturedAtTags)[number] // < prevents duplicate keys
>[]
