export const AllFiletypes = {
  ...BrowserFiletypes,
  ...SidecarFiletypes,
  ...SharpImageFiletypes,
  ...RawImageFiletypes,
  ...VideoFiletypes
} as const

export const AssetFiletypes = {
  ...SharpImageFiletypes,
  ...HeifFiletypes,
  ...RawImageFiletypes,
  ...VideoFiletypes
} as const

export const ExifFiletypes = {
  ...AssetFiletypes,
  ...SidecarFiletypes
} as const

const Ext2mime = lazy(() => {
  const result = {} as Record<string, string>
  for (const [mime, exts] of Object.entries(AllFiletypes)) {
    for (const ext of exts) {
      result[ext] = mime
    }
  }
  return result
})

const ExtAliases = lazy(() => {
  const result = {} as Record<string, string[]>
  for (const exts of Object.values(AllFiletypes)) {
    for (const ext of exts) {
      result[ext] = exts as string[]
    }
  }
  return result
})

export const AllFileExts = lazy(() =>
  uniq(flatten(Object.values(AllFiletypes as any)))
)

export const AssetFileExts = lazy(() =>
  uniq(flatten(Object.values(AssetFiletypes as any)))
)

export function extMimetype(ext: MaybeFileish): Maybe<string> {
  return Ext2mime()[normalizeExt(ext) ?? ""]
}

export function mimetypeExt(mime: Maybe<string>): Maybe<string> {
  return (AllFiletypes as any)[toS(mime).trim().toLowerCase()]?.[0]
}

export function getExtAliases(ext: MaybeFileish): Maybe<string[]> {
  return ExtAliases()[normalizeExt(ext) ?? ""]
}

export const OldBrowserMimetypes = [
  "image/gif",
  "image/jpeg",
  "image/png"
] as const

export const CurrentBrowserMimetypes = [
  ...OldBrowserMimetypes,
  "image/svg+xml",
  "image/webp",
  "video/mp4",
  "video/webm"
] as const

const ext2types = lazy(() => {
  const m = new MultiMap<string, ExtType>()

  for (const { exts, type } of [
    { exts: SharpImageFiletypes, type: ExtTypes.Sharp },
    { exts: HeifFiletypes, type: ExtTypes.HEIF },
    { exts: RawImageFiletypes, type: ExtTypes.RawImage },
    { exts: VideoFiletypes, type: ExtTypes.Video },
    { exts: SidecarFiletypes, type: ExtTypes.Sidecar },
    { exts: AssetFiletypes, type: ExtTypes.AssetFile },
    { exts: ExifFiletypes, type: ExtTypes.Exif }
  ]) {
    for (const ext of flatten(Object.values(exts))) {
      m.add(ext as any, type)
    }
  }

  for (const mimetype of OldBrowserMimetypes) {
    for (const ext of AllFiletypes[mimetype] ?? []) {
      m.add(ext, ExtTypes.SupportedByOldBrowser)
    }
  }

  for (const mimetype of CurrentBrowserMimetypes) {
    for (const ext of AllFiletypes[mimetype] ?? []) {
      m.add(ext, ExtTypes.SupportedByCurrentBrowser)
    }
  }

  return m
})

export function equivalentExts(
  pathlike1: MaybeFileish,
  pathlike2: MaybeFileish
) {
  const ext1 = normalizeExt(pathlike1)
  const ext2 = normalizeExt(pathlike2)
  if (ext1 == null || ext2 == null) return false
  if (ext1 === ext2) return true
  return getExtAliases(ext1)?.includes(ext2) ?? false
}

export function extTypes(ext: MaybeFileish): Maybe<ExtType[]> {
  const e = normalizeExt(ext)
  return e == null ? undefined : ext2types().get(e)
}

export function isExtType(ext: MaybeFileish, extType: ExtType): boolean {
  return extTypes(ext)?.includes(extType) ?? false
}

export function mimeRootType(ext: MaybeFileish): Maybe<"image" | "video"> {
  const arr = extTypes(ext)
  return arr == null
    ? undefined
    : arr.includes(ExtTypes.Sharp) || arr.includes(ExtTypes.RawImage)
    ? "image"
    : arr.includes(ExtTypes.Video)
    ? "video"
    : undefined
}

export function isRawImageExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.RawImage)
}
export function isSharpExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.Sharp)
}
export function isVideoExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.Video)
}
export function isAssetFileExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.AssetFile)
}
export function isExifExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.Exif)
}
export function isSupportedByCurrentBrowserExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.SupportedByCurrentBrowser)
}
export function isSupportedByOldBrowserExt(ext: MaybeFileish): boolean {
  return isExtType(ext, ExtTypes.SupportedByOldBrowser)
}

export function stripExt(
  base: string,
  extType: ExtType = ExtTypes.AssetFile
): string {
  const ext = splitLast(base, ".")
  return isExtType(ext, extType) ? base.slice(0, -(ext.length + 1)) : base
}
