import { lazy } from "../../core/Lazy"
import { secondMs } from "../../fe/Date"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { sortIgnoreCase } from "../String"
import { stdout_ } from "../child/ChildProcess"
import { fsutil } from "../fs/PathTo"
import { PowerShell } from "../pwsh/PowerShell"

const logger = lazy(() => mkLogger("volumes.MountpointsWin"))

// we really do want to lazy() this: it's used both by mountpoints() and dfWin()
export const mountpointsWin = lazy(async () => {
  try {
    // powershell should be easier on the system, as it doesn't involve a fork
    return await thenOrTimeoutError(mountpointsWinPwsh(), ShortCommandTimeoutMs)
  } catch (error) {
    logger().warn("mountpointsWinPwsh()", { error })
    return mountpointsWinFsutil()
  }
})

export async function mountpointsWinPwsh() {
  // Note that Get-PSDrive can be quite slow if you don't specificy just the
  // Root -Property, but seems to be quick (~10ms) when asking just for Root.
  const arr = await PowerShell.instance().executeJsonToA(
    "Get-PSDrive -PSProvider FileSystem | Select-Object -Property Root"
  )
  // [{"Root":"C:\\"},{"Root":"D:\\"},{"Root":"Q:\\"},{"Root":"R:\\"}]
  return arr == null
    ? undefined
    : sortIgnoreCase(arr.map(ea => ea.Root as string))
}

const driveletterRe = /\s([A-Z]:\\)/g

/**
 * fsutil-based list of all currently active drive letters
 *
 * @return ["C:\", "D:\", ...]
 */
export async function mountpointsWinFsutil() {
  // Note that `fsutil fsinfo drives` seems to be about an order of magnitude
  // faster than ` wmic logicaldisk get caption`.

  // Example output: Drives: C:\ H:\ I:\""
  const sout = await stdout_(fsutil(), ["fsinfo", "drives"], {
    timeoutMs: 10 * secondMs
  })
  const result: string[] = []
  let match: RegExpExecArray | null
  while ((match = driveletterRe.exec(sout)) !== null) {
    result.push(match[1])
  }
  return sortIgnoreCase(result)
}
