export const DefaultWatchedStatFields: (keyof fs.Stats)[] = [
  "ino",
  "size",
  "mtimeMs",
  "birthtimeMs"
]

class WatchMgr extends EndableInterval {
  static readonly instance = new WatchMgr()
  readonly path2watcher = new Map<string, fs.FSWatcher>()
  readonly id2watchers = new Map<string, FsWatcher>()

  private constructor() {
    super({
      name: "fs.WatchMgr",
      callback: () => this.vacuum(),
      intervalMs: minuteMs,
      unref: true
    })
    this.onEnds.push(() => {
      for (const [k, v] of this.path2watcher) {
        try {
          v.close()
        } catch (err) {
          this.logger.warn("failed to close FsWatcher for " + k, err)
        }
      }
    })
  }

  vacuumEnded() {
    for (const [id, w] of this.id2watchers) {
      if (w.ended) this.id2watchers.delete(id)
    }
  }

  registeredPaths() {
    return new Set(
      [...this.id2watchers.values()].map(ea => ea.target.nativePath)
    )
  }

  watchedPaths() {
    return [...this.path2watcher.keys()]
  }

  vacuum() {
    this.vacuumEnded()
    const oldPaths = diff(this.watchedPaths(), this.registeredPaths())
    // remove any now-unwatched paths:
    for (const k of oldPaths) {
      try {
        this.logger.warn("closing orphaned FsWatcher for " + k)
        this.path2watcher.get(k)?.close()
        this.path2watcher.delete(k)
      } catch (err) {
        this.logger.warn("failed to close FsWatcher for " + k, err)
      }
    }
    const unwatchedPaths = diff(this.registeredPaths(), this.watchedPaths())
    // Are there any paths we can watch now?
    for (const np of unwatchedPaths) {
      this.maybeWatch(np)
    }

    this.logger.debug("vacuum() complete", {
      watchedPaths: [...this.watchedPaths()],
      registeredPaths: [...this.registeredPaths()],
      oldPaths: [...oldPaths],
      unwatchedPaths: [...unwatchedPaths]
    })
  }

  maybeWatch(nativePath: string) {
    if (this.watchersForPath(nativePath).length === 0) {
      throw new Error("no registered watchers for " + nativePath)
    }
    if (this.path2watcher.get(nativePath) != null) return

    // only add a FSWatcher if the file or dir exists
    if (fs.existsSync(nativePath)) {
      const arr = this.watchersForPath(nativePath)
      const persistent = or(arr.map(ea => ea.args.persistent))
      this.logger.info("Setting up fs.watch for " + nativePath)
      const fsw = fs.watch(nativePath, { persistent }, () =>
        this.onChange(nativePath)
      )
      this.path2watcher.set(nativePath, fsw)
      fsw.on("close", () => {
        this.path2watcher.delete(nativePath)
      })
      fsw.on("error", error => {
        this.path2watcher.delete(nativePath)
        this.logger.warn("fs.watch failed", {
          error,
          watchers: this.watchersForPath(nativePath)
            ?.filter(ea => !ea.ended)
            .map(ea => ea.name)
        })
      })
    }
  }

  registerWatcher(w: FsWatcher) {
    this.id2watchers.set(w.id, w)
    this.maybeWatch(w.target.nativePath)
  }

  watchersForPath(nativePath: string) {
    const result: FsWatcher[] = []
    for (const ea of this.id2watchers.values()) {
      if (ea.target.eql(nativePath)) {
        result.push(ea)
      }
    }
    return result
  }

  onChange(nativePath: string) {
    for (const ea of this.id2watchers.values()) {
      if (ea.target.eql(nativePath)) {
        ea.debouncedCheck()
      }
    }
  }
}

// TODO: use https://www.npmjs.com/package/@parcel/watcher ?

export type FsWatcherArgs = ConstructorParameters<typeof FsWatcher>[0]

export class FsWatcher extends EndableInterval {
  readonly id = uid()
  readonly target: PosixFile
  #partialStatPrior: Maybe<Partial<fs.Stats>>
  #shaPrior: Maybe<string>
  #childrenPrior: Maybe<string[]>

  constructor(
    readonly args: {
      target: string | SimpleFile
      onChange: () => void
      maxPollIntervalMs: number
      initialDelayMs?: number
      sha?: boolean
      watchDebounceMs?: number
      applyOnChangeAtSetupIfExists?: boolean
      applyOnChangeIfTargetMissing?: boolean
      persistent?: boolean
    }
  ) {
    super({
      name: `fs.FileWatcher(${toNativePath_(args.target)})`,
      callback: () => this.debouncedCheck(),
      intervalMs: args.maxPollIntervalMs,
      rank: EndableRanks.first,
      initialDelayMs: args.initialDelayMs
    })
    this.target = PosixFile.for(args.target)

    const watchDebounceMs =
      args.watchDebounceMs ?? Settings.watchDebounceMs.valueOrDefault
    const maxPollIntervalMs = args.maxPollIntervalMs
    if (gt0(maxPollIntervalMs) && maxPollIntervalMs <= watchDebounceMs) {
      args.maxPollIntervalMs = watchDebounceMs + 100
      this.setIntervalMs(args.maxPollIntervalMs)
      this.logger.error(
        "Bad configuration: watchDebounceMs must be less than maxPollIntervalMs. Increasing maxPollIntervalMs.",
        {
          watchDebounceMs,
          maxPollIntervalMs,
          newMaxPollIntervalMs: args.maxPollIntervalMs
        }
      )
    }
    this.onEnds.push(() => {
      this.reset()
      later(() => WatchMgr.instance.vacuum())
    })
    this.logger.debug("new FsWatcher()", {
      watchDebounceMs,
      maxPollIntervalMs: args.maxPollIntervalMs
    })
    void this.setup()
    WatchMgr.instance.registerWatcher(this)
  }

  get nativePath() {
    return this.target.nativePath
  }

  readonly setup: MemoizedThunk<Promise<FsWatcher>> = lazy(async () => {
    const s = statSync(this.nativePath)
    this.#partialStatPrior =
      s == null ? undefined : pick(s, ...DefaultWatchedStatFields)
    if (true === s?.isDirectory()) await this.#checkDirChanged()
    if (true === s?.isFile()) await this.#checkFileChanged()
    this.logger.info("setup()", {
      stat: this.#partialStatPrior,
      sha: this.#shaPrior,
      children: this.#childrenPrior
    })
    if (s != null && true === this.args.applyOnChangeAtSetupIfExists) {
      this.args.onChange()
    }
    return this
  })

  // exposed for tests
  get partialStatPrior() {
    return this.#partialStatPrior
  }

  get debounceMs(): number {
    return this.debouncedCheck.timeoutMs()
  }

  readonly debouncedCheck = debounce(
    () => this.check(),
    this.args.watchDebounceMs ?? Settings.watchDebounceMs.valueOrDefault
  )

  forcedCheck() {
    this.debouncedCheck.reset()
    return this.check.force()
  }

  async #checkDirChanged() {
    const children = await thenOrTimeout(
      fse.readdir(this.nativePath),
      Settings.statTimeoutMs.valueOrDefault
    )
    if (children === Timeout) {
      this.logger.warn("Failed to read directory: timeout", {
        timeoutMs: Settings.statTimeoutMs.valueOrDefault
      })
      this.#childrenPrior = undefined
      return false
    }
    const now = sortIgnoreCase(children)
    if (!eql(this.#childrenPrior, now)) {
      this.logger.debug("checkDirChanged(): children changed", {
        now,
        prior: this.#childrenPrior
      })
      this.#childrenPrior = now
      return true
    } else {
      return false
    }
  }

  async #checkFileChanged() {
    if (true !== this.args.sha) return true
    const sha = await fileSha_(this.nativePath).catch(() => undefined)
    if (sha != null && sha === this.#shaPrior) {
      return false
    } else {
      this.#shaPrior = sha
      return true
    }
  }

  // exposed for tests
  readonly check = oneAtATime({
    fn: async () => {
      if (this.ended) return

      this.logger.debug("check(): starting", {
        prior: this.#partialStatPrior
      })

      const partialStatPrior = this.#partialStatPrior
      const statNow = await statMaybe(this.nativePath)

      if (statNow == null) {
        if (
          this.#partialStatPrior != null &&
          true === this.args.applyOnChangeIfTargetMissing
        ) {
          this.target.clear()
          this.args.onChange()
        }
        this.logger.debug("check(): missing stat, closing file watcher")
        this.reset()
        // file or directory onChange shouldn't care when target is deleted
        return
      }

      later(() => WatchMgr.instance.registerWatcher(this))

      const partialStatNow = pick(statNow!, ...DefaultWatchedStatFields)
      const statEql = eql(partialStatPrior, partialStatNow)

      // .stat() on directories may not change when children are added or deleted, so we always have to check:
      const changed = statNow.isDirectory()
        ? await this.#checkDirChanged()
        : statEql
        ? false
        : statNow.isFile()
        ? await this.#checkFileChanged()
        : false

      // this.logger.log(result ? LogLevels.debug : LogLevels.trace, ".check()", {
      this.logger.debug(".check()", {
        changed,
        partialStatNow,
        partialStatPrior,
        isDir: statNow.isDirectory(),
        isFile: statNow.isFile()
      })
      if (changed) {
        ee().emit("fileChanged", this.nativePath)
        this.#partialStatPrior = partialStatNow
        this.args.onChange()
      }
    }
  })

  reset() {
    // no infinite loops (where w.close() calls this.close()):
    this.#partialStatPrior = undefined
    this.#shaPrior = undefined
    this.#childrenPrior = undefined
  }
}
