import { uniq } from "../fe/Array"
import { Maybe } from "../fe/MaybeTypes"
import { MultiMap } from "../fe/MultiMap"
import * as o from "../fe/Object"
import { toA } from "../fe/toA"
import { diceCoeff } from "./StringSimilarity"

/**
 * ONLY BASE-ASCII CASE-INSENSITIVE!
 */
export class CaseInsensitiveValued<V> {
  private readonly m: MultiMap<string, string>
  constructor(
    readonly obj: o.Valued<V>,
    readonly synonyms: (key: string, value: V) => Maybe<string[]> = () =>
      undefined
  ) {
    this.m = new MultiMap()
    this.vacuum()
  }

  vacuum() {
    this.m.clear()
    for (const [k, v] of o.entries(this.obj)) {
      for (const key of uniq(
        [k, ...toA(this.synonyms(k, v))].map(ea => ea.toLowerCase())
      )) {
        this.m.add(key, k)
      }
    }
    this.m.vacuum()
  }

  lookup(key: string): Maybe<{ key: string; value: V }> {
    if (key == null) return
    const value = this.obj[key]
    return value != null
      ? { key, value }
      : this.getFirst(this.m.get(key.toLowerCase()))
  }

  lookupNearest(key: string, minDiceCoeff = 0.4) {
    const exact = this.lookup(key)
    if (exact != null) return exact
    let bestCoeff = -1
    let best: Maybe<{ key: string; value: V }>
    for (const [k, arr] of this.m.entries()) {
      const coeff = diceCoeff(k, key)
      if (coeff > minDiceCoeff && coeff > bestCoeff) {
        const newBest = this.getFirst(arr)
        if (newBest != null) {
          best = newBest
          bestCoeff = coeff
        }
      }
    }

    return best
  }

  get(key: string): Maybe<V> {
    return this.lookup(key)?.value
  }

  getFirst(keys: Maybe<string[]>): Maybe<{ key: string; value: V }> {
    if (keys == null) return
    for (const k of keys) {
      // return the first non-null value:
      const value = this.obj[k]
      if (value != null) return { key: k, value }
    }
    return
  }

  delete(key: string) {
    const lc = key.toLowerCase()
    const arr = this.m.get(lc)
    if (arr != null) {
      for (const k of arr) {
        delete this.obj[k]
      }
      this.m.delete(lc)
    }
  }

  pick(...keys: string[]): Record<string, V> {
    const result: Record<string, V> = {}
    for (const key of keys) {
      const l = this.lookup(key)
      if (l?.key != null) {
        result[l.key] = l.value
      }
    }
    return result
  }
}
