import { lazy } from "../../core/Lazy"
import { compact } from "../../fe/Array"
import { minuteMs } from "../../fe/Date"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { allNotBlank } from "../Array"
import { mkLogger } from "../Logger"
import { isSyncService } from "../ServiceNames"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { unoctal } from "../String"
import { filterAsync, thenMap } from "../async/Promise"
import { FsWatcher } from "../fs/FsWatcher"
import { readLines_ } from "../fs/ReadLines"
import { isEmptyFile, isReadableDirectory } from "../fs/Stat"
import { LogLevels } from "../log/LogLevel"
import { isLinux } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { dfPosixRaw } from "./DfPosixRaw"
import { gioVolumes, isGioSupported } from "./Gio"
import { mountpoints } from "./Mountpoints"
import { isExcludedMountpoint } from "./PosixVolumes"

const logger = lazy(() => mkLogger("volumes.MountpointsPosix"))

export async function mountpointsPosix() {
  const result =
    (await readProcMounts()) ??
    (await thenMap(dfPosixRaw({ localsOnly: false }), vols =>
      vols.map(vol => vol.mountpoint)
    ))
  if (result != null && (await isGioSupported())) {
    try {
      await thenMap(gioVolumes(), gioVols =>
        result.push(...gioVols.map(ea => ea.mountpoint))
      )
    } catch (err) {
      // may fail due to timeouts. It's better to have *some* volumes than crash:
      logger().warn("Failed to fetch gio volumes", err)
    }
  }
  // HEY FUTURE ME: mountpoints() does filtering for us.
  return result
}

const ProcMounts = "/proc/mounts"

export function defaultFilesystemTypePredicate(fsType: string) {
  return !Settings.excludedFilesystemTypes.has(fsType)
}

function defaultMountpointPredicate(mountpoint: string): SyncOrAsync<boolean> {
  return (
    !isExcludedMountpoint(mountpoint) &&
    (Settings.validateMountpoints.valueOrDefault
      ? isReadableDirectory(mountpoint)
      : true)
  )
}

export async function readProcMounts(
  procMounts = ProcMounts,
  filesystemTypePredicate = defaultFilesystemTypePredicate,
  mountpointPredicate = defaultMountpointPredicate
): PromiseMaybe<string[]> {
  if (!isLinux) return
  try {
    const lines = await readLines_(procMounts)
    const tokens = compact(
      lines.map(line => {
        // this whitespace split is safe because linux will escape whitespace with octals:
        const [fs, mp] = line.split(/\s+/)
        return allNotBlank(fs, mp)
          ? ([fs, unoctal(mp)] as [string, string])
          : undefined
      })
    )
    const result = await filterAsync({
      name: "mountpoints filter",
      arr: tokens,
      f: async ([fs, mp]) => {
        const fsOK = filesystemTypePredicate(fs)
        const mpOK = await mountpointPredicate(mp)
        return logger().tap({
          level: LogLevels.trace,
          msg: "readProcMounts().predicate filters",
          result: fsOK && mpOK,
          meta: { fs, mp, fsOK, mpOK }
        })
      }
    })

    return logger().tap({
      msg: "readProcMounts()",
      result: result.map(([, ea]) => ea),
      meta: {
        procMounts
      }
    })
  } catch (error) {
    logger().info("Failed to read /proc/mount", error)
    return
  }
}

export const maybeWatchProcMounts = lazy(async () =>
  !isLinux || !isSyncService() || (await isEmptyFile(ProcMounts))
    ? undefined
    : new FsWatcher({
        target: ProcMounts,
        maxPollIntervalMs: minuteMs,
        onChange: () => {
          logger().info("detected change in " + ProcMounts)
          mountpoints.unset()
        },
        initialDelayMs: ShortCommandTimeoutMs,
        sha: true
      })
)
