import { MaybeNull } from "./MaybeTypes"
import { toA } from "./toA"

/**
 * Compacts and flattens ONE LEVEL
 */
export function flatten<T>(
  arr: (MaybeNull<T> | MaybeNull<T>[])[],
  result: T[] = []
): T[] {
  for (const ea1 of toA(arr)) {
    if (ea1 != null) {
      for (const ea2 of toA(ea1)) {
        if (ea2 != null) result.push(ea2)
      }
    }
  }
  return result
}
