import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { MultiMap } from "../../fe/MultiMap"
import { toA } from "../../fe/toA"

export function l33t(s: string): string {
  let result = ""
  const m = a2l()
  for (const ea of s) {
    result += m.get(ea)?.[0] ?? ea
  }
  return result
}

const l2a = lazy(() => {
  const m = new MultiMap<string, string>()
  for (const { l, a } of [
    // { l: "!", a: ["i"] },
    // { l: "(", a: ["c"] },
    // { l: "@", a: ["a"] },
    // { l: "&", a: ["g"] },
    // { l: "+", a: ["t"] },
    // { l: "|", a: ["i", "l"] },
    // { l: "$", a: ["s"] },
    { l: "0", a: ["o"] },
    { l: "1", a: ["i", "l"] },
    { l: "2", a: ["z"] },
    { l: "3", a: ["e"] },
    { l: "4", a: ["a"] },
    { l: "5", a: ["s"] },
    { l: "6", a: ["b"] },
    { l: "7", a: ["t"] },
    { l: "8", a: ["b"] },
    { l: "9", a: ["g", "q"] }
  ]) {
    m.add(l, ...a)
  }
  return m
})

const a2l = lazy(() => {
  const m = l2a().inverse()
  for (const ea of m.values()) {
    ea.sort()
  }
  return m
})

export function unl33t(s: string): string[] {
  if (blank(s)) return [""]
  const origPrefix = s.substring(0, 1)
  const prefixes = [origPrefix, ...toA(l2a().get(origPrefix))]
  const suffixes = unl33t(s.substring(1))
  return flatten(suffixes.map(suff => prefixes.map(pref => pref + suff)))
}
