import { mkdirpSync } from "fs-extra"
import { compact } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { isReadWriteableDirectorySync } from "../fs/StatSync"

export interface FirstDirArg {
  dir: Maybe<string>
  /**
   * If set, and this dir does not exist, don't mkdirp `dir`.
   */
  preexistingDir?: Maybe<string>
}

/**
 * Return the first directory that exists (or whose required parent exists), and
 * that can be `mkdir -p`'ed with sufficient permissions.
 */
export function firstDir({
  dirs,
  desc
}: {
  dirs: Maybe<FirstDirArg>[]
  desc: string
}): Maybe<string> {
  for (const { dir, preexistingDir } of compact(dirs)) {
    if (blank(dir)) continue
    if (isReadWriteableDirectorySync(dir)) return dir
    if (blank(preexistingDir) || isReadWriteableDirectorySync(preexistingDir)) {
      try {
        mkdirpSync(dir)
        return dir
      } catch (err) {
        console.error("Failed to mkdir for " + desc, err)
      }
    }
  }
  return
}

export function filterDirs({
  dirs,
  desc
}: {
  dirs: Maybe<FirstDirArg>[]
  desc: string
}): string[] {
  return compact(dirs.map(dir => firstDir({ dirs: [dir], desc })))
}
