import { lazy } from "../../core/Lazy"
import { processName } from "../ServiceNames"
import { logFilter } from "./LogFilter"
import { SimpleLogger } from "./Logger"
import { LogLevel } from "./LogLevel"
import { pushLogEntries } from "./LogTailEntries"

/**
 * Works in concert with LogTail so both local and remote log messages are
 * emitted in chronological order.
 */
export class ConsoleLogger implements SimpleLogger {
  static readonly instance = lazy(() => new ConsoleLogger())

  log(level: LogLevel, context: string, msg: string, meta?: any) {
    if (this.enabled(level, context)) {
      pushLogEntries({
        ts: Date.now(),
        l: level,
        from: processName(),
        ctx: context,
        msg,
        meta
      })
    }
  }

  enabled(level: LogLevel, context: string): boolean {
    return logFilter().enabled(level, context)
  }

  async flush() {
    // no op
  }

  end() {
    // no op
  }

  toJSON() {
    // if we add an object that contains a logger to LogEntry.meta...
    return {
      _ctor: "ConsoleLogger"
    }
  }
}
