import { lazy } from "../../core/Lazy"
import { Latch } from "../../fe/Latch"
import { Logger, mkLogger } from "../Logger"
import { Endable, EndableRank, EndableRanks, addEndable } from "./Endable"
import { awaitSettled } from "./Promise"

/**
 * Base class for Endables.
 */
export class EndableWrapper implements Endable {
  #name!: string
  logger!: Logger

  protected readonly onEnds: (() => any)[] = []
  readonly #endPromise = lazy(() => new Latch())
  /**
   * @param name for logging
   * @param onEnd called by `this.end()`. May return a promise.
   * @param _isEnded if the wrapped instance can end from other state mutations,
   * providing this method will allow this wrapper to be garbage collected.
   */
  constructor(
    name: string,
    onEnd?: () => Promise<any> | any,
    rank: EndableRank = EndableRanks.first,
    readonly endTimeoutMs?: number
  ) {
    this.name = name
    if (onEnd != null) this.onEnds.push(onEnd)
    addEndable(rank, this)
  }

  get name() {
    return this.#name
  }

  set name(n: string) {
    this.#name = n
    this.logger = mkLogger(n)
  }

  get ended() {
    return this.end.hasPrior()
  }

  end = lazy(async () => {
    await awaitSettled(this.onEnds.map(ea => ea()))
    void this.#endPromise().resolve()
  })

  awaitEnd(): Promise<void> {
    return this.#endPromise()
  }
}
