import { isIterable } from "./Iterable"
import { isString } from "./String"

export type Arrayish<T> = ArrayLike<T> | Iterable<T> | Set<T>

export type OneOrMore<T> = T | Arrayish<T>

export function toA<T>(arr: undefined | null | Arrayish<T> | T): T[] {
  return Array.isArray(arr) // < strings are not arrays
    ? (arr as T[])
    : arr == null
    ? []
    : isString(arr) // < don't rely on isIterable rejecting Strings
    ? [arr as T]
    : isIterable(arr)
    ? Array.from(arr)
    : [arr as T]
}
