import crypto from "crypto"
import fs from "fs"
import { pipeline } from "stream/promises"
import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { eql } from "../../fe/Eql"
import { stringify } from "../../fe/JSON"
import { pick } from "../../fe/Object"
import { FifoCacheAsync } from "../FifoCacheAsync"
import { HashBits } from "../HashBits"
import { mkLogger } from "../Logger"
import { shim1 } from "../Shim1"
import { stringShaToBuffer } from "../StringHash"
import { FileStats, fileStats_ } from "./FileStats"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"
import { stat_ } from "./Stat"

const logger = lazy(() => mkLogger("fs.Hash"))

export interface ShaMeta {
  sha: string
  stats: FileStats
  elapsedMs: number
}

const cache = lazy(
  () =>
    // we're not using a FileCache here, because SHAs are so expensive we want
    // to manage cache invalidation ourselves.
    new FifoCacheAsync<ShaMeta>({
      name: "fs.fileSha",
      maxSize: 512,
      timeoutMs: 0, // TODO: we should be able to read any file in under a minute? What about videos?
      clearEveryMs: 0, // no periodic clearing--no need.
      useFsCache: true,
      fsCacheTtlMs: 30 * minuteMs
    })
)

/**
 * Consumers should not directly call this: use {@link fileSha_()} instead.
 * @return a Buffer with the first 192 bits of a SHA512 digest.
 * @throws on read error
 */
export const shimmedFileSha_ = shim1({
  name: "fs.Hash",
  cache,
  impl: uncachedFileSha_
})

/**
 * Automatically invalidates SHAs whose stats have changed.
 */
export async function fileShaMeta_(
  f: SimpleFileOrString,
  { autoInvalidate = true }: { autoInvalidate?: boolean } = {}
): Promise<ShaMeta> {
  const nativePath = toNativePath_(f)
  if (autoInvalidate) {
    const prior = cache().get(nativePath)
    if (prior != null) {
      if (eql(prior.stats, await fileStats_(f))) {
        logger().debug("fileSha_() cache matches current Stats", {
          nativePath,
          prior
        })
        return prior
      } else {
        logger().debug("fileSha_() prior cache is invalid, re-reading SHA", {
          nativePath,
          prior
        })
        cache().delete(nativePath) // prior sha is invalid!
      }
    }
  }
  return shimmedFileSha_(f)
}

/**
 * Automatically invalidates SHAs whose stats have changed.
 */
export async function fileSha_(f: SimpleFileOrString): Promise<string> {
  return (await fileShaMeta_(f)).sha
}

/**
 * Uncached file SHA. Throws on error.
 */
export async function uncachedFileSha_(f: SimpleFileOrString) {
  const start = Date.now()
  const nativePath = toNativePath_(f)
  return {
    sha: await streamSha_(fs.createReadStream(nativePath)),
    stats: pick(await stat_(nativePath), "size", "mtimeMs"),
    elapsedMs: Date.now() - start
  }
}

export async function streamSha_(
  source: NodeJS.ReadableStream,
  transforms: NodeJS.ReadWriteStream[] = [],
  hashAlgorithm: string = "sha512"
): Promise<string> {
  const hash = crypto.createHash(hashAlgorithm)
  await pipeline([source, ...transforms, hash])
  return hash
    .digest()
    .subarray(0, HashBits / 8)
    .toString("base64")
}

