export function numericSha(obj: any, msbits = 48): number {
  return parseInt(stringShaToBuffer(stringify(obj), msbits).toString("hex"), 16)
}

export function randomSha() {
  return crypto.randomBytes(HashBits / 8).toString("base64")
}
