import { eql } from "../../fe/Eql"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { toErr } from "../../fe/toErr"
import { Deferred } from "./Deferred"

export type MemoizedDeferredListener<T> = {
  onChange(newResult: T): any
  onError(error: Error): any
}

export interface MemoizedDeferred<T> {
  (): Deferred<T>

  /**
   * Clears the prior value but doesn't return it (so we don't have to
   * explicitly ignore unawaited promises)
   */
  unset(): void

  /**
   * Force the prior value to be `t`
   */
  set(t: T): void

  /**
   * Forces the underlying thunk or later to be applied
   */
  refresh(): Deferred<T>

  /**
   * @return the milliseconds a prior result may be returned by `apply`, or
   * undefined if no TTL is set
   */
  ttl(): Maybe<number>

  /**
   * @param ttl undefined to remove TTL expirations
   */
  setTTL(ttl: Maybe<number>): void

  /**
   * Calls the listener if the result changes later. The underlying thunk is
   * not applied now (like with {@link MemoizedThunk.watch}).
   */
  watchLater(watcher: MemoizedDeferredListener<T>): void

  /**
   * @return undefined if unset
   */
  lastSetAgoMs(): Maybe<number>

  /**
   * @return the previously-memoized Deferred, if it isn't stale (according to
   * `ttl()`)
   */
  prior(): Maybe<Deferred<T>>

  /**
   * @return the last memoized awaited result (even if it's stale!).
   */
  lastValue(): Maybe<T>

  /**
   * @return the number of times the underlying thunk has been applied
   */
  settledCount(): number

  /**
   * Has a result not been calculated, or was calculated more than `ttl()` ms
   * ago?
   */
  isStale(): boolean

  /**
   * How long did the last fulfilled result take to calculate?
   */
  elapsedMs(): Maybe<number>

  toString(): string
}

/**
 * @param ttlMs if 0 or null, the result of `later` will be cached forever.
 */
export function lazyAsync<T>({
  later,
  desc,
  ttlMs,
  timeoutMs
}: {
  later: () => Promise<T>
  desc?: string
  ttlMs?: number
  timeoutMs?: number
}): MemoizedDeferred<T> {
  let lastSetTs: number | undefined
  let settledCount = 0
  let lastValue: T | undefined
  let result: Deferred<T> | undefined
  const watchers: MemoizedDeferredListener<T>[] = []

  async function onSetResult(
    priorD: Deferred<T> | undefined,
    resultD: Deferred<T>
  ) {
    const resultIdAtStart = result?.id
    try {
      // There's a chance that the prior or current result has already
      // changed. Don't submit onChanges or change priorValue if the current
      // Deferred result has changed.

      const current = await resultD
      if (result?.id === resultIdAtStart) {
        lastValue = current
      }

      if (
        result?.id === resultIdAtStart &&
        !eql(priorD?.value ?? (await priorD?.promise), current)
      ) {
        for (const ea of watchers) {
          await ea.onChange(current)
        }
      }
    } catch (err) {
      const error = toErr(err)
      if (error != null) for (const ea of watchers) ea.onError(error)
    } finally {
      settledCount++
    }
  }

  function setResult(t: Promise<T>): Deferred<T> {
    lastSetTs = Date.now()
    const priorResult = result
    result =
      t instanceof Deferred
        ? t
        : new Deferred<T>(desc ?? "lazyAsync").observe(t)
    if (gt0(timeoutMs)) {
      void result.setTimeout(timeoutMs)
    }
    void onSetResult(priorResult, result)
    return result
  }

  function isStale() {
    return lastSetTs == null || (gt0(ttlMs) && lastSetTs + ttlMs < Date.now())
  }

  const f = (() =>
    // NO PROMISE AWAITING HERE. Otherwise N calls would go through while we
    // wait for the promise to resolve.
    result == null || isStale() ? setResult(later()) : result) as any

  f.unset = () => {
    settledCount = 0
    lastSetTs = undefined
    lastValue = undefined
    result = undefined
  }

  f.set = (t: T) => setResult(Deferred.resolve(t))

  f.isStale = isStale

  f.settledCount = () => settledCount

  f.prior = () => {
    if (isStale()) f.unset()
    return result
  }

  f.lastValue = () => {
    return result?.value ?? lastValue
  }

  f.refresh = () => setResult(later())

  f.ttl = () => ttlMs

  f.setTTL = (ttl: Maybe<number>) => {
    ttlMs = gt0(ttl) ? ttl : undefined
  }

  f.watchLater = (watcher: MemoizedDeferredListener<T>) => {
    watchers.push(watcher)
  }

  f.toString = f.toLogJSON = f.toJSON = () => desc ?? "[LazyAsync]"

  f.lastSetAgoMs = () =>
    lastSetTs == null ? undefined : Date.now() - lastSetTs

  f.elapsedMs = () => result?.settledMs ?? result?.elapsedMs

  return f
}
