import { inspect, InspectOptions } from "util"
import { compactBlanks } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { fromEntries } from "../../fe/Object"
import { toS } from "../../fe/toS"
import { processName } from "../ServiceNames"
import { rightPad, stripAnsiEsc } from "../String"
import { LogEntry } from "./LogEntry"
import { LogFormatter } from "./LogFormatter"
import { LogLevel, LogLevels } from "./LogLevel"

export class PlaintextLogFormatter implements LogFormatter {
  constructor(
    readonly inspectOptions: InspectOptions = {
      colors: false,
      depth: 4,
      compact: true,
      customInspect: true
    }
  ) {}

  readonly paddedLogLevels = fromEntries(
    LogLevels.values.map(ea => [ea, rightPad(ea, 5, " ")])
  )

  formatLogEntry(le: LogEntry): string {
    return compactBlanks([
      new Date(le.ts).toISOString(),
      le.from ?? processName(),
      this.paddedLogLevels[le.l],
      stripAnsiEsc(le.ctx),
      stripAnsiEsc(le.msg),
      map(le.meta, ea => inspect(ea, this.inspectOptions))
    ])
      .map(ea => toS(ea))
      .join(" ")
  }

  format(
    level: LogLevel,
    context: string,
    message: string,
    meta?: any
  ): string {
    return this.formatLogEntry({
      ts: Date.now(),
      l: level,
      from: processName(),
      ctx: context,
      msg: message,
      meta
    })
  }
}
