import { promises as dns } from "dns"
import { lazy } from "../../core/Lazy"
import { blank, notBlank } from "../../fe/Blank"
import { minuteMs, secondMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { Timeout, thenOrTimeout } from "../../fe/ThenOrTimeout"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { within } from "../Number"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { equalsIgnoreCase } from "../String"
import { memoizeAsync } from "../async/MemoizedAsyncFunc"
import { thenMap2Or } from "../async/Promise"
import { ee } from "../event/EventEmitter"
import { ipv4Re } from "./ping"

const octetRE = new RegExp("^" + ipv4Re.source + "$")

/**
 * Try to return the "canonical" name for the given `ipOrName`.
 */
export const friendlyname = memoizeAsync(
  async (ipOrName: string) => {
    const result =
      octetRE.exec(ipOrName) == null ? ipOrName : await nslookup(ipOrName)
    return toS(result).toLowerCase().normalize()
  },
  {
    name: "net.nslookup.friendlyname",
    maxSize: 128,
    timeoutMs: ShortCommandTimeoutMs
  }
)

const loopbackRE =
  /^(?:(?:localhost(?:\.?(?:localdomain\.?)?))|(?:127(?:\.\d{1,3}){3}))$/i

export function isLoopback(name: string): boolean {
  return loopbackRE.exec(name) != null
}

export function octets(nameOrIp: string): Maybe<number[]> {
  const result: number[] = nameOrIp
    .split(".")
    .map(ea => toInt(ea))
    .filter(ea => within(0, 255, ea)) as number[]
  return result.length === 4 ? result : undefined
}

/**
 * @return 1 or more IPv4 addresses for the given name or IP address
 */
export const resolve4 = memoizeAsync(
  async (nameOrIp: string): PromiseMaybe<string[]> => {
    if (blank(nameOrIp)) return
    if (octets(nameOrIp) != null) return [nameOrIp]
    try {
      return await dns.resolve4(nameOrIp)
    } catch (err) {
      logger().warn("No name found for " + nameOrIp)
      return
    }
  },
  {
    name: "net.nslookup.resolve4",
    maxSize: 256,
    timeoutMs: ShortCommandTimeoutMs,
    clearEveryMs: 10 * minuteMs
  }
)

const logger = lazy(() => mkLogger("net.nslookup"))

later(() => ee().on("clearCache", () => nslookup.clear()))

/**
 * @return the name or IP address
 */
export const nslookup = memoizeAsync(
  async (nameOrIp: string) => {
    try {
      const names = await thenOrTimeout(
        isLoopback(nameOrIp)
          ? nameOrIp.startsWith("127.")
            ? ["localhost"]
            : ["127.0.0.1"]
          : octets(nameOrIp) != null
          ? dns.reverse(nameOrIp)
          : dns.resolve4(nameOrIp),
        5 * secondMs // < DNS resolution should be milliseconds.
      )
      if (names === Timeout) {
        logger().info("nslookup(" + nameOrIp + "): timeout")
        return nameOrIp
      }
      const firstNonBlank = names.find(notBlank)
      if (firstNonBlank == null) {
        logger().warn("No name found for " + nameOrIp)
        return nameOrIp
      } else {
        return firstNonBlank
      }
    } catch (err) {
      logger().warn("Failed to look up " + nameOrIp + ", using name.", err)
      return nameOrIp
    }
  },
  {
    name: "net.nslookup",
    maxSize: 256,
    timeoutMs: ShortCommandTimeoutMs,
    clearEveryMs: 10 * minuteMs
  }
)

export async function isEquivalentHost(
  a: Maybe<string>,
  b: Maybe<string>
): Promise<boolean> {
  if (blank(a) || blank(b)) return false
  if (equalsIgnoreCase(a, b)) return true
  if (isLoopback(a) && isLoopback(b)) return true
  return thenMap2Or(
    resolve4(a),
    resolve4(b),
    (aAddrs, bAddrs) => aAddrs.some(ea => bAddrs.includes(ea)),
    () => false
  )
}
