import { ee } from "../event/EventEmitter"

let paused = false

export function isPaused(): boolean {
  return paused
}

export function pause(newPausedValue = true): void {
  if (newPausedValue === false) {
    resume()
  } else {
    // no infinite loops
    if (!paused) {
      paused = true
      ee().emit("pause")
    }
  }
}

export function resume(): void {
  // no infinite loops
  if (paused) {
    paused = false
    ee().emit("clearCache")
    ee().emit("resume")
  }
}

