import { inspect } from "util"
import { isEmpty } from "../fe/Array"
import { ChangeListener } from "../fe/ChangeListener"
import { eql } from "../fe/Eql"
import { MemoizedLazy, MemoizedThunk } from "../fe/Lazy"
import { Maybe } from "../fe/MaybeTypes"
import { gt0 } from "../fe/Number"
import { setUnrefTimeout } from "../fe/UnrefTimeout"
import { ExtensibleFunction } from "./ExtensibleFunction"

// USED BY LOG: DON'T DEPEND ON ANYTHING (much)

// TODO: INLINE
export { MemoizedLazy, MemoizedThunk, UnwrapLazy } from "../fe/Lazy"

export function lazy<T>(thunk: () => T, ttlMs?: number): MemoizedThunk<T> {
  return new LazyClass(thunk, ttlMs) as any
}

class LazyClass<T> extends ExtensibleFunction<T> implements MemoizedLazy<T> {
  private lastSetTs?: number
  private result?: T
  readonly watchers: ChangeListener<T>[] = []

  constructor(
    private readonly thunk: () => T,
    private ttlMs?: number
  ) {
    super()
  }

  override _call() {
    if (this.isStale()) {
      // NO PROMISE AWAITING HERE. Otherwise N calls would go through while we
      // wait for the promise to resolve.
      this.#setResult(this.thunk())
    }
    return this.result!
  }

  private async onSetResult(priorP: Maybe<T>, currentP: T) {
    if (isEmpty(this.watchers)) return
    // prior and current could be a promise!

    // eslint-disable-next-line @typescript-eslint/await-thenable
    const _prior = await priorP
    // eslint-disable-next-line @typescript-eslint/await-thenable
    const current = await currentP
    if (!eql(_prior, current)) {
      for (const ea of this.watchers) ea(current)
    }
    // We're interpreting undefined as unset, and `null` as a valid set value.
    if (this.ttlMs != null && this.ttlMs > 0 && current !== undefined) {
      // help the garbage collector:
      setUnrefTimeout(() => this.vacuum(), this.ttlMs)
    }
  }

  vacuum() {
    if (this.isStale()) this.unset()
  }

  #setResult(t: T) {
    this.lastSetTs = Date.now()
    void this.onSetResult(this.result, t)
    return (this.result = t)
  }

  isStale() {
    return (
      this.lastSetTs == null ||
      (gt0(this.ttlMs) && this.lastSetTs + this.ttlMs < Date.now())
    )
  }

  set(t: T) {
    this.#setResult(t)
  }

  unset() {
    this.#setResult(undefined as any)
    this.lastSetTs = undefined
  }

  clear() {
    const _prior = this.result
    this.unset()
    return _prior
  }

  prior() {
    this.vacuum()
    return this.result
  }

  refresh() {
    return this.#setResult(this.thunk())
  }

  ttl() {
    return this.ttlMs
  }

  setTTL(ttl: number | undefined) {
    this.ttlMs = gt0(ttl) ? ttl : undefined
  }

  watchLater(watcher: ChangeListener<T>) {
    this.watchers.push(watcher)
  }

  watch(watcher: ChangeListener<T>) {
    watcher(this())
    this.watchLater(watcher)
  }

  toString() {
    return "[Lazy]"
  }

  // If you move this back to fe, you'll need to switch this with something
  // like `const inspect_custom = isNode ? require("util").inspect.custom :
  // Symbol.for("inspect.custom")`
  [inspect.custom]() {
    return "[Lazy]"
  }

  lastSetAgoMs() {
    return this.lastSetTs == null ? undefined : Date.now() - this.lastSetTs
  }

  hasPrior() {
    return this.lastSetTs != null
  }
}

class ROLazy<T> extends LazyClass<T> {
  override set(_: T) {}
}

export function rolazy<T>(thunk: () => T, ttlMs?: number): MemoizedThunk<T> {
  return new ROLazy(thunk, ttlMs) as any
}
