import _path from "path"
import { lazy } from "../../core/Lazy"
import { compact } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { toS } from "../../fe/toS"
import { SimpleAppName } from "../AppName"
import { getEnv } from "../env/GetEnv"
import { isMac } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"
import { systemSettingsFile } from "../settings/SystemSettings"
import { readTomlFileSync } from "../settings/Toml"
import { cacheDirs } from "./CacheDir"
import { DefaultDockerLibraryDir } from "./DefaultDockerLibraryDir"
import { firstDir } from "./FirstDir"
import { homeDir } from "./HomeDir"

export function defaultLogDirs() {
  if (isDocker()) {
    return [
      {
        dir: "/ps/logs",
        preexistingDir: "/ps/logs"
      },
      {
        dir: DefaultDockerLibraryDir + "/.photostructure/logs",
        preexistingDir: "/ps/library"
      },
      {
        dir: "/tmp/photostructure-logs",
        preexistingDir: "/tmp" // this will certainly exist, but not be persistent. It's only here as a backstop.
      }
    ]
  } else if (isMac) {
    return [
      {
        dir: _path.resolve(homeDir(), "Library", "Logs", SimpleAppName),
        preexistingDir: homeDir()
      }
    ]
  } else {
    // Windows and Linux puts logs into the cache dir:
    return compact(cacheDirs()).map(ea => ({
      dir: map(ea.dir, dir => _path.resolve(dir, "logs")),
      preexistingDir: ea.preexistingDir
    }))
  }
}

export function defaultLogDir_() {
  return firstDir({ dirs: defaultLogDirs(), desc: "logs" })!
}

// this bare-bones implementation means we can set up the logger without pulling
// in Settings, SettingsIO and all those dependencies:

export const logDir = lazy(() => {
  try {
    const e = getEnv("logDir") // < this will check logDir and PS_LOG_DIR
    if (notBlank(e)) return e

    const s = readTomlFileSync(systemSettingsFile())
    const ld = toS(s?.logDir)
    if (notBlank(ld)) return ld

    return defaultLogDir_()
  } catch (error) {
    console.error("defaultLogDir_() failed", error)
    return _path.resolve(homeDir(), "logs")
  }
})
