import { isEmpty } from "./Array"
import { Maybe, MaybeNull } from "./MaybeTypes"
import { toS } from "./toS"

export type Primitive = number | string | boolean
const primitiveTypes = ["number", "string", "boolean"]

export interface PrimitiveValued {
  [key: string]: Primitive
}

export interface Primitivable {
  valueOf(): Primitive
}

export interface Primitivables {
  valueOf(): Primitive[]
}

// HEY FUTURE ME, looking for something like DeepComparable? See the `Json` type in src/fe/JSON.ts

export type Comparable = Primitive | Date | Comparable[]

export function isPrimitive(obj: any): obj is Primitive {
  return primitiveTypes.indexOf(typeof obj) !== -1
}

export function mapPrimitive<T>(a: any, f: (t: Primitive) => T): Maybe<T> {
  return isPrimitive(a) ? f(a) : undefined
}

export function mapPrimitiveOr<T>(
  a: any,
  f: (t: Primitive) => T,
  defaultValue: () => T
): Maybe<T> {
  return isPrimitive(a) ? f(a) : defaultValue()
}

export function isPrimitiveArray(obj: any): obj is Primitive[] {
  return Array.isArray(obj) && (obj as any[]).every(isPrimitive)
}

// See https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/typeof
const TypeLevel = [
  "boolean",
  "number",
  "bigint",
  "symbol",
  "string",
  "object",
  "function"
] // typeof array is "object"

/**
 * (null || undefined) < false < true < numbers < strings < arrays
 * @return 1 if a > b, -1 if a < b, 0 if a == b.
 */
export function cmp<T extends Maybe<Comparable> | Maybe<Comparable>[]>(
  a: MaybeNull<T>,
  b: MaybeNull<T>
): number {
  // undefined == undefined:
  if (a == null && b == null) return 0

  // undefined should be < defined. We can't use typeof here because typeof null
  // is "object" and typeof undefined = "undefined".
  if (a == null) return -1
  if (b == null) return 1

  const aType = typeof a
  const bType = typeof b

  if (
    (aType === "string" || aType === "symbol") &&
    (bType === "string" || bType === "symbol")
  ) {
    // in German,  sorts before z, in Swedish,  sorts after z
    return toS(a).localeCompare(toS(b))
  }
  if (Array.isArray(a) && Array.isArray(b)) {
    return cmpArr(a, b)
  }
  if (aType !== bType) {
    return TypeLevel.indexOf(aType) - TypeLevel.indexOf(bType)
  } else {
    return a > b ? 1 : a < b ? -1 : 0
  }
}

export function lt<T extends Comparable>(
  a: MaybeNull<T>,
  b: MaybeNull<T>
): boolean {
  return cmp(a, b) < 0
}

export function lte<T extends Comparable>(
  a: MaybeNull<T>,
  b: MaybeNull<T>
): boolean {
  return cmp(a, b) <= 0
}

export function gte<T extends Comparable>(
  a: MaybeNull<T>,
  b: MaybeNull<T>
): boolean {
  return cmp(a, b) >= 0
}

export function gt<T extends Comparable>(
  a: MaybeNull<T>,
  b: MaybeNull<T>
): boolean {
  return cmp(a, b) > 0
}

/**
 * @return 1 if a > b, -1 if a < b, 0 if a == b.
 */
export function cmpArr<T extends Comparable | Comparable[]>(
  a: T[],
  b: T[],
  shorterArrayIsLessThan = true
): number {
  if (isEmpty(a) && isEmpty(b)) return 0
  const len = Math.min(a.length, b.length)
  for (let i = 0; i < len; i++) {
    const c = cmp(a[i], b[i])
    if (c !== 0) {
      return c
    }
  }
  return cmp(a.length, b.length) * (shorterArrayIsLessThan ? 1 : -1)
}
