import { denull } from "./Maybe"
import { Maybe, MaybeNull } from "./MaybeTypes"
import { isFunction } from "./isFunction"

export type Json =
  | string
  | number
  | boolean
  | null
  | undefined
  | { [property: string]: Json }
  | Json[]

export type DatedJson =
  | string
  | number
  | boolean
  | null
  | undefined
  | Date
  | { [property: string]: DatedJson }
  | DatedJson[]

/**
 * Work-alike to JSON.stringify, except that circular dependencies are replaced
 * with a string reference.
 */
export function stringify(
  ea: any,
  replacer?: (this: any, key: string, value: any) => any,
  space?: string | number
): string {
  return JSON.stringify(decycle(ea), replacer, denull(space))
}

export interface JsonReviver {
  name: string
  fromJSON(ea: any): any
}

const Revivers = new Map<string, JsonReviver>()

export function addJsonRevivers(...arr: JsonReviver[]) {
  for (const ea of arr) {
    Revivers.set(ea.name, ea)
  }
}

export function parseJSON_<T>(s: string): T {
  return retrocycle(JSON.parse(s, reviver)) as any
}

export function parseJSON<T = any>(s: MaybeNull<string | Buffer>): Maybe<T> {
  try {
    const s2 = s?.toString().trim()
    if (s2 != null && s2.length > 0) {
      return parseJSON_(s2)
    }
  } catch {
    //
  }
  return
}

export function JSON_<T>(s: string): T {
  return retrocycle(JSON.parse(s, reviver)) as any
}

function reviver(_key: string, value: any) {
  // PERF: map inlined
  const r = Revivers.get(value?._ctor)
  return r == null ? value : r.fromJSON(value)
}

// https://github.com/douglascrockford/JSON-js/blob/master/cycle.js

/**
 * Make a deep copy of an object or array, assuring that there is at most
 * one instance of each object or array in the resulting structure. The
 * duplicate references (which might be forming cycles) are replaced with
 * an object of the form
 *      {"$ref": PATH}
 * where the PATH is a JSONPath string that locates the first occurrence.
 * So,
 *      var a = [];
 *      a[0] = a;
 *      return JSON.stringify(JSON.decycle(a));
 * produces the string '[{"$ref":"$"}]'.
 * If a replacer function is provided, then it will be called for each value.
 * A replacer function receives a value and returns a replacement value.
 * JSONPath is used to locate the unique object. $ indicates the top level of
 * the object or array. [NUMBER] or [STRING] indicates a child element or
 * property.
 */
function decycle(object: any) {
  const objects: any[] = [] // Keep a reference to each unique object or array
  const paths: string[] = [] // Keep the path to each unique object or array

  return (function derez(value, path): any {
    // The derez recurses through the object, producing the deep copy.

    if (isFunction(value)) return undefined

    let i: number // The loop counter
    let name: string // Property name
    let nu: any // The new object or array

    let _value = value

    try {
      _value = value.toJSON()
    } catch (error) {
      //
    }

    // typeof null === 'object', so go on if this value is really an object but not
    // one of the weird builtin objects.

    if (typeof _value === "object" && _value != null) {
      // Remove all fields that shouldn't be serialized:
      for (const k in _value) {
        if (k.startsWith("__")) {
          if (value === _value) {
            _value = { ..._value }
          }
          delete _value[k]
        }
      }

      // If the value is an object or array, look to see if we have already
      // encountered it. If so, return a $ref/path object. This is a hard way,
      // linear search that will get slower as the number of unique objects grows.

      for (i = 0; i < objects.length; i += 1) {
        if (objects[i] === _value) {
          return { $ref: paths[i] }
        }
      }

      // Otherwise, accumulate the unique value and its path.

      objects.push(_value)
      paths.push(path)

      // If it is an array, replicate the array.

      if (Array.isArray(_value)) {
        nu = []

        for (i = 0; i < _value.length; i += 1) {
          nu[i] = derez(_value[i], path + "[" + i + "]")
        }
      } else {
        // If it is an object, replicate the object.

        nu = {}

        for (name in _value) {
          if (Object.prototype.hasOwnProperty.call(_value, name)) {
            nu[name] = derez(
              _value[name],
              path + "[" + JSON.stringify(name) + "]"
            )
          }
        }
      }

      return nu
    }

    return _value
  })(object, "$")
}

/**
  * Restore an object that was reduced by decycle. Members whose values are
  * objects of the form
  *      {$ref: PATH}
  * are replaced with references to the value found by the PATH. This will
  * restore cycles. `value` will be mutated.

  * The eval function is used to locate the values described by a PATH. The
  * root object is kept in a $ variable. A regular expression is used to
  * assure that the PATH is extremely well formed. The regexp contains nested
  * * quantifiers. That has been known to have extremely bad performance
  * problems on some browsers for very long strings. A PATH is expected to be
  * reasonably short. A PATH is allowed to belong to a very restricted subset of
  * Goessner's JSONPath.

  * So,
  *      const s = '[{"$ref":"$"}]';
  *      return JSON.retrocycle(JSON.parse(s));
  * produces an array containing a single element which is the array itself.
*/
function retrocycle($: any) {
  const px =
    /^\$(?:\[(?:\d+|\"(?:[^\\\"\u0000-\u001f]|\\([\\\"\/bfnrt]|u[0-9a-zA-Z]{4}))*\")\])*$/

  ;(function rez(value) {
    // The rez function walks recursively through the object looking for $ref
    // properties. When it finds one that has a value that is a path, then it
    // replaces the $ref object with a reference to the value that is found by
    // the path.

    let i, item, name, path

    if (value && typeof value === "object") {
      if (Object.prototype.toString.apply(value) === "[object Array]") {
        for (i = 0; i < value.length; i += 1) {
          item = value[i]

          if (item != null && typeof item === "object") {
            path = item.$ref

            if (typeof path === "string" && px.test(path)) {
              value[i] = eval(path)
            } else {
              rez(item)
            }
          }
        }
      } else {
        for (name in value) {
          if (typeof value[name] === "object") {
            item = value[name]

            if (item != null) {
              path = item.$ref

              if (typeof path === "string" && px.test(path)) {
                value[name] = eval(path)
              } else {
                rez(item)
              }
            }
          }
        }
      }
    }
  })($)

  return $
}
