import { sortBy } from "../../fe/ArraySort"

export function lerp(v0: number, v1: number, t = 0.5): number {
  // See https://en.wikipedia.org/wiki/Linear_interpolation#Programming_language_support
  return (1 - t) * v0 + t * v1
}

export interface Point {
  x: number
  y: number
}

/**
 * @return y for the given x, given the line defined p0 and p1
 */
export function lerp2d(x: number, ...arr: Point[]): number {
  const nearest2 = sortBy(arr, ea => Math.abs(x - ea.x)).slice(0, 2)
  const [p0, p1] = sortBy(nearest2, ea => ea.x)

  const dx = p1.x - p0.x
  const xdx = x - p0.x
  const t = xdx / dx
  return lerp(p0.y, p1.y, t)
}

export function lerp2d_(x: number, p0: Point, p1: Point): number {
  // https://en.wikipedia.org/wiki/Linear_interpolation
  return (p0.y * (p1.x - x) + p1.y * (x - p0.x)) / (p1.x - p0.x)
}
