const logger = lazy(() => mkLogger("child.ChildProcess"))

function cp2log(cp: child_process.ChildProcess) {
  return pick(
    cp as any,
    "pid",
    "killed",
    "connected",
    "exitCode",
    "signalCode",
    "spawnfile",
    "spawnargs"
  )
}

export async function endProcess(
  cp: Maybe<child_process.ChildProcess>,
  timeoutMs = 30 * secondMs
): Promise<boolean> {
  if (cp == null) return false
  logger().debug("endProcess()", cp2log(cp))

  const pid = cp.pid

  if (pid == null || pid <= 0) {
    logger().warn("endProcess(): asked to end invalid pid", cp2log(cp))
    return false
  }
  if (pid === process.pid) {
    logger().warn("endProcess(): asked to end MY pid", cp2log(cp))
    return false
  }
  if (pid === process.ppid) {
    logger().warn("endProcess(): asked to end my parent pid", cp2log(cp))
    return false
  }

  closeStreams(cp)

  {
    // Send a SIGTERM
    const killResult = cp.kill()
    logger().debug("endProcess(" + pid + ")", {
      killResult,
      childGotSigterm: cp.killed
    })
    if (!killResult) {
      await killPid(pid).catch(err => {
        logger().warn("endProcess(): kill(" + pid + ",false) failed: " + err)
      })
    }
  }

  // We don't use messaging, but in case we do later:
  // TODO: do we need to do this?
  // if (cp.connected) Try(() => cp.disconnect())

  // We can exit even if this child is still running:
  Try(() => cp.unref())

  // don't wait for cleanup if we're running tests:
  if (isSingleSpecTests()) return true

  if (await waitForPidExit(pid, timeoutMs)) {
    logger().debug("endProcess(): exitted", cp2log(cp))
    return true
  }

  {
    // OK, shite got real. time for kill -9.
    void Pids.instance()?.onKill(pid)
    const killResult = cp.kill("SIGKILL")
    logger().warn("endProcess(" + pid + ") had to resort to SIGKILL", {
      killResult
    })
    if (!killResult) {
      await killPid(pid, true).catch(err => {
        logger().warn("endProcess(): kill(" + pid + ",true) failed: " + err)
      })
    }
  }

  // Give the OS some time to clean up:
  return waitForPidExit(pid, 5000)
}

export const ReniceAfterMs = lazy(() => 15 * secondMs)
export const AddPidAfterMs = lazy(() => 30 * secondMs)

// NOTE: MUST NOT BE ASYNC
function newProc(
  cp: child_process.ChildProcess,
  cmd: string,
  args: ReadonlyArray<string>,
  maxAgeMs = 0
) {
  const start = new Date()
  // If we write the pidfile now and hold up processing, `cp.stderr` and
  // `cp.stdout` event listeners will miss initial data, so we put this in a
  // timeout for later.

  // Note that THIS IS NOT UNREF'ed! We want pids to be cleaned up!

  const timeouts: NodeJS.Timeout[] = []

  // Renice after a second
  if (niceable(cmd, args)) {
    timeouts.push(
      setUnrefTimeout(
        () => {
          if (gt0(cp.pid)) {
            void renice(cp.pid)
          }
        },
        ReniceAfterMs() // only track and renice processes that run for a while
      )
    )
  }

  // Force-end processes after maxAgeMs
  if (gt(maxAgeMs, secondMs)) {
    timeouts.push(
      setUnrefTimeout(() => {
        if (gt0(cp.pid)) {
          void endProcess(cp)
        }
      }, maxAgeMs - 250)
    )
  }

  // Track long-lived processes:
  timeouts.push(
    setUnrefTimeout(
      () => {
        if (gt0(cp.pid)) {
          return addPid(
            { pid: cp.pid, cmd, maxAgeMs, ppid: process.pid },
            start
          )
        }
        return
      },
      AddPidAfterMs() // only track and renice processes that run for a while
    )
  )
  // This allows cp resources to be GC'ed:
  cp.on("exit", () => timeouts.forEach(clearTimeout))

  return cp
}

export function spawn(
  command: string,
  args: ReadonlyArray<string>,
  maxAgeMs: number,
  options?: child_process.SpawnOptions
): child_process.ChildProcess {
  const opts = spawnOptions(options)
  logger().debug("spawn()", { command, args, maxAgeMs })
  return newProc(
    child_process.spawn(command, args, opts),
    command,
    args,
    maxAgeMs
  )
}

export type ExecFileOptions =
  | child_process.ExecFileOptions
  | child_process.ExecFileOptionsWithBufferEncoding
  | child_process.ExecFileOptionsWithStringEncoding

export function execFile(
  command: string,
  args: ReadonlyArray<string>,
  maxAgeMs?: number,
  options?: ExecFileOptions
): child_process.ChildProcess {
  const opts = spawnOptions(options)
  // Only log the opts (which are long: they include ENV!) if we're tracing:
  if (isLogged("trace", logger().context)) {
    logger().debug("execFile()", { command, args, maxAgeMs, opts })
  } else {
    logger().debug("execFile()", { command, args, maxAgeMs })
  }

  return newProc(
    child_process.execFile(command, args, opts),
    command,
    args,
    maxAgeMs
  )
}

export type StdoutOpts = {
  timeoutMs: number
  quiet?: boolean
  disconnect?: boolean
  ignoreStderr?: boolean
  ignoreExitCode?: boolean
  isIgnorableError?: (err: any) => boolean
} & child_process.ExecFileOptions

export type StdoutResult = {
  result: string
  pid?: number
  code?: number
  signal?: string
}

export async function stdoutResult_(
  cmd: string,
  args: string[],
  opts: StdoutOpts
): Promise<StdoutResult> {
  const quiet = opts.quiet ?? false
  const ignoreStderr = opts.ignoreStderr ?? false
  const ignoreExitCode = opts.ignoreExitCode ?? false
  const proc: child_process.ChildProcess = execFile(
    cmd,
    args,
    opts.timeoutMs,
    omit(opts, "timeout", "quiet", "ignoreStderr", "ignoreExitCode")
  )

  if (opts.disconnect === true) {
    try {
      // disconnect may not exist!
      proc.disconnect?.()
    } catch {
      //
    }
    return { result: "", pid: proc.pid! }
  }

  const pid = proc.pid
  // don't panic if we don't have a pid yet: this happens on Windows!

  const name = stringify({ pid, cmd, args })
  const stdoutChunks: (Buffer | string)[] = []
  const stderrChonks: (Buffer | string)[] = []
  const errors: Error[] = []
  const exitCode = new Deferred<number>(name)

  proc.on("error", err => errors.push(err))
  proc.on("exit", e => {
    if (isNumber(e) && exitCode.isPending) void exitCode.resolve(e)
  })
  proc.on("close", e => {
    if (isNumber(e) && exitCode.isPending) void exitCode.resolve(e)
  })

  void endStream(proc.stdin)

  const stdoutEnded = new Latch()

  if (proc.stdout == null) {
    void stdoutEnded.resolve()
  } else {
    proc.stdout.on("error", err => errors.push(err))
    proc.stdout.on("data", data => stdoutChunks.push(data))
    proc.stdout.on("end", () => stdoutEnded.resolve())
  }

  const stderrEnded = new Latch()

  if (proc.stderr == null || ignoreStderr) {
    void stderrEnded.resolve()
  } else {
    proc.stderr?.on("error", err => errors.push(err))
    proc.stderr?.on("data", err => stderrChonks.push(err))
    proc.stderr?.on("end", () => stderrEnded.resolve())
  }

  // Wait for the process to finish...
  await thenOrTimeoutError(exitCode.promise, opts.timeoutMs)

  // Wait for chonks (which may show up AFTER the process exits). It shouldn't
  // take long to flush these streams.
  await thenOrTimeoutError(stdoutEnded.promise, 2 * secondMs)
  await thenOrTimeoutError(stderrEnded.promise, 2 * secondMs)

  // Add stderr chunks to errors:
  const stderr = stderrChonks.join("")
  if (notBlank(stderr)) errors.push(new Error(stderr))
  if (!quiet && isNotEmpty(errors)) {
    logger().warn(name + " resulted in errors:", errors)
  }

  // Any errors that we can't ignore?
  const isIgnorableFunc = opts.isIgnorableError ?? isIgnorableError
  const causes = errors.filter(ea => isIgnorableFunc(ea) !== true)
  if (causes.length > 0) {
    throw causes.length === 1
      ? causes[0]
      : new WrappedError("Failed to run " + cmd + " " + args.join(" "), {
          causes
        })
  }

  // Was the exit code sad?
  if (!ignoreExitCode && exitCode.value !== 0) {
    throw new Error(name + ": exit code " + exitCode.value)
  }

  const result = stdoutChunks.join("")

  return {
    result,
    pid,
    code: exitCode.value
  }
}

/**
 * @return just the stdout stream
 * @see stdoutResult_ if you need more information about the command.
 */
export async function stdout_(
  cmd: string,
  args: string[],
  opts: StdoutOpts
): Promise<string> {
  const result = await stdoutResult_(cmd, args, opts)
  return logger().tap({
    level: "trace",
    msg: "stdout_()",
    result: trimLastNewline(result.result),
    meta: { cmd, args, opts, result }
  })
}
