import { FSWatcher, watch } from "fs"
import { join, sep } from "path"
import { pid, stdout } from "process"
import { clearInterval, setInterval } from "timers"
import { lazy } from "../../core/Lazy"
import { notBlank, notBlankOr } from "../../fe/Blank"
import { minuteMs, secondMs } from "../../fe/Date"
import { getOrSet } from "../../fe/Map"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gte } from "../../fe/Primitive"
import { thenMap } from "../../fe/Promise"
import { isTest } from "../NodeEnv"
import { serviceName } from "../ServiceNames"
import { consoleError, consoleLog } from "../Stdout"
import { stdoutEnded } from "../StdoutEnded"
import { TTLSet } from "../TTLSet"
import { EndableRanks, ending } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { Promises } from "../async/Promises"
import { nowish } from "../date/Date"
import { errorToS } from "../error/Error"
import { DirectoryEntry } from "../fs/DirectoryEntry"
import { mkdirp_ } from "../fs/Path"
import { Settings } from "../settings/Settings"
import { readSystemSettings } from "../settings/SettingsIO"
import { DefaultLogFormatter } from "./DefaultLogFormatter"
import { DefaultLogFlushMs } from "./LogCommon"
import { datedLogDir } from "./LogDir"
import { LogEntry } from "./LogEntry"
import { ifLog } from "./LogFilter"
import { LogLevels } from "./LogLevel"
import {
  popExpiredLogEntries,
  pushLogEntries,
  setLogTailEnabled
} from "./LogTailEntries"
import { readLogEntries } from "./ReadLogEntries"

export function DefaultLogEntryWriter(arr: LogEntry[]) {
  const f = DefaultLogFormatter()
  for (const ea of arr) {
    consoleLog(f.formatLogEntry(ea))
  }
}

/**
 * Tail all PhotoStructure/log//.log files.
 */
export class LogTail extends EndableWrapper {
  static readonly instance = lazy(() => {
    return stdoutEnded() ? undefined : new LogTail()
  })
  private root!: DirectoryEntry
  private watchers: Map<string, FSWatcher> = new Map()
  private flushTimeout?: NodeJS.Timeout
  private scanTimeout?: NodeJS.Timeout
  private ignorableFilename: string
  private readonly filenameMutexes = new Promises("log.LogTail", () => 8)

  private readonly file2pos = new Map<string, number>()
  private readonly lastReadFiles = new TTLSet<string>(5 * secondMs)

  constructor(
    readonly rootNativePath: Maybe<string> = undefined,
    readonly writer = DefaultLogEntryWriter
  ) {
    super("LogTail", () => this.close(), EndableRanks.logtail)
    this.ignorableFilename = sep + serviceName() + "-" + pid + "-"
    if (stdout.writableFinished) return

    setLogTailEnabled(true)
    // Not an unref'ed interval, so logtail doesn't exit:
    this.flushTimeout = setInterval(() => this.flush(), DefaultLogFlushMs / 2)
    this.scanTimeout = setInterval(() => this.scan(), minuteMs)
    stdout.on("end", () => this.end())
    void this.setup()
  }

  readonly setup = lazy(async () => {
    await readSystemSettings()
    const logdir = notBlankOr(
      this.rootNativePath,
      Settings.logDir.valueOrDefault
    )
    if (isTest) consoleLog("tailing " + logdir + "...")
    await mkdirp_(logdir)
    this.root = (await DirectoryEntry.for(logdir))!
    await this.scan(true)
  })

  async flush() {
    this.writer(popExpiredLogEntries())
  }

  private readable(nativePath: string) {
    return (
      // Don't tail .gz files: they're never updated.
      nativePath.endsWith(".log") &&
      !nativePath.includes(this.ignorableFilename)
    )
  }

  private watchDir(dir: string) {
    if (ending() || this.ended) return
    getOrSet(this.watchers, dir, () => {
      ifLog(LogLevels.debug, () => consoleLog("LogTail(): watching " + dir))
      try {
        return watch(dir, (event: string, filename: string | null) => {
          if (notBlank(filename)) {
            void this.watchListener(event, join(dir, filename))
          }
        })
      } catch (err) {
        ifLog(LogLevels.warn, () =>
          consoleError("LogTail(): failed to read " + dir, err)
        )
        return
      }
    })
  }

  async scan(firstTime = false) {
    if (ending() || this.ended) return
    // scan through logs and set the position for all current entries:
    await this.root.visitDescendants(async ea => {
      if (!this.readable(ea.nativePath)) return
      if (firstTime) {
        await thenMap(ea.size(), size => this.file2pos.set(ea.nativePath, size))
      }
      if (nowish(await ea.mtimeMs(), 5 * minuteMs)) {
        this.watchDir(ea.dir)
      }
    })
    if (ending() || this.ended) return
    const currentLogDir = datedLogDir(this.root.nativePath)
    try {
      // if they start logtail before anything else, we want to see those new
      // files:
      await mkdirp_(currentLogDir)
      this.watchDir(currentLogDir)
    } catch (err) {
      ifLog(LogLevels.warn, () =>
        consoleError(
          "LogTail(): Failed to list or mkdir -p " +
            currentLogDir +
            ": there may be permission issues.",
          err
        )
      )
    }
    if (ending() || this.ended) return
    if (firstTime) {
      consoleLog("LogTail(): Tailing " + this.root + "/**/*.log...")
    }
  }

  private async close() {
    setLogTailEnabled(false)
    map(this.flushTimeout, clearInterval)
    this.flushTimeout = undefined
    map(this.scanTimeout, clearInterval)
    this.scanTimeout = undefined
    for (const ea of this.watchers.values()) {
      ea.close()
    }
    this.watchers.clear()
    for (const ea of this.lastReadFiles) {
      await this.watchListener("onEnd", ea)
    }
    try {
      this.writer(popExpiredLogEntries(-1))
    } catch (error) {
      console.warn("failed to flush last log entries", error)
    }
  }

  private async watchListener(event: string, filename: string) {
    if (!this.readable(filename) || event === "rename") {
      return
    }
    await this.filenameMutexes.serial(filename, async () => {
      try {
        const f = await DirectoryEntry.for(filename)
        if (f == null) {
          // file was deleted or compressed. DON'T FREAK OUT.
          return
        }
        // if (!nowish(await f.mtimeMs(), 10 * minuteMs)) return // old file
        const currentSize = await f.size()
        if (currentSize == null || currentSize <= 0) {
          // consoleError("watchListener: empty file", { event, filename })
          return
        }
        const priorSize = this.file2pos.get(f.nativePath) ?? 0
        if (gte(priorSize, currentSize)) return
        await thenMap(
          readLogEntries(f, { start: priorSize, end: currentSize }),
          arr => pushLogEntries(...arr)
        )
        this.lastReadFiles.add(f.nativePath)
        // If gzip reading fails, don't update current size:
        this.file2pos.set(f.nativePath, currentSize)
      } catch (err) {
        ifLog(LogLevels.warn, () =>
          consoleError("Failed to read " + filename + ": " + errorToS(err))
        )
      }
    })
    return
  }
}
