import { setInterval, setTimeout } from "timers"

export function setUnrefTimeout(
  callback: (...ea: any[]) => void,
  ms: number,
  ...args: any[]
): NodeJS.Timeout {
  return setTimeout(callback, Math.round(ms), ...args).unref()
}

export function setUnrefInterval(
  callback: (...ea: any[]) => void,
  ms: number,
  ...args: any[]
): NodeJS.Timeout {
  return setInterval(callback, Math.round(ms), ...args).unref()
}
