import { Writable, WritableOptions } from "stream"
import { Deferred } from "../async/Deferred"

/**
 * Concats the result of a stream's data into a `Buffer`
 */
export class WritableToBuffer extends Writable {
  private readonly deferred = new Deferred<Buffer>("WritableToBuffer")
  private readonly _buf: Buffer[] = []

  constructor(opts?: WritableOptions) {
    super(opts)
    this.on("finish", () => {
      void this.deferred.resolve(this.data)
    })
    this.on("error", err => {
      void this.deferred.reject(err)
    })
  }

  get data(): Buffer {
    return Buffer.concat(this._buf)
  }

  /**
   * Final result. Will only be resolved on finish.
   */
  get buffer(): Promise<Buffer> {
    return this.deferred.promise
  }

  _write(chunk: any, encoding: BufferEncoding, next: () => void) {
    this._buf.push(
      Buffer.isBuffer(chunk) ? chunk : Buffer.from(chunk, encoding)
    )
    next()
  }
}
