import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { minuteMs, secondMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { MultiMap } from "../../fe/MultiMap"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { mkLogger } from "../Logger"
import { isSingleSpecTests, isTest } from "../NodeEnv"
import { firstGt0 } from "../Number"
import { getDevEnvFlag } from "../env/DevEnvFlags"
import { setUnrefInterval } from "./Timers"

const logger = lazy(() => mkLogger("async.Endable"))

export interface Endable {
  readonly name: string
  readonly ended: boolean
  readonly endTimeoutMs?: number
  end(): Promise<any> | any
}

const endablesByRank = new MultiMap<EndableRank, Endable>()

setUnrefInterval(() => vacuumEndables(), 1 * minuteMs)

const DefaultTimeoutMs = 5 * secondMs

export const EndableRanks = strEnum(
  "first",
  "stats",
  "service",
  "predb",
  "db",
  "postdb",
  "logger",
  "logtail"
)
export type EndableRank = StrEnumKeys<typeof EndableRanks>

/**
 * Add an endable with an arbitrary end rank
 */
export function addEndable<T extends Endable>(
  rank: EndableRank,
  endable: T
): T {
  EndableRanks.validOrElse(rank, () => {
    throw new Error("internal error: invalid rank " + rank)
  })
  endablesByRank.add(rank, endable)
  return endable
}

let _ending = false

export function ending(): boolean {
  return _ending
}

export function setEnding(value: boolean) {
  if (isTest) {
    _ending = value
  } else {
    throw new Error("cannot set ending")
  }
}

export function endAll(...arr: Maybe<Endable>[]) {
  return Promise.all(arr.map(ea => end(ea)))
}

export async function end(e: MaybeSyncOrAsync<Endable>, endTimeoutMs?: number) {
  const endable = await e
  if (endable == null || true === endable?.ended) return
  const timeoutMs =
    isTest && getDevEnvFlag("PS_SINGLE_SPEC_TESTS")
      ? 100
      : firstGt0(endTimeoutMs, endable.endTimeoutMs, DefaultTimeoutMs)!
  logger().trace(endable.name + " ending...", { timeoutMs })
  try {
    await thenOrTimeoutError(endable.end(), timeoutMs, false)
  } catch (err) {
    try {
      // the logger might throw errors at the very end:
      logger().warn(endable.name + ".end() failed", err)
    } catch {
      //
    }
  }
}

function vacuumEndables() {
  endablesByRank.filterInPlace((_, v) => v.ended !== true)
  logger().debug(
    "vacuumEndables()",
    endablesByRank.entriesArray().map(([k, v]) => [k, v.map(ea => ea.name)])
  )
}

export const endEndables = lazy(async () => {
  const endTimeoutMs = isSingleSpecTests() ? 500 : undefined
  logger().info("endEndables()", {
    isTest,
    isSingleSpecTests: isSingleSpecTests()
  })
  if (!isTest) _ending = true
  vacuumEndables()
  for (const rank of EndableRanks.values) {
    const arr = endablesByRank.get(rank) ?? []
    if (isNotEmpty(arr)) {
      logger().debug("endEndables(): ending " + rank)
      await Promise.allSettled(arr.map(ea => end(ea, endTimeoutMs)))
    }
  }
})

export function endablesStats() {
  const result: any = {}
  for (const rank of EndableRanks.values) {
    const arr = endablesByRank.get(rank) ?? []
    result[rank] = arr.map(ea => ({ name: ea.name, ended: ea.ended }))
  }
  return result
}
