import { uniqBy } from "../../fe/Array"
import { blank, firstNotBlank, toNotBlank } from "../../fe/Blank"
import { maybeAnd } from "../../fe/Boolean"
import {
  ErrorDelimiter,
  errorCode,
  errorErrno,
  errorName
} from "../../fe/Error"
import { stringify } from "../../fe/JSON"
import { Obj, isEmptyObj, omit } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { toErrs } from "../../fe/toErr"
import { first } from "../Array"
import { dedupeNeedle, ellipsize, isString, splitUniq } from "../String"
import { joinErrorMessages, splitErrorMessage } from "./Error"
import {
  DoNotSendErrorFlag,
  ErrorFlag,
  FatalErrorFlag,
  IgnorableErrorFlag,
  NonRetriableErrorFlag,
  PleaseSendErrorFlag,
  RetriableErrorFlag,
  sortErrorFlags
} from "./ErrorFlags"
import {
  extractErrorFlags,
  isDoNotSendError,
  isFatalError,
  isIgnorableError,
  isRetriableError,
  stripErrorFlags
} from "./ErrorTypes"

export function toWrappedError(
  error: string | Error | unknown,
  meta?: unknown | Error | (NewWrappedError & { error?: Error | unknown })
): WrappedError {
  const args: NewWrappedError =
    meta == null
      ? {}
      : meta instanceof Error
      ? { cause: meta }
      : { cause: (meta as any)?.error, ...meta }

  if (error instanceof WrappedError) {
    // if meta doesn't do anything, don't re-wrap.
    if (
      meta == null ||
      blank(args.message) ||
      error.message.includes(args.message)
    ) {
      return error
    }
  }
  const message = firstNotBlank(
    isString(error) ? error : undefined,
    (error as any)?.message,
    args?.message
  )

  return new WrappedError(message ?? "(missing error message)", args)
}

export interface NewWrappedError {
  name?: string
  message?: string
  stack?: string
  code?: string
  codes?: string[]
  errno?: number
  syscall?: string
  path?: string // this isn't "nativePath" because native Node errors may include a `path` field that we want to capture.
  cause?: any // because catch(err) is `unknown`
  causes?: (Error | string)[]
  retriable?: boolean
  ignorable?: boolean
  fatal?: boolean
  doNotSend?: boolean
  flags?: ErrorFlag[]
  meta?: Obj
}

function mkCtx(opts?: NewWrappedError) {
  if (opts == null) return

  const ctx = omit(
    opts as any,
    "name",
    "message",
    "stack",
    "code",
    "codes",
    "errno",
    "syscall",
    "path",
    "cause",
    "causes",
    "retriable",
    "ignorable",
    "fatal",
    "doNotSend",
    "flags"
  )
  return isEmptyObj(ctx) ? undefined : stringify(ctx)
}

function mkMessage(message?: string, opts?: NewWrappedError) {
  let arr = splitErrorMessage(
    message,
    opts?.message,
    opts?.cause,
    ...(opts?.causes ?? [])
  )
  const path = opts?.path
  if (!blank(path)) {
    arr = dedupeNeedle(arr, path, "file")
  }

  return joinErrorMessages([
    arr[0],
    opts?.code,
    ...toA(opts?.codes),
    opts?.syscall,
    message,
    ...arr.slice(1)
  ])
}

export class WrappedError extends Error {
  readonly code?: string
  readonly codes?: string[]
  readonly errno?: number
  readonly syscall?: string
  readonly path?: string
  readonly causes: Error[]
  readonly retriable?: boolean
  readonly ignorable?: boolean
  readonly fatal?: boolean
  readonly doNotSend?: boolean
  readonly flags: ErrorFlag[]

  /**
   * No string ellipsis-ing. Flags are shoved to the end and uniq'ed.
   */
  static mkMessage(message?: string, opts?: NewWrappedError) {
    const s = splitErrorMessage(
      message,
      opts?.cause,
      ...toA(opts?.causes),
      mkCtx(opts)
    ).join(ErrorDelimiter)
    return stripErrorFlags(s) + extractErrorFlags(s).join("")
  }

  constructor(message: string, opts: Partial<NewWrappedError> = {}) {
    super(message)

    this.causes = uniqBy(
      toErrs(opts.cause, ...toA(opts.causes)),
      ea => ea.message
    )

    this.name = errorName(opts) ?? first(this.causes, errorName) ?? "Error"

    this.codes = splitUniq(
      [opts.code, ...this.causes.map(errorCode)],
      ErrorDelimiter
    )
    this.code = this.codes[0]
    // unset this.codes if this.code is sufficient:
    if (this.codes.length <= 1) this.codes = undefined

    this.errno = opts.errno ?? first(this.causes, ea => errorErrno(ea))

    this.syscall =
      opts.syscall ?? first(this.causes, (ea: any) => toNotBlank(ea["syscall"]))

    this.path =
      opts.path ?? first(this.causes, (ea: any) => toNotBlank(ea["path"]))

    this.flags = opts?.flags ?? []
    const arr = [opts, this.flags.join(""), ...this.causes]

    // If any are fatal, we're fatal.
    this.fatal = opts.fatal ?? arr.some(isFatalError)

    // If any are NOT retriable, we aren't retriable. If none make an assertion,
    // we shouldn't either.
    this.retriable = opts.retriable ?? maybeAnd(arr.map(isRetriableError))

    // If any are NOT ignorable, we aren't ignorable. If none make an assertion,
    // we shouldn't either.
    this.ignorable = opts.ignorable ?? maybeAnd(arr.map(isIgnorableError))

    // If any say we need to send, we should.
    this.doNotSend = opts.doNotSend ?? maybeAnd(arr.map(isDoNotSendError))

    this.flags = sortErrorFlags([
      ...this.flags,
      this.fatal ? FatalErrorFlag : undefined,
      this.retriable === true ? RetriableErrorFlag : undefined,
      this.retriable === false ? NonRetriableErrorFlag : undefined,
      this.ignorable === true ? IgnorableErrorFlag : undefined,
      this.doNotSend === false ? PleaseSendErrorFlag : undefined,
      this.doNotSend === true ? DoNotSendErrorFlag : undefined
    ])

    // Now that we have all the flags, rebuild the message:
    this.message =
      stripErrorFlags(mkMessage(message, this)) + this.flags.join("")

    this.stack = opts.stack ?? first(this.causes, (ea: any) => ea["stack"])
    if (this.stack == null) Error.captureStackTrace(this)
  }

  toJSON() {
    return {
      _ctor: "WrappedError",
      ...this,
      message: this.message
    }
  }

  static fromJSON(json: any) {
    return new WrappedError(json.message, json)
  }

  toString() {
    return this.message
  }

  toS(opts?: { maxLen: number; trailingChars: number }): string {
    return opts == null
      ? this.message
      : ellipsize(
          this.message, // flags are already at the end--just don't
          opts.maxLen,
          Math.max(opts.trailingChars, this.flags.length + 1)
        )
  }
}
