// TODO: move to ./proc/Pids.ts
const logger = lazy(() => mkLogger("proc.Pids"))

export interface PidfileInfo {
  pid: number
  cmd: string // < only for debugging
  ppid: number // < if ppid is gone, reap.
  /**
   * If <= 0, the process can run indefinitely (like web or sync)
   */
  maxAgeMs: number
}

export interface PidfileJson extends PidfileInfo {
  startTime: number
  timeoutTime?: number
}

// yeah, it's a lot of slop, but addPid() is called after a timeout, so start
// isn't really the start time.
const AllowableStartSlopMs = 10 * secondMs

function matchesPidfileInfo(
  info: Maybe<PidfileJson>,
  entry: Maybe<ProcEntry>
): boolean {
  if (info == null || entry == null || info.pid !== entry.pid) return false

  // The start times need to match, ish:
  const entryStartTime = map(entry.start, ea => ea.getTime())
  const fileStartTime = info.startTime
  return (
    gt0(entryStartTime) &&
    gt0(fileStartTime) &&
    Math.abs(entryStartTime - fileStartTime) < AllowableStartSlopMs
  )

  // Command names can change, so we just use the start time to match PIDs.
}

function killPidWinTaskkill(pid: number, force = false) {
  const args = ["/PID", toS(toInt(pid)), "/T"]
  if (force) {
    args.push("/F")
  }
  child_process.execFile("taskkill", args)
}

async function killPidWin(pid: number, force = false) {
  if (ending() || PowerShell.instance().ended) {
    killPidWinTaskkill(pid, force)
  } else {
    try {
      // https://docs.microsoft.com/en-us/powershell/module/microsoft.powershell.management/stop-process?view=powershell-6
      const cmd = compact([
        "Stop-Process",
        "-Id",
        toInt(pid),
        force ? "-Force" : undefined
      ]).join(" ")
      await PowerShell.instance().execute(cmd, identity)
    } catch (err) {
      logger().warn("killWin(): pwsh error, using TASKKILL: " + err)
      killPidWinTaskkill(pid, force)
    }
  }
}

async function killPidWithNode(pid: number, force = false) {
  try {
    process.kill(pid, force ? "SIGKILL" : "SIGTERM")
  } catch (err) {
    if (!String(err).includes("ESRCH")) throw err
  }
}

/**
 * Send a signal to the given process id.
 *
 * @param {number} pid the process id. Required.
 * @param {boolean} [force=false] if true, and the current user has
 * permissions to send the signal, the pid will be forced to shut down.
 */
export function killPid(
  pid: number,
  force = false,
  rewritePidfileOnForce = true
) {
  logger().log(force ? "warn" : "debug", "killPid", { pid, force })
  if (pid === process.pid || pid === process.ppid) {
    throw new Error("cannot self-terminate")
  }
  if (force && rewritePidfileOnForce) {
    void Pids.instance()?.onKill(pid)
  }
  return isWin ? killPidWin(pid, force) : killPidWithNode(pid, force)
}

export class Pids {
  static readonly instance = lazy(() =>
    map(configDir(), dir => new Pids(BaseFile.for(dir).join("pids")))
  )
  private readonly recentPids = new FifoCache<Promise<BaseFile>>(10 * secondMs)
  constructor(readonly pidsDir: BaseFile) {
    //
  }

  async addPid(
    info: PidfileInfo,
    start: Date,
    force = false
  ): Promise<BaseFile> {
    if (info == null) {
      throw new Error("undefined info")
    }
    const pid = info.pid
    if (!gt0(pid)) {
      throw new Error("undefined pid")
    }
    const key = info.ppid + ":" + info.pid
    if (force) this.recentPids.delete(key)
    return this.recentPids.getOrSet(key, async () => {
      const f = this.pidsDir.join(info.pid + ".json")
      const cmd = opt(Try(() => parseNativePath(info.cmd).base))
        .filter(notBlank)
        .getOrElse(() => info.cmd)

      const startTime = start.getTime()
      const json: PidfileJson = {
        ...info,
        cmd,
        startTime
      }
      // no need to fslock for pidfiles
      await f.writeJson_(json)
      logger().debug("addPid() wrote " + f, json)
      return f
    })
  }

  pidfiles() {
    return this.pidsDir.clear().children(ea => {
      const pid = toInt(ea.name)
      return ea.ext === ".json" && pid != null && pid !== process.pid
    })
  }

  async pids(pidfiles: MaybeSyncOrAsync<BaseFile[]> = this.pidfiles()) {
    return compact(toA(await pidfiles).map(ea => toInt(ea.name)))
  }

  async onKill(pid: number): PromiseMaybe<BaseFile> {
    const pidfile = this.pidsDir.join(pid + ".json")
    return thenMap(pidfile.clear().readJson<PidfileJson>(), priorInfo =>
      this.addPid({ ...priorInfo, maxAgeMs: 1 }, ago(minuteMs), true).catch(
        err => {
          logger().info("onKill(): failed to rewrite pidfile: " + err, { pid })
          return undefined
        }
      )
    )
  }

  readonly vacuumOldWip = rateLimited({
    name: "Pids.vacuumOldWip()",
    minCallDelayMs: minuteMs,
    f: async () => {
      // look for and delete old WIPs
      const minMtime = Date.now() - 2 * minuteMs
      const old = await this.pidsDir.childFiles(
        async ea =>
          isJsonExt(ea) &&
          isHiddenBasename(ea.base) &&
          lt(await ea.mtimeMs(), minMtime)
      )
      for (const ea of toA(old)) {
        logger().warn("killOldProcs(): deleting old pid WIP, " + ea.base)
        await ea.unlink("debug")
      }
    }
  })

  /**
   * @param everything if `true`, ignore pidfile maxAgeMs.
   * @param force given to `kill`.
   */
  readonly killOldProcs = rateLimited({
    name: "Pids.killOldProcs()",
    minCallDelayMs: minuteMs,
    f: async (
      opts: {
        everything?: boolean
        force?: boolean
        everythingBefore?: number
      } = {}
    ) => {
      await this.vacuumOldWip()
      const everything = opts.everything ?? false
      const force = opts.force ?? isWin
      const pidfiles = await this.pidfiles()
      const pids = await this.pids(pidfiles ?? [])
      if (isEmpty(pids)) {
        logger().info("killOldProcs(): no pidfiles")
        return []
      }
      const killed: (PidfileJson & Partial<ProcEntry>)[] = []

      const endPromises: Promise<void>[] = []

      const pidEntries = await pidInfos(pids)
      if (pidEntries == null) {
        onError("Pids.killOldProcs(): failed to get process information")
        return
      }

      for (const pidfile of pidfiles!) {
        const pid = toInt(pidfile.name)
        if (pid == null) {
          logger().warn("killOldProcs(): null pid from " + pidfile.base)
          await pidfile.unlink("debug")
          continue
        }

        const json = await pidfile.readJson<PidfileJson>()
        if (json == null) {
          logger().warn(
            "killOldProcs(): failed to read pidfile " + pidfile.base
          )
          await pidfile.unlink("debug")
          continue
        }
        if (json.pid !== pid) {
          // WHAT?
          logger().warn(
            "killOldProcs(): json.pid != name: unlinking invalid pidfile " +
              pidfile.base,
            { json, pid }
          )
          await pidfile.unlink("debug")
          continue
        }

        const pidEntry = pidEntries.find(ea => ea.pid === pid)
        if (pidEntry == null || !matchesPidfileInfo(json, pidEntry)) {
          logger().debug(
            "killOldProcs(): pid no longer present: unlinking " + pidfile.base,
            { json, pidEntry }
          )
          await pidfile.unlink("debug")
          killed.push(json)
          continue
        }

        const timeoutAt = gt0(json.maxAgeMs)
          ? json.startTime + json.maxAgeMs
          : undefined

        const reason = everything
          ? "all pids are being shut down"
          : gt(Date.now(), timeoutAt)
          ? `timed out ${Date.now() - timeoutAt!} ms ago`
          : lt(json.startTime, opts.everythingBefore)
          ? "cleanup everything before " +
            new Date(opts.everythingBefore!).toISOString()
          : undefined

        if (reason != null) {
          logger().info("killOldProcs(): killing", {
            reason,
            json
          })
          endPromises.push(killPid(pid, force, false))
          killed.push({ ...json, ...pidEntry })
          // ... we'll remove the pidfile once the process exits ...
        }
      }
      if (endPromises.length > 0) {
        await Promise.allSettled(endPromises)
      }

      return killed
    }
  })
}

export function addPid(
  info: PidfileInfo,
  start: Date
): Maybe<Promise<BaseFile>> {
  return Pids.instance()?.addPid(info, start)
}

// Should only be invoked by MainService
export const ProcCleaner = lazy<Endable>(() => {
  const timers = [
    // TCBH: prime intervals so they don't collide unnecessarily:
    { everything: false, force: false, intervalMs: 5 * minuteMs },
    { everything: false, force: true, intervalMs: 17 * minuteMs }
  ].map(ea =>
    setUnrefInterval(() => Pids.instance()?.killOldProcs(ea), ea.intervalMs)
  )
  return new EndableWrapper(
    "ProcCleaner",
    () => {
      timers.map(clearInterval)
      return Pids.instance()?.killOldProcs()
    },
    EndableRanks.predb
  )
})
