import * as fs from "fs"
import { readFile } from "fs-extra"
import { lazy } from "../../core/Lazy"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { LogLevel, LogLevels } from "../log/LogLevel"

const logger = lazy(() => mkLogger("fs.ReadFile"))

export async function readFileMaybe(
  nativePath: string,
  errLogLevel: LogLevel = LogLevels.info
): PromiseMaybe<Buffer> {
  try {
    return await readFile(nativePath)
  } catch (err) {
    logger().log(errLogLevel, ".readFileMaybe(" + nativePath + ")", err)
    return
  }
}

export function readFileSync(nativePath: string): Maybe<Buffer> {
  try {
    return fs.readFileSync(nativePath)
  } catch {
    //
  }
  return
}

export function intFromFileSync(nativePath: string): Maybe<number> {
  return logger().tap({
    msg: "intFromFileSync()",
    result: toInt(readFileSync(nativePath)?.toString(), {
      defaultValue: undefined
    }),
    meta: { nativePath }
  })
}
