import crypto from "crypto"
import { HashBits } from "./HashBits"
import { GeoRadix, Radix58 } from "./math/Radix"

export function stringShaToBuffer(input: string, msbits = HashBits): Buffer {
  return crypto
    .createHash("sha512")
    .update(input)
    .digest()
    .subarray(0, msbits / 8)
}
/**
 * Encoding of the SHA of `input`.
 */

export function shortStringSha(
  input: string,
  len = 9,
  radix = Radix58,
  msbits = 224 // < pre-v0.3.5 used 224 bits, so this keeps those SHAs stable
): string {
  return radix.encodeBuffer(stringShaToBuffer(input, msbits)).substring(0, len)
}
// filesystem-safe short shas

export function shortFsStringSha(
  input: string,
  len = 24,
  radix = GeoRadix,
  msbits = 224 // < pre-v0.3.5 used 224 bits, so this keeps those SHAs stable
): string {
  return shortStringSha(input, len, radix, msbits)
}
