import { Maybe } from "../fe/MaybeTypes"
import { DefaultNormalizer } from "./StringNormalizer"

export class CaseInsensitiveMap<V> implements Map<string, V> {
  // the value contains the original-cased key
  private readonly store = new Map<string, [string, V]>()

  constructor(
    initialContents: [string, V][] = [],
    readonly normalizer = DefaultNormalizer
  ) {
    for (const [k, v] of initialContents) {
      this.set(k, v)
    }
  }
  get(key: string): Maybe<V> {
    const k = this.normalizer(key)
    return k == null ? undefined : this.store.get(k)?.[1]
  }
  lookup(key: Maybe<string>): Maybe<[string, V]> {
    const k = this.normalizer(key)
    return k == null ? undefined : this.store.get(k)
  }
  has(key: Maybe<string>): boolean {
    const k = this.normalizer(key)
    return k == null ? false : this.store.has(k)
  }
  set(key: Maybe<string>, value: V): this {
    const k = this.normalizer(key)
    if (key != null && k != null) this.store.set(k, [key, value])
    return this
  }
  clear(): void {
    this.store.clear()
  }
  delete(key: string): boolean {
    const k = this.normalizer(key)
    return k == null ? false : this.store.delete(k)
  }
  forEach(
    callbackfn: (value: V, key: string, map: Map<string, V>) => void
  ): void {
    this.store.forEach(v => callbackfn(v[1], v[0], this))
  }
  get size(): number {
    return this.store.size
  }
  entries(): IterableIterator<[string, V]> {
    return this.store.values()
  }
  keys(): IterableIterator<string> {
    return [...this.store.values()].map(ea => ea[0])[Symbol.iterator]()
  }
  values(): IterableIterator<V> {
    return [...this.store.values()].map(ea => ea[1])[Symbol.iterator]()
  }
  [Symbol.iterator](): IterableIterator<[string, V]> {
    return this.entries()
  }
  get [Symbol.toStringTag](): string {
    return "CaseInsensitiveMap"
  }

  pick(...keys: string[]): Record<string, V> {
    const result: Record<string, V> = {}
    for (const key of keys) {
      const l = this.lookup(key)
      if (l != null) {
        result[l[0]] = l[1]
      }
    }
    return result
  }
}
