import os from "os"
import process from "process"
import { notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { ELECTRON_RUN_AS_NODE } from "../env/EnvKeys"

// THIS IS USED BY EVERYONE IN CORE, so only rely on fe code!

const _platform = os.platform()

export const inspectFlag =
  process.argv.includes("--inspect") || isTrue(process.env.NODE_INSPECT)

export const isCI = isTrue(process.env["CI"])

export const isWin = _platform === "win32" || _platform === "cygwin"
export const isWinPortable =
  isWin && notBlank(process.env.PORTABLE_EXECUTABLE_DIR)

export const isMac = _platform === "darwin"

export const isLinux = _platform === "linux"
export const isLinux_x64 = isLinux && os.arch() === "x64"
export const isArm = null != /^arm\b/i.exec(os.arch()) // "arm" on RPi, "arm64" on M1
export const isLinux_arm = isLinux && isArm
export const isLinuxAppImage =
  isLinux && (notBlank(process.env.APPIMAGE) || notBlank(process.env.APPDIR))
export const isLinuxSnap = isLinux && notBlank(process.env.SNAP_USER_DATA)

export const isPosix = isMac || isLinux

export const isMainElectron = process.versions["electron"] != null

export const isElectron =
  isMainElectron || isTrue(process.env[ELECTRON_RUN_AS_NODE])

/**
 * Compatible with electron-builder:
 */
export type Platform = "win" | "mac" | "linux"

export const platformName: Platform = isWin
  ? "win"
  : isMac
  ? "mac"
  : isLinux
  ? "linux"
  : (_platform as any) // < punt
