import { compact } from "./Array"
import { isError } from "./isError"
import { toA } from "./toA"
import { toS } from "./toS"

export function toErr(obj: any): Error | undefined {
  return obj == null ? undefined : isError(obj) ? obj : new Error(toS(obj))
}

export function toErrs(...objs: any[]): Error[] {
  return compact(toA(objs).map(toErr))
}
