const logger = lazy(() => mkLogger("settings.SettingsIO"))

export function librarySettingsFile(
  libraryDir?: Maybe<string | PosixFile>
): Maybe<PosixFile> {
  return libraryDataDirPosixFile(libraryDir)?.join(SettingsToml)
}

export const readSettings = lazy(async () => {
  await readSystemSettings()
  if (libraryHasSettings()) {
    await readLibrarySettings()
  }
}, minuteMs)

// Called by Service.setup:
export async function readSystemSettings(
  settingsFile: Maybe<string | SimpleFile> = systemSettingsFile()
) {
  if (settingsFile == null) return
  return _readSettings(BaseFile.for(settingsFile))
}

export async function envOrSavedLibraryDir() {
  return Settings.libraryDir.valueOrDefault ?? savedLibraryDir()
}

export async function savedLibraryDir(): PromiseMaybe<string> {
  try {
    return toNotBlank(
      (await readTomlFile_(systemSettingsFile()))?.[Settings.libraryDir.name]
    )
  } catch {
    return
  }
}

export async function systemSettingsVersion(): PromiseMaybe<string> {
  return readSettingsVersion(systemSettingsFile())
}

export async function librarySettingsVersion(
  libraryDir?: Maybe<string>
): PromiseMaybe<string> {
  return map(librarySettingsFile(libraryDir), ea => readSettingsVersion(ea))
}

// MUST BE CALLED AFTER SYSTEM SETTINGS HAVE BEEN READ
export async function maybeUpgradeSystemSettings() {
  // write system settings if they are missing or outdated.
  if (version !== (await systemSettingsVersion())) {
    await writeSystemSettings_()
  }
}

export async function maybeUpgradeLibrarySettings() {
  if (libraryHasSettings() && version !== (await librarySettingsVersion())) {
    await writeLibrarySettings_()
  }
}

export const libraryHasSettings = lazy(() => _libraryHasSettings())

// we don't want to unset this, as it is watched by HealthCheck--and .unset()
// will set it to null, and then immediately set it to true as soon as someone
// calls it again.
libraryHasSettings.unset = () => {
  logger().error("libraryHasSettings.unset() is invalid", shortStack())
}

later(() => {
  function refresh() {
    // we don't want to unset this, as it is watched by HealthCheck--and
    // .unset() will set it to null, and then immediately set it to true
    // again.
    libraryHasSettings.refresh()
  }
  ee().on("clearCache", refresh)
  ee().on("settingsChanged", refresh)
  Settings.libraryDir.watchLater(refresh)
})

// This needs to be sync for WebService and MenuItems:
export function _libraryHasSettings(libraryDir?: string): boolean {
  const lsf = librarySettingsFile(libraryDir)
  return logger().tap({
    msg: "_libraryHasSettings",
    result: lsf?.clear().existsSync() ?? false,
    level: "info",
    meta: {
      libraryDir,
      settings: Settings.libraryDir.valueOrDefault,
      librarySettingsFile: lsf?.nativePath
    }
  })
}

const VersionRE = /^# PhotoStructure v(?<version>\d+\.\d+\.\d+(?:-\S+)?)$/i

async function readSettingsVersion(
  file: Maybe<string | SimpleFile>
): PromiseMaybe<string> {
  if (blank(file)) return
  const match = await BaseFile.for(file).firstMatchingLine(VersionRE)
  return match?.groups?.version
}

async function writeToml_(file: PosixFile, settings: Setting<any>[]) {
  if (file == null) {
    logger().warn("writeToml(): null file, failed to write settings", {
      file,
      settings
    })
    return
  }
  const wip = await file.clear().isNonEmpty()
  const dest = wip ? file.wip() : file
  await _writeToml_(dest, settings)
  logger().info("writeToml(): wrote settings", {
    dest,
    file,
    nonDefaults: settingsToObj(settings),
    wip
  })

  if (wip) {
    const a = await readTomlFile_(dest)
    const b = await readTomlFile_(file)
    if (!eql(a, b)) {
      logger().info("Archiving prior, different contents", { dest, file })
      await file.renameYMDHMS_({ subdir: "old" })
      await dest.unwip_()
    } else {
      logger().info("Equivalent contents", { dest, file, a, b })
      await dest.unlink()
    }
  }
}

// This is lazy so that SettingIO.spec can force version to an expected value:
export const versionForSettings = lazy(() => version)

async function _writeToml_(
  file: BaseFile,
  settings: Setting<any>[]
): Promise<void> {
  // const priorValues: JsonMap = {}
  // for (const ea of priors) {
  //   const prior = await readTomlFile(ea)
  //   logger().info("writeToml(): importing " + ea, prior)
  //   assignFields(priorValues, prior)
  // }
  // logger().info("writeToml(): final prior values:", priorValues)

  const lines: string[] = [padding("#", 80)]
  lines.push(
    ...wrapComments(
      "",
      "Howdy!",
      "",
      `These are ${settings[0].categoryType} settings for PhotoStructure.`,
      "",
      " - Please shut down PhotoStructure before editing this file.",
      "",
      " - PhotoStructure has TWO settings files! See",
      "   https://photostructure.com/getting-started/advanced-settings/#omg-why-2",
      "   to see why.",
      "",
      ` - NOTE: lines starting with a "#" are commented out.`,
      "",
      ` - To override a setting, uncomment the line by removing the "# " from`,
      `   the beginning of the line, and change the value.`,
      "",
      ` - All duration settings end with "Duration" or "Ms" (for milliseconds).`,
      `   See https://photostructure.com/getting-started/advanced-settings#duration`,
      `   for details.`,
      "",
      ` - See https://photostructure.com/settings/ for more documentation.`,

      "Thanks for using PhotoStructure! Visit https://forum.photostructure.com/ if you find any bugs or have any questions, ideas, or feedback. We'd love to hear from you.",
      "",
      "-- ", // old email signature!
      "",
      // DON'T EDIT THIS! WE USE IT TO CHECK THE VERSION! See `VersionRE` above
      "PhotoStructure v" + versionForSettings()
    )
  )

  lines.push("", "")

  let priorCat = ""
  for (const setting of settings) {
    const cat = `${capitalize(
      setting.categoryType
    )}.${setting.category.toLowerCase()}`

    if (cat !== priorCat) {
      priorCat = cat
      lines.push(
        "",
        padding("#", 78),
        "#",
        "# Settings for " + cat + ":",
        "#",
        "",
        ""
      )
    }
    lines.push(...setting.toTomlLines())
    lines.push("", "")
  }

  await file.writeText_("\n" + lines.map(trimRight).join("\n") + "\n\n")
  ee().emitDebounced("settingsChanged")
}

export async function writeSystemSettings_(
  dest: Maybe<string | SimpleFile> = systemSettingsFile()
): PromiseMaybe<PosixFile> {
  const pf = PosixFile.forMaybe(dest)
  if (pf != null) await writeToml_(pf, persistedSystemSettings())
  return pf
}

export function writeAllSettings_(dest: string | SimpleFile) {
  return writeToml_(PosixFile.for(dest), persistedSettings())
}

export function readLibrarySettings(libraryDir?: string) {
  return _readSettings(librarySettingsFile(libraryDir))
}

/**
 * @throws if there are errors
 */
export async function writeLibrarySettings_(
  libraryDir?: string
): PromiseMaybe<PosixFile> {
  await setupLibraryDataDir_(
    firstNotBlank(libraryDir, Settings.libraryDir.value)!
  )
  const file = librarySettingsFile(libraryDir)
  logger().info("writeLibrarySettings()", { file })
  if (file != null) {
    await writeToml_(file, persistedLibrarySettings())
    libraryHasSettings.refresh()
  }
  return file
}

export async function _readSettings(
  f: Maybe<BaseFile>
): Promise<Setting<any>[]> {
  if (f == null) return []
  const log = logger().addContext(".importFileSettings(" + f?.nativePath + ")")
  try {
    const result = await importFileSettings_(f)
    if (isNotEmpty(result.warnings)) {
      log.warn("Problems during import", result.warnings)
    }
    return result.settings
  } catch (err) {
    log.error("Cannot read: " + errorToS(err))
    return []
  }
}

export async function importFileSettings_(
  f: BaseFile
): Promise<{ settings: Setting<any>[]; warnings?: string[] }> {
  const log = logger().addContext(".importFileSettings_(" + f.nativePath + ")")
  const st = await f.stat()
  if (st == null) {
    return { settings: [], warnings: ["Cannot read"] }
  }
  if (st.size === 0) {
    // empty file is OK?
    return { settings: [], warnings: ["Cannot read empty file"] }
  }
  const tomlMap = await readTomlFile_(f)
  if (tomlMap == null) {
    return { settings: [], warnings: ["No settings found"] }
  }

  const warnings: string[] = []

  const settings = compact(
    entries(tomlMap).map(([key, value]) => {
      const s = getSettingByNameOrKey(key)
      if (s == null) {
        warnings.push(
          `Failed to import "${key}"${mapOr(
            ciSettings().lookupNearest(key),
            ea => `: did you mean "${ea.key}"?`,
            ""
          )}`
        )
      } else {
        if (null == s.normalizeValue(value)) {
          warnings.push(`Invalid value for "${key}": ${stringify(value)}`)
        } else {
          s.fileValue = value
          return s
        }
      }
      return undefined
    })
  )
  log.info("loaded", {
    settings: settingsToObj(settings),
    warnings: toNotEmpty(warnings)
  })
  handleMetaSettings(settings)
  await handleDeprecatedSettings()
  return { settings, warnings }
}

const UnclearableSettings = lazy(
  () =>
    new Set(
      [
        Settings.httpPort,
        Settings.license,
        Settings.logStdout,
        Settings.logLevel
      ].map(ea => ea.key)
    )
)

export async function clearLibraryDirSetting() {
  await readSystemSettings()
  Settings.libraryDir.unset()
  await writeSystemSettings_()
}

/**
 * Force all in-memory settings to default values.
 */
export function clearSettings() {
  values(Settings)
    // don't reset LOG, NODE_ENV, or random ports!
    .filter(ea => !UnclearableSettings().has(ea.key))
    .forEach(ea => ea.unset())
  ee().emit("settingsChanged")
  // we're not emitting clearCache here--use nukeSettings() if you need that.
  // (see PathToLibraryAsset for an example why)
}

/**
 * Force all in-memory Settings to default values, and delete both system and
 * library settings.
 *
 * Only useful for tests.
 */
export async function nukeSettings() {
  await map(systemSettingsFile(), ea => BaseFile.for(ea))?.unlink("trace")
  await map(librarySettingsFile(), ea => ea.unlink("trace"))
  clearSettings()
  ee().emit("clearCache")
  setSettingsDefaults.refresh()
  return
}

function wrapComments(...lines: string[]): string[] {
  return flatten(lines.map(ea => wrap(ea, WrapComments)))
}

export async function writeEnv(
  file: BaseFile,
  settings: Setting<any>[]
): Promise<void> {
  const lines: string[] = []
  lines.push(
    ...wrapComments(
      "",
      `Welcome to PhotoStructure! These are the settings for version ${baseVersion()}.`,
      "",
      "Please see https://photostructure.com/environment-variables for more information about using environment variables with PhotoStructure.",
      "",
      `As of version 2.1, ".env" files (like this one) can be imported by PhotoStructure, but requires extra configuration. See https://photostructure.com/faq/environment-variables/#PS_ENV_FILE for details.`
    )
  )
  lines.push("", "")
  lines.push(
    ...wrapComments(
      "",
      "The following settings categories are stored in the system settings.toml:",
      "",
      ...sort([...SystemCategories]).map(ea => "* System." + ea),
      "",
      `The following settings categories are stored in the library settings.toml:`,
      "",
      ...sort([...LibraryCategories]).map(ea => "* Library." + ea),
      "",
      "Please visit https://forum.photostructure.com if you find anything that may be a bug or have any questions, ideas, or feedback. We'd love to hear from you!"
    )
  )
  lines.push("", "")
  lines.push(
    ...wrapComments(
      `-------------`,
      `PS_CONFIG_DIR`,
      `-------------`,
      "",
      `If the environment variable PS_CONFIG_DIR is set, PhotoStructure will use that value as your system configuration directory.`,
      "",
      `This is an advanced setting--we recommend using the default value, if possible.`,
      "",
      `If the PS_CONFIG_DIR environment variable is not set, the default is specific to your operating system:`,
      "",
      `- On Docker: "/ps/config" if that bind-mount exists, otherwise "/ps/library/.photostructure/docker-config".`,
      "",
      `- On Linux: firstNonBlank([$XDG_DATA_HOME, $XDG_CONFIG_HOME, "$HOME/.config"]) + "/PhotoStructure"`,
      "",
      `- On macOS: "$HOME/Library/Application Support/PhotoStructure"`,
      "",
      `- On Windows: "$APPDATA/PhotoStructure" ($APPDATA is normally set to $HOME/AppData/Roaming)`
    )
  )
  lines.push("", "")
  lines.push(
    ...wrapComments(
      `-----------`,
      `PS_ENV_FILE`,
      `-----------`,
      "",
      "If set, PhotoStructure will attempt to read the .env file specified by this environment variable.",
      "",
      "This must be a full pathname.",
      "",
      "Any value set by this file will override values in settings.toml files.",
      "",
      "See https://photostructure.com/faq/environment-variables/#alternative-coming-in-v21-support-for-env-files for details."
    )
  )
  lines.push("", "")

  let priorCat = ""
  settings.forEach(setting => {
    const cat = `${capitalize(
      setting.categoryType
    )}.${setting.category.toLowerCase()}`

    if (cat !== priorCat) {
      priorCat = cat
      lines.push(
        "",
        padding("#", 78),
        "#",
        "# Settings for " + cat + ":",
        "#",
        ""
      )
    }

    const name = `${setting.key} or ${setting.name}`

    const extra: any = { ...setting.addToJSON() }
    mapNotEmpty(setting.altKeys, arr => (extra.aliases = orList(arr)))

    const extraLines = entries(extra).map(
      ([k, v]) =>
        `${capitalize(camel2snake(k)).replace(/_+/g, " ")}: ${valueToS(v)}`
    )

    if (isNotEmpty(extraLines)) extraLines.push("")

    lines.push(
      ...wrapComments(
        padding("-", name.length),
        name,
        padding("-", name.length),
        "",
        ...(setting.opts.deprecated === true
          ? [
              "NOTE: this setting has been deprecated and will be removed in a future version of PhotoStructure.",
              ""
            ]
          : []),
        setting.opts.description.replace(/\n+/g, "\n\n"),
        "",
        ...(setting.transient
          ? [
              `This setting is transient and only set via environment variables.`,
              "\n"
            ]
          : []),
        ...extraLines,
        `${setting.key}=${stringify(
          toS(setting.opts.toEnv(setting.fileValue ?? setting.defaultValue))
        )}`
      )
    )
    lines.push("", "")
  })

  await file.writeText_(lines.map(ea => trimRight(ea)).join("\n"))
}
