import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { postrun } from "./Postrun"

export interface OneAtATime<T> {
  (): Promise<T>
  isRunning(): boolean
  force(): Promise<T>
  prior(): Promise<T>
}

export function oneAtATime<R>({
  fn,
  runLaterIfBusy = false,
  undefinedIfBusy
}: {
  fn: () => SyncOrAsync<R>
  runLaterIfBusy?: boolean
  undefinedIfBusy?: boolean
}): OneAtATime<R> {
  let prior: Promise<R>
  let running = false
  let runAgain = false
  // this isn't async just to reduce promise creation:
  const result = () => {
    if (running) {
      runAgain = true
      return true === undefinedIfBusy ? (undefined as any) : prior
    } else {
      running = true
      return (prior = postrun({
        fn,
        postrun: () => {
          running = false
          if (runLaterIfBusy === true && runAgain) {
            runAgain = false
            setImmediate(result)
          }
        }
      }))
    }
  }
  result.isRunning = () => running

  result.prior = () => prior

  result.force = async () => {
    await prior
    return result()
  }

  return result
}
