import process from "process"
import { lazy } from "../core/Lazy"
import { compactBlanks } from "../fe/Array"
import { Maybe } from "../fe/MaybeTypes"
import { ServiceName, ServiceNames } from "../fe/ServiceNames"
import { toS } from "../fe/toS"
import { isTest } from "./NodeEnv"
import { isPacked } from "./platform/IsPacked"

export const serviceName = lazy<ServiceName>(() => {
  if (isPacked()) {
    throw Error("serviceName() is unset")
  } else {
    return "" as any
  }
})

export const processName = lazy(() =>
  compactBlanks([serviceName.prior(), toS(process.pid)]).join("-")
)

serviceName.watchLater(() => {
  processName.unset()
})

export function serviceNameIndex(s: Maybe<ServiceName>): number {
  return ServiceNames.indexOf(s) ?? ServiceNames.length + 1
}

export const MainServices: ServiceName[] = [
  ServiceNames.main,
  ServiceNames.desktop
]

/**
 * If a database isn't available, these services can't run
 */
export const DbServices: ServiceName[] = [
  ServiceNames.sync,
  ServiceNames.info,
  ServiceNames.web,
  ServiceNames.list,
  ServiceNames.test
]

export function isDbService() {
  return DbServices.includes(serviceName())
}

export function isLibraryRequiredService() {
  return [ServiceNames.sync, ServiceNames.list].includes(serviceName() as any)
}

export function isServiceThatAcceptsFatalErrors() {
  return !isMainService() && !isWebService()
}

export function isMainService(name?: ServiceName) {
  // "main" and "desktop" are both "main services". We only discriminate between
  // them when the CLI is rendered.
  return MainServices.includes(name ?? serviceName())
}

export function isWebService(name?: ServiceName) {
  return (name ?? serviceName()) === ServiceNames.web
}

export function isBillingService(name?: ServiceName) {
  return (name ?? serviceName()) === ServiceNames.billing
}

export function isWorkerService(name?: ServiceName) {
  return (name ?? serviceName()) === ServiceNames.worker
}

export function isSyncService(name?: ServiceName) {
  return (name ?? serviceName()) === ServiceNames.sync
}

export function isInfoService() {
  return serviceName() === ServiceNames.info
}

export function isTestService() {
  return serviceName() === ServiceNames.test
}

function isTestNotWorker() {
  return (isTest || isTestService()) && !isWorkerService()
}

export const StatsDbServices: ServiceName[] = [ServiceNames.sync]

export function isStatsDbService() {
  return isSyncService()
}

export function isStatsDbMigrator() {
  return isStatsDbService() || isTestNotWorker()
}

export function isModelDbMigrator() {
  // Normally the web service is doing the migrations -- it's first to set up
  // a library.
  return isSyncService() || isWebService() || isTestNotWorker()
}

// Lazy so info can force --cleanup to run a backup:
export const isDbJanitorService = lazy(() => isSyncService() || isTestService())
