import { inspect } from "util"
import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import {
  bgBlack,
  bgRedBright,
  black,
  blue,
  blueBright,
  cyan,
  darkGrey,
  green,
  magenta,
  redBright,
  yellow,
  yellowBright
} from "../Chalk"
import { isTest } from "../NodeEnv"
import { processName } from "../ServiceNames"
import { Settings } from "../settings/Settings"
import { LogEntry } from "./LogEntry"
import { logFilter } from "./LogFilter"
import { FormatterOptions, LogFormatter } from "./LogFormatter"
import { LogLevel } from "./LogLevel"
import { prepMeta } from "./LogMeta"

const procColors = lazy(() => [
  { re: /billing|worker/, f: cyan },
  { re: /sync/, f: blue },
  { re: /web/, f: green },
  { re: /main/, f: yellow },
  { re: /info/, f: blueBright },
  { re: /test|log/, f: magenta }
])

export function colorProcessName(s: string): string {
  if (blank(s)) return ""
  const pc = procColors().find(ea => s.match(ea.re))
  return pc != null ? bgBlack(pc.f(s)) : s
}

export class ColoredLogFormatter implements LogFormatter {
  private readonly logLevels: Record<LogLevel, string> = {
    trace: "trace",
    debug: darkGrey("debug"),
    // the underscores support grepping by log level:
    info: cyan("info_"),
    warn: yellowBright("warn_"),
    error: redBright("error_"),
    fatal: bgRedBright(black("fatal"))
  }

  readonly inspectOptions: FormatterOptions

  defaultProcessName!: string

  constructor(readonly _inspectOptions: Partial<FormatterOptions> = {}) {
    this.inspectOptions = {
      showHidden: false,
      depth: 5,
      colors: true,
      compact: true,
      customInspect: true,
      maxArrayLength: Settings.logContextLimit.valueOrDefault + 3, // +3 so inspect doesn't do any array trimming
      processName: processName,
      ...this._inspectOptions
    }
    if (isTest) {
      this.inspectOptions.breakLength = 255
    }
    // This assumes we'll only make one of these! DON'T LEAK!
    Settings.logContextLimit.watchLater(curr => {
      this.inspectOptions.maxArrayLength = curr + 1
    })
    this.inspectOptions.processName.watch(
      ea => (this.defaultProcessName = colorProcessName(ea))
    )
  }

  formatMeta(meta?: Maybe<any[]>) {
    if (meta == null) return undefined
    const m = prepMeta(meta)
    return m == null ? undefined : inspect(m, this.inspectOptions)
  }

  formatLogEntry(le: LogEntry): string {
    const contextColor = logFilter().highlight(le.ctx) ? yellow : blue
    return compactBlanks([
      darkGrey(new Date(le.ts).toISOString()),
      le.from == null ? this.defaultProcessName : colorProcessName(le.from),
      this.logLevels[le.l],
      contextColor(le.ctx),
      le.msg,
      this.formatMeta(le.meta)
    ])
      .map(ea => toS(ea))
      .join(" ")
  }

  format(
    level: LogLevel,
    context: string,
    message: string,
    meta?: any
  ): string {
    return this.formatLogEntry({
      ts: Date.now(),
      l: level,
      from: processName(),
      ctx: context,
      msg: message,
      meta
    })
  }
}
