import { lazy } from "../../core/Lazy"
import { notBlankOr, toNotBlank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { version } from "../Version"
import { PosixFile } from "../fs/PosixFile"
import { safeUUID } from "../math/UUID"
import { Settings } from "../settings/Settings"

const logger = lazy(() => mkLogger("volumes.WriteVolumeUUID"))

export function volumeUuidSubpath() {
  return toNotBlank(Settings.volumeUuidFilePaths.values[0]) ?? ".uuid"
}

export async function writeVolumeUuid({
  mountpoint,
  uuid
}: {
  mountpoint: string
  uuid?: Maybe<string>
}): PromiseMaybe<string> {
  const f = PosixFile.for(mountpoint).join(volumeUuidSubpath())
  uuid = notBlankOr(uuid, safeUUID)
  if (await f.parent().isReadWritable()) {
    try {
      // Try to persist the UUID for later (or if the volume is remotely mounted)
      await f.writeText_(
        uuid,
        "",
        "# This volume UUID was written by PhotoStructure v" + version,
        "# See https://photostructure.com/faq/what-is-a-volume/ for details."
      )
      logger().info("writeVolumeUuid(): Saved new UUID for " + mountpoint, {
        uuid
      })
      return uuid
    } catch (error) {
      // Only return the UUID if we can persist it successfully.
      logger().warn(
        "writeVolumeUuid(): Failed to save new UUID for " + mountpoint,
        { error }
      )
    }
  } else {
    logger().warn(
      `writeVolumeUuid(): cannot write to ${f}, directory is not read/write`
    )
  }
  return
}
