/**
 * `minuend - subtrahend`, using eql() to compare elements.
 *
 * @template T must have a .valueOf() implementation
 * @param {T[]} minuend (haystack)
 * @param {T[]} subtrahend (needles)
 * @returns {T[]} values in `minuend` that, according to `eql()`, are
 * not in `subtrahend`. Neither arg is mutated.
 */
export function diffEql<T>(minuend: T[], subtrahend: T[]): T[] {
  return minuend.filter(ea => !subtrahend.some(eb => eql(ea, eb)))
}

// Extract the element type of an array:
export type ElementType<T extends readonly unknown[]> = T[number]
// type T = ElementType<SomeArrayType[]>

/**
 * @return true iff all `objects` are neither `undefined` nor `null`. Note
 * that `[]` returns true.
 */
export function allDefined<T>(
  arr: MaybeNull<MaybeNull<T>[]>
): arr is Defined<T>[] {
  return defined(arr) && arr.every(defined)
}

export function mapAllDefined<T, U>(
  arr: MaybeNull<MaybeNull<T>[]>,
  f: (t: T[]) => U
): Maybe<U> {
  return allDefined(arr) ? f(arr) : undefined
}

export function mapAll<T, V>(
  arr: MaybeNull<MaybeNull<T>[]>,
  f: (ea: T[]) => V
): Maybe<V> {
  return allDefined(arr) ? f(arr) : undefined
}

/**
 * @return true iff all `objects` are `undefined` or `null`
 */
export function allNotDefined(objects: any[]): boolean {
  return objects == null || objects.every(ea => ea == null)
}

export function allNotBlank(...arr: MaybeNull<any>[]): boolean {
  return arr != null && arr.every(notBlank)
}

/**
 * @return true iff any `objects` are `undefined` or `null`. Note that `[]`
 * returns false (as there aren't not-defined instances).
 */
export function anyNotDefined(objects: any[]): boolean {
  return objects == null || objects.some(ea => ea == null)
}

export function anyDefined(objects: any[]): boolean {
  return objects != null && objects.some(ea => ea != null)
}

export async function findAsync<T>(
  arr: Maybe<Maybe<T>[]>,
  f: (t: T) => MaybeSyncOrAsync<boolean>
): PromiseMaybe<T> {
  for (const ea of toA(arr)) {
    if (ea != null && true === (await f(ea))) return ea
  }
  return
}

export async function findLastAsync<T>(
  arr: Maybe<Maybe<T>[]>,
  f: (t: T) => MaybeSyncOrAsync<boolean>
): PromiseMaybe<T> {
  arr = toA(arr)
  for (let i = arr.length - 1; i >= 0; i--) {
    const ea = arr[i]
    if (ea != null && true === (await f(ea))) return ea
  }
  return
}

/**
 * @return the first non-`null` result of `f`, or if `f` is omitted, the first
 * non-null value in `arr`.
 */
export function first<T, R>(
  arr: Maybe<Maybe<T>[]>,
  f: (t: T) => MaybeNull<R>
): Maybe<R> {
  // PERF: avoid compact() to avoid creating a new array
  if (arr != null) {
    for (const ea of toA(arr)) {
      if (ea != null) {
        const r = f(ea)
        if (r != null) {
          return r
        }
      }
    }
  }
  return
}

/**
 * Return the first result from `f` that is defined.
 * @see Array#first
 * @see Later#firstDefinedLater
 */
export async function firstAsync<T, R>(
  arr: Maybe<Maybe<T>[]>,
  f: (t: T, index: number) => SyncOrAsync<MaybeNull<R>>
): PromiseMaybe<R> {
  if (arr != null) {
    let index = -1
    for (const t of arr) {
      index++
      try {
        if (t != null) {
          const r = await f(t, index)
          if (r != null) {
            return r
          }
        }
      } catch (err) {
        //
      }
    }
  }
  return undefined
}

/**
 * @see Later#firstDefinedLater
 */
export function firstNonEmptyThunk<T>(
  ...thunks: Thunk<Maybe<T[]>>[]
): Maybe<T[]> {
  for (const t of thunks) {
    const arr = t()
    if (isNotEmpty(arr)) {
      return arr
    }
  }
  return
}

/**
 * @return the last value in the array that satisfies the predicate
 */
export function findFromIndex<T>(
  arr: T[],
  predicate: (t: T) => boolean,
  fromIndex: number
): Maybe<number> {
  for (let i = fromIndex; i <= arr.length - 1; i++) {
    if (predicate(arr[i])) return i
  }
  return
}

/**
 * @return the last value in the array that satisfies the predicate
 */
export function findLast<T>(arr: T[], predicate: (t: T) => boolean): Maybe<T> {
  for (let i = arr.length - 1; i >= 0; i--) {
    if (predicate(arr[i])) return arr[i]
  }
  return
}

/**
 * @return the last index in the array that satisfies the predicate
 */
export function findLastIndex<T>(
  arr: T[],
  predicate: (t: T) => boolean
): number {
  for (let i = arr.length - 1; i >= 0; i--) {
    if (predicate(arr[i])) return i
  }
  return -1
}

export function concat<T>(...arrs: (MaybeNull<T> | MaybeNull<T>[])[]): T[] {
  const arr: T[] = []
  for (const ea of arrs) {
    if (Array.isArray(ea)) {
      for (const elem of ea) {
        if (elem != null) arr.push(elem)
      }
    } else if (ea != null) {
      arr.push(ea)
    }
  }
  return arr
}

export function moveToEnd<T>(arr: T[], element: T): T[] {
  remove(arr, element)
  arr.push(element)
  return arr
}

export function moveIndexToEnd<T>(arr: T[], index: number): T[] {
  const moved = arr[index]
  if (moved == null) return arr
  arr.push(moved)
  for (let i = index; i < arr.length - 1; i++) {
    arr[i] = arr[i + 1]
  }
  arr.length = arr.length - 1
  return arr
}

/**
 * @template T must have a .valueOf() implementation
 * @return the values in both `a` and `b`
 */
export function intersection<T>(
  a: T[],
  b: T[],
  valueOf: (t: T) => Primitive = primitiveValueOfOrElse
): T[] {
  const s = new Set(b.map(valueOf))
  return a.filter(ea => s.has(valueOf(ea)))
}

export function diceCoeff<T>(
  a: T[],
  b: T[],
  valueOf: (t: T) => Primitive = primitiveValueOfOrElse
): number {
  if (isEmpty(a) && isEmpty(b)) return 1
  return (intersection(a, b, valueOf).length * 2) / (a.length + b.length)
}

/**
 * @return true iff all elements in `a` are in `b` and all elements in `b` are
 * in `a`. Order is ignored. Elements must be sortable, however.
 */
export function eqlUnordered(a: Maybe<any[]>, b: Maybe<any[]>): boolean {
  return a == null || b == null
    ? false
    : zip(sortBy(a, stringifySorted), sortBy(b, stringifySorted)).every(
        ([x, y]) => eql(x, y)
      )
}

/**
 * Remove the first item from `arr` where the `predicate` returns `true`.
 * @return the element removed from `arr`
 */
export function removeFirst<T>(
  arr: T[],
  predicate: (item: T, index: number, sourceArray: T[]) => boolean
): Maybe<T> {
  const idx = arr.findIndex(predicate)
  return idx >= 0 ? arr.splice(idx, 1)[0] : undefined
}

export function uniqInPlace<T>(
  arr: T[],
  f: (t: T) => Maybe<Primitive> = ea => stringify(ea)
): void {
  copyArrayTo(uniqBy(arr, f), arr)
}

export function partition<T>(
  arr: Iterable<T>,
  filter: (t: T, index: number) => boolean
): [T[], T[]] {
  const accept: T[] = []
  const reject: T[] = []
  let i = 0
  for (const ea of arr) {
    ;(filter(ea, i++) ? accept : reject).push(ea)
  }
  return [accept, reject]
}

export function isUniq<T extends Comparable>(t: T[]): boolean {
  return t.every((ea, i) => t.indexOf(ea) === i)
}

/**
 * `Uniq -c`: return unique strings and their count
 */
export function uniqCount<T extends Primitive>(
  arr: T[]
): { t: T; count: number }[] {
  return _uniqCount(arr.sort())
}

function _uniqCount<T extends Primitive>(
  sortedArr: T[]
): { t: T; count: number }[] {
  if (sortedArr == null || sortedArr.length === 0) return []
  const t = sortedArr[0]
  const lastElem = sortedArr.lastIndexOf(t)
  return [
    { t, count: lastElem + 1 },
    ..._uniqCount(sortedArr.slice(lastElem + 1))
  ]
}

export function mapCompact<T, R>(
  arr: MaybeNull<T>[],
  f: (t: T) => Maybe<R>
): R[] {
  return compact(compact(arr).map(f))
}

export function toMapEntries<T, K, V>(
  arr: T[],
  f: (t: T) => MaybeNull<[K, V]>
): Map<K, V> {
  return new Map<K, V>(arr.map(f).filter(defined) as [K, V][])
}

export function flatMap<T, U>(arr: T[], f: (t: T) => Maybe<Maybe<U>[]>): U[] {
  return arr.reduce(
    (prev: U[], curr: T) => prev.concat(...compact(f(curr))),
    []
  )
}

export function retainLastN<T>(arr: T[], length: number): T[] {
  if (arr.length > length) {
    arr.splice(0, arr.length - length)
  }
  return arr
}

export function retainFirstN<T>(arr: T[], length: number): T[] {
  arr.length = Math.min(arr.length, length)
  return arr
}

