export function dateComparator(a: any, b: any): undefined | boolean {
  if (a instanceof Date && b instanceof Date) {
    return a.getTime() === b.getTime()
  }
  return undefined
}

export function functionComparator(a: any, b: any): undefined | boolean {
  return isFunction(a) && isFunction(b)
    ? a.toString() === b.toString()
    : undefined
}

export type Comparator = (a: any, b: any) => undefined | boolean

const objComparators: Comparator[] = [dateComparator, functionComparator]
const fieldComparators = new Map<string | symbol, Comparator>()

export function pushObjComparator(c: Comparator) {
  objComparators.push(c)
}

export function unshiftObjComparator(c: Comparator) {
  objComparators.unshift(c)
}

export function setFieldComparator(fieldName: string | symbol, c: Comparator) {
  fieldComparators.set(fieldName, c)
}

function comparator(a: any, b: any, key?: string | symbol): boolean | null {
  if (a == null || b == null) return a === b // null should not eql undefined
  if (key != null) {
    const c = fieldComparators.get(key)
    if (c != null) return c(a, b) ?? null
  }
  for (const ea of objComparators) {
    const r = ea(a, b)
    if (r != null) return r
  }
  return null
}

