import { lazy } from "../../core/Lazy"
import { sortBy } from "../../fe/ArraySort"
import { fromEntries, values } from "../../fe/Object"
import { BoundedList } from "../BoundedList"
import { LogEntry } from "./LogEntry"
import { LogLevel, LogLevels } from "./LogLevel"

export const SentLogLevels = lazy(() =>
  LogLevels.values.filter(ea => ea !== LogLevels.trace)
)

const MaxPerLevel = 48

// https://github.com/Microsoft/TypeScript/issues/24220#issuecomment-449325451
type Level2Recent = { [l in LogLevel]: BoundedList<LogEntry> }

/**
 * These are log entries that were *not* written to the log file (but will be
 * interesting if we want to "send recent logs".
 *
 * They are flushed to the logfile by log.error or by calling
 * writeRecentLogEntries()
 */
const recentLogEntriesByLevel = lazy<Level2Recent>(() =>
  fromEntries(
    SentLogLevels().map(ea => [ea, new BoundedList<LogEntry>(MaxPerLevel)])
  )
)

export function clearRecentLogEntries() {
  values(recentLogEntriesByLevel()).forEach(ea => ea.clear())
}

export function addRecentLogEntry(le: LogEntry) {
  recentLogEntriesByLevel()[le.l]?.push(le)
}

export function recentLogEntries() {
  const arr: LogEntry[] = []
  for (const bl of values(recentLogEntriesByLevel())) {
    arr.push(...bl.toA())
  }
  return sortBy(arr, ea => ea.ts)
}
