import { lazy } from "../../core/Lazy"
import { compact, uniq } from "../../fe/Array"
import { sort } from "../../fe/ArraySort"
import { blank, notBlank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, gte0 } from "../../fe/Number"
import { Obj, compactBlankValues } from "../../fe/Object"
import { ensureSuffix } from "../../fe/String"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { equalsIgnoreCase } from "../String"
import { mapAsync } from "../async/Promise"
import { onError } from "../error/OnError"
import { PowerShell } from "../pwsh/PowerShell"
import { Settings } from "../settings/Settings"
import { mountpointsWin } from "./MountpointsWin"
import { parseRemoteName } from "./RemoteVolumesWin"
import { Volume } from "./Volume"
import { commandTimeoutMs } from "./VolumeTtls"

const logger = lazy(() => mkLogger("volumes.DfWin"))

// This is already cached by _volumesImpl()
export async function dfWin() {
  const disks = await volumeInfoWin()
  return disks.filter(d => d.ok !== false && gt0(d.size))
}

const DriveLetterRE = /([a-z]+)(?::\\)?/i

function cleanDriveLetter(driveLetter?: Maybe<string>) {
  return driveLetter?.match(DriveLetterRE)?.[1]?.toUpperCase()
}

export function normalizeDriveLetter(driveLetter: Maybe<string>) {
  return map(cleanDriveLetter(driveLetter), ea => ea + ":\\")
}

// Returns network-mapped drives, but doesn't get operational status or UUIDs:
export function getPsDriveCommand(driveLetter?: Maybe<string>) {
  return `Get-PSDrive -PSProvider FileSystem ${toS(
    cleanDriveLetter(driveLetter)
  )} | Select-Object -Property Root,DisplayRoot,Description,Used,Free`
}

function parseGetPsDriveRow(ea: Obj): Maybe<Volume> {
  return ea == null || blank(ea.Root) || !gte0(ea.Free) || !gte0(ea.Used)
    ? undefined
    : (compactBlankValues({
        mountpoint: ensureSuffix(ea.Root, ":\\").toUpperCase(),
        label: ea.Description,
        size: ea.Used + ea.Free,
        used: ea.Used,
        available: ea.Free,
        remote: notBlank(ea.DisplayRoot),
        ...map(parseRemoteName(ea.DisplayRoot), remote => ({
          remoteHost: remote.host,
          remoteShare: remote.share
        }))
      }) as Volume)
}

// Note: Get-Disk and Get-Partition return physical (not logical) disk info.

// No remote drives, but we get UUIDs:
export function getVolumeCommand(driveLetter?: Maybe<string>) {
  return `Get-Volume ${toS(
    cleanDriveLetter(driveLetter)
  )} | Select-Object -Property DriveLetter,FileSystem,FileSystemLabel,Size,SizeRemaining,HealthStatus,OperationalStatus,UniqueId`
}

export function getLocalVolumesWin_() {
  return PowerShell.instance().executeJsonToA(getVolumeCommand())
}

const uuidRE = /\{([-a-z0-9]{7,})\}/i

/**
 * @param s "\\?\Volume{717926df-0000-0000-0000-50e01f000000}\"
 */
function uniqueId2uuid(s: Maybe<string>) {
  return logger().tap({
    msg: "uniqueId2uuid",
    result: map(uuidRE.exec(toS(s)), m => m[1]),
    meta: { s }
  })
}

function toDefinedAndNotEql(s: Maybe<string>, v: string) {
  s = s?.trim()
  return !blank(s) && !equalsIgnoreCase(s, v) ? s : undefined
}

export function parseGetVolumeRow(o: Obj): Maybe<Volume> {
  // We don't care about system recovery volumes:
  if (
    o.DriveLetter == null ||
    o.DriveLetter === "null" ||
    o.FileSystemLabel === "System Reserved"
  ) {
    logger().debug("parseGetVolumeRow(): skipping system recovery volume", {
      o
    })
    return
  }
  const mountpoint = normalizeDriveLetter(o.DriveLetter)
  if (mountpoint == null) {
    logger().debug(
      "parseGetVolumeRow(): skipping volume: invalid DriveLetter",
      { o }
    )
    return
  }
  const notOkStatus = compact([
    toDefinedAndNotEql(o.HealthStatus, "healthy"),
    toDefinedAndNotEql(o.OperationalStatus, "OK")
  ]).join("; ")
  const ok =
    o.Size != null &&
    o.SizeRemaining != null &&
    notBlank(o.DriveLetter) &&
    blank(notOkStatus)
  return logger().tap({
    msg: "parseGetVolumeRow()",
    result: {
      mountpoint,
      filesystem: o.FileSystem,
      label: o.FileSystemLabel,
      uuid: uniqueId2uuid(o.UniqueId),
      size: o.Size,
      used: o.Size - o.SizeRemaining,
      available: o.SizeRemaining,
      remote: false, // Get-Volume only returns local drives.
      ok,
      status: notOkStatus
    },
    meta: { input: o }
  })
}

export async function volumeInfoWin(): Promise<Volume[]> {
  // Windows can sometimes get confused and map the same network drive to both
  // f:\ and F:\
  const driveLetters = uniq(
    toA(await mountpointsWin()).map(normalizeDriveLetter)
  )

  // We ask for each drive letter separately to parallelize the requests.
  const psDrivesJson = await mapAsync({
    name: "volumeInfoWin.psDrivesJson",
    arr: driveLetters,
    f: drive =>
      PowerShell.instance()
        .executeJsonToA(getPsDriveCommand(drive))
        .catch(error => {
          onError("volumeInfoWin(): Get-PSDrive failed for drive " + drive, {
            error
          })
          return []
        }),
    maxConcurrent: Settings.powerShellProcs.valueOrDefault,
    timeoutMs: commandTimeoutMs()
  })

  const psDriveVols = compact(flatten(psDrivesJson).map(parseGetPsDriveRow))

  // NOTE: Get-Volume doesn't return network drives or virtual drives

  const getVolumesJson = await mapAsync({
    name: "volumeInfoWin.volumesJson",
    arr: driveLetters,
    f: drive =>
      PowerShell.instance()
        .executeJsonToA(getVolumeCommand(drive))
        .catch(err => {
          logger().debug(
            "volumeInfoWin(): Get-Volume failed for drive " + drive,
            err
          )
          return []
        }),
    maxConcurrent: Settings.powerShellProcs.valueOrDefault,
    timeoutMs: commandTimeoutMs()
  })

  const getVolumesVols = compact(flatten(getVolumesJson).map(parseGetVolumeRow))

  // Don't include any mountpoints that are unhealthy:
  const unhealthy = uniq(
    [...psDriveVols, ...getVolumesVols]
      .filter(ea => ea.ok === false)
      .map(ea => ea.mountpoint)
  )

  const healthyMountpoints = sort(
    uniq([...psDriveVols, ...getVolumesVols].map(ea => ea.mountpoint)).filter(
      ea => !unhealthy.includes(ea)
    )
  )

  logger().info("volumeInfoWin()", {
    psDriveVols,
    getVolumesVols,
    healthyMountpoints,
    unhealthy
  })

  const vols = healthyMountpoints.map(mountpoint => ({
    ...psDriveVols.find(ea => mountpoint === ea.mountpoint)!,
    ...getVolumesVols.find(ea => mountpoint === ea.mountpoint)!
  })) as Volume[]

  vols.forEach(vol => {
    const sad = unhealthy.includes(vol.mountpoint)
    if (sad) vol.ok = false
    vol.ignorable = vol.ignorable === true || vol.ok === false
  })

  return vols
}
