import { lazy } from "../../core/Lazy"
import { minuteMs, secondMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { gte } from "../../fe/Number"
import { shim1 } from "../Shim1"
import { TTLMap } from "../TTLMap"
import { ee } from "../event/EventEmitter"
import { ProgressEvt, ProgressEvtWithPath, isProgressEvt } from "./ProgressEvt"

export const DefaultThrottleMs = 500

export const recentProgress = lazy(
  () => new TTLMap<string, ProgressEvtWithPath>(15 * secondMs)
)

export const recentDone = lazy(
  () => new TTLMap<string, ProgressEvtWithPath>(2 * minuteMs)
)

export function emitProgressEvt(pe: Maybe<ProgressEvt>) {
  if (isProgressEvt(pe)) {
    const done = true === pe.done
    if (done) {
      if (pe.path != null) recentDone().set(pe.path, pe as any)
    }
    // Throttle progress for any given file, unless it's done.
    // Don't throttle progress without a file.
    if (
      done ||
      pe.path == null ||
      !gte(
        recentProgress().lastSetOrGetTs(pe.path),
        Date.now() - DefaultThrottleMs
      )
    ) {
      void onProgressEvt(pe)
      if (pe.path != null) recentProgress().set(pe.path, pe as any)
    }
  }
}

export const onProgressEvt = shim1({
  name: "onProgressEvt",
  impl: async (pe: ProgressEvt) => {
    ee().emit("progress", pe)
  }
})
