import { dayMs, minuteMs } from "../fe/Date"
import { allDefined } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { toInt } from "../fe/Number"
import { opt } from "../fe/Opt"
import { within } from "./Number"

const wmi =
  /((?:19|20)\d\d)([01]\d)([0123]\d)([012]\d)([012345]\d)([012345]\d)\.(\d{6})([+-]\d{3})?/
/**
 * yyyymmddHHMMSS.mmmmmmsUUU or 20190415124112.947119-420
 *
 * @see https://msdn.microsoft.com/en-us/library/aa387237(v=vs.85).aspx
 */
export function wmiDate(s: string): Maybe<Date> {
  const m = wmi.exec(s)
  if (m == null) return
  const arr = m.slice(1, 8).map(ea => toInt(ea))
  if (!allDefined(arr)) return undefined
  const [year, mon, day, hour, min, sec, micros] = arr as number[]
  const offset = toInt(m[8], { defaultValue: 0 })!

  // console.dir({ s, m, year, mon, day, hour, min, sec, micros, offset })
  return new Date(
    Date.UTC(year, mon - 1, day, hour, min, sec, micros / 1000) -
      offset * minuteMs
  )
}

const WinDateRe = /Date\((\d+)\)/

/**
 * PowerShell renders Dates in JSON structures as "/Date(1552014999676)/"
 */
export function pwshJsonDate(s: string): Maybe<Date> {
  return opt(s)
    .flatMap(ea => WinDateRe.exec(ea))
    .flatMap(ea => ea[1])
    .flatMap(toInt)
    .filter(ea => within(0, Date.now() + dayMs, ea))
    .map(ea => new Date(ea))
    .get()
}
