import process from "process"
import { lazy } from "../../core/Lazy"
import { toNotBlank } from "../../fe/Blank"
import { magenta } from "../Chalk"
import { mkLogger } from "../Logger"
import { stderrWrite } from "../StdoutWrite"
import { toWrappedError } from "../error/WrappedError"
import { LogLevels } from "../log/LogLevel"
import { endEndables, ending } from "./Endable"

const logger = lazy(() => mkLogger(magenta("Exit")))

export const onFatalHandlers: ((reason: string) => any)[] = []

export async function exit(args: {
  reason?: string
  status?: number
  error?: any
}) {
  logger().log(args.status === 0 ? LogLevels.info : LogLevels.warn, "exit()", {
    ...args,
    ending: ending()
  })
  const error = toWrappedError(args.error, {
    message: args.reason,
    errno: args.status
  })
  if (error.errno !== 0 || error.fatal === true) {
    stderrWrite({ fatal: true, ...args })
    const reason = toNotBlank(args?.reason) ?? error.toString()
    for (const ea of onFatalHandlers) {
      await ea(reason)
    }
  }

  if (!ending()) {
    await endEndables()
    process.exit(args.status)
  }
}

export const exitOnStdStream = lazy(() => {
  for (const streamName of ["stdin", "stdout", "stderr"] as const) {
    for (const eventName of ["close", "error", "disconnect", "end"] as const) {
      process[streamName].on(eventName, (error: any) => {
        void exit({
          reason: `${streamName}:${eventName}`,
          status: 0,
          error
        })
      })
    }
  }
})
