import { notBlank } from "../../fe/Blank"
import { isBoolean } from "../../fe/Boolean"

export interface SimpleDirent {
  /**  isFile */
  isFile: boolean
  /**  isDirectory */
  isDirectory: boolean
  /** full base name */
  basename: string
}
export function isSimpleDirent(d: any): d is SimpleDirent {
  return (
    d != null &&
    notBlank(d.basename) &&
    isBoolean(d.isFile) &&
    isBoolean(d.isDirectory)
  )
}
