import { lazy } from "../core/Lazy"
import { ConsoleLogger } from "./log/ConsoleLogger"
import { ContextualLogger } from "./log/ContextualLogger"
import { SimpleLogger } from "./log/Logger"

export const rootLoggers = lazy<SimpleLogger[]>(() => [
  ConsoleLogger.instance()
])

const consoleLogger = lazy(() => [ConsoleLogger.instance()])

export type Logger = ContextualLogger // TODO: inline?

export function mkLogger(context: string): ContextualLogger {
  return new ContextualLogger(context, rootLoggers)
}

export function mkConsoleLogger(context: string): ContextualLogger {
  return new ContextualLogger(context, consoleLogger)
}
