import { Command } from "commander"
import { joinLines } from "../fs/CRLF"
import { cliWrap } from "./CliWrap"
import { DescriptionFooter } from "./DescriptionFooter"

export const CliDesc = {
  main: "PhotoStructure's main process manager. Runs and manages web and sync services.",
  desktop: "PhotoStructure for Desktops. Manages web and sync services.",
  info: "Configuration, file metadata and import diagnostics tool.",
  list: "List paths in a PhotoStructure Library.",
  logcat: "Chronologically sort and pretty-print PhotoStructure logfiles.",
  logtail:
    "View the log messages of currently-running PhotoStructure processes as they are emitted. (Like `tail -f`).",
  web: "PhotoStructure's web service. Automatically started by main.",
  sync: "PhotoStructure's directory synchronization service. Automatically started by main."
  // worker doesn't have a CLI.
}

export function addFooter(c: Command): Command {
  return c.on("--help", () => {
    console.log(joinLines(...cliWrap(DescriptionFooter)))
  })
}
