import { pidExists } from "batch-cluster"
import { Dirent } from "fs"
import fse from "fs-extra"
import { clearTimeout } from "timers"
import { compactBlanks, filterInPlace } from "../../fe/Array"
import { at } from "../../fe/At"
import { secondMs } from "../../fe/Date"
import { eql } from "../../fe/Eql"
import { fmtDuration } from "../../fe/FmtDuration"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, round, toInt } from "../../fe/Number"
import { randomFloat } from "../../fe/Random"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { toS } from "../../fe/toS"
import { isTest } from "../NodeEnv"
import { escapeRegExp } from "../RegExp"
import { serviceName } from "../ServiceNames"
import { EndableRank, EndableRanks, ending } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { oneAtATime } from "../async/OneAtATime"
import { InternalErrorFlag } from "../error/ErrorFlags"
import { LogLevels } from "../log/LogLevel"
import { TokenRadix } from "../math/Radix"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { BaseFile } from "./BaseFile"
import {
  PsLockExt,
  fsLockDir,
  fsLockFileBasename,
  isPsLockFileOrDir
} from "./FsLockDir"
import { SimpleFileOrString, basename } from "./SimpleFile"

export interface FsLockOptions {
  name?: string
  file: BaseFile
  /**
   * If 0, no timeout
   */
  timeoutMs: number
  noopIfContested?: boolean
  /**
   * If true, ignore fslock
   */
  skipFsLock?: boolean

  endableRank?: EndableRank

  /**
   * If this is lock in a cache directory, don't bother running rmdir'ing the
   * lock dir on release.
   */
  dirty?: boolean
}

type VacuumArg = { vacuum: boolean }

const LastNumberRe = new RegExp("-(?<pid>\\d+)" + escapeRegExp(PsLockExt) + "$")

/**
 * FsLock is a filesystem-based advisory lock for a given file, where:
 *  - lock contention is expected both inter and intra processes
 *  - fair lock acquisition (first in, first served)
 *  - locks are only held temporarily
 *  - locks are held per given instance, not per process
 *
 * The instance that holds the lock is the instance whose filename is
 * alphabetically first in the lock dir.
 */
export class FsLock extends EndableWrapper {
  static #lastUidTime: number = 0
  static internalErrors = 0
  static #instanceCount = 0

  static newUid(pid = process.pid) {
    // Make sure lastUidTime is always increasing:
    this.#lastUidTime = Math.max(Date.now(), this.#lastUidTime + 1)
    // The time uid MUST BE FIRST in the UID to ensure locks are acquired in
    // temporal order:
    return compactBlanks([
      TokenRadix.encode(this.#lastUidTime), // filename-safe radix
      serviceName.prior(),
      toS(pid)
    ]).join("-")
  }

  static pidFromLockfile(lockfile: SimpleFileOrString | Dirent): Maybe<number> {
    return FsLock.pidFromBasename(basename(lockfile))
  }

  static pidFromBasename(base: string): Maybe<number> {
    return toInt(LastNumberRe.exec(toS(base))?.groups?.pid)
  }

  static #instances: FsLock[] = []

  /**
   * Use `FsLock.for()` instead of the constructor to ensure the same fslock
   * instance is used for the same file
   */
  static for(opts: FsLockOptions): FsLock {
    return (
      this.#instances.find(ea => ea.opts.file === opts.file) ?? new FsLock(opts)
    )
  }

  readonly #instanceId = ++FsLock.#instanceCount
  readonly lockdir: BaseFile
  #acquired = false
  /** only set when acquisition is requested */
  lockfile: Maybe<BaseFile>
  #refreshTimer: Maybe<NodeJS.Timeout>
  #rmLockdirLater: Maybe<NodeJS.Timeout>
  #lastVacuumTs = Date.now()
  readonly staleMs: number

  constructor(readonly opts: FsLockOptions) {
    super(
      opts.name ?? "FsLock",
      () => this.release(),
      opts.endableRank ?? EndableRanks.postdb,
      commandTimeoutMs()
    )
    this.lockdir = fsLockDir(this.opts.file)

    if (opts.name == null) {
      this.name =
        "fs.FsLock(" + opts.file.baseWithGrandparent + ")#" + this.#instanceId
    }

    // files should be checked for being stale at least once during the
    // timeoutMs period. We make this a random period less than timeoutMs to
    // avoid a thundering herd of processes all trying to vacuum at the same
    // time.
    this.staleMs = round(this.opts.timeoutMs / randomFloat(2, 4))
    FsLock.#instances.push(this)
  }

  get acquired() {
    return this.#acquired
  }

  toLogJSON() {
    return {
      name: this.name,
      acquired: this.acquired
    }
  }

  async #maybeVacuum(arg?: VacuumArg) {
    const why =
      true === arg?.vacuum
        ? "arg.vacuum"
        : gt0(this.staleMs) && Date.now() > this.#lastVacuumTs + this.staleMs
        ? "lastVacuumTs was too long ago"
        : undefined

    return this.logger.tap({
      msg: "#maybeVacuum()",
      level: why == null ? "trace" : "debug",
      result: why == null ? undefined : await this.#vacuum(),
      meta: { why }
    })
  }

  async #vacuum() {
    this.#lastVacuumTs = Date.now()
    const survivors: string[] = []

    // NOTE: we don't no-op this whole section if #staleMs is 0, because we
    // can still determine lockfiles are stale if their pid is gone.

    const names = await this.#selfAndSiblingNames_()

    for (const ea of names) {
      const child = this.lockdir.join(ea)
      if (gt0(this.staleMs)) {
        const staleAt =
          Math.round((Date.now() - (this.staleMs + secondMs)) / 1000) * 1000
        const childMtimeMs = await child.mtimeMs({ refresh: true })
        if (childMtimeMs != null && childMtimeMs < staleAt) {
          this.logger.warn(
            "#vacuum(): child " +
              child.base +
              " is stale by " +
              fmtDuration(staleAt - childMtimeMs),
            { staleMs: this.staleMs, childMtimeMs, staleAt }
          )
          await child.unlink()
          continue
        }
      }
      const pid = FsLock.pidFromLockfile(child)
      if (pid == null) {
        this.logger.error(
          "#vacuum(): Lockfile " +
            child.base +
            " has no pid." +
            InternalErrorFlag
        )
        FsLock.internalErrors++
        await child.unlink()
        continue
      }
      if (!pidExists(pid)) {
        this.logger.warn(
          "#vacuum(): Lockfile " + child.base + "'s pid is gone.",
          { pid }
        )
        await child.unlink()
        continue
      }
      this.logger.debug(
        "#vacuum(): Lockfile " + child.base + " is still valid.",
        { pid }
      )
      survivors.push(ea)
    }
    return survivors
  }

  async #selfAndSiblingNames_() {
    const arr = await fse.readdir(this.lockdir.nativePath, {
      withFileTypes: true
    })
    return arr
      .filter(ea => ea.isFile() && isPsLockFileOrDir(ea))
      .map(ea => ea.name)
  }

  async selfAndSiblingNames_(arg?: VacuumArg): Promise<string[]> {
    return (await this.#maybeVacuum(arg)) ?? this.#selfAndSiblingNames_()
  }

  async siblingNames(arg?: VacuumArg): Promise<string[]> {
    return (await this.selfAndSiblingNames_(arg)).filter(
      ea => ea !== this.lockfile?.base
    )
  }

  async #iAmNext_(arg?: VacuumArg): Promise<boolean> {
    if (this.lockfile == null) {
      this.release()
      return false
    }
    const children = await this.selfAndSiblingNames_(arg)
    // someone can sneak into .release() while I was checking sibling names:
    if (this.lockfile == null) {
      this.release()
      return false
    }
    if (!children.includes(this.lockfile.base)) {
      if (isTest) {
        FsLock.internalErrors++
        this.logger.throw(
          "#iAmNext_(): missing lockfile, " + this.lockfile + InternalErrorFlag,
          { children, lockfile: this.lockfile }
        )
      } else {
        this.release()
        return false
      }
    }

    const result = children[0] === this.lockfile.base
    if (result) {
      this.#acquired = true
    } else {
      // If the current owner is dead, vacuum.
      if (arg?.vacuum !== true) {
        const ownerPid = FsLock.pidFromBasename(children[0])
        if (gt0(ownerPid) && !pidExists(ownerPid)) {
          this.logger.info("#iAmNext_(): auto-vacuuming, owner pid is gone.")
          return this.#iAmNext_({ vacuum: true })
        }
      }
    }
    return this.logger.tap({
      msg: "#iAmNext_()",
      result,
      meta: {
        lockfiles: children
      }
    })
  }

  async iAmLast_(arg?: VacuumArg): Promise<boolean> {
    return (
      this.lockfile != null &&
      at(await this.selfAndSiblingNames_(arg), -1) === this.lockfile.base
    )
  }

  async iAmOnly_(arg?: VacuumArg): Promise<boolean> {
    return (
      this.lockfile != null &&
      eql(await this.selfAndSiblingNames_(arg), [this.lockfile.base]) === true
    )
  }

  readonly tryAcquire_ = oneAtATime<boolean>({
    runLaterIfBusy: false,
    undefinedIfBusy: true,
    fn: async () => {
      if (this.#acquired) return true
      await (this.lockfile ??= this.lockdir.join(
        fsLockFileBasename(FsLock.newUid())
      )).touch_()
      this.startRefreshTimer_()
      try {
        // something may have raced us to release() before we could check if
        // we were next:
        if (this.lockfile == null) return false
        if (await this.#iAmNext_()) {
          this.logger.debug("tryAcquire(): acquired")
          return true
        }
        return false
      } catch (error) {
        this.release()
        this.logger.error("tryAcquire() failed", error)
        return false
      }
    }
  })

  startRefreshTimer_() {
    if (this.#refreshTimer == null) {
      if (this.staleMs > 0) {
        const lockfileRefreshMs = Math.max(
          // Make sure we refresh more frequently than the staleMs:
          this.staleMs / 2,
          // But not more than once a second:
          isTest ? 10 : secondMs
        )
        this.#refreshTimer = setInterval(
          () => this.#refreshLockfile_(),
          lockfileRefreshMs
        )
      }
    }
  }

  stopRefreshTimer() {
    if (this.#refreshTimer != null) {
      clearInterval(this.#refreshTimer)
      this.#refreshTimer = undefined
    }
  }

  async #refreshLockfile_() {
    if (
      this.lockfile == null ||
      (await this.lockfile.notExists({ refresh: true }))
    ) {
      // This _shouldn't_ happen...
      this.logger.warn("#refreshLockfile_(): null or missing lockfile", {
        lockfile: this.lockfile,
        notExists: await this.lockfile?.notExists()
      })
      // But if it does, do the bookkeeping to make sure our state is in sync
      // with the filesystem:
      this.release()
    } else {
      try {
        await this.lockfile.touch_()
        this.logger.debug("#refreshLockfile_(): Touched lockfile", {
          lockfile: this.lockfile,
          selfAndSiblings: await this.#selfAndSiblingNames_()
        })
      } catch (err) {
        this.logger.warn("#refreshLockfile_(): failed, force-releasing.", err)
        this.release()
        throw err
      }
    }
  }

  async acquire_({
    releaseOnFailure = true,
    timeoutMs = this.opts.timeoutMs
  }: {
    releaseOnFailure?: boolean
    timeoutMs?: number
  } = {}): Promise<boolean> {
    if (this.#acquired) {
      return true
    }

    try {
      // Feeling lucky? (if this works, we can skip the fswatch!)
      if (await this.tryAcquire_()) return true

      await this.lockdir.watchUntil({
        f: () => this.tryAcquire_(),
        persistent: false,
        recursive: false,
        timeoutMs,
        intervalMs: Math.round(Math.min(this.staleMs, timeoutMs) / 5)
      })

      if (!this.acquired && releaseOnFailure) {
        this.release()
      }
      return this.acquired
    } catch (error) {
      if (releaseOnFailure) this.release()
      this.logger.warn("acquire_() failed", { error })
      throw error
    }
  }

  #rmLockDir() {
    this.logger.debug("#rmLockDir() attempting lockdir rmdir...", {
      // This will fail if there is other contents (we're not calling .rmrf!)
      success: this.lockdir.rmdirSync("trace")
    })
  }

  // sync to avoid concurrent state changes:
  release(): boolean {
    if (this.ended) {
      filterInPlace(
        FsLock.#instances,
        ea => ea.#instanceId !== this.#instanceId
      )
    }
    this.stopRefreshTimer()

    // we set our state first, before we change the filesystem to make sure
    // the lockfile refresher doesn't re-touch the lockfile:
    this.#acquired = false

    map(this.#rmLockdirLater, ea => clearTimeout(ea))

    try {
      // release is sync to avoid concurrent state changes:
      this.lockfile?.unlinkSync(LogLevels.trace)
      this.lockfile = undefined
      this.logger.debug("#release() success")
      return true
    } catch (err) {
      this.logger.warn("#release() failed to delete lockfile", err)
      return false
    } finally {
      if (this.opts.dirty === true) {
        // no need to do anything else.
      } else if (ending() || this.ended) {
        // Don't wait, just try now (which might fail, but who cares)
        void this.#rmLockDir()
      } else {
        // Try to clean up the lockdir but only after a bit, to make sure there's
        // no subsequent lock contention:
        this.logger.debug("#release() scheduling lockdir cleanup", {
          ms: this.staleMs * 2
        })
        // unref'ed timer--it doesn't need to keep the process alive:
        this.#rmLockdirLater = setTimeout(
          () => this.#rmLockDir(),
          this.staleMs * 2
        )
        this.#rmLockdirLater.unref()
      }
    }
  }

  /**
   * @return undefined if the lock couldn't be acquired, or the result of f()
   * @throws if f() throws
   */
  async withLock_<T>(f: (lock: FsLock) => T | Promise<T>): Promise<Maybe<T>> {
    if (this.opts.skipFsLock === true) {
      this.logger.debug("withLock(): skipLock is true, running f()")
      return f(this)
    }
    if (this.#acquired) {
      // nested locks (!)
      this.logger.debug("withLock() already acquired")
      return f(this)
    }
    try {
      const acquired = await (this.opts.noopIfContested === true
        ? this.tryAcquire_()
        : this.acquire_())
      this.logger.debug("withLock()", { acquired })
      if (acquired) {
        return await thenOrTimeoutError(f(this), this.opts.timeoutMs, false)
      } else {
        return undefined
      }
    } catch (err) {
      this.logger.warn("withLock_() rejected", err)
      throw err
    } finally {
      this.release()
    }
  }
}

/**
 * Two-phase lock. Try to make the .fslock dir, then write my id into the
 * lockdir. I only have the lock once I am the least-valued UID. Once I am
 * done with the lock, I remove my id file. If I am the last one in, I delete
 * the lockdir.
 *
 * Note that this lock is unique per call, per process--if the lock is already
 * acquired by a previous stackframe, this will deadlock.
 */
export async function withLock_<T>(
  opts: FsLockOptions,
  f: (lock?: FsLock) => T | Promise<T>
): PromiseMaybe<T> {
  if (opts.skipFsLock === true) return f()
  const l = new FsLock(opts)
  try {
    return await l.withLock_(f)
  } finally {
    void l.end()
  }
}
