// See https://moment.github.io/luxon/docs/manual/parsing.html#table-of-tokens
// and https://en.wikipedia.org/wiki/Date_format_by_country
export const DefaultDateTimeFormats = [
  "y-M-d 'at' H.m.s", // macOS screenshots
  "y-M-d H-m-s", // gnome screenshots
  "y-MMM-d H-m-s", // reasonable timestamp with short month name
  "y-MMMM-d H-m-s", // reasonable timestamp with full month name
  "y-MM-dd HHmmss", // windows screenshots
  "yMMdd_HHmmss", // smartphone camera filenames
  "yMMdd-HHmmss", // smartphone camera filenames
  // "MM.dd.yyyy HH:mm:ss", // old LG datestamps
  "F", // short localized date and time with seconds, may have slashes
  "FF" // less short localized date and time with seconds

  // We don't need to add y-m-d parsers here--those are handled automatically
  // by FuzzyDateParser.
]
