import { blank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { Obj, entries } from "../../fe/Object"
import { wrap } from "../../fe/String"
import { toA } from "../../fe/toA"
import { splitLines } from "../fs/CRLF"
import { WrapComments } from "./WrapComments"

export function stringifyToml(obj: Obj) {
  return splitLines(...entries(obj).map(([k, v]) => kvToToml(k, v)))
}

function kvToToml(k: string, v: any) {
  // We add 2 spaces here to make long array values wrap:
  return k + " = " + stringify(v, undefined, 2)
}

export function wrapTomlToLines(args: {
  lines: string[]
  wrap?: typeof WrapComments
  key: Maybe<string>
  value: any
  prepend?: string[]
}) {
  if (blank(args.key) || args.value == null) return args.lines
  const lines = [...toA(args.prepend), kvToToml(args.key, args.value)]
  if (args.lines.length > 0) lines.unshift("")
  args.lines.push(...wrap(lines, args.wrap))
  return args.lines
}
