import { compactBlanks } from "../../fe/Array"
import { flatten } from "../../fe/Flatten"
import { Maybe } from "../../fe/MaybeTypes"
import { newlineRe } from "../../fe/String"
import { toS } from "../../fe/toS"
import { isWin } from "../platform/Platform"

export const Newline = isWin ? "\r\n" : "\n"

export function crlf(...s: string[]): string {
  const result = s.map(toS).join("\n")
  return isWin ? result.replace(newlineRe(), "\r\n") : result
}

export function splitLines(...arr: Maybe<string | Buffer>[]): string[] {
  return flatten(
    arr.map(ea =>
      Array.isArray(ea) ? splitLines(...ea) : toS(ea).split(newlineRe())
    )
  )
}

export function joinLines(...lines: string[]): string {
  return splitLines(...lines).join(Newline)
}

export function splitCompactLines(...arr: Maybe<string | Buffer>[]): string[] {
  return compactBlanks(splitLines(...arr))
}
