import { ensurePrefix } from "../../fe/String"
import { camel2snake, snake2camel } from "../StringCase"
import { SettingsName } from "./SettingsName"

export function nameFromEnv(name: string): string {
  return snake2camel(name.replace(/^PS_/, "").toLowerCase())
}

export function envFromName(name: SettingsName | string): string {
  return ensurePrefix(camel2snake(name).toUpperCase(), "PS_")
}
