import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { BaseFile } from "../fs/BaseFile"

export const Schemas = strEnum("models", "stats")
export type Schema = StrEnumKeys<typeof Schemas>

export const SqliteExt = ".sqlite3"

export const SqliteBase = "db" + SqliteExt

export function pathToDbDir<T extends BaseFile>(dataDir: T, schema: Schema): T {
  return dataDir.join(schema)
}
export function pathToDb<T extends BaseFile>(dataDir: T, schema: Schema): T {
  return pathToDbDir(dataDir, schema).join(SqliteBase)
}
