import { StrEnum } from "../../fe/StrEnum"
import { Setting, SettingOpts } from "./Setting"

export class StringEnumSetting<T extends string> extends Setting<T> {
  readonly strEnum: StrEnum<T>
  constructor(opts: SettingOpts<T> & { strEnum: StrEnum<T> }) {
    super({
      toEnv: s => opts.strEnum.getCI(s),
      fromEnv: s => opts.strEnum.getCI(s),
      ...opts
    })
    this.strEnum = opts.strEnum
    // NOTE: do not compactBlanks! A blank may be a valid value!
    const dv = this.defaultValue
    // Allow defaultValue to be undefined:
    if (!this.strEnum.has(dv)) {
      throw new Error(
        `${this.strEnum.values}, doesn't include defaultValue, ${opts.defaultValue}`
      )
    }
  }
  addToJSON() {
    return { "valid values": this.strEnum.values }
  }
}
