import { notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { Setting, SettingOpts } from "./Setting"

export class StringSetting extends Setting<string> {
  constructor(opts: SettingOpts<string>) {
    super({
      toEnv: trim,
      fromEnv: trim,
      ...opts
    })
  }

  hasValue() {
    return notBlank(this.value)
  }
}
function trim(s: Maybe<string>) {
  return s == null ? undefined : s.trim()
}
