import { isEmpty, sum } from "./Array"
import { Maybe } from "./MaybeTypes"
import { gt0 } from "./Number"

function rndInt(min: number, max: number): number {
  // CAUTION: using Math.round() will give you a non-uniform distribution!
  // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/random
  return Math.floor(Math.random() * (max - min)) + min
}

/**
 * @param min (inclusive)
 * @param max (exclusive)
 * @return a random integer between min (included) and max (excluded),
 * excluding the values in `excluded`.
 */
export function randomInt(
  min: number,
  max: number,
  excluded?: number[]
): number {
  return randomInts(min, max, 1, excluded)[0]
}

/**
 * @param min (inclusive)
 * @param max (exclusive)
 * @return a set of `size` random integers between min (included) and max
 * (excluded), excluding the values in `excluded`.
 */
export function randomInts(
  min: number,
  max: number,
  size: number,
  excluded?: number[]
): number[] {
  min = Math.ceil(min)
  max = Math.floor(max)

  if (max < min) {
    throw new Error(`randomInts(): invalid range: ${min} > ${max}`)
  }
  const range = max - min
  const excludeSet = new Set(excluded ?? [])

  if (max === min && range === 0 && excludeSet.size === 0 && size === 1) {
    return [min]
  }

  const slotsRemaining = range - excludeSet.size

  if (size > slotsRemaining) {
    throw new Error(
      `randomInts(): cannot satisfy request: ${JSON.stringify({
        max,
        min,
        range,
        size,
        slotsRemaining
      })}`
    )
  }
  // const luckThreshold = 0.6 // found with minSearch()
  // if ((slotsRemaining - size) / range < luckThreshold) {

  // HEY FUTURE ME: turns out that either implementation is fine, even for
  // large ranges and large excluded sets, but the retained implementation
  // uses less memory allocation, so it wins.

  // const result = []
  // const eligible = []
  // for (let i = min; i < max; i++) {
  //   if (!excludeSet.has(i)) {
  //     eligible.push(i)
  //   }
  // }
  // for (let i = 0; i < size; i++) {
  //   const idx = rndInt(0, eligible.length)
  //   result.push(eligible.splice(idx, 1)[0])
  // }
  // return result
  // } else {
  const result = new Set<number>()
  while (result.size < size) {
    const candidate = rndInt(min, max)
    if (!excludeSet.has(candidate)) {
      result.add(candidate)
    }
  }
  return [...result]
  // }
}

export function randomBoolean(trueRatio = 0.5): boolean {
  return Math.random() <= trueRatio
}

/**
 * @return a random float between `min` and `max`
 */
export function randomFloat(min: number, max: number) {
  return Math.random() * (max - min) + min
}

export const NumericChars = "0123456789"
export const LowercaseChars = "abcdefghijkmnopqrstuvwxyz"
// No upper case to support case-insensitive filesystems. Exported for tests.
// We could have used GeoRadix.numerals, but that's a possible circular dep.
export const RandomChars = NumericChars + LowercaseChars

/**
 * @return a random string matching /[a-z0-9]{count}/
 */
export function randomChars(
  count: number,
  chars: string = RandomChars
): string {
  let result = ""
  for (let i = 0; i < count; i++) {
    result += randomChar(chars)
  }
  return result
}

export function randomChar(chars: string = RandomChars): string {
  return chars[randomInt(0, chars.length)]
}

export function pickRandom<T>(arr: T[]): Maybe<T> {
  return arr[randomInt(0, arr.length)]
}

/**
 * Fisher-yates shuffle on shallow clone of `arr`
 */
export function shuffle<T>(arr: T[]): T[] {
  const r = [...arr]
  for (let i = r.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1))
    if (i !== j) [r[i], r[j]] = [r[j], r[i]]
  }
  return r
}

export function sample<T>(arr: T[], size: number): T[] {
  const result: T[] = []
  for (const idx of randomInts(0, arr.length, size)) {
    result.push(arr[idx])
  }
  return result
}

export function pickWeightedRandom<T extends { priority?: number }>(
  arr: T[]
): Maybe<T> {
  if (isEmpty(arr)) return
  const nonZeroes = arr.filter(ea => gt0(ea.priority))
  const totalPriority = sum(nonZeroes, ea => ea.priority!)
  let rand = randomFloat(0, totalPriority)
  return nonZeroes.find(ea => {
    rand -= ea.priority!
    return rand <= 0
  })
}
