import { delay } from "./Delay"
import { Maybe } from "./MaybeTypes"
import { gt0 } from "./Number"
import { SyncOrAsync } from "./SyncOrAsync"
import { thenOrTimeoutError } from "./ThenOrTimeout"
import { toErr } from "./toErr"

export async function retryOnReject<T>(
  f: () => SyncOrAsync<T>,
  opts: {
    maxRetries: number
    timeoutMs?: number
    retryDelay?: number
    onRetryWaitUntil?: (retryCount: number) => any
    errorIsRetriable?: (error: Error) => Maybe<boolean>
  }
): Promise<T> {
  const g = gt0(opts.timeoutMs)
    ? () => thenOrTimeoutError(f(), opts.timeoutMs!)
    : f

  if (opts.maxRetries <= 0) {
    return g()
  }
  const onRetryWaitUntil =
    opts.onRetryWaitUntil ??
    ((retry: number) =>
      delay(Math.max(250, (opts.retryDelay ?? 250) * (retry ?? 1))))

  let retryCount = 0
  const h: () => Promise<T> = async () => {
    try {
      return await g()
    } catch (err) {
      const error = toErr(err)!
      if (
        false === opts.errorIsRetriable?.(error) ||
        retryCount > opts.maxRetries
      ) {
        throw error
      } else {
        retryCount++
        await onRetryWaitUntil(retryCount)
        return h()
      }
    }
  }
  return h()
}
