import { lazy } from "../../core/Lazy"
import { Maybe } from "../../fe/MaybeTypes"
import { levelIndex, LogLevel, LogLevels } from "./LogLevel"

export interface LogFilter {
  highlight(context: string): boolean

  /**
   * @return true iff the given level and context are not configured to be
   * squelched/omitted from downstream log writers.
   */
  enabled(level: LogLevel, context?: string): boolean
  /**
   * The level index that is enabled by default (ignoring context-specific overrides)
   */
  readonly defaultLevelIndex: number
  silent: boolean
}

export class SimpleLogFilter implements LogFilter {
  readonly defaultLevelIndex
  constructor(readonly minLogLevel: LogLevel) {
    this.defaultLevelIndex = levelIndex(minLogLevel)
  }
  highlight() {
    return false
  }
  enabled(level: LogLevel) {
    // fatal < error < info < debug < trace
    return levelIndex(level) <= this.defaultLevelIndex
  }
  silent = false
}

export const LogLevelRe = /^(?:(.+?):)?(fatal|error|warn|info|debug|trace)$/i

export interface LogLevelContext {
  prefix: string
  levelIndex: number
}

// Replaced with a LogFilterImpl at log setup time (to avoid a dependency loop)
export const logFilter = lazy<LogFilter>(
  () => new SimpleLogFilter(LogLevels.warn)
)

// set to watch Settings.logLevel by SetupLogger:
export const defaultLogLevel = lazy(
  () => LogLevels.values[logFilter().defaultLevelIndex]
)

export function isDefaultLogLevelAtLeast(logLevel: LogLevel) {
  return logFilter().defaultLevelIndex >= levelIndex(logLevel)
}

export function silently<T>(f: () => T): T {
  try {
    logFilter().silent = true
    return f()
  } finally {
    logFilter().silent = false
  }
}

export async function silentlyAsync<T>(f: () => PromiseLike<T>): Promise<T> {
  try {
    logFilter().silent = true
    return await f()
  } finally {
    logFilter().silent = false
  }
}

/**
 * Apply `f()` if the given logLevel is enabled.
 */
export function ifLog<T>(logLevel: LogLevel, f: () => T): Maybe<T> {
  return logFilter().enabled(logLevel) ? f() : undefined
}

export function isLogged(logLevel: LogLevel, context?: string) {
  return logFilter().enabled(logLevel, context)
}
