import { resolve } from "path"
import { lazy } from "../../core/Lazy"
import { notBlank } from "../../fe/Blank"
import { env } from "../env/Env"
import { isWin } from "../platform/Platform"
import { PowerShell } from "../pwsh/PowerShell"
import { homeDir } from "./HomeDir"

export async function picturesDirWindows() {
  return isWin
    ? (PowerShell.instance().executeJson(
        `Get-ItemPropertyValue "Registry::HKEY_CURRENT_USER\\SOFTWARE\\Microsoft\\Windows\\CurrentVersion\\Explorer\\User Shell Folders" -name "My Pictures"`
      ) as Promise<string>)
    : undefined
}

export const picturesDir = lazy<Promise<string>>(async () => {
  if (isWin) {
    const result = await picturesDirWindows()
    if (notBlank(result)) return result
    // else fall through and return default
  }
  return defaultPicturesDir()
})

export const defaultPicturesDir = lazy<string>(
  // See https://wiki.archlinux.org/title/XDG_user_directories
  () => env().XDG_PICTURES_DIR ?? resolve(homeDir(), "Pictures")
)
