import { parse } from "plist"
import { lazy } from "../../core/Lazy"
import { uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { mapOr } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { thenMap } from "../../fe/Promise"
import { toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { mapAsyncSerial } from "../async/Promise"
import { thenOrTimeout } from "../async/ThenOrTimeout"
import { stdout_ } from "../child/ChildProcess"
import { dfPosixRaw } from "./DfPosixRaw"
import { lazyFsAsync } from "./LazyFsAsync"
import { DfVolume, Volume } from "./Volume"
import { commandTimeoutMs } from "./VolumeTtls"

const logger = lazy(() => mkLogger("volumes.LocalVolumesMac"))

export async function addLocalVolumeInfoMac(
  vols: DfVolume[]
): PromiseMaybe<Volume[]> {
  return thenMap(mnt2uuidMac(), uuids =>
    vols.map(vol =>
      mapOr(
        uuids.get(vol.mountpoint),
        info => ({ ...vol, ...info }),
        () => vol
      )
    )
  )
}

export const mnt2uuidMac = lazyFsAsync("mnt2uuidMac", _mnt2uuidMac, 0)

const diskRE = /^(\/dev\/disk\d+)/

async function _mnt2uuidMac() {
  const l = await dfPosixRaw({ localsOnly: true })
  const diskDevices = uniq(l.map(ea => ea.filesystem?.match(diskRE)?.[1]))
  const timeoutMs = Math.round(commandTimeoutMs() / 3)

  const stdouts = await mapAsyncSerial({
    name: "_mnt2uuidMac()",
    arr: diskDevices,
    f: async dev =>
      thenOrTimeout(
        stdout_("diskutil", ["list", "-plist", dev], {
          timeoutMs
        }),
        timeoutMs,
        () => logger().warn("Timeout: failed to get disk metadata for " + dev)
      )
  })

  const result = new Map<string, Partial<Volume>>()

  for (const out of stdouts) {
    try {
      const p = parse(out) as any
      for (const vols of toA(p.AllDisksAndPartitions)) {
        for (const vol of toA(vols.APFSVolumes)) {
          if (!blank(vol.MountPoint) && !isTrue(vol.OSInternal)) {
            result.set(vol.MountPoint, {
              filesystem: "/dev/" + vol.DeviceIdentifier,
              label: vol.VolumeName,
              size: vol.Size,
              uuid: vol.VolumeUUID
            })
          } else {
            logger().debug("skipping", vol)
          }
        }
      }
    } catch (err) {
      logger().warn("Failed to parse diskutil output", { err, out })
    }
  }
  return result
}
