import { Stats } from "fs"
import _fse from "fs-extra"
import { lazy } from "../../core/Lazy"
import { uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { isDate, unixtime } from "../../fe/Date"
import { Maybe, MaybePromiseMaybe } from "../../fe/MaybeTypes"
import { diff, isNumber } from "../../fe/Number"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { mkLogger } from "../Logger"
import { onTimeout } from "../async/TimeoutHandler"
import { isRecentMs } from "../date/Date"
import { LogLevel, LogLevels } from "../log/LogLevel"
import { access, isStatRWX } from "./Access"
import { isEmptyStats } from "./StatSync"
import { statTimeoutMs } from "./StatTimeout"

const logger = lazy(() => mkLogger("fs.Stat"))

export async function stat_(nativePath: Maybe<string>): Promise<Stats> {
  if (blank(nativePath)) {
    return logger().throw("stat_() for blank path", {
      nativePath,
      retriable: false,
      fatal: false
    })
  }
  const start = Date.now()
  try {
    return await thenOrTimeoutError(
      _fse.stat(nativePath), // < no throwIfNoEntry option for async stat()
      statTimeoutMs(),
      true
    )
  } finally {
    const elapsedMs = Date.now() - start
    if (elapsedMs > statTimeoutMs() / 2) {
      logger().warn("soft timeout for " + nativePath, { elapsedMs })
      onTimeout({ soft: true })
    }
  }
}

export function statMaybe(
  nativePath: Maybe<string>,
  logLevel: LogLevel = LogLevels.info
): MaybePromiseMaybe<Stats> {
  return blank(nativePath)
    ? undefined
    : stat_(nativePath).catch(error => {
        logger().log(logLevel, "stat() failed", { error, nativePath })
        return undefined
      })
}

export function statToType(a: Stats) {
  return a.isDirectory() ? "directory" : a.isFile() ? "file" : "special"
}

function statFieldToComparable(ea: Maybe<number | Date>): Maybe<number> {
  return isDate(ea) ? unixtime(ea) : isNumber(ea) ? ea : undefined
}

/**
 * Given `Stats` objects from the same path, is it likely that the file has
 * changed?
 */
export function whyStatNotEql(a: Maybe<Stats>, b: Maybe<Stats>) {
  if (a == null || b == null) {
    return (
      "both must be defined: " +
      (a == null ? "null" : "stat") +
      "  " +
      (b == null ? "null" : "stat")
    )
  }
  const aType = statToType(a)
  const bType = statToType(b)
  if (aType !== bType) {
    return "inconsistent file type: " + aType + "  " + bType
  }
  // no atime, and birthtime isn't settable on linux--we're assuming we're
  // comparing the same path.
  for (const field of ["size", "mtime"] as const) {
    const aVal = a[field]
    const bVal = b[field]
    const delta = diff(statFieldToComparable(aVal), statFieldToComparable(bVal))
    // mtime and birthtime may be fractional milliseconds off.
    if (delta == null || delta > 1) {
      return "unmatched " + field + ": " + aVal + "  " + bVal
    }
  }
  return
}

export function statEql(a: Maybe<Stats>, b: Maybe<Stats>) {
  return whyStatNotEql(a, b) == null
}

export async function exists(
  nativePath: string,
  logLevel: LogLevel = LogLevels.info
) {
  return null != (await statMaybe(nativePath, logLevel))
}

/**
 * @return if the file is missing or empty
 */
export async function isEmptyFile(
  nativePath: string,
  opts?: { emptyIsNew?: boolean; logLevel?: LogLevel }
) {
  const s = await statMaybe(nativePath, opts?.logLevel ?? LogLevels.trace)
  return s == null || (opts?.emptyIsNew ?? true) ? isEmptyStats(s) : false
}

export async function isNonEmptyFile(nativePath: string, minFileSize = 0) {
  const s = await statMaybe(nativePath)
  return s != null && s.isFile() && s.size >= minFileSize
}

export async function nativePathIsFile(
  nativePath: Maybe<string>,
  logLevel = LogLevels.info
): Promise<boolean> {
  return true === (await statMaybe(nativePath, logLevel))?.isFile()
}

export async function isDirectory(nativePath: Maybe<string>): Promise<boolean> {
  return true === (await statMaybe(nativePath))?.isDirectory()
}

export async function isReadableDirectory(
  nativePath: Maybe<string>,
  logLevel: LogLevel = LogLevels.trace
): Promise<boolean> {
  const stat = await statMaybe(nativePath, logLevel)
  return (
    stat != null && stat.isDirectory() && access({ stat, r: true, x: true })
  )
}

export async function isReadWriteableDirectory(
  nativePath: Maybe<string>,
  logLevel: LogLevel = LogLevels.trace
): Promise<boolean> {
  const stat = await statMaybe(nativePath, logLevel)
  return stat != null && stat.isDirectory() && isStatRWX(stat)
}

export async function isMtimeRecent(
  nativePath: Maybe<string>,
  staleMs: number
): Promise<boolean> {
  return isRecentMs((await statMaybe(nativePath))?.mtimeMs, staleMs)
}

/**
 * @return the uniq stat times, including birthtime and mtime (not atime or
 * ctime!)
 */
export function statTimes(s: Stats): number[] {
  return uniq(
    // birthtimeMs will be 0 on OSes that don't support it. remember that
    // ctime is last-status-change-time, (directory-change-time), _not_
    // create-time. We don't want ctime!
    [s.birthtimeMs, s.mtimeMs].filter(ea => ea != null && ea !== 0)
  )
}
