import fs from "fs"
import { stat } from "fs/promises"
import { pick } from "../../fe/Object"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"

export type FileStats = Pick<fs.Stats, "size" | "mtimeMs">

export async function fileStats_(f: SimpleFileOrString): Promise<FileStats> {
  return pick((await stat(toNativePath_(f)))!, "size", "mtimeMs")
}
