import os from "os"
import { lazy } from "../../core/Lazy"
import { gt0 } from "../../fe/Number"
import { intFromFileSync } from "../fs/ReadFile"
import { isDocker } from "../platform/isDocker"

// cpu() can take 500ms!
export const cpuInfo = lazy(() => os.cpus())

export const cpuCount = lazy(() => {
  if (isDocker()) {
    // Are we in a pod?
    // See https://www.reddit.com/r/PhotoStructure/comments/sn68f9/initial_scan_not_adding_everything/hw4bqmj/
    const quota = intFromFileSync("/sys/fs/cgroup/cpu/cpu.cfs_quota_us")
    const period =
      quota != null
        ? intFromFileSync("/sys/fs/cgroup/cpu/cpu.cfs_period_us")
        : undefined
    if (gt0(quota) && gt0(period)) {
      return quota / period
    }

    const shares = intFromFileSync("/sys/fs/cgroup/cpu/cpu.shares")
    if (gt0(shares)) {
      return (cpuInfo().length * shares) / 1024
    }
  }
  return cpuInfo().length
})
