import { createReadStream, ReadStream } from "fs"
import { createBrotliDecompress, createGunzip } from "zlib"
import { Maybe } from "../../fe/MaybeTypes"
import { stripCopySuffixFromName } from "../fs/FuzzyNames"
import { isExtOrCompressedExt } from "../fs/Gzip"
import { LineReader } from "../fs/LineReader"
import { SimpleFile } from "../fs/SimpleFile"
import { SortedSet } from "../SortedSet"
import { LogEntry, logEntrySorter } from "./LogEntry"
import { chunkToLogEntry } from "./ReadLogEntries"

// Lines were out of order on speedy with 128, and seemed OK at 512.
const LowWatermark = 1024
const HighWatermark = LowWatermark * 10

export class LogReader {
  readonly start = Date.now()
  readonly lines = new SortedSet<LogEntry>(logEntrySorter)
  private readonly fileStream: ReadStream
  private readonly stream: LineReader
  private readonly from: string
  #ended = false
  #paused = false
  #hasErrors = false

  constructor(
    readonly f: SimpleFile,
    errorHandler: (error: Error) => any
  ) {
    this.from = stripCopySuffixFromName(f.name)
    this.fileStream = createReadStream(f.nativePath, { autoClose: true })
    if (!isExtOrCompressedExt(f.ext, ".log")) {
      throw new Error(f + " is not a logfile")
    }
    this.stream = (
      f.ext.toLowerCase().endsWith(".gz")
        ? this.fileStream.pipe(createGunzip())
        : f.ext.toLowerCase().endsWith(".br")
        ? this.fileStream.pipe(createBrotliDecompress())
        : this.fileStream
    )
      .pipe(new LineReader())
      .on("error", err => {
        errorHandler(err)
        this.#hasErrors = true
      })
    this.stream.on("data", this.#onData.bind(this))
    this.stream.on("close", () => {
      this.fileStream.resume()
      this.#ended = true
    })
    this.stream.on("end", () => {
      this.#ended = true
    })
  }

  #onData(chunk: string) {
    if (chunk === null) {
      this.#ended = true
    } else {
      const le = chunkToLogEntry(chunk)
      if (le != null) {
        this.lines.add({ ...le, from: this.from } as LogEntry)
      }
      if (this.lines.length > HighWatermark && !this.#paused) {
        this.fileStream.pause()
        this.#paused = true
      }
    }
  }

  toString() {
    return "LogReader(" + this.f.nativePath + ")"
  }

  status() {
    return {
      hasErrors: this.#hasErrors,
      ended: this.#ended,
      destroyed: this.stream.destroyed,
      lines: this.lines.length,
      paused: this.#paused
    }
  }

  hasErrors() {
    return this.#hasErrors
  }

  get ended() {
    return this.#ended
  }

  ready() {
    return this.ended || this.lines.length > 32
  }

  complete() {
    return this.ended && this.lines.length === 0
  }

  peek(): Maybe<LogEntry> {
    return this.ready() ? this.lines.store[0] : undefined
  }

  shift() {
    const result = this.lines.store.shift()
    if (result != null) this.#onShift()
    return result
  }

  shiftLte(ts: number) {
    const result = this.lines.shiftLte(ts)
    if (result.length > 0) this.#onShift()
    return result
  }

  #onShift() {
    if (this.lines.length < LowWatermark && this.#paused) {
      this.fileStream.resume()
      this.#paused = false
    }
  }
}
