import { lazy } from "../../core/Lazy"
import { Args } from "../Args"
import { EventName, PhotoStructureEvents } from "./PhotoStructureEvents"
import { SharedState } from "./SharedState"

export function broadcastEvent_<E extends EventName>(
  name: E,
  ...args: Args<PhotoStructureEvents[E]>
) {
  return SharedState.instance()?.broadcastEvent_({ name, args })
}

export function broadcastPause() {
  return broadcastEvent_("pause")
}

export function broadcastResume() {
  return broadcastEvent_("resume")
}

export const broadcastExit = lazy(() => broadcastEvent_("exit"))
