import { Maybe } from "../../fe/MaybeTypes"
import { decodeDuration, encodeDuration } from "../date/DurationCodec"
import { fmtFullDuration } from "../date/DurationFormat"
import { BaseSettingOpts, Setting, SettingOpts } from "./Setting"

export class DurationSetting extends Setting<number> {
  constructor(opts: SettingOpts<string | number>) {
    super({
      ...(opts as any),
      toEnv: encodeDuration,
      fromEnv: decodeDuration
    })
  }

  get valueOrDefault() {
    // code is expecting this to be in millis.
    return decodeDuration(super.valueOrDefault) as any
  }

  get value() {
    // code is expecting this to be in millis.
    return decodeDuration(super.value) as any
  }

  set value(o: any) {
    super.userValue = decodeDuration(o)
  }

  get humanValue() {
    return fmtFullDuration(this.value)
  }

  // the file value is the only one 8601-encoded:
  get fileValue() {
    return encodeDuration(super.fileValue) as any
  }

  set fileValue(o: any) {
    super.fileValue = decodeDuration(o)
  }
}

export class OptionalDurationSetting extends Setting<Maybe<number>> {
  constructor(opts: BaseSettingOpts<Maybe<string | number>>) {
    super({
      ...(opts as any),
      toEnv: encodeDuration,
      fromEnv: decodeDuration,
      defaultValue: undefined
    })
  }

  get valueOrDefault() {
    return this.value ?? decodeDuration(this.defaultValue as any)!
  }

  get fileValue() {
    return encodeDuration(super.fileValue) as any
  }
}
