import { compact, isEmpty } from "./Array"
import { dayMs, hourMs, minuteMs, secondMs, weekMs, yearMs } from "./Date"
import { mapOr } from "./Maybe"
import { gte0, isNumber } from "./Number"
import { plur } from "./Units"

const Units = [
  { ms: yearMs, s: "year", p: "years" },
  { ms: yearMs / 12, s: "month", p: "months" }, // oof, but this is fuzzy
  { ms: weekMs, s: "week", p: "weeks" },
  { ms: dayMs, s: "day", p: "days" },
  { ms: hourMs, s: "hour", p: "hours" },
  { ms: minuteMs, s: "minute", p: "minutes" },
  { ms: secondMs, s: "second", p: "seconds" }
]

/**
 * CAUTION: this is for human consumption, not robots: we render rough
 * "months"!
 */
export function fmtDuration(
  ms: number,
  units: number = 2,
  suffix?: { singular: string; plural: string }
): string {
  if (!gte0(ms)) {
    return !isNumber(ms) ? "" : "-" + fmtDuration(Math.abs(ms), units)
  }
  const largestUnitIndex = Units.findIndex(ea => ea.ms <= ms)
  if (largestUnitIndex === -1) return ""
  let remaining =
    ms +
    // hack for rounding:
    mapOr(
      Units[largestUnitIndex + Math.max(2, units) - 1],
      ea => Math.round(ea.ms * 0.4),
      0
    )
  const result = compact(
    Units.slice(largestUnitIndex, largestUnitIndex + units).map(unit => {
      if (unit.ms > remaining) {
        return
      } else {
        const i = Math.floor(remaining / unit.ms)
        remaining -= i * unit.ms
        return { i, s: plur(i, unit.s, unit.p) }
      }
    })
  )
  if (isEmpty(result)) return ""
  return (
    result.map(ea => ea.s).join(", ") +
    mapOr(
      suffix,
      ea => " " + (result[result.length - 1].i !== 1 ? ea.singular : ea.plural),
      ""
    )
  )
}
