import process from "process"
import { lazy } from "../core/Lazy"
import { isEmpty, isNotEmpty, uniq } from "../fe/Array"
import { sortBy } from "../fe/ArraySort"
import { blank, notBlank } from "../fe/Blank"
import { secondMs } from "../fe/Date"
import { orElse } from "../fe/Maybe"
import { PromiseMaybe } from "../fe/MaybeTypes"
import { gt0, toInt } from "../fe/Number"
import { onlyReqValued } from "../fe/Object"
import { toA } from "../fe/toA"
import { toS } from "../fe/toS"
import { collectBatchedAsync } from "./Array"
import { parseFixed } from "./Fixed"
import { mkLogger } from "./Logger"
import { existingPids } from "./Pid"
import { StartTs } from "./StartTs"
import { pwshJsonDate, wmiDate } from "./WinDate"
import { thenMap } from "./async/Promise"
import { StdoutOpts, stdoutResult_, stdout_ } from "./child/ChildProcess"
import { wmic } from "./fs/PathTo"
import { isWin } from "./platform/Platform"
import { PowerShell } from "./pwsh/PowerShell"

export interface ProcEntry {
  pid: number
  start: Date
  cmd: string
}

// TODO: move to ./proc/Ps.ts
const logger = lazy(() => mkLogger("proc.ps"))

export function isProcEntry(pe: any): pe is ProcEntry {
  return pe != null && gt0(pe.pid) && pe.start != null && notBlank(pe.cmd)
}

export async function ps(): Promise<ProcEntry[]> {
  const procs = await (isWin ? psWin() : psPosix())
  return orElse(
    sortBy(procs.filter(isProcEntry), ea => ea.pid),
    []
  )
}

export async function pidInfo(pid: number): PromiseMaybe<ProcEntry> {
  return thenMap(pidInfos([pid]), arr => toA(arr).find(ea => ea.pid === pid))
}

export async function notExistingPids(pids: number[]): PromiseMaybe<number[]> {
  if (isEmpty(pids)) return []
  return thenMap(existingPids(pids), arr => {
    const alive = [process.pid, ...arr]
    return pids.filter(ea => !alive.includes(ea))
  })
}

export async function pidInfos(pids: number[]): PromiseMaybe<ProcEntry[]> {
  const arr = existingPids(pids)

  const arr2 = await collectBatchedAsync<number, ProcEntry>(
    arr,
    20,
    isWin ? pidInfoWin : pidInfoPosix
  )
  const result = arr2.filter(ea => isProcEntry(ea) && arr.includes(ea.pid))
  return logger().tap({ msg: "pidInfos()", result, meta: { pids } })
}

function win2pe(arr: any[]): ProcEntry[] {
  return arr.map((entry: any) => ({
    pid: entry.Id,
    start: pwshJsonDate(entry.StartTime),
    cmd: entry.ProcessName
  })) as ProcEntry[]
}

const PsWinCmd = "Get-Process"
const PsWinSelectObj = "| Select-Object -Property Id,ProcessName,StartTime"

async function psWin(): Promise<ProcEntry[]> {
  if (PowerShell.instance().ended) return psWinWmic()
  const result = await PowerShell.instance().executeJsonToA(
    [PsWinCmd, PsWinSelectObj].join(" ")
  )
  return result == null ? psWinWmic() : win2pe(result)
}

function pidToS(pids: number[]): string {
  return uniq([...pids.filter(gt0), process.pid]).join(",")
}

async function pidInfoWin(pids: number[]): PromiseMaybe<ProcEntry[]> {
  if (PowerShell.instance().ended) return psWinWmic(pids)
  const cmd = [
    PsWinCmd,
    "-Id",
    // PowerShell wants the IDs joined by comma:
    pidToS(pids),
    "-ErrorAction SilentlyContinue", // I expect it not to find some pids
    PsWinSelectObj
  ].join(" ")
  return thenMap(PowerShell.instance().executeJsonToA(cmd), ea => win2pe(ea))
}

const stdoutOpts: StdoutOpts = {
  maxBuffer: 1024 * 1024, // windows process lists can be enormous
  timeoutMs: 15 * secondMs, // ps sometimes takes a long time
  ignoreExitCode: true,
  ignoreStderr: true
}

const headers: ("CommandLine" | "CreationDate" | "ProcessId")[] = [
  "CommandLine",
  "CreationDate",
  "ProcessId"
]

// NOTE: only used if powershell is shut down
export async function psWinWmic(pids?: number[]): Promise<ProcEntry[]> {
  const args = ["process"]
  if (isNotEmpty(pids)) {
    // wmic wants multiple PIDs as or clauses, like
    // wmic process where "ProcessId=11308 or ProcessId=9416"
    const processIdClause = uniq([...pids.filter(gt0), process.pid])
      .map(ea => `ProcessId=${ea}`)
      .join(" or ")
    args.push("where", processIdClause)
  }
  args.push("get", headers.join(","))
  const result = await stdoutResult_(wmic(), args, stdoutOpts)
  const results = onlyReqValued(
    parseFixed(headers, result.result).map((ea: any) => ({
      pid: toInt(ea.ProcessId, { defaultValue: -1 })!,
      start: wmiDate(ea.CreationDate),
      cmd: toS(ea.CommandLine)
    }))
  )
  if (!results.find(ea => ea.pid === process.pid)) {
    results.push({
      pid: process.pid,
      start: new Date(StartTs),
      cmd: "node " + process.title
    })
  }
  return results
}

function psStdout2ProcEntry(result: string): ProcEntry[] {
  if (blank(result)) return []
  return parseFixed(
    ["PID", { text: "STARTED", greedyLeft: true }, "COMMAND"],
    result
  ).map((ea: any) => ({
    pid: toInt(ea.PID, { defaultValue: -1 })!,
    start: new Date(ea.STARTED),
    cmd: toS(ea.COMMAND)
  }))
}

async function psPosix(): Promise<ProcEntry[]> {
  return psStdout2ProcEntry(
    await stdout_("ps", ["-ewwwo", "pid,lstart,command"], stdoutOpts)
  )
}

/**
 * @param pid the pid to look for
 * @return an array of either just this process, or the process and the requested pid.
 */
async function pidInfoPosix(pids: number[]): Promise<ProcEntry[]> {
  const r = await stdoutResult_(
    "ps",
    // we include the current pid to prevent either `ps` or the fixed parser
    // from grumping at us:
    ["-p", pidToS(pids), "-wwwo", "pid,lstart,command"],
    {
      ...stdoutOpts,
      ignoreExitCode: true
    }
  )
  return psStdout2ProcEntry(r.result)
}
