import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { clamp, gt0, sigFigs, toGt0 } from "../../fe/Number"
import { MB } from "../../fe/Units"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { ee } from "../event/EventEmitter"
import { LogLevels } from "../log/LogLevel"
import { lerp2d } from "../math/Lerp"
import { Settings } from "../settings/Settings"
import { cpuCount } from "./CpuInfo"
import { estimatedFreeMem } from "./FreeMem"
import { timeoutRate } from "./WorkTimeout"

const logger = lazy(() => mkLogger("work.MaxCpus"))

later(() => {
  if (isTest) {
    ee().on("clearCache", () =>
      // we actually set() some of these in some specs:
      clearMaxWorkCaches()
    )
  }
  ee().on("settingsChanged", clearMaxWorkCaches)
  Settings.cpuLoadPercent.watchLater(clearMaxWorkCaches)
  Settings.maxConcurrentImports.watchLater(clearMaxWorkCaches)
})

export function clearMaxWorkCaches() {
  estimatedFreeMem.unset()
  maxCpus.unset()
  maxConcurrentImports.unset()
  timeoutRate.prior()?.clear()
  sharpThreadsPerProcess.unset()
  cpuCount.unset()
}

const maxProcsForMem = lazy(() => {
  // they may bloat, but they'll recycle if they get too big.
  const worstCaseMemPerProc = Settings.maxMemoryMb.valueOrDefault * MB
  // We don't want to over-subscribe system memory, but we need to run at
  // least one:
  return logger().tap({
    msg: "maxProcsForMem",
    result: Math.max(1, Math.floor(estimatedFreeMem() / worstCaseMemPerProc)),
    meta: {
      estFreeMemMb: Math.round(estimatedFreeMem() / MB),
      worstCaseMemPerProcMb: Math.round(worstCaseMemPerProc / MB)
    }
  })
}, minuteMs)

export function timeoutPenalization() {
  const timeoutsPerMinute = Math.round(
    timeoutRate.prior()?.eventsPerMinute ?? 0
  )
  const timeoutThrottleCoeff = Settings.timeoutThrottleCoeff.valueOrDefault

  const result =
    timeoutsPerMinute === 0 || timeoutThrottleCoeff === 0
      ? 1 // < timeouts are disabled
      : sigFigs(timeoutThrottleCoeff / timeoutsPerMinute, 2)

  return logger().tap({
    msg: "timeoutPenalization()",
    level: result > 1 ? LogLevels.warn : LogLevels.debug,
    result,
    meta: {
      timeoutsPerMinute,
      timeoutThrottleCoeff
    }
  })
}

export function targetCpuLoadPct() {
  return (toGt0(Settings.cpuLoadPercent.valueOrDefault) ?? 1) / 100
}

export function singleThreadMode() {
  return (
    Math.round(targetCpuLoadPct() * cpuCount()) <= 1 ||
    Settings.cpuLoadPercent.valueOrDefault <= 1 ||
    maxCpus() === 1
  )
}

// We don't need a timeout TTL here: if we get a timeout, that will get set. See
// WorkTimeout.ts
export const maxCpus = lazy(_maxCpus)

function _maxCpus() {
  const cpuPortion = Math.max(1, Math.floor(targetCpuLoadPct() * cpuCount()))

  // maxCpus must be at least 1, and not more than min(cpuPortion, maxProcsForMem)
  let result = cpuPortion

  const timeoutsPerSecond = timeoutRate.prior()?.eventsPerMinute ?? 0

  // Make the lazy estimation that most work takes about a second:
  const penalization = timeoutPenalization()

  if (penalization !== 1) {
    // we don't need to go below this setting
    const resultBeforePenalization = result
    result = result * penalization
    logger().warn("Recent timeouts! Throttling down.", {
      penalization,
      resultBeforePenalization,
      resultAfterPenalization: result
    })
  }

  // We don't want to over-subscribe system memory, but we need to run at
  // least one:
  result = clamp(1, Math.min(cpuPortion, maxProcsForMem()), Math.ceil(result))

  return logger().tap({
    msg: "maxCpus()",
    level: LogLevels.info,
    result,
    meta: {
      cpuLoadPct: targetCpuLoadPct(),
      cpuCount: cpuCount(),
      cpuPortion,
      penalization,
      timeoutsPerSecond,
      maxProcsForMem: maxProcsForMem()
    }
  })
}

export const maxConcurrentImports = lazy(_maxConcurrentImports)

function _maxConcurrentImports() {
  if (gt0(Settings.maxConcurrentImports.valueOrDefault)) {
    return clamp(1, maxCpus(), Settings.maxConcurrentImports.valueOrDefault)
  }
  return maxCpus()
}

export const sharpThreadsPerProcess = lazy(() => {
  const i = Settings.sharpThreadsPerProcess.valueOrDefault
  return i > 0
    ? i
    : clamp(
        1,
        Math.min(maxCpus(), Settings.sharpThreadsPerProcess.max),
        // bigger second x scales down sharp threads
        Math.floor(lerp2d(maxCpus(), { x: 1, y: 1 }, { x: 6, y: 2 }))
      )
})
