import { delay } from "../fe/Delay"
import { Latch } from "../fe/Latch"
import { Maybe, PromiseMaybe } from "../fe/MaybeTypes"
import { gt0, max_ } from "../fe/Number"
import { SyncOrAsync } from "../fe/SyncOrAsync"
import { Deferred } from "./async/Deferred"
import { setUnrefTimeout } from "./async/Timers"
import { WrappedError } from "./error/WrappedError"

export interface RateLimited<T> {
  (...args: any[]): PromiseMaybe<T>
  // unschedule any pending future calls
  clear(): void
  isRateLimited(): boolean
  minCallDelayMs(): number
  setMinCallDelayMs(millis: number): void
  donePromise(): Promise<void>
  force(...args: any[]): PromiseMaybe<T>
  status(): {
    running: boolean
    needToDelay: boolean
    delayNextRunUntil: number
  }
}

/**
 * Ensures no concurrent invocations, and a delay between method calls to be at
 * least `minCallDelayMs`.
 *
 * As opposed to debounce, this implementation will only postpone a
 * call to f() at most every timeoutMs, rather than postponing forever.
 */
export function rateLimited<T>({
  f,
  minCallDelayMs,
  timeoutMs = undefined,
  nullOnBusy,
  name
}: {
  f: (...args: any[]) => SyncOrAsync<T>
  minCallDelayMs: number
  timeoutMs?: number
  nullOnBusy?: boolean
  name: string
}): RateLimited<T> {
  let running = false
  let delayNextRunUntil: number = 0
  let priorResult: Maybe<Deferred<T>>
  let nextArgs: Maybe<any[]>
  minCallDelayMs = Math.ceil(minCallDelayMs)

  const doneLatches: Latch[] = []

  function isRateLimited() {
    return running || delayNextRunUntil > Date.now()
  }

  function afterRunDelay() {
    if (nextArgs != null) {
      const arr = nextArgs
      nextArgs = undefined
      void r(...arr)
    }
  }

  async function postRun() {
    running = false
    delayNextRunUntil = Date.now() + minCallDelayMs

    setUnrefTimeout(afterRunDelay, minCallDelayMs + 1)

    // only release latches _after_ we've marked ourselves as not running:
    doneLatches.forEach(ea => ea.resolve())
    doneLatches.length = 0
  }

  async function run(...args: any[]) {
    running = true
    delayNextRunUntil = Date.now() + max_(minCallDelayMs, timeoutMs)

    // We get to run!
    const d = (priorResult = new Deferred<T>(name))
    if (gt0(timeoutMs)) void d.setTimeout(timeoutMs)
    try {
      void d.resolve(await f(...args))
    } catch (cause) {
      void d.reject(
        new WrappedError("RateLimited(" + name + ") failed", { cause })
      )
    } finally {
      void postRun()
    }
    return d.promise
  }

  const r: any = (...args: any[]) => {
    if (isRateLimited()) {
      if (nullOnBusy === true) {
        return null
      }
      const result = priorResult?.promise
      nextArgs = args
      // the delay breaks hard loops:
      return delay(1).then(() => result)
    } else {
      return run(...args)
    }
  }
  r.clear = () => {
    nextArgs = undefined
  }

  r.donePromise = () => {
    if (!running) return Promise.resolve()
    const l = new Latch()
    doneLatches.push(l)
    return l
  }

  r.force = async (...args: any[]) => {
    // unschedule any other runs
    nextArgs = undefined
    // wait for the prior run:
    if (running) await r.donePromise()
    running = false
    delayNextRunUntil = 0
    return r(...args)
  }

  r.isRateLimited = () => isRateLimited()
  r.status = () => {
    return {
      running,
      needToDelay: delayNextRunUntil > Date.now(),
      minNextDelayMs: Math.max(0, Date.now() - delayNextRunUntil)
    }
  }
  r.minCallDelayMs = () => minCallDelayMs
  r.setMinCallDelayMs = (ms: number) => {
    minCallDelayMs = ms
  }
  return r
}
