import { isNotEmpty } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { keys } from "../../fe/Object"
import { EndableRanks } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { ee } from "../event/EventEmitter"
import { FifoCacheAsync, FifoCacheAsyncOpts } from "../FifoCacheAsync"
import { mkLogger } from "../Logger"
import { mapEntries } from "../Object"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"

// DEPLOOP DANGER: DON'T REFERENCE BASEFILE!

export const InstanceCacheMaxSize = 256

export interface CacheableFile {
  clear(args?: { emit?: boolean }): this
}

export function isCacheableFile(o: any): o is CacheableFile {
  return typeof o === "object" && typeof o.clear === "function"
}

export class FileCache<T> extends FifoCacheAsync<T> {
  constructor(
    opts: Partial<FifoCacheAsyncOpts> & Pick<FifoCacheAsyncOpts, "name">
  ) {
    super({
      maxSize: InstanceCacheMaxSize,
      timeoutMs: ShortCommandTimeoutMs,
      ...opts
    })

    // break circular deps:
    later(() => {
      ee().on("fileChanged", path => this.#clearFromPath(path))
      ee().on("clearCache", () => this.clear())

      new EndableWrapper(
        opts.name + " stats",
        () => {
          const s = mapEntries(this.stats(), (_, v) => (v > 0 ? v : undefined))
          if (isNotEmpty(keys(s))) {
            mkLogger(opts.name).info("stats", s)
          }
        },
        EndableRanks.stats
      )
    })
  }

  async #clearFromPath(fromPath?: string) {
    if (blank(fromPath)) {
      // full wipe: the singleton expectation can't/shouldn't be expected.
      await this.clear()
    } else {
      for (const k of this.cache.keys()) {
        if (k.startsWith(fromPath)) {
          const v = await this.cache.get(k) // values may be promises
          if (isCacheableFile(v)) {
            // NOTE: BaseFile and PosixFile instances are expected to be
            // singletons for a given path, so if we remove the instance from
            // here, they won't be singletons anymore: we just want to remove
            // any cached state they currently have.
            v.clear({ emit: false })
          } else {
            this.cache.delete(k)
          }
        }
      }
    }
  }
}
