import { JsonMap, parse } from "@iarna/toml"
import { readFile, readFileSync, writeFile } from "fs-extra"
import { blank, mapNotBlank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { Obj } from "../../fe/Object"
import { debom } from "../Buffer"
import { mkLogger } from "../Logger"
import { dumbquote } from "../String"
import { joinLines } from "../fs/CRLF"
import { SimpleFileOrString, toNativePath_ } from "../fs/SimpleFile"
import { isFileSync } from "../fs/StatSync"
import { stringifyToml } from "./TomlStringify"

function parseToml(s: Maybe<string>) {
  return blank(s) ? undefined : parse(dumbquote(s))
}

function parseTomlBuffer(buf: Buffer) {
  return parseToml(debom(buf))
}

export function readTomlFileSync(
  file: Maybe<SimpleFileOrString>
): Maybe<JsonMap> {
  const nativePath = mapNotBlank(file, toNativePath_)
  if (nativePath == null) return
  try {
    return isFileSync(nativePath)
      ? parseTomlBuffer(readFileSync(nativePath))
      : undefined
  } catch (error) {
    mkLogger("settings.Toml").warn("readTomlFileSync() failed", {
      nativePath,
      error
    })
    return
  }
}

export async function readTomlFile_(
  file: Maybe<SimpleFileOrString>
): PromiseMaybe<JsonMap> {
  return mapNotBlank(file, async ea =>
    parseTomlBuffer(await readFile(toNativePath_(ea)))
  )
}

export function writeTomlFile_(file: SimpleFileOrString, data: Obj) {
  return writeFile(toNativePath_(file), joinLines(...stringifyToml(data)))
}
