import { join } from "path"
import { lazy } from "../../core/Lazy"
import { Maybe } from "../../fe/MaybeTypes"
import { MultiMap } from "../../fe/MultiMap"
import { stripDiacritics, stripEmoji } from "../String"
import { splitCompactLines } from "../fs/CRLF"
import { ProjectPath } from "../fs/ProjectPath"
import { zcat } from "../fs/zcat"
import { unl33t } from "./Leet"

export const CussNativePath = lazy(() =>
  join(ProjectPath.Data(), "cuss.txt.br")
)

export const loadCuss = lazy(async () => {
  const words = splitCompactLines(await zcat(CussNativePath()))
  cussTrie.set(asTrie(words))
})

const cussTrie = lazy<ReturnType<typeof asTrie>>(() => ({
  trie: new MultiMap<string, string>(),
  small: []
}))

// uuid spec passes fastest with 3:
const TriePrefixLength = 3

function asTrie(words: string[]): {
  trie: MultiMap<string, string>
  small: string[]
} {
  const trie = new MultiMap<string, string>()
  const small: string[] = []
  for (const ea of words) {
    if (ea.length < TriePrefixLength) {
      small.push(ea)
    } else {
      trie.add(ea.slice(0, TriePrefixLength), ea)
    }
  }
  // console.log(
  //   "asTrie(): " +
  //     words.length +
  //     " words, " +
  //     trie.store.size +
  //     " prefixes, " +
  //     small.length +
  //     " too small"
  // )
  return { trie, small }
}

export function getCuss(s: string, naughtyWords?: string[]): Maybe<string> {
  const lc = stripEmoji(stripDiacritics(s.toLowerCase().normalize()))
  const { small, trie } =
    naughtyWords == null ? cussTrie() : asTrie(naughtyWords)
  for (const w of [lc.replace(/[^a-z]/gi, ""), ...unl33t(lc)]) {
    const smol = small.find(ea => w.includes(ea))
    if (smol != null) return smol
    for (let i = 0; i < w.length - (TriePrefixLength - 1); i++) {
      const arr = trie.get(w.slice(i, TriePrefixLength))
      if (arr != null) {
        const sub = w.slice(i)
        const bad = arr.find(ea => sub.startsWith(ea))
        if (bad != null) {
          return bad
        }
      }
    }
  }
  return
}

export function isCussy(s: string) {
  return getCuss(s) != null
}

export function decuss(f: () => string): string {
  let retries = 10
  let s = ""
  do {
    s = f()
  } while (retries-- > 0 && isCussy(s.replace(/[^a-z]/gi, "")))

  return s
}
