import { toNotBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { resolveMaybe } from "../fs/Path"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { Settings } from "../settings/Settings"

export function libraryDir(libraryDirOverride?: Maybe<SimpleFileOrString>) {
  return (
    toNotBlank(toS(libraryDirOverride)) ?? Settings.libraryDir.valueOrDefault
  )
}

export function originalsDir(libraryDirOverride?: Maybe<SimpleFileOrString>) {
  return resolveMaybe(
    libraryDir(libraryDirOverride),
    Settings.originalsDir.valueOrDefault
  )
}

export function libraryDataDir(libraryDirOverride?: Maybe<SimpleFileOrString>) {
  return resolveMaybe(
    libraryDir(libraryDirOverride),
    // subdir is all lowercase to minimize case-(in)sensitive filesystem pain
    // and suffering
    ".photostructure"
  )
}

export function libraryPreviewsDir(
  libraryDirOverride?: Maybe<SimpleFileOrString>
) {
  return resolveMaybe(
    libraryDir(libraryDirOverride),
    Settings.previewsDir.valueOrDefault
  )
}
