import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toGt0 } from "../../fe/Number"
import { groupids, userids } from "../UserInfo"
import { env } from "../env/Env"
import { isDocker } from "../platform/isDocker"

function whyMismatched(
  desc: string,
  envKey: string,
  actual: number[]
): Maybe<string> {
  const val = toGt0(env()[envKey])
  return val != null && !actual.includes(val)
    ? `WARNING: ${desc} is running as ${actual} but $${envKey}=${val}`
    : undefined
}

export function whyUidGidMismatched(): Maybe<string[]> {
  const arr = compactBlanks([
    whyMismatched("user id", "PUID", userids()),
    whyMismatched("group id", "PGID", groupids())
  ])
  return arr.length === 0 ? undefined : arr
}

export const verifyUidGid = lazy(() => {
  if (isDocker()) {
    map(whyUidGidMismatched(), why =>
      console.log(
        [
          "",
          ...why,
          "This may result in file permission issues!",
          // Angle-bracket-surrounded URLs may be included in the URL (!!) by UnRAID. BOO
          "See https://forum.photostructure.com/t/1597/2 for details.",
          ""
        ].join("\n")
      )
    )
  }
})
