import { isEmpty } from "./Array"
import { ChangeListener } from "./ChangeListener"
import { eql } from "./Eql"
import { Maybe } from "./MaybeTypes"
import { gt0 } from "./Number"
import { setUnrefTimeout } from "./UnrefTimeout"
import { UnwrapPromiseLike } from "./UnwrapPromiseLike"

// USED BY LOG: DON'T DEPEND ON ANYTHING (much)

export interface MemoizedLazy<T> {
  /**
   * Force the prior value to be `t`
   */
  set(t: T): void
  /**
   * clears the prior value
   * @return the prior value
   */
  clear(): Maybe<T>
  /**
   * Clears the prior value but doesn't return it (so we don't have to
   * explicitly ignore unawaited promises)
   */
  unset(): void
  /**
   * Forces the underlying thunk or later to be applied
   */
  refresh(): T
  /**
   * @return the milliseconds a prior result may be returned by `apply`, or
   * undefined if no TTL is set
   */
  ttl(): Maybe<number>
  /**
   * @param ttl undefined to remove TTL expirations
   */
  setTTL(ttl: number | undefined): void
  /**
   * Immediately calls the listener with current value, and calls the listener
   * if the result changes.
   */
  watch(watcher: ChangeListener<T>): void
  /**
   * Calls the listener if the result changes later. The underlying thunk is
   * not applied now (like with {@link MemoizedThunk.watch}).
   */
  watchLater(watcher: ChangeListener<T>): void
  lastSetAgoMs(): Maybe<number>
  /**
   * Was this applied and not cleared or unset?
   */
  hasPrior(): boolean
  /**
   * @return the previously-memoized value, if it isn't stale (according to
   * `ttl()`)
   */
  prior(): Maybe<T>

  /**
   * Has a result not been calculated, or was calculated more than `ttl()` ms
   * ago?
   */
  isStale(): boolean

  toString(): string
}

export interface MemoizedThunk<T> extends MemoizedLazy<T> {
  (): T
}

export type UnwrapLazy<T> = T extends MemoizedThunk<infer U> ? U : T
export type UnwrapLazyAsync<T> = T extends MemoizedThunk<infer U>
  ? UnwrapPromiseLike<U>
  : T

// HEY, FUTURE ME: yeah, I know you think something here should call
// ee().on("clearCache",), but DON'T DO IT, MAN. lazy is used by lots of
// instances that we want to be garbage collected, and that would create a huge
// memory leak.

/**
 * @param ttlMs if 0 or null, the result of `thunk` will be cached forever.
 */
export function lazy<T>(thunk: () => T, ttlMs?: number): MemoizedThunk<T> {
  let lastSetTs: number | undefined
  let result: T | undefined
  const watchers: ChangeListener<T>[] = []

  // HEY FUTURE ME: We're using named functions here, instead of fat arrows,
  // so they show up properly in stacktraces.

  function Lazy() {
    if (Lazy.isStale()) {
      // NO PROMISE AWAITING HERE. Otherwise N calls would go through while we
      // wait for the promise to resolve.
      Lazy.setResult(thunk())
    }
    return result!
  }
  // REMEMBER: function `.name` is not allowed to be overridden!

  async function onSetResult(priorP: Maybe<T>, currentP: T) {
    if (isEmpty(watchers)) return
    // prior and current could be a promise!

    // eslint-disable-next-line @typescript-eslint/await-thenable
    const _prior = await priorP
    // eslint-disable-next-line @typescript-eslint/await-thenable
    const current = await currentP
    if (!eql(_prior, current)) {
      for (const ea of watchers) ea(current)
    }
    // We're interpreting undefined as unset, and `null` as a valid set value.
    if (ttlMs != null && ttlMs > 0 && current !== undefined) {
      // help the garbage collector:
      setUnrefTimeout(() => Lazy.vacuum(), ttlMs)
    }
  }

  Lazy.vacuum = function vacuum() {
    if (Lazy.isStale()) Lazy.unset()
  }

  Lazy.setResult = function setResult(t: T) {
    lastSetTs = Date.now()
    void onSetResult(result, t)
    return (result = t)
  }

  Lazy.isStale = function isStale() {
    return lastSetTs == null || (gt0(ttlMs) && lastSetTs + ttlMs < Date.now())
  }

  Lazy.set = function set(t: T) {
    Lazy.setResult(t)
  }

  Lazy.unset = function unset() {
    Lazy.setResult(undefined as any)
    lastSetTs = undefined
  }

  Lazy.clear = function clear() {
    const _prior = result
    Lazy.unset()
    return _prior
  }

  Lazy.prior = function prior() {
    Lazy.vacuum()
    return result
  }

  Lazy.refresh = function refresh() {
    return Lazy.setResult(thunk())
  }

  Lazy.ttl = function ttl() {
    return ttlMs
  }

  Lazy.setTTL = function setTTL(ttl: number | undefined) {
    ttlMs = gt0(ttl) ? ttl : undefined
  }

  Lazy.watchLater = function watchLater(watcher: ChangeListener<T>) {
    watchers.push(watcher)
  }

  Lazy.watch = function watch(watcher: ChangeListener<T>) {
    watcher(Lazy())
    Lazy.watchLater(watcher)
  }

  Lazy.toString = function toString() {
    return "[Lazy]"
  }

  Lazy.toJSON = function toJSON() {
    // We don't support JSON encode/decode:
    return undefined
  }

  Lazy.lastSetAgoMs = function lastSetAgoMs() {
    return lastSetTs == null ? undefined : Date.now() - lastSetTs
  }

  Lazy.hasPrior = function hasPrior() {
    return lastSetTs != null
  }

  return Lazy
}
