import { DateObjectUnits, DateTime, Duration, Zone } from "luxon"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { hourMs, isDate, secondMs } from "../../fe/Date"
import { mapOr } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { closeTo as closeToNumber, gt0, round } from "../../fe/Number"
import { isObject, omit } from "../../fe/Object"
import { cmp } from "../../fe/Primitive"
import { mkLogger } from "../Logger"

const logger = lazy(() => mkLogger("date.Date"))

// WHAT THE ACTUAL FLYING F luxon 2 removed zone from DateObject.

// SITS: Luxon considers 0 to be INVALID for month, day, and hour, so these
// are all optional.
export interface DateObject
  extends Pick<
      DateObjectUnits,
      "year" | "month" | "day" | "hour" | "minute" | "second"
    >,
    Pick<DateObjectUnits, "millisecond"> {
  zone?: string | Zone | undefined
  rawValue?: string
  tzoffsetMinutes?: number | undefined
}

export function isDateObject(a: any): a is DateObject {
  return isObject(a) && ["year", "month", "day"].every(k => k in a)
}

export function cmpDate(a?: Date, b?: Date): number {
  const aTime = mapOr(
    a,
    d => d.getTime(),
    () => 0
  )
  const bTime = mapOr(
    b,
    d => d.getTime(),
    () => 0
  )
  return cmp(aTime, bTime)
}

export function msUntil(d?: Date): number {
  if (d == null) return 0
  const ts = d.getTime()
  const n = Date.now()
  return ts <= n ? 0 : ts - n
}

export function closeTo(a: Maybe<Date>, b: Maybe<Date>, maxMsDelta: number) {
  return (
    a != null && b != null && Math.abs(a.getTime() - b.getTime()) <= maxMsDelta
  )
}

export function nowish(d: Maybe<number | Date>, maxMsDelta = 2500): boolean {
  return d == null
    ? false
    : isDate(d)
    ? closeTo(d, new Date(), maxMsDelta)
    : Math.abs(d - Date.now()) < maxMsDelta
}

export function isRecentMs(timeMs: Maybe<number>, deltaMs: number): boolean {
  return closeToNumber(Date.now(), timeMs, deltaMs)
}

export function fmtMs(ms: number): string {
  return Duration.fromMillis(ms).toFormat("m:ss.SSS")
}

export function durationToPaddedHMS(ms: number): string {
  return Duration.fromMillis(ms).toFormat("hhhh:mm:ss.SSS")
}

export function durationHMS(ms: number): string {
  return Duration.fromMillis(round(ms / secondMs) * secondMs).toFormat(
    ms < hourMs ? "m:ss" : "h:mm:ss"
  )
}

export function isoNow() {
  return new Date().toISOString()
}

export function utcIsoToTs(iso: Maybe<string>): Maybe<number> {
  if (blank(iso)) return
  const dt = DateTime.fromISO(iso, { zone: "utc" })
  return dt.isValid ? dt.toMillis() : undefined
}

export function dateObjectToDateTime(obj: Maybe<DateObject>): Maybe<DateTime> {
  if (obj == null) return
  try {
    const dou: DateObjectUnits = omit(
      obj,
      "zone",
      "rawValue",
      "tzoffsetMinutes"
    )
    for (const k of ["month", "day", "hour"] as const) {
      if (!gt0(dou[k])) {
        delete dou[k]
      }
    }
    const d = DateTime.fromObject(dou, { zone: obj.zone })
    if (d != null && !d.isValid) {
      logger().debug("dateObjectToDateTime() invalid", {
        obj,
        why: d.invalidExplanation
      })
    }
    return d?.isValid ? d : undefined
  } catch (error) {
    logger().debug("dateObjectToDateTime() failed", { obj, error })
    return
  }
}

// DON'T USE JS DATE TO PARSE! js date parsing erases timezones and forces
// everything to local or UTC.
