import { gte, isNumber, lt, round } from "./Number"
import { StringValued } from "./Object"
import { toS } from "./toS"

const pads: StringValued = {}

export function padding(char: string, length: number): string {
  if (length < 1) return ""
  if (!gte(pads[char]?.length, length)) {
    let s = pads[char] ?? char
    while (s.length < length) {
      s += char
    }
    pads[char] = s
  }
  return pads[char]!.substring(0, length)
}

// first added for irony, then used nonironically:
export function leftPad(s: any, minLength: number, padChar: string): string {
  if (padChar.length === 0) throw new Error("leftPad() given empty pad")
  if (lt(s, 0)) {
    return "-" + leftPad(String(Math.abs(s)), minLength - 1, padChar)
  }
  const str = toS(s)
  return padding(padChar, minLength - str.length) + str
}

/**
 * Left-zero-pad
 */
export function padNumeric(i: any, minLength: number): string {
  return leftPad(isNumber(i) ? round(i) : "0", minLength, "0")
}

export function pad2(s: any): string {
  return padNumeric(s, 2)
}

export function pad3(s: any): string {
  return padNumeric(s, 3)
}

export function pad4(s: any): string {
  return padNumeric(s, 4)
}
