import { stringify } from "./JSON"

/**
 * Similar to `String(a)`, but `undefined` and `null` render as "", arrays are
 * comma-separated with no square bracket prefix/suffix
 */
export function toS(a?: any | any[]): string {
  return toStr(a, ",")
}

// eslint-disable-next-line @typescript-eslint/unbound-method
const DefaultToString = {}.toString

// types: "undefined" "object" "boolean" "number" "bigint" "string" "symbol" "function" "object"
export function toStr(a: any | any[], joinChar = ","): string {
  if (a == null) {
    return ""
  } else {
    switch (typeof a) {
      case "string":
        return a
      case "boolean":
      case "number":
      case "bigint":
      case "symbol":
        return String(a)
      default: // "function" or "object"
        if (Array.isArray(a)) {
          return a.map(ea => toStr(ea, joinChar)).join(joinChar)
        } else if (a.toString !== DefaultToString) {
          return a.toString()
        } else {
          return stringify(a)
        }
    }
  }
}
