import { noColor } from "./NoColor"

// we're not referencing Setting.logColor here to break deploop:
let colorEnabled = !noColor()

export function setColorEnabled(on?: boolean) {
  colorEnabled = on ?? !noColor()
}

function withAsciiEscape(on: number, off: number) {
  return (msg: string) =>
    colorEnabled ? `\u001b[${on}m${msg}\u001b[${off}m` : msg
}

// https://en.wikipedia.org/wiki/ANSI_escape_code#3/4_bit

export const reset = withAsciiEscape(0, 0)
export const bold = withAsciiEscape(1, 22)
export const dim = withAsciiEscape(2, 22)
export const italic = withAsciiEscape(3, 23)
export const underline = withAsciiEscape(4, 24)
export const overline = withAsciiEscape(53, 55)
export const inverse = withAsciiEscape(7, 27)
export const hidden = withAsciiEscape(8, 28)
export const strikethrough = withAsciiEscape(9, 29)

export const black = withAsciiEscape(30, 39)
export const red = withAsciiEscape(31, 39)
export const green = withAsciiEscape(32, 39)
export const yellow = withAsciiEscape(33, 39)
export const blue = withAsciiEscape(34, 39)
export const magenta = withAsciiEscape(35, 39)
export const cyan = withAsciiEscape(36, 39)

export const lightGrey = withAsciiEscape(37, 39)
export const darkGrey = withAsciiEscape(90, 39)
export const redBright = withAsciiEscape(91, 39)
export const greenBright = withAsciiEscape(92, 39)
export const yellowBright = withAsciiEscape(93, 39)
export const blueBright = withAsciiEscape(94, 39)
export const magentaBright = withAsciiEscape(95, 39)
export const cyanBright = withAsciiEscape(96, 39)
export const white = withAsciiEscape(97, 39)

export const bgBlack = withAsciiEscape(40, 49)
export const bgRed = withAsciiEscape(41, 49)
export const bgGreen = withAsciiEscape(42, 49)
export const bgYellow = withAsciiEscape(43, 49)
export const bgBlue = withAsciiEscape(44, 49)
export const bgMagenta = withAsciiEscape(45, 49)
export const bgCyan = withAsciiEscape(46, 49)

export const bgLightGrey = withAsciiEscape(47, 49)
export const bgDarkGrey = withAsciiEscape(100, 49)
export const bgRedBright = withAsciiEscape(101, 49)
export const bgGreenBright = withAsciiEscape(102, 49)
export const bgYellowBright = withAsciiEscape(103, 49)
export const bgBlueBright = withAsciiEscape(104, 49)
export const bgMagentaBright = withAsciiEscape(105, 49)
export const bgCyanBright = withAsciiEscape(106, 49)
export const bgWhite = withAsciiEscape(107, 49)

