import { randomBytes } from "crypto"
import { decuss } from "../words/Cuss"

/**
 * Type-4 UUID
 *
 * @see https://en.wikipedia.org/wiki/Universally_unique_identifier
 */
export function uuid() {
  const b = randomBytes(16)

  // Set the version bits:
  b[6] = (b[6] & 0x0f) | 0x40

  // RFC 4122 Section 3 requires that the characters be generated in lower case
  const s = b.toString("hex")

  // canonical 8-4-4-4-12 format string:
  return [
    s.slice(0, 8),
    s.slice(8, 12),
    s.slice(12, 16),
    s.slice(16, 20),
    s.slice(20)
  ].join("-")
}

/**
 * Type-4 UUID without naughty words
 */
export function safeUUID(): string {
  return decuss(uuid)
}

// only useful for tests:
export const UUIDRegExp = /^\w{8}-\w{4}-\w{4}-\w{4}-\w{12}$/
