import { notBlank, notBlankToS } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, toInt } from "../../fe/Number"
import { opt } from "../../fe/Opt"
import { Setting, SettingOpts } from "./Setting"

export class BoundedIntegerSetting extends Setting<number> {
  constructor(
    readonly options: SettingOpts<number> & { min: number; max: number }
  ) {
    super({
      ...options,
      toEnv: notBlankToS,
      fromEnv: (value: Maybe<string>) =>
        opt(value)
          .filter(notBlank)
          .flatMap(toInt)
          .map(ea => clamp(options.min, options.max, ea))
          .get()
    })
  }

  get min(): number {
    return this.options.min
  }

  get max(): number {
    return this.options.max
  }

  clamp(value: Maybe<number>) {
    return clamp(this.min, this.max, value ?? this.min)
  }

  addToJSON() {
    return {
      "minimum value": this.options.min,
      "maximum value": this.options.max
    }
  }
}
