import { lazy } from "../../core/Lazy"
import { isEmpty } from "../../fe/Array"
import { mapNotBlank, notBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { opt } from "../../fe/Opt"
import { mkLogger } from "../Logger"
import { isBillingService } from "../ServiceNames"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { stripPrefix, stripSuffix } from "../String"
import { memoizeAsync } from "../async/MemoizedAsyncFunc"
import { mapAsyncSerial, thenFlatten, thenMap } from "../async/Promise"
import { thenOrTimeout } from "../async/ThenOrTimeout"
import { stdoutResult_, stdout_ } from "../child/ChildProcess"
import { BaseFile } from "../fs/BaseFile"
import { isReadableDirectory } from "../fs/Stat"
import { isLinux } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"
import { dfPosixRaw } from "./DfPosixRaw"
import { setupVolumeTTL } from "./LazyFsAsync"
import { readProcMounts } from "./MountpointsPosix"
import { commandTimeoutMs, mountpointsTtlMs } from "./VolumeTtls"

export const isGioSupported = lazy(async () => {
  if (!isLinux || isDocker() || isBillingService()) {
    return false
  }

  try {
    const result = await stdoutResult_(GioCommand, ["version"], {
      timeoutMs: commandTimeoutMs(),
      ignoreStderr: true
    })
    return result.code === 0
  } catch (err) {
    return false
  }
})

export const GioCommand = "gio"
export const GioMountMonitorArgs = ["mount", "--monitor", "--anonymous"]

const logger = lazy(() => mkLogger("volumes.Gio"))

/**
 * The `gio` subsystem may not be installed, in which case, we have to poll
 * the results of `df`.
 *
 * These volumes won't be in a `df` list.
 */
// DO NOT USE lazyFsAsync here, as mountpoints() uses this function!
export const gioVolumes = lazy(() =>
  thenOrTimeout(
    async () => {
      // We need to find the FUSE mountpoints that gvfs is using:
      const fuseDirs = await gvfsFuseDirectories()
      if (isEmpty(fuseDirs)) return []

      logger().info("gioVolumes()", { fuseDirs })

      return mapAsyncSerial({
        arr: fuseDirs,
        name: "gioVolumes",
        timeoutMs: mountpointsTtlMs(),
        f: async dir => {
          // df doesn't return the correct mountpoint, at least on Ubuntu:

          // $ df -kP '/run/user/1000/gvfs/smb-share:server=mini.local,share=shared%20folder'
          // Filesystem     1024-blocks     Used Available Capacity Mounted on
          // gvfsd-fuse       195296444 67824088 127472356      35% /run/user/1000/gvfs

          const vols = await dfPosixRaw({
            paths: [dir.nativePath]
          })

          const vol = vols?.[0]

          if (vol == null || !dir.nativePath.startsWith(vol.mountpoint)) {
            logger().warn("df failed to find " + dir, { vols })
            return
          }
          // fix the mountpoint to be the correct full path:
          vol.mountpoint = dir.nativePath
          const remoteInfo = await getRemoteInfo(vol.mountpoint)
          return remoteInfo == null
            ? vol
            : { remote: true, ...vol, ...remoteInfo }
        }
      })
    },
    mountpointsTtlMs(),
    () =>
      logger().warn("gioVolumes(): timed out after " + mountpointsTtlMs + "ms"),
    result =>
      logger().log(isEmpty(result) ? "debug" : "info", "gioVolumes()", {
        result
      })
  )
)

later(() => setupVolumeTTL(gioVolumes))

const getRemoteInfo = memoizeAsync(
  async (mountpoint: string) => {
    try {
      const lines = (
        await stdout_(GioCommand, ["info", mountpoint], {
          timeoutMs: commandTimeoutMs()
        })
      ).split(/[\r\n]+/)
      const uri = mapNotBlank(
        lines.find(ea => ea.startsWith("uri: ")),
        ea => new URL(stripPrefix(ea, "uri: "))
      )
      return {
        displayName: map(
          lines.find(ea => ea.startsWith("display name: ")),
          ea => stripPrefix(ea, "display name: ")
        ),
        remoteHost: map(uri, ea => ea.hostname),
        remoteShare: opt(uri)
          .flatMap(ea => ea.pathname)
          .flatMap(ea => stripPrefix(ea, "/"))
          .flatMap(ea => stripSuffix(ea, "/"))
          .flatMap(decodeURIComponent)
          .filter(notBlank)
          .get()
      }
    } catch (err) {
      logger().warn("getRemoteInfo(): info failed", { mountpoint, err })
      return
    }
  },
  {
    name: "gio.getRemoteInfo",
    maxSize: 255,
    timeoutMs: ShortCommandTimeoutMs,
    clearEveryMs: 10 * minuteMs
  }
)

// $ grep gvfs /etc/mtab
// gvfsd-fuse /run/user/1000/gvfs fuse.gvfsd-fuse rw,nosuid,nodev,relatime,user_id=1000,group_id=1000 0 0

async function gvfsFuseDirectories(): Promise<BaseFile[]> {
  if (!(await isGioSupported())) {
    return logger().tap({
      msg: "gvfsFuseDirectories(): gio is not supported",
      result: []
    })
  }

  return logger().tap({
    msg: "gvfsFuseDirectories()",
    result: await thenFlatten(
      await thenMap(
        readProcMounts(
          undefined,
          fs => fs === "gvfsd-fuse",
          isReadableDirectory // we want all readable gvfsd-fuse mountpoints
        ),
        arr => arr.map(ea => BaseFile.for(ea).clear().childDirectories())
      )
    )
  })
}
