import { sum } from "../../fe/Array"
import { map2 } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { absdiff, clamp, times, toInt } from "../../fe/Number"
import { randomInt } from "../../fe/Random"
import { zip } from "../Array"
import { BitZipDimension } from "./BitZipDimension"
import { avg } from "./Vector"

export interface Dimension {
  min: number
  max: number
}
export interface ValuedDimension extends Dimension {
  value: number
}

export interface BitMixArgs {
  dims: ValuedDimension[]
  bitDepth: number
}

export function concatBits(arr: number[], bitsPerValue: number): number {
  const max = Math.pow(2, bitsPerValue)
  return arr.reduce(
    (acc, i) => acc * max + clamp(0, max, toInt(i, { defaultValue: 0 })!),
    0
  )
}

export function splitBits(n: number, bitsPerValue: number): number[] {
  const max = Math.pow(2, bitsPerValue)
  const result: number[] = []
  while (n > 0) {
    result.unshift(n % max)
    n = Math.floor(n / max)
  }
  return result
}

export function diffConcattedBits(
  a: Maybe<number>,
  b: Maybe<number>,
  bitsPerValue: number
): Maybe<number> {
  return map2(a, b, (ea1, ea2) =>
    sum(
      zip(splitBits(ea1, bitsPerValue), splitBits(ea2, bitsPerValue)),
      ([i, j]) => absdiff(i, j)!
    )
  )
}

export class BitZip {
  constructor(readonly dims: BitZipDimension[]) {}

  clampValue(arr: number[]): number[] {
    return arr.map((ea, i) => clamp(this.dims[i].min, this.dims[i].max, ea))
  }

  randomValue() {
    return this.dims.map(dim => randomInt(dim.min, dim.max))
  }

  zipMin(bitDepth: number): number {
    return this.zip(
      this.dims.map(ea => ea.min),
      bitDepth
    )
  }

  zipMax(bitDepth: number): number {
    return this.zip(
      this.dims.map(ea => ea.max),
      bitDepth
    )
  }

  zip(value: number[], bitDepth: number) {
    // We clone these because we change them as we process bits.
    const min = this.dims.map(d => d.min)
    const max = this.dims.map(d => d.max)
    const clamped = this.clampValue(value)

    let result = 0

    for (let bit = 0; bit < bitDepth; bit++) {
      // shift left, but this may be > 32 bits, so we can't use << operator,
      // YAY JAVASCRIPT
      result *= 2

      const dimIndex = bit % min.length
      const mid = (min[dimIndex] + max[dimIndex]) / 2
      if (clamped[dimIndex] > mid) {
        result += 1 // < no bit twiddling, we may be > 32 bits!
        min[dimIndex] = mid
      } else {
        max[dimIndex] = mid
      }
    }
    return result
  }

  unzip(n: number, bitDepth: number) {
    const min = this.dims.map(d => d.min)
    const max = this.dims.map(d => d.max)
    for (let bit = 0; bit < bitDepth; bit++) {
      const dIdx = bit % min.length
      const mid = (min[dIdx] + max[dIdx]) / 2
      if (isBitSet(n, bitDepth - bit - 1)) min[dIdx] = mid
      else max[dIdx] = mid
    }
    return times(min.length, i => (min[i] + max[i]) / 2)
  }
}

const BitShiftMax = Math.pow(2, 32) - 1

export function isBitSet(n: number, bit: number, disableBitshifting?: boolean) {
  if (disableBitshifting !== true && n < BitShiftMax && bit < 32) {
    return ((n >> bit) & 1) === 1
  } else {
    return 1 === Math.floor(n / Math.pow(2, bit)) % 2
  }
}

export function bitZip(a: BitMixArgs): number {
  a.dims.forEach(dim => (dim.value = clamp(dim.min, dim.max, dim.value)))

  let result = 0

  for (let bit = 0; bit < a.bitDepth; bit++) {
    result *= 2 // < no bit twiddling, we may be > 32 bits!
    const dIdx = bit % a.dims.length
    const d = a.dims[dIdx]
    const mid = avg([d.min, d.max])!
    if (d.value > mid) {
      result += 1 // < no bit twiddling, we may be > 32 bits!
      d.min = mid
    } else {
      d.max = mid
    }
  }
  return result
}

export interface BitUnmixArgs {
  dims: Dimension[]
  bitDepth: number
}

export function bitUnzip(n: number, a: BitUnmixArgs): Maybe<number[]> {
  if (a.bitDepth > 52 || a.bitDepth < 0) return
  const setBits = bitsSetBig(n)
  for (let bit = 0; bit < a.bitDepth; bit++) {
    const dIdx = bit % a.dims.length
    const d = a.dims[dIdx]
    const mid = avg([d.min, d.max])!
    if (setBits.includes(a.bitDepth - bit - 1)) {
      d.min = mid
    } else {
      d.max = mid
    }
  }
  return a.dims.map(d => avg([d.min, d.max])!)
}

// Supports > 32 bit numbers
export function bitsSetBig(n: number): number[] {
  return [...n.toString(2)]
    .reverse()
    .map((ea, idx) => (ea === "1" ? idx : -1))
    .filter(ea => ea !== -1)
}

/**
 * Number of set bits in `x`
 * @see https://stackoverflow.com/questions/109023/how-to-count-the-number-of-set-bits-in-a-32-bit-integer#109117
 */
export function pop(x: number): number {
  x = x - ((x >> 1) & 0x55555555)
  x = (x & 0x33333333) + ((x >> 2) & 0x33333333)
  x = (x + (x >> 4)) & 0x0f0f0f0f
  x = x + (x >> 8)
  x = x + (x >> 16)
  return x & 0x0000003f
}

export function bits<T>(arr: T[], f: (t: T, index: number) => boolean): number {
  return sum(arr, (ea, index) =>
    f(ea, index) ? Math.pow(2, arr.length - index - 1) : 0
  )
}
