import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { isNumber } from "../../fe/Number"
import { FifoCache } from "../FifoCache"
import { fmtFullDuration, parseDuration } from "./DurationFormat"

const decCache = new FifoCache<Maybe<number>>(512)

/**
 * @param s either number in milliseconds or an ISO 8601 duration string
 * @return milliseconds encoded by `s`
 */
export function decodeDuration(s: Maybe<string | number>): Maybe<number> {
  return isNumber(s)
    ? s
    : blank(s)
    ? undefined
    : decCache.getOrSet(s, () => parseDuration(s))
}

const encCache = new FifoCache<Maybe<string>>(512)

/**
 * @param value either number in milliseconds or an ISO 8601 duration string
 * @return ISO 8601 duration string
 */
export function encodeDuration(value: Maybe<number | string>): Maybe<string> {
  return blank(value)
    ? undefined
    : encCache.getOrSet(value, () =>
        fmtFullDuration(isNumber(value) ? value : decodeDuration(value))
      )
}
