import process from "process"
import { WriteStream } from "tty"
import { notBlank } from "../fe/Blank"
import { stringify } from "../fe/JSON"
import { Maybe } from "../fe/MaybeTypes"
import { isWorkerService } from "./ServiceNames"
import { stdoutEnded } from "./StdoutEnded"
import { FailStr } from "./error/ErrorFlags"
import { isFatalError, isHealthCheckError } from "./error/ErrorTypes"
import { BaseFile } from "./fs/BaseFile"
import { streamEnded } from "./fs/StreamEnded"
import { Setting } from "./settings/Setting"

export const ReadyStr = JSON.stringify({ ready: true })

export function stdoutWriteReady(ready: boolean = true) {
  if (!stdoutEnded()) {
    process.stdout.write(JSON.stringify({ ready }) + "\n")
  }
}

export function stdoutWrite(obj: any, ready?: boolean) {
  return writeTTY({ obj, ready, stream: process.stdout })
}

export function stderrWrite(obj: any) {
  return writeTTY({ obj, stream: process.stdout })
}

export function writeTTY({
  obj,
  ready,
  stream
}: {
  obj: any
  ready?: boolean
  stream: Maybe<WriteStream>
}) {
  const ttyEnded = stream == null || streamEnded(stream)
  // if (obj != null) {
  //   try {
  //     mkLogger("writeTTY").debug("()", { obj, ready, ttyEnded })
  //   } catch {
  //     // don't care if I can't log
  //   }
  // }

  if (ttyEnded) return

  const str = stringify(obj)
  stream.write(str + "\n")

  // Only the worker service runs under batch-cluster
  if (isWorkerService() && ready != null) {
    if (str.includes(FailStr) || isFatalError(str) || isHealthCheckError(str)) {
      stream.write(FailStr + "\n")
    } else {
      stream.write(ReadyStr + "\n")
    }
  }
}

export function stdoutWriteSettings(...arr: Setting<any>[]) {
  const o = {}
  for (const ea of arr) {
    ea.addToEnv(o)
  }
  stdoutWrite(o)
}

export function stdoutWriteMigration(migrationFile: BaseFile) {
  return stdoutWrite({ migration: migrationFile.name })
}

export interface MigrationEvent {
  migration: string
}

export function isMigrationEvent(o: any): o is MigrationEvent {
  return o != null && notBlank(o.migration)
}

export const StdoutWrite = {
  shutdownSync: () => stdoutWrite({ shutdownSync: true }),
  restartSync: () => stdoutWrite({ restartSync: true }),
  forceRestartSync: () => stdoutWrite({ forceRestartSync: true }),
  rebuildLibrary: () => stdoutWrite({ rebuildLibrary: true }),
  shutdown: () => stdoutWrite({ shutdown: true })
}
