import { pidExists, Rate } from "batch-cluster"
import { ChildProcess } from "child_process"
import { lazy } from "../../core/Lazy"
import { compact } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { minuteMs, secondMs } from "../../fe/Date"
import { denull, map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt, gt0, lt } from "../../fe/Number"
import { Obj } from "../../fe/Object"
import { toS } from "../../fe/toS"
import {
  addEndable,
  Endable,
  EndableRank,
  EndableRanks,
  ending
} from "../async/Endable"
import { Promises } from "../async/Promises"
import { isRecentMs } from "../date/Date"
import { FatalErrorFlag } from "../error/ErrorFlags"
import { onError } from "../error/OnError"
import { WrappedError } from "../error/WrappedError"
import { onDataChunked } from "../fs/StreamChunker"
import { mkLogger } from "../Logger"
import { waitForPidExit } from "../Pid"
import { Settings } from "../settings/Settings"
import { endProcess, spawn } from "./ChildProcess"

export interface WatchedChildOpts {
  name: string
  childFactory: () => ChildProcess | Promise<ChildProcess>
  dataSep?: string | RegExp
  maxErrorsPerMinute?: number
  endableRank?: EndableRank
  exitCommand?: string
  restartOnExit?: boolean // should only be false when we test sync
  endTimeoutMs?: number | null
}

export interface WatchedChildListener {
  onStdout(data: string): void

  /**
   * @return true if the error should be propagated to onError
   */
  onStderr?(data: string): boolean

  /**
   * @return true if the error requires the child process to be restarted
   */
  onError(source: string, error: any): boolean

  onRestart?(): void

  /**
   * Is it OK to start if we can't shut down a prior child process?
   */
  ignoreStopErrors: boolean // should be true for non-PS daemons
}

// so basic
export function mkBasicWatchedChild(
  args: {
    cmd: string
    args: string[]
  } & Partial<WatchedChildListener>
) {
  const wc: WatchedChild = new WatchedChild({
    name: compact([args.cmd, ...args.args]).join(" "),
    childFactory: () => {
      // Restart every 30 minutes to prevent memory leaks:
      return spawn(args.cmd, args.args, 30 * minuteMs)
    },
    onStdout: () => undefined,
    onError: () => true,
    ignoreStopErrors: true,
    ...args
  })
  return wc
}

/**
 * BatchCluster is for ephemeral clusters of stateless processes.
 *
 * To watch a single, long-lived process, though, we need different management
 * heuristics than what batch-cluster gives us:
 *
 * 1. Start the child on construction
 * 2. Delegate all stdout, and errors to the provided listener
 * 3. Restart the child if it crashes/ends and this process hasn't ended
 * 4. The caller may need to specifically stop(), and then arbitrarily later, start()
 * 5. Notify the listener if the error rate is too high, or restarts are
 *    "flapping" (more than N restarts per some period of where the child spins
 *    up and immediately fails). It's up to the listener to then do something
 *    with that state information (including potentially end the current
 *    process).
 *
 * This class is used for `gio`, `findmnt`, `diskutil`, as well as the `sync`
 * and `web` PhotoStructure daemons.
 */
export class WatchedChild implements Endable {
  readonly name: string
  readonly startTs = Date.now()
  private _stopped = false
  private readonly logger = lazy(() =>
    mkLogger("WatchedChild(" + compact([this.name, this.pid]).join(":") + ")")
  )
  readonly startRate = new Rate()
  readonly mutex = new Promises("child.WatchedChild.mutex", () => 1)
  readonly opts: Required<WatchedChildOpts> & WatchedChildListener
  #ended = false
  private lastError?: WrappedError
  private cp?: ChildProcess

  constructor(opts: WatchedChildOpts & WatchedChildListener) {
    this.name = opts.name
    this.opts = {
      dataSep: "\n",
      maxErrorsPerMinute: Settings.fatalErrorRatePerMinute.valueOrDefault,
      endableRank: EndableRanks.first,
      exitCommand: "",
      restartOnExit: true,
      endTimeoutMs: null,
      ...opts
    }
    // Let the web or db service take some time to close, vacuum, and back up the db.
    addEndable(this.opts.endableRank, this)
    void this.#restart()
  }

  get endTimeoutMs() {
    return denull(this.opts.endTimeoutMs)
  }

  get stopped() {
    return this._stopped
  }

  get ended() {
    return this.#ended
  }

  async end() {
    this.#ended = true
    return this.#stop()
  }

  get proc(): Maybe<ChildProcess> {
    return this.cp
  }

  get pid(): Maybe<number> {
    return map(this.cp, ea => ea.pid)
  }

  running(): boolean {
    return pidExists(this.pid)
  }

  notRunning(): boolean {
    return !this.running()
  }

  /**
   * Shuts down the current process. The process won't start again until restart() is called.
   * @return false if stop failed
   */
  async stop(): Promise<boolean> {
    this.logger().info("stop()")
    this._stopped = true
    return this.mutex.serial("", () => {
      this._stopped = true
      return this.#stop()
    })
  }

  async #stop(): Promise<boolean> {
    this.logger().info("#stop()", {
      stopped: this._stopped,
      ended: this.#ended
    })
    const cp = this.cp
    this.cp = undefined
    if (cp == null) return true // nothing to stop
    return this.#stopChild(cp)
  }

  async #stopChild(cp: ChildProcess) {
    // Send the exit command, if it isn't blank, and stdin is still open:
    if (
      gt0(cp.pid) &&
      notBlank(this.opts.exitCommand) &&
      true === cp.stdin?.writable
    ) {
      try {
        cp.stdin.write(this.opts.exitCommand + "\n")
        // Given the child a chance to exit gracefully before we call
        // endProcess:
        await waitForPidExit(cp.pid, (this.endTimeoutMs ?? secondMs) / 2)
      } catch (error) {
        this.logger().warn("stopChild: error writing to stdin", { error })
      }
    }
    return endProcess(cp, this.endTimeoutMs)
  }

  readonly onError = (src: string, cause: Buffer | string | Error | Obj) => {
    const error = new WrappedError(src, {
      cause
    })
    const ignorable = error.ignorable !== false
    this.logger().log(ignorable ? "warn" : "error", "onError()", error)
    if (this.#ended || ignorable) {
      return
    }

    this.lastError = error
    onError(error)

    if (error.fatal === true) {
      void this.end()
      return
    }

    const requiresRestart = this.opts.onError(src, error)
    if (requiresRestart) {
      this.logger().warn("onError requested restart", { src, error })
      void this.#restart()
    }
    return
  }

  isErrorRateExceeded() {
    return this.logger().tap({
      msg: "isErrorRateExceeded()",
      result: gt(this.startRate.eventsPerMinute, this.opts.maxErrorsPerMinute),
      meta: {
        startRatePerMin: this.startRate.eventsPerMinute,
        maxErrorsPerMin: this.opts.maxErrorsPerMinute
      }
    })
  }

  async restart(force = false) {
    this.logger().info("restart()", {
      stopped: this._stopped,
      ended: this.#ended
    })
    if (this.#ended || ending()) return false
    if (
      !force &&
      lt(
        this.startRate.msSinceLastEvent,
        Settings.minTimeBetweenServiceRestartsMs.valueOrDefault
      )
    ) {
      this.logger().info("restart(): last restart was too recent. Ignoring.", {
        msSinceLastEvent: this.startRate.msSinceLastEvent,
        minTimeBetweenServiceRestartsMs:
          Settings.minTimeBetweenServiceRestartsMs.valueOrDefault
      })
      return
    }
    return this.mutex.serial("", async () => {
      await this.#stop()
      this._stopped = false
      return this.#start()
    })
  }

  async start() {
    this.logger().info("start()", {
      stopped: this._stopped,
      ended: this.#ended
    })
    return this.mutex.serial("", async () => {
      this._stopped = false
      return this.#start()
    })
  }

  /**
   * Only called by onError/onExit, and pauses restarts if error rate is too high.
   * @return false if errors
   */
  async #restart() {
    this.logger().info("#restart()", {
      stopped: this._stopped,
      ended: this.#ended
    })
    return this.mutex.maybeRun("", async () => {
      await this.#stop()
      if (this._stopped || this.#ended) return false
      if (this.isErrorRateExceeded()) {
        this.logger().warn("Cannot restart, error/restart rate is too high.", {
          errorsPerMinute: this.startRate.eventsPerMinute,
          msSinceLastStart: this.startRate.msSinceLastEvent
        })
        // If we're just spinning up, die.
        if (isRecentMs(this.startTs, Settings.probationMs.valueOrDefault)) {
          onError(
            "Can't restart " +
              this.name +
              ", failure rate is too high." +
              FatalErrorFlag,
            this.lastError
          )
        }
        return false
      }
      this.logger().info("#restart()", {
        currentPid: this.pid,
        startRate: this.startRate,
        maxErrorsPerMinute: this.opts.maxErrorsPerMinute
      })

      // eslint-disable-next-line no-unused-expressions
      this.opts.onRestart?.()

      return this.#start()
    })
  }

  // CAREFUL: this must be protected by this.mutex!

  /**
   * @return true if new pid was started
   */
  async #start(): Promise<boolean> {
    this.logger().info("#start()", {
      stopped: this._stopped,
      ended: this.#ended
    })
    if (this._stopped || this.#ended) return false
    if (this.running()) return false
    this.startRate.onEvent()
    const cp = (this.cp = await this.opts.childFactory())

    // Pick up the new PID:
    this.logger.unset()
    this.logger().info("#start(): spawned pid " + this.pid)

    const ctx = "cp(" + cp.pid + ")"
    ;[
      { o: cp, desc: "" },
      { o: cp.stdin, desc: ".stdin" },
      { o: cp.stdout, desc: ".stdout" },
      { o: cp.stderr, desc: ".stderr" }
    ].forEach(({ o, desc }) => {
      map(o, ea =>
        ea.on("error", err => this.onError(ctx + desc + ".on(error)", err))
      )
    })

    void map(this.cp.stdout, sout =>
      onDataChunked(sout, this.opts.dataSep, ea => {
        this.logger().trace("onDataChunked()", ea)
        this.opts.onStdout(ea)
      })
    )

    void map(this.cp.stderr, serr =>
      serr.on("data", s => {
        if (toS(s).includes("Error: Cannot find module")) {
          onError("Failed to start " + this.name + FatalErrorFlag, new Error(s))
        }
        if (this.opts.onStderr?.(s) === true) {
          void this.onError(ctx + ".stderr.on(data)", s)
        }
      })
    )

    this.cp.on("exit", async (code: number | null, signal: string | null) => {
      this.logger().info("onExit", {
        code,
        signal,
        stopped: this._stopped,
        ended: this.#ended
      })
      if (ending()) {
        return
      } else if (this.opts.restartOnExit) {
        await this.#restart()
        this.logger().info("onExit(): finished setting up new child", {
          pid: this.pid
        })
      } else {
        this.logger().info(
          "onExit(): this.opts.restartOnExit is false. Ending.",
          { pid: this.pid }
        )
        void this.end()
      }
    })
    return true
  }
}
