import { notBlankToS } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { BaseSettingOpts, Setting } from "./Setting"

export class OptionalIntegerSetting extends Setting<Maybe<number>> {
  constructor(opts: BaseSettingOpts<Maybe<number>>) {
    super({
      ...opts,
      toEnv: notBlankToS,
      fromEnv: toInt,
      defaultValue: undefined
    })
  }
}
