import { Defined } from "./Maybe"
import { Maybe } from "./MaybeTypes"
import {
  MaybeNullSyncOrAsync,
  MaybeSyncOrAsync,
  SyncOrAsync
} from "./SyncOrAsync"
import { toA } from "./toA"

// Looking for UnwrapPromiseLike? See fe/UnwrapPromiseLike.ts

/**
 * Safe calls for Optional promises:
 */
export async function thenMap<T1, T2>(
  objP: MaybeNullSyncOrAsync<T1>,
  f: (t: T1) => MaybeSyncOrAsync<T2>
): Promise<Maybe<T2>> {
  const obj = await objP
  return obj == null ? undefined : f(obj)
}

/**
 * Serialized promise gathering and compaction
 * @see ../core/async/Promise#tuples
 */
export async function thenCollect<T1, T2>(
  arr: MaybeSyncOrAsync<MaybeSyncOrAsync<T1>[]>,
  f: (t: T1) => MaybeSyncOrAsync<T2>
): Promise<Defined<T2>[]> {
  const result: Defined<T2>[] = []
  for (const eaP of toA(await arr)) {
    if (eaP != null) {
      const ea = await eaP
      if (ea != null) {
        const r = await f(ea)
        if (r != null) result.push(r as any)
      }
    }
  }
  return result
}

export async function thenTap<T>(
  p: SyncOrAsync<T>,
  f: (t: T) => any = console.dir.bind(console)
): Promise<T> {
  const result = await p
  // We await f so if it raises errors, thenTap will propagate the error:
  await f(result)
  return result
}

export function isPromiseLike(o: any): o is PromiseLike<any> {
  return o != null && typeof o === "object" && typeof o["then"] === "function"
}

export function isPromise(o: any): o is Promise<any> {
  return (
    isPromiseLike(o) &&
    typeof (o as any)["catch"] === "function" &&
    typeof (o as any)["finally"] === "function"
  )
}

/**
 * Only run `ifTrue` if `predicate` is true after `p` finishes.
 */
export async function thenIf<T, U>({
  p,
  predicate,
  ifTrue
}: {
  p: Promise<T>
  predicate: (t: T) => boolean
  ifTrue: (t: T) => SyncOrAsync<U>
}): Promise<Maybe<U>> {
  const t = await p
  return predicate(t) ? ifTrue(t) : undefined
}
