import { strEnum, StrEnumKeys } from "./StrEnum"

/**
 * "settled" means either resolved or rejected.
 *
 * "fulfilled" is an alias for "resolved." We use "resolved" as that matches
 * the Promise API terminology.
 *
 * @see
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Promise
 */
export const PromiseStates = strEnum("pending", "resolved", "rejected")
export type PromiseState = StrEnumKeys<typeof PromiseStates>
