import { later } from "../fe/Delay"
import { stringify } from "../fe/JSON"
import { MemoizedThunk } from "../fe/Lazy"
import { Maybe } from "../fe/MaybeTypes"
import { gt0 } from "../fe/Number"
import { isString } from "../fe/String"
import { SyncOrAsync } from "../fe/SyncOrAsync"
import { FifoCacheAsync } from "./FifoCacheAsync"
import { Shim0 } from "./Shim0"
import { ee } from "./event/EventEmitter"
import { Settings } from "./settings/Settings"

// NOTE: this is used statically, so BE CAREFUL WITH NEW CORE IMPORTS: they will
// probably cause a dependency loop.

export interface Shim1<I, O> {
  (input: I): Promise<O>
  setShim(f: Maybe<(input: I) => SyncOrAsync<O>>): void
  hasShim(): boolean
  cache?: MemoizedThunk<FifoCacheAsync<O>>
  cacheDelete(input: I): void
}

export function shim1<I, R>({
  name,
  impl,
  cache,
  toKey = ea => (isString(ea) ? ea : stringify(ea))
}: {
  name: string
  impl: (input: I) => SyncOrAsync<R>
  cache?: MemoizedThunk<FifoCacheAsync<R>>
  toKey?: (input: I) => string
}): Shim1<I, R> {
  let shim: Maybe<(input: I) => SyncOrAsync<R>>

  const f = async (input: I) => {
    const start = Date.now()
    const result = await (shim != null ? shim(input) : impl(input))
    ee().emit(
      "timing",
      name + (shim == null ? "(local)" : "(remote)"),
      Date.now() - start
    )
    return result
  }

  const result: Shim1<I, R> = async (input: I) => {
    if (cache == null) return f(input)
    return cache().getOrSetAsync(toKey(input), async () => f(input))
  }

  result.setShim = (i: Maybe<(input: I) => Promise<R>>) => {
    shim = i

    if (cache != null && gt0(Settings.fsCacheSlowMs.valueOrDefault)) {
      const c = cache()
      const useFsCache = c.opts.useFsCache ?? false
      c.readFsCache = useFsCache
      c.writeFsCache = useFsCache && shim == null
    }
  }

  later(() => {
    // Make sure changes to useFsCache are reflected in the cache:
    Settings.fsCacheSlowMs.watchLater(() => {
      result.setShim(shim)
    })
  })

  result.hasShim = () => shim != null

  result.cache = cache

  result.cacheDelete = (input: I) => cache?.().delete(toKey(input))

  return result
}

export type ShimReturnType<
  T extends Shim0<any> | Shim1<any, any> | ((...args: any) => any)
> = T extends Shim0<infer R>
  ? Promise<R>
  : T extends Shim1<any, infer R>
  ? Promise<R>
  : T extends (...args: any) => infer R
  ? R
  : any
