import { strEnum, StrEnumKeys } from "./StrEnum"

/**
 * @see https://en.wikipedia.org/wiki/Graphics_display_resolution
 */
export const FitSizes = strEnum(
  "uhd8k",
  "uhd5k",
  "uhd4k",
  "qhd",
  "fhd",
  "hd",
  "wvga",
  "qvga",
  "qqvga"
)
export type FitSize = StrEnumKeys<typeof FitSizes>
export const FitSizeValues = FitSizes.values

export const SqSizes = strEnum("s480", "s240", "s120", "s60")
export type SqSize = StrEnumKeys<typeof SqSizes>

export const SqWidths = [60, 120, 240, 480]

export type ImageSizeName = FitSize | SqSize
