import { List } from "../fe/List"
import { Maybe } from "../fe/MaybeTypes"
import { clamp, times } from "../fe/Number"

export class BoundedList<T> implements Iterable<T>, List<T> {
  // round-robin implementation:
  private readonly store: T[]
  private _length = 0
  private _firstIndex = 0
  constructor(readonly maxLength: number) {
    if (maxLength > 1000)
      throw new Error("BoundedList.maxLength of " + maxLength)
    this.store = new Array<T>(...(times(maxLength, () => null) as any))
  }

  /**
   * @param index follows https://github.com/tc39/proposal-item-method
   */
  private mapIndex<U>(index: number, f: (storeIndex: number) => U): Maybe<U> {
    index = Math.trunc(index) ?? 0
    if (index < 0) {
      index += this._length
    }
    return index < 0 || index >= this._length
      ? undefined
      : f((index + this._firstIndex + this.maxLength) % this.maxLength)
  }

  // [i: number]: T {
  //   return this.get(i) as any
  // }

  /**
   * @see https://github.com/tc39/proposal-item-method
   */
  at(n: number): T {
    return this.mapIndex(n, idx => this.store[idx]) as T
  }

  /**
   * @return the last value in this list
   */
  get last(): Maybe<T> {
    return this.at(-1)
  }

  set(n: number, value: T) {
    return this.mapIndex(n, idx => (this.store[idx] = value))
  }

  get length(): number {
    return this._length
  }

  set length(l: number) {
    this._length = clamp(0, this._length, l)
  }

  clear() {
    this.length = 0
  }

  [Symbol.iterator](): IterableIterator<T> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<T> {
      for (let i = 0; i < self.length; i++) {
        yield self.mapIndex(i, ea => self.store[ea])!
      }
    }
    return iter()
  }

  push(...items: T[]): number {
    // Don't bother pushing more than the last maxLength items:
    for (const item of items.slice(-this.maxLength)) {
      if (this._length < this.maxLength) {
        this._length++
      } else {
        // push 1 entry off the head:
        this._firstIndex++
        this._firstIndex = this._firstIndex % this.maxLength
      }
      this.mapIndex(this._length - 1, idx => {
        this.store[idx] = item
      })
    }
    return this._length
  }

  pop(): T | undefined {
    return this.mapIndex(this._length - 1, idx => {
      this._length--
      return this.store[idx]
    })
  }

  unshift(...items: T[]): number {
    for (const item of items.reverse()) {
      if (this._length < this.maxLength) {
        this._length++
      }
      // push 1 entry off the head:
      this._firstIndex--
      this.mapIndex(0, idx => {
        this.store[idx] = item
        this._firstIndex = idx
      })
    }
    return this._length
  }

  shift(): T | undefined {
    return this.mapIndex(0, idx => {
      this._firstIndex++
      this._length--
      return this.store[idx]
    })
  }

  shiftOrFirst() {
    return this.length > 1 ? this.shift() : this.at(0)
  }

  every(callbackfn: (value: T, index: number) => boolean): boolean {
    for (let i = 0; i < this._length; i++) {
      if (!callbackfn(this.at(i), i)) return false
    }
    return true
  }

  some(callbackfn: (value: T, index: number) => boolean): boolean {
    for (let i = 0; i < this._length; i++) {
      if (callbackfn(this.at(i), i)) return true
    }
    return false
  }

  forEach(callbackfn: (value: T, index: number) => void): void {
    for (let i = 0; i < this._length; i++) {
      callbackfn(this.at(i), i)
    }
  }

  map<U>(callbackfn: (value: T, index: number) => U): U[] {
    const arr = []
    for (let i = 0; i < this._length; i++) {
      arr.push(callbackfn(this.at(i), i))
    }
    return arr
  }

  reduce<U>(
    callbackfn: (previousValue: U, currentValue: T, currentIndex: number) => U,
    initialValue: U
  ): U {
    let acc = initialValue
    for (let i = 0; i < this._length; i++) {
      acc = callbackfn(acc, this.at(i), i)
    }
    return acc
  }

  reverse(): this {
    for (let i = 0; i < Math.floor(this._length / 2); i++) {
      this.mapIndex(i, from => {
        this.mapIndex(this._length - 1 - i, to => {
          const swap = this.store[to]
          this.store[to] = this.store[from]
          this.store[from] = swap
        })
      })
    }
    return this
  }

  toA(): T[] {
    return [...this]
  }

  slice(start?: number | undefined, end?: number | undefined): T[] {
    return [...this].slice(start, end)
  }

  // sort(compareFn?: ((a: T, b: T) => number) | undefined): this {
  //   throw new Error("Method not implemented.")
  // }
  // indexOf(searchElement: T, fromIndex?: number | undefined): number {
  //   throw new Error("Method not implemented.");
  // }
  // lastIndexOf(searchElement: T, fromIndex?: number | undefined): number {
  //   throw new Error("Method not implemented.");
  // }
  // filter<S extends T>(callbackfn: (value: T, index: number, array: T[]) => value is S, thisArg?: any): S[];
  // filter(callbackfn: (value: T, index: number, array: T[]) => , thisArg?: any): T[];
  // filter(callbackfn: any, thisArg?: any) {
  //   throw new Error("Method not implemented.");
  // }
  // reduce(callbackfn: (previousValue: T, currentValue: T, currentIndex: number, array: T[]) => T): T;
  // reduce(callbackfn: (previousValue: T, currentValue: T, currentIndex: number, array: T[]) => T, initialValue: T): T;
  // reduce(callbackfn: any, initialValue?: any) {
  //   throw new Error("Method not implemented.");
  // }
  // reduceRight(callbackfn: (previousValue: T, currentValue: T, currentIndex: number, array: T[]) => T): T;
  // reduceRight(callbackfn: (previousValue: T, currentValue: T, currentIndex: number, array: T[]) => T, initialValue: T): T;
  // reduceRight<U>(callbackfn: (previousValue: U, currentValue: T, currentIndex: number, array: T[]) => U, initialValue: U): U;
  // reduceRight(callbackfn: any, initialValue?: any) {
  //   throw new Error("Method not implemented.");
  // }
  // find<S extends T>(predicate: (this: void, value: T, index: number, obj: T[]) => value is S, thisArg?: any): S | undefined;
  // find(predicate: (value: T, index: number, obj: T[]) => boolean, thisArg?: any): T | undefined;
  // find(predicate: any, thisArg?: any) {
  //   throw new Error("Method not implemented.");
  // }
  // findIndex(predicate: (value: T, index: number, obj: T[]) => boolean, thisArg?: any): number {
  //   throw new Error("Method not implemented.");
  // }
  // fill(value: T, start?: number | undefined, end?: number | undefined): this {
  //   throw new Error("Method not implemented.");
  // }
  // copyWithin(target: number, start: number, end?: number | undefined): this {
  //   throw new Error("Method not implemented.");
  // }
  // entries(): IterableIterator<[number, T]> {
  //   throw new Error("Method not implemented.");
  // }
  // keys(): IterableIterator<number> {
  //   throw new Error("Method not implemented.");
  // }
  // values(): IterableIterator<T> {
  //   throw new Error("Method not implemented.");
  // }
  // [Symbol.unscopables](): { copyWithin: boolean; entries: boolean; fill: boolean; find: boolean; findIndex: boolean; keys: boolean; values: boolean; } {
  //   throw new Error("Method not implemented.");
  // }
  // includes(searchElement: T, fromIndex?: number | undefined): boolean {
  //   throw new Error("Method not implemented.");
  // }
}
