import { homedir } from "os"
import { resolve } from "path"
import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { getEnv } from "../env/GetEnv"
import { isDirectorySync } from "../fs/StatSync"
import { isWin } from "../platform/Platform"

export const homeDir = lazy(() => {
  const paths = []
  if (isWin) {
    // cygwin may mess with HOMEPATH and HOME. Only trust USERPROFILE.
    paths.push(getEnv("USERPROFILE"))
  } else {
    paths.push(getEnv("HOME"))
  }
  for (const ea of compactBlanks(paths)) {
    const d = resolve(ea)
    if (isDirectorySync(d)) return d
  }
  return homedir()
})
