// lazy so tests can isProd.set() us into production:
export const isProd = lazy(() => _isProd)
const isTest = () => !isProd()

// WTH, amirite? This lets TypeScript not swoon over the Settings typings.
function exposeNetworkWithoutAuth(): boolean {
  return Settings.exposeNetworkWithoutAuth.valueOrDefault
}

