// First set everyone's name:
for (const [k, v] of entries(Settings)) {
  v._setName(k)
}

/**
 * Force the given path-separated paths to be the suggested values on the
 * welcome page.
 */
export const SuggestedDirsEnvKey = "SUGGESTED_DIRS"

// Only exported for testing:
export function withDefaultPaths(paths: Maybe<string>): string {
  const arr = (blank(paths) ? "" : paths).split(delimiter)
  if (isDocker()) {
    arr.unshift("/opt/photostructure/tools/bin")
  }
  arr.push(...Settings.toolPaths.valueOrDefault)
  return uniq(arr).filter(notBlank).join(delimiter)
}

// use getEnv(), not env().PATH, because Windows can use "Path"
export const pathWithDefaults = lazy(() => withDefaultPaths(getEnv("PATH")))

export const allSettings = lazy(() => {
  return sortBy(values(Settings), settingsSorter)
})

export const persistedSettings = lazy(() => {
  return allSettings().filter(ea => !ea.transient)
})

export const transientSettings = lazy(() => {
  return allSettings().filter(ea => ea.transient)
})

export const persistedSystemSettings = lazy(() => {
  return persistedSettings().filter(ea =>
    SystemCategories.includes(ea.category)
  )
})

export const persistedLibrarySettings = lazy(() => {
  return persistedSettings().filter(ea =>
    LibraryCategories.includes(ea.category)
  )
})

function settingsSorter(s: Setting<any>) {
  return [
    s.categoryType === "system" ? 0 : 1,
    SettingCategories.indexOf(s.category) ?? SettingCategories.length + 1,
    s.advanced,
    s.name
  ]
}

const SettingsByNameOrKey = lazy(() => {
  const settings = new CaseInsensitiveMap<Setting>()
  for (const s of persistedSettings()) {
    // Support both logLevel, log_level, and PS_LOG_LEVEL:
    for (const ea of s.names) {
      settings.set(ea, s)
    }
    for (const ea of s.keys) {
      settings.set(ea, s)
    }
  }
  return settings
})

export function getSettingByNameOrKey(s: string) {
  return SettingsByNameOrKey().get(toS(s).toLowerCase())
}

export const ciSettings = lazy(
  () => new CaseInsensitiveValued(Settings, (_, s) => [...s.names, ...s.keys])
)

