import { Maybe } from "../../fe/MaybeTypes"
import { isReadWriteableDirectorySync } from "../fs/StatSync"
import { isDocker } from "../platform/isDocker"
import { DefaultDockerLibraryDir } from "./DefaultDockerLibraryDir"

// This was extracted from LibraryDir to break a deploop with PosixFile

export function defaultLibraryDir(): Maybe<string> {
  return isDocker() ? DefaultDockerLibraryDir : undefined
}

export function defaultOriginalsDir(): string {
  return isDocker() && isReadWriteableDirectorySync("/ps/originals")
    ? "/ps/originals"
    : "." // < relative to $PS_LIBRARY
}
