import { isEmpty } from "./Array"
import { blank } from "./Blank"
import { lazy } from "./Lazy"
import { map, mapOr } from "./Maybe"
import { Maybe } from "./MaybeTypes"
import { isNumber, toFixedStr } from "./Number"
import { pad2, pad3 } from "./Pad"
import { ensurePrefix } from "./String"
import { plur } from "./Units"

export const secondMs = 1000
export const minuteMs = 60 * secondMs
export const hourMs = 60 * minuteMs
export const dayMs = 24 * hourMs
export const weekMs = 7 * dayMs
/** TRY TO AVOID USING THIS (it's 32 * dayMs) */
export const monthMs = 32 * dayMs
export const yearMs = 365.25 * dayMs

const shortDateTimeFormat = lazy(
  () =>
    new Intl.DateTimeFormat(undefined, {
      // weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
      second: "numeric"
    })
)
const shortDateFormat = lazy(
  () =>
    new Intl.DateTimeFormat(undefined, {
      day: "numeric",
      year: "numeric",
      month: "short"
    })
)

export function toDate(d: Date | number): Date {
  return d instanceof Date ? d : new Date(d)
}

export function fmtDateShort(d: Date | number) {
  if (d == null) return
  return d instanceof Date &&
    d.getHours() === 0 &&
    d.getMinutes() === 0 &&
    d.getSeconds() === 0
    ? shortDateFormat().format(d)
    : shortDateTimeFormat().format(d)
}

/**
 * split an HMS string into a (possibly empty) prefix of zeroes and the remaining suffix.
 */
export function splitHMS(hms: string): string[] {
  return mapOr(
    /^[0:]{1,4}/.exec(hms),
    m => [m[0], hms.slice(m[0].length)],
    () => ["", hms]
  )
}

export function isDate(obj: any): obj is Date {
  return obj instanceof Date
}

export function thisYear() {
  return new Date().getFullYear()
}

/**
 * @return Date `deltaMs` in the past
 */
export function ago(deltaMs: number, from?: Date): Date {
  return new Date((from?.getTime() ?? Date.now()) - deltaMs)
}

/**
 * @return Date `deltaMs` in the future
 */
export function hence(deltaMs: number, from?: Date): Date {
  return ago(-deltaMs, from)
}

export function unixtime(d?: Date | number): number {
  const ms = isDate(d) ? d.getTime() : isNumber(d) ? d : Date.now()
  return Math.floor(ms / secondMs)
}

/**
 * Appropriate for filenames: yMMddHHmmss
 */
export function fmtYMDHMS(arg: Date | number): string {
  const d = toDate(arg)
  return (
    d.getFullYear() +
    pad2(d.getMonth() + 1) +
    pad2(d.getDate()) +
    pad2(d.getHours()) +
    pad2(d.getMinutes()) +
    pad2(d.getSeconds())
  )
}

/**
 * Format millis duration in HH:MM:SS.sss
 */
export function fmtHMS(
  ms: number,
  opts: { includeMs: boolean } = { includeMs: true }
): string {
  const h = Math.floor(ms / hourMs)
  ms -= h * hourMs
  const m = Math.floor(ms / minuteMs)
  ms -= m * minuteMs
  const s = Math.floor(ms / secondMs)
  const millis = Math.floor(ms - s * secondMs)
  return (
    pad2(h) +
    ":" +
    pad2(m) +
    ":" +
    pad2(s) +
    (opts.includeMs ? "." + pad3(millis) : "")
  )
}

const Units = [
  { ms: yearMs, s: "Y" },
  { ms: weekMs, s: "W" },
  { ms: dayMs, s: "D" },
  { ms: hourMs, s: "H" },
  { ms: minuteMs, s: "M" }
]

export function fmtIsoDuration(ms: number): string {
  if (ms === 0) return "PT0S"
  const dateParts: string[] = []
  const timeParts: string[] = []
  for (const unit of Units.slice(Units.findIndex(ea => ea.ms <= ms!))) {
    const i = Math.floor(ms / unit.ms)
    ms -= i * unit.ms
    if (i > 0) (unit.ms >= dayMs ? dateParts : timeParts).push(i + unit.s)
  }
  if (ms > 0) {
    timeParts.push(toFixedStr(ms / secondMs, 3) + "S")
  }
  return (
    "P" +
    dateParts.join("") +
    (isEmpty(timeParts) ? "" : "T" + timeParts.join(""))
  )
}

/**
 * ISO-formatted datestamp: y-MM-dd
 */
export function fmtIsoDate(ts: Date | number): string {
  const d = isDate(ts) ? ts : new Date(ts)
  return (
    d.getFullYear() + "-" + pad2(d.getMonth() + 1) + "-" + pad2(d.getDate())
  )
}

export function localToDate(local: Maybe<number>): Maybe<Date> {
  if (local == null || local < 0) return
  let i = local
  const pop2 = () => {
    const result = i % 100
    i = Math.floor(i / 100)
    return result
  }
  const millisecond = 10 * pop2()
  const second = pop2()
  const minute = pop2()
  const hour = pop2()
  const day = pop2()
  const month = pop2()
  const year = i
  return new Date(year, month - 1, day, hour, minute, second, millisecond)
}

export function fmtLocalToShort(cs: number): Maybe<string> {
  return map(localToDate(cs), fmtDateShort)
}

export function nextMidnightTs() {
  return new Date().setHours(24, 0, 0, 0)
}

export function msUntilMidnight() {
  // .setHours() returns getTime() (!?!)
  return nextMidnightTs() - Date.now()
}

export function fmtAgo(ts: number, suffix = "ago"): string {
  return fmtElapsedMs(Date.now() - ts, suffix)
}

export function fmtElapsedMs(ms: number, suffix = "ago"): string {
  if (ms < 1000) return "just now"
  suffix = blank(suffix) ? "" : ensurePrefix(suffix, " ")
  if (ms < minuteMs) return plur(Math.floor(ms / secondMs), "second") + suffix
  if (ms < hourMs) return plur(Math.floor(ms / minuteMs), "minute") + suffix
  if (ms < dayMs) return plur(Math.floor(ms / hourMs), "hour") + suffix
  if (ms < weekMs) return plur(Math.floor(ms / dayMs), "day") + suffix
  if (ms < yearMs) return plur(Math.floor(ms / weekMs), "week") + suffix
  return plur(Math.floor(ms / yearMs), "year") + suffix
}
