import { at } from "../fe/At"
import { Maybe } from "../fe/MaybeTypes"
import { Comparable } from "../fe/Primitive"

export class SortedSet<T> {
  readonly store: T[] = []

  constructor(readonly valueOf: (t: T) => Maybe<Comparable>) {}

  get length() {
    return this.store.length
  }

  addAll(...t: Maybe<T>[]) {
    return t.map(ea => this.add(ea))
  }

  findIndex(t: Maybe<T>) {
    return this.findValueIndex(t == null ? undefined : this.valueOf(t))
  }

  findValueIndex(v: Maybe<Comparable>) {
    if (v == null) return

    let low = 0,
      high = this.store.length - 1

    while (low <= high) {
      const mid = low + Math.floor((high - low) / 2)
      const midV = this.valueOf(this.store[mid])!
      if (v === midV) return { found: true, index: mid }
      else if (v > midV) low = mid + 1
      else high = mid - 1
    }
    return { found: false, index: low }
  }

  add(t: Maybe<T>) {
    const idx = this.findIndex(t)
    if (idx?.found === false) {
      this.store.splice(idx.index, 0, t!)
    }
    return idx
  }

  /**
   * @see https://github.com/tc39/proposal-relative-indexing-method#polyfill
   */
  at(idx: number) {
    return at(this.store, idx)
  }

  toA() {
    return [...this.store]
  }

  /**
   * Remove all the entries less than the given entry
   */
  shiftLte(c: Comparable): T[] {
    const idx = this.findValueIndex(c)
    if (idx == null || (idx.index === 0 && !idx.found)) return []
    if (idx.index === this.store.length - 1) {
      const result = [...this.store]
      this.store.length = 0
      return result
    } else {
      return this.store.splice(0, idx.index + (idx.found ? 1 : 0))
    }
  }

  /**
   * Removes elements from an array and, if necessary, inserts new elements in their place, returning the deleted elements.
   * @param start The zero-based location in the array from which to start removing elements.
   * @param deleteCount The number of elements to remove.
   */
  splice(start: number, deleteCount?: number): T[] {
    const result = this.store.splice(start, deleteCount)
    return result
  }

  retainLastN(n: number) {
    if (n < this.length) {
      this.splice(0, this.store.length - n)
    }
    return this.store
  }
}
