import _fs from "fs"
import { join } from "path"
import { lazy } from "../../core/Lazy"
import { nextMidnightTs, secondMs } from "../../fe/Date"
import { delay } from "../../fe/Delay"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { Thunk } from "../../fe/Thunk"
import { RateLimited, rateLimited } from "../RateLimited"
import { processName } from "../ServiceNames"
import { stripAnsiEsc } from "../String"
import { EndableRanks } from "../async/Endable"
import { EndableInterval } from "../async/EndableInterval"
import { ensureNewNativePath_ } from "../fs/EnsureNew"
import { gzip_ } from "../fs/Gzip"
import { endStream } from "../fs/Streams"
import { Settings } from "../settings/Settings"
import { ConsoleLogger } from "./ConsoleLogger"
import { DefaultLogFlushMs } from "./LogCommon"
import { datedLogDir } from "./LogDir"
import { LogEntry } from "./LogEntry"
import { LogFilter, logFilter } from "./LogFilter"
import { ContextProvider } from "./LogFormatter"
import { LogLevel } from "./LogLevel"
import { prepMeta } from "./LogMeta"
import { SimpleLogger } from "./Logger"
import { clearRecentLogEntries, recentLogEntries } from "./RecentLogs"

export class CaptureLogger implements SimpleLogger {
  readonly logEntries: LogEntry[] = []
  log(level: LogLevel, context: string, message: string, meta?: any) {
    this.logEntries.push({
      ts: Date.now(),
      l: level,
      ctx: context,
      msg: message,
      meta
    })
  }

  enabled() {
    return true
  }

  end() {
    // no-op
  }

  async flush() {
    // no-op
  }
}

export interface LogWriterOptions extends ContextProvider {
  maxLinesPerFile: number
  errorLogger: SimpleLogger
  flushEveryMs: number
  logFilter?: Thunk<LogFilter>
}

interface Logfile {
  stream: NodeJS.WritableStream
  nativePath: string
}

function defaultLogWriterOptions(): Required<LogWriterOptions> {
  return {
    maxLinesPerFile: 250000, // ~ 10-20 Mb
    errorLogger: ConsoleLogger.instance(),
    flushEveryMs: DefaultLogFlushMs,
    processName: processName,
    logFilter: logFilter
  }
}

/**
 * Writes to logDir/YYYY-MM-DD/name-00N.log
 */
export class LogWriter extends EndableInterval implements SimpleLogger {
  private readonly logOpts: Required<LogWriterOptions>
  private readonly pendingWrites: LogEntry[] = []
  private _linesSinceRotate: number = 0
  private _nextForcedRotateTs = nextMidnightTs()
  private _logfile: Maybe<Logfile>

  readonly flush: RateLimited<void>

  private _startIndex = 0

  // gzipping the logfiles should not take longer than 10 seconds (assuming logs
  // are on fast local disk)
  readonly endTimeoutMs = 15 * secondMs

  constructor(
    readonly logDir: string,
    options: Partial<LogWriterOptions> = {}
  ) {
    super({
      name: "LogWriter(" + logDir + ")",
      callback: () => this.flush(),
      intervalMs: Math.round(options.flushEveryMs ?? DefaultLogFlushMs * 1.25),
      rank: EndableRanks.logger
    })
    this.logOpts = {
      ...defaultLogWriterOptions(),
      ...options
    }
    this.onEnds.push(() => this.#end())
    this.setIntervalMs(this.logOpts.flushEveryMs)

    this.flush = rateLimited({
      name: "LogWriter.flush()",
      minCallDelayMs: this.logOpts.flushEveryMs,
      f: () => this.#flush()
    })
  }

  toJSON() {
    return {
      _ctor: "LogWriter",
      logDir: this.logDir
    }
  }

  enabled(level: LogLevel, context?: string): boolean {
    return this.logOpts.logFilter().enabled(level, context)
  }

  log(level: LogLevel, context: string, msg: string, meta?: any) {
    if (this.enabled(level, context)) {
      if (this.ended && level === "error") {
        // If it's bad, send the message someplace, at least.
        this.logOpts.errorLogger.log(level, context, msg, meta)
      } else {
        const entry: LogEntry = { ts: Date.now(), l: level, ctx: context, msg }
        if (meta != null) entry.meta = prepMeta(meta)
        if (level === "error") {
          // If we push entry before this, the recentLogEntries show out of order:
          this.writeRecentLogEntries()
        }
        this.pendingWrites.push(entry)
      }
    }
  }

  // must not be async (so this.log can stay sync)
  writeRecentLogEntries() {
    this.pendingWrites.push(...recentLogEntries())
    // we don't want to write these entries more than once:
    clearRecentLogEntries()
  }

  readonly #end = lazy(async () => {
    await this.flush.force()
    return this.#closeCurrent()
  })

  shouldRotate() {
    return (
      this._logfile == null ||
      this._linesSinceRotate >= this.logOpts.maxLinesPerFile ||
      Date.now() >= this._nextForcedRotateTs
    )
  }

  async #flush() {
    const toFlush = [...this.pendingWrites]
    this.pendingWrites.length = 0
    while (toFlush.length > 0) {
      if (this.shouldRotate()) await this.#rotate()
      const stream = this._logfile?.stream
      if (stream == null) {
        this.logOpts.errorLogger.log(
          "error",
          "LogWriter.flush()",
          "this._rotate() returned an empty stream"
        )
        return
      }
      const remainingCapacity = clamp(
        0,
        toFlush.length,
        this.logOpts.maxLinesPerFile - this._linesSinceRotate
      )

      const lines = toFlush.splice(0, remainingCapacity)

      this._linesSinceRotate += lines.length
      stream.write(lines.map(ea => stringify(ea) + "\n").join(""))
    }
    return
  }

  #onError(source: string, error: any) {
    this.logOpts.errorLogger.log("error", "Caught error from " + source, error)
  }

  #errback(source: string) {
    return (error: any) => {
      this.#onError(source, error)
      return this.#closeCurrent()
    }
  }

  // Must only be called by this._flush()
  async #rotate(): Promise<void> {
    await this.#closeCurrent()
    const name = stripAnsiEsc(this.logOpts.processName())
    const nativePath = await ensureNewNativePath_({
      nativePath: join(datedLogDir(this.logDir), name + ".log"),
      emptyIsNew: true,
      startIndex: ++this._startIndex,
      requireNumber: true,
      leftPad: 3 // we're in trouble if we're writing more than 100 log files a day.
    })
    const stream = _fs
      .createWriteStream(nativePath)
      .on("error", this.#errback("file write stream"))
    this._logfile = { stream, nativePath }
    this._nextForcedRotateTs = nextMidnightTs()
  }

  async #closeCurrent() {
    // not this.end(), because we don't want to flush:
    const prior = this._logfile
    this._logfile = undefined
    this._linesSinceRotate = 0

    if (prior != null) {
      try {
        await endStream(prior.stream)
        if (Settings.logCompression.valueOrDefault) {
          // let logtail see the result of the file:
          await delay(this.logOpts.flushEveryMs)
          await gzip_(prior.nativePath)
        }
      } catch (err) {
        this.#onError("_closeCurrent()", err)
      }
    }
  }
}
