import { compact } from "../../fe/Array"
import { isFalse, isTrue } from "../../fe/Boolean"
import { errorCode } from "../../fe/Error"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { errorToS } from "../error/Error"
import {
  DoNotSendErrorFlag,
  ErrorFlag,
  ErrorFlags,
  ErrorFlagsRE,
  FatalErrorFlag,
  FatalErrorRe,
  HealthCheckErrorFlag,
  IgnorableErrorFlag,
  InternalErrorRe,
  NonFatalErrorFlag,
  NonRetriableErrorFlag,
  PleaseSendErrorFlag,
  RetriableErrorFlag
} from "./ErrorFlags"

/**
 * @param msg may contain error flags
 * @return `msg` with any included error flags, and all
 * non-null `flags`, sorted, at the end of the string.
 */
export function addErrorFlags(msg: string, ...flags: Maybe<ErrorFlag>[]) {
  const s = toS(msg) + compact(flags).join("")
  return stripErrorFlags(s) + extractErrorFlags(s).join("")
}

export function stripErrorFlags(err: Maybe<string>): string {
  return toS(err).replace(ErrorFlagsRE, "").trim()
}

/**
 * @return ordered and uniq'ed flags found in `err`
 */
export function extractErrorFlags(err: string): ErrorFlag[] {
  return ErrorFlags.values.filter(ea => err.includes(ea))
}

export function hasErrorFlag(err: string): boolean {
  return ErrorFlags.values.some(ea => err.includes(ea))
}

