export function isHealthCheckError(err: any): boolean {
  return errorToS(err).includes(HealthCheckErrorFlag)
}

export function isPleaseSendError(err: any): boolean {
  return errorToS(err).includes(PleaseSendErrorFlag)
}

const notIgnorablePatterns = [/Can't write [a-z0-9]+ files/i]

const ignorablePatterns = [
  IgnorableErrorFlag,
  "0 output files created", // From exiftool
  "debugger attached", //nodeJS inspector issues
  "debugger listening on", //nodeJS inspector issues
  "diskutil: interrupted", // dang flaky macOS tool
  "ECONNRESET", // read ECONNRESET from broken socket
  "EPIPE", // meh whatev
  "for help", //nodeJS inspector issues
  "Format error in file", // from exiftool
  "https://nodejs.org/en/docs/inspector", //nodeJS inspector issues
  "Missing expected status message", // From exiftool
  "net::ERR_", //See https://cs.chromium.org/codesearch/f/chromium/src/net/base/net_error_list.h  like net::ERR_TIMED_OUT
  "This socket has been ended by the other party", // so rude
  "Unexpected error while trimming", // From sharp
  "Warning" // I mean, it's _a warning_
].map(ea => ea.toLowerCase())

/**
 * Ignorable errors are expected, and not even important enough to log.
 */
export function isIgnorableError(err: any): Maybe<boolean> {
  if (err == null) return true

  const b = err?.ignorable
  if (typeof b === "boolean") return b

  const msg = errorToS(err).toLowerCase()

  return notIgnorablePatterns.some(ea => ea.test(msg))
    ? false
    : ignorablePatterns.some(ea => msg.includes(ea))
    ? true
    : undefined
}

const BusyErrorRe = /SQLITE_BUSY|database is locked/i

export function isSqliteBusyError(err: any): boolean {
  return err.code === "SQLITE_BUSY" || null != errorToS(err).match(BusyErrorRe)
}

export function isSqliteDisconnectedError(err: any): boolean {
  return null != errorToS(err).match(/database .+ not open/i)
}

export function isSqliteConstraintError(err: any): boolean {
  return null != errorToS(err).match(/SQLITE_CONSTRAINT|constraint failed/i)
}

export function isRetriableError(err: any): Maybe<boolean> {
  // NOTE: this certainly isn't the most efficient boolean logic.

  // It's structured this way to be the easiest to read and be correct.

  if (err == null) return

  if (isNonRetriableError(err) === true) {
    return false
  }

  const b = err?.retriable
  if (typeof b === "boolean") {
    return b
  }

  if (
    errorCode(err) === "EBUSY" ||
    isSqliteBusyError(err) ||
    isSqliteDisconnectedError(err)
  ) {
    return true
  }

  const str = errorToS(err)

  return str.includes("EBUSY") || str.includes(RetriableErrorFlag)
    ? true
    : undefined
}

export function isNonRetriableError(err: any): Maybe<boolean> {
  return isFalse(err.retriable) ||
    errorToS(err).includes(NonRetriableErrorFlag) ||
    isSqliteConstraintError(err)
    ? true
    : undefined
}

const doNotSendPatterns = [
  // everything here isn't worth sending to Sentry
  DoNotSendErrorFlag,
  "Corrupt JPEG data", // invalid jpeg
  "ENOSPC", // I can't help with that...
  "Invalid data found when processing input", // from invalid ffmpeg file
  "premature end of data segment", // invalid jpeg
  "VipsJpeg" // invalid jpeg
]

/**
 * These may be fatal errors (like loss of library lock), and the user will care
 * about them, but we don't need to tell Sentry about them.
 */
export function isDoNotSendError(err: any): Maybe<boolean> {
  const b = err?.doNotSend
  if (typeof b === "boolean") return b

  if (isPleaseSendError(err)) return false

  const msg = errorToS(err).toLowerCase()
  if (doNotSendPatterns.some(ea => msg.includes(ea))) return true
  return // we don't know, AND THAT'S OK
}

/**
 * Does `err` represent a "fatal" error?
 */
export function isFatalError(err: any): boolean {
  if (err == null) return false
  if (isTrue(err.fatal)) return true
  const s = errorToS(err)
  return s.includes(FatalErrorFlag)
    ? true
    : s.includes(NonFatalErrorFlag)
    ? false
    : FatalErrorRe.test(s)
}

export function isInternalError(err: any): boolean {
  return err == null
    ? false
    : isTrue(err.internalError) || InternalErrorRe.test(errorToS(err))
}
