import { join } from "path"
import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { setColorEnabled } from "../Chalk"
import { rootLoggers } from "../Logger"
import { end } from "../async/Endable"
import { cacheDir } from "../dir/CacheDir"
import { logDir } from "../dir/LogDir"
import { env } from "../env/Env"
import { isEnvTrue } from "../env/GetEnv"
import { writeTextfile_ } from "../fs/WriteFile"
import { Settings } from "../settings/Settings"
import { ColoredLogFormatter } from "./ColoredLogFormatter"
import { ConsoleLogger } from "./ConsoleLogger"
import { currentFileLogger } from "./CurrentFileLogger"
import { DefaultLogFormatter } from "./DefaultLogFormatter"
import { logBroadcaster } from "./LogBroadcaster"
import { defaultLogLevel, logFilter } from "./LogFilter"
import { LogFilterImpl } from "./LogFilterImpl"
import { LogTail } from "./LogTail"
import { LogWriter } from "./LogWriter"
import { SimpleLogger } from "./Logger"
import { PlaintextLogFormatter } from "./PlaintextLogFormatter"

const readme = `PhotoStructure writes logfiles to this directory.

Although you _can_ directly read these .json files, you don't need to!

Use PhotoStructure's "logcat" and "logtail" tools to merge and pretty-print these files.

See https://photostructure.com/server/tools/#logging for details.
`

export const setupLogger = lazy(() => {
  if (!(logFilter() instanceof LogFilterImpl)) {
    logFilter.set(new LogFilterImpl())
  }
  setupLogFormatter()

  const dir = logDir()
  let fl = currentFileLogger()
  if (fl == null || fl.logDir !== dir) {
    void end(fl)
    void writeTextfile_(join(dir, "README.txt"), readme).catch(() => null)
    fl = new LogWriter(dir)
  }
  const loggers: SimpleLogger[] = [fl]
  const pushConsoleLogger =
    isEnvTrue("logStdout") ||
    Settings.logStdout.valueOrDefault ||
    Settings.tailLogs.valueOrDefault

  if (pushConsoleLogger) {
    loggers.push(ConsoleLogger.instance())
  }
  map(logBroadcaster(), ea => loggers.push(ea))

  rootLoggers.set(loggers)

  if (Settings.tailLogs.valueOrDefault) {
    LogTail.instance()
  }

  Settings.logLevel.watchLater(() => defaultLogLevel.unset())

  setColorEnabled(Settings.logColor.valueOrDefault)

  // make sure cacheDir() is set up for FileCache():
  cacheDir()

  // After we're done, then watch for later changes:
  watchSettings()
})

export const setupLogFormatter = lazy(() => {
  const current = DefaultLogFormatter()
  const currentIsColored = current instanceof ColoredLogFormatter
  const settingIsColored =
    env().NO_COLOR == null && Settings.logColor.valueOrDefault
  if (currentIsColored !== settingIsColored) {
    DefaultLogFormatter.set(
      settingIsColored ? new ColoredLogFormatter() : new PlaintextLogFormatter()
    )
  }
})

const watchSettings = lazy(() => {
  // watch later, because this is called by setupLogger() already...
  Settings.logStdout.watchLater(() => setupLogger.refresh())
  Settings.logDir.watchLater(() => setupLogger.refresh())
  Settings.tailLogs.watchLater(() => setupLogger.refresh())
  Settings.logColor.watchLater(() => setupLogFormatter.refresh())
})
