import { notBlankToS } from "../../fe/Blank"
import { toBoolean } from "../../fe/Boolean"
import { Setting, SettingOpts } from "./Setting"

export class BooleanSetting extends Setting<boolean> {
  constructor(opts: SettingOpts<boolean>) {
    super({
      ...opts,
      toEnv: notBlankToS,
      fromEnv: toBoolean
    })
  }
}
