import { lazy } from "../core/Lazy"
import { isProd, nodeEnv } from "./NodeEnv"
import { version } from "./Version"

export const SimpleAppName = "PhotoStructure"
export const SimpleShortAppName = "phstr" // < phstr.com redirects to photostructure.com
export const SimpleAppNameLC = SimpleAppName.toLowerCase()

export const AppName = lazy(() => SimpleAppName + (isProd ? "" : `-${nodeEnv}`))
export const AppNameVersion = lazy(() => AppName() + " " + version)
