import os from "os"
import { lazy } from "../../core/Lazy"
import { gt0 } from "../../fe/Number"
import { fmtBytes } from "../../fe/Units"
import { mkLogger } from "../Logger"
import { intFromFileSync } from "../fs/ReadFile"
import { isDocker } from "../platform/isDocker"

const logger = lazy(() => mkLogger("work.FreeMem"))

// lazy for tests:

export const estimatedFreeMem = lazy(() => {
  const freemem = os.freemem()
  const totalmem = os.totalmem()
  // By using both free and total memory, we don't ignore
  // current system load, but aren't borked if all memory is used currently (by
  // filesystem cache, presumably)
  const result = (freemem * 2 + totalmem) / 3
  if (isDocker()) {
    // See
    // https://www.reddit.com/r/PhotoStructure/comments/sn68f9/initial_scan_not_adding_everything/hw4bqmj/

    const mem = intFromFileSync("/sys/fs/cgroup/memory/memory.limit_in_bytes")
    // k8s may return 9223372036854771712 if memory isn't limited (which means use the system memory...)
    if (gt0(mem))
      return logger().tap({
        msg: "estimatedFreeMem() (docker mode)",
        result: Math.min(mem, result),
        meta: { freemem, totalmem }
      })
  }
  return logger().tap({
    msg: "estimatedFreeMem() ",
    result,
    meta: {
      result: fmtBytes(result),
      freemem: fmtBytes(freemem),
      totalmem: fmtBytes(totalmem)
    }
  })
})
