import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { RemoteVolume, Volume } from "./Volume"

/* 

on mac el capitan for SMB/CIFS:

//mrm@rusty2._afpovertcp._tcp.local/home  7496824260 4120551220 3376273040    55%    /Volumes/home
//GUEST:@unraid._smb._tcp.local/system    9764349900   68111664 9696238236     1%    /Volumes/system

//mrm@rusty._smb._tcp.local/homes ...
//mrm@rusty.local/photos ...
//mrm@rusty/web ...

on ubuntu 18:

//10.1.1.7/homes ...

With NFS:

10.1.1.9:/volume1/homes

192.168.1.10:/opt/Media ...

*/

const smbRe =
  /^\/\/(?:.+@)?(.+?)(?:\._(?:smb|afs|nfs|tcp|afpovertcp)){0,2}(\.local)?\/(.+)$/i

// should also handle sshfs:
export const nfsRe = /^([^:\s]+):(\/.*)$/

export function addRemoteVolumeInfoPosix(volumes: Volume[]): Volume[] {
  for (const vol of volumes) {
    if (vol.remote === true) {
      const result = parseRemote(vol.filesystem)
      if (result != null) {
        vol.remoteHost = result.remoteHost
        vol.remoteShare = result.remoteShare
      }
    }
  }
  return volumes
}

export function parseRemote(filesystem: Maybe<string>): Maybe<RemoteVolume> {
  if (blank(filesystem)) return
  const smb = smbRe.exec(filesystem)
  if (smb != null) {
    const remoteHost = smb[1] + toS(smb[2])
    const remoteShare = smb[3]
    if (!blank(remoteHost) && !blank(remoteShare)) {
      return {
        remoteHost,
        remoteShare
      }
    }
  }
  const nfs = nfsRe.exec(filesystem)
  if (nfs != null) {
    const remoteHost = nfs[1]
    const remoteShare = nfs[2]
    if (!blank(remoteHost) && !blank(remoteShare)) {
      return {
        remoteHost,
        remoteShare
      }
    }
  }
  return
}
