import { blank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { StringValued } from "../fe/Object"
import { splitCompactLines } from "./fs/CRLF"

/**
 * Parser for sh environment variable assignments, like `FOO="bar"`.
 *
 * NO, WE DON'T SUPPORT NEWLINES IN VALUES. Use `\n` like a reasonable person.
 *
 * Pairs may be one per line or joined on a single line.
 *
 * \# Comments are removed.
 */
export function parseEnvTokens({
  input,
  lowerCaseKeys
}: {
  input: Maybe<string>
  lowerCaseKeys: boolean
}): StringValued {
  const result: StringValued = {}
  if (blank(input)) return result
  let m
  for (const line of splitCompactLines(input)) {
    const re =
      /(?:\s*#.*$)|\s*(?:export\s+)?(?<key>[a-z_]+)\s*=\s*(["'])?(?<val>(?:\\["']|.)*?)(\2)(?:$|\s+|#.*?)\s*/gim

    while ((m = re.exec(line)) != null) {
      if (m.groups == null) continue
      const { key, val } = m.groups
      if (blank(key) || val == null) continue
      const str = val.replace(/\\n/g, "\n").replace(/\\(["'])/g, "$1") // unescape escaped quotes
      // DON'T DO THIS: it makes values like "20.10" turn into 20.1
      // const val = blank(quot) && isNumeric(v) ? toFloat(v) : v
      result[lowerCaseKeys ? key.toLowerCase() : key] = str
    }
  }
  return result
}
