import { isTrue, toBoolean } from "../fe/Boolean"

/**
 * @see https://no-color.org/
 */
export function noColor(): boolean {
  // force color if PS_LOG_COLOR is true
  return (
    toBoolean(process.env.PS_LOG_COLOR) ??
    (isTrue(process.env.NO_COLOR) ||
      ["dumb", "unknown"].includes(process.env.TERM as string))
  )
}
