import { Maybe, MaybeNull } from "./MaybeTypes"
import { ThunkOrT, tot } from "./Thunk"
import { toS } from "./toS"

export function map<T, U>(obj: MaybeNull<T>, f: (t: T) => U): Maybe<U> {
  // isNaN({a:1}) === true
  return obj == null ? undefined : f(obj)
}

export function mapTry<T, U>(f: () => MaybeNull<T>, g: (t: T) => U): Maybe<U> {
  try {
    return map(f(), g)
  } catch {
    return
  }
}

export function map2<T1, T2, U>(
  t1: MaybeNull<T1>,
  t2: MaybeNull<T2>,
  f: (ea1: T1, ea2: T2) => U
): Maybe<U> {
  return t1 == null || t2 == null ? undefined : f(t1, t2)
}

export function map3<T1, T2, T3, U>(
  t1: MaybeNull<T1>,
  t2: MaybeNull<T2>,
  t3: MaybeNull<T3>,
  f: (ea1: T1, ea2: T2, ea3: T3) => U
): Maybe<U> {
  return t1 == null || t2 == null || t3 == null ? undefined : f(t1, t2, t3)
}

export function orElse<T>(obj: MaybeNull<T>, defaultValue: ThunkOrT<T>): T {
  return obj != null ? obj : tot(defaultValue)
}

export function mapOr<T, U>(
  obj: MaybeNull<T>,
  f: (t: T) => U,
  defaultValue: ThunkOrT<U>
): U {
  return obj != null ? f(obj) : tot(defaultValue)
}

export function map2Or<T1, T2, U>(
  t1: MaybeNull<T1>,
  t2: MaybeNull<T2>,
  f: (ea1: T1, ea2: T2) => U,
  defaultValue: () => U
): U {
  return orElse(map2(t1, t2, f), defaultValue)
}

// https://www.typescriptlang.org/docs/handbook/advanced-types.html#predefined-conditional-types

export type Diff<T, U> = T extends U ? never : T

export type Unpick<T, U> = { [P in keyof T]: P extends U ? never : T[P] }

export type Defined<T> = Diff<T, null | undefined | void>

/**
 * @return true iff all `objects` are not `null` or `undefined`
 */
export function defined<T>(object: T): object is Defined<T> {
  return object != null
}

/**
 * Like `map`, but calls `f` if `obj` is not `undefined`.
 *
 * To clarify, if `obj` is `null`, `f` will be called with `null`.
 */
export function ifDefined<T, U>(obj: T | undefined, f: (t: T) => U): Maybe<U> {
  return obj === undefined ? undefined : f(obj)
}

export function allDefined<T>(arr: T[]): arr is Defined<T>[] {
  return arr != null && arr.every(defined)
}

export function firstDefined<T>(...objects: MaybeNull<T>[]): Maybe<T> {
  return objects.find(defined)
}

export function denull<T>(t: T | undefined | null): T | undefined {
  return t == null || toS(t) === "null" ? undefined : t
}

export function nulled<T>(t: T | undefined | null): T | null {
  return t == null ? null : t
}
