import { blank } from "./Blank"

const irregular = new Map([
  ["child", "children"],
  ["photo", "photos"],
  ["video", "videos"]
])
const es = /(s|ss|sh|ch|x|z)$/
const consonant_y = /[^aeiou]y$/
const vowel_y = /[aeiou]y$/
const is = /is$/

export function pluralize(singular: string) {
  if (blank(singular)) return singular

  const irr = irregular.get(singular)
  if (irr != null) return irr

  // https://www.grammarly.com/blog/plural-nouns/
  if (singular.match(es) != null) {
    return singular + "es"
  }
  if (singular.match(consonant_y) != null) {
    return singular.replace(/y$/i, "ies")
  }
  if (singular.match(vowel_y) != null) {
    return singular + "s"
  }
  if (singular.endsWith("o")) {
    return singular + "es"
  }
  if (singular.match(is) != null) {
    return singular.replace(is, "es")
  }

  return singular + "s"
}
