import fse from "fs-extra"
import { utimes } from "utimes"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { isDate } from "../../fe/Date"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { compactValues, keys } from "../../fe/Object"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { Settings } from "../settings/Settings"
import { mkdirp_ } from "./Path"
import {
  SimpleFile,
  SimpleFileOrString,
  dirname,
  toNativePath_
} from "./SimpleFile"
import { statMaybe } from "./Stat"
import { statTimeoutMs } from "./StatTimeout"

// const logger = lazy(() => mkLogger("fs.Touch"))

function toMs(d: number | Date | undefined): Maybe<number> {
  return d == null
    ? undefined
    : isDate(d)
    ? d.getTime()
    : gt0(d)
    ? d
    : undefined
}

export async function touch_(args: {
  file: SimpleFileOrString
  ensureFile?: boolean
  atimeMs?: number | Date
  mtimeMs?: number | Date
  btimeMs?: number | Date
  reference?: SimpleFile
}): Promise<void> {
  const ensureFile = args.ensureFile ?? true
  if (args.file == null || blank(args.file))
    throw new Error("can't touch blank path")
  const nativePath = toNativePath_(args.file)
  if (ensureFile) {
    await mkdirp_(dirname(args.file))
  }
  const rStat = lazy(() => statMaybe(args.reference?.nativePath))
  const mtimeMs = Math.round(
    toMs(args.mtimeMs) ?? (await rStat())?.mtimeMs ?? Date.now()
  )
  const atimeMs = Math.round(
    toMs(args.atimeMs) ?? (await rStat())?.atimeMs ?? mtimeMs
  )
  const btimeMs = map(toMs(args.btimeMs) ?? (await rStat())?.birthtimeMs, ea =>
    Math.round(ea)
  )
  // logger().debug("touch_(): utime()", { args, mtimeMs, atimeMs, btimeMs })
  try {
    await thenOrTimeoutError(
      btimeMs != null && Settings.retainFileBirthtimes.valueOrDefault
        ? utimes(
            nativePath,
            compactValues({
              atime: atimeMs,
              mtime: mtimeMs,
              btime: btimeMs
            })
          )
        : fse.utimes(nativePath, new Date(atimeMs), new Date(mtimeMs)),
      statTimeoutMs()
    )
  } catch (error: any) {
    if (ensureFile && error.code === "ENOENT") {
      // fsp.ensureFile() has issues: the file may not exist when the returned
      // promise is resolved.
      await thenOrTimeoutError(fse.ensureFile(nativePath), statTimeoutMs())
      if (keys(args).length > 1) {
        // args has more than just "file":
        await touch_({ ...args, ensureFile: false })
      }
    } else {
      throw error
    }
  }
}
