import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { SettingsName } from "../settings/SettingsName"

// We don't need to check for PS_APP_DATA or PS_PATH:

export const EnvKeys = strEnum(
  "HOME",
  "NO_COLOR",
  "PATH",
  "PS_CONFIG_DIR",
  "TMP",
  "TEMP",
  "XDG_CACHE_HOME",

  // Windows ENV keys: inconsistent casing here matches windows 10 box via
  // `node -e "console.log(process.env)"`:
  "APPDATA",
  "LOCALAPPDATA",
  "ProgramData",
  "ProgramFiles",
  "ProgramFiles(x86)",
  "ProgramW6432",
  "SystemDrive",
  "SystemRoot",
  "USERPROFILE",
  "windir" // < "C:\Windows"
)
export type EnvKey = StrEnumKeys<typeof EnvKeys>

export type EnvName = SettingsName | EnvKey
