import { clearInterval, clearTimeout, setInterval, setTimeout } from "timers"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, toInt } from "../../fe/Number"
import { pick } from "../../fe/Object"
import { EndableRank, EndableRanks, ending } from "./Endable"
import { EndableWrapper } from "./EndableWrapper"

export class EndableInterval extends EndableWrapper {
  #timer: Maybe<NodeJS.Timeout>
  #timeout: Maybe<NodeJS.Timeout>
  constructor(
    readonly opts: {
      name: string
      callback: (...args: any[]) => any
      intervalMs: number
      initialDelayMs?: number
      rank?: EndableRank
      endTimeoutMs?: number
      unref?: boolean // < defaults to false to prevent tools from exiting unexpectedly
      onEnd?: () => unknown
    }
  ) {
    super(
      opts.name,
      () => this.#clear(),
      opts.rank ?? EndableRanks.first,
      opts.endTimeoutMs
    )
    if (opts.onEnd != null) {
      this.onEnds.push(opts.onEnd)
    }
    this.setIntervalMs(this.opts.intervalMs, this.opts.initialDelayMs, true)
  }

  #clear() {
    map(this.#timer, clearInterval)
    this.#timer = undefined
    map(this.#timeout, clearTimeout)
    this.#timeout = undefined
  }

  hasTimer() {
    return this.#timer != null
  }

  hasTimeout() {
    return this.#timeout != null
  }

  get timingOpts() {
    return pick(this.opts, "intervalMs", "initialDelayMs")
  }

  readonly #callback = () => {
    if (!ending() && !this.ended) {
      this.opts.callback()
    }
  }

  get #canRun() {
    return !ending() && !this.ended
  }

  /**
   * @return true if the given args or state resulted in new intervals being scheduled
   */
  setIntervalMs(
    intervalMs: number,
    initialDelayMs?: number,
    force = false
  ): boolean {
    intervalMs = toInt(intervalMs) ?? 0
    initialDelayMs = toInt(initialDelayMs) ?? 0
    if (
      !force &&
      this.opts.intervalMs === intervalMs &&
      (this.opts.initialDelayMs ?? 0) === initialDelayMs
    ) {
      // no-op if these are already the correct value
      return false
    }

    this.opts.intervalMs = intervalMs
    this.opts.initialDelayMs = initialDelayMs

    this.#clear()

    const maybeScheduleInterval = () => {
      if (this.#canRun && gt0(this.opts.intervalMs)) {
        this.#timer = setInterval(this.#callback, this.opts.intervalMs)
        if (this.opts.unref === true) this.#timer.unref()
      }
    }

    if (gt0(this.opts.initialDelayMs)) {
      this.#timeout = setTimeout(() => {
        if (this.#canRun) {
          this.#callback()
          maybeScheduleInterval()
        }
      }, this.opts.initialDelayMs)
      if (this.opts.unref === true) this.#timeout.unref()
    } else {
      maybeScheduleInterval()
    }
    return true
  }
}
