import { secondMs } from "./Date"
import { isBrowser, isNode } from "./NodeOrBrowser"
import { gt0 } from "./Number"
import { maybeCall } from "./Object"

export function delayUntil(date: Date | number) {
  const t = gt0(date) ? date : date.getTime()
  const delayMs = t - Date.now()
  if (delayMs < 0) {
    if (delayMs > -500) {
      // meh close enough
      return
    } else {
      throw new Error(
        "Mr. Fusion not found, cannot time travel back " + -delayMs + "ms"
      )
    }
  }
  return delay(delayMs).then(() => delayMs)
}

export function delay(millis: number, unref: boolean = true) {
  // On windows at least, setTimeout() only ensures N-1 millis have passed.
  return new Promise<void>(resolve => {
    if (millis <= 0) {
      resolve()
    } else {
      // ceil(+0.5) to make sure we wait enough time:

      // SITS: any casting shenanigans to make this work on the frontend and
      // backend:
      const t: any = setTimeout(() => resolve(), Math.ceil(millis + 0.5))
      if (millis > secondMs && unref && isNode) {
        // if the millis is trivial, don't bother to unref
        t.unref() // < only node has unref!
      }
    }
  })
}

export function later(f: () => any, delayMs: number = 1) {
  const ms = Math.max(1, Math.ceil(delayMs))
  const t = isNode && ms <= 1 ? setImmediate(f) : setTimeout(f, ms)
  return isBrowser || delayMs <= secondMs ? t : maybeCall(t, "unref") ?? t
}
