import { stringify } from "../../fe/JSON"
import { Maybe, MaybePromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, pct } from "../../fe/Number"
import { rateLimited } from "../RateLimited"
import { EndableRanks, ending } from "../async/Endable"
import { EndableInterval } from "../async/EndableInterval"
import { thenMap } from "../async/Promise"
import { errorToS } from "../error/Error"
import { ProgressEvt } from "./ProgressEvt"
import { DefaultThrottleMs, emitProgressEvt } from "./ProgressObserver"

export class PullProgressObserver extends EndableInterval {
  private readonly start = Date.now()
  private done?: boolean

  constructor(
    readonly ctx: Pick<ProgressEvt, "path" | "op">,
    readonly total: number,
    readonly progress: () => Maybe<number> | MaybePromiseMaybe<number>,
    readonly throttleMs: number = DefaultThrottleMs
  ) {
    super({
      name: "PullProgressObserver(" + stringify(ctx) + ")",
      callback: () => this.onInterval(),
      intervalMs: throttleMs,
      rank: EndableRanks.first
    })
  }

  readonly onInterval = rateLimited({
    name: "PullProgressObserver.onInterval",
    f: () =>
      ending() ? this.end() : thenMap(this.progress(), ea => this.emit(ea)),
    minCallDelayMs: this.throttleMs
  })

  observe<T>(p: Promise<T>): Promise<T> {
    // we throw this promise chain away:
    p.then(
      () => this.completed(),
      err => {
        this.logger.warn("failed: ", err)
        this.ctx.op += " (failed: " + errorToS(err, { maxLen: 128 }) + ")"
        void this.end()
      }
    )
    return p
  }

  private emit(current: Maybe<number>) {
    if (gt0(current)) {
      emitProgressEvt({
        ...this.ctx,
        pct: pct(current, this.total),
        elapsedMs: Date.now() - this.start,
        done: this.done
      })
    }
  }

  completed() {
    this.done = true
    if (this.ended) return
    this.emit(this.total)
    void this.end()
  }
}
