import { compactBlanks, uniq } from "./Array"

export function fmtList(arr: string[], joinWord: string): string {
  arr = uniq(compactBlanks(arr))
  return arr == null || arr.length === 0
    ? ""
    : arr.length === 1
    ? arr.join("")
    : arr.slice(0, -1).join(", ") +
      " " +
      joinWord.trim() +
      " " +
      arr[arr.length - 1]
}

export function orList(arr: string[]): string {
  return fmtList(arr, "or")
}

export function andList(arr: string[]): string {
  return fmtList(arr, "and")
}
