import _fse from "fs-extra"
import _path from "path"
import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { retryOnReject } from "../../fe/AsyncRetry"
import { blank, notBlank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { ensurePrefix, splitLast } from "../../fe/String"
import { toS } from "../../fe/toS"
import { greatestBy } from "../Array"
import { mkLogger } from "../Logger"
import {
  ensureSuffix,
  equalsIgnoreCase,
  spliceCapture,
  stripPrefix
} from "../String"
import { isRetriableError } from "../error/ErrorTypes"
import { isPosix, isWin } from "../platform/Platform"
import { native2posix, posix2native } from "./PosixNative"
import {
  SimpleFile,
  SimpleFileOrString,
  isSimpleFile,
  toNativePath_
} from "./SimpleFile"
import { isReadableDirectory } from "./Stat"
import { isReadWriteableDirectorySync } from "./StatSync"

export const logger = lazy(() => mkLogger("fs.Path"))

export function isHiddenBasename(f: SimpleFileOrString): boolean {
  const base = isSimpleFile(f) ? f.base : _path.basename(_path.resolve(f))
  return base.startsWith(".")
}

export function containsHiddenPathname(f: SimpleFileOrString): boolean {
  return toPathnames(f).some(isHiddenBasename)
}

export function isNotHiddenPosixPath(f: SimpleFileOrString): boolean {
  return toPathnames(f).every(ea => !isHiddenBasename(ea))
}

const driveRe = /^(?<drive>[A-Z]:)(?<path>[\\/].*)?$/i

function upcaseDriveLetters(nativePath: string): string {
  const g = driveRe.exec(nativePath)?.groups
  return g == null
    ? nativePath
    : g.drive.toUpperCase() + toS(g.path ?? _path.sep)
}

export function resolveSimpleFile(f: SimpleFileOrString) {
  return isSimpleFile(f) ? f.nativePath : resolve(f.toString())
}

export function resolve(...paths: string[]): string {
  if (blank(paths.join(""))) {
    throw new Error("resolve(): empty paths")
  }
  return _path.resolve(upcaseDriveLetters(paths[0]), ...paths.slice(1))
}

/**
 * Reject if any of the paths are blank
 */
export function resolveMaybe(...paths: Maybe<string>[]): Maybe<string> {
  return paths.some(blank) ? undefined : resolve(...(paths as string[]))
}

export interface ParsedFile {
  /** "/home/user/dir" of "/home/user/dir/file.txt" */
  readonly dir: string
  /** "file.txt" of "/home/user/dir/file.txt" */
  readonly base: string
  /** "file" of "/home/user/dir/file.txt" */
  readonly name: string
  /** ".txt" or ".log.gz" of "/home/user/dir/file.txt" */
  readonly ext: string
}

export function parsePosixPath(posixPath: string): ParsedFile {
  return parseNativePath(posix2native(posixPath))
}

/** @return ".txt" or ".log.gz" of "/home/user/dir/file.txt" */
export function extname(nativePath: string): string {
  return parseNativePath(nativePath).ext
}

/**
 * @return parent directory of `fileish`
 */
export function dirname(fileish: SimpleFileOrString): string {
  return _path.dirname(toNativePath_(fileish))
}

export function extMatches(pathlike1: Maybe<string>, pathlike2: Maybe<string>) {
  if (blank(pathlike1) || blank(pathlike2)) return false
  return equalsIgnoreCase(
    _path.parse(pathlike1).ext,
    _path.parse(pathlike2).ext
  )
}

const CompressedExtRE = /(\.(?:gz|z|7z|xz|bz2))$/i

/**
 * Supports hidden files and extracting ".log.gz" as the `ext` for "file.log.gz"
 */
export function parseNativePath(nativePath: string): ParsedFile {
  const r = spliceCapture(nativePath, CompressedExtRE)
  const p = _path.parse(r?.uncaptured ?? nativePath)
  return {
    ...p,
    ...(r == null
      ? {}
      : {
          ext: p.ext + r.captured,
          base: p.base + r.captured
        })
  }
}

// BEWARE!

// > require("path").parse("C:")
// { root: 'C:', dir: 'C:', base: '', ext: '', name: '' }

/**
 * Return the name (NOT the path!) of the parent directory
 */
export function parentBasename(nativePath: SimpleFileOrString) {
  const p = _path.parse(toNativePath_(nativePath))
  return p.root === p.dir ? p.root : splitLast(p.dir, _path.sep)
}

export function grandParentBasename(nativePath: SimpleFileOrString) {
  return parentBasename(_path.parse(toNativePath_(nativePath)).dir)
}

export function containedByNativePath(args: {
  ancestor: Maybe<SimpleFileOrString>
  descendant: Maybe<SimpleFileOrString>
  acceptSelf?: boolean // false
}): boolean {
  try {
    if (args.descendant == null || args.ancestor == null) return false
    const ancestor = toNativePath_(args.ancestor)
    const descendant = toNativePath_(args.descendant)
    if (blank(ancestor) || blank(descendant)) return false
    const acceptSelf = args.acceptSelf ?? false
    if (ancestor === descendant) return acceptSelf
    return descendant.startsWith(ensureSuffix(ancestor, _path.sep))
  } catch (error) {
    logger().warn("containedByNativePath() failed (returning false)", {
      args,
      error
    })
    return false
  }
}

export function toPathnames(nativePath: SimpleFileOrString): string[] {
  return isSimpleFile(nativePath)
    ? nativePath.pathnames
    : nativePath.split(_path.sep).filter(ea => ea != null && ea !== "")
}

export function pathIsRoot(f: Maybe<SimpleFileOrString>): boolean {
  return notBlank(f) && toPathnames(f).length === (isWin ? 1 : 0)
}

export function pathDepth(f: Maybe<SimpleFileOrString>): Maybe<number> {
  return blank(f) ? undefined : toPathnames(f).length - (isWin ? 1 : 0)
}

export type NativePathed = Pick<SimpleFile, "nativePath">

export function posixPathFrom(
  parent: string | NativePathed,
  child: string | NativePathed
): string {
  const p = toNativePath_(parent)
  const c = toNativePath_(child)
  if (blank(p) || blank(c))
    throw new Error("posixPathFrom empty args " + stringify({ parent, child }))

  return p === c
    ? ""
    : // The ensureSuffix handles windows drive letters properly:
      stripPrefix(
        native2posix(c).normalize(),
        ensureSuffix(native2posix(p), "/").normalize()
      ).normalize() // < macOS requires us to normalize
}

/**
 * @return the native path of `p`. If the full path is longer than
 * `maxLength`, only include the root and parent directory names until the
 * path is at most `maxLength` characters long.
 */
export function ellipsizePath({
  p,
  maxLength
}: {
  p: SimpleFileOrString
  maxLength: number
}) {
  const np = toNativePath_(p)
  if (np.length <= maxLength) return np
  const pathnames = toPathnames(p)
  const base = pathnames.pop()!
  const lhs = []
  const rhs = [base]
  while (pathnames.length > 0) {
    if (lhs.length < rhs.length) {
      // pop off something for lhs:
      lhs.push(pathnames.shift()!)
      if (joinNativePath([...lhs, ...rhs]).length >= maxLength) {
        pathnames.unshift(lhs.pop()!)
        break
      }
    } else {
      rhs.unshift(pathnames.pop()!)
      if (joinNativePath([...lhs, ...rhs]).length >= maxLength) {
        pathnames.push(rhs.shift()!)
        break
      }
    }
  }

  if (pathnames.length > 0) {
    lhs.push("")
  }
  return joinNativePath([...lhs, ...rhs])
}

export function posixPathFromParent(nativePath: string): string {
  return toPathnames(nativePath).slice(-2).join("/")
}

export function posixPathFromGrandparent(nativePath: string): string {
  return toPathnames(nativePath).slice(-3).join("/")
}

export function addNameSuffix(basename_: string, suffix: string): string {
  const p = parseNativePath(basename_)
  return `${p.base}${suffix}${p.ext}`
}

export function isUNC(nativePath: string) {
  return nativePath.startsWith("\\\\")
}

export function isAbsolute(nativePath: string) {
  return (
    (isPosix && nativePath.startsWith("/")) ||
    (isWin && (isUNC(nativePath) || nativePath.match(driveRe) != null))
  )
}

export async function mkdirp_(nativePath: string) {
  try {
    // Don't try to make the mountpoint (or, say, "c:")
    if (await isReadableDirectory(nativePath)) return
    await _fse.mkdirp(nativePath)
  } catch (err) {
    // concurrent mkdirp() calls spuriously throw EACCES or EEXIST
    if (await isReadableDirectory(nativePath)) return
    else throw err
  }
}

export function mkdirpSync_(
  nativePath: string,
  options?: _fse.EnsureDirOptions
) {
  try {
    // Don't try to make the mountpoint (or, say, "c:")
    if (isReadWriteableDirectorySync(nativePath)) return
    _fse.mkdirpSync(nativePath, options)
  } catch (err) {
    // concurrent mkdirp() calls spuriously throw EACCES
    if (isReadWriteableDirectorySync(nativePath)) return
    else throw err
  }
}

export async function move_(srcNativePath: string, destNativePath: string) {
  return retryOnReject(
    () => _fse.move(srcNativePath, destNativePath, { overwrite: true }),
    {
      errorIsRetriable: isRetriableError,
      maxRetries: 5, // windows may throw EBUSY
      retryDelay: 250
    }
  )
}

export function splitNativePath(nativePath: SimpleFileOrString): string[] {
  const result = toNativePath_(nativePath)
  // DON'T USE compactBlanks: (correct) tests will fail, because a path can
  // validly be " "
  return result.split(_path.sep).filter(ea => ea != null && ea !== "")
}

export function joinNativePath(filePaths: string[]): string {
  return ensurePrefix(
    compactBlanks(filePaths).join(_path.sep),
    isWin ? "" : _path.sep
  )
}

export function joinNativePathMaybe(...paths: Maybe<string>[]): Maybe<string> {
  return paths.some(blank) ? undefined : joinNativePath(paths as string[])
}

export function bestMountpointForDir(
  dir: SimpleFileOrString,
  mountpoints: SimpleFileOrString[]
): Maybe<SimpleFileOrString> {
  return greatestBy(mountpoints, ea =>
    // containedByNativePath handles blank and null properly:
    containedByNativePath({
      ancestor: ea,
      descendant: dir,
      acceptSelf: true
    })
      ? [toNativePath_(ea).length]
      : undefined
  )
}

export function posixPathIncludesPathElement(
  posixPath: string,
  name: string
): boolean {
  return posixPath.split("/").includes(name)
}
